import type DetailedMapData from '../../map-data-objects/detailed-map-data';

export function hasWashroomCategory(thing: DetailedMapData<any>) {
	return thing.locationProfiles.some(profile =>
		profile.categories.some(category => category.name.includes('Restroom')),
	);
}

export function hasParkingCategory(thing: DetailedMapData<any>) {
	return thing.locationProfiles.some(profile => profile.categories.some(category => category.name.includes('Parking')));
}
