import type { PackedMessage } from './system';
import { processColliders } from './collider-processor';
import { unpackMessage } from './utils';

export class SyncWorker {
	onerror: (e: any) => void = () => {};
	onmessage: (e: any) => void = () => {};
	postMessage = (e: PackedMessage) => {
		const unpacked = unpackMessage(e);

		this.onmessage({
			data: processColliders(
				unpacked.colliders,
				unpacked.totalWidth,
				unpacked.totalHeight,
				unpacked.watermarkWidth,
				unpacked.watermarkHeight,
				unpacked.watermarkPosition,
				unpacked.msgId,
			),
		});
	};

	terminate = () => {};
}
