import { typedEntries } from '@mappedin/mvf-core/utils';
import type { FloorStackId } from '@mappedin/mvf-floor-stacks';
import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Facade = Pick<MVFv2_STANDARD_MVFv3, 'facade'>;
export type V2Facade = Pick<ParsedMVF, 'facade'>;

const convertFloorStackIdToFacadeId = (floorStackId: FloorStackId) => `fc_${floorStackId.slice(3)}`;

export function buildFacade(v3: V3Facade, { geometryMaps }: Pick<BuilderOptions, 'geometryMaps'>): V2Facade {
	if (!v3.facade) {
		return {};
	}

	const v2: V2Facade = {};

	const v2FacadeCollection: V2Facade['facade'] = {};

	for (const [floorId, v3Facades] of typedEntries(v3.facade)) {
		if (!v3Facades) {
			continue;
		}

		for (const v3Facade of Object.values(v3Facades)) {
			const { floorStackId, geometryIds } = v3Facade;
			const facadeId = convertFloorStackIdToFacadeId(floorStackId);

			v2FacadeCollection[floorStackId] ??= {
				id: facadeId,
				spaces: [],
			};

			v2FacadeCollection[floorStackId].spaces.push(
				...geometryIds.map((geometryId) => ({
					floorId,
					spaceId: geometryMaps.space.get(geometryId)?.ids[0] ?? '',
				})),
			);
		}
	}

	if (Object.keys(v2FacadeCollection).length > 0) {
		v2.facade = v2FacadeCollection;
	}
	return v2;
}
