import { PubSub } from '@packages/internal/common';
import { MarkerComponent } from '../../components/marker';
import { Geometry2DComponents } from '../../entities';
import type { Geometry2D } from '../../entities';
import type { RendererState } from '../../renderer';
import type { Entity2DHTMLDivElementContainer } from '../../types';

export class DOMResizeSystem extends PubSub<{
	'dimensions-update': void;
}> {
	state: RendererState;
	dirty = true;
	domTree: Entity2DHTMLDivElementContainer;
	observers: Map<string | number, ResizeObserver> = new Map();
	constructor(state: RendererState, domTree: Entity2DHTMLDivElementContainer) {
		super();
		this.state = state;
		this.domTree = domTree;
	}

	setupObserver() {
		if (this.observers) {
			for (const resizeObserver of this.observers.values()) {
				resizeObserver.disconnect();
			}
			this.observers.clear();
		}
		let entity: Geometry2D;
		let component: MarkerComponent;
		for (const child of this.domTree.children) {
			const id = child.getAttribute('data-marker-id');
			if (id != null) {
				entity = this.state.geometry2DMap.get(id) as Geometry2D;
				component = entity.components[Geometry2DComponents.UI] as MarkerComponent; // implied by getAttribut
				if (component.options?.dynamicResize) {
					const resizeObserver = new ResizeObserver(this.updateDimensions);
					resizeObserver.observe(component.containerEl); // observing the container el instead of the marker el to avoid pin (::before) pasudo element triggering mutation.
					this.observers.set(child.userData.entityId, resizeObserver);
				}
			}
		}
	}

	updateDimensions = (mutations: ResizeObserverEntry[]) => {
		mutations.forEach(mutation => {
			const target = mutation.target as HTMLElement;
			const id = target.parentElement?.getAttribute('data-marker-id'); // since we are observing the containerEl so we need to access to the markerContainerEl for marker id
			if (id) {
				const entity = this.state.geometry2DMap.get(id);
				const component = entity?.components[Geometry2DComponents.UI];
				if (entity && component instanceof MarkerComponent) {
					component.updateDimensions(mutation.contentRect.width, mutation.contentRect.height);
					component.collisionDirty = true;
				}
			}
		});
		this.publish('dimensions-update');
	};

	update() {
		if (this.dirty) {
			this.setupObserver();
			this.dirty = false;
		}
	}

	destroy() {
		for (const resizeObserver of this.observers.values()) {
			resizeObserver.disconnect();
		}
		this.observers.clear();
	}
}
