import type { RendererState } from '../../renderer';
import type { MarkerComponent } from '../../components/marker';
import type { Geometry2D } from '../../entities/geometry2d';
import { Geometry2DComponents } from '../../entities/geometry2d';
import type LabelComponent from '../../components/label';
import { Vector3 } from 'three';

export const ANIMATION_DURATION = 300;

export class DOMDrawSystem {
	state: RendererState;
	constructor(state: RendererState) {
		this.state = state;
	}

	#colliderWorldPosition = new Vector3();
	#cameraWorldPosition = new Vector3();

	update(isUserInteracting: boolean) {
		let entity: Geometry2D | undefined;
		let component: MarkerComponent | LabelComponent | undefined;

		this.state.cameraObject.getWorldPosition(this.#cameraWorldPosition);
		for (const entityId of this.state.geometry2DIdsInScene) {
			entity = this.state.geometry2DMap.get(entityId);
			if (entity) {
				component = entity.components[Geometry2DComponents.UI];

				if (component && 'containerEl' in component && component.markerContainer != null) {
					if (component.visibilityNeedsUpdate !== false) {
						if (component.visibilityNeedsUpdate === 'show') {
							component.canShow = true;
						} else {
							component.canShow = false;
						}
					}
					// [PERF]: Ensure that we only update the DOM if the component is visible or if it is animating
					if (
						((component.canShow && component.visible === true) || component.animation?.pending === true) &&
						!component.offscreen
					) {
						if (component.options.zIndex != null) {
							component.markerContainer.style.transform = `translate3D(${component.projection.x}px, ${component.projection.y}px, ${component.options.zIndex}px)`;
						} else {
							entity.object3d.getWorldPosition(this.#colliderWorldPosition);
							const distanceFromCamera = this.#cameraWorldPosition.distanceTo(this.#colliderWorldPosition);
							component.markerContainer.style.transform = `translate3D(${component.projection.x}px, ${
								component.projection.y
							}px, ${-distanceFromCamera}px)`;
						}
					}
					if (component.canShow === true && component.visible === true) {
						component.markerContainer.style.pointerEvents = isUserInteracting ? 'none' : component.pointerEvents;
					} else {
						component.markerContainer.style.pointerEvents = 'none';
					}

					// prevent showing the marker if it hasn't picked a placement on its way get shown only
					if (
						component.currentStrategyIndex === -1 &&
						component.visible &&
						component.visibilityNeedsUpdate === 'show'
					) {
						continue;
					}

					if (component.visibilityNeedsUpdate !== false && !component.offscreen) {
						if (component.visibilityNeedsUpdate === 'show') {
							component.markerContainer.style.opacity = '0';
						}

						if (component.animation) {
							component.animation.cancel();
						}

						// setup animation
						component.animation = component.markerContainer.animate(
							[{ opacity: component.canShow && component.visible ? '1' : '0' }],
							{
								duration: ANIMATION_DURATION,
								easing: 'ease-in-out',
								fill: 'forwards',
							},
						);

						component.visibilityNeedsUpdate = false;
					}
				}
			}
		}
	}
}
