import { Geometry3D, Geometry3DComponents } from '../../entities';
import type { RendererState } from '../../renderer';

/**
 * This system is responsible for disposing of geometries that are no longer needed.
 * It runs at the end of the render loop, so that we can create outlines and whatever is needed in the frame.
 */

export class GeometryDisposalSystem {
	dirty = true;
	constructor(private readonly state: RendererState) {}

	update() {
		if (!this.dirty) {
			return;
		}
		for (const entityId of this.state.geometry3DIdsInScene) {
			const entity = this.state.geometry3DMap.get(entityId);
			if (
				entity instanceof Geometry3D &&
				entity.type === 'geometry' &&
				entity.components[Geometry3DComponents.Mesh]?.geometry != null
			) {
				const meshComponent = entity.components[Geometry3DComponents.Mesh];
				if (meshComponent.geometry) {
					meshComponent.geometry.dispose();
					meshComponent.geometry = undefined;
				}
			}
		}
		this.dirty = false;
	}
}
