/**
 * This file contains the API types for MappedinSDK - before they get turned into Classes
 */

export type TLocationType = 'amenities' | 'tenant';

export type TNode = {
	id: string;
	x: number;
	y: number;
	map: string;
	externalId?: string;
	paths: {
		node: string;
		map: string;
		weight?: number;
	}[];
	[propName: string]: any;
};

export type TImage = {
	original?: string;
	large?: string;
	'140x140'?: string;
	xxlarge?: string;
	xsmall?: string;
	'66x66'?: string;
	xlarge?: string;
	medium?: string;
	xxsmall?: string;
	small?: string;
};

export type TLogo = TImage;

export type TGalleryImage = {
	image: TImage;
	caption?: string;
};

export type TPhone = {
	number?: string;
};

export type TSocial = {
	website?: string;
	twitter?: string;
	facebook?: string;
	instagram?: string;
};

export type TColor = {
	hex: string;
	opacity: number;
	rgba: string;
};

export type TVortex = {
	id: string;
	name: string;
	type: string;
	weight: number;
	multiplier: number;
	[propName: string]: any;
};

export type TPicture = {
	original?: string;
};

export type TOpeningHours = {
	'@type': string;
	opens: string;
	closes: string;
	dayOfWeek: string[];
	validFrom?: string;
	validThrough?: string;
};

export type TSiblingGroup = {
	label: string;
	siblings: string[];
};

export type TState = {
	type: string;
	start?: string;
	end?: string;
};

export type TCategory = {
	name?: string;
	externalId?: string;
	parents?: string[];
	id?: string;
	icon?: TPicture;
};

export type TEvent = {
	id: string;
	type: string;
	name: string;
	location?: string;
	description?: string;
	image?: TImage;
	startDate?: number;
	endDate?: number;
	showDate?: number;
};

export interface TGeoReference {
	target: { x: number; y: number };
	control: { x: number; y: number };
}

export interface TMap {
	id: string;
	name: string;
	shortName: string;
	elevation?: number;
	scale?: number;
	x_scale?: number;
	georeference?: TGeoReference[];
	[propName: string]: any;
}

export type TMapGroup = {
	name: string;
	id: string;
};

// A location representing a map group
export type TBuilding = TLocation & {
	groupId: string;
};

export type TLocation = {
	id: string;
	name: string;
	type: string;
	nodes: {
		map: TMap['id'];
		node: TNode['id'];
	}[];
	polygons: {
		map: TMap['id'];
		id: TPolygon['id'];
	}[];
	categories: string[];
	description?: string;
	sortOrder?: number;
	logo?: TLogo;
	phone?: TPhone;
	social?: TSocial;
	color?: TColor;
	shortName?: string;
	detailsUrl?: string;
	parent?: string | null;
	tags?: string[];
	externalId?: string;
	picture?: TPicture;
	states?: TState[];
	operationHours?: TOpeningHours[] | undefined;
	siblingGroups?: TSiblingGroup[] | undefined;
	gallery?: TGalleryImage[] | undefined;
	[propName: string]: any;
};

export type TPolygon = {
	id: string;
	map: string;
	layer?: string;
	layerId?: string;
	externalId?: string;
	name?: string;
	entrances: {
		map: TMap['id'];
		id: TNode['id'];
	}[];
};

export type TPolygonRanking = {
	polygonId: string;
	entranceNodeId: string;
	score: number;
};

export type TLocationState = {
	id: string;
	name: string;
	value: string;
};

export type TVenue = {
	address?: string;
	city?: string;
	countrycode?: string;
	defaultMap?: string;
	externalId?: string;
	id?: string;
	latitude?: number;
	logo?: TLogo;
	longitude?: number;
	metadata?: any;
	name?: string;
	operationHours?: TOpeningHours[];
	postal?: string;
	slug?: string;
	state?: string;
	telephone?: string;
	tzid?: string;
	tzidOverride?: string;
	utcOffset?: string;
	website?: string;
};

export type TMappedinAPI = {
	nodes: TNode[];
	locations: TLocation[];
	categories: TCategory[];
	mapGroups: TMapGroup[];
	polygons: TPolygon[];
	maps: TMap[];
	themes: any;
	venue: TVenue;
	vortexes: TVortex[];
	locationStates?: TLocationState[];
	imageBinaries?: Map<string, Uint8Array>;
};
