import type { ModelComponent } from '../../components/model';
import type { ModelStyleComponnet } from '../../components/styles';
import { Geometry3DComponents } from '../../entities';
import type { EntityTypes, RendererState } from '../../renderer';
import { RENDER_LAYERS } from '../../utils/constants';

/**
 * System for managing dynamic render order assignment for always-on-top elements.
 */
export class RenderOrderSystem {
	// TODO: use groupings to support more than just models for this
	private currentOrder = RENDER_LAYERS.ALWAYS_ON_TOP_MODELS;
	private assignedOrders = new WeakMap<object, number>();
	private state: RendererState;

	constructor(state: RendererState) {
		this.state = state;
	}

	/**
	 * Assigns a unique render order for an overlay object
	 * @param object - The object to assign render order to
	 * @returns The assigned render order
	 */
	assignOrder(object: object): number {
		if (this.assignedOrders.has(object)) {
			return this.assignedOrders.get(object)!;
		}

		this.currentOrder += 1;
		this.assignedOrders.set(object, this.currentOrder);

		return this.currentOrder;
	}

	/**
	 * Releases the render order for an object
	 * @param object - The object to release render order from
	 * @returns The default render order
	 */
	releaseOrder(object: object): number {
		this.assignedOrders.delete(object);

		return RENDER_LAYERS.GEOMETRY;
	}

	/**
	 * Gets the current highest render order
	 * @returns The current highest render order
	 */
	getHighestOrder(): number {
		return this.currentOrder;
	}

	/**
	 * Gets the assigned render order for an object
	 * @param object - The object to get render order for
	 * @returns The render order or undefined if not assigned
	 */
	getOrder(object: object): number | undefined {
		return this.assignedOrders.get(object);
	}

	update() {
		let styleComponent: ModelStyleComponnet | undefined;
		let meshComponent: ModelComponent | undefined;
		let entity: EntityTypes | undefined;
		for (const entityId of this.state.geometry3DIdsInScene) {
			entity = this.state.geometry3DMap.get(entityId);

			// TODO: support more than just models for this
			if (entity && entity.type === 'model') {
				if (entity.components[Geometry3DComponents.Style]?.dirty) {
					styleComponent = entity.components[Geometry3DComponents.Style];
					meshComponent = entity.components[Geometry3DComponents.Mesh];

					if (styleComponent.visibleThroughGeometry === true) {
						const renderOrder = this.assignOrder(meshComponent);
						if (meshComponent.renderOrder !== renderOrder) {
							meshComponent.renderOrder = renderOrder;
						}
					} else if (meshComponent.renderOrder > RENDER_LAYERS.GEOMETRY) {
						meshComponent.renderOrder = this.releaseOrder(meshComponent);
					}
				}
			}
		}
	}
}
