import type { Rectangle } from '../rectangle';

/**
 * Check if two rectangles intersect
 * @param rect1 {Rectangle}
 * @param rect2 {Rectangle}
 */
export function intersects(rect1: Rectangle, rect2: Rectangle) {
	const x1 = rect2.x;
	const y1 = rect2.y;
	const x2 = rect2.x + rect2.w;
	const y2 = rect2.y + rect2.h;
	const x3 = rect1.x;
	const y3 = rect1.y;
	const x4 = rect1.x + rect1.w;
	const y4 = rect1.y + rect1.h;

	return x1 < x4 && x3 < x2 && y1 < y4 && y3 < y2;
}
