import type { DirectionFeature, DirectionsCollection } from '@packages/internal/geojson-navigator';
import type { Connection, Floor, Node, MapDataInternal } from '../../map-data-objects';
import { Coordinate } from '../../map-data-objects';
import type { TDirectionInstructionAction, TDirectionInstruction, TNavigationTarget } from '../../types';
import { differenceBetweenAngles, getRelativeBearing } from '../utils';
import { randomId } from '@packages/internal/common';

const BEARING_CHANGE_THRESHOLD = Math.PI * 0.1;

/**
 * Represents a set of directions between two points.
 *
 * Directions are used to represent the path between two points on the map,
 * as well as the instructions to follow the path.
 */
export class Directions {
	id: string;
	#cache: {
		distance?: number;
		coordinates?: Coordinate[];
		instructions?: TDirectionInstruction[];
	} = {};
	#mapData: MapDataInternal;
	#directions: DirectionFeature[];

	/**
	 * @internal
	 */
	constructor(
		directions: DirectionsCollection,
		mapData: MapDataInternal,
		departure: TNavigationTarget,
		destination: TNavigationTarget,
		options?: { id?: string },
	) {
		this.id = options?.id ?? randomId();
		this.#mapData = mapData;
		this.#directions = directions.features;
		this.departure = departure;
		this.destination = destination;
	}

	/**
	 * @internal
	 */
	get path(): Node[] {
		return this.#directions
			.map(direction => this.#mapData.getById('node', direction.properties.id))
			.filter(Boolean) as Node[];
	}

	/**
	 * The selected departure.
	 */
	departure: TNavigationTarget;

	/**
	 * The selected destination.
	 */
	destination: TNavigationTarget;

	/**
	 * All the coordinates ({@link Coordinate}) of the directions.
	 */
	get coordinates(): Coordinate[] {
		if (this.#cache.coordinates) {
			return this.#cache.coordinates;
		}
		this.#cache.coordinates = this.#directions.map(
			feature =>
				new Coordinate(feature.geometry.coordinates[1], feature.geometry.coordinates[0], feature.properties.groupBy),
		);

		return this.#cache.coordinates;
	}

	/**
	 * The total distance of the path in meters.
	 */
	get distance(): number {
		if (this.#cache.distance) {
			return this.#cache.distance;
		}

		this.#cache.distance = this.#directions.reduce((acc, feature) => acc + (feature.properties.distance || 0), 0);

		return this.#cache.distance;
	}

	/**
	 * The array of instructions ({@link TDirectionInstruction}).
	 */
	get instructions(): TDirectionInstruction[] {
		if (this.#directions.length < 2) {
			return [];
		} else if (this.#cache.instructions) {
			return this.#cache.instructions;
		}

		const instructions: TDirectionInstruction[] = [];
		let distance = 0;

		for (let i = 0; i < this.#directions.length; i++) {
			const direction: DirectionFeature = this.#directions[i];
			const prevDirection: DirectionFeature | undefined = this.#directions[i - 1];
			const nextDirection: DirectionFeature | undefined = this.#directions[i + 1];
			const coordinate = new Coordinate(
				direction.geometry.coordinates[1],
				direction.geometry.coordinates[0],
				direction.properties.groupBy,
			);

			const actions = this.#getActions({
				prevDirection,
				direction,
				nextDirection,
			});
			distance += prevDirection?.properties.distance || 0;

			for (const action of actions) {
				if (action) {
					instructions.push({
						action,
						distance,
						coordinate,
						node: this.#mapData.getById('node', direction.properties.id)!,
					});
					distance = 0;
				}
			}
		}
		this.#cache.instructions = instructions;

		return this.#cache.instructions;
	}

	#lastConnectionAction?: 'take' | 'exit'; // the instruction action type for the last connection
	#lastConnection?: Connection;
	#lastMVFConnection?: any; // Self-serve doors are not connections, so won't get saved as this.#lastConnection

	/**
	 * Get the action for an instruction.
	 *
	 * @hidden
	 * @param options
	 */
	#getActions(options: {
		prevDirection: DirectionFeature | undefined;
		direction: DirectionFeature;
		nextDirection: DirectionFeature | undefined;
	}): (TDirectionInstructionAction | null)[] {
		const { prevDirection, direction, nextDirection } = options;
		const prevFloor = prevDirection && this.#mapData.getById('floor', prevDirection.properties.groupBy!);
		const floor = this.#mapData.getById('floor', direction.properties.groupBy!);
		const nextFloor = nextDirection && this.#mapData.getById('floor', nextDirection.properties.groupBy!);
		const actions: (TDirectionInstructionAction | null)[] = [];

		if (!prevDirection) {
			actions.push({ type: 'Departure' });
		}

		const mvfConnection = this.#mapData.getMVFFeatureByNodeId('connection', direction.properties.id);
		const curConnection = mvfConnection ? this.#mapData.getById('connection', mvfConnection.id) : undefined;
		// NOTE: Ignore connections that
		// - have less than 1 node: A vortex should have more than one nodes attached to it for enter and exit.
		// - direction/edge is not along the path
		const connectionValid = mvfConnection && mvfConnection.nodes.length > 1;
		// TODO: Fix this check for self-serve. Currently, it fails to correctly identify doors, because they are not connections in self-serve.
		const connectionUnused =
			this.#mapData.enterpriseMode &&
			(!direction.properties.destination ||
				!mvfConnection?.nodes.includes(direction.properties.destination) ||
				!mvfConnection?.nodes.includes(direction.properties.id));
		const ignoreConnection = !connectionValid || connectionUnused;

		if (mvfConnection && !ignoreConnection) {
			/**
			 * If a connection is associated with the direction, we handle it as a take/exit action before processing the floor logic.
			 * This is necessary because some connections do not involve changing floors.
			 */
			if (!this.#lastConnectionAction || this.#lastConnectionAction === 'exit') {
				actions.push(this.#createConnectionAction('TakeConnection', floor, nextFloor, curConnection, mvfConnection));
				this.#lastConnectionAction = 'take';
				this.#lastConnection = curConnection;
				this.#lastMVFConnection = mvfConnection;
			} else {
				const prevMvfConnection = this.#mapData.getMVFFeatureByNodeId('connection', prevDirection?.properties.id ?? '');
				const prevConnection = this.#mapData.getById('connection', prevMvfConnection?.id ?? '');
				actions.push(
					this.#createConnectionAction('ExitConnection', floor, prevFloor, prevConnection, prevMvfConnection),
				);
				this.#lastConnectionAction = 'exit';
				this.#lastConnection = curConnection;
				this.#lastMVFConnection = mvfConnection;
			}
		} else if (this.#lastConnectionAction === 'take') {
			/**
			 * This handles the case where a shuttle is about to arrive at its destination; the next connection is available but the current one is not (i.e., a turn action).
			 * Therefore, we should exit the last connection (i.e., exit shuttle).
			 */
			actions.push(
				this.#createConnectionAction('ExitConnection', floor, prevFloor, this.#lastConnection, this.#lastMVFConnection),
			);
			this.#lastConnectionAction = 'exit';
			this.#lastConnection = curConnection;
		} else if (nextFloor && floor?.id !== nextFloor.id) {
			actions.push(this.#createConnectionAction('TakeConnection', floor, nextFloor, curConnection));
		} else if (prevFloor && prevFloor.id !== floor?.id) {
			actions.push(this.#createConnectionAction('ExitConnection', floor, prevFloor, curConnection));
		} else {
			actions.push(this.#createTurnAction(prevDirection, direction));
		}

		if (!nextDirection) {
			actions.push({ type: 'Arrival' });
		}

		return actions;
	}

	/**
	 * Create a connection action.
	 *
	 * @hidden
	 * @param type
	 * @param fromFloor
	 * @param toFloor
	 * @param connection
	 * @param mvfConnection
	 */
	#createConnectionAction(
		type: 'TakeConnection' | 'ExitConnection',
		fromFloor?: Floor,
		toFloor?: Floor,
		connection?: Connection,
		mvfConnection?: any,
	): TDirectionInstructionAction {
		let direction: TDirectionInstructionAction['direction'] = 'none';
		const fromFloorElevation = fromFloor ? fromFloor.elevation : 0;

		if (fromFloorElevation != null && toFloor?.elevation != null) {
			const elevationDelta = fromFloorElevation - toFloor.elevation;
			if (elevationDelta > 0) {
				direction = 'down';
			} else if (elevationDelta < 0) {
				direction = 'up';
			}
		}

		const _connectionType = connection?.type ?? mvfConnection?.type ?? 'stairs';

		return { type, fromFloor, toFloor, direction, connection, connectionType: _connectionType };
	}

	/**
	 * Create a turn action.
	 *
	 * @hidden
	 * @param prevDirection
	 * @param direction
	 */
	#createTurnAction(
		prevDirection: DirectionFeature | undefined,
		direction: DirectionFeature,
	): TDirectionInstructionAction | null {
		const exits = direction.properties.edges;
		if (!exits) {
			return null;
		}

		/**
		 * Identify other possible exits at a junction to determine if there's a fork.
		 * A fork exists when there are additional exits that are:
		 * - Not the path we just came from (prevDirection)
		 * - Not the path we're about to take (direction)
		 */
		const otherExits = exits.filter(
			exit =>
				exit.destination.properties.id !== prevDirection?.properties.id &&
				exit.destination.properties.id !== direction.properties.destination,
		);
		if (otherExits.length === 0) {
			return null;
		}

		const relativeBearingAngle = prevDirection
			? -differenceBetweenAngles(prevDirection.properties.angle!, direction.properties.angle!)
			: 0;

		if (Math.abs(relativeBearingAngle) >= BEARING_CHANGE_THRESHOLD) {
			return {
				type: 'Turn',
				bearing: getRelativeBearing(relativeBearingAngle, BEARING_CHANGE_THRESHOLD),
				referencePosition: 'At',
			} as TDirectionInstructionAction;
		}

		return null;
	}

	/**
	 * Serializes the directions to JSON.
	 * All getter properties are evaluated and included in the serialized output.
	 *
	 * @returns An object representing the directions with all properties evaluated.
	 */
	toJSON() {
		return {
			id: this.id,
			directions: this.#directions,
			__type: 'directions',
			from: this.departure,
			to: this.destination,
		};
	}
}
