import { Logger, SafeStorage, debounce } from '@packages/internal/common';
import { fetch } from '../utils/fetch';

// prettier-ignore
/**
 * Valid track-analytics API contexts. These should match the expected values of that endpoint or the requests will fail.
 * If a set context is not in this list, it will default to the first VALID_CONTEXTS value.
 */
export const VALID_CONTEXTS = [ 'websdk', 'web', 'webv2', 'kiosk-v2', 'mobile', 'iossdk', 'androidsdk', 'reactnativesdk', 'gen7', 'bespoke', 'reactsdk' ] as const;
type ValidContext = (typeof VALID_CONTEXTS)[number];
export const DEFAULT_CONTEXT = VALID_CONTEXTS[0];

export const ANALYTICS_URL = 'https://api-gateway.mappedin.com/track-analytics/a/';

const DENY_WATCH_POSITION = 'deny-watchposition';
const LOAD_MAPVIEW = 'load-mapview';
const LOAD_MAPDATA = 'load-mapdata';
const CHANGE_LANGUAGE = 'change-language';
const QUERY_DIRECTION = 'query-directions';
export const FOUND_POSITION = 'found-position';
export const FOUND_FLOOR = 'found-floor';
type BlueDotEvents = typeof FOUND_FLOOR | typeof FOUND_POSITION;

export class AnalyticsInternal {
	// keep track of the options that's been set
	#options: Readonly<AnalyticsOptions> = Object.freeze({});
	#state: AnalyticState;
	private oneTimeEventsSent: Set<string> = new Set();

	constructor() {
		this.#state = this.#getStateWithOption(this.#options);
	}

	init(options: AnalyticsUpdateOptions) {
		this.reset();
		this.updateStateWithOptions(options);
	}

	/**
	 * Reset state and options
	 */
	reset() {
		this.#options = {};
		this.#state = this.#getStateWithOption({});
		this.oneTimeEventsSent.clear();
	}

	private updateStateWithOptions(options: AnalyticsUpdateOptions) {
		this.#options = Object.freeze(Object.assign({}, this.#options, options));
		this.#state = this.#getStateWithOption(options);
		this.#saveStateToStorage(this.#state);

		return this.#state;
	}

	#saveStateToStorage(state: Partial<Pick<AnalyticState, 'sessionId' | 'deviceId'>>) {
		if (state.sessionId) {
			SafeStorage.getInstance().saveSessionData('id', state.sessionId);
		}
		if (state.deviceId) {
			SafeStorage.getInstance().saveLocalData('deviceId', state.deviceId);
		}
	}

	#getStateWithOption(options: AnalyticsOptions) {
		const baseUri = options.baseUri ?? ANALYTICS_URL;

		return {
			baseUri,
			analyticsBaseUrl: getBaseUrl({ ...options, baseUri }),
			version: process?.env?.npm_package_version || 'v6',
			deviceId: options.deviceId || getDeviceIDFromStorage(),
			accessToken: options.accessToken,
			mapId: options.mapId,
			sessionId: options.sessionId || getSessionIDFromStorage(),
			platformString: options.platformString || '',
			context: getValidContext(options.context) || DEFAULT_CONTEXT,
			geolocationMode: false,
			sendEvents: options.sendEvents ?? true,
			logEvents: options.logEvents ?? false,
			noAuth: options.noAuth || false,
			key: options.key,
			secret: options.secret,
		} satisfies AnalyticState as AnalyticState;
	}

	updateState(update: UpdateStateParam) {
		const updatedState: Partial<AnalyticState> = {
			...update,
		};
		if (updatedState.mapId) {
			updatedState.analyticsBaseUrl = getBaseUrl({ ...this.#state, mapId: updatedState.mapId });
		}
		if (updatedState.context) {
			updatedState.context = getValidContext(updatedState.context);
		}
		if (updatedState.baseUri) {
			updatedState.analyticsBaseUrl = getBaseUrl({ baseUri: updatedState.baseUri, mapId: this.#state.mapId });
		}
		Object.assign(this.#state, updatedState);
		this.handleStateUpdate(update);
		this.#saveStateToStorage(updatedState);
	}

	// side effects of state updated
	private handleStateUpdate(update: UpdateStateParam) {
		if (update.userPosition?.floorLevel) {
			this.sendBlueDotEvents('found-floor');
		} else if (update.userPosition) {
			this.sendBlueDotEvents('found-position');
		}
	}

	get authReady() {
		const { key, secret, accessToken } = this.getState();

		return !!(key && secret) || !!accessToken;
	}

	getState() {
		return this.#state;
	}

	private getContext() {
		return this.getState().context || this.#options.context || DEFAULT_CONTEXT;
	}

	private getSessionId() {
		return this.getState().sessionId || this.#options.sessionId || getSessionIDFromStorage();
	}

	private getDeviceId() {
		return this.getState().deviceId || this.#options.deviceId || getDeviceIDFromStorage();
	}

	private sendAnalyticEvent(target: string, data: Record<string, any> = {}) {
		const state = this.getState();
		const url = buildUrl(state.analyticsBaseUrl, target);

		const headers: HeadersInit = {
			'mi-context': this.getContext(),
			'mi-session': this.getSessionId(),
			'mi-device': this.getDeviceId(),
		} satisfies AnalyticsHeaders;

		if (state.logEvents) {
			Logger.log('Analytics.track', url, headers);
		}

		if (!state.sendEvents) {
			// Turns off analytics track events for internal testing
			return Promise.resolve();
		}
		if (!this.authReady) return Logger.warn('Auth needs to be ready to send analytics.');

		const jsonData = { ...data, g: state.geolocationMode, sdk_version: state.version };
		if (state.userPosition) {
			Object.assign(jsonData, state.userPosition);
		}

		headers['Content-Type'] = 'application/json';

		if (state.accessToken) {
			headers['Authorization'] = `Bearer ${state.accessToken}`;
		} else if (!state.noAuth) {
			if (state.secret && state.key) {
				headers['x-mappedin-key'] = btoa(state.key);
				headers['x-mappedin-secret'] = btoa(state.secret);
			}
		}

		return fetch(url, {
			method: 'POST',
			mode: 'cors',
			headers,
			body: JSON.stringify(jsonData),
		});
	}

	capture<T extends keyof CaptureEventsPayloadMap>(
		eventName: T,
		query: CaptureEventsPayloadMap[T],
	): Promise<Response> | Promise<void>;
	capture<T extends keyof CaptureEventsPayloadMap | (string & NonNullable<unknown>)>(
		target: T,
		query: T extends keyof CaptureEventsPayloadMap ? CaptureEventsPayloadMap[T] : Record<string, any>,
	): Promise<Response> | Promise<void>;
	capture<T extends keyof CaptureEventsPayloadMap | string>(
		target: T,
		query: T extends keyof CaptureEventsPayloadMap ? CaptureEventsPayloadMap[T] : Record<string, any>,
	) {
		if (USER_CANNOT_SEND.includes(target)) {
			Logger.warn(`${target} event is sent internally.`);

			return Promise.resolve();
		}
		const eventName = RESERVED_EVENTS.includes(target as keyof CaptureEventsPayloadMap) ? target.slice(1) : target; // get rid of '$'

		return this.sendAnalyticEvent(eventName, query as Record<string, any>);
	}

	/**
	 * @internal
	 */
	sendGetMapDataEvent(payload: { parseDuration: number; downloadDuration: number; viewId?: string }) {
		return this.sendAnalyticEvent(LOAD_MAPDATA, payload);
	}

	sendChangeLanguageEvent(payload: { fromLanguage: string }) {
		return this.sendAnalyticEvent(CHANGE_LANGUAGE, payload);
	}

	sendWatchPositionDenied() {
		return this.sendAnalyticEvent(DENY_WATCH_POSITION);
	}

	/**
	 * @internal
	 */
	sendMapViewLoadedEvent({
		firstRenderDuration,
		dimension,
		collisionWorkerDisabled,
		outdoorsWorkerDisabled,
	}: {
		firstRenderDuration: number;
		dimension: { height: number; width: number };
		/**
		 * True when setWorkerURL is set, but the worker file cannot be found, so we disable the collision worker and use sync collision detection instead
		 */
		collisionWorkerDisabled: boolean;
		/**
		 * True when setWorkerURL is set, but the worker file cannot be found, so we disable the outdoor context
		 */
		outdoorsWorkerDisabled: boolean;
	}) {
		return this.sendAnalyticEvent(LOAD_MAPVIEW, {
			type: '3d',
			firstRenderDuration,
			width: dimension.width,
			height: dimension.height,
			devicePixelRatio: globalThis.devicePixelRatio || 1,
			collisionWorkerDisabled,
			outdoorsWorkerDisabled,
		});
	}

	/**
	 * @internal
	 */
	sendGetDirectionsEvent = debounce((start: string, end: string, accessible?: boolean) => {
		const payload: Record<string, any> = {
			start: start,
			end: end,
			type: 'location_to_location',
		};

		if (accessible !== undefined) {
			payload.accessible = accessible;
		}

		return this.sendAnalyticEvent(QUERY_DIRECTION, payload);
	}, 2500);

	sendBlueDotEvents(event: BlueDotEvents) {
		if ((event === 'found-floor' || event === 'found-position') && this.oneTimeEventsSent.has(event)) {
			return;
		}
		this.oneTimeEventsSent.add(event);

		return this.sendAnalyticEvent(event);
	}

	// TODO: cancel all outgoing requests
	// destroy() {
	// }
}

function getBaseUrl({ mapId, baseUri = ANALYTICS_URL }: { mapId?: string; baseUri: string }) {
	return `${baseUri}${mapId}/`;
}

function getDeviceIDFromStorage(): string {
	return SafeStorage.getInstance().loadLocalData('deviceId');
}

function getSessionIDFromStorage(): string {
	return SafeStorage.getInstance().loadSessionData('id');
}

function buildUrl(baseUrl: string, target: string) {
	return baseUrl + target;
}

function getValidContext(context?: string) {
	return VALID_CONTEXTS.find(c => c === context);
}

type UpdateStateParam = Partial<
	Pick<
		AnalyticState,
		| 'geolocationMode'
		| 'context'
		| 'logEvents'
		| 'userPosition'
		| 'mapId'
		| 'sendEvents'
		| 'logEvents'
		| 'accessToken'
		| 'sessionId'
		| 'deviceId'
	>
>;

type CaptureEventsPayloadMap = {
	'$select-location': {
		id: string;
	};
	'$select-category': {
		id: string;
	};
	'$query-suggest': {
		query: string;
		suggestions?: string[];
	};
	'$query-search': {
		query: string;
		hits?: string[];
	};
};

// keep this up-to-date with CaptureEventsPayloadMap
const RESERVED_EVENTS = ['$select-location', '$select-category', '$query-suggest', '$query-search'] as const;
const USER_CANNOT_SEND = [LOAD_MAPVIEW, LOAD_MAPDATA, QUERY_DIRECTION, DENY_WATCH_POSITION, CHANGE_LANGUAGE];

export const analyticsInternal = new AnalyticsInternal();

export class Analytics {
	#internal: AnalyticsInternal;

	/**
	 * @internal
	 */
	constructor(internalAnalytics: AnalyticsInternal) {
		this.#internal = internalAnalytics;
		this.capture = this.#internal.capture.bind(this.#internal);
		this.updateState = this.#internal.updateState.bind(this.#internal);
	}

	/**
	 * Captures an analytic event with a custom target and query payload.
	 *
	 * @param target - The event name or target can be one of: `$select-location`, `$select-category`, `$query-suggest`, `$query-search`
	 * @param query - The payload associated with the event.
	 * @returns A promise that resolves to the server response or void.
	 */
	capture: typeof AnalyticsInternal.prototype.capture;

	/**
	 * Updates the analytics state with the provided parameters.
	 * @param update - The state parameters to update.
	 */
	updateState: (update: TAnalyticsUpdateState) => void;

	/**
	 * Returns the current analytics state.
	 * @internal
	 * @returns the current analytics state
	 */
	getState() {
		// For privacy, discard sessionId and deviceId from the public state
		// eslint-disable-next-line @typescript-eslint/no-unused-vars
		const { sessionId, deviceId, ...publicState } = this.#internal.getState();

		return publicState;
	}

	// For user privacy reasons, keep these functions private and undiscoverable.
	// @ts-ignore intentionally unused method
	private getContext() {
		return this.#internal['getContext']();
	}
	// @ts-ignore intentionally unused method
	private getSessionId() {
		return this.#internal['getSessionId']();
	}
	// @ts-ignore intentionally unused method
	private getDeviceId() {
		return this.#internal['getDeviceId']();
	}
}

type AnalyticsUserPosition = {
	bluedotTimestamp: number;
	latitude: number;
	longitude: number;
	floorLevel?: number;
	accuracy?: number;
};

type AnalyticsHeaders = {
	'mi-context': string;
	'mi-session': string;
	'mi-device': string;
	Authorization?: string;
};

type AnalyticsAuth = {
	/** The API key for authentication. */
	key?: string;
	/** The API secret for authentication. */
	secret?: string;
	/** The access token for authentication. */
	accessToken?: string;
};

export type AnalyticState = {
	version: string;
	/** The platform string to be included in analytics. */
	platformString: string;
	/** The base URI for the analytics endpoint. */
	baseUri: string;
	/** The base URI with mapId appended. */
	analyticsBaseUrl: string;
	/** Flag to disable authentication. */
	noAuth: boolean;
	/** Flag to enable geolocation mode. */
	geolocationMode: boolean;
	/** @internal The device ID to be used for analytics. */
	deviceId: string;
	/** @internal The session ID to be used for analytics. */
	sessionId: string;
	/** The context in which the analytics are being used. */
	context: ValidContext;
	/** The last known user position. */
	userPosition?: AnalyticsUserPosition;
	/** The ID of the map to be used for analytics. */
	mapId?: string;
	/** Flag to enable logging of events. */
	logEvents: boolean;
	/** Flag to enable sending of events. */
	sendEvents: boolean;
} & AnalyticsAuth;

type AnalyticsOptions = Partial<
	Omit<AnalyticState, 'version' | 'analyticsBaseUrl' | 'geolocationMode' | 'userPosition'>
>;

export type AnalyticsUpdateOptions = Omit<AnalyticsOptions, keyof AnalyticsAuth> &
	(
		| (Required<Pick<AnalyticsAuth, 'key' | 'secret'>> & Partial<Pick<AnalyticsAuth, 'accessToken'>>)
		| (Required<Pick<AnalyticsAuth, 'accessToken'>> & Partial<Pick<AnalyticsAuth, 'key' | 'secret'>>)
	);

/**
 * Options for updating the current state of analytics.
 * @interface
 */
export type TAnalyticsUpdateState = Pick<
	Partial<AnalyticState>,
	'logEvents' | 'sendEvents' | 'baseUri' | 'accessToken'
>;
