import type { All3DTypes, ImagePlacementOptions, RendererState } from '../../renderer';
import { Geometry3DComponents } from '../../entities';
import type { MeshComponent } from '../../components/mesh';
import {
	AxesHelper,
	DoubleSide,
	Frustum,
	MathUtils,
	Mesh,
	MeshLambertMaterial,
	PlaneGeometry,
	SphereGeometry,
	SRGBColorSpace,
	Vector2,
	Vector3,
	type Texture,
	Matrix4,
} from 'three';
import { TextureLoader } from '../../utils/texture-loader';
import type { RendererCore } from '../..';
import type { StyleComponent } from '../../components/styles/style';
import { PubSub, round } from '@packages/internal/common';
import { EntityMesh, type ImageComponent } from '../../components/image';
import type { Position as GeoJsonPosition } from 'geojson';
import type { Rectangle } from '../../utils/largest-interior-rectangle';
import { findLargestInteriorRectangle } from '../../utils/largest-interior-rectangle';
import { Rectangle as QuadTreeRectangle } from '@packages/internal/quad-tree';
import { RENDER_LAYERS } from '../../utils/constants';
const TWO_PI = Math.PI * 2;
const HALF_PI = Math.PI / 2;
const ONE_AND_A_HALF_PI = Math.PI * 1.5;
const ROUNDED_HALF_PI = round(HALF_PI, 2);
const ROUNDED_ONE_AND_A_HALF_PI = round(ONE_AND_A_HALF_PI, 2);
export const DEFAULT_VERTICAL_OFFSET = 0.01;

const SHOW_DEBUG_IMAGE_CONTAINERS = false;

export class ImageSystem extends PubSub<{
	'image-loaded': void;
}> {
	private rendererState: RendererState;
	private convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'];
	private projectToScreen: (projectVector: Vector2, position: Vector3) => void;
	private initialBearing = 0;
	private naturalBearing = 0;
	private imagePlacementOptions: ImagePlacementOptions;
	private cameraFrustum = new Frustum();
	constructor(
		rendererState: RendererState,
		convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'],
		projectToScreen: (projectVector: Vector2, position: Vector3) => void,
		initialBearing = 0,
		naturalBearing = 0,
		imagePlacementOptions: ImagePlacementOptions = {},
	) {
		super();
		this.rendererState = rendererState;
		this.convertTo3DMapPosition = convertTo3DMapPosition;
		this.projectToScreen = projectToScreen;
		this.initialBearing = initialBearing;
		this.naturalBearing = naturalBearing;
		this.imagePlacementOptions = imagePlacementOptions;
	}

	private createImageMesh(
		entityId: string | number,
		url: string,
		polygonImage: { width: number; height: number; rotation?: number; position?: GeoJsonPosition },
		verticalOffset = 0,
		position: GeoJsonPosition,
		polygonPoints: { x: number; y: number }[],
	) {
		const originalRectangle: Rectangle = {
			width: polygonImage.width,
			height: polygonImage.height,
			center: this.convertTo3DMapPosition(position),
			rotation: polygonImage.rotation ?? 0,
		};

		const imagePlacementMode = this.imagePlacementOptions.mode ?? 'default';

		const rectangle = (() => {
			switch (imagePlacementMode) {
				case 'fit-nearest-90':
					// Calculate the bearing difference and normalize to 0-180, so we can see if it's close to 90 degrees
					let bearingDiff = (this.initialBearing - this.naturalBearing + 360) % 180;
					if (bearingDiff >= 45 && bearingDiff <= 135) {
						return findLargestInteriorRectangle(
							originalRectangle,
							polygonPoints,
							originalRectangle.rotation + 90,
							this.imagePlacementOptions,
						);
					} else {
						return originalRectangle;
					}
				case 'fit-initial-bearing':
					return findLargestInteriorRectangle(
						originalRectangle,
						polygonPoints,
						this.initialBearing,
						this.imagePlacementOptions,
					);
				case 'default':
				case 'none':
				default:
					return originalRectangle;
			}
		})();

		if (SHOW_DEBUG_IMAGE_CONTAINERS && rectangle.polygonCenter) {
			const debugCenter = new Mesh(new SphereGeometry(1), new MeshLambertMaterial({ color: 0x00ff00 }));
			debugCenter.position.set(rectangle.polygonCenter.x, rectangle.polygonCenter.y, verticalOffset);
			this.rendererState.entityScene.add(debugCenter);
		}

		const material = new MeshLambertMaterial({
			transparent: true,
			/**
			 * Disable writing to depth buffer by default. This avoids discarding pixels that are further from the camera but rendered later.
			 * In Three.js, the rendering order is sorted by distance to the camera, ensuring that farther items are rendered first. This prevents them
			 * from being discarded by the depth buffer. However, this default approach does not account for cases where two meshes clip through each other.
			 * Leaving depthWrite disabled helps handle these scenarios more effectively. When changing this property later ensure to test this scenario to get the best result.
			 * Generally, it's recommended to disable depthWrite when rendering 2D planes that should always appear on top of other objects.
			 */
			depthWrite: false,
			side: DoubleSide,
		});

		const geo = new PlaneGeometry(rectangle.width, rectangle.height);
		const imageMesh = new EntityMesh(geo);
		imageMesh.visible = false;
		imageMesh.renderOrder = RENDER_LAYERS.IMAGE_MESH;

		this.getOrDownloadImage(url)?.then(texture => {
			material.map = texture;
			material.needsUpdate = true;
			material.map.colorSpace = SRGBColorSpace;
			imageMesh.visible = true;
			this.publish('image-loaded');
		});

		imageMesh.userData.entityId = entityId;
		imageMesh.material = material;

		if (SHOW_DEBUG_IMAGE_CONTAINERS) {
			const containerMesh = new Mesh(
				new PlaneGeometry(polygonImage.width, polygonImage.height),
				new MeshLambertMaterial({ color: 'gray' }),
			);
			imageMesh.add(containerMesh);
			const axisHelper = new AxesHelper();
			imageMesh.add(axisHelper);
			axisHelper.scale.set(10, 10, 10);
		}

		// Use rectangle center instead of original position
		imageMesh.position.set(rectangle.center.x, rectangle.center.y, verticalOffset + DEFAULT_VERTICAL_OFFSET);

		// Apply rotation, ensuring it's in the range 0 - 2pi
		imageMesh.rotation.z = (-MathUtils.degToRad(rectangle.rotation) + TWO_PI) % TWO_PI;

		return imageMesh;
	}

	private getOrDownloadImage(url: string) {
		if (this.imageLoadingCache.has(url)) {
			return this.imageLoadingCache.get(url)?.promise;
		} else {
			const promise = new Promise<Texture>(resolve => {
				TextureLoader.getLoader().load(url, texture => {
					const state = this.imageLoadingCache.get(url);
					if (state && !state.loaded) {
						state.loaded = true;
					}
					resolve(texture);
				});
			});
			this.imageLoadingCache.set(url, { loaded: false, promise });

			return promise;
		}
	}

	/**
	 * We need to track the loaded state separately from the promise since we can't check
	 * the resolution state of a promise after it settles. This allows components to check
	 * if an image has finished loading without having to await the promise.
	 */
	imageLoadingCache = new Map<string, { loaded: boolean; promise: Promise<Texture> }>();

	private flipIfNeeded(meshComponent: MeshComponent | ImageComponent, cameraRotationRadians: number) {
		if (meshComponent.imageMesh == null) return;

		// Negate camera rotation to match our coordinate system
		const normalizedCameraRotation = -cameraRotationRadians;
		const imageRotation = meshComponent.imageMesh.rotation.z;

		// Calculate relative angle between image and camera
		const angle = round((imageRotation - normalizedCameraRotation + TWO_PI) % TWO_PI, 2);

		// Round the angles to avoid floating point precision issues
		if (angle > ROUNDED_HALF_PI && angle < ROUNDED_ONE_AND_A_HALF_PI) {
			meshComponent.imageMesh.rotation.z = (imageRotation + Math.PI) % TWO_PI;
		}
	}

	currentImageScreenBBoxes: QuadTreeRectangle[] = [];

	private viewProjectionMatrix = new Matrix4();

	update(cameraRotationRadians: number) {
		this.currentImageScreenBBoxes = [];
		this.viewProjectionMatrix
			.identity()
			.multiply(this.rendererState.cameraObject.projectionMatrix)
			.multiply(this.rendererState.cameraObject.matrixWorldInverse);
		this.cameraFrustum.setFromProjectionMatrix(this.viewProjectionMatrix);

		let entity: All3DTypes | undefined;
		let meshComponent: MeshComponent | ImageComponent | undefined;
		let styleComponent: StyleComponent | undefined;
		for (let entityId of this.rendererState.geometry3DIdsInScene) {
			entity = this.rendererState.geometry3DMap.get(entityId);

			if (entity?.type === 'image') {
				meshComponent = entity.components[Geometry3DComponents.Mesh] as ImageComponent;
				styleComponent = entity.components[Geometry3DComponents.Style] as StyleComponent;
				if (styleComponent.url && meshComponent.imageMesh == null) {
					// Convert coordinates to {x, y} points
					const point = this.convertTo3DMapPosition(meshComponent.feature.geometry.coordinates);
					const pos = { x: point.x, y: point.y };

					meshComponent.imageMesh = this.createImageMesh(
						entity.id,
						styleComponent.url,
						meshComponent.feature.properties,
						meshComponent.feature.properties.verticalOffset,
						meshComponent.feature.geometry.coordinates,
						[pos],
					);
					meshComponent.mesh.add(meshComponent.imageMesh);
					meshComponent.position.copy(meshComponent.imageMesh.position);
				}

				if (meshComponent.imageMesh) {
					// now rotate so it tries to face the camera
					if (styleComponent.flipImageToFaceCamera) {
						this.flipIfNeeded(meshComponent, cameraRotationRadians);
					}
					if (meshComponent.imageMesh.material instanceof MeshLambertMaterial && styleComponent.dirty) {
						meshComponent.imageMesh.material.opacity = styleComponent.opacity;
					}
				}
			} else if (entity?.type === 'geometry') {
				meshComponent = entity.components[Geometry3DComponents.Mesh] as MeshComponent;
				styleComponent = entity.components[Geometry3DComponents.Style] as StyleComponent;

				if (
					styleComponent.showImage &&
					meshComponent.feature.properties?.image &&
					meshComponent.imageMesh == null &&
					meshComponent.visible &&
					meshComponent.mesh != null &&
					meshComponent.mesh.parent != null &&
					meshComponent.feature.properties?.image.position != null &&
					meshComponent.feature.geometry.coordinates != null &&
					meshComponent.feature.geometry.coordinates.length > 0
				) {
					// Convert coordinates to {x, y} points
					const points = meshComponent.feature.geometry.coordinates[0].map(coord => {
						const pos = this.convertTo3DMapPosition(coord);

						return { x: pos.x, y: pos.y };
					});

					const mesh = this.createImageMesh(
						entityId,
						meshComponent.feature.properties?.image.path,
						meshComponent.feature.properties?.image,
						styleComponent.height,
						meshComponent.feature.properties?.image.position,
						points,
					);
					meshComponent.mesh.parent.add(mesh);
					meshComponent.imageMesh = mesh;
				}

				if (meshComponent.imageMesh) {
					if (!meshComponent.visible) {
						meshComponent.imageMesh.visible = false;
					} else if (styleComponent.showImage && !meshComponent.imageMesh.visible) {
						if (styleComponent.url) {
							const state = this.imageLoadingCache.get(styleComponent.url);
							if (state && state.loaded) {
								meshComponent.imageMesh.visible = true;
							}
						} else {
							meshComponent.imageMesh.visible = true;
						}
					} else if (!styleComponent.showImage && meshComponent.imageMesh.visible) {
						meshComponent.imageMesh.visible = false;
					}

					// now rotate so it tries to face the camera
					if (styleComponent.flipImageToFaceCamera) {
						this.flipIfNeeded(meshComponent, cameraRotationRadians);
					}
					if (styleComponent.enableImageCollisions === true) {
						this.updateImageScreenBBoxes(meshComponent);
					}
				}
			}
		}
	}
	updateImageScreenBBoxes(meshComponent: MeshComponent | ImageComponent) {
		if (meshComponent.imageMesh == null) return;
		// let's project the bounding box onto the screen and store as polygon
		const bbox = meshComponent.imageMesh.geometry.boundingBox;
		if (bbox != null) {
			const worldPosition = new Vector3();
			meshComponent.imageMesh.getWorldPosition(worldPosition);
			// cheap check to see if the image is in the camera frustum
			if (!this.cameraFrustum.containsPoint(worldPosition)) {
				return;
			}
			const centerScreen = new Vector2();
			this.projectToScreen(centerScreen, worldPosition);

			// Get all 8 corners of the bounding box in local space
			const corners = [
				new Vector3(bbox.min.x, bbox.min.y, bbox.min.z),
				new Vector3(bbox.max.x, bbox.min.y, bbox.min.z),
				new Vector3(bbox.min.x, bbox.max.y, bbox.min.z),
				new Vector3(bbox.max.x, bbox.max.y, bbox.min.z),
				new Vector3(bbox.min.x, bbox.min.y, bbox.max.z),
				new Vector3(bbox.max.x, bbox.min.y, bbox.max.z),
				new Vector3(bbox.min.x, bbox.max.y, bbox.max.z),
				new Vector3(bbox.max.x, bbox.max.y, bbox.max.z),
			];

			// Transform all corners to world space and project to screen
			const screenCorners: Vector2[] = [];
			for (const corner of corners) {
				const worldCorner = corner.clone().applyMatrix4(meshComponent.imageMesh.matrixWorld);
				const screenCorner = new Vector2();
				this.projectToScreen(screenCorner, worldCorner);
				screenCorners.push(screenCorner);
			}

			// Find the actual screen bounds by taking min/max of all projected corners
			let minX = Infinity;
			let minY = Infinity;
			let maxX = -Infinity;
			let maxY = -Infinity;
			for (const screenCorner of screenCorners) {
				minX = Math.min(minX, screenCorner.x);
				minY = Math.min(minY, screenCorner.y);
				maxX = Math.max(maxX, screenCorner.x);
				maxY = Math.max(maxY, screenCorner.y);
			}

			const rectangle = new QuadTreeRectangle(centerScreen.x, centerScreen.y, maxX - minX, maxY - minY);
			this.currentImageScreenBBoxes.push(rectangle);
		}
	}
}
