import type { Position } from 'geojson';

/**
 * Calculates the intersection point of two line segments defined by their endpoints.
 *
 * Modified line intercept math by Paul Bourke http://paulbourke.net/geometry/pointlineplane/
 * Determines the intersection point of two line segments.
 * Return null if the lines don't intersect.
 *
 * @param {Position} line1Point1 - First endpoint of the first line segment ([longitude, latitude]).
 * @param {Position} line1Point2 - Second endpoint of the first line segment ([longitude, latitude]).
 * @param {Position} line2Point1 - First endpoint of the second line segment ([longitude, latitude]).
 * @param {Position} line2Point2 - Second endpoint of the second line segment ([longitude, latitude]).
 * @hidden
 * @returns {Position | null} The intersection point as [longitude, latitude] if it exists on both segments, otherwise null.
 */
export function getLineIntersection(
	line1Point1: Position,
	line1Point2: Position,
	line2Point1: Position,
	line2Point2: Position,
) {
	const [lon1, lat1] = line1Point1;
	const [lon2, lat2] = line1Point2;
	const [lon3, lat3] = line2Point1;
	const [lon4, lat4] = line2Point2;

	// Check if any of the lines are of zero length
	if ((lat1 === lat2 && lon1 === lon2) || (lat3 === lat4 && lon3 === lon4)) {
		return null;
	}

	// Calculate denominator to check for parallel lines
	const denominator = (lon4 - lon3) * (lat2 - lat1) - (lat4 - lat3) * (lon2 - lon1);
	if (denominator === 0) {
		return null; // Lines are parallel
	}

	// Calculate the intersection parameters
	const ua = ((lat4 - lat3) * (lon1 - lon3) - (lon4 - lon3) * (lat1 - lat3)) / denominator;
	const ub = ((lat2 - lat1) * (lon1 - lon3) - (lon2 - lon1) * (lat1 - lat3)) / denominator;

	// Check if intersection is within the segments
	if (ua < 0 || ua > 1 || ub < 0 || ub > 1) {
		return null;
	}

	// Calculate the exact intersection coordinates
	const lat = lat1 + ua * (lat2 - lat1);
	const lon = lon1 + ua * (lon2 - lon1);

	return [lon, lat] as Position;
}
