import { CONNECTION_TYPE, type Connection } from '@mappedin/mvf-connections';
import type { FloorId, GeometryFeature } from '@mappedin/mvf-core';
import { type NavigationFlagDeclaration, WELL_KNOWN_FLAGS, hasFlag } from '@mappedin/mvf-navigation-flags';
import type { ParsedMVF } from '../../types/bundle.js';
import type { EntranceFeature } from '../../types/core.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Connections = Pick<MVFv2_STANDARD_MVFv3, 'connections' | 'navigationFlags' | 'nodes' | 'geometry'>;
export type V2Connections = Pick<ParsedMVF, 'connection.json' | 'entrance'>;
export type V2Connection = V2Connections['connection.json'][number];

export function buildConnections(
	v3: V3Connections,
	{ nodesByGeometryId }: Pick<BuilderOptions, 'nodesByGeometryId'>,
): V2Connections {
	const v2: V2Connections = {
		'connection.json': [],
		entrance: {},
	};

	const ACCESSIBLE_FLAG = v3.navigationFlags?.[WELL_KNOWN_FLAGS.accessible];
	const OUTDOORS_FLAG = v3.navigationFlags?.[WELL_KNOWN_FLAGS.outdoors];
	const geometryFeatureById = new Map(
		Object.values(v3.geometry ?? {})
			.flatMap((floor) => floor?.features ?? [])
			.map((f) => [f.properties.id, f]),
	);

	for (const connection of v3.connections ?? []) {
		// Convert line string doors to entrances, except when they transition
		// between floors (as these then become "real" connections). Keep CMS
		// style point doors
		if (isLineDoor(connection, geometryFeatureById) && !doesConnectionTransitionFloors(connection)) {
			const entrances = buildEntranceFeaturesFromConnection(
				connection,
				geometryFeatureById,
				nodesByGeometryId,
				OUTDOORS_FLAG,
			);
			for (const [floorId, entrance] of entrances) {
				if (!v2.entrance[floorId]) {
					v2.entrance[floorId] = {
						type: 'FeatureCollection',
						features: [],
					};
				}
				v2.entrance[floorId].features.push(entrance);
			}
			continue;
		}

		// A connection is accessible if it has the accessible flag set for some entrance and exit
		const accessible =
			ACCESSIBLE_FLAG != undefined &&
			connection.entrances.some((e) => hasFlag(e, ACCESSIBLE_FLAG)) &&
			connection.exits.some((e) => hasFlag(e, ACCESSIBLE_FLAG));

		// Asymmetric connections are not supported in V2, so we just give it all the nodes
		const nodes = Array.from(
			new Set(
				[
					...connection.entrances.map((e) => nodesByGeometryId.get(e.geometryId)),
					...connection.exits.map((e) => nodesByGeometryId.get(e.geometryId)),
				]
					.flat()
					.filter((n) => n != undefined),
			),
		);

		const v2Connection: V2Connection = {
			id: connection.id,
			externalId: connection.details?.externalId ?? '',
			type: connection.type,
			accessible,
			nodes,
		};
		if (connection.details?.name) {
			v2Connection.details = { name: connection.details.name };
		}
		if (connection.extra) {
			v2Connection.extra = connection.extra;
		}

		v2['connection.json'].push(v2Connection);
	}
	return v2;
}

export function isLineDoor(connection: Connection, geometryFeatureById: Map<string, GeometryFeature>): boolean {
	return (
		connection.type == CONNECTION_TYPE.door &&
		connection.entrances.every((e) => {
			const feature = geometryFeatureById.get(e.geometryId);
			// It's not clear what to do in MVFv2 if this was a multi-line string. Probably not multiple identical entrances.
			return feature?.geometry.type === 'LineString';
		})
	);
}

export function doesConnectionTransitionFloors(connection: Connection): boolean {
	if (connection.entrances.length === 0 || connection.exits.length === 0) {
		// If a connection can't be traversed, it can't transition floors
		return false;
	}
	const visitedFloors = new Set([
		...connection.entrances.map((e) => e.floorId),
		...connection.exits.map((e) => e.floorId),
	]);
	return visitedFloors.size >= 2;
}

/**
 * This builds one or more entrances from a connection. It will only produce anything if isLineDoor
 * is true for this connection, but it doesn't rely on that function.
 *
 * It will produce tuples of [FloorId, EntranceFeature[]] for every entrance line string entrance in the connection
 * in the connection.
 * @param connection
 * @param geometryFeatureById
 */
export function buildEntranceFeaturesFromConnection(
	connection: Connection,
	geometryFeatureById: Map<string, GeometryFeature>,
	nodesByGeometryId: BuilderOptions['nodesByGeometryId'],
	outdoorsFlag?: NavigationFlagDeclaration,
): [FloorId, EntranceFeature][] {
	if (connection.type !== CONNECTION_TYPE.door) {
		return [];
	}

	const entrances: [FloorId, EntranceFeature][] = [];

	for (const entrance of connection.entrances) {
		const geometryFeature = geometryFeatureById.get(entrance.geometryId);
		if (geometryFeature?.geometry.type === 'LineString') {
			const entranceFeature: EntranceFeature = {
				type: 'Feature',
				geometry: geometryFeature.geometry,
				properties: {
					id: connection.id, // Entrance IDs will not be unique. Is that a problem?
					externalId: connection.details?.externalId ?? '',
					isExterior: isExterior(connection, outdoorsFlag),
				},
			};
			const details = connection.details;
			if (details) {
				entranceFeature.properties.details = {
					name: details.name,
					description: details.description,
				};
				if (details.icon) {
					entranceFeature.properties.details.images = [{ url: details.icon, altText: `${details.name} icon` }];
				}
			}

			const nodes = nodesByGeometryId.get(entrance.geometryId);
			if (nodes && nodes.length > 0) {
				entranceFeature.properties.node = nodes[0];
			}
			entrances.push([entrance.floorId, entranceFeature]);
		}
	}
	return entrances;
}

/**
 * A connection is exterior if ANY of it's entrances or exits have the well-known outdoors flag.
 *
 * This also means a connection that is fully outdoors (such as a pedestrian bridge) is also exterior.
 * @param connection
 * @param outdoorsFlag
 * @returns true if the connection goes or comes from the exterior
 */
export function isExterior(connection: Connection, outdoorsFlag?: NavigationFlagDeclaration): boolean {
	return (
		connection.entrances.some((e) => hasFlag(e, outdoorsFlag)) || connection.exits.some((e) => hasFlag(e, outdoorsFlag))
	);
}
