import type {
	EntranceCollection,
	ObstructionCollection,
	SpaceCollection,
	Polygon,
	MultiPolygon,
	FeatureCollection,
	PolygonStyle,
	LineStringStyle,
	FloorProperties as MVFFloor,
	FloorImageFeatureCollection,
	ParsedMVF,
	Feature,
	FloorTextFeatureCollection,
	SpaceProperties,
	LineString,
	Point,
	AreaCollection,
} from '@mappedin/mvf-v2';
import type { GeometryState, RendererCore, LineStyle, GroupContainerState, BBox } from '@mappedin/core-sdk';
import type { AggregatedStyleMap, WithId } from './utils';
import {
	translateToCoreStyle,
	cutEntrancesFromLineStrings,
	groupGeometryByStyle,
	getBoundingBox,
	generateImageId,
	parseFloorGap,
} from './utils';
import type { TShow3DMapOptions } from '..';
import {
	AREAS_LAYER,
	EXTERIOR_DOORS_ID,
	EXTERIOR_WALLS_ID,
	FACADES_LAYER,
	FOOTPRINT_LAYER,
	GEOMETRY_LAYER,
	IMAGES_LAYER,
	INTERIOR_DOORS_ID,
	LABELS_LAYER,
	MARKERS_LAYER,
	OCCLUSION_LAYER,
	PATHS_LAYER,
	WALLS_ID,
} from '../constants';
import { randomId } from '../utils';
import { Logger } from '@packages/internal/common';
import { Coordinate, type MapDataInternal } from '../map-data-objects';
import type { GeojsonApiMapObject } from './map-object';

type LineStringStyleWithTopColor = Omit<LineStringStyle, 'lineStrings'> & {
	id?: string;
	topColor?: string;
};

const DEFAULT_WALL_STYLE: LineStringStyleWithTopColor = {
	altitude: 0,
	height: 1,
	color: '#ffffff',
	opacity: 1,
	width: 0.15,
};

const DEFAULT_DOOR_STYLE = {
	opacity: 0.25,
	color: '#a7d1ec',
	height: 0.8,
	width: 0.05,
	altitude: 0.1,
	cap: 'square' as LineStyle['cap'],
	visible: false,
};

const DEFAULT_AREA_STYLE = {
	color: 'red',
	opacity: 0.2,
	height: DEFAULT_WALL_STYLE.height + 0.1,
	visible: true,
	borderVisible: false,
};

const MIN_DOOR_HEIGHT = 0.5;

class Layer {
	containerId: string;
	layers: Map<string, string>;
	renderer: RendererCore;
	constructor(renderer: RendererCore, containerId: string, layers = new Map()) {
		this.containerId = containerId;
		this.layers = layers;
		this.renderer = renderer;
	}

	get visible() {
		return this.renderer.getState<GroupContainerState>(this.containerId).visible;
	}

	setVisible(visible: boolean) {
		this.renderer.setState<GroupContainerState>(this.containerId, {
			visible,
		});
	}
}

let imageIdx = 0;

export class FloorObject implements MVFFloor {
	id: string;
	name?: string;
	elevation: number;
	maxHeight?: number;
	containerId: string;
	externalId: string;
	metadata?: Partial<Record<string, unknown>> | undefined;
	shortName?: string | undefined;
	subtitle?: string | undefined;
	occluderId?: number;
	private loaded = false;
	private styleMap: AggregatedStyleMap = {};
	private obstructions?: ObstructionCollection;
	private entrances?: EntranceCollection;
	private spaces?: SpaceCollection;
	private floorImages?: FloorImageFeatureCollection;
	private floorText?: FloorTextFeatureCollection;
	private areas?: AreaCollection;
	private renderer: RendererCore;
	private mapObject: GeojsonApiMapObject;
	private options: TShow3DMapOptions;
	layers: Map<string, Layer> = new Map();
	footprint?: Feature<Polygon | MultiPolygon | null, MVFFloor>;
	floorStackId: string;
	private multiFloorView?: Required<TShow3DMapOptions['multiFloorView']>;
	private mapDataInternal?: MapDataInternal;
	/** Map of styleId to array of facadeIds for styling */
	facadesByStyleId: Map<string, string[]> = new Map();
	#geoJSONBoundingBox?: BBox;
	get geoJSONBoundingBox() {
		if (this.#geoJSONBoundingBox) {
			return this.#geoJSONBoundingBox;
		}

		if (!this.footprint) {
			Logger.warn(`Attempting to get bounding box with no footprint for floor ${this.id}`);

			return undefined;
		}
		this.#geoJSONBoundingBox = getBoundingBox(this.footprint as Feature<Polygon | MultiPolygon, any>);

		return this.#geoJSONBoundingBox;
	}

	constructor(
		parentId: string,
		floor: MVFFloor,
		mapObject: GeojsonApiMapObject,
		multiFloorView: Required<TShow3DMapOptions['multiFloorView']>,
		options: TShow3DMapOptions,
		mvf?: ParsedMVF,
		styleMap?: AggregatedStyleMap,
		mapDataInternal?: MapDataInternal,
	) {
		this.mapObject = mapObject;
		this.renderer = mapObject.renderer;
		this.id = floor.id;
		this.options = options;
		const floorContainer = this.renderer.addGroupContainer(
			this.id,
			{
				visible: false,
				interactive: true,
				preloadGeometry: options.preloadFloors?.map(f => f.id).includes(floor.id),
			},
			parentId,
		);
		this.containerId = floorContainer.id.toString();
		this.externalId = floor.externalId;
		this.metadata = floor.metadata;
		this.shortName = floor.shortName;
		this.subtitle = floor.subtitle;
		this.name = floor.name;
		this.maxHeight = floor.maxHeight;
		this.elevation = floor.elevation;
		if (mvf != null && styleMap != null) {
			this.styleMap = styleMap;
			this.spaces = mvf.space[this.id]!;
			this.entrances = mvf.entrance[this.id]!;
			this.areas = mvf.area?.[this.id];
			this.obstructions = mvf.obstruction[this.id]!;
			this.floorImages = mvf.floorImages?.[this.id];
			this.floorText = mvf.enterprise?.floorText?.[this.id];
			this.footprint = mvf['floor.geojson']?.features.find(f => f.properties.id === floor.id);
		}
		this.floorStackId = parentId;
		this.multiFloorView = multiFloorView;
		this.mapDataInternal = mapDataInternal;
	}

	load = () => {
		if (this.loaded) {
			return this;
		}
		if (
			this.footprint?.geometry == null &&
			this.obstructions?.features.length === 0 &&
			this.spaces?.features.length === 0
		) {
			// floors with no spaces/obstructions are valid in mvf
			// lets warn but allow it to load and render whatever we have
			Logger.warn(`Floor ${this.id} exists in the data but contains no geometry.`);
		}
		this.loaded = true;

		const labelsLayer = this.renderer.addGroupContainer(`${LABELS_LAYER}-${this.id}`, {}, this.id);
		this.layers.set(LABELS_LAYER, new Layer(this.renderer, labelsLayer.id.toString()));

		const markersLayer = this.renderer.addGroupContainer(`${MARKERS_LAYER}-${this.id}`, {}, this.id);
		this.layers.set(MARKERS_LAYER, new Layer(this.renderer, markersLayer.id.toString()));

		const allGeometry = this.renderer.addGroupContainer(`${GEOMETRY_LAYER}-${this.id}`, {}, this.id);
		this.layers.set(GEOMETRY_LAYER, new Layer(this.renderer, allGeometry.id.toString()));

		const pathsContainer = this.renderer.addGroupContainer(`${PATHS_LAYER}-${this.id}`, {}, this.id);
		this.layers.set(PATHS_LAYER, new Layer(this.renderer, pathsContainer.id.toString()));

		const floorAltitudes = this.mapObject.getFloorStackAltitudeData(this.floorStackId);

		if (this.footprint?.geometry != null && this.footprint?.geometry.coordinates.length !== 0) {
			let style = DEFAULT_WALL_STYLE;
			if (this.obstructions != null && this.obstructions.features.length > 0) {
				const exteriorWallFeatures = this.obstructions.features.filter(
					f =>
						f.geometry.type === 'LineString' &&
						f.properties.kind?.toLowerCase() === 'wall' &&
						f.properties.footprintComponent === true,
				);

				if (exteriorWallFeatures != null && exteriorWallFeatures.length > 0) {
					style = this.styleMap[exteriorWallFeatures[0].properties?.id] as WithId<LineStringStyle>;
				}
			}

			const footprintGroup = this.renderer.addGeometryGroup(
				`Footprint-${this.id}`,
				{
					features: [
						{
							geometry: this.footprint.geometry,
							properties: {
								id: `Footprint-Floor-${this.id}`,
							},
						},
					],
				} as any,
				{
					...style,
					height:
						floorAltitudes.get(this.elevation)?.effectiveHeight ??
						parseFloorGap(this.multiFloorView?.floorGap, this.maxHeight),
					altitude: 0,
					outline: this.options.style?.outlines ?? true,
					visible: false,
					opacity: this.elevation < 0 ? 1 : 0.4,
					// we want back-facing material for underground floors
					// and front-facing material for above-ground floors
					side: this.elevation < 0 ? 'back' : 'front',
					color: this.elevation < 0 ? '#F0EEE8' : style.color,
					renderOrder: 2,
				},
				this.id,
			);

			this.layers.set(FOOTPRINT_LAYER, new Layer(this.renderer, footprintGroup.id.toString()));
		}

		if (this.spaces != null && this.spaces.features.length > 0) {
			const spaceFeatures: Feature<Polygon | LineString | Point, SpaceProperties>[] = [];
			const facadeSpaceGroups = new Map<string, Feature<Polygon | LineString | Point, SpaceProperties>[]>();

			this.spaces.features
				.filter(space => space.geometry.type === 'Polygon' && space.properties.kind?.toLowerCase() !== 'void')
				.forEach(space => {
					const facade = this.mapDataInternal?.facadesBySpaceId[space.properties.id];
					if (facade) {
						// If space is part of a facade it will be processed in its own group
						const group = facadeSpaceGroups.get(facade.id) || [];
						facadeSpaceGroups.set(facade.id, [...group, space]);
					} else {
						spaceFeatures.push(space);
					}
				});

			const spaceStyleGroups = groupGeometryByStyle(spaceFeatures, this.styleMap);

			const spaces = this.renderer.addGroupContainer(`spaces-${this.id}`, {}, allGeometry);
			let index = 0;

			for (const [style, features] of spaceStyleGroups) {
				const group = this.renderer.addGeometryGroup(
					`Spaces-${this.id}-${index}`,
					{
						features,
					} as FeatureCollection<Polygon, any>,
					translateToCoreStyle(style as PolygonStyle, this.options),
					spaces,
				);

				index++;
				const groupId = spaces.id.toString();
				this.layers.get(GEOMETRY_LAYER)!.layers.set(style.id || groupId, group.id.toString());
			}

			if (facadeSpaceGroups.size > 0) {
				const facadesLayer = this.renderer.addGroupContainer(`${FACADES_LAYER}-${this.id}`, {}, this.id);
				this.layers.set(FACADES_LAYER, new Layer(this.renderer, facadesLayer.id.toString()));

				for (const [facadeId, features] of facadeSpaceGroups) {
					// Wrap the facade geometry in a layer that is focusable. When we change the visibility of the geometry,
					// we want to keep this container visible so it can be raycasted.
					const facadeContainer = this.renderer.addGroupContainer(facadeId, { focusable: true }, facadesLayer);
					const facadeGeometryLayer = this.renderer.addGroupContainer(`${facadeId}-geometry`, {}, facadeContainer);
					const spaceFacadeStyleGroups = groupGeometryByStyle(features, this.styleMap);
					let index = 0;
					for (const [style, features] of spaceFacadeStyleGroups) {
						const geometry = this.renderer.addGeometryGroup(
							`Facades-${this.id}-${facadeId}-${index}`,
							{
								features,
							} as FeatureCollection<Polygon, any>,
							{
								...translateToCoreStyle(style as PolygonStyle, this.options),
								renderOrder: 3, // Facades need to render above other geometry to prevent opacity fighting
							},
							facadeGeometryLayer,
						);
						index++;

						const groupId = style.id || geometry.id.toString();
						// Facades can contain and share multiple styleIds
						// Use a composite key for looking up the styleId of a specific facade
						this.layers.get(FACADES_LAYER)!.layers.set(`${facadeId}-${groupId}`, geometry.id.toString());
						// Store the array of facadeIds for a given styleId
						if (!this.facadesByStyleId.has(groupId)) {
							this.facadesByStyleId.set(groupId, [facadeId]);
						} else {
							this.facadesByStyleId.get(groupId)?.push(facadeId);
						}
					}
				}
			}
		}

		if (this.obstructions != null && this.obstructions.features.length > 0) {
			const objectFeatures = this.obstructions.features.filter(
				f =>
					f.geometry.type === 'Polygon' &&
					(f.properties.kind === 'object' ||
						// MVF v3 downconversion is incorrectly typing objects as type "unknown"
						// TODO: Remove this when it's fixed in MVF and we can regenerate all MVFs
						f.properties.kind === 'unknown'),
			);

			const objectStyleGroups = groupGeometryByStyle(objectFeatures, this.styleMap);

			const objects = this.renderer.addGroupContainer(`objects-${this.id}`, {}, allGeometry);
			let index = 0;
			for (const [style, features] of objectStyleGroups) {
				const group = this.renderer.addGeometryGroup(
					`Objects-${this.id}-${index}`,
					{
						features,
					} as FeatureCollection<Polygon, any>,
					translateToCoreStyle(style as PolygonStyle, this.options),
					objects,
				);
				index++;
				const groupId = objects.id.toString();
				this.layers.get(GEOMETRY_LAYER)!.layers.set(style.id || groupId, group.id.toString());
			}

			const wallFeatures = this.obstructions.features.filter(
				f =>
					f.geometry.type === 'LineString' &&
					f.properties.kind?.toLowerCase() === 'wall' &&
					f.properties.footprintComponent === false,
			);
			const exteriorWallFeatures = this.obstructions.features.filter(
				f =>
					f.geometry.type === 'LineString' &&
					f.properties.kind?.toLowerCase() === 'wall' &&
					f.properties.footprintComponent === true,
			);

			const entrances = this.entrances;
			let wallStyle = DEFAULT_WALL_STYLE;
			let exteriorWallStyle = DEFAULT_WALL_STYLE;

			if (wallFeatures != null && wallFeatures.length > 0) {
				const style = this.styleMap[wallFeatures[0].properties?.id] as WithId<LineStringStyle>;
				if (style != null) {
					wallStyle = { ...style };
				}
				exteriorWallStyle = wallStyle; // exterior walls default to interior wall style

				const { walls, entrances: entrancesCollection } = cutEntrancesFromLineStrings(
					wallFeatures,
					entrances?.features || [],
				);

				const doorHeight = Math.max(wallStyle.height - 0.11, MIN_DOOR_HEIGHT);
				const doorStyle = { ...DEFAULT_DOOR_STYLE, height: doorHeight };
				const interiorDoors = this.renderer.addGeometryGroup(
					`InteriorDoors-${this.id}`,
					entrancesCollection,
					doorStyle,
					allGeometry,
				);
				this.layers.get(GEOMETRY_LAYER)!.layers.set(INTERIOR_DOORS_ID, interiorDoors.id.toString());

				const interiorWalls = this.renderer.addGeometryGroup(
					`Walls-${this.id}`,
					{
						features: [walls],
					} as any,
					translateToCoreStyle(wallStyle as LineStringStyle, this.options),
					allGeometry,
				);

				this.layers.get(GEOMETRY_LAYER)!.layers.set(WALLS_ID, interiorWalls.id.toString());
			}
			if (exteriorWallFeatures != null && exteriorWallFeatures.length > 0) {
				const style = this.styleMap[exteriorWallFeatures[0].properties?.id] as WithId<LineStringStyle>;
				if (style != null) {
					exteriorWallStyle = { ...style, id: EXTERIOR_WALLS_ID };
				}

				const { walls, entrances: entrancesCollection } = cutEntrancesFromLineStrings(
					exteriorWallFeatures,
					entrances?.features || [],
				);
				const doorHeight = Math.max(exteriorWallStyle.height - 0.11, MIN_DOOR_HEIGHT);
				const doorStyle = { ...DEFAULT_DOOR_STYLE, height: doorHeight };
				const exteriorDoors = this.renderer.addGeometryGroup(
					`ExteriorDoors-${this.id}`,
					entrancesCollection,
					doorStyle,
					allGeometry,
				);

				this.layers.get(GEOMETRY_LAYER)!.layers.set(EXTERIOR_DOORS_ID, exteriorDoors.id.toString());

				const exteriorWalls = this.renderer.addGeometryGroup(
					`ExteriorWalls-${this.id}`,
					{
						features: [walls],
					} as any,
					translateToCoreStyle(exteriorWallStyle as LineStringStyle, this.options),
					allGeometry,
				);
				this.layers.get(GEOMETRY_LAYER)!.layers.set(EXTERIOR_WALLS_ID, exteriorWalls.id.toString());
			}
		}

		// load floating images for floor
		if (this.floorImages != null) {
			const imageContainer = this.renderer.addGroupContainer(`images-${this.id}`, {}, this.id);
			for (const feature of this.floorImages.features || []) {
				// only process floating images
				if (
					!(
						'anchorId' in feature.properties ||
						('anchorId' in feature.properties && feature.properties?.anchorId == null)
					)
				) {
					/**
					 * WARNING:This generated id is used to associate images with compat invisible polygon images.
					 * Do not change this without updating the logic in compat!
					 *
					 * We use the same hashing function along with a unique index to ensure that the id is unique.
					 * the index is unique per venue.
					 */
					const id = generateImageId(feature, imageIdx);
					this.mapObject.api.Image3D.add(
						new Coordinate({
							latitude: feature.geometry.coordinates[1],
							longitude: feature.geometry.coordinates[0],
							floorId: this.id,
						}),
						feature.properties.path,
						{
							id,
							flipImageToFaceCamera: this.options.flipImagesToFaceCamera ?? true,
							...feature.properties,
						},
					);

					imageIdx++;
				}
			}
			this.layers.set(IMAGES_LAYER, new Layer(this.renderer, imageContainer.id.toString()));
		}

		if (this.floorText != null) {
			for (const feature of this.floorText.features || []) {
				let verticalOffset = 0;
				if ('geometryId' in feature.properties) {
					verticalOffset = this.renderer.getState<GeometryState>(feature.properties.geometryId)?.height ?? 0;
				} else {
					verticalOffset = feature.properties.verticalOffset ?? 0;
				}
				this.renderer.addText3D(
					randomId(),
					{ ...feature, properties: { ...feature.properties, verticalOffset } },
					{},
					this.id,
				);
			}
		}

		if (this.areas != null) {
			const areasLayer = this.renderer.addGroupContainer(`areas-${this.id}`, {}, this.id);
			this.layers.set(AREAS_LAYER, new Layer(this.renderer, areasLayer.id.toString()));

			const group = this.renderer.addGeometryGroup(
				`Areas-${this.id}`,
				{
					features: this.areas.features,
				} as FeatureCollection<Polygon, any>,
				{
					color: DEFAULT_AREA_STYLE.color,
				},
				areasLayer,
			);

			// TODO: swap over to the styleMap function in the future:
			for (const feature of this.areas.features || []) {
				this.renderer.setState(feature.properties.id, {
					...DEFAULT_AREA_STYLE,
					visible: false,
				});
			}

			this.layers.get(AREAS_LAYER)!.layers.set(group.id.toString(), group.id.toString());
		}

		if (this.multiFloorView?.enabled) {
			this.renderer.setState(this.id, {
				altitude:
					floorAltitudes.get(this.elevation)?.altitude ??
					this.elevation * parseFloorGap(this.multiFloorView?.floorGap, this.maxHeight),
			});
		}

		this.renderer.render();

		return this;
	};

	/**
	 * Add an occluder to the floor if it has a footprint.
	 */
	addOccluder() {
		if (this.footprint?.geometry == null) {
			return;
		}
		// add container so we can show/hide the occluder
		const occluderContainer = this.renderer.addGroupContainer(`${OCCLUSION_LAYER}-${this.id}`, {}, this.id);
		const occluderLayer = new Layer(this.renderer, occluderContainer.id.toString());
		this.layers.set(OCCLUSION_LAYER, occluderLayer);

		this.occluderId = this.renderer.addOccluder(
			this.footprint as Feature<Polygon | MultiPolygon, any>,
			occluderLayer.containerId,
		);
	}

	/**
	 * Whether the floor contains facade geometry.
	 */
	get hasFacadeGeometry(): boolean {
		return this.facadesByStyleId.size > 0;
	}

	get visible() {
		return this.renderer.getState<GroupContainerState>(this.containerId).visible;
	}

	setVisible(visible: boolean) {
		this.renderer.setState<GroupContainerState>(this.containerId, {
			visible,
		});
	}
}
