import type { GeometryState } from '@mappedin/core-sdk';
import { z } from 'zod';
import { parseCompleteState } from './state-utils';

const GEOMETRY_STATE_TYPE = 'geometry';
/**
 * Zod schema for geometry state validation
 */
export const geometryStateSchema = z.object({
	type: z.literal(GEOMETRY_STATE_TYPE).default(GEOMETRY_STATE_TYPE),
	/**
	 * Whether the geometry is visible.
	 * @default true
	 */
	visible: z.boolean().default(true),
	/**
	 * Color of the geometry element. Color can be set using CSS colors names `DarkGoldenRod`, as Hex strings `#0000FF` or the rgb values `rgb(255,0,0)`.
	 * @default #ffffff
	 */
	color: z.union([z.string(), z.literal('initial')]).default('#ffffff'),
	/**
	 * Color of the geometry element's top. Color can be set using CSS colors names `DarkGoldenRod`, as Hex strings `#0000FF` or the rgb values `rgb(255,0,0)`.
	 */
	topColor: z.union([z.string(), z.literal('initial')]).optional(),
	/**
	 * Color of the geometry element when hovered over. Color can be set using CSS colors names `DarkGoldenRod`, as Hex strings `#0000FF` or the rgb values `rgb(255,0,0)`.
	 */
	hoverColor: z.union([z.string(), z.literal('initial')]).optional(),
	/**
	 * Indicates if the geometry element is interactive. Color can be set using CSS colors names `DarkGoldenRod`, as Hex strings `#0000FF` or the rgb values `rgb(255,0,0)`.
	 * @default false
	 */
	interactive: z.boolean().default(false),
	/**
	 * Opacity of the geometry element.
	 * @default 1
	 */
	opacity: z.number().default(1),
	/**
	 * Height of the geometry element in meters.
	 * @default 0
	 */
	height: z.number().default(0),
	/**
	 * The altitude of the geometry element in meters.
	 * @default 0
	 */
	altitude: z.number().default(0),
	/**
	 * Texture to apply to the geometry element.
	 */
	texture: z
		.object({
			url: z.string(),
		})
		.optional(),
	/**
	 * Top texture to apply to the geometry element.
	 */
	topTexture: z
		.object({
			url: z.string(),
		})
		.optional(),
	/**
	 * Whether the image should be flipped to face the camera.
	 * @default false
	 */
	flipImageToFaceCamera: z.boolean().default(false),
	/**
	 * Whether the anchored image should collide with other images.
	 * @default false
	 */
	enableImageCollisions: z.boolean().default(false),
	/**
	 * Whether the geometry has a border
	 */
	borderVisible: z.boolean().default(false),
	/**
	 * The color of the border. borderVisible must be true for this to take effect.
	 */
	borderColor: z.string().optional(),
	/**
	 * The width of the border. borderVisible must be true for this to take effect.
	 */
	borderWidth: z.number().optional(),
});

export const geometryStateSchemaPartial = geometryStateSchema.partial();
const geometryStateSchemaStrict = z.strictObject(geometryStateSchema.shape);

/**
 * Defines the state for geometry elements like {@link Space} when updated.
 */
export type TGeometryState = z.infer<typeof geometryStateSchemaStrict>;
export type TGeometryUpdateState = z.infer<typeof geometryStateSchemaPartial>;

export function getGeometryState(geometryState: GeometryState): TGeometryState {
	const {
		altitude,
		interactive,
		hoverColor,
		color,
		visible,
		opacity,
		height,
		texture,
		topTexture,
		flipImageToFaceCamera,
		enableImageCollisions,
		topColor,
		borderVisible,
		borderColor,
		borderWidth,
	} = geometryState;

	return parseCompleteState(geometryStateSchema, {
		interactive,
		hoverColor,
		color,
		visible,
		opacity,
		height,
		texture: texture ? { url: geometryState.texture } : undefined,
		topTexture: topTexture ? { url: topTexture } : undefined,
		flipImageToFaceCamera,
		enableImageCollisions,
		topColor,
		altitude,
		borderVisible,
		borderColor,
		borderWidth,
	});
}

export function translateGeometryStateToGeojsonCore(
	state: TGeometryUpdateState,
	currentState: Partial<GeometryState>,
): Partial<GeometryState> {
	const update: Partial<GeometryState> = {};
	if (currentState == null) {
		return update;
	}
	if (state.color) {
		if (state.color === 'initial') {
			update.color = currentState.initialColor;
		} else {
			update.color = state.color;
		}
	}
	if (state.topColor) {
		if (state.topColor === 'initial') {
			update.topColor = currentState.initialTopColor;
		} else {
			update.topColor = state.topColor;
		}
	}
	if (state.visible !== undefined) {
		update.visible = state.visible;
	}
	if (state.texture?.url !== undefined) {
		update.texture = state.texture.url;
	}
	if (state.topTexture?.url !== undefined) {
		update.topTexture = state.topTexture.url;
	}
	if (state.hoverColor !== undefined) {
		update.hoverColor = state.hoverColor;
	}
	if (state.interactive !== undefined) {
		update.interactive = state.interactive;
	}
	if (state.height !== undefined) {
		update.height = state.height;
	}
	if (state.opacity !== undefined) {
		update.opacity = state.opacity;
	}
	if (state.flipImageToFaceCamera !== undefined) {
		update.flipImageToFaceCamera = state.flipImageToFaceCamera;
	}
	if (state.enableImageCollisions !== undefined) {
		update.enableImageCollisions = state.enableImageCollisions;
	}
	if (state.altitude !== undefined) {
		update.altitude = state.altitude;
	}
	if (state.borderVisible !== undefined) {
		update.borderVisible = state.borderVisible;
	}
	if (state.borderColor !== undefined) {
		update.borderColor = state.borderColor;
	}
	if (state.borderWidth !== undefined) {
		update.borderWidth = state.borderWidth;
	}

	return update;
}
