import { AnnotationsExtension } from '@mappedin/mvf-annotations';
import { CMSExtension } from '@mappedin/mvf-cms';
import { ConnectionsExtension } from '@mappedin/mvf-connections';
import { type DecompressError, type MVFParserTransformedBundle, createParser } from '@mappedin/mvf-core';
import { DefaultStyleExtension } from '@mappedin/mvf-default-style';
import { FacadeExtension } from '@mappedin/mvf-facade';
import { FloorImagesExtension } from '@mappedin/mvf-floor-images';
import { FloorStackExtension } from '@mappedin/mvf-floor-stacks';
import { KindsExtension } from '@mappedin/mvf-kinds';
import { LocationsExtension } from '@mappedin/mvf-locations';
import { NavigationFlagsExtension } from '@mappedin/mvf-navigation-flags';
import { NodesExtension } from '@mappedin/mvf-nodes';
import { OutdoorsExtension } from '@mappedin/mvf-outdoors';
import { TilesetExtension } from '@mappedin/mvf-tileset';
import { TraversabilityExtension } from '@mappedin/mvf-traversability';
import { type Result, err, ok } from '@mappedin/safe-types/result';
import type { ParsedMVF } from '../types/bundle.js';
import { buildBuilderOptions } from './builderOptions.js';
import { buildAnnotations } from './builders/annotationBuilder.js';
import { buildConnections } from './builders/connectionBuilder.js';
import { buildEnterprise } from './builders/enterpriseBuilder.js';
import { buildFacade } from './builders/facadeBuilder.js';
import { buildFloors } from './builders/floorBuilder.js';
import { buildFloorImages } from './builders/floorImagesBuilder.js';
import { buildFloorStacks } from './builders/floorStackBuilder.js';
import { buildGeometry } from './builders/geometryBuilder.js';
import { buildLocations } from './builders/locationBuilder.js';
import { buildManifest } from './builders/manifestBuilder.js';
import { buildNodes } from './builders/nodeBuilder.js';
import { buildStyles } from './builders/styleBuilder.js';

const STANDARD_MVF_V3_PARSER = createParser()
	.extendOptional(NavigationFlagsExtension)
	.extendOptional(NodesExtension)
	.extendOptional(ConnectionsExtension)
	.extendOptional(KindsExtension)
	.extendOptional(TraversabilityExtension)
	.extendOptional(DefaultStyleExtension)
	.extendOptional(FloorStackExtension)
	.extendOptional(OutdoorsExtension)
	.extendOptional(TilesetExtension)
	.extendOptional(FloorImagesExtension)
	.extendOptional(FacadeExtension)
	.extendOptional(AnnotationsExtension)
	.extendOptional(LocationsExtension)
	.extendOptional(CMSExtension)
	.build({ cspSafe: true })
	.expect('Invalid parser, this should never happen. Likely a bug in the MVFv2 library, or one of the extensions.');

/**
 * This represents the result of parsing an MVFv3 object with the extensions the MVFv2 converter
 * can use. Note that nearly all extensions are actually optional.
 */
export type MVFv2_STANDARD_MVFv3 = MVFParserTransformedBundle<typeof STANDARD_MVF_V3_PARSER>;

/**
 * Converts an unzipped MVFv3 object that has the extensions needed for the MVFv2 converter
 * into a MVFv2 object.
 *
 * If the MVFv3 is still zipped, use {@link extractAndConvertMVFv3} instead.
 *
 * @param v3 - The unzipped MVFv3 object to convert.
 * @returns The converted MVFv2 object.
 */
export function convertUnzippedStandardMVFv3ToParsedMVFv2(v3: MVFv2_STANDARD_MVFv3): ParsedMVF {
	const builderOptions = buildBuilderOptions(v3);

	const floors = buildFloors(v3, builderOptions);
	const geometry = buildGeometry(v3, builderOptions);
	const nodes = buildNodes(v3, builderOptions);
	const connections = buildConnections(v3, builderOptions);
	const styles = buildStyles(v3, builderOptions);
	const floorStacks = buildFloorStacks(v3, builderOptions);
	const floorImages = buildFloorImages(v3, builderOptions);
	const facade = buildFacade(v3, builderOptions);
	const annotations = buildAnnotations(v3, builderOptions);
	const locations = buildLocations(v3, builderOptions);
	const enterprise = buildEnterprise(v3, builderOptions);
	const manifest = buildManifest(v3, builderOptions);
	const v2: ParsedMVF = {
		// Slightly risky, extending these v3 definitions will add those properties to the v2 object
		...(v3.tileset ? { 'tileset.json': v3.tileset } : {}),
		...(v3.navigationFlags ? { 'navigationFlags.json': v3.navigationFlags } : {}),
		...floors,
		...geometry,
		...nodes,
		...connections,
		...styles,
		...floorStacks,
		...floorImages,
		...facade,
		...annotations,
		...locations,
		...enterprise,
		...manifest,
	};

	return v2;
}

/**
 * Converts a zip file that hopefully contains an MVFv3 object to an MVFv2 format.
 * This is the main entry point for MVF conversion.
 *
 * @param data - The MVFv3 zip file data to convert.
 * @returns A promise containing the Result of the conversion, which can be unwrapped into an MVFv2 object or an error if the MVFv3 is invalid.
 */
export async function extractAndConvertMVFv3(data: Uint8Array): Promise<Result<ParsedMVF, DecompressError>> {
	const result = await STANDARD_MVF_V3_PARSER.decompress(data);
	if (result.isOk()) {
		return ok(convertUnzippedStandardMVFv3ToParsedMVFv2(result.value));
	} else {
		return err(result.error);
	}
}

/**
 * Converts an MVFv3 zip file to an MVFv2 format, WITHOUT validating the structure of the MVFv3.
 *
 * If you are absolutely sure that the MVFv3 is valid, and do not want to pay the validation cost, you can use this function.
 *
 * However, if this is NOT a valid MVFv3, you may get unexpected results.
 *
 * @param data - The MVFv3 zip file data to convert.
 * @returns A promise containing the Result of the conversion, which can be unwrapped into an MVFv2 object or an error if the MVFv3 is invalid.
 */
export async function extractAndConvertMVFv3Unsafe(data: Uint8Array): Promise<Result<ParsedMVF, DecompressError>> {
	const result = await STANDARD_MVF_V3_PARSER.decompressUnsafe(data);
	if (result.isOk()) {
		return ok(convertUnzippedStandardMVFv3ToParsedMVFv2(result.value as MVFv2_STANDARD_MVFv3));
	} else {
		return err(result.error);
	}
}

/**
 * Converts a zip file that hopefully contains an MVFv3 object to an MVFv2 format, synchronously.
 *
 * @param data - The MVFv3 zip file data to convert.
 * @returns A promise containing the Result of the conversion, which can be unwrapped into an MVFv2 object or an error if the MVFv3 is invalid.
 */
export function extractAndConvertMVFv3Sync(data: Uint8Array): Result<ParsedMVF, DecompressError> {
	const result = STANDARD_MVF_V3_PARSER.decompressSync(data);
	if (result.isOk()) {
		return ok(convertUnzippedStandardMVFv3ToParsedMVFv2(result.value as MVFv2_STANDARD_MVFv3));
	} else {
		return err(result.error);
	}
}

/**
 * Converts an MVFv3 zip file to an MVFv2 format synchronously, WITHOUT validating the structure of the MVFv3.
 *
 * If you are absolutely sure that the MVFv3 is valid, and do not want to pay the validation cost, you can use this function.
 *
 * However, if this is NOT a valid MVFv3, you may get unexpected results.
 *
 * @param data - The MVFv3 zip file data to convert.
 * @returns A promise containing the Result of the conversion, which can be unwrapped into an MVFv2 object or an error if the MVFv3 is invalid.
 */
export function extractAndConvertMVFv3UnsafeSync(data: Uint8Array): Result<ParsedMVF, DecompressError> {
	const result = STANDARD_MVF_V3_PARSER.decompressUnsafeSync(data);
	if (result.isOk()) {
		return ok(convertUnzippedStandardMVFv3ToParsedMVFv2(result.value as MVFv2_STANDARD_MVFv3));
	} else {
		return err(result.error);
	}
}
