/**
 * Returns the remaining expiration time of a token.
 */
export const fromNow = (expires: number) => {
	const currentTimestamp = Date.now();
	const remainingExpirationTime = expires - currentTimestamp;

	return remainingExpirationTime;
};

/**
 * Whether a token is within the buffer time before it expires.
 */
export const shouldRefreshToken = (expires: number, bufferMinutes) => {
	const remainingExpirationTime = fromNow(expires);

	return remainingExpirationTime <= bufferMinutes * 60 * 1000;
};
