import booleanPointInPolygon from '@turf/boolean-point-in-polygon';

import { Edge } from '../edge';
import { getAngle, hasLineOfSight } from '../utils';
import { equirectangularDistance, euclideanDistance } from '../../../common/utils';

import type { NodeCollection, NodeFeature } from '../types/node';
import type { DirectionsZone } from '../navigator';
import type { Position } from 'geojson';

/*
Default buffer radius of 0.4 meters.
This value is chosen to account for common navigational constraints such as doorways,
which are typically around 0.85 meters wide in Canada and the US.
By setting the buffer radius to slightly less than half the average doorway width,
we ensure a comfortable margin that allows for accurate pathfinding around typical obstacles.
 */
export const DEFAULT_BUFFER_RADIUS = 0.4;

/**
 * Represents a navigation graph for pathfinding operations, holding nodes and edges with utilities to perform routing.
 * @see {@link https://mappedin.atlassian.net/wiki/spaces/SDK/pages/234487854/A+Search+Algorithm+in+Navigation+Graph}
 * @hidden
 */
export class NavigationGraph {
	public readonly edges: { [propName: string]: Edge[] } = {};
	// The map of  node features by their ID for quick access
	public readonly nodesById: { [propName: string]: NodeFeature };
	// The map of node features by their group(specified by a property) for quick access
	public readonly nodesByGroup = new Map<string, NodeFeature[]>();
	#groupBy: string;

	constructor({
		nodes,
		groupBy,
		multiplicativeDistanceWeightScaling,
	}: {
		nodes: NodeCollection;
		groupBy: string;
		multiplicativeDistanceWeightScaling?: boolean;
	}) {
		this.#groupBy = groupBy;
		const { features: nodesList } = nodes;

		// Mapping of node IDs to node features for quick access
		this.nodesById = this.#createNodesById(nodesList);
		// Ensure there are no references to non-existent nodes
		this.#removeInvalidNeighbors();
		// Initialize edges based on node neighbors with computed distance and angle and group nodes by a specified property
		this.#initializeEdgesAndGroups(nodesList, groupBy, { multiplicativeDistanceWeightScaling });
	}

	/**
	 * Calculates the shortest Euclidean distance from the origin node to any of the destination nodes.
	 *
	 * @param origin - The origin node.
	 * @param destinations - An array of destination nodes.
	 * @returns The shortest Euclidean distance.
	 */
	public getShortestEuclideanDistance(origin: NodeFeature, destinations: NodeFeature[]): number {
		return destinations.reduce((result, destination) => {
			const distance = euclideanDistance(origin.geometry.coordinates, destination.geometry.coordinates);

			return Math.min(result, distance);
		}, Number.MAX_VALUE);
	}

	public hasLineOfSight = (
		origin: Position,
		destination: Position,
		edges: Position[][] = [],
		bufferRadius = DEFAULT_BUFFER_RADIUS,
	): boolean => {
		const originFeature = {
			type: 'Feature',
			properties: {
				id: 'origin',
				neighbors: [],
			},
			geometry: {
				type: 'Point',
				coordinates: origin,
			},
		} as NodeFeature;

		const destinationFeature = {
			type: 'Feature',
			properties: {
				id: 'destination',
				neighbors: [],
			},
			geometry: {
				type: 'Point',
				coordinates: destination,
			},
		} as NodeFeature;

		return hasLineOfSight(originFeature, destinationFeature, edges, bufferRadius) === true;
	};

	/**
	 * Performs a Dijkstra search to find nodes within a given travel distance that satisfy a goal function, sorted by distance.
	 *
	 * @param originId - Origin node ID.
	 * @param maxTravelDistance - The maximum travel distance.
	 * @param includedNodeIds - Array of node IDs to include in the search.
	 * @param obstructionEdges - Array of obstruction edges for line of sight calculations.
	 * @param useLineOfSight - Whether to use line of sight checking.
	 * @returns An Array of nodes within the travel distance that satisfy the goal function.
	 * - feature: The node feature.
	 * - distance: The distance to the node.
	 * - edges: The edges to the node.
	 */
	public dijkstraFindWithinTravelDistance(
		originId: string,
		maxTravelDistance: number,
		includedNodeIds: string[],
		obstructionEdges: Position[][],
		useLineOfSight: boolean,
		limitNumberOfResults: number,
	): {
		feature: NodeFeature;
		distance: number;
	}[] {
		const origin = this.nodesById[originId];

		if (!origin) {
			return [];
		}

		// Priority queue for nodes to explore, sorted by distance
		const frontier: { node: NodeFeature; distance: number }[] = [];
		// Map of node IDs to their predecessors in the path
		const cameFrom: { [propName: string]: Edge | null } = {};
		// Map of node IDs to the cost of the path from origin to that node
		const costSoFar: { [propName: string]: number } = {};
		// Set of visited nodes to avoid revisiting
		const visited = new Set<string>();
		// Results array for nodes that satisfy the goal function
		const results: {
			feature: NodeFeature;
			distance: number;
		}[] = [];

		// Initialize with origin node
		frontier.push({ node: origin, distance: 0 });
		cameFrom[origin.properties.id] = null;
		costSoFar[origin.properties.id] = 0;

		while (frontier.length > 0) {
			// TODO: consider using minHeap instead of sort the entire array in the loop
			// Get the node with the smallest distance
			frontier.sort((a, b) => a.distance - b.distance);
			const current = frontier.shift();
			if (!current) break;

			const currentNode = current.node;
			const currentDistance = current.distance;

			// Skip if already visited
			if (visited.has(currentNode.properties.id)) {
				continue;
			}

			// Mark as visited
			visited.add(currentNode.properties.id);

			if (results.length >= limitNumberOfResults) {
				break;
			}

			// Check if this node satisfies the goal function
			if (currentNode.properties.id != null && includedNodeIds.includes(currentNode.properties.id?.toString())) {
				// check if the node is visible from the origin
				if (useLineOfSight) {
					// TODO: can we make this faster by caching?
					if (this.hasLineOfSight(origin.geometry.coordinates, currentNode.geometry.coordinates, obstructionEdges)) {
						// if the node is visible, add it to the results
						results.push({
							feature: currentNode,
							distance: currentDistance,
						});
					}
				} else {
					results.push({
						feature: currentNode,
						distance: currentDistance,
					});
				}
			}

			// Explore neighbors
			for (const edge of this.edges[currentNode.properties.id]) {
				const neighbor = edge.destination;
				// Use edge.distance for actual travel distance, edge.weight for cost
				const newDistance = currentDistance + edge.distance;
				const newCost = costSoFar[currentNode.properties.id] + edge.weight;

				// Skip if beyond max travel distance
				if (newDistance > maxTravelDistance) {
					continue;
				}

				// Skip if already visited or if we found a better path
				if (
					visited.has(neighbor.properties.id) ||
					(costSoFar[neighbor.properties.id] !== undefined && newCost >= costSoFar[neighbor.properties.id])
				) {
					continue;
				}

				// Update path information
				costSoFar[neighbor.properties.id] = newCost;
				cameFrom[neighbor.properties.id] = edge;
				frontier.push({ node: neighbor, distance: newDistance });
			}
		}

		// Sort results by distance (nearest first)
		return results.sort((a, b) => a.distance - b.distance);
	}

	/**
	 * Performs A* pathfinding from specified origins to destinations, considering optional constraints like accessibility.
	 *
	 * @param {string[]} originIds - Array of origin node IDs.
	 * @param {string[]} destinationNodeIds - Array of destination node IDs.
	 * @param {Set<string>} [disabledConnectionNodeIds] - Optional set of connection node IDs that are disabled (ie. act as regular nodes).
	 * @param {Set<string>} [disabledNodeIds] - Optional set of node IDs which are ignored during pathfinding.
	 * @returns {Edge[]} An array of edges representing the shortest path, or an empty array if no path is found.
	 */
	public aStar({
		originIds,
		destinationNodeIds,
		disabledConnectionNodeIds,
		zones,
		overrideEdgeWeights,
		disabledNodeIds,
	}: {
		originIds: string[];
		destinationNodeIds: string[];
		zones: DirectionsZone[];
		disabledConnectionNodeIds?: Set<string>;
		overrideEdgeWeights?: Map<Edge, number>;
		disabledNodeIds?: Set<string>;
	}): Edge[] {
		const origins = this.#filterValidNodes(originIds);
		const destinations = this.#filterValidNodes(destinationNodeIds);

		if (origins.length === 0 || destinations.length === 0) {
			return [];
		}

		/*
		 * frontier - Priority queue of nodes to explore, sorted by the cost of the path to the node
		 * cameFrom - Map of nodes and their predecessors in the path
		 * costSoFar - Map of nodes and the cost of the path from the start node to the node
		 */
		const { frontier, cameFrom, costSoFar } = this.#initializeSearch(origins, destinations);

		while (frontier.length > 0) {
			const current = frontier.shift(); // Use shift() to remove the first element
			if (!current) break;

			// Check if the current node is a destination node and reconstruct the path if true
			if (this.#isDestinationNode(current.origin, destinations)) {
				return this.#reconstructPath(cameFrom, current.origin.properties.id);
			}

			// Explore each edge from the current node
			for (const edge of this.edges[current.origin.properties.id]) {
				const edgeWeight = overrideEdgeWeights?.get(edge) ?? edge.weight;
				// if the connection is disabled, we don't want to consider edges that would take us to
				// a different floor, but we do want to consider edges on the current floor
				// Otherwise, if the node itself is disabled, skip it.
				if (
					(disabledConnectionNodeIds?.has(edge.origin.properties.id) &&
						edge.destination.properties.map !== edge.origin.properties.map) ||
					disabledNodeIds?.has(edge.origin.properties.id)
				) {
					continue;
				}

				// Calculate the new cost to reach the neighbor node
				const newCost =
					costSoFar[current.origin.properties.id] + edgeWeight + this.#getZonalCost(edge.destination, zones);

				if (newCost === Infinity) continue;

				// If this path to the neighbor is cheaper or the neighbor hasn't been visited yet, update the cost and priority
				if (costSoFar[edge.destination.properties.id] == null || newCost < costSoFar[edge.destination.properties.id]) {
					// Calculate the total priority for the neighbor node
					const cost = newCost + this.getShortestEuclideanDistance(edge.destination, destinations);

					// Update costSoFar and cameFrom for the neighbor node
					costSoFar[edge.destination.properties.id] = newCost;
					cameFrom[edge.destination.properties.id] = edge;
					frontier.push({ origin: edge.destination, priority: cost });
				}
			}

			frontier.sort((a, b) => a.priority - b.priority);
		}

		return [];
	}

	#filterValidNodes(nodeIds: string[]): NodeFeature[] {
		return nodeIds.map(id => this.nodesById[id]).filter(Boolean);
	}

	#createNodesById(nodesList: NodeFeature[]): { [propName: string]: NodeFeature } {
		return nodesList.reduce((result, node) => {
			result[node.properties.id] = node;

			return result;
		}, {});
	}

	#removeInvalidNeighbors() {
		for (const id in this.nodesById) {
			if (this.nodesById.hasOwnProperty(id)) {
				this.nodesById[id].properties.neighbors = this.nodesById[id].properties.neighbors.filter(
					({ id }) => !!this.nodesById[id],
				);
			}
		}
	}

	#initializeEdgesAndGroups(
		nodesList: NodeFeature[],
		groupBy: string,
		options?: { multiplicativeDistanceWeightScaling?: boolean },
	) {
		nodesList.forEach(origin => {
			const groupId = groupBy === 'default' ? groupBy : origin.properties[groupBy];
			if (!this.nodesByGroup.has(groupId)) {
				this.nodesByGroup.set(groupId, []);
			}

			this.nodesByGroup.get(groupId)!.push(origin);
			this.edges[origin.properties.id] = origin.properties.neighbors.map(({ id, weight: pathWeight }) => {
				const destination = this.nodesById[id];
				const distance = equirectangularDistance(origin.geometry.coordinates, destination.geometry.coordinates);
				const angle = getAngle(origin.geometry.coordinates, destination.geometry.coordinates);

				return new Edge({
					origin,
					destination,
					pathWeight,
					distance,
					angle,
					multiplicativeDistanceWeightScaling: options?.multiplicativeDistanceWeightScaling,
				});
			});
		});
	}

	#initializeSearch(origins: NodeFeature[], destinations: NodeFeature[]) {
		const frontier: { origin: NodeFeature; priority: number }[] = [];
		const cameFrom: { [propName: string]: Edge | null } = {};
		const costSoFar: { [propName: string]: number } = {};

		origins.forEach(origin => {
			const priority = this.getShortestEuclideanDistance(origin, destinations);

			frontier.push({ origin, priority });
			cameFrom[origin.properties.id] = null;
			costSoFar[origin.properties.id] = 0;
		});

		frontier.sort((a, b) => a.priority - b.priority);

		return { frontier, cameFrom, costSoFar };
	}

	#isDestinationNode(currentNode: NodeFeature, destinations: NodeFeature[]): boolean {
		return destinations.some(destination => destination.properties.id === currentNode.properties.id);
	}

	#reconstructPath(cameFrom: { [propName: string]: Edge | null }, currentId: string): Edge[] {
		const directions: Edge[] = [];
		let step = cameFrom[currentId];

		while (step) {
			directions.push(step);
			step = cameFrom[step.origin.properties.id];
		}

		return directions.reverse();
	}

	#getZonalCost = (node: NodeFeature, zones: DirectionsZone[]): number => {
		let cost = 0;

		for (const zone of zones) {
			const validZone = !zone[this.#groupBy] || zone[this.#groupBy] === node.properties[this.#groupBy];

			if (validZone && booleanPointInPolygon(node.geometry.coordinates, zone.geometry.geometry)) {
				/*
				 * When newCost is lower than previous values, the node is pushed back into the frontier,
				 * potentially with a higher priority.
				 * This can cause the algorithm to reprocess the same nodes multiple times,
				 * especially if the cost keeps decreasing, creating a loop.
				 * The frontier doesn't get smaller, and the algorithm can continue to loop indefinitely.
				 * So we have to ensure that the cost is always positive.
				 */
				cost += Math.max(0, zone.cost); // Ensure no negative costs to avoid ifinite loops
			}
		}

		return cost;
	};
}
