import { TAP_TIMEOUT, TWO_FINGER_TAP_THRESHOLD } from './constants';

export enum TapType {
	onefinger,
	twofinger,
}
export class Tap {
	type: TapType;
	event: PointerEvent;
	constructor(event: PointerEvent) {
		this.type = TapType.onefinger;
		this.event = event;
	}
	get timestamp() {
		return this.event.timeStamp;
	}
}

export class TapsController {
	private taps: Tap[] = [];

	add(tap: Tap) {
		if (this.lastPointerDown && tap.timestamp - this.lastPointerDown.timestamp < TWO_FINGER_TAP_THRESHOLD) {
			this.lastPointerDown.type = TapType.twofinger;
		} else {
			this.taps.push(tap);
		}
	}

	get lastPointerDown() {
		return this.taps.length >= 1 ? this.taps[this.taps.length - 1] : undefined;
	}

	get isSingleTapWithTwoFingers() {
		return this.isSingleTap && this.lastPointerDown?.type === TapType.twofinger;
	}

	get isSingleTapWithOneFinger() {
		return this.isSingleTap && this.lastPointerDown?.type === TapType.onefinger;
	}

	get isDoubleTapWithOneFinger() {
		return this.isDoubleTap && this.taps[0]?.type === TapType.onefinger && this.taps[1]?.type === TapType.onefinger;
	}

	get isSingleTap() {
		return this.taps.length === 1;
	}

	get isDoubleTap() {
		return this.taps.length === 2;
	}

	discardOutsideOfWaitWindow(timestamp: number) {
		this.taps = this.taps.filter(t => {
			return timestamp - t.timestamp < TAP_TIMEOUT;
		});
	}

	flush() {
		this.taps = [];
	}

	// exposed for testing purpose
	get _taps() {
		return this.taps;
	}

	destroy() {
		this.flush();
	}
}
