import Logger from '@packages/internal/common/Mappedin.Logger';
import type { Complete } from '@packages/internal/common/type-utils';
import { z } from 'zod';
import type { TDoorsState, TDoorsUpdateState } from './doors-state';
import type { TFacadeState, TFacadeUpdateState } from './facade-state';
import type { TFloorState, TFloorUpdateState } from './floor-state';
import type { TGeometryState, TGeometryUpdateState } from './geometry-state';
import type { TImage3DState, TImage3DUpdateState } from './image-state';
import type { TLabelState, TLabelUpdateState } from './label-state';
import type { TMarkerState, TMarkerUpdateState } from './marker-state';
import type { TModelState, TModelUpdateState } from './model-state';
import type { TPathState, TPathUpdateState } from './path-state';
import type { TShapeState, TShapeUpdateState } from './shape-state';
import type { TText3DState, TText3DUpdateState } from './text3d-state';
import type { TWallsState, TWallsUpdateState } from './walls-state';
import type { Label, Marker, Model, Path, PathSegment, Shape, Text3DView, Image3DView } from '../map-view-objects';
import type { DOORS, WALLS } from '../types';
import type { Area, Door, Facade, Floor, MapObject, Space } from '../map-data-objects';
import type { ReadonlyDeep } from 'type-fest';

type ParseStateOptions = {
	warn?: boolean;
};

/**
 * Extracts the type from a Zod schema by looking for a 'type' field with a literal value
 * @param schema - The Zod schema to extract type from
 * @returns The type string or 'unknown' if not found
 */
function extractTypeFromSchema(schema: z.ZodObject<any>): string {
	try {
		// Try to get the type from the schema shape
		const shape = schema.shape;
		if ('type' in shape) {
			let typeSchema = shape.type;

			// Unwrap optional schemas (e.g., from .partial())
			if (typeSchema instanceof z.ZodOptional) {
				typeSchema = typeSchema._def.innerType;
			}

			// Check if it's a literal with a value
			if ('value' in typeSchema) {
				return String(typeSchema.value);
			}

			// Check if it's a literal with default value
			if (typeSchema instanceof z.ZodDefault && 'value' in typeSchema._def.innerType) {
				return String(typeSchema._def.innerType.value);
			}
		}

		return 'unknown';
	} catch {
		return 'unknown';
	}
}

/**
 * Extracts invalid fields from a Zod parse result
 * @param parseResult - The result from schema.safeParse()
 * @returns Object with field names as keys and error messages as values
 */
function extractInvalidFields(parseResult: ReturnType<typeof z.ZodType.prototype.safeParse>): Record<string, string[]> {
	if (parseResult.success) {
		return {};
	}

	// Manually extract field errors from issues array (flatten() is deprecated in v4)
	const fieldErrors: Record<string, string[]> = {};
	for (const issue of parseResult.error.issues) {
		// Only process issues with a path (field-level errors)
		if (issue.path.length > 0) {
			const fieldName = String(issue.path[0]);
			if (!fieldErrors[fieldName]) {
				fieldErrors[fieldName] = [];
			}
			fieldErrors[fieldName].push(issue.message);
		}
	}
	return fieldErrors;
}

/**
 * Logs invalid fields and their error messages to the console
 * @param invalidFields - Object with field names as keys and error messages as values
 * @param elementType - The type of element being parsed (for logging)
 */
function logInvalidFields(invalidFields: Record<string, string[]>, elementType: string) {
	const fieldEntries = Object.entries(invalidFields);

	if (fieldEntries.length > 0) {
		const fieldDetails = fieldEntries.map(([field, messages]) => `${field}: ${messages.join(', ')}`).join('; ');
		Logger.warn(`${fieldEntries.length} invalid field(s) in ${elementType ?? 'unknown'} state: [${fieldDetails}]`);
	}
}

/**
 * Parses state with partial validation - warns about invalid fields and strips them,
 * but keeps valid fields and continues processing.
 *
 * @param schema - Partial Zod schema to validate against (already has .partial() applied)
 * @param state - Input state to parse
 * @param options - Optional configuration
 * @returns Parsed and cleaned state with only valid fields
 */
export function parsePartialState<T extends z.ZodObject<any>>(
	schema: T,
	state: object,
	options?: ParseStateOptions,
): Partial<z.infer<T>> {
	if (state == null || typeof state !== 'object') {
		return {};
	}

	const parseResult = schema.safeParse(state);
	if (parseResult.success) {
		// Zod v4 applies defaults even in optional fields, so we need to filter out
		// fields that weren't in the original input to preserve partial update behavior
		const result: Record<string, unknown> = {};
		for (const key of Object.keys(state)) {
			if (key in parseResult.data) {
				result[key] = parseResult.data[key];
			}
		}

		return result as Partial<z.infer<T>>;
	}

	const invalidFields = extractInvalidFields(parseResult);

	if (options?.warn) {
		logInvalidFields(invalidFields, extractTypeFromSchema(schema));
	}

	// Build result with only valid fields (copy from original input)
	const result: Record<string, unknown> = {};
	for (const key of Object.keys(state)) {
		if (!(key in invalidFields) && key in schema.shape) {
			result[key] = state[key];
		}
	}

	return result as Partial<z.infer<T>>;
}

/**
 * Parses state with full validation - uses schema defaults for missing fields
 * and returns the complete inferred type.
 *
 * @param schema - Zod schema to validate against
 * @param state - Input state to parse
 * @param options - Optional configuration
 * @returns Parsed state with all fields (using defaults where needed)
 */
export function parseCompleteState<T extends z.ZodObject<any>>(
	schema: T,
	state: object,
	options?: ParseStateOptions,
): z.infer<Complete<T>> {
	const parseResult = schema.safeParse(state);

	if (parseResult.success) {
		return parseResult.data as z.infer<Complete<T>>;
	}

	if (options?.warn === true) {
		const invalidFields = extractInvalidFields(parseResult);
		logInvalidFields(invalidFields, extractTypeFromSchema(schema));
	}

	return state as z.infer<Complete<T>>;
}

/**
 * Centralized mapping of element types to their get state types.
 */
export type MapElementToGetState = {
	// Map View Objects
	[Marker.__type]: ReadonlyDeep<TMarkerState>;
	[Label.__type]: ReadonlyDeep<TLabelState>;
	[Shape.__type]: ReadonlyDeep<TShapeState>;
	[Model.__type]: ReadonlyDeep<TModelState>;
	[Path.__type]: ReadonlyDeep<TPathState>;
	[PathSegment.__type]: ReadonlyDeep<TPathState>;
	[Text3DView.__type]: ReadonlyDeep<TText3DState>;
	[Image3DView.__type]: ReadonlyDeep<TImage3DState>;

	// Map Data Objects
	[Space.__type]: ReadonlyDeep<TGeometryState>;
	[MapObject.__type]: ReadonlyDeep<TGeometryState>;
	[Floor.__type]: ReadonlyDeep<TFloorState>;
	[Facade.__type]: ReadonlyDeep<TFacadeState>;
	[Door.__type]: ReadonlyDeep<TDoorsState>;
	[Area.__type]: ReadonlyDeep<TGeometryState>;

	// Enums
	[WALLS.Exterior]: ReadonlyDeep<TWallsState>;
	[WALLS.Interior]: ReadonlyDeep<TWallsState>;
	[DOORS.Exterior]: ReadonlyDeep<TDoorsState>;
	[DOORS.Interior]: ReadonlyDeep<TDoorsState>;
};

/**
 * Centralized mapping of element types to their update state types.
 */
export type MapElementToUpdateState = {
	// Map View Objects
	[Marker.__type]: TMarkerUpdateState;
	[Label.__type]: TLabelUpdateState;
	[Shape.__type]: TShapeUpdateState;
	[Model.__type]: TModelUpdateState;
	[Path.__type]: TPathUpdateState;
	[PathSegment.__type]: TPathUpdateState;
	[Text3DView.__type]: TText3DUpdateState;
	[Image3DView.__type]: TImage3DUpdateState;

	// Map Data Objects
	[Space.__type]: TGeometryUpdateState;
	[MapObject.__type]: TGeometryUpdateState;
	[Floor.__type]: TFloorUpdateState;
	[Facade.__type]: TFacadeUpdateState;
	[Door.__type]: TDoorsUpdateState;
	[Area.__type]: TGeometryUpdateState;

	// Enums
	[WALLS.Exterior]: TWallsUpdateState;
	[WALLS.Interior]: TWallsUpdateState;
	[DOORS.Exterior]: TDoorsUpdateState;
	[DOORS.Interior]: TDoorsUpdateState;
};
