import { typedEntries } from '@mappedin/mvf-core/utils';
import type {
	AnchoredImageFeature,
	FloatingImageFeature,
	FloorImageFeature,
	FloorImageFeatureCollection,
} from '../../extensions/floorImage.js';
import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3FloorImages = Pick<MVFv2_STANDARD_MVFv3, 'floorImages'>;
export type V2FloorImages = Pick<ParsedMVF, 'floorImages'>;
export type V2FloorImage = FloorImageFeature;

export function buildFloorImages(
	v3: V3FloorImages,
	{ geometryMaps }: Pick<BuilderOptions, 'geometryMaps'>,
): V2FloorImages {
	const v2: V2FloorImages = {};

	const v2FloorImages: V2FloorImages['floorImages'] = {};

	for (const [floorId, v3FloorImageCollection] of typedEntries(v3.floorImages ?? {})) {
		const v2FloorImageCollection: FloorImageFeatureCollection = {
			features: [],
			type: 'FeatureCollection',
		};

		for (const v3FloorImage of v3FloorImageCollection?.features ?? []) {
			if ('verticalOffset' in v3FloorImage.properties) {
				v2FloorImageCollection.features.push(v3FloorImage as FloatingImageFeature);
			} else {
				const { geometryId, ...rest } = v3FloorImage.properties;
				// There's no way to assign an image to part of a multi-part geometry, so pulling the first ID is the best we can do
				const anchorId = geometryMaps.space.get(geometryId)?.ids[0] ?? geometryMaps.obstruction.get(geometryId)?.ids[0];
				if (!anchorId) {
					// Images can only be anchored to spaces or obstructions, so if we don't have an anchor, skip it
					continue;
				}

				const feature: AnchoredImageFeature = {
					geometry: v3FloorImage.geometry,
					properties: {
						...rest,
						anchorId,
					},
					type: v3FloorImage.type,
				};

				v2FloorImageCollection.features.push(feature);
			}
		}
		v2FloorImages[floorId] = v2FloorImageCollection;
	}
	if (Object.keys(v2FloorImages).length > 0) {
		v2.floorImages = v2FloorImages;
	}

	return v2;
}
