import type { DirectionsCollection } from '@packages/internal/geojson-navigator';
import type { MapDataInternal } from '../../map-data-objects';
import {
	Connection,
	Coordinate,
	Door,
	EnterpriseLocation,
	MapObject,
	PointOfInterest,
	Space,
	Node,
	Area,
	Facade,
	Annotation,
	LocationProfile,
} from '../../map-data-objects';
import type { TNavigationTarget } from '../../types';
import type { TFindNearestOptions } from '../../query';

const findNearestOptions: TFindNearestOptions = {
	lineOfSight: true,
	mode: 'absolute-distance',
	maxResults: 1,
};

function getOptions(enterpriseMode: boolean): TFindNearestOptions {
	return {
		...findNearestOptions,
		lineOfSight: enterpriseMode ? false : true,
	};
}

/**
 * Cache the nodes for each feature to avoid duplicate calls to getNodesFromTarget
 */
const featureNodeCache = new WeakMap<TNavigationTarget, string[]>();

type TNearestCandidate = Annotation | Coordinate | PointOfInterest;

/**
 * @deprecated use nearestNodeIdsAsync unless you must call this synchronously.
 * Get nearest node ids synchronously. This is only used for CMS venues via compat.
 * @param feature - The feature to get the nearest node ids for
 * @param mapData - The map data instance
 * @returns The node ids
 */
export function nearestNodeIdsSync(
	feature: TNearestCandidate,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): string[] {
	const options = getOptions(enterpriseMode);

	const result = mapData.Query.__LEGACY_NEAREST_SYNC(feature, mapData.getByType('node'), options);
	const id = result?.[0]?.feature.id;
	if (id) return [id];

	return [];
}

export async function nearestNodeIds(
	feature: TNearestCandidate,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): Promise<string[]> {
	const options = getOptions(enterpriseMode);
	const result = await mapData.Query.nearest(feature, mapData.getByType('node'), options);
	const id = result?.[0]?.feature.id;
	if (id) return [id];

	return [];
}

/** @deprecated use locationProfileNodeIds unless you must call this synchronously. */
export function locationProfileNodeIdsSync(
	target: LocationProfile,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): string[] {
	const nodeIds: string[] = [];
	for (const navigationTarget of target.navigationTarget) {
		const result = getNodesFromTargetSync(navigationTarget, mapData, enterpriseMode);
		nodeIds.push(...result);
	}
	return nodeIds;
}

export async function locationProfileNodeIds(
	target: LocationProfile,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): Promise<string[]> {
	const nodeIds: string[] = [];
	for (const navigationTarget of target.navigationTarget) {
		const result = await getNodesFromTarget(navigationTarget, mapData, enterpriseMode);
		nodeIds.push(...result);
	}
	return nodeIds;
}

export function doorNodeIds(target: Door, mapData: MapDataInternal): string[] {
	const mvfDoor = mapData?.getMVFFeatureById('entrance', target.id);
	const nodeId = mvfDoor?.properties.node;
	if (nodeId) return [nodeId];

	return [];
}

export function areaNodeIds(target: Area, mapData: MapDataInternal): string[] {
	const mvfArea = mapData?.getMVFFeatureById('area', target.id);
	return mvfArea?.properties.destinationNodes ?? [];
}

export function spaceNodeIds(target: Space, mapData: MapDataInternal): string[] {
	const mvfSpace = mapData?.getMVFFeatureById('space', target.id);
	if (mvfSpace == null) return [];

	const precomputedEntrances = mapData.entranceNodeIdsBySpaceId?.[target.id];
	if (precomputedEntrances && precomputedEntrances.length > 0) {
		return precomputedEntrances;
	}

	/**
	 * No precomputed entrances - this is probably a CMS venue without doors.
	 * Usually these are hand drawn node graphs so destination nodes will be a small array.
	 * If this is autopathed, this will be expensive, but not much we can do about it.
	 */
	return mvfSpace.properties.destinationNodes ?? [];
}

/**
 * @deprecated use connectionNodeIds unless you must call this synchronously.
 */
export function connectionNodeIdsSync(target: Connection, mapData: MapDataInternal, enterpriseMode: boolean): string[] {
	const nodeIds: string[] = [];
	for (const coordinate of target.coordinates) {
		const nearest = nearestNodeIdsSync(coordinate, mapData, enterpriseMode);
		if (nearest.length > 0) nodeIds.push(nearest[0]);
	}

	return nodeIds;
}

export async function connectionNodeIds(
	target: Connection,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): Promise<string[]> {
	const nodeIds: string[] = [];
	for (const coordinate of target.coordinates) {
		const nearest = await nearestNodeIds(coordinate, mapData, enterpriseMode);
		if (nearest.length > 0) nodeIds.push(nearest[0]);
	}

	return nodeIds;
}

export function mapObjectNodeIds(target: MapObject, mapData: MapDataInternal): string[] {
	return mapData?.objectEntranceNodeIdsByObstructionId[target.id] ?? [];
}

export function facadeNodeIds(target: Facade, mapData: MapDataInternal): string[] {
	return target.spaces
		.flatMap(space => mapData?.getMVFFeatureById('space', space.id)?.properties.destinationNodes ?? [])
		.filter(node => typeof node === 'string');
}

export function enterpriseLocationNodeIds(target: EnterpriseLocation): string[] {
	return target.nodes.map(node => node.id);
}

/** @deprecated use getNodesFromTarget unless you must call this synchronously. */
export function getNodesFromTargetSync(
	target: TNavigationTarget,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): string[] {
	if (featureNodeCache.has(target)) {
		return featureNodeCache.get(target)!;
	}
	if (LocationProfile.is(target)) {
		const nodeIds = locationProfileNodeIdsSync(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Annotation.is(target)) {
		const nodeIds = nearestNodeIdsSync(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Coordinate.is(target)) {
		const nodeIds = nearestNodeIdsSync(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (PointOfInterest.is(target)) {
		return nearestNodeIdsSync(target, mapData, enterpriseMode);
	} else if (Door.is(target)) {
		const nodeIds = doorNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Area.is(target)) {
		const nodeIds = areaNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Space.is(target)) {
		const nodeIds = spaceNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Connection.is(target)) {
		const nodeIds = connectionNodeIdsSync(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (MapObject.is(target)) {
		const nodeIds = mapObjectNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Facade.is(target)) {
		const nodeIds = facadeNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (EnterpriseLocation.is(target)) {
		const nodeIds = enterpriseLocationNodeIds(target);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Node.is(target)) {
		const nodeIds = [target.id];
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else {
		return [];
	}
}

/**
 * Get the nodes from the navigation target.
 *
 * @hidden
 * @param target
 * @param mapData
 */
export async function getNodesFromTarget(
	target: TNavigationTarget,
	mapData: MapDataInternal,
	enterpriseMode: boolean,
): Promise<string[]> {
	if (featureNodeCache.has(target)) {
		return featureNodeCache.get(target)!;
	}

	if (LocationProfile.is(target)) {
		const nodeIds = await locationProfileNodeIds(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Annotation.is(target)) {
		const nodeIds = await nearestNodeIds(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Coordinate.is(target)) {
		const nodeIds = await nearestNodeIds(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (PointOfInterest.is(target)) {
		const nodeIds = await nearestNodeIds(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Door.is(target)) {
		const nodeIds = doorNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Area.is(target)) {
		const nodeIds = areaNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Space.is(target)) {
		const nodeIds = spaceNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Connection.is(target)) {
		const nodeIds = await connectionNodeIds(target, mapData, enterpriseMode);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (MapObject.is(target)) {
		const nodeIds = mapObjectNodeIds(target, mapData);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Facade.is(target)) {
		return facadeNodeIds(target, mapData);
	} else if (EnterpriseLocation.is(target)) {
		const nodeIds = enterpriseLocationNodeIds(target);
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else if (Node.is(target)) {
		const nodeIds = [target.id];
		featureNodeCache.set(target, nodeIds);
		return nodeIds;
	} else {
		return [];
	}
}

export function getDepartureFromDirections(
	directions: DirectionsCollection,
	from: TNavigationTarget[],
	featureToNodeIdsMap: Map<TNavigationTarget, string[]>,
): TNavigationTarget {
	const firstNodeId = directions.features[0]?.properties.id;
	let departure = from[0];
	if (from.length > 1) {
		for (const target of from) {
			const nodeIds = featureToNodeIdsMap.get(target) ?? [];
			if (nodeIds.includes(firstNodeId)) {
				departure = target;
				break;
			}
		}
	}

	return departure;
}

export function getDestinationFromDirections(
	directions: DirectionsCollection,
	to: TNavigationTarget[],
	featureToNodeIdsMap: Map<TNavigationTarget, string[]>,
): TNavigationTarget {
	const lastNodeId = directions.features[directions.features.length - 1]?.properties.id;
	let destination = to[0];
	if (to.length > 1) {
		for (const target of to) {
			const nodeIds = featureToNodeIdsMap.get(target) ?? [];
			if (nodeIds.includes(lastNodeId)) {
				destination = target;
				break;
			}
		}
	}

	return destination;
}
