import type { FloorStack as MVFFloorStack } from '@mappedin/mvf-v2';
import type { MapDataInternal, Floor, Facade } from '.';
import BaseMapData from './base-object';
import { Logger } from '@packages/internal/common';

/**
 * A class representing floor stack data within the map.
 *
 * FloorStacks are used to represent a group of floors that are part of a single entity.
 */
class FloorStack extends BaseMapData {
	/**
	 * @internal
	 */
	static readonly __type = 'floor-stack';

	/**
	 * @internal
	 */
	readonly __type = FloorStack.__type;

	#data: MapDataInternal;
	#cache: Partial<{
		floors: [Floor, ...Floor[]];
		defaultFloor: Floor;
	}> = {};
	#mvfData: MVFFloorStack;
	#facadeId?: string;
	/**
	 * Checks if the provided instance is of type FloorStack.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a FloorStack, false otherwise.
	 */
	static is(instance: object): instance is FloorStack {
		return (instance as { __type: string }).__type === FloorStack.__type;
	}

	/**
	 * @internal
	 */
	constructor(data: MapDataInternal, options: { mvfData: MVFFloorStack; facadeId?: string }) {
		super(options.mvfData.id, data);
		this.#data = data;
		this.#mvfData = options.mvfData;
		this.#facadeId = options.facadeId;
	}

	#translate<T extends keyof MVFFloorStack>(prop: T) {
		return this.#data.getPropTranslation(this.__type, prop, this.id, this.#mvfData[prop as string]);
	}

	/**
	 * Gets the type of FloorStack.
	 */
	get type(): MVFFloorStack['type'] {
		return this.#mvfData.type;
	}

	/**
	 * Gets the name of the FloorStack.
	 *
	 * @returns {string} The name of the FloorStack.
	 */
	get name(): string {
		const name = this.#translate('name');
		return typeof name === 'string' ? name : '';
	}

	/**
	 * Gets the short name of the FloorStack.
	 *
	 * @returns {string} The short name of the FloorStack.
	 */
	get shortName(): string {
		const shortName = this.#translate('shortName');
		return typeof shortName === 'string' ? shortName : '';
	}

	/**
	 * Gets the externalId of FloorStack
	 */
	get externalId(): string {
		return this.#mvfData.externalId ?? '';
	}

	/**
	 * Gets the floors ({@link Floor}) included in this FloorStack.
	 *
	 * @returns {Floor[]} An array of Floor objects in this FloorStack.
	 */
	get floors(): [Floor, ...Floor[]] {
		if (this.#cache.floors == null) {
			const floorsInStack = (this.#mvfData.floors ?? this.#mvfData.maps ?? []).reduce((acc, floorId) => {
				const floor = this.#data.floorsById[floorId];

				if (floor) acc.push(floor);

				return acc;
			}, [] as Floor[]);

			if (floorsInStack.length === 0) {
				Logger.error(`Floor stack ${this.id} has no floors`);
			}

			// Sort floors by elevation
			this.#cache.floors = floorsInStack.sort((a, b) => a.elevation - b.elevation) as [Floor, ...Floor[]];
		}

		return this.#cache.floors;
	}

	/**
	 * Get the default floor for this floor stack
	 */
	get defaultFloor(): Floor {
		if (this.#cache.defaultFloor != null) {
			return this.#cache.defaultFloor;
		}

		const defaultFloor = this.#mvfData.defaultFloor
			? this.#data.getById('floor', this.#mvfData.defaultFloor)
			: undefined;

		this.#cache.defaultFloor =
			defaultFloor ||
			this.floors.reduce((prev, curr) => {
				return Math.abs(curr.elevation) < Math.abs(prev.elevation) ? curr : prev;
			});

		return this.#cache.defaultFloor;
	}

	/**
	 * Gets the facade ({@link Facade}) representing this floor stack, if it exists.
	 */
	get facade(): Facade | undefined {
		return this.#facadeId ? this.#data.facadesById[this.#facadeId] : undefined;
	}

	/**
	 * Serializes the FloorStack data to JSON.
	 *
	 * @returns An object representing the FloorStack.
	 */
	toJSON() {
		return {
			__type: FloorStack.__type,
			id: this.id,
			name: this.name,
			shortName: this.shortName,
			type: this.type,
			floors: this.floors.map(floor => floor.id),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of globalThis.Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default FloorStack;
