/* eslint-disable @typescript-eslint/ban-ts-comment */
import type { LocationLink, Hyperlink as MVFHyperlink } from '@mappedin/mvf-v2';
import { randomId } from '../utils/randomId';
import BaseMetaData from './base-metadata-object';

/**
 * A class representing hyperlink link data within the map.
 */
class Hyperlink extends BaseMetaData {
	/**
	 * @internal
	 */
	static readonly __type = 'hyperlink';

	/**
	 * @internal
	 */
	readonly __type = Hyperlink.__type;

	#cache: Partial<object> = {};
	#mvfData: MVFHyperlink | LocationLink;

	/**
	 * Checks if the provided instance is of type Hyperlink.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Hyperlink, false otherwise.
	 */
	static is(instance: object): instance is Hyperlink {
		return (instance as { __type: string }).__type === Hyperlink.__type;
	}

	/**
	 * @internal
	 */
	constructor(options: { mvfData: MVFHyperlink | LocationLink }) {
		super(String(randomId()));
		this.#mvfData = options.mvfData;
	}

	/**
	 * Gets the url of the hyperlink.
	 *
	 * @returns {string} The url of the hyperlink.
	 */
	get url(): string {
		return this.#mvfData.url;
	}

	/**
	 * Gets the name of the hyperlink.
	 *
	 * @returns {string | undefined } The name of the hyperlink.
	 */
	get name(): string | undefined {
		if ('displayName' in this.#mvfData) {
			return this.#mvfData.displayName;
		}

		if ('label' in this.#mvfData) {
			return this.#mvfData.label;
		}

		return undefined;
	}

	/**
	 * Serializes the hyperlink data to JSON.
	 *
	 * @returns An object representing the hyperlink.
	 */
	toJSON() {
		return {
			__type: Hyperlink.__type,
			id: this.id,
			url: this.url,
			name: this.name,
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of globalThis.Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Hyperlink;
