import { normalizeUrl } from '../../common/normalize-url';
import { SafeStorage } from '../../common/storage';
import { DEFAULT_ENTERPRISE_AUTH_URL, DEFAULT_V6_URI } from '../constants';
import { isomorphicFetch, type TGetMapDataWithCredentialsOptions } from '../mvf-utils';
import { shouldRefreshToken } from './utils';

export const ACCESS_TOKEN_REFRESH_BUFFER_MINUTES = 60;
export const ACCESS_TOKEN_REFRESH_BUFFER_MS = ACCESS_TOKEN_REFRESH_BUFFER_MINUTES * 60 * 1000;

export const ACCESS_TOKEN_STORAGE_KEY = 'accessTokens';

type AccessToken = {
	accessToken: string;
	expiresTimestamp: number;
};

type TGetAccessTokenOptions = Pick<
	TGetMapDataWithCredentialsOptions,
	'baseUri' | 'baseAuthUri' | 'key' | 'secret' | 'customAuthUri'
>;

/**
 * Get an access token from storage or fetch it from the API using API keys.
 */
export async function getAccessToken(userOptions: TGetAccessTokenOptions, enterprise = false): Promise<AccessToken> {
	const key = `${userOptions.key}`;
	const safeStorage = SafeStorage.getInstance();
	const cachedTokens = (safeStorage?.loadSessionData(ACCESS_TOKEN_STORAGE_KEY) || {}) as Record<string, AccessToken>;

	const currentTimestamp = Math.floor(Date.now() / 1000);

	// We have to store different access tokens for different Mappedin auth keys
	// to avoid conflicts when somebody uses multiple keys during the same session
	if (
		cachedTokens[key] &&
		!shouldRefreshToken(cachedTokens[key].expiresTimestamp * 1000, ACCESS_TOKEN_REFRESH_BUFFER_MINUTES)
	) {
		return cachedTokens[key];
	}

	const tokenData = enterprise
		? await fetchAccessTokenEnterprise(userOptions, currentTimestamp)
		: await fetchAccessToken(userOptions, currentTimestamp);
	const newTokens = { ...cachedTokens, [key]: tokenData };

	safeStorage?.saveSessionData(ACCESS_TOKEN_STORAGE_KEY, newTokens);

	return tokenData;
}

/**
 * Fetch an access token for enterprise.
 */
export async function fetchAccessTokenEnterprise(
	userOptions: TGetAccessTokenOptions,
	currentTimestamp: number,
): Promise<AccessToken> {
	let url: string | undefined;
	if (userOptions.customAuthUri) {
		url = userOptions.customAuthUri;
	} else {
		url = `${userOptions.baseAuthUri ?? DEFAULT_ENTERPRISE_AUTH_URL}oauth2/token`;
	}
	let response: Awaited<ReturnType<typeof fetch>>;

	try {
		response = await isomorphicFetch()(url, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				grant_type: 'client_credentials',
				client_id: userOptions.key,
				client_secret: userOptions.secret,
			}),
		});
	} catch (error) {
		const errorMessage = error instanceof Error ? error.message : String(error);
		throw new Error(`Failed to fetch enterprise access token from ${url}: ${errorMessage}`);
	}

	if (!response.ok) {
		throw new Error(`${response.status} ${response.statusText}: ${await response.text()}`);
	}

	const tokenData = (await response.json()) as unknown as { access_token: string; expires_in: number };

	return { accessToken: tokenData.access_token, expiresTimestamp: currentTimestamp + tokenData.expires_in };
}

/**
 * Fetch an access token for self-serve.
 */
async function fetchAccessToken(userOptions: TGetAccessTokenOptions, currentTimestamp: number): Promise<AccessToken> {
	let url: string | undefined;
	if (userOptions.customAuthUri) {
		url = userOptions.customAuthUri;
	} else {
		url = `${userOptions.baseAuthUri ? normalizeUrl(userOptions.baseAuthUri) : DEFAULT_V6_URI}api/v1/api-key/token`;
	}
	let response: Awaited<ReturnType<typeof fetch>>;
	try {
		response = await isomorphicFetch()(url, {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({ key: userOptions.key, secret: userOptions.secret }),
		});
	} catch (e) {
		const errorMessage = e instanceof Error ? e.message : String(e);
		throw new Error(`Failed to fetch access token from ${url}: ${errorMessage}`);
	}

	if (!response.ok) {
		throw new Error(`${response.status} ${response.statusText}: ${await response.text()}`);
	}

	const tokenData = (await response.json()) as unknown as { access_token: string; expires_in: number };

	return { accessToken: tokenData.access_token, expiresTimestamp: currentTimestamp + tokenData.expires_in };
}
