import { Connection, Door, Facade, type MapDataInternal, PointOfInterest } from '../../map-data-objects';
import type DetailedMapData from '../../map-data-objects/detailed-map-data';
import { type Label } from '../../map-view-objects';
import type { TAddLabelOptions } from '../../types';
import type { GeoJsonApi } from '../api';
import { type IconName, getBuiltInIcon, fetchIcon } from '../icons';
import { hasParkingCategory, hasWashroomCategory } from './utils';

type AutoLabelThing = DetailedMapData<any> | Facade;

export const LABEL_COLORS = {
	default: '#3D5676',
	connection: '#128387',
	washroom: '#3161B4',
	point: '#AD468F',
	parking: '#418425',
	door: '#A0552C',
} as const;

export const LABEL_ICONS = {
	elevator: 'connection-elevator-12x12',
	escalator: 'connection-stairs-12x12',
	stairs: 'connection-stairs-12x12',
	point: 'point-of-interest-12x12',
	door: 'door-12x12',
	building: 'building-12x12',
	ladder: 'ladder-12x12',
} as const;

function getDefaultIcon(thing: AutoLabelThing): string | null {
	let iconName: IconName | null = null;
	if (Connection.is(thing)) {
		switch (thing.type) {
			case 'elevator':
				iconName = LABEL_ICONS.elevator;
				break;
			case 'ladder':
				iconName = LABEL_ICONS.ladder;
				break;
			case 'escalator':
			case 'stairs':
			default:
				iconName = LABEL_ICONS.stairs;
		}
	} else if (PointOfInterest.is(thing)) {
		iconName = LABEL_ICONS.point;
	} else if (Door.is(thing)) {
		iconName = LABEL_ICONS.door;
	} else if (Facade.is(thing)) {
		iconName = LABEL_ICONS.building;
	}

	if (iconName != null) {
		return getBuiltInIcon(iconName, '#FFFFFF', 12);
	}

	return null;
}

function getLabelColor(thing: AutoLabelThing) {
	if (Connection.is(thing)) {
		return LABEL_COLORS.connection;
	} else if (Facade.is(thing)) {
		return LABEL_COLORS.default;
	} else if (hasWashroomCategory(thing)) {
		return LABEL_COLORS.washroom;
	} else if (hasParkingCategory(thing)) {
		return LABEL_COLORS.parking;
	} else if (PointOfInterest.is(thing)) {
		return LABEL_COLORS.point;
	} else if (Door.is(thing)) {
		return LABEL_COLORS.door;
	}

	return LABEL_COLORS.default;
}

async function getLabelIcon(thing: AutoLabelThing, options: { color?: string }) {
	let icon: Awaited<ReturnType<typeof fetchIcon>> | null = null;
	const url = 'locationProfiles' in thing ? thing.locationProfiles.find(location => location.icon != null)?.icon : null;
	if (url?.url != null) {
		icon = await fetchIcon(url.url, {
			color: options.color,
			style: 'fill',
		});
	}

	if (icon == null) {
		const defaultIcon = getDefaultIcon(thing);
		if (defaultIcon != null) {
			icon = { icon: defaultIcon, type: 'svg' };
		}
	}

	return icon;
}

async function getLabelRank(thing: AutoLabelThing): Promise<TAddLabelOptions['rank']> {
	if (Connection.is(thing)) {
		return 'low';
	} else if (Door.is(thing)) {
		return 'low';
	} else if (PointOfInterest.is(thing)) {
		return 'high';
	} else if (Facade.is(thing)) {
		return 'high';
	}
	return 'medium';
}

async function getLabelOptions(thing: AutoLabelThing): Promise<TAddLabelOptions> {
	const options: TAddLabelOptions = {
		appearance: {
			color: getLabelColor(thing),
			pinOutlineColor: getLabelColor(thing),
			iconPadding: 8,
			iconSize: 24,
			textSize: 12.5,
			iconScale: {
				on: 'zoom-level',
				input: [18, 20],
				output: [0.7, 1],
			},
		},
		rank: await getLabelRank(thing),
		interactive: true,
	};

	const icon = await getLabelIcon(thing, { color: '#FFFFFF' });
	if (icon != null) {
		options.appearance!.icon = icon.icon;
		if (icon.type === 'svg') {
			if (Facade.is(thing)) {
				options.appearance!.iconSize = 30;
				options.appearance!.iconPadding = 10;
			}
		} else if (icon.type === 'image') {
			options.appearance!.iconSize = 48;
			options.appearance!.iconPadding = 2;
			options.appearance!.pinOutlineColor = '#FFFFFF';
			// Image labels should have a high rank regardless of type
			options.rank = 'high';
		}
	}

	return options;
}

export function autoLabels(mapData: MapDataInternal, api: GeoJsonApi): Promise<Label[]> {
	const promises = [
		...mapData.getByType('connection'),
		...mapData.getByType('point-of-interest'),
		...mapData.getByType('space'),
		...mapData.getByType('object'),
		...mapData.getByType('door'),
		...mapData.getByType('facade'),
	].flatMap(async thing => {
		let labels: Label[] = [];
		const options = await getLabelOptions(thing);
		if (Connection.is(thing)) {
			// Connections always get a label with an icon
			labels = thing.coordinates.map(coordinate => api.Labels.add(coordinate, thing.name, options));
		} else if (Facade.is(thing)) {
			if (thing.spaces.length > 0 && thing.floorStack.name != null && thing.floorStack.name !== '') {
				// Make sure these get attached to a space, so they disappear when the facade is hidden
				labels.push(api.Labels.add(thing.spaces[0], thing.floorStack.name, options));
			}
		} else if ('name' in thing && thing.name != null && thing.name !== '') {
			labels.push(api.Labels.add(thing, thing.name, options));
		}
		return labels;
	});
	return Promise.all(promises).then(labels => labels.flat());
}
