import { Geometry3D, Geometry3DComponents } from '../../entities';
import type { All3DTypes, RendererState } from '../../renderer';
import { getGroupContainerOpacity } from '../../utils';
import { OUTLINE_MAX_ZOOM_LEVEL, OUTLINE_MIN_ZOOM_LEVEL } from './constants';
import { BufferAttribute, EdgesGeometry, FrontSide, LineSegments, Color } from 'three';
import { MeshComponent } from '../../components/mesh';
import { GeometryGroupObject3D } from '../../entities/geometry-group';
import { mergeGeometries } from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import type { OutlineComponent } from '../../components/outline';
import { hasOutline, needsOutline, updateBufferGeometryHeight } from './utils';
import { extractBufferGeometryFromBatchedGeometry } from '../mesh-detachment/utils';
import { interpolate, PubSub } from '@packages/internal/common';
import { OutlineLineMaterial } from './outline-material';
import { IdleQueue } from '../../utils/idle-queue';

const GEOMETRY_OUTLINE_THRESHOLD = 80;

// we should be able to use buffer attributes to control each individual spaces' outline color and opacity by storing them in the buffer
// instead of detaching them.

export class OutlineInterpolationSystem extends PubSub<{
	'needs-render': void;
}> {
	private rendererState: RendererState;
	/**
	 * Set to true when we need to update the outline opacity of all entities, regardless of zoom level.
	 */
	outlineOpacitiesDirty = true;

	/**
	 * Use IdleQueue to ensure the thread isn't blocked by building outlines
	 * @internal for unit tests only
	 */
	useIdleFramesOnly = true;

	/*
	 * set when geometries in the scene have changed, so we don't loop through all the geometries every render
	 */
	#geometries3DDirty = true;

	get geometries3DDirty() {
		return this.#geometries3DDirty;
	}

	set geometries3DDirty(value: boolean) {
		this.#geometries3DDirty = value;
	}

	#lastOpacity = -Infinity;

	/**
	 * Allow the building of outlines to occur when we have idle frames, so it doesn't impact initial load times
	 */
	idleBuildQueue = new IdleQueue();

	/**
	 * Based on perf testing computing bounding sphere can take a very long time,
	 * and we only need it for frustum checking, which can wait a little, so putting it into its own idle frame queue
	 */
	idleComputeBoundingSphereQueue = new IdleQueue();

	constructor(rendererState: RendererState) {
		super();
		this.rendererState = rendererState;
		/**
		 * Since the building of geometries is async, we need one more render to ensure the outline styles get applied
		 * This is called when the queue is empty
		 */
		this.idleBuildQueue.onComplete(this.#publishRender);
	}

	#mergedGeometries = new Map<GeometryGroupObject3D, [boolean, Geometry3D[]]>();

	#material = new OutlineLineMaterial({
		vertexColors: true,
		linewidth: 1,
		transparent: true,
		opacity: 1.0,
		side: FrontSide,
	});

	buildOutlines(geometryGroupEntity: GeometryGroupObject3D) {
		const edgeGeometries: [OutlineComponent, EdgesGeometry][] = [];
		for (const childEntityId of geometryGroupEntity.userData.entities3D.values()) {
			const childGeometry3DEntity = this.rendererState.geometry3DMap.get(childEntityId);

			if (
				childGeometry3DEntity instanceof Geometry3D &&
				childGeometry3DEntity.type === 'geometry' &&
				childGeometry3DEntity.components[Geometry3DComponents.Outline] &&
				childGeometry3DEntity.components[Geometry3DComponents.Outline].dirty === true &&
				childGeometry3DEntity.components[Geometry3DComponents.Mesh].material != null
			) {
				let geometry = childGeometry3DEntity.components[Geometry3DComponents.Mesh].geometry;
				let needsDisposal = false;
				if (!geometry) {
					geometry = extractBufferGeometryFromBatchedGeometry(
						childGeometry3DEntity.components[Geometry3DComponents.Mesh],
					);
					needsDisposal = true;
				}

				const style = childGeometry3DEntity.components[Geometry3DComponents.Style];
				edgeGeometries.push([
					childGeometry3DEntity.components[Geometry3DComponents.Outline],
					// The default 80-degree threshold is designed for sharp edges in simple extruded shapes. Beveled geometries have smooth transitions with angles much smaller than 80 degrees, so they need a lower threshold to detect edges properly.
					new EdgesGeometry(geometry, style.bevel?.enabled ? 1 : GEOMETRY_OUTLINE_THRESHOLD), //
				]);

				if (needsDisposal) {
					geometry?.dispose();
					geometry = undefined;
				}
			}
		}

		if (edgeGeometries.length === 0) {
			return;
		}

		// merge individual outlines into single draw call
		const mergedGeometry = mergeGeometries(
			edgeGeometries.map(e => e[1]),
			false,
		);

		if (mergedGeometry == null) {
			return;
		}

		// create buffers for updates
		const edgePositions = mergedGeometry.getAttribute('position');
		const edgeColors = new Float32Array(edgePositions.count * 4);
		const colorBuffer = new BufferAttribute(edgeColors, 4);
		mergedGeometry.setAttribute('color', colorBuffer);

		const edgeVisibility = new Uint8Array(edgePositions.count);
		edgeVisibility.fill(0); // Start with everything hidden
		const visibilityBuffer = new BufferAttribute(edgeVisibility, 1, false);
		mergedGeometry.setAttribute('visibility', visibilityBuffer);

		// Calculate ranges based on the original edge geometries
		let currentStart = 0;
		for (let i = 0; i < edgeGeometries.length; i++) {
			const [outlineComponent, individualGeometry] = edgeGeometries[i];
			const vertexCount = individualGeometry.getAttribute('position').count;

			outlineComponent.edgeColors = colorBuffer;
			outlineComponent.edgeVisibility = visibilityBuffer;
			outlineComponent.geometry = mergedGeometry;

			outlineComponent.ranges = {
				count: vertexCount,
				start: currentStart,
			};

			currentStart += vertexCount;
			// dispose of the individual geometry as we don't need it anymore
			individualGeometry.dispose();
		}

		const outline = new LineSegments(mergedGeometry, this.#material);
		outline.userData.isOutline = true;
		geometryGroupEntity.add(outline);
	}

	/**
	 * Update the outline color of all entities with an outline component.
	 * @param zoomLevel - The current zoom level.
	 * @param isCameraZoomDirty - Whether the zoom level has changed since the last update. If this is true, the system will update if zoomLevel falls between range.
	 */
	update(zoomLevel: number, isCameraZoomDirty = true) {
		let geometryGroupEntity: All3DTypes | undefined;
		if (this.geometries3DDirty) {
			for (const entityId of this.rendererState.geometry3DIdsInScene) {
				const geometryGroupEntity = this.rendererState.geometry3DMap.get(entityId);
				if (
					geometryGroupEntity instanceof GeometryGroupObject3D &&
					needsOutline(geometryGroupEntity, this.rendererState) &&
					!hasOutline(geometryGroupEntity) &&
					geometryGroupEntity.userData.entities3D.size > 0
				) {
					if (this.useIdleFramesOnly) {
						this.idleBuildQueue.add(() => this.buildOutlines(geometryGroupEntity));
					} else {
						this.buildOutlines(geometryGroupEntity);
					}
				}
			}

			this.#geometries3DDirty = false;
			this.outlineOpacitiesDirty = true;
		}

		// update color and height
		for (const entityId of this.rendererState.geometry3DIdsInScene) {
			const entity = this.rendererState.geometry3DMap.get(entityId);

			if (
				entity instanceof Geometry3D &&
				entity.type === 'geometry' &&
				entity.components[Geometry3DComponents.Outline] &&
				entity.components[Geometry3DComponents.Outline].dirty === true &&
				entity.components[Geometry3DComponents.Mesh].material != null &&
				entity.parentObject3D instanceof GeometryGroupObject3D
			) {
				const outlineMergedGeometry = entity.components[Geometry3DComponents.Outline].geometry;
				if (!outlineMergedGeometry) continue;

				const outlineComponent = entity.components[Geometry3DComponents.Outline];
				if (!outlineComponent) continue;

				const visible = outlineComponent.enabled && entity.components[Geometry3DComponents.Style].visible;
				if (outlineComponent.visible !== visible) {
					outlineComponent.visible = visible;
				}

				if (!outlineComponent.visible) continue;

				const edgeColors = outlineComponent.edgeColors;
				if (!edgeColors) continue;

				if (!outlineComponent.ranges) continue;

				const color = new Color(outlineComponent.color);

				for (let i = 0; i < outlineComponent.ranges!.count; i++) {
					const vertexIndex = outlineComponent.ranges!.start + i;
					edgeColors.setXYZ(vertexIndex, color.r, color.g, color.b);
				}

				// let's update the height of the outline
				const height = entity.components[Geometry3DComponents.Style].height;

				const altitude = entity.components[Geometry3DComponents.Style].altitude;

				// updateBufferGeometryHeight logic was designed for simple extruded shapes with flat tops and bottoms. It flattens geometry to just two Z-levels (altitude and altitude+height), which destroys the intermediate Z-values that create the beveled profile.
				const hasBevel = entity.components[Geometry3DComponents.Style]?.bevel?.enabled;
				if (!hasBevel) {
					updateBufferGeometryHeight(outlineMergedGeometry, outlineComponent.ranges, height, altitude);
				}

				outlineMergedGeometry.attributes.color.needsUpdate = true;

				// Recompute bounding sphere after modifying positions for proper frustum culling
				this.idleComputeBoundingSphereQueue.add(() => outlineMergedGeometry.computeBoundingSphere());
				outlineComponent.dirty = false;
			}
		}

		const outlineOpacity = isCameraZoomDirty
			? interpolate(zoomLevel, OUTLINE_MIN_ZOOM_LEVEL, OUTLINE_MAX_ZOOM_LEVEL, 0, 1)
			: this.#lastOpacity;

		if (outlineOpacity !== this.#lastOpacity || this.outlineOpacitiesDirty) {
			if (this.rendererState.geometry3DIdsInScene.size > 0) {
				for (const entityId of this.rendererState.geometry3DIdsInScene) {
					geometryGroupEntity = this.rendererState.geometry3DMap.get(entityId);
					if (
						geometryGroupEntity?.type === 'geometry' &&
						geometryGroupEntity.components[Geometry3DComponents.Mesh] instanceof MeshComponent
					) {
						const outline = geometryGroupEntity.components[Geometry3DComponents.Outline];
						if (outline && outline.edgeColors && outline.geometry && outline.visible) {
							const targetOpacity = Math.min(
								outlineOpacity,
								geometryGroupEntity.components[Geometry3DComponents.Style].opacity *
									getGroupContainerOpacity(geometryGroupEntity),
							);

							if (outline.currentOpacity !== targetOpacity) {
								for (let i = 0; i < outline.ranges!.count; i++) {
									const vertexIndex = outline.ranges!.start + i;
									outline.edgeColors.setW(vertexIndex, targetOpacity);
								}
								outline.geometry.attributes.color.needsUpdate = true;
							}
						}
					}
				}
			}
		}

		this.#lastOpacity = outlineOpacity;
		this.#geometries3DDirty = false;
		this.outlineOpacitiesDirty = false;
	}

	#publishRender = () => {
		this.publish('needs-render');
	};

	destroy() {
		this.#mergedGeometries.clear();
		this.idleBuildQueue.clearAll();
		this.idleComputeBoundingSphereQueue.clearAll();
	}
}
