import { type All3DTypes, type RendererState } from '../../renderer';
import { Geometry3D, Geometry3DComponents } from '../../entities';
import { EntityBatchedMesh, MeshComponent } from '../../components/mesh';
import { GeometryGroupObject3D } from '../../entities/geometry-group';
import { StyleComponent } from '../../components/styles/style';
import { Color, DoubleSide, Matrix4 } from 'three';
import { BatchedStandardMaterial } from '../mesh-creation-and-optimization/batched-material';
import { getGroupContainerOpacity } from '../../utils';
import { transferGeometryToNewBatchedMesh } from './utils';

/**
 * This system is responsible for updating the opacity of the mesh, which results in the mesh
 * being detached from the parent's optimized geometry group and rendered as a separate mesh.
 */
export class MeshDetachmentSystem {
	rendererState: RendererState;
	dirty = false;
	constructor(rendererState: RendererState) {
		this.rendererState = rendererState;
	}

	#meshComponentBeingProcessed?: MeshComponent;
	#geometryGroupBeingProcessed?: GeometryGroupObject3D;
	#styleComponentBeingProcessed?: StyleComponent;
	#processingMatrix = new Matrix4();

	updateOne(geometry: All3DTypes | undefined) {
		if (geometry instanceof Geometry3D) {
			if (
				geometry.components[Geometry3DComponents.Mesh] instanceof MeshComponent &&
				geometry.components[Geometry3DComponents.Style] instanceof StyleComponent &&
				geometry.parentObject3D instanceof GeometryGroupObject3D
			) {
				this.#meshComponentBeingProcessed = geometry.components[Geometry3DComponents.Mesh];
				this.#styleComponentBeingProcessed = geometry.components[Geometry3DComponents.Style];

				if (
					!this.#meshComponentBeingProcessed.detached &&
					this.#meshComponentBeingProcessed.shouldDetach &&
					this.#meshComponentBeingProcessed.mesh != null &&
					this.#meshComponentBeingProcessed.material != null &&
					this.#meshComponentBeingProcessed.batchedProps != null
				) {
					this.#geometryGroupBeingProcessed = geometry.parentObject3D;
					this.#meshComponentBeingProcessed.visible = false;
					const color = new Color(this.#styleComponentBeingProcessed.color);
					const opacity = this.#styleComponentBeingProcessed.opacity * getGroupContainerOpacity(geometry);

					// TODO: a bigger change but we should be able to use a regular Mesh and MeshLambertMaterial for these
					// This isn't a huge deal as we're creating batched material and mesh only for one geometry but there's probably
					// some overhead that could be avoided
					const newMaterial = new BatchedStandardMaterial(
						{
							color,
							side: DoubleSide,
							opacity: opacity ?? 1,
							transparent: opacity !== undefined ? opacity < 1 : false,
						},
						1,
						this.#meshComponentBeingProcessed.material.repeatYFactor,
					);

					const newMesh = new EntityBatchedMesh(
						1,
						this.#meshComponentBeingProcessed.batchedProps.positionCount,
						0,
						newMaterial,
					);

					const instance = transferGeometryToNewBatchedMesh(
						this.#meshComponentBeingProcessed,
						newMesh,
						this.#processingMatrix,
					);

					if (instance == null) {
						return;
					}

					// this is needed for interaction system to work
					newMesh.userData = {
						entities: {
							0: geometry.id,
						},
						detached: true,
					};

					this.#meshComponentBeingProcessed.instanceIndex = instance;
					this.#meshComponentBeingProcessed.material.setColor(
						this.#meshComponentBeingProcessed.instanceIndex,
						color,
						this.#styleComponentBeingProcessed.topColor
							? new Color(this.#styleComponentBeingProcessed.topColor)
							: color,
					);

					this.#meshComponentBeingProcessed.mesh = newMesh;
					this.#meshComponentBeingProcessed.material = newMaterial;

					this.#geometryGroupBeingProcessed.add(newMesh);
					this.#meshComponentBeingProcessed.shouldDetach = false;
					this.#meshComponentBeingProcessed.detached = true;
				}
			}
		}
	}

	update() {
		if (!this.dirty) {
			return;
		}
		let geometry: All3DTypes | undefined;
		for (let geometryId of this.rendererState.geometry3DIdsInScene) {
			geometry = this.rendererState.geometry3DMap.get(geometryId);
			this.updateOne(geometry);
		}
		this.dirty = false;
		this.cleanup();
	}

	private cleanup() {
		this.#meshComponentBeingProcessed = undefined;
		this.#geometryGroupBeingProcessed = undefined;
		this.#styleComponentBeingProcessed = undefined;
	}
}
