import type {
	Feature,
	FeatureCollection,
	Point,
	SpaceProperties,
	Connection as MVFConnection,
	Position,
} from '@mappedin/mvf-v2';
import type Coordinate from './coordinate';
import type Node from './node';
import type { Floor, LocationProfile, MapDataInternal } from '.';
import { notNull, serializeCoords } from '../utils';
import type { IFocusable, TConnectionType } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * A class representing connection data within the map.
 *
 * Connections are used to represent pathways between different map nodes.
 */
class Connection
	extends DetailedMapData<Feature<Point, SpaceProperties> | Feature<Point, MVFConnection>>
	implements IFocusable
{
	/**
	 * @internal
	 */
	static readonly __type = 'connection';

	/**
	 * @internal
	 */
	readonly __type = Connection.__type;

	#data: MapDataInternal;
	#cache: Partial<{ coordinates: Coordinate[]; locationProfiles: LocationProfile[] }>;
	// Connections are spaces in the MVF
	#mvfDataByFloorId:
		| Record<string, FeatureCollection<Point, SpaceProperties>['features'][number]>
		| Record<string, Feature<Point, MVFConnection>>;

	/**
	 * Checks if the provided instance is of type Connection.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Connection, false otherwise.
	 */
	static is(instance: object): instance is Connection {
		return (instance as { __type: string }).__type === Connection.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			mvfDataByFloorId:
				| Record<string, FeatureCollection<Point, SpaceProperties>['features'][number]>
				| Record<string, Feature<Point, MVFConnection>>;
			accessible?: boolean;
		},
	) {
		const singularData = Object.values(options.mvfDataByFloorId)[0] as
			| FeatureCollection<Point, SpaceProperties>['features'][number]
			| Feature<Point, MVFConnection>;
		// TODO: Derive a more stable ID from all the MVF data.
		super(data, singularData);
		this.#cache = {};
		this.#data = data;
		this.#mvfDataByFloorId = options.mvfDataByFloorId;
	}

	/**
	 * Whether the connection is accessible. For example elevators are accessible while stairs are not.
	 *
	 * @returns {boolean} Whether the connection is accessible.
	 */
	get accessible(): boolean {
		return Object.values(this.#mvfDataByFloorId)[0].properties.accessible ?? false;
	}

	/**
	 * Gets the type of the connection.
	 *
	 * @returns {TConnectionType} The type of the connection.
	 */
	get type(): TConnectionType {
		// All the MVF connection spaces should share a kind, so we can just grab the first one.
		return Object.values(this.#mvfDataByFloorId)[0].properties.kind?.split('.')[1] ?? 'unknown';
	}

	/**
	 * Extra properties of the connection.
	 *
	 * @returns {Record<string, unknown> | undefined} Extra properties of the connection.
	 */
	get extra(): Record<string, unknown> | undefined {
		return Object.values(this.#mvfDataByFloorId)[0].properties.extra;
	}

	/**
	 * Gets the coordinates ({@link Coordinate}) of the connection.
	 *
	 * @returns {Coordinate[]} An array of coordinates for the connection.
	 */
	get coordinates() {
		if (this.#cache.coordinates == null) {
			this.#cache.coordinates = Object.entries(this.#mvfDataByFloorId).map(([floorId, mvfData]) =>
				getCenterCoordinate(mvfData, floorId),
			);
		}

		return this.#cache.coordinates;
	}

	/**
	 * Gets the nodes ({@link Node}) associated with the connection.
	 */
	get nodes(): Node[] {
		return Object.values(this.#mvfDataByFloorId)[0].properties.nodes.map(nodeId => this.#data.getById('node', nodeId));
	}

	/**
	 * Gets the floors ({@link Floor}) associated with the connection.
	 *
	 * @returns {Floor[]} An array of floors for the connection.
	 */
	get floors(): Floor[] {
		return this.coordinates
			.map(coordinate => (coordinate.floorId ? this.#data.getById('floor', coordinate.floorId) : undefined))
			.filter(notNull);
	}

	/**
	 * Gets the location profiles ({@link LocationProfile}) associated with the connection.
	 */
	get locationProfiles() {
		if (this.#cache.locationProfiles != null) {
			return this.#cache.locationProfiles;
		}

		/**
		 * Location profiles are attached to the connection in the connections.json, not the connection space
		 * in the space collections. We need to jump through some hoops to find them.
		 *
		 * TODO: Fix this for MVFv3
		 */
		let profiles = super.locationProfiles;
		if (profiles == null || profiles.length === 0) {
			const position: Position = [this.coordinates[0]?.longitude ?? 0, this.coordinates[0]?.latitude ?? 0];
			const latLonString = serializeCoords(position);
			const mvfConnectionIds = this.#data.mvfConnectionIdsByLatLon[latLonString] ?? [];
			for (const connectionId of mvfConnectionIds) {
				const maybeProfiles = this.#data.locationProfilesByAttachedFeatureId[connectionId];
				if (maybeProfiles != null) {
					profiles = maybeProfiles;
					break;
				}
			}
		}
		this.#cache.locationProfiles = profiles ?? [];

		return profiles;
	}

	/** @internal */
	get focusTarget() {
		return this.coordinates;
	}

	/**
	 * Serializes the connection data to JSON.
	 *
	 * @returns An object representing the connection.
	 */
	toJSON() {
		return {
			__type: Connection.__type,
			id: this.id,
			name: this.name,
			type: this.type,
			externalId: this.externalId,
			coordinates: this.coordinates.map(coordinate => coordinate.toJSON()),
			floors: this.floors.map(floor => floor.id),
			extra: this.extra,
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Connection;
