import { normalizeUrl } from '../../common/normalize-url';
import { SafeStorage } from '../../common/storage';
import { DEFAULT_V6_URI } from '../constants';
import { isomorphicFetch } from '../mvf-utils';
import { shouldRefreshToken } from './utils';

export const SAS_TOKEN_REFRESH_BUFFER_MINUTES = 60;
export const SAS_TOKEN_REFRESH_BUFFER_MS = SAS_TOKEN_REFRESH_BUFFER_MINUTES * 60 * 1000;
export const SAS_TOKEN_STORAGE_KEY = 'miSasTokens';

type SasToken = {
	token: string;
	expires: number;
};

/**
 * Fetch the SAS Token either from sesion storage or from the API using the access token.
 * @param userOptions - The user options.
 * @returns The SAS Token.
 */
export async function getSasToken(
	userOptions: { mapId: string; baseUri?: string },
	accessToken: string,
): Promise<SasToken> {
	const key = `${accessToken}__${userOptions.mapId}`;
	const safeStorage = SafeStorage.getInstance();
	const cachedTokens = (safeStorage?.loadSessionData(SAS_TOKEN_STORAGE_KEY) || {}) as Record<string, SasToken>;

	// We have to store different sas tokens for different access tokens
	// to avoid conflicts when somebody uses multiple keys during the same session
	if (cachedTokens[key] && !shouldRefreshToken(cachedTokens[key].expires, SAS_TOKEN_REFRESH_BUFFER_MINUTES)) {
		return cachedTokens[key];
	}

	const tokenData = await fetchSasToken(userOptions, accessToken);
	const newTokens = {
		...cachedTokens,
		[key]: { token: tokenData.token, expires: new Date(tokenData.expires).getTime() },
	};

	safeStorage?.saveSessionData(SAS_TOKEN_STORAGE_KEY, newTokens);

	return tokenData;
}

/**
 * Fetch the SAS Token for private images using the access token.
 * @param userOptions - The user options.
 * @param accessToken - The access token.
 * @returns The SAS Token.
 */
export async function fetchSasToken(userOptions: { mapId: string; baseUri?: string }, accessToken: string) {
	const url = `${userOptions.baseUri ? normalizeUrl(userOptions.baseUri) : DEFAULT_V6_URI}api/v1/venue/${
		userOptions.mapId
	}/sas-token`;
	const response = await isomorphicFetch()(url, {
		method: 'GET',
		headers: { Authorization: `Bearer ${accessToken}` },
	});

	if (!response.ok) {
		throw new Error(`${response.status} ${response.statusText}: ${await response.text()}`);
	}

	const tokenData = (await response.json()) as unknown as SasToken;

	return {
		token: tokenData.token,
		expires: new Date(tokenData.expires).getTime(),
	};
}
