import type { RendererState } from '../../renderer';
import { PubSub } from '@packages/internal/common';
import { Geometry3D, Geometry3DComponents } from '../../entities';
import { type Texture } from 'three';
import { MeshComponent } from '../../components/mesh';
import { TextureComponent } from '../../components/texture';
import { getMaterial } from '../draw/utils';
import { TextureLoader } from '../../utils/texture-loader';

export class TextureSystem extends PubSub<{
	'texture-added': void;
}> {
	dirty = true;
	state: RendererState;
	constructor(state: RendererState) {
		super();

		this.state = state;
	}

	texturePromiseCache: Map<string, Promise<Texture>> = new Map();

	async loadTexture(path: string) {
		if (this.texturePromiseCache.has(path)) {
			return this.texturePromiseCache.get(path);
		}
		const promise = new Promise<Texture>((resolve, reject) => {
			TextureLoader.getLoader().load(path, resolve, undefined, reject);
		});
		this.texturePromiseCache.set(path, promise);
		return promise;
	}

	update() {
		for (const geometryId of this.state.geometry3DIdsInScene) {
			const geometry = this.state.geometry3DMap.get(geometryId);
			if (!(geometry instanceof Geometry3D)) continue;

			const textureComponent = geometry.components[Geometry3DComponents.TextureComponent];
			if (!(textureComponent instanceof TextureComponent)) continue;

			const meshComponent = geometry.components[Geometry3DComponents.Mesh];
			if (!(meshComponent instanceof MeshComponent)) continue;

			if (!textureComponent.dirty) continue;

			const material = getMaterial(meshComponent);
			if (material == null) continue;

			if (textureComponent.texture != null) {
				if (textureComponent.texture.path === '') {
					material.texture = null;
					material.hideTextures(meshComponent.instanceIndex);
					textureComponent.dirty = false;
				} else {
					this.loadTexture(textureComponent.texture.path).then(texture => {
						if (texture) {
							textureComponent.textureInstance = texture;
							material.texture = texture;
							material.blendTexture = true;
							material.showTextures(meshComponent.instanceIndex);
						}
						this.publish('texture-added');
					});
				}
			}
			if (textureComponent.topTexture != null) {
				if (textureComponent.topTexture.path === '') {
					material.topTexture = null;
					material.hideTextures(meshComponent.instanceIndex);
					textureComponent.dirty = false;
				} else {
					this.loadTexture(textureComponent.topTexture.path).then(texture => {
						if (texture) {
							textureComponent.topTextureInstance = texture;
							material.topTexture = texture;
							material.blendTexture = true;
							material.showTextures(meshComponent.instanceIndex);
						}
						this.publish('texture-added');
					});
				}
			}
			textureComponent.dirty = false;
		}
	}

	destroy() {
		this.texturePromiseCache.forEach(async t => {
			(await t).dispose();
		});
		this.texturePromiseCache.clear();
	}
}
