import type { Language } from '@mappedin/mvf-v2';
import { isBrowser, Logger } from '@packages/internal/common';
import type { EnterpriseVenue } from '../map-data-objects';

export type TFindPreferredLanguageInVenueOptions = {
	/**
	 * Preferred language code to use.
	 */
	languageCode?: string;
	/**
	 * Whether to use browser's language settings as fallback if the supplied language code is not available.
	 * @default true
	 * */
	fallbackToNavigatorLanguage?: boolean;
};

/**
 * Finds the first language that exists in both the venue and navigator.
 * @param venue - The MappedinVenue object containing available languages
 * @returns The preferred language, or undefined if no language is found
 */
export function findNavigatorPreferredLanguageInVenue(venue: EnterpriseVenue): Language | undefined {
	if (!venue.languages || !isBrowser() || !globalThis.navigator?.languages?.length) return;
	for (let i = 0; i < globalThis.navigator.languages.length; i++) {
		// find the first language that exists in the venue and navigator
		const language = venue.languages.find(
			l => l.code.toLowerCase() === globalThis.navigator.languages[i].toLowerCase(),
		);
		if (language) {
			return {
				code: language.code,
				name: language.name,
			};
		}
	}

	return;
}

/**
 * Finds the ideal language for the venue based on the following priority:
 * 1. User specified language (if it exists in venue languages)
 * 2. Browser navigator languages (if fallback is enabled)
 * 3. Venue's default language
 *
 * @param venue - The MappedinVenue object containing available languages
 * @param options - Configuration options
 * @returns An object containing the selected language code and name, or undefined if no language is found
 */
export function findPreferredLanguageInVenue(
	venue: EnterpriseVenue,
	options?: TFindPreferredLanguageInVenueOptions,
): Language {
	const languageCode = options?.languageCode ?? '';
	const fallbackToNavigatorLanguage = options?.fallbackToNavigatorLanguage ?? true;

	const preferredLanguageInVenue = venue?.languages?.find(l => l.code.toLowerCase() === languageCode.toLowerCase());
	let result: Language | undefined;

	if (!preferredLanguageInVenue) {
		if (fallbackToNavigatorLanguage ?? true) {
			try {
				const navigatorLanguage = findNavigatorPreferredLanguageInVenue(venue);
				if (navigatorLanguage) {
					result = navigatorLanguage;
				}
			} catch (e) {
				Logger.error(
					'fallbackToNavigatorLanguage is set, however, there was an error getting the navigator language',
					e,
				);
			}
		}
	} else {
		result = preferredLanguageInVenue;
	}

	return result ?? venue.defaultLanguage;
}
