export const OBSTRUCTION_KINDS_TO_SKIP = new Set(['wall', 'area']);
export const SPACE_KINDS_TO_SKIP = new Set(['void', 'desk-entrance']);

export type MapDataRecords = {
	// Primary ID Lookups
	spacesById: Record<string, Space>;
	nodesById: Record<string, Node>;
	objectsById: Record<string, MapObject>;
	floorsById: Record<string, Floor>;
	floorStacksById: Record<string, FloorStack>;
	facadesById: Record<string, Facade>;
	facadesBySpaceId: Record<string, Facade>;
	connectionsById: Record<string, Connection>;
	doorsById: Record<string, Door>;
	doorsByNodeId: Record<string, Door>;
	poisById: Record<string, PointOfInterest>;
	annotationsById: Record<string, Annotation>;
	areasById: Record<AreaId, Area>;
	spaceIdsByNodeId: Record<string, SpaceId[]>;
	objectIdsByNodeId: Record<string, ObstructionId[]>;
	destinationNodesIdsBySpaceId: Record<SpaceId, NodeId[]>;
	/**
	 * Map of space id to door-linked node ids for fast routing.
	 */
	entranceNodeIdsBySpaceId?: Record<string, string[]>;
	locationProfilesById: Record<string, LocationProfile>;
	locationCategoriesById: Record<string, LocationCategory>;
	locationProfilesByCategoryId: Record<string, LocationProfile[]>;
	locationCategoriesByParentId: Record<string, LocationCategory[]>;

	// External ID Lookups
	spacesByExternalId: Record<string, Space[]>;
	nodesByExternalId: Record<string, Node[]>;
	poisByExternalId: Record<string, PointOfInterest[]>;
	doorsByExternalId: Record<string, Door[]>;
	floorStacksByExternalId: Record<string, FloorStack[]>;
	floorsByExternalId: Record<string, Floor[]>;
	objectsByExternalId: Record<string, MapObject[]>;
	areasByExternalId: Record<string, Area[]>;
	locationProfilesByExternalId: Record<string, LocationProfile[]>;

	// Special records
	objectEntranceNodeIdsByObstructionId: Record<string, string[]>;
	obstructionIdByEntranceId: Record<string, string>;
	connectionIdsByLatLon: Record<string, string[]>;
	mvfConnectionIdsByLatLon: Record<string, string[]>;
	locationProfilesByAttachedFeatureId: Record<string, LocationProfile[]>;

	// MVF data types
	mvfSpacesById: Record<string, SpaceCollection['features'][number]>;
	mvfNodesById: Record<string, NodeCollection['features'][number]>;
	mvfObstructionById: Record<string, ObstructionCollection['features'][number]>;
	mvfFloorsById: Record<string, MVFFloor>;
	mvfFloorStacksById: Record<string, MVFFloorStack>;
	mvfConnectionsById: Record<string, MVFConnection>;
	mvfConnectionsByNodeId: Record<string, MVFConnection>;
	mvfEntrancesById: Record<string, EntranceCollection['features'][number]>;
	mvfAnnotationsById: Record<string, AnnotationCollection['features'][number]>;
	mvfNodesByFloorId: { [floorId: string]: NodeCollection['features'][number][] };
	mvfSpacesByFloorId: { [floorId: string]: SpaceCollection['features'][number][] };
	mvfPoisByFloorId: { [floorId: string]: FeatureCollection<Point, SpaceProperties>['features'][number][] };
	mvfEntrancesByFloorId: { [floorId: string]: EntranceCollection['features'][number][] };
	mvfAnnotationsByFloorId: { [floorId: string]: AnnotationCollection['features'][number][] };
	mvfAreasById: Record<AreaId, AreaCollection['features'][number]>;
};

export type EnterpriseMapDataRecords = {
	connectionsByExternalId: Record<string, Connection[]>;
	locationsByExternalId: Record<string, EnterpriseLocation[]>;
	categoriesByExternalId: Record<string, EnterpriseCategory[]>;
	locationsById: Record<MVFEnterpriseLocationId, EnterpriseLocation>;
	categoriesById: Record<MVFEnterpriseCategoryId, EnterpriseCategory>;
	locationIdsByNodeId: Record<string, MVFEnterpriseLocationId[]>;
	locationInstancesById: Record<string, EnterpriseLocationInstance>;
	venue: MVFEnterpriseVenue;

	mvfCategoriesById: Record<string, MVFEnterpriseCategory>;
	mvfLocationsById: Record<string, MVFEnterpriseLocation>;
	mvfLocationsByGeometryId: Record<string, MVFEnterpriseLocation[]>;
};

export const processMVFLocationProfilesAndCategories = (mvf: ParsedMVF, data: MapDataInternal) => {
	const locationProfilesById: MapDataRecords['locationProfilesById'] = {};
	const locationCategoriesById: MapDataRecords['locationCategoriesById'] = {};
	const locationCategoriesByParentId: MapDataRecords['locationCategoriesByParentId'] = {};
	const locationProfilesByExternalId: MapDataRecords['locationProfilesByExternalId'] = {};
	const locationProfilesByCategoryId: MapDataRecords['locationProfilesByCategoryId'] = {};
	const locationProfilesByAttachedFeatureId: MapDataRecords['locationProfilesByAttachedFeatureId'] = {};

	for (const locationProfile of mvf['location.json'] || []) {
		const newLocationProfile = new LocationProfile(data, {
			mvfData: locationProfile,
		});
		locationProfilesById[locationProfile.id] = newLocationProfile;
		if (locationProfile.externalId) {
			if (locationProfilesByExternalId[locationProfile.externalId] == null) {
				locationProfilesByExternalId[locationProfile.externalId] = [];
			}
			locationProfilesByExternalId[locationProfile.externalId].push(newLocationProfile);
		}

		if (locationProfile.categories) {
			for (const categoryId of locationProfile.categories) {
				if (locationProfilesByCategoryId[categoryId] == null) {
					locationProfilesByCategoryId[categoryId] = [];
				}
				locationProfilesByCategoryId[categoryId].push(newLocationProfile);
			}
		}

		// Make these all optional to handle old MVFs
		const attachedFeatures = [
			...(locationProfile.obstructions?.map(obstruction => obstruction.id) ?? []),
			...(locationProfile.spaces?.map(space => space.id) ?? []),
			...(locationProfile.annotations?.map(annotation => annotation.id) ?? []),
			...(locationProfile.connections ?? []),
			...(locationProfile.entrances?.map(entrance => entrance.id) ?? []),
			...(locationProfile.areas?.map(area => area.id) ?? []),
		];

		for (const featureId of attachedFeatures) {
			if (featureId) {
				if (locationProfilesByAttachedFeatureId[featureId] == null) {
					locationProfilesByAttachedFeatureId[featureId] = [];
				}
				locationProfilesByAttachedFeatureId[featureId].push(newLocationProfile);
			}
		}
	}

	for (const locationCategory of mvf['category.json'] || []) {
		const newLocationCategory = new LocationCategory(data, {
			mvfData: locationCategory,
		});
		locationCategoriesById[locationCategory.id] = newLocationCategory;
		if (locationCategory.parent) {
			if (locationCategoriesByParentId[locationCategory.parent] == null) {
				locationCategoriesByParentId[locationCategory.parent] = [];
			}
			locationCategoriesByParentId[locationCategory.parent].push(newLocationCategory);
		}
	}

	return {
		locationCategoriesByParentId,
		locationProfilesById,
		locationCategoriesById,
		locationProfilesByExternalId,
		locationProfilesByCategoryId,
		locationProfilesByAttachedFeatureId,
	};
};

export const processMVFSpaces = (
	mvf: ParsedMVF,
	mvfConnectionsByNodeId: MapDataRecords['mvfConnectionsByNodeId'],
	data: MapDataInternal,
): Pick<
	MapDataRecords,
	| 'spacesById'
	| 'spacesByExternalId'
	| 'poisById'
	| 'poisByExternalId'
	| 'mvfSpacesById'
	| 'objectEntranceNodeIdsByObstructionId'
	| 'connectionsById'
	| 'connectionIdsByLatLon'
	| 'mvfSpacesByFloorId'
	| 'mvfPoisByFloorId'
> => {
	const spacesById: MapDataRecords['spacesById'] = {};
	const spacesByExternalId: MapDataRecords['spacesByExternalId'] = {};
	const poisById: MapDataRecords['poisById'] = {};
	const poisByExternalId: MapDataRecords['poisByExternalId'] = {};
	const objectEntranceNodeIdsByObstructionId: MapDataRecords['objectEntranceNodeIdsByObstructionId'] = {};
	const mvfSpacesById: MapDataRecords['mvfSpacesById'] = {};
	const mvfSpacesByFloorId: MapDataRecords['mvfSpacesByFloorId'] = {};
	const mvfPoisByFloorId: MapDataRecords['mvfPoisByFloorId'] = {};
	const connectionIdsByLatLon: MapDataRecords['connectionIdsByLatLon'] = {};
	const connectionsById: MapDataRecords['connectionsById'] = {};

	// This is not returned, just used to populate the above records correctly
	const floorIdBySpaceId: Record<string, string> = {};

	const floorIdsWithSpaces = globalThis.Object.keys(mvf.space);
	for (const floorId of floorIdsWithSpaces) {
		const mvfSpaces = mvf.space[floorId];

		if (mvfSpaces == null) {
			continue;
		}

		for (const space of mvfSpaces.features) {
			/**
			 * We always want to do some data processing on the MVF data
			 */
			floorIdBySpaceId[space.properties.id] = floorId;
			mvfSpacesById[space.properties.id] = space;
			if (space.properties.kind === 'desk-entrance') {
				// The external ID of a desk-entrance should be the object ID of the desk
				if (objectEntranceNodeIdsByObstructionId[space.properties.externalId] == null) {
					objectEntranceNodeIdsByObstructionId[space.properties.externalId] = [];
				}
				objectEntranceNodeIdsByObstructionId[space.properties.externalId].push(...space.properties.destinationNodes);
			}

			// Some space types need to be skipped when creating SDK data
			if (shouldSkipSpace(space)) {
				continue;
			}

			// Connections are multiple spaces in MVF, but we want to consolidate them in the SDK
			if (space.properties.kind?.includes('connection')) {
				const latLonString = serializeCoords(space.geometry.coordinates);
				if (connectionIdsByLatLon[latLonString] == null) {
					connectionIdsByLatLon[latLonString] = [];
				}
				connectionIdsByLatLon[latLonString].push(space.properties.id);
				continue;
			}

			// POIs are spaces in MVF v2, but not in the Platform tooling, so we keep them as separate APIs
			if (
				space.properties.kind === 'poi' ||
				/**
				 * We don't expect point spaces to not have a kind, but in case we encounter one, it's reasonably safe to assume
				 * it should be a POI.
				 */
				(space.geometry.type === 'Point' && space.properties.kind == null)
			) {
				const newPOI = new PointOfInterest(data, {
					floorId,
					mvfData: space as Feature<Point, SpaceProperties>,
				});
				poisById[space.properties.id] = newPOI;
				if (!mvfPoisByFloorId[floorId]) {
					mvfPoisByFloorId[floorId] = [];
				}
				mvfPoisByFloorId[floorId].push(space as Feature<Point, SpaceProperties>);
				if (space.properties.externalId) {
					if (poisByExternalId[space.properties.externalId] == null) {
						poisByExternalId[space.properties.externalId] = [];
					}
					poisByExternalId[space.properties.externalId].push(newPOI);
				}
			} else if (space.geometry.type === 'Point') {
				/**
				 * If the kind is intentionally not null, but we don't recognize it, it's either a data issue or a new type of feature this
				 * version of the SDK doesn't support. In both these cases, it's safer to skip it. We definitely don't want point geometries
				 * to become spaces.
				 */
				Logger.warn('Encountered a point space with an unknown kind!', space.properties.kind);
			} else {
				// For now this is only "default" or "hallway", but more support may be added in the future
				const newSpace = new Space(data, {
					floorId,
					mvfData: space,
				});
				spacesById[space.properties.id] = newSpace;
				if (space.properties.externalId) {
					if (spacesByExternalId[space.properties.externalId] == null) {
						spacesByExternalId[space.properties.externalId] = [];
					}
					spacesByExternalId[space.properties.externalId].push(newSpace);
				}
				if (!mvfSpacesByFloorId[floorId]) {
					mvfSpacesByFloorId[floorId] = [];
				}
				mvfSpacesByFloorId[floorId].push(space);
			}
		}
	}

	// Now generate a single connection for each connection space that shares a coordinate
	for (const latLonString in connectionIdsByLatLon) {
		const connectionIds = connectionIdsByLatLon[latLonString];
		const mvfDataByFloorId: Record<string, FeatureCollection<Point, SpaceProperties>['features'][number]> = {};
		for (const connectionId of connectionIds) {
			const floorId = floorIdBySpaceId[connectionId];
			const mvfConnectionPointSpaceData = mvfSpacesById[connectionId];

			// the connection node id minus the 'n_' prefix is the same as the point space id
			// so we can infer the connection data from the point space data
			// and expose the accessible property
			const mvfConnectionData =
				mvfConnectionsByNodeId?.['n_' + mvfConnectionPointSpaceData.properties.id.split('s_')[1]];

			mvfDataByFloorId[floorId] = {
				...mvfConnectionPointSpaceData,
				properties: {
					...mvfConnectionPointSpaceData.properties,
					accessible: mvfConnectionData?.accessible === true,
				},
			} as FeatureCollection<Point, SpaceProperties>['features'][number];
		}

		const newConnection = new Connection(data, {
			mvfDataByFloorId,
		});
		connectionsById[newConnection.id] = newConnection;
	}

	return {
		spacesById,
		spacesByExternalId,
		poisById,
		poisByExternalId,
		connectionsById,
		objectEntranceNodeIdsByObstructionId: objectEntranceNodeIdsByObstructionId,
		mvfSpacesById,
		connectionIdsByLatLon,
		mvfSpacesByFloorId,
		mvfPoisByFloorId,
	};
};

export const processMVFNodes = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<MapDataRecords, 'nodesById' | 'nodesByExternalId' | 'mvfNodesById' | 'mvfNodesByFloorId'> => {
	const nodesById: MapDataRecords['nodesById'] = {};
	const nodesByExternalId: MapDataRecords['nodesByExternalId'] = {};
	const mvfNodesById: MapDataRecords['mvfNodesById'] = {};
	const mvfNodesByFloorId: MapDataRecords['mvfNodesByFloorId'] = {};

	for (const node of mvf['node.geojson'].features) {
		mvfNodesById[node.properties.id] = node;
		const newNode = new Node(data, {
			mvfData: node,
			floorId: node.properties.floor ?? node.properties.map,
		});
		nodesById[node.properties.id] = newNode;
		if (node.properties.externalId) {
			if (nodesByExternalId[node.properties.externalId] == null) {
				nodesByExternalId[node.properties.externalId] = [];
			}
			nodesByExternalId[node.properties.externalId].push(newNode);
		}
		const floorId = node.properties.floor ?? node.properties.map;
		if (!mvfNodesByFloorId[floorId]) {
			mvfNodesByFloorId[floorId] = [];
		}
		mvfNodesByFloorId[floorId].push(node);
	}

	return {
		nodesById,
		nodesByExternalId,
		mvfNodesById,
		mvfNodesByFloorId,
	};
};

export const processMVFObstructions = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<MapDataRecords, 'objectsById' | 'objectsByExternalId' | 'mvfObstructionById' | 'obstructionIdByEntranceId'> => {
	const objectsById: MapDataRecords['objectsById'] = {};
	const objectsByExternalId: MapDataRecords['objectsByExternalId'] = {};
	const mvfObstructionById: MapDataRecords['mvfObstructionById'] = {};
	const obstructionIdByEntranceId: MapDataRecords['obstructionIdByEntranceId'] = {};

	const floorIdsWithObstructions = globalThis.Object.keys(mvf.obstruction);
	for (const floorId of floorIdsWithObstructions) {
		const mvfObstructions = mvf.obstruction[floorId];

		if (mvfObstructions == null) {
			continue;
		}

		for (const obstruction of mvfObstructions.features) {
			mvfObstructionById[obstruction.properties.id] = obstruction;

			// Save all the entrances for each obstruction so we can query them later
			for (const entrance of obstruction.properties.entrances) {
				obstructionIdByEntranceId[entrance] = obstruction.properties.id;
			}

			// We'll skip creating classes for walls for now since you can't do anything with them
			// We'll also skip areas since they are processed separately and have no style
			if (OBSTRUCTION_KINDS_TO_SKIP.has(obstruction.properties.kind?.toLowerCase() ?? '')) {
				continue;
			}

			const newObject = new MapObject(data, {
				floorId,
				mvfData: obstruction,
			});
			objectsById[obstruction.properties.id] = newObject;
			if (obstruction.properties.externalId) {
				if (objectsByExternalId[obstruction.properties.externalId] == null) {
					objectsByExternalId[obstruction.properties.externalId] = [];
				}
				objectsByExternalId[obstruction.properties.externalId].push(newObject);
			}
		}
	}

	return {
		objectsById,
		objectsByExternalId,
		mvfObstructionById,
		obstructionIdByEntranceId,
	};
};

export const processMVFFloors = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<MapDataRecords, 'floorsById' | 'floorsByExternalId' | 'mvfFloorsById'> => {
	const floorsById: MapDataRecords['floorsById'] = {};
	const floorsByExternalId: MapDataRecords['floorsByExternalId'] = {};
	const mvfFloorsById: MapDataRecords['mvfFloorsById'] = {};

	if ('floor.geojson' in mvf && mvf['floor.geojson'] != null) {
		for (const floor of mvf['floor.geojson'].features) {
			mvfFloorsById[floor.properties.id] = floor.properties;
			const newFloor = new Floor(data, {
				mvfData: floor,
			});
			floorsById[floor.properties.id] = newFloor;
			if (floor.properties.externalId) {
				if (floorsByExternalId[floor.properties.externalId] == null) {
					floorsByExternalId[floor.properties.externalId] = [];
				}
				floorsByExternalId[floor.properties.externalId].push(newFloor);
			}
		}
	} else {
		for (const map of mvf['map.geojson']) {
			mvfFloorsById[map.id] = map;
			const newFloor = new Floor(data, {
				mvfData: { properties: map } as any,
			});
			floorsById[map.id] = newFloor;
		}
	}

	return {
		floorsById,
		floorsByExternalId,
		mvfFloorsById,
	};
};

export const processMVFFloorStacks = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<
	MapDataRecords,
	'floorStacksById' | 'floorStacksByExternalId' | 'mvfFloorStacksById' | 'facadesById' | 'facadesBySpaceId'
> => {
	const floorStacksById: MapDataRecords['floorStacksById'] = {};
	const floorStacksByExternalId: MapDataRecords['floorStacksByExternalId'] = {};
	const mvfFloorStacksById: MapDataRecords['mvfFloorStacksById'] = {};
	const facadesById: MapDataRecords['facadesById'] = {};
	const facadesBySpaceId: MapDataRecords['facadesBySpaceId'] = {};

	const floorStacks = mvf['floorstack.json'] || mvf['mapstack.json'] || mvf['mapstack.geojson'];
	if (floorStacks != null) {
		for (const floorStack of floorStacks) {
			const facade = mvf.facade?.[floorStack.id];

			const newFloorStack = new FloorStack(data, {
				mvfData: floorStack,
				facadeId: facade?.id,
			});

			floorStacksById[floorStack.id] = newFloorStack;
			if (floorStack.externalId) {
				if (floorStacksByExternalId[floorStack.externalId] == null) {
					floorStacksByExternalId[floorStack.externalId] = [];
				}
				floorStacksByExternalId[floorStack.externalId].push(newFloorStack);
			}
			mvfFloorStacksById[floorStack.id] = floorStack;

			if (facade) {
				const newFacade = new Facade(data, {
					mvfData: facade,
					floorStackId: floorStack.id,
				});
				facadesById[facade.id] = newFacade;
				for (const space of facade.spaces) {
					facadesBySpaceId[space.spaceId] = newFacade;
				}
			}
		}
	}

	return {
		floorStacksById,
		floorStacksByExternalId,
		mvfFloorStacksById,
		facadesById,
		facadesBySpaceId,
	};
};

export const processMVFConnections = (
	mvf: ParsedMVF,
	mvfNodesById?: MapDataRecords['mvfNodesById'],
): Pick<MapDataRecords, 'mvfConnectionsById' | 'mvfConnectionsByNodeId' | 'mvfConnectionIdsByLatLon'> => {
	const mvfConnectionsById: MapDataRecords['mvfConnectionsById'] = {};
	const mvfConnectionIdsByLatLon: MapDataRecords['mvfConnectionIdsByLatLon'] = {};
	const mvfConnectionsByNodeId = mvf['connection.json'].reduce((acc, connection) => {
		connection.nodes.forEach(nodeId => (acc[nodeId] = connection));
		mvfConnectionsById[connection.id] = connection;
		const firstNode = mvfNodesById?.[connection.nodes[0]];
		if (firstNode != null) {
			const latLonString = serializeCoords(firstNode.geometry.coordinates);
			if (mvfConnectionIdsByLatLon[latLonString] == null) {
				mvfConnectionIdsByLatLon[latLonString] = [];
			}
			mvfConnectionIdsByLatLon[latLonString].push(connection.id);
		}

		return acc;
	}, {} as MapDataRecords['mvfConnectionsByNodeId']);

	return {
		mvfConnectionsById,
		mvfConnectionsByNodeId,
		mvfConnectionIdsByLatLon,
	};
};

export const processMVFEntrances = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<
	MapDataRecords,
	'doorsById' | 'doorsByExternalId' | 'doorsByNodeId' | 'mvfEntrancesById' | 'mvfEntrancesByFloorId'
> => {
	const doorsById: MapDataRecords['doorsById'] = {};
	const doorsByExternalId: MapDataRecords['doorsByExternalId'] = {};
	const doorsByNodeId: MapDataRecords['doorsByNodeId'] = {};
	const mvfEntrancesById: MapDataRecords['mvfEntrancesById'] = {};
	const mvfEntrancesByFloorId: MapDataRecords['mvfEntrancesByFloorId'] = {};
	const floorIdsWithEntrances = globalThis.Object.keys(mvf.entrance);
	for (const floorId of floorIdsWithEntrances) {
		const mvfEntrances = mvf.entrance[floorId];

		if (mvfEntrances == null) {
			continue;
		}

		for (const entrance of mvfEntrances.features) {
			mvfEntrancesById[entrance.properties.id] = entrance;
			const newDoor = new Door(data, {
				floorId,
				mvfData: entrance,
			});

			if (entrance.properties.node) doorsByNodeId[entrance.properties.node] = newDoor;

			doorsById[entrance.properties.id] = newDoor;
			if (entrance.properties.externalId) {
				if (doorsByExternalId[entrance.properties.externalId] == null) {
					doorsByExternalId[entrance.properties.externalId] = [];
				}
				doorsByExternalId[entrance.properties.externalId].push(newDoor);
			}
			if (!mvfEntrancesByFloorId[floorId]) {
				mvfEntrancesByFloorId[floorId] = [];
			}
			mvfEntrancesByFloorId[floorId].push(entrance);
		}
	}

	return {
		doorsById,
		doorsByExternalId,
		doorsByNodeId,
		mvfEntrancesById,
		mvfEntrancesByFloorId,
	};
};

const processMVFTextAreas = (
	mvf: ParsedMVF,
	spacesById: MapDataRecords['mvfSpacesById'],
	obstructionsById: MapDataRecords['mvfObstructionById'],
): void => {
	if (mvf.textAreas == null) {
		return;
	}

	for (const floorId in mvf.textAreas) {
		const mvfTextAreas = mvf.textAreas[floorId]?.features;

		if (mvfTextAreas == null) {
			continue;
		}

		// iterate through all images on a floor and assign them to the correct space or obstruction
		for (const feature of mvfTextAreas) {
			if ('anchorId' in feature.properties) {
				const anchoredTextArea = feature?.properties;
				const polygonOrObstruction =
					spacesById[anchoredTextArea.anchorId] || obstructionsById[anchoredTextArea.anchorId];
				if (polygonOrObstruction != null && anchoredTextArea != null) {
					const properties = polygonOrObstruction.properties as WithTextArea<WithPolygonImage<SpaceProperties>>;
					properties.textArea = anchoredTextArea;
					if (properties.textArea) {
						// @TODO: figure out how to type this better. The SDK expects a position, but the MVF doesn't have it
						// @ts-ignore
						properties.textArea.position = feature.geometry.coordinates;
					}
				}
			}
		}
	}
};

export const processMVFPolygonImages = (
	mvf: ParsedMVF,
	spacesById: MapDataRecords['mvfSpacesById'],
	obstructionsById: MapDataRecords['mvfObstructionById'],
): void => {
	if (mvf.floorImages == null) {
		return;
	}

	for (const floorId in mvf.floorImages) {
		const mvfMapImages = mvf.floorImages[floorId]?.features;

		if (mvfMapImages == null) {
			continue;
		}

		// iterate through all images on a floor and assign them to the correct space or obstruction
		for (const feature of mvfMapImages) {
			if ('anchorId' in feature.properties) {
				const anchoredImage = feature?.properties;
				const polygonOrObstruction = spacesById[anchoredImage.anchorId] || obstructionsById[anchoredImage.anchorId];
				if (polygonOrObstruction != null && anchoredImage != null) {
					const properties = polygonOrObstruction.properties as WithPolygonImage<SpaceProperties>;
					properties.image = anchoredImage;
					if (properties.image) {
						// @TODO: figure out how to type this better. The SDK expects a position, but the MVF doesn't have it
						// @ts-ignore
						properties.image.position = feature.geometry.coordinates;
					}
				}
			}
		}
	}
};

const processMVFEnterprisePolygonStyles = (mvf: ParsedMVF, spacesById: MapDataRecords['mvfSpacesById']): void => {
	if (mvf.enterprise?.enterpriseStyles == null) {
		return;
	}

	const styleMap = mvf.enterprise?.enterpriseStyles;

	for (const styleId in styleMap) {
		const style = styleMap[styleId];
		style.geometryAnchors.forEach(anchor => {
			const polygon = spacesById[anchor.geometryId];
			if (polygon != null) {
				const properties = polygon.properties as WithEntepriseStyle<SpaceProperties>;
				properties.style = { styleId, edgeOffset: style.edgeOffset };
			}
		});
	}
};

export const processMVFEnterpriseTextures = (
	mvf: ParsedMVF,
	spacesById: MapDataRecords['mvfSpacesById'],
	obstructionsById: MapDataRecords['mvfObstructionById'],
): void => {
	if (mvf.enterprise == null || mvf.enterprise['textures'] == null) {
		return;
	}

	for (const texture of mvf.enterprise['textures']) {
		for (const anchor of texture.geometryAnchors) {
			const polygonOrObstruction = spacesById[anchor.geometryId] || obstructionsById[anchor.geometryId];
			if (polygonOrObstruction != null) {
				const properties = polygonOrObstruction.properties as WithTextures<SpaceProperties>;
				if (properties.textures == null) {
					properties.textures = [];
				}
				properties.textures.push(texture);
			}
		}
	}
};

export const processMVFAnnotations = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<MapDataRecords, 'annotationsById' | 'mvfAnnotationsById' | 'mvfAnnotationsByFloorId'> => {
	const annotationsById: MapDataRecords['annotationsById'] = {};
	const mvfAnnotationsById: MapDataRecords['mvfAnnotationsById'] = {};
	const mvfAnnotationsByFloorId: MapDataRecords['mvfAnnotationsByFloorId'] = {};
	// const nodeIdsByAnnotationId: MapDataRecords['nodeIdsByAnnotationId'] = {};

	if (mvf.annotation == null) {
		return {
			annotationsById,
			mvfAnnotationsById,
			mvfAnnotationsByFloorId,
		};
	}

	const floorIdsWithAnnotations = globalThis.Object.keys(mvf.annotation);
	for (const floorId of floorIdsWithAnnotations) {
		const mvfAnnotations = mvf.annotation[floorId];

		if (mvfAnnotations == null) {
			continue;
		}

		for (const annotation of mvfAnnotations.features) {
			mvfAnnotationsById[annotation.properties.id] = annotation;
			const newAnnotation = new Annotation(data, {
				floorId,
				mvfData: annotation,
			});
			annotationsById[annotation.properties.id] = newAnnotation;
			if (!mvfAnnotationsByFloorId[floorId]) {
				mvfAnnotationsByFloorId[floorId] = [];
			}
			mvfAnnotationsByFloorId[floorId].push(annotation);
		}
	}

	return {
		annotationsById,
		mvfAnnotationsById,
		mvfAnnotationsByFloorId,
	};
};

export const processMVFAreas = (
	mvf: ParsedMVF,
	data: MapDataInternal,
): Pick<MapDataRecords, 'areasById' | 'areasByExternalId' | 'mvfAreasById'> => {
	const areasById: MapDataRecords['areasById'] = {};
	const areasByExternalId: MapDataRecords['areasByExternalId'] = {};
	const mvfAreasById: MapDataRecords['mvfAreasById'] = {};

	if (mvf.area == null) {
		return {
			areasById,
			areasByExternalId,
			mvfAreasById,
		};
	}

	const floorIdsWithAreas = globalThis.Object.keys(mvf.area);
	for (const floorId of floorIdsWithAreas) {
		const mvfAreas = mvf.area[floorId];

		if (mvfAreas == null) {
			continue;
		}

		for (const area of mvfAreas.features) {
			mvfAreasById[area.properties.id] = area;
			const newArea = new Area(data, {
				floorId,
				mvfData: area,
			});
			areasById[area.properties.id] = newArea;
			if (area.properties.externalId) {
				if (areasByExternalId[area.properties.externalId] == null) {
					areasByExternalId[area.properties.externalId] = [];
				}
				areasByExternalId[area.properties.externalId].push(newArea);
			}
		}
	}

	return {
		areasById,
		areasByExternalId,
		mvfAreasById,
	};
};

export const processDestinationNodes = (
	mvfSpacesById: MapDataRecords['mvfSpacesById'],
	doorsByNodeId: MapDataRecords['doorsByNodeId'],
): Pick<
	MapDataRecords,
	'entranceNodeIdsBySpaceId' | 'spaceIdsByNodeId' | 'objectIdsByNodeId' | 'destinationNodesIdsBySpaceId'
> => {
	const entranceNodeIdsBySpaceId: MapDataRecords['entranceNodeIdsBySpaceId'] = {};
	const spaceIdsByNodeId: MapDataRecords['spaceIdsByNodeId'] = {};
	const objectIdsByNodeId: MapDataRecords['objectIdsByNodeId'] = {};
	const destinationNodesIdsBySpaceId: MapDataRecords['destinationNodesIdsBySpaceId'] = {};

	/**
	 * Precompute the destinationNodes for each space which are doors.
	 * This is necessary to reduce the A* search from thousands of nodes.
	 */
	for (const spaceId in mvfSpacesById) {
		const mvfSpace = mvfSpacesById[spaceId];
		if (mvfSpace == null) {
			continue;
		}
		const destinationNodes = mvfSpace.properties.destinationNodes ?? [];
		if (destinationNodes.length < 1) continue;
		const entranceNodes: string[] = [];
		for (const nodeId of destinationNodes) {
			if (doorsByNodeId[nodeId] != null) {
				entranceNodes.push(nodeId);
			}

			if (spaceIdsByNodeId[nodeId] == null) {
				spaceIdsByNodeId[nodeId] = [];
			}
			spaceIdsByNodeId[nodeId].push(mvfSpace.properties.id);

			if (mvfSpace.properties.kind === 'desk-entrance') {
				/**
				 * Objects are related to nodes through spaces (usually point or line geometries) with kind "desk-entrance".
				 * These spaces have the object's nodes as destination nodes and the object's id as the externalId.
				 */
				if (objectIdsByNodeId[nodeId] == null) {
					objectIdsByNodeId[nodeId] = [];
				}
				objectIdsByNodeId[nodeId].push(mvfSpace.properties.externalId);
			}
		}
		if (entranceNodes.length > 0) {
			entranceNodeIdsBySpaceId[spaceId] = entranceNodes;
		}
		destinationNodesIdsBySpaceId[spaceId] = destinationNodes;
	}

	return {
		entranceNodeIdsBySpaceId,
		spaceIdsByNodeId,
		objectIdsByNodeId,
		destinationNodesIdsBySpaceId,
	};
};

/**
 * @internal
 */
export const createEnterpriseDataFromMVF = (mvf: ParsedMVF, data: MapDataInternal): EnterpriseMapDataRecords => {
	const connectionsById: MapDataRecords['connectionsById'] = {};
	const connectionsByExternalId: EnterpriseMapDataRecords['connectionsByExternalId'] = {};
	const locationsById: EnterpriseMapDataRecords['locationsById'] = {};
	const locationsByExternalId: EnterpriseMapDataRecords['locationsByExternalId'] = {};
	const mvfLocationsById: EnterpriseMapDataRecords['mvfLocationsById'] = (mvf.enterprise?.locations ?? []).reduce(
		(acc, location) => {
			acc[location.id] = location;

			return acc;
		},
		{} as EnterpriseMapDataRecords['mvfLocationsById'],
	);
	const categoriesById: EnterpriseMapDataRecords['categoriesById'] = {};
	const categoriesByExternalId: EnterpriseMapDataRecords['categoriesByExternalId'] = {};
	const mvfCategoriesById: EnterpriseMapDataRecords['mvfCategoriesById'] = {};
	const mvfLocationsByGeometryId: EnterpriseMapDataRecords['mvfLocationsByGeometryId'] = {};
	const categoryIdsByLocationId: Record<string, MVFEnterpriseCategory['id'][]> = {};
	const locationIdsByNodeId: EnterpriseMapDataRecords['locationIdsByNodeId'] = {};
	const venue: EnterpriseMapDataRecords['venue'] = mvf.enterprise?.venue || ({} as MVFEnterpriseVenue);
	const locationInstancesById: EnterpriseMapDataRecords['locationInstancesById'] = {};

	for (const category of mvf.enterprise?.categories ?? []) {
		const newCategory = new EnterpriseCategory(data, { mvfData: category });
		categoriesById[category.id] = newCategory;
		if (category.externalId) {
			if (categoriesByExternalId[category.externalId] == null) {
				categoriesByExternalId[category.externalId] = [];
			}
			categoriesByExternalId[category.externalId].push(newCategory);
		}
		mvfCategoriesById[category.id] = category;

		category.locations.forEach(locationId => {
			if (categoryIdsByLocationId[locationId] == null) {
				categoryIdsByLocationId[locationId] = [];
			}
			categoryIdsByLocationId[locationId].push(category.id);
		});
	}

	const locationInstancesByParentId: Record<string, EnterpriseLocation[]> = {};
	for (const locationInstance of mvf.enterprise?.locationInstances ?? []) {
		const mvfLocation = mvfLocationsById[locationInstance.parent];

		// extract all keys from the parent location
		// so they can be used in getters, without copying the entire parent location
		const parentKeys = Object.keys(mvfLocation);
		const keysObject = {};
		for (const key of parentKeys) {
			keysObject[key] = undefined;
		}

		// create a new location object with the parent's keys, but undefined values
		// this is so that when we get the properties, we can check if the instance has a value for it
		// if not, we can fallback to the parent's value
		const location = {
			...keysObject,
			polygons: [], // A location instance should have nodes and polygons set to what it's attached to, but it's not actually enforced by the spec.
			nodes: [], // So the SDK will set them to empty arrays before applying the location instance's optional properties.
			...locationInstance,
		};

		if (locationInstancesByParentId[location.parent] == null) {
			locationInstancesByParentId[location.parent] = [];
		}
		locationInstancesByParentId[location.parent].push(
			new EnterpriseLocation(data, {
				mvfData: location as MVFEnterpriseLocation,
				categoryIds: mvf.enterprise?.categoryPriorities?.[location.id] || categoryIdsByLocationId[location.id],
				parentId: locationInstance.parent,
			}),
		);
		locationInstancesById[location.id] = locationInstance;
	}

	for (const location of mvf.enterprise?.locations ?? []) {
		const newLocation = new EnterpriseLocation(data, {
			mvfData: location,
			categoryIds: mvf.enterprise?.categoryPriorities?.[location.id] || categoryIdsByLocationId[location.id],
			locationInstances: locationInstancesByParentId[location.id],
		});
		locationsById[location.id] = newLocation;
		if (location.externalId) {
			if (locationsByExternalId[location.externalId] == null) {
				locationsByExternalId[location.externalId] = [];
			}
			locationsByExternalId[location.externalId].push(newLocation);
		}

		location.nodes.forEach(node => {
			if (locationIdsByNodeId[node.id] == null) {
				locationIdsByNodeId[node.id] = [];
			}
			locationIdsByNodeId[node.id].push(location.id);
		});

		for (const polygon of location.polygons) {
			if (mvfLocationsByGeometryId[polygon.id] == null) {
				mvfLocationsByGeometryId[polygon.id] = [];
			}
			mvfLocationsByGeometryId[polygon.id].push(location);
		}
	}

	if (data.enterpriseMode === true) {
		for (const connectionId in data.mvfConnectionsById) {
			const mvfData: Record<
				string,
				Feature<Point, MVFConnection & { kind: string; destinationNodes: string[]; flags: '' }>
			> = {};
			const mvfConnection = data.mvfConnectionsById[connectionId];
			for (const nodeId of data.mvfConnectionsById[connectionId].nodes) {
				const connectionNode = data.nodesById[nodeId];
				if (connectionNode != null) {
					mvfData[connectionNode.floor.id] = {
						type: 'Feature',
						geometry: {
							type: 'Point',
							coordinates: [connectionNode.coordinate.longitude, connectionNode.coordinate.latitude],
						},
						properties: {
							id: connectionId,
							externalId: mvfConnection.externalId,
							kind: 'enterprise.' + mvfConnection.type,
							destinationNodes: [],
							flags: '',
							type: mvfConnection.type,
							nodes: mvfConnection.nodes,
							accessible: mvfConnection.accessible,
							extra: mvfConnection.extra,
							details: mvfConnection.details,
						},
					};
				}
			}
			if (Object.keys(mvfData).length > 0) {
				connectionsById[connectionId] = new Connection(data, {
					mvfDataByFloorId: mvfData,
				});
				if (mvfConnection.externalId) {
					if (connectionsByExternalId[mvfConnection.externalId] == null) {
						connectionsByExternalId[mvfConnection.externalId] = [];
					}
					connectionsByExternalId[mvfConnection.externalId].push(connectionsById[connectionId]);
				}
			}
		}
		data.connectionsById = connectionsById;
	}

	return {
		connectionsByExternalId,
		locationIdsByNodeId,
		mvfLocationsByGeometryId,
		mvfLocationsById,
		mvfCategoriesById,
		locationsById,
		locationsByExternalId,
		locationInstancesById,
		categoriesById,
		categoriesByExternalId,
		venue,
	};
};

/**
 * @internal
 */
export const createDataFromMVF = (mvf: ParsedMVF, data: MapDataInternal): MapDataRecords => {
	const { nodesById, nodesByExternalId, mvfNodesById, mvfNodesByFloorId } = processMVFNodes(mvf, data);
	const { mvfConnectionsById, mvfConnectionsByNodeId, mvfConnectionIdsByLatLon } = processMVFConnections(
		mvf,
		mvfNodesById,
	);

	const {
		spacesById,
		spacesByExternalId,
		poisById,
		poisByExternalId,
		mvfSpacesById,
		objectEntranceNodeIdsByObstructionId,
		connectionsById,
		connectionIdsByLatLon,
		mvfSpacesByFloorId,
		mvfPoisByFloorId,
	} = processMVFSpaces(mvf, mvfConnectionsByNodeId, data);

	const { objectsById, objectsByExternalId, mvfObstructionById, obstructionIdByEntranceId } = processMVFObstructions(
		mvf,
		data,
	);
	const { floorsById, floorsByExternalId, mvfFloorsById } = processMVFFloors(mvf, data);
	const { floorStacksById, floorStacksByExternalId, mvfFloorStacksById, facadesById, facadesBySpaceId } =
		processMVFFloorStacks(mvf, data);

	const { doorsById, doorsByExternalId, doorsByNodeId, mvfEntrancesById, mvfEntrancesByFloorId } = processMVFEntrances(
		mvf,
		data,
	);
	const { annotationsById, mvfAnnotationsById, mvfAnnotationsByFloorId } = processMVFAnnotations(mvf, data);
	const { areasById, areasByExternalId, mvfAreasById } = processMVFAreas(mvf, data);
	processMVFPolygonImages(mvf, mvfSpacesById, mvfObstructionById);
	processMVFTextAreas(mvf, mvfSpacesById, mvfObstructionById);
	processMVFEnterprisePolygonStyles(mvf, mvfSpacesById);
	processMVFEnterpriseTextures(mvf, mvfSpacesById, mvfObstructionById);

	const {
		locationCategoriesByParentId,
		locationProfilesById,
		locationCategoriesById,
		locationProfilesByExternalId,
		locationProfilesByCategoryId,
		locationProfilesByAttachedFeatureId,
	} = processMVFLocationProfilesAndCategories(mvf, data);

	const { entranceNodeIdsBySpaceId, spaceIdsByNodeId, objectIdsByNodeId, destinationNodesIdsBySpaceId } =
		processDestinationNodes(mvfSpacesById, doorsByNodeId);

	return {
		spacesById,
		spacesByExternalId,
		nodesById,
		nodesByExternalId,
		objectsById,
		objectsByExternalId,
		floorsById,
		floorsByExternalId,
		floorStacksById,
		floorStacksByExternalId,
		facadesById,
		facadesBySpaceId,
		connectionsById,
		doorsById,
		doorsByExternalId,
		doorsByNodeId,
		poisById,
		poisByExternalId,
		annotationsById,
		areasById,
		areasByExternalId,
		objectEntranceNodeIdsByObstructionId,
		obstructionIdByEntranceId,
		connectionIdsByLatLon,
		locationProfilesById,
		locationCategoriesById,
		locationProfilesByExternalId,
		locationProfilesByCategoryId,
		locationCategoriesByParentId,
		mvfSpacesById,
		mvfNodesById,
		mvfObstructionById,
		mvfFloorsById,
		mvfFloorStacksById,
		mvfConnectionsById,
		mvfConnectionsByNodeId,
		mvfEntrancesById,
		mvfAnnotationsById,
		mvfNodesByFloorId,
		mvfSpacesByFloorId,
		mvfPoisByFloorId,
		mvfAreasById,
		mvfEntrancesByFloorId,
		mvfAnnotationsByFloorId,
		locationProfilesByAttachedFeatureId,
		mvfConnectionIdsByLatLon,
		entranceNodeIdsBySpaceId,
		spaceIdsByNodeId,
		objectIdsByNodeId,
		destinationNodesIdsBySpaceId,
	};
};

function shouldSkipSpace(space: SpaceCollection['features'][number]) {
	return (
		/**
		 * Ben Liu, 2025-09-18
		 *
		 * Polygonal spaces we can always create, because they must get rendered. However point and line geometries
		 * should generally not be created as spaces. We should create them as POIs or Connections or whatnot depending
		 * on their kind. If they have an undefined kind, then we don't know what to do with them, so we skip them.
		 * However, if the kind key is not present, that is indicative of an old MVF and we should load it as a space;
		 * this is mostly relevant for tests.
		 */
		(!['Polygon', 'MultiPolygon'].includes(space.geometry.type) &&
			'kind' in space.properties &&
			space.properties.kind == null) ||
		SPACE_KINDS_TO_SKIP.has(space.properties.kind ?? '')
	);
}
