import type { GeometryState, RendererCore } from '@mappedin/core-sdk';
import type { FloorId, FloorStack as MVFFloorStack, Facade as MVFFacade } from '@mappedin/mvf-v2';
import { Logger } from '@packages/internal/common';
import type { FloorObject } from './floor-object';
import { MappedinError } from '@packages/internal/common/errors';

export class FloorStackObject implements Omit<MVFFloorStack, 'maps' | 'floors' | 'defaultFloor' | 'footprint'> {
	id: MVFFloorStack['id'];
	externalId: MVFFloorStack['externalId'];
	name: MVFFloorStack['name'];
	type: MVFFloorStack['type'];
	floorIds: FloorId[];
	defaultFloorId?: MVFFloorStack['defaultFloor'];

	/** Sorted floor objects by elevation */
	floorObjects: FloorObject[] = [];
	floorObjectsByElevation: Map<number, FloorObject> = new Map();
	metadata?: MVFFloorStack['metadata'];
	containerId: string;
	facade?: MVFFacade;

	private renderer: RendererCore;
	constructor(floorStack: MVFFloorStack, parentId: string, renderer: RendererCore, facade?: MVFFacade) {
		this.id = floorStack.id;
		this.externalId = floorStack.externalId;
		this.name = floorStack.name;
		this.type = floorStack.type;
		this.metadata = floorStack.metadata;
		this.floorIds = floorStack.floors ?? floorStack.maps;
		this.defaultFloorId = floorStack.defaultFloor;
		this.renderer = renderer;
		const groupContainer = renderer.addGroupContainer(
			this.id,
			{
				visible: true,
				interactive: true,
			},
			parentId,
		);
		this.containerId = groupContainer.id.toString();
		this.facade = facade;
	}

	#defaultFloorObject?: FloorObject;
	get defaultFloor(): FloorObject {
		if (this.#defaultFloorObject == null) {
			if (this.floorObjects.length === 0) {
				throw new MappedinError(`No floors in floor stack (${this.id}). Could not get default floor.`);
			}

			let defaultFloor: FloorObject | undefined;
			if (this.defaultFloorId) {
				// find the floor with the default floor id
				defaultFloor = this.floorObjects.find(floor => floor.id === this.defaultFloorId);
			}

			if (!defaultFloor) {
				// if no default floor id, find the floor with the elevation closest to 0
				defaultFloor = this.floorObjects.reduce((prev, curr) => {
					return Math.abs(curr.elevation) < Math.abs(prev.elevation) ? curr : prev;
				});
			}

			this.#defaultFloorObject = defaultFloor;
		}

		return this.#defaultFloorObject;
	}

	/**
	 * Whether the floorstack is an outdoor stack.
	 * This may not be accurate if the type property is incorrect or if the building contains another facade.
	 *
	 * A floor stack being outdoors is determined by the following:
	 * - The type is 'outdoor'
	 * - It does not have a facade
	 * - One of the floors contains facades
	 */
	get isOutdoors(): boolean {
		const type = this.type?.toLowerCase();
		if (type === 'outdoor') {
			return true;
		}

		// if this floorstack has a facade, it's a building
		if (this.facade != null) {
			return false;
		}

		// if one of the floors has facade geometry, this is probably an outdoor stack
		return this.floorObjects.some(floor => floor.hasFacadeGeometry);
	}

	addFloor(floor: FloorObject) {
		if (this.floorObjectsByElevation.has(floor.elevation)) {
			Logger.error(
				`Floor with same elevation ${floor.elevation} already exists in Stack (${this.id}). Floor (${floor.id}) will not be added.`,
			);

			return;
		}
		this.floorObjects.push(floor);
		this.floorObjectsByElevation.set(floor.elevation, floor);
		if (this.facade) {
			// if this floorstack has a facade, we make floors focusable so they occlude the raycast
			this.renderer.setState<GeometryState>(floor.containerId, {
				focusable: true,
			});
		}
	}

	loadAllFloors() {
		for (const floor of this.floorObjects) {
			floor.load();
			if (
				// Don't add occluders to outdoor floor stacks, they collide with the lower floors of buildings
				!this.isOutdoors &&
				// If the floor has facades, it's probably an outdoor enterprise map
				!floor.hasFacadeGeometry &&
				floor.footprint != null
			) {
				floor.addOccluder();
			}
		}
	}
}
