/**
 * Based on an array of flags, check if a specific flag is set.
 * @param flags - The array of flags. Each entry is a number that should be read as a bitmap.
 * @param index - The index of the entry in the flags array on which to check the bit.
 * @param bit - The bit of the flag to check.
 * @returns - True if the flag is set, false otherwise.
 */
export function hasFlag(flags: number[] | undefined, index: number, bit: number) {
	if (flags == null || flags[index] == null) {
		return false;
	}

	const mask = 1 << bit;
	return (flags[index] & mask) !== 0;
}
