import { type MapDataInternal } from '../../map-data-objects';
import { type GeoJsonApi } from '../api';

export function autoGeometries(mapData: MapDataInternal, api: GeoJsonApi) {
	[...mapData.getByType('space'), ...mapData.getByType('object')].forEach(thing => {
		if (thing.name == null || thing.name === '') {
			return;
		}

		api.updateState(thing, {
			interactive: true,
		});
	});
}
