import type { FeatureCollection, Point, SpaceProperties } from '@mappedin/mvf-v2';
import type { Floor, MapDataInternal } from '.';
import type Coordinate from './coordinate';
import type { IGeoJSONData } from './types';
import type Hyperlink from './hyperlink';
import type ImageMetaData from './image';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * A class representing point of interest data within the map.
 *
 * Points of interest are used to represent specific points
 * on the map with additional information(e.g. ATMs, Water Fountains).
 */
class PointOfInterest
	extends DetailedMapData<FeatureCollection<Point, SpaceProperties>['features'][number]>
	implements IGeoJSONData, IFocusable, IAnchorable
{
	/**
	 * @internal
	 */
	static readonly __type = 'point-of-interest';

	/**
	 * @internal
	 */
	readonly __type = PointOfInterest.__type;

	#data: MapDataInternal;
	#floorId: string;
	#cache: Partial<{ coordinate: Coordinate; links: Hyperlink[]; images: ImageMetaData[] }> = {};
	#mvfData: FeatureCollection<Point, SpaceProperties>['features'][number];

	/**
	 * Checks if the provided instance is of type PointOfInterest.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a PointOfInterest, false otherwise.
	 */
	static is(instance: object): instance is PointOfInterest {
		return (instance as { __type: string }).__type === PointOfInterest.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: FeatureCollection<Point, SpaceProperties>['features'][number];
		},
	) {
		super(data, options.mvfData);
		this.#data = data;
		this.#mvfData = options.mvfData;
		this.#floorId = options.floorId;
	}

	/**
	 * Gets the {@link Floor} object associated with the POI.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Gets the {@link Coordinate} of the POI.
	 *
	 * @returns {Coordinate} The POI's coordinate.
	 */
	get coordinate(): Coordinate {
		if (this.#cache.coordinate == null) {
			this.#cache.coordinate = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.coordinate;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this PointOfInterest.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this.coordinate;
	}

	/** @internal */
	get anchorTarget() {
		return this.coordinate;
	}

	/**
	 * Serializes the POI data to JSON.
	 *
	 * @returns An object representing the POI.
	 */
	toJSON() {
		return {
			__type: PointOfInterest.__type,
			id: this.id,
			name: this.name,
			floor: this.floor.id,
			coordinate: this.coordinate.toJSON(),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key in this.#cache) {
			this.#cache[key] = null;
		}
	}
}

export default PointOfInterest;
