const regex = /<svg([^>]*)>/g;

export function getStyledIcon(iconName: string, color: string, size: number): string {
	const icon = ICONS.get(iconName);
	if (!icon) {
		return '';
	}

	return icon.replace(regex, `<svg$1 style="fill:${color}; width:${size}px; height:${size}px">`);
}

export default ICONS;
