import { toRadians } from '../../../packages/common/utils';

export interface Point {
	x: number;
	y: number;
}

export interface Rectangle {
	center: Point;
	width: number;
	height: number;
	rotation: number;
	polygonCenter?: Point;
}

/**
 * Rotates a point around the origin by the given angle in radians
 */
export function rotatePoint(point: Point, angleRad: number): Point {
	return {
		x: point.x * Math.cos(angleRad) - point.y * Math.sin(angleRad),
		y: point.x * Math.sin(angleRad) + point.y * Math.cos(angleRad),
	};
}

/**
 * Rotates all points in an array by the given angle in radians
 */
export function rotatePoints(points: Point[], angleRad: number): Point[] {
	return points.map(p => rotatePoint(p, angleRad));
}

/**
 * Calculates the bounding box of an array of points
 */
export function getBounds(points: Point[]) {
	let minX = Infinity;
	let minY = Infinity;
	let maxX = -Infinity;
	let maxY = -Infinity;
	for (const p of points) {
		minX = Math.min(minX, p.x);
		minY = Math.min(minY, p.y);
		maxX = Math.max(maxX, p.x);
		maxY = Math.max(maxY, p.y);
	}

	return { minX, minY, maxX, maxY };
}

/**
 * Checks if a point is inside a polygon using ray casting algorithm
 */
export function pointInPolygon(point: Point, polygon: Point[]): boolean {
	let inside = false;
	for (let i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
		const xi = polygon[i].x;
		const yi = polygon[i].y;
		const xj = polygon[j].x;
		const yj = polygon[j].y;

		const intersect = yi > point.y !== yj > point.y && point.x < ((xj - xi) * (point.y - yi)) / (yj - yi) + xi;
		if (intersect) inside = !inside;
	}

	return inside;
}

/**
 * Checks if a rectangle of the given dimensions is valid within the grid
 */
export function isValidRectangle(grid: number[][], x: number, y: number, width: number, height: number): boolean {
	for (let dy = 0; dy < height; dy++) {
		for (let dx = 0; dx < width; dx++) {
			if (grid[y + dy][x + dx] !== 1) {
				return false;
			}
		}
	}

	return true;
}

/**
 * Calculates grid dimensions that maintain a specific aspect ratio
 */
export function calculateGridDimensions(bounds: ReturnType<typeof getBounds>, aspectRatio: number, gridSize: number) {
	const boxWidth = bounds.maxX - bounds.minX;
	const boxHeight = bounds.maxY - bounds.minY;

	let dx: number;
	let dy: number;
	let gridBounds = { ...bounds };

	if (boxWidth / boxHeight > aspectRatio) {
		// Box is wider than needed
		dy = boxHeight / gridSize;
		dx = dy * aspectRatio;
		// Center the grid horizontally
		gridBounds.minX += (boxWidth - dx * gridSize) / 2;
	} else {
		// Box is taller than needed
		dx = boxWidth / gridSize;
		dy = dx / aspectRatio;
		// Center the grid vertically
		gridBounds.minY += (boxHeight - dy * gridSize) / 2;
	}

	return { dx, dy, gridBounds };
}

/**
 * Creates a grid and marks points inside the polygon
 */
export function createAndFillGrid(
	rotatedPoints: Point[],
	bounds: ReturnType<typeof getBounds>,
	gridSize: number,
	dx: number,
	dy: number,
): number[][] {
	const grid = Array(gridSize)
		.fill(0)
		.map(() => Array(gridSize).fill(0));

	for (let y = 0; y < gridSize; y++) {
		for (let x = 0; x < gridSize; x++) {
			const testPoint = { x: bounds.minX + x * dx, y: bounds.minY + y * dy };
			if (pointInPolygon(testPoint, rotatedPoints)) {
				grid[y][x] = 1;
			}
		}
	}

	return grid;
}

/**
 * Calculates the maximum rectangle size possible at each grid point
 */
export function calculateRectSizes(grid: number[][], gridSize: number): number[][] {
	const rectSizes = Array(gridSize)
		.fill(0)
		.map(() => Array(gridSize).fill(0));

	for (let y = 0; y < gridSize; y++) {
		for (let x = 0; x < gridSize; x++) {
			if (grid[y][x] === 1) {
				// Find largest rectangle containing this point
				let maxSize = 0;
				for (let size = 1; size <= Math.min(gridSize - x, gridSize - y); size++) {
					if (!isValidRectangle(grid, x, y, size, size)) break;
					maxSize = size;
				}
				rectSizes[y][x] = maxSize;
			}
		}
	}

	return rectSizes;
}

/**
 * Calculates the weighted center of the polygon
 */
export function calculatePolygonCenter(
	grid: number[][],
	rectSizes: number[][],
	bounds: ReturnType<typeof getBounds>,
	gridSize: number,
	dx: number,
	dy: number,
): Point {
	let totalWeight = 0;
	let weightedSumX = 0;
	let weightedSumY = 0;

	for (let y = 0; y < gridSize; y++) {
		for (let x = 0; x < gridSize; x++) {
			if (grid[y][x] === 1) {
				const weight = Math.pow(rectSizes[y][x], 2); // Square the size for more emphasis on larger areas
				const px = bounds.minX + x * dx;
				const py = bounds.minY + y * dy;

				totalWeight += weight;
				weightedSumX += px * weight;
				weightedSumY += py * weight;
			}
		}
	}

	return { x: weightedSumX / totalWeight, y: weightedSumY / totalWeight };
}

/**
 * Finds the best rectangle based on area and center proximity
 */
export function findBestRectangle(
	grid: number[][],
	gridBounds: ReturnType<typeof getBounds>,
	dx: number,
	dy: number,
	polygonCenter: Point,
	centerWeight: number,
	buffer: number,
): Omit<Rectangle, 'rotation'> | null {
	let maxScore = -Infinity;
	let bestRect: Omit<Rectangle, 'rotation'> | null = null;

	const gridSize = grid.length;

	// Calculate maximum possible distance for normalization
	const maxDistance = Math.sqrt(
		Math.pow(gridBounds.maxX - gridBounds.minX, 2) + Math.pow(gridBounds.maxY - gridBounds.minY, 2),
	);

	for (let y = 0; y < gridSize; y++) {
		for (let x = 0; x < gridSize; x++) {
			if (grid[y][x] === 1) {
				for (let w = 1; w <= gridSize - x; w++) {
					const h = w; // Height will maintain aspect ratio due to dx/dy scaling
					if (h > gridSize - y) continue;

					if (isValidRectangle(grid, x, y, w, h)) {
						const area = w * h;
						const centerX = gridBounds.minX + (x + w / 2) * dx;
						const centerY = gridBounds.minY + (y + h / 2) * dy;
						const centerPoint = { x: centerX, y: centerY };

						// Calculate distance from polygon center
						const distanceFromCenter = Math.sqrt(
							Math.pow(centerPoint.x - polygonCenter.x, 2) + Math.pow(centerPoint.y - polygonCenter.y, 2),
						);

						// Normalize distance (0 to 1)
						const normalizedDistance = distanceFromCenter / maxDistance;

						// Keep the original scoring formula that worked well for rectangular shapes
						const score = area * (1 - centerWeight) - normalizedDistance * centerWeight * area;

						if (score > maxScore) {
							maxScore = score;

							bestRect = {
								center: centerPoint,
								width: w * dx * buffer,
								height: w * dy * buffer,
							};
						}
					}
				}
			}
		}
	}

	return bestRect;
}

/**
 * Finds the largest interior rectangle within a polygon that maintains a given aspect ratio,
 * with a preference for more centered positions.
 */
export function findLargestInteriorRectangle(
	originalRect: Rectangle,
	points: Point[],
	preferredRotation: number,
	options: {
		scaleFactor?: number;
		centerPriority?: number;
		resolution?: number;
		minimumSizeRatio?: number;
	} = {},
): Rectangle & { polygonCenter?: Point } {
	const { scaleFactor = 0.9, centerPriority = 0.5, resolution = 100, minimumSizeRatio = 0.5 } = options;

	const rotationRad = toRadians(preferredRotation);
	const aspectRatio = originalRect.width / originalRect.height;

	// Rotate points
	const rotatedPoints = rotatePoints(points, rotationRad);

	// Find bounding box
	const bounds = getBounds(rotatedPoints);

	// Calculate grid dimensions maintaining aspect ratio
	const { dx, dy, gridBounds } = calculateGridDimensions(bounds, aspectRatio, resolution);

	// Create and fill the grid
	const grid = createAndFillGrid(rotatedPoints, gridBounds, resolution, dx, dy);

	// Calculate maximum rectangle sizes at each point
	const rectSizes = calculateRectSizes(grid, resolution);

	// Calculate the weighted center of the polygon
	const polygonCenter = calculatePolygonCenter(grid, rectSizes, gridBounds, resolution, dx, dy);

	// Find the best rectangle
	const bestRect = findBestRectangle(grid, gridBounds, dx, dy, polygonCenter, centerPriority, scaleFactor);

	// Check if the calculated rectangle is too small compared to what could fit
	if (
		!bestRect ||
		bestRect.width < originalRect.width * minimumSizeRatio ||
		bestRect.height < originalRect.height * minimumSizeRatio
	) {
		return originalRect;
	}

	return {
		...bestRect,
		center: rotatePoint(bestRect.center, -rotationRad),
		rotation: preferredRotation,
		polygonCenter: rotatePoint(polygonCenter, -rotationRad),
	};
}
