import type { GeojsonApiMapObject } from './map-object';
import type { TAddPathOptions } from '..';
import { Path } from '../map-view-objects';
import type { Coordinate } from '../map-data-objects';
import { randomId } from '../utils';
import type { TPathSectionHighlightOptions } from '../types';
import { getHighlightedPathSegments } from '../navigation/utils';
import { Tween } from '@tweenjs/tween.js';

/**
 * ## Paths in Mappedin JS
 *
 * Paths allow you to draw custom routes on the map, from simple straight lines between coordinates to complex navigation paths that avoid obstacles. While {@link Navigation} provides complete turn-by-turn directions, Paths give you fine-grained control over route visualization.
 *
 * ### Features
 * - Draw paths between any coordinates
 * - Customizable styling (color, width, animation, etc.)
 * - Integration with navigation directions
 * - Animated path drawing and pulse effects
 * - Support for dashed lines and arrows
 *
 * > **Best Practice:** Use `MapData.getDirections()` to generate navigation-aware paths that avoid walls and follow walkable routes, rather than drawing straight lines between coordinates.
 *
 * ### Example Usage
 * ```ts
 * // Draw a simple straight path
 * const path = mapView.Paths.add([coord1, coord2], { color: '#ff0000' });
 *
 * // Draw a navigation path using directions
 * const directions = await mapView.getDirections(space1, space2);
 * if (directions) {
 *   const path = mapView.Paths.add(directions.path, {
 *     color: '#4b90e2',
 *     showPulse: true,
 *     animateDrawing: true
 *   });
 * }
 *
 * // Remove a path
 * mapView.Paths.remove(path);
 *
 * // Remove all paths
 * mapView.Paths.removeAll();
 * ```
 *
 * ### Path Styling Options
 * - `color`: Path color (default: '#4b90e2')
 * - `width`: Path width. Can be a number or an {@link Interpolation}.
 * - `showPulse`: Animated pulse indicating direction
 * - `animateDrawing`: Animate path drawing
 * - `displayArrowsOnPath`: Show direction arrows
 * - `dashed`: Use dashed line style
 *
 * ### Advanced
 * - Use `interactive: true` to make paths clickable.
 *
 * ### More Information
 * - [Wayfinding Guide](https://developer.mappedin.com/web-sdk/wayfinding#drawing-a-path)
 *
 * This class is accessed using {@link MapView.Paths}.
 */
export class Paths {
	#paths: Map<string, { path: Path; entityIds: string[] }> = new Map();
	#getCurrentMap: CurrentMapGetter;
	/**
	 * @internal
	 */
	get currentMap() {
		return this.#getCurrentMap();
	}

	/**
	 * @internal
	 */
	constructor({ currentMapGetter }: { currentMapGetter: CurrentMapGetter }) {
		this.#getCurrentMap = currentMapGetter;
	}

	/**
	 * @internal
	 */
	getById(id: string) {
		return this.#paths.get(id)?.path;
	}

	/**
	 * Adds a path ({@link Path}) to the map.
	 * @param coordinates Array of coordinates ({@link Coordinate}) to form the path.
	 * @param options Optional styling and behavior options (see {@link TAddPathOptions}).
	 * @returns A {@link Path} instance representing the created path.
	 *
	 * @example Draw a simple red path
	 * ```ts
	 * const path = mapView.Paths.add([coord1, coord2], { color: '#ff0000' });
	 * ```
	 *
	 * @example Draw a navigation path with animation
	 * ```ts
	 * const directions = await mapView.getDirections(space1, space2);
	 * if (directions) {
	 *   const path = mapView.Paths.add(directions.path, {
	 *     color: '#4b90e2',
	 *     showPulse: true,
	 *     animateDrawing: true,
	 *     displayArrowsOnPath: true
	 *   });
	 * }
	 * ```
	 *
	 * @example Draw a dashed path with custom width
	 * ```ts
	 * const path = mapView.Paths.add(coordinates, {
	 *   color: '#00ff00',
	 *   dashed: true,
	 *   width: 1,
	 * });
	 * ```
	 *
	 * @see [Wayfinding Guide](https://developer.mappedin.com/web-sdk/wayfinding#drawing-a-path)
	 */
	add(coordinates: Coordinate[], options?: { id?: string } & TAddPathOptions): Path {
		const map = this.currentMap;
		if (!map) throw new Error('currentMap should exist');
		const internalPath = map.Paths.add(coordinates, options);
		const entityIds = internalPath.paths.map(p => p.id.toString());

		const newPath = new Path(options?.id || randomId(), internalPath.animation, coordinates, internalPath.paths);

		for (const id of entityIds) {
			this.#paths.set(id, {
				path: newPath,
				entityIds,
			});
		}
		this.#paths.set(newPath.id, {
			path: newPath,
			entityIds,
		});

		return newPath;
	}
	/**
	 *
	 * Highlights a section of a path between two coordinates.
	 *
	 * @param path The path to highlight.
	 * @param start The start coordinate.
	 * @param end The end coordinate.
	 * @param options The options for the highlight.
	 */
	highlightPathSection(path: Path, start: Coordinate, end: Coordinate, options?: TPathSectionHighlightOptions) {
		const projectionOrigin = this.currentMap?.api.getMapData()?.mapCenter;
		if (!projectionOrigin) {
			return;
		}

		const highlightedPathSegments = getHighlightedPathSegments([path], start, end, projectionOrigin);
		if (!highlightedPathSegments) {
			return;
		}

		const animationDuration = options?.animationDuration ?? 1500;

		let resolveFn: () => void = () => undefined;

		const completedPromise = new Promise(res => {
			resolveFn = res as () => void;
		});

		const tween = new Tween({ fraction: 0.0 })
			.to({ fraction: 1.0 }, animationDuration)
			.onUpdate(value => {
				for (const highlightedPathSegment of highlightedPathSegments) {
					this.currentMap?.api.updateState(highlightedPathSegment.segment, {
						highlightCompleteFraction: value.fraction,
					});
				}
			})
			.onStart(() => {
				for (const highlightedPathSegment of highlightedPathSegments) {
					this.currentMap?.api.updateState(highlightedPathSegment.segment, {
						highlightStartFraction: highlightedPathSegment.startHighlightFraction,
						highlightEndFraction: highlightedPathSegment.endHighlightFraction,
						highlightColor: options?.color ?? 'red',
						highlightWidthMultiplier: options?.widthMultiplier ?? 1,
					});
				}
			})
			.onComplete(() => {
				resolveFn();
				this.currentMap?.api.removeTween(tween);
			});

		this.currentMap?.api.tweenGroup().add(tween);

		tween.start();

		return {
			animation: completedPromise,
		};
	}

	/**
	 * Clears the highlighted section of a path.
	 * @param path The path to clear the highlight from.
	 */
	clearHighlightedPathSection(path: Path) {
		this.currentMap?.api.updateState(path, {
			highlightCompleteFraction: 0.0,
			highlightStartFraction: 0.0,
			highlightEndFraction: 0.0,
		});
	}

	/**
	 * Clears the highlighted section of all paths.
	 */
	clearAllHighlightedPathSections() {
		for (const path of this.#paths.values()) {
			this.clearHighlightedPathSection(path.path);
		}
	}

	/**
	 * Removes a specific path ({@link Path}) from the map.
	 * @param path The path to be removed.
	 * @example
	 * mapView.Paths.remove(path);
	 */
	remove(path: Path) {
		const found = this.#paths.get(path.id);
		if (!found) return;
		this.currentMap?.Paths.remove(found.entityIds);
		this.#paths.delete(path.id);
		found.entityIds.forEach(id => this.#paths.delete(id));
	}
	/**
	 * Removes all paths ({@link Path}) from the map.
	 *
	 * @returns An array of all removed {@link Path} instances.
	 * @example
	 * const removedPaths = mapView.Paths.removeAll();
	 */
	removeAll() {
		const removed: Path[] = [];
		this.#paths.forEach(({ path }) => {
			this.remove(path);
			removed.push(path);
		});

		return removed;
	}
}
type CurrentMapGetter = () => GeojsonApiMapObject | undefined;
