import type {
	EnterpriseCategoryId,
	EnterpriseCategory as V3EnterpriseCategory,
	EnterpriseCategoryId as V3EnterpriseCategoryId,
	EnterpriseLocation as V3EnterpriseLocation,
	EnterpriseLocationInstance as V3EnterpriseLocationInstance,
	EnterpriseVenue as V3EnterpriseVenue,
} from '@mappedin/mvf-cms';
import type { FloorId } from '@mappedin/mvf-core/identifiers';
import { typedEntries } from '@mappedin/mvf-core/utils';
import type { Location, LocationCategory, LocationCategoryId, LocationId } from '@mappedin/mvf-locations';
import type {
	AnchoredFloorTextProperties,
	EnterpriseCategoryPriority,
	EnterpriseCollection,
	EnterpriseFloorText,
	EnterpriseLayer,
	EnterpriseStyles,
	EnterpriseTexture,
	FloatingFloorTextProperties,
	FloorTextCommonProperties,
	FloorTextFeatureCollection,
	FloorTextProperties,
	EnterpriseCategory as V2EnterpriseCategory,
	EnterpriseLocation as V2EnterpriseLocation,
	EnterpriseLocationInstance as V2EnterpriseLocationInstance,
} from '../../extensions/enterprise.js';
import type {
	AnchoredTextAreaProperties,
	BaseTextAreaProperties,
	FloatingTextAreaProperties,
	TextAreaFeature,
	TextAreaFeatureCollection,
	TextAreaProperties,
} from '../../extensions/textArea.js';
import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import { convertLocationCategoryId, convertLocationId, convertLocationInstanceId } from '../converterUtils.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Enterprise = Pick<
	MVFv2_STANDARD_MVFv3,
	'cms' | 'manifest' | 'locations' | 'locationCategories' | 'defaultStyle' | 'locationInstances'
>;
export type V2Enterprise = Pick<ParsedMVF, 'enterprise' | 'textAreas'>;

export function buildEnterprise(
	v3: V3Enterprise,
	{ geometryMaps, nodesByGeometryId }: Pick<BuilderOptions, 'geometryMaps' | 'nodesByGeometryId'>,
): V2Enterprise {
	const v2Enterprise: EnterpriseCollection = {
		categories: [],
		locations: [],
		locationInstances: [],
		layers: [],
		venue: {
			slug: '',
			name: '',
			externalId: '',
			defaultLanguage: {
				name: 'English',
				code: 'en',
			},
			languages: [],
		},
	};

	// Enterprise Venue
	const v3Manifest = v3.manifest?.features[0];

	// Start by filling in what we can off the manifest
	if (v3Manifest) {
		v2Enterprise.venue.defaultFloor = v3Manifest.properties.defaultFloor;
		v2Enterprise.venue.tzid = v3Manifest.properties.tzid;
		v2Enterprise.venue.name = v3Manifest.properties.name;
		v2Enterprise.venue.defaultLanguage = {
			name: v3Manifest.properties.language ?? 'English',
			code: v3Manifest.properties.language ?? 'en',
		};
	}
	//  Then fill in the rest off the enterprise

	const v3Venue = v3.cms?.venue;

	if (v3Venue) {
		v2Enterprise.venue = {
			...v2Enterprise.venue,
			slug: v3Venue.slug,
			name: v3Venue.details?.name ?? v2Enterprise.venue.name ?? v3Manifest.properties.name,
			externalId: v3Venue.details?.externalId ?? '',
			defaultLanguage: v3Venue.defaultLanguage,
			languages: v3Venue.languages ?? [],
			countrycode: v3Venue.countrycode,
			coverImage: v3Venue.coverImage,
			logo: v3Venue.logo,
			icon: v3Venue.details?.icon,
			extra: v3Venue.extra,
			mappedinWebUrl: v3Venue.mappedinWebUrl,
			operationHours: v3Venue.operationHours,
			topLocations: v3Venue.topLocations,
		};
	}

	// Build this for the Enterprise Categories to use later
	const locationsByLocationCategoryId = new Map<LocationCategoryId, LocationId[]>();

	// Enterprise Locations
	const v3Locations = v3.locations ?? [];
	const v3EnterpriseLocations = v3.cms?.locations ?? [];
	const v3EnterpriseCategories = v3.cms?.categories ?? [];

	// Create mapping from V3 location category IDs to V2 enterprise category IDs
	const enterpriseCategoryByLocationCategoryId = new Map<LocationCategoryId | undefined, V3EnterpriseCategory>(
		(v3EnterpriseCategories ?? []).map((cat) => [cat.categoryId, cat]),
	);

	if (v3Locations.length > 0) {
		const eneterpriseLocationsByLocationId = new Map(
			v3EnterpriseLocations.map((enterpriseLocation) => [enterpriseLocation.locationId, enterpriseLocation]),
		);
		for (const v3Location of v3Locations) {
			const v3EnterpriseLocation = (eneterpriseLocationsByLocationId.get(v3Location.id) ??
				{}) as Partial<V3EnterpriseLocation>;
			if (v3Location) {
				const details = v3Location.details;
				const v2EnterpriseLocation: V2EnterpriseLocation = {
					id:
						v3EnterpriseLocation?.cmsId !== undefined
							? `el_${v3EnterpriseLocation?.cmsId}`
							: convertLocationId(v3Location.id),
					name: details.name,
					externalId: details.externalId ?? '',
					type: v3EnterpriseLocation.type ?? 'location',
					sortOrder: v3EnterpriseLocation.sortOrder ?? 0,
					amenity: v3EnterpriseLocation.amenity,
					description: details.description,
					extra: v3Location.extra,
					links: v3Location.links,
					operationHours: v3Location.openingHours,
					gallery: v3EnterpriseLocation.gallery,
					logo: v3Location.logo,
					shortName: details.shortName,
					picture: v3EnterpriseLocation.picture,
					showFloatingLabelWhenImagePresent: v3EnterpriseLocation.showFloatingLabelWhenImagePresent,
					showLogo: v3EnterpriseLocation.showLogo,
					siblingGroups: v3EnterpriseLocation.siblingGroups,
					states: v3EnterpriseLocation.states,
					tags: v3EnterpriseLocation.tags,
					spaces: [],
					nodes: [],
					polygons: [],
				};

				if (v3Location.phone) {
					v2EnterpriseLocation.phone = { number: v3Location.phone };
				}
				if (v3EnterpriseLocation.primaryCategory) {
					const v2EnterpriseCategory = enterpriseCategoryByLocationCategoryId.get(v3EnterpriseLocation.primaryCategory);
					if (v2EnterpriseCategory) {
						v2EnterpriseLocation.primaryCategory = v2EnterpriseCategory.id;
					}
				}
				const social = convertSocials(v3Location.social);

				if (v3Location.website) {
					social.website = v3Location.website.url;
				}
				v2EnterpriseLocation.social = social;

				for (const anchor of v3Location.geometryAnchors) {
					const space = geometryMaps.space.get(anchor.geometryId);
					const obstruction = geometryMaps.obstruction.get(anchor.geometryId);
					// A CMS generated MVFv3 will never have obstruction anchors
					const converted = space ?? obstruction;
					if (converted) {
						const polygons = new Set<string>(
							converted.features.filter((f) => f.geometry.type === 'Polygon').map((f) => f.properties.id),
						);
						for (const anchor of converted.anchors) {
							if (space != null && converted === space) {
								v2EnterpriseLocation.spaces.push({
									floor: anchor.floorId,
									id: anchor.geometryId,
								});
							}
							if (polygons.has(anchor.geometryId)) {
								v2EnterpriseLocation.polygons.push({
									map: anchor.floorId,
									id: anchor.geometryId,
								});
							}
						}
					}
					const nodes = nodesByGeometryId.get(anchor.geometryId);
					for (const node of nodes ?? []) {
						v2EnterpriseLocation.nodes.push({
							map: anchor.floorId,
							id: node,
						});
					}
				}

				v2Enterprise.locations.push(v2EnterpriseLocation);

				// To be used when building enterprise categories
				for (const category of v3Location.categories) {
					locationsByLocationCategoryId.set(category, [
						...(locationsByLocationCategoryId.get(category) ?? []),
						v3Location.id,
					]);
				}
			}
		}
	}

	// Enterprise Location Instances
	const v3EnterpriseLocationInstances = v3.cms?.locationInstances ?? [];
	const v3LocationInstances = v3.locationInstances;
	if (v3LocationInstances) {
		const v3EnterpriseLocationInstancesByLocationInstanceId = new Map(
			v3EnterpriseLocationInstances.map((instance) => [instance.locationInstanceId, instance]),
		);
		const v2EnterpriseLocationInstances: V2EnterpriseLocationInstance[] = [];

		for (const v3LocationInstance of v3LocationInstances) {
			const v3EnterpriseLocationInstance =
				v3EnterpriseLocationInstancesByLocationInstanceId.get(v3LocationInstance.id) ??
				({} as Partial<V3EnterpriseLocationInstance>);

			if (v3LocationInstance) {
				const v2EnterpriseLocationInstance: V2EnterpriseLocationInstance = {
					id: convertLocationInstanceId(v3LocationInstance.id),
					parent: v3EnterpriseLocationInstance.parentId ?? convertLocationId(v3LocationInstance.parentId),
					name: v3LocationInstance.details?.name,
					externalId: v3LocationInstance.details?.externalId,
					type: v3EnterpriseLocationInstance.type,
					sortOrder: v3EnterpriseLocationInstance.sortOrder,
					amenity: v3EnterpriseLocationInstance.amenity,
					description: v3LocationInstance.details?.description,
					extra: v3LocationInstance.extra,
					links: v3LocationInstance.links,
					operationHours: v3LocationInstance.openingHours,
					gallery: v3EnterpriseLocationInstance.gallery,
					picture: v3EnterpriseLocationInstance.picture,
					showFloatingLabelWhenImagePresent: v3EnterpriseLocationInstance.showFloatingLabelWhenImagePresent,
					showLogo: v3EnterpriseLocationInstance.showLogo,
					siblingGroups: v3EnterpriseLocationInstance.siblingGroups,
					states: v3EnterpriseLocationInstance.states,
					tags: v3EnterpriseLocationInstance.tags,
					logo: v3LocationInstance.logo,
					shortName: v3LocationInstance.details?.shortName,
				};
				if (v3LocationInstance.phone) {
					v2EnterpriseLocationInstance.phone = { number: v3LocationInstance.phone };
				}
				const social = convertSocials(v3LocationInstance.social ?? []);
				if (v3LocationInstance.website) {
					social.website = v3LocationInstance.website.url;
				}

				if (Object.keys(social).length > 0) {
					v2EnterpriseLocationInstance.social = social;
				}
				if (v3LocationInstance.geometryAnchors?.length) {
					const spaces = v3LocationInstance.geometryAnchors.flatMap(
						(anchor) => geometryMaps.space.get(anchor.geometryId)?.anchors ?? [],
					);
					if (spaces.length > 0) {
						v2EnterpriseLocationInstance.spaces = spaces.map((space) => ({
							floor: space.floorId,
							id: space.geometryId,
						}));
					}
				}

				// If we have any geometry anchors, we must have overrides for spaces, polygons, and ndoes.
				if (v3LocationInstance.geometryAnchors) {
					v2EnterpriseLocationInstance.spaces = [];
					v2EnterpriseLocationInstance.polygons = [];
					v2EnterpriseLocationInstance.nodes = [];
					for (const anchor of v3LocationInstance.geometryAnchors) {
						const converted = geometryMaps.space.get(anchor.geometryId);
						if (converted) {
							const polygons = new Set<string>(
								converted.features.filter((f) => f.geometry.type === 'Polygon').map((f) => f.properties.id),
							);
							for (const anchor of converted.anchors) {
								v2EnterpriseLocationInstance.spaces.push({
									floor: anchor.floorId,
									id: anchor.geometryId,
								});
								if (polygons.has(anchor.geometryId)) {
									v2EnterpriseLocationInstance.polygons.push({
										map: anchor.floorId,
										id: anchor.geometryId,
									});
								}
							}
						}
						const nodes = nodesByGeometryId.get(anchor.geometryId);
						for (const node of nodes ?? []) {
							v2EnterpriseLocationInstance.nodes.push({
								map: anchor.floorId,
								id: node,
							});
						}
					}
				}

				v2EnterpriseLocationInstances.push(v2EnterpriseLocationInstance);
			}
		}
		if (v2EnterpriseLocationInstances.length > 0) {
			v2Enterprise.locationInstances = v2EnterpriseLocationInstances;
		}
	}

	// Enterprise Categories
	const v3Categories = v3.locationCategories;
	if (v3Categories) {
		// V2 Enterprise Categories have children references to other Enterprise Categories,
		// but V3 Enterprise categories have a parent to a Location Category, which then has a parent to another Location category.
		// This means we need to pre-build a map of children.
		const v2EnterpriseCategories: V2EnterpriseCategory[] = [];
		const v3CategoriesById = new Map<LocationCategoryId | undefined, LocationCategory>(
			v3Categories.map((cat) => [cat.id, cat]),
		);
		const enterpriseCategoryByLocationCategoryId = new Map<LocationCategoryId | undefined, V3EnterpriseCategory>(
			(v3EnterpriseCategories ?? []).map((cat) => [cat.categoryId, cat]),
		);
		const categoryChildren = new Map<LocationCategoryId, LocationCategoryId[]>();

		for (const v3Category of v3Categories) {
			const parent = v3CategoriesById.get(v3Category.parent);
			if (parent) {
				const children = categoryChildren.get(parent.id) ?? [];
				children.push(v3Category.id);
				categoryChildren.set(parent.id, children);
			}
		}
		// Now build the actual Enterprise Categories
		for (const v3Category of v3Categories) {
			const v3EnterpriseCategory = enterpriseCategoryByLocationCategoryId.get(v3Category.id);
			if (v3Category) {
				const children = (categoryChildren.get(v3Category.id) ?? []).map(convertLocationCategoryId);

				const v2EnterpriseCategory: V2EnterpriseCategory = {
					id: convertLocationCategoryId(v3Category.id),
					name: v3Category.details.name,
					externalId: v3Category.details.externalId ?? '',
					sortOrder: v3EnterpriseCategory?.sortOrder ?? 0,
					children,
					locations: (locationsByLocationCategoryId.get(v3Category.id) ?? []).map(convertLocationId),
					icon: v3Category.details.icon,
					picture: v3EnterpriseCategory?.picture,
					color: v3EnterpriseCategory?.color,
					extra: v3Category.extra,
					iconFromDefaultList: v3EnterpriseCategory?.iconFromDefaultList,
				};
				v2EnterpriseCategories.push(v2EnterpriseCategory);
			}
		}
		v2Enterprise.categories = v2EnterpriseCategories;

		// Build the category priorities
		const v2CategoryPriorities: EnterpriseCategoryPriority = {};
		for (const v3Location of v3Locations) {
			v2CategoryPriorities[convertLocationId(v3Location.id)] = v3Location.categories
				.map(convertLocationCategoryId)
				.filter((e): e is V3EnterpriseCategoryId => e !== undefined);
		}
		v2Enterprise.categoryPriorities = v2CategoryPriorities;
	}

	// Build the enterprise styles
	const v2EnterpriseStyles: EnterpriseStyles = {};
	for (const [styleId, style] of Object.entries(v3.defaultStyle ?? {})) {
		// 0 or undefined buffers can be ignored. Positive or negative buffers get converted to edgeOffset.
		if (style.buffer) {
			const convertedAnchors = style.geometryAnchors.flatMap(
				(anchor) => geometryMaps.space.get(anchor.geometryId)?.anchors ?? [],
			);

			v2EnterpriseStyles[styleId] = {
				edgeOffset: style.buffer,
				geometryAnchors: convertedAnchors,
			};
		}
	}
	if (Object.keys(v2EnterpriseStyles).length > 0) {
		v2Enterprise.enterpriseStyles = v2EnterpriseStyles;
	}

	// Build the enterprise layers
	const v2EnterpriseLayers: Map<string, EnterpriseLayer> = new Map();
	for (const layers of Object.values(v3.cms?.layers ?? {})) {
		if (!layers) {
			continue;
		}
		for (const [geometryId, layer] of typedEntries(layers)) {
			// This shouldn't happen, but typescript doesn't seem to know that
			if (typeof layer !== 'string') {
				continue;
			}

			const v2EnterpriseLayer: EnterpriseLayer = v2EnterpriseLayers.get(layer) ?? {
				name: layer,
				id: `ely_${layer}`,
				spaces: [],
			};
			v2EnterpriseLayer.spaces.push(
				...(geometryMaps.space.get(geometryId)?.anchors.map((anchor) => ({
					floorId: anchor.floorId,
					spaceId: anchor.geometryId,
				})) ?? []),
			);
			v2EnterpriseLayers.set(layer, v2EnterpriseLayer);
		}
	}
	if (v2EnterpriseLayers.size > 0) {
		v2Enterprise.layers = Array.from(v2EnterpriseLayers.values());
	}

	// Build the enterprise textures
	const v2EnterpriseTextures: EnterpriseTexture[] = [];
	for (const textures of Object.values(v3.cms?.textures ?? {})) {
		for (const texture of textures) {
			const anchors = geometryMaps.space.get(texture.geometryId)?.anchors;
			// No anchor,s or Unsupported texture types
			if (!anchors || anchors.length < 1 || texture.face === 'unknown' || texture.surface === 'unknown') {
				continue;
			}
			v2EnterpriseTextures.push({
				repeat: texture.repeat,
				offset: texture.offset,
				rotation: texture.rotation,
				path: texture.path,
				face: texture.face,
				surface: texture.surface,
				bounds: texture.bounds,
				geometryAnchors: anchors,
			});
		}
	}
	if (v2EnterpriseTextures.length > 0) {
		v2Enterprise.textures = v2EnterpriseTextures;
	}

	// Build the enterprise floor text
	const v2EnterpriseFloorText: EnterpriseFloorText = {};
	for (const [floorId, floorText] of Object.entries(v3.cms?.floorText ?? {})) {
		const v2FloorTextFeatureCollection: FloorTextFeatureCollection = {
			type: 'FeatureCollection',
			features: [],
		};
		for (const feature of floorText.features) {
			if (feature.properties.align === 'unknown') {
				continue;
			}
			const commonProperties: FloorTextCommonProperties = {
				align: feature.properties.align as 'left' | 'center' | 'right',
				color: feature.properties.color,
				fontFamily: feature.properties.fontFamily,
				fontSize: feature.properties.fontSize,
				rotation: feature.properties.rotation,
				text: feature.properties.text,
				visible: feature.properties.visible,
			};
			if ('geometryId' in feature.properties) {
				(commonProperties as AnchoredFloorTextProperties).geometryId = feature.properties.geometryId;
			} else {
				(commonProperties as FloatingFloorTextProperties).verticalOffset = feature.properties.verticalOffset;
			}
			v2FloorTextFeatureCollection.features.push({
				type: 'Feature',
				geometry: feature.geometry,
				properties: commonProperties as FloorTextProperties,
			});
		}
		if (v2FloorTextFeatureCollection.features.length > 0) {
			v2EnterpriseFloorText[floorId] = v2FloorTextFeatureCollection;
		}
	}
	if (Object.keys(v2EnterpriseFloorText).length > 0) {
		v2Enterprise.floorText = v2EnterpriseFloorText;
	}

	const result: V2Enterprise = { enterprise: v2Enterprise };

	// Build regular v2 text areas from enterprise text areas
	if (v3.cms?.textAreas) {
		const v2TextAreas: { [floorId: FloorId]: TextAreaFeatureCollection } = {};
		for (const [floorId, floorText] of typedEntries(v3.cms.textAreas)) {
			const v2FloorTextFeatureCollection: TextAreaFeatureCollection = {
				type: 'FeatureCollection',
				features: [],
			};
			for (const feature of floorText.features) {
				if (feature.properties.align === 'unknown') {
					continue;
				}
				const commonProperties: BaseTextAreaProperties = {
					align: feature.properties.align as 'near' | 'center',
					maxWidth: feature.properties.maxWidth,
					maxHeight: feature.properties.maxHeight,
					rotation: feature.properties.rotation,
				};
				if ('geometryId' in feature.properties) {
					(commonProperties as AnchoredTextAreaProperties).anchorId = feature.properties.geometryId;
				} else {
					(commonProperties as FloatingTextAreaProperties).verticalOffset = feature.properties.verticalOffset;
				}
				const v2FloorTextFeature: TextAreaFeature = {
					type: 'Feature',
					geometry: feature.geometry,
					properties: commonProperties as TextAreaProperties,
				};
				v2FloorTextFeatureCollection.features.push(v2FloorTextFeature);
			}
			v2TextAreas[floorId] = v2FloorTextFeatureCollection;
		}
		result.textAreas = v2TextAreas;
	}
	return result;
}

const SUPPORTED_SOCIALS = ['facebook', 'instagram', 'twitter'] as const;
export function convertSocials(v3Socials: Location['social']): NonNullable<V2EnterpriseLocation['social']> {
	const v2Socials: NonNullable<V2EnterpriseLocation['social']> = {};
	for (const { name, url } of v3Socials) {
		const lowerName = name.toLowerCase() as (typeof SUPPORTED_SOCIALS)[number];
		if (SUPPORTED_SOCIALS.includes(lowerName)) {
			v2Socials[lowerName] = url;
		}
	}
	return v2Socials;
}
