import z from 'zod';

export const updateGlobalStateSchema = z.strictObject({
	environment: z.union([z.literal('basic'), z.literal(false)]).optional(),
	text3d: z
		.object({
			hoverColor: z.string().optional(),
		})
		.optional(),
	geometry: z
		.object({
			hoverColor: z.string().optional(),
		})
		.optional(),
	backgroundColor: z.string().optional(),
	backgroundAlpha: z.number().optional(),
});
