import { coordEach } from '@turf/meta';
import type {
	IFocusable,
	TCameraAnimationOptions,
	TCameraTarget,
	TFocusOnOptions,
	TCameraInteractionsSetOptions,
} from '../types';
import type { GroupContainerState, InsetPadding, Position, RendererCore } from '../../../geojson/src';
import { Coordinate, Floor } from '../map-data-objects';
import { isPosition } from '../utils';
import type { GeoJsonApi } from './api';
import { convertPanBoundsToCameraBounds } from './utils';
import type { CameraBounds } from '../events';

/**
 * ## Camera in Mappedin JS
 *
 * The Camera class controls the map's viewpoint, including position, pitch, bearing, and zoom. It can instantly reposition or animate to a new location, and can focus on one or more map elements.
 *
 * ### Features
 * - Focus on any map element (space, coordinate, marker, etc.)
 * - Smooth animation and custom easing
 * - Control pan, zoom, bearing, and pitch
 * - Set safe screen areas (insets) for UI overlays
 *
 * > **Best Practice:** Use `focusOn` for user-driven navigation (e.g., "Show this store"), and `animateTo` for smooth transitions between known camera states.
 *
 * ### Example Usage
 * ```ts
 * // Focus on a single space
 * mapView.Camera.focusOn(space, { minZoomLevel: 5 });
 *
 * // Animate to a specific camera target
 * mapView.Camera.animateTo({ center: coordinate, zoomLevel: 6, bearing: 45 });
 *
 * // Set the camera instantly
 * mapView.Camera.set({ center: coordinate, zoomLevel: 7 });
 *
 * // Set safe screen insets (e.g., for a sidebar)
 * mapView.Camera.setScreenOffsets({ left: 300, top: 0, right: 0, bottom: 0 });
 * ```
 *
 * ### Advanced
 * - Use `getFocusOnTransform` to calculate the camera transform before applying it.
 * - Use `interactions.set` to enable/disable pan, zoom, or rotation.
 *
 * ### More Information
 * - [Camera Guide](https://developer.mappedin.com/web-sdk/camera)
 *
 * This class is accessed using {@link MapView.Camera}.
 */
export class Camera {
	#rendererCore: RendererCore;
	#api: GeoJsonApi;
	/**
	 * @internal
	 */
	constructor(api: GeoJsonApi) {
		this.#api = api;
		this.#rendererCore = api.core;
	}

	/**
	 * Controls which camera interactions are enabled/disabled.
	 *
	 * @example
	 * // Disable zoom and rotation
	 * mapView.Camera.interactions.set({ zoom: false, bearingAndPitch: false });
	 *
	 * // Enable all interactions
	 * mapView.Camera.interactions.enable();
	 *
	 * // Disable all interactions
	 * mapView.Camera.interactions.disable();
	 */
	interactions = {
		set: (options: TCameraInteractionsSetOptions) => {
			if (options?.pan != null) {
				this.#rendererCore.getSystems().cameraSystem.enablePan = options.pan;
			}
			if (options?.zoom != null) {
				this.#rendererCore.getSystems().cameraSystem.enableZoom = options.zoom;
			}
			if (options?.bearingAndPitch != null) {
				this.#rendererCore.getSystems().cameraSystem.enableRotate = options.bearingAndPitch;
			}
		},
		enable: () => {
			this.#rendererCore.getSystems().cameraSystem.enablePan = true;
			this.#rendererCore.getSystems().cameraSystem.enableZoom = true;
			this.#rendererCore.getSystems().cameraSystem.enableRotate = true;
		},
		disable: () => {
			this.#rendererCore.getSystems().cameraSystem.enablePan = false;
			this.#rendererCore.getSystems().cameraSystem.enableZoom = false;
			this.#rendererCore.getSystems().cameraSystem.enableRotate = false;
		},
	};

	/**
	 * Define an area of the screen that is safe for the camera. Anything outside the safe area is assumed to be covered in some way. The camera will not place any map elements there when calling {@link Camera.focusOn}.
	 *
	 * @param padding The padding to apply. Can specify individual sides and optionally the type.
	 * - `type`: 'pixel' (absolute pixels) or 'portion' (fraction of canvas, 0-1). Defaults to 'pixel'.
	 * - When updating without specifying type, the existing type is preserved.
	 * @example
	 * // Reserve 300px on the left for a sidebar
	 * mapView.Camera.setScreenOffsets({ left: 300, top: 0, right: 0, bottom: 0 });
	 *
	 * @example
	 * // Reserve 20% of the screen height at the top using portion type
	 * mapView.Camera.setScreenOffsets({ top: 0.2, type: 'portion' });
	 *
	 * @example
	 * // Update only the bottom padding, preserving existing type and other values
	 * mapView.Camera.setScreenOffsets({ bottom: 50 });
	 */
	setScreenOffsets(padding: Partial<InsetPadding>) {
		this.#rendererCore.camera.setInsetPadding(padding);
	}

	get screenOffsets() {
		return this.#rendererCore.camera.insetsPadding;
	}

	/**
	 * The camera's current pan mode.
	 */
	get panMode() {
		return this.#rendererCore.camera.panMode;
	}

	/**
	 * Set the camera's pan mode. 'elevation' moves the camera up and down, while 'default' allows the camera to pan in all directions.
	 * @experimental
	 * @param panMode The new pan mode.
	 */
	setPanMode(panMode: 'default' | 'elevation') {
		if (panMode !== 'default' && panMode !== 'elevation') {
			throw new Error('Invalid pan mode');
		}
		if (panMode === this.panMode) {
			return;
		}
		this.#rendererCore.camera.setPanMode(panMode);
	}

	/**
	 * Get the camera transform that can be used to focus on a target or array of targets. Similar to {@link Camera.focusOn} but returns the transform directly.
	 * @param target The target(s) to get the camera transform for.
	 * @param options Optional settings for the camera transform.
	 * @returns The camera transform which can then be passed to {@link Camera.set} or {@link Camera.animateTo}.
	 *
	 * @example
	 * // Focus on a single space
	 * const transform = mapView.Camera.getFocusOnTransform(space, { minZoomLevel: 5 });
	 * mapView.Camera.animateTo(transform);
	 */
	getFocusOnTransform(target: IFocusable | IFocusable[], options?: TFocusOnOptions): TCameraTarget {
		const frame = this.#rendererCore.camera.getCameraFrameForCoordinates(this.#computeCoordinateCloud(target), {
			pitch: options?.pitch,
			bearing: options?.bearing,
			padding: options?.screenOffsets || this.screenOffsets,
			maxZoomLevel: options?.maxZoomLevel,
			verticalPadding: options?.axisAlignedPadding?.vertical,
			minZoomLevel: options?.minZoomLevel,
			duration: options?.duration,
			easing: options?.easing,
		});

		return {
			center: new Coordinate(frame.center[1], frame.center[0]),
			zoomLevel: frame.zoomLevel,
			bearing: options?.bearing ?? this.bearing,
			pitch: options?.pitch ?? this.pitch,
		};
	}

	#getFloorAltitude(thing: IFocusable) {
		const fallback = this.#rendererCore.camera.elevation;
		if (Coordinate.is(thing) && thing.floorId != null) {
			return this.#rendererCore.getState<GroupContainerState>(thing.floorId)?.altitude ?? fallback;
		} else if (Floor.is(thing)) {
			return this.#rendererCore.getState<GroupContainerState>(thing.id)?.altitude ?? fallback;
		} else if ('floor' in thing && Floor.is(thing.floor as Floor)) {
			return this.#rendererCore.getState<GroupContainerState>((thing.floor as Floor).id)?.altitude ?? fallback;
		}

		return fallback;
	}

	#computeCoordinateCloud(target: IFocusable | IFocusable[]) {
		let targets: (Position | string)[] = [];
		const thingsArr = Array.isArray(target) ? target : [target];
		const flattenedThings = thingsArr.flatMap(t => t?.focusTarget ?? [t]);

		for (const thing of flattenedThings) {
			if (thing == null) {
				continue;
			}

			if (Coordinate.is(thing)) {
				// get the floor altitude, if the thing is on a floor
				const floorAltitude = this.#getFloorAltitude(thing);

				targets.push([thing.longitude, thing.latitude, floorAltitude]);
			} else if (thing?.id != null) {
				// get the floor altitude, if the thing is on a floor
				const floorAltitude = this.#getFloorAltitude(thing);

				// NOTE: Prefer direct coordinates to camera.focusOn - camera space calculations
				// provide tighter bounds than world space AABB
				// When sending floor IDs: Enterprise doesn't have floor geometries in MVF,
				// so we pass IDs to core to get world coordinates for camera calculations.
				if (Floor.is(thing) && this.#api.getMapDataInternal()?.enterpriseMode) {
					targets.push(thing.id);
					continue;
				} else if ('geoJSON' in thing && thing.geoJSON.geometry.coordinates.length > 0) {
					coordEach(thing.geoJSON, coord => {
						targets.push([coord[0], coord[1], floorAltitude]);
					});

					continue;
				}

				// if sending down geojson doesn't work we use bounding box approach.
				const boundingBox = this.#rendererCore.getBoundingArea(thing.id);
				if (boundingBox) {
					targets.push(...boundingBox.map(coord => [coord[0], coord[1], floorAltitude] as Position));
				} else if ('geoJSONBoundingBox' in thing) {
					const floorAltitude = this.#getFloorAltitude(thing);
					// if the thing has a geoJSONBoundingBox, use that
					if (thing.geoJSONBoundingBox != null) {
						const coordinates = [
							[thing.geoJSONBoundingBox[0], thing.geoJSONBoundingBox[1], floorAltitude],
							[thing.geoJSONBoundingBox[2], thing.geoJSONBoundingBox[3], floorAltitude],
						] as Position[];
						targets.push(...coordinates);
					}
				}
			} else if (isPosition(thing)) {
				targets.push(thing);
			}
		}

		return targets;
	}

	/**
	 * Focuses the camera on a specific target or array of targets.
	 *
	 * @param target The target(s) to focus on, either a single element or an array of elements.
	 * @param options Optional settings for focusing the camera (see {@link TFocusOnOptions}).
	 * @returns A promise that resolves when the camera animation is complete.
	 *
	 * @example Focus on a single space
	 * ```ts
	 * mapView.Camera.focusOn(space, { minZoomLevel: 5 });
	 * ```
	 *
	 * @example Focus on multiple elements
	 * ```ts
	 * mapView.Camera.focusOn([space1, space2], { pitch: 45 });
	 * ```
	 *
	 * @see [Camera Guide](https://developer.mappedin.com/web-sdk/camera)
	 */
	focusOn(target: IFocusable | IFocusable[], options?: TFocusOnOptions): Promise<void> {
		return this.#rendererCore.camera.focusOn(this.#computeCoordinateCloud(target), {
			pitch: options?.pitch,
			bearing: options?.bearing,
			padding: options?.screenOffsets || this.screenOffsets,
			maxZoomLevel: options?.maxZoomLevel,
			minZoomLevel: options?.minZoomLevel,
			duration: options?.duration,
			verticalPadding: options?.axisAlignedPadding?.vertical,
			easing: options?.easing,
		});
	}

	/**
	 * Animates the camera to a specific target.
	 *
	 * @param target The camera target ({@link TCameraTarget}) to animate to.
	 * @param options Optional animation options (see {@link TCameraAnimationOptions}).
	 * @returns A promise that resolves when the animation is complete.
	 *
	 * @example
	 * mapView.Camera.animateTo({ center: coordinate, zoomLevel: 6, bearing: 45 }, { duration: 1000 });
	 */
	animateTo(target: TCameraTarget, options?: TCameraAnimationOptions): Promise<void> {
		const { center: _center, zoomLevel, bearing, pitch } = target;

		const center = _center ?? this.center;

		return this.#rendererCore.camera.animateTo({
			center:
				_center != null
					? ([center.longitude, center.latitude, this.#rendererCore.camera.elevation] as Position)
					: undefined,
			zoomLevel: zoomLevel,
			bearing: bearing,
			pitch: pitch,
			duration: options?.duration ?? 600,
			easing: options?.easing,
		});
	}

	/**
	 * Instantly sets the camera to a specific target.
	 *
	 * @param target The camera target ({@link TCameraTarget}) to set.
	 *
	 * @example
	 * mapView.Camera.set({ center: coordinate, zoomLevel: 7 });
	 */
	set(target: TCameraTarget): void {
		const { center = this.center, zoomLevel = this.zoomLevel, bearing = this.bearing, pitch = this.pitch } = target;

		const position = [center.longitude, center.latitude] as Position;

		this.#rendererCore.camera.setCenter(position);
		this.#rendererCore.camera.setZoomLevel(zoomLevel);
		this.#rendererCore.camera.setBearing(bearing);
		this.#rendererCore.camera.setPitch(pitch);
	}

	/**
	 * The current center coordinate ({@link Coordinate}) of the camera.
	 */
	get center() {
		const center = this.#rendererCore.camera.center;

		return new Coordinate(center[1], center[0], undefined);
	}

	/**
	 * Toggle the mode of the camera to automatically set the minimum zoom level based on the size of the scene.
	 * It will be automatically disabled when the minimum zoom level is set manually.
	 * @param value The new value for the auto min zoom level mode.
	 */
	setAutoMinZoomLevel(value: boolean) {
		this.#rendererCore.camera.setAutoMinZoomLevel(value);
	}

	/**
	 * The mode of the camera to automatically set the minimum zoom level based on the size of the scene.
	 */
	get autoMinZoomLevel() {
		return this.#rendererCore.camera.autoMinZoomLevel;
	}

	/**
	 * The current zoom level of the camera in mercator zoom levels.
	 *
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 */
	get zoomLevel() {
		return this.#rendererCore.camera.zoomLevel;
	}

	/**
	 * The current pitch of the camera in degrees.
	 */
	get pitch() {
		return this.#rendererCore.camera.pitch;
	}

	/**
	 * The minimum pitch of the camera in degrees.
	 */
	get minPitch() {
		return this.#rendererCore.camera.minPitch;
	}

	/**
	 * The maximum pitch of the camera in degrees.
	 */
	get maxPitch() {
		return this.#rendererCore.camera.maxPitch;
	}

	/**
	 * Update the minimum pitch of the camera in degrees.
	 * @param minPitch The new minimum pitch.
	 */
	setMinPitch = (minPitch: number) => {
		this.#rendererCore.camera.setMinPitch(minPitch);
	};

	/**
	 * Update the maximum pitch of the camera in degrees.
	 * @param maxPitch The new maximum pitch.
	 */
	setMaxPitch = (maxPitch: number) => {
		this.#rendererCore.camera.setMaxPitch(maxPitch);
	};

	/**
	 * The current bearing of the camera in degrees clockwise from North. 0 degrees is North, 90 degrees is East, 180 degrees is South, and 270 degrees is West.
	 */
	get bearing() {
		const bearing = this.#rendererCore.camera.bearing;

		return (bearing < 0 ? bearing + 360 : bearing) % 360;
	}
	/**
	 * The minimum zoom level of the camera in mercator zoom levels.
	 *
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 */
	get minZoomLevel() {
		return this.#rendererCore.camera.minZoomLevel;
	}
	/**
	 * Update the minimum zoom level of the camera in mercator zoom levels.
	 * @param zoomLevel The new minimum zoom level.
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 */
	setMinZoomLevel = (zoomLevel: number) => {
		this.#rendererCore.camera.setMinZoomLevel(zoomLevel, true);
	};
	/**
	 * The maximum zoom level of the camera in mercator zoom levels.
	 *
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 */
	get maxZoomLevel() {
		return this.#rendererCore.camera.maxZoomLevel;
	}
	/**
	 * Update the maximum zoom level of the camera in mercator zoom levels.
	 * @param zoomLevel The new maximum zoom level.
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 * @default 22
	 * @max 24
	 * @min 0
	 */
	setMaxZoomLevel = (zoomLevel: number) => {
		this.#rendererCore.camera.setMaxZoomLevel(zoomLevel);
	};

	/**
	 * Set the camera's elevation in meters.
	 * @experimental
	 * @param elevation The new elevation in meters.
	 */
	setElevation(elevation: number) {
		this.#rendererCore.camera.setElevation(elevation);
	}

	/**
	 * The current elevation of the camera in meters.
	 */
	get elevation() {
		return this.#rendererCore.camera.elevation;
	}

	/**
	 * Animate the camera's elevation to a specified elevation.
	 * @experimental
	 * @param elevation The target elevation in meters.
	 * @param options Optional settings for the camera animation.
	 */
	animateElevation(elevation: number, options?: TCameraAnimationOptions) {
		return this.#rendererCore.camera.animateElevation(elevation, options);
	}

	/**
	 * The current pan boundary of the camera.
	 */
	get bounds(): CameraBounds {
		return convertPanBoundsToCameraBounds(
			this.#rendererCore.getSystems().cameraSystem.panBounds,
			this.#rendererCore.convert3DMapPositionToCoordinate,
		);
	}

	get isAnimating() {
		return this.#rendererCore.camera.isAnimating;
	}

	cancelAnimation() {
		this.#rendererCore.camera.cancelAnimation();
	}

	/**
	 * @internal
	 * @experimental
	 * Force an update of the facades in view.
	 */
	updateFacadesInView() {
		this.#rendererCore.getSystems().geometryInFocusSystem.raycast();
	}
}
