export async function createRenderer(container: HTMLElement, options?: RendererCoreOptions & { [key: string]: any }) {
	const useWorkers = options?.useWorkers ?? true;

	const coreOptions = {
		...options,
		watermark: options?.watermark,
		attribution: options?.attribution,
		antialias: options?.antialias,
		occlusionEnabled: options?.occlusionEnabled,
		transformImageRequest: options?.transformImageRequest,
		naturalBearing: options?.naturalBearing ?? 0,
		imagePlacementOptions: options?.imagePlacementOptions,
		onWebGLContextCreationError: options?.onWebGLContextCreationError,
		onWebGLContextLost: options?.onWebGLContextLost,
		onWebGLContextRestored: options?.onWebGLContextRestored,
		onWebGLRendererError: options?.onWebGLRendererError,
		useStandaloneCamera: options?.useStandaloneCamera ?? false,
		useCollisionWorker: options?.useCollisionWorker ?? true,
		useWorkers,
	};

	if (useWorkers === true && options?.outdoorView?.enabled) {
		let headers = options.outdoorView.headers;
		if (headers == null) {
			headers = await getRequestHeaders(authURL);
		}

		const center = (options.center || [0, 0]) as LngLatLike;

		const map = new MapLibreMap({
			container,
			style: options.outdoorView.style || url,
			center,
			attributionControl: false, // we handle this ourselves
			zoom: options.zoomLevel ?? DEFAULT_ZOOM_LEVEL,
			pitch: options.pitch ?? DEFAULT_PITCH,
			bearing: options.bearing ?? DEFAULT_BEARING,
			transformRequest: transformRequest(headers),
			trackResize: false,
			pixelRatio: options.outdoorView.lowDpi ? 1 : getPixelRatio(),
			canvasContextAttributes: {
				antialias: typeof options.antialias === 'boolean' ? options.antialias : true,
				powerPreference: 'high-performance',
			},
			interactive: false,
			validateStyle: false,
			centerClampedToGround: false,
			boxZoom: false,
			doubleClickZoom: false,
			scrollZoom: false,
			dragPan: false,
			dragRotate: false,
			keyboard: false,
			touchZoomRotate: false,
		});

		const gl = map.painter.context.gl;

		const core = new RendererCore(container, {
			center: center as Position,
			zoomLevel: map.getZoom(),
			pitch: map.getPitch(),
			bearing: map.getBearing(),
			gl: gl,
			map: map,
			mode: 'outdoors-interleaved',
			...coreOptions,
		});

		const cachedPosition = {
			center: map.getCenter().toArray() as LngLatLike,
			zoom: map.getZoom(),
			bearing: map.getBearing(),
			pitch: map.getPitch(),
			elevation: map.getCenterElevation(),
		};

		map.setMinZoom(core.camera.minZoomLevel);
		map.setMaxZoom(core.camera.maxZoomLevel);
		map.setMaxPitch(core.camera.maxPitch);
		map.setMinPitch(core.camera.minPitch);

		core.on('camera-change', position => {
			cachedPosition.center = position.center as LngLatLike;
			cachedPosition.zoom = position.zoomLevel;
			cachedPosition.bearing = position.bearing;
			cachedPosition.pitch = position.pitch;
			cachedPosition.elevation = position.elevation;
			map.jumpTo(cachedPosition);
		});

		const modelAsMercatorCoordinate = MercatorCoordinate.fromLngLat(center as LngLatLike, 0);

		// @TODO: should viewCamera be exposed this way?
		const layer = createCustomLayer(modelAsMercatorCoordinate, core.viewCamera, () => {
			core.renderSync();
		});

		const styleLoadHandler = async () => {
			if (!map) {
				return;
			}
			map.addLayer(layer);
			core.renderSync();
		};
		core.onMapLibreEvent('style.load', styleLoadHandler);

		let outdoorsLoaded = false; // will be true when outdoor view has finished loading in map.on('idle')
		let outdoorStyleLoaded = false; // set to true in map.on('idle'), set back to false in map.on('styledata')
		const idleHandler = () => {
			if (!map) {
				return;
			}
			if (!outdoorsLoaded) {
				core.publish('outdoor-view-loaded');
				outdoorsLoaded = true;
			}
			if (!outdoorStyleLoaded) {
				outdoorStyleLoaded = true;
				// we can't rely on style.load event, as it's not fired when the style changes
				core.publish('outdoor-style-loaded');
			}
		};
		core.onMapLibreEvent('idle', idleHandler);

		const updateData = (e: MapDataEvent) => {
			if (
				e &&
				(e.sourceDataType === 'metadata' ||
					e.sourceDataType === 'visibility' ||
					e.dataType === 'style' ||
					e.type === 'terrain')
			) {
				core.getSystems().htmlControlsSystem.updateData(map);
			}
		};

		const styleDataHandler = (e: MapStyleDataEvent) => {
			if (map && e && e.dataType === 'style' && e.type === 'styledata') {
				outdoorStyleLoaded = false;
			}
			updateData(e as unknown as MapDataEvent);
		};
		core.onMapLibreEvent('styledata', styleDataHandler);
		core.onMapLibreEvent('sourcedata', updateData);
		core.onMapLibreEvent('terrain', updateData);

		const errorHandler = (e: any) => {
			if (e.error?.url === url) {
				Logger.error(
					`Failed to load outdoor view style, possibly due to invalid token "${headers['x-mappedin-tiles-key']}"`,
				);

				return;
			}

			Logger.warn(e);
		};
		core.onMapLibreEvent('error', errorHandler);

		return core;
	} else {
		return new RendererCore(container, coreOptions);
	}
}
