	/**
	 * Takes a screenshot of the current scene and returns it as a data URL.
	 *
	 * NOTE: This only captures the 3D scene, and optionally the outdoor context, not the UI elements like labels, markers, etc.
	 * Also, this does not cause the screenshot to be saved to the user's device, it only returns
	 * the data.
	 *
	 * @param options - Options for taking a screenshot.
	 * @param options.withOutdoorContext - Whether to include the outdoor context in the screenshot.
	 * @default false
	 *
	 * @returns A Promise that resolves with the screenshot as a base64-encoded data URL string
	 * @experimental
	 */
	async takeScreenshot(options?: TTakeScreenshotOptions): Promise<string> {
		const takeScreenshotOptions = {
			withOutdoorContext: options?.withOutdoorContext ?? false,
		};

		const indoorCanvas = this.#core.rendererDomElement;

		if (takeScreenshotOptions.withOutdoorContext) {
			const outdoorMap = this.#core.map;

			if (!outdoorMap) {
				await this.#core.renderSync();
				return indoorCanvas.toDataURL('image/png');
			}

			if (!outdoorMap.loaded()) {
				await new Promise(resolve => {
					outdoorMap.once('idle', resolve);
				});
			}

			const outdoorCanvas = outdoorMap.getCanvas();

			const compositeCanvas = document.createElement('canvas');
			const compositeCtx = compositeCanvas.getContext('2d');
			compositeCanvas.width = indoorCanvas.width;
			compositeCanvas.height = indoorCanvas.height;

			if (!compositeCtx) {
				throw new Error('Failed to get 2D context for composite canvas');
			}

			compositeCtx.drawImage(outdoorCanvas, 0, 0, compositeCanvas.width, compositeCanvas.height);
			compositeCtx.drawImage(indoorCanvas, 0, 0, compositeCanvas.width, compositeCanvas.height);

			return compositeCanvas.toDataURL('image/png');
		}

		await this.#core.renderSync();
		return indoorCanvas.toDataURL('image/png');
	}

