import type { AnnotationCollection } from '@mappedin/mvf-v2';
import type { Floor, MapDataInternal } from '.';
import type Coordinate from './coordinate';
import type { IGeoJSONData } from './types';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * A class representing annotation data within the map.
 *
 * Annotations are used to mark specific points or areas on the map with additional information.
 * It includes some details on a map that may be relevant to safety or accessibility (e.g. Fire Extinguishers).
 * Refer to the [Annotation Guide](https://developer.mappedin.com/web-sdk/annotations) for more information.
 */
class Annotation
	extends DetailedMapData<AnnotationCollection['features'][number]>
	implements IGeoJSONData, IFocusable, IAnchorable
{
	/**
	 * @internal
	 */
	static readonly __type = 'annotation';

	/**
	 * @internal
	 */
	readonly __type = Annotation.__type;

	#data: MapDataInternal;
	#floorId: string;
	#cache: Partial<{ coordinate: Coordinate }> = {};
	#mvfData: AnnotationCollection['features'][number];

	/**
	 * Checks if the provided instance is of type Annotation.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is an Annotation, false otherwise.
	 */
	static is(instance: object): instance is Annotation {
		return (instance as { __type: string }).__type === Annotation.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: AnnotationCollection['features'][number];
		},
	) {
		super(data, options.mvfData);
		this.#data = data;
		this.#floorId = options.floorId;
		this.#mvfData = options.mvfData;
	}

	/**
	 * Gets the group and type of the annotation.
	 *
	 * @returns {object} An object containing group and type.
	 */
	#getGroupAndTypeFromSymbolId() {
		const [group, type] = this.#mvfData.properties.symbolId.split('--');

		return { group, type };
	}

	/**
	 * Gets the group of the annotation.
	 *
	 * @returns {string} The annotation group.
	 */
	get group() {
		return this.#getGroupAndTypeFromSymbolId().group;
	}

	/**
	 * Gets the type of the annotation.
	 *
	 * @returns {string} The annotation type.
	 */
	get type(): string {
		return this.#getGroupAndTypeFromSymbolId().type;
	}

	/**
	 * Gets the center {@link Coordinate} of the annotation.
	 *
	 * @returns {Coordinate} The annotation's coordinate.
	 */
	get coordinate(): Coordinate {
		if (this.#cache.coordinate == null) {
			this.#cache.coordinate = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.coordinate;
	}

	/**
	 * Gets the {@link Floor} object associated with the annotation.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	// TODO: Implement this
	// get icon() {
	//      return ''
	// }

	/**
	 * Gets the underlying GeoJSON Feature representation of this Annotation.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this.coordinate;
	}

	/** @internal */
	get anchorTarget() {
		return this.coordinate;
	}

	/**
	 * Converts the annotation information to a JSON object.
	 *
	 * @returns An object representing the annotation.
	 */
	toJSON() {
		return {
			__type: Annotation.__type,
			id: this.id,
			group: this.group,
			type: this.type,
			coordinate: this.coordinate.toJSON(),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Annotation;
