import type Coordinate from './coordinate';
import type { EnterpriseLocation, MapDataInternal, MapObject, Space } from '.';
import type Floor from './floor';
import BaseMetaData from './base-metadata-object';
import type { IGeoJSONData, MVFNodeFeature } from './types';
import type { IAnchorable, IFocusable } from '../types';
import { getCenterCoordinate, hasFlag } from './utils';
import type { Writable } from 'type-fest';

/**
 * A class representing {@link Path} node data within the map.
 *
 * Nodes are used to define points in the map's pathfinding graph.
 *
 */
class Node extends BaseMetaData implements IGeoJSONData, IFocusable, IAnchorable {
	/**
	 * @internal
	 */
	static readonly __type = 'node';

	/**
	 * @internal
	 */
	readonly __type = Node.__type;

	#data: MapDataInternal;
	#cache: Partial<Writable<Node>> = {};
	#floorId: string;
	#mvfData: MVFNodeFeature;

	/**
	 * Checks if the provided instance is of type Node.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Node, false otherwise.
	 */
	static is(instance: object): instance is Node {
		return (instance as { __type: string }).__type === Node.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: MVFNodeFeature;
		},
	) {
		super(options.mvfData.properties.id);
		this.#data = data;
		this.#floorId = options.floorId;
		this.#mvfData = options.mvfData;
	}

	/** @internal */
	get locations(): EnterpriseLocation[] {
		if (this.#cache.locations) {
			return this.#cache.locations;
		}

		this.#cache.locations = (this.#data.locationIdsByNodeId[this.id] || [])
			.map(id => this.#data.getById('enterprise-location', id))
			.filter(Boolean) as EnterpriseLocation[];

		return this.#cache.locations;
	}

	/**
	 * Gets the {@link Space} object associated with the node.
	 */
	get space(): Space | undefined {
		return this.#data.getById('space', this.#data.spaceIdsByNodeId[this.id]?.[0]);
	}

	/**
	 * Gets the {@link MapObject} object associated with the node.
	 */
	get object(): MapObject | undefined {
		return this.#data.getById('object', this.#data.objectIdsByNodeId[this.id]?.[0]);
	}

	/**
	 * Gets the {@link Floor} associated with the node.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Gets the {@link Coordinate} of the node.
	 *
	 * @returns {Coordinate} The node's coordinate.
	 */
	get coordinate(): Coordinate {
		if (this.#cache.coordinate == null) {
			this.#cache.coordinate = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.coordinate;
	}

	/**
	 * Gets the external ID of the node.
	 *
	 * @returns {string} The external ID of the node.
	 */
	get externalId(): string {
		return this.#mvfData.properties.externalId ?? '';
	}

	/**
	 * Gets the neighboring nodes of this node.
	 *
	 * @returns {Node[]} An array of neighboring Node objects.
	 */
	get neighbors(): Node[] {
		if (this.#cache.neighbors == null) {
			const _neighbours = this.#mvfData.properties.neighbors.map(neighbor => {
				const _node = this.#data.getById('node', neighbor.id);
				if (_node == null) {
					throw new Error(`Node ${neighbor.id} not found!`);
				}

				return _node;
			});
			this.#cache.neighbors = _neighbours;
		}

		return this.#cache.neighbors;
	}

	/**
	 * Gets the navigation flags of the node.
	 *
	 * Navigation flags are string identifiers that describe properties of this node for navigation,
	 * such as `accessible` for wheelchair accessibility or `outdoors` for outdoor routes.
	 *
	 * @returns {string[]} An array of navigation flags present on this node.
	 */
	get navigationFlags(): readonly string[] {
		if (this.#cache.navigationFlags == null) {
			const flags: string[] = [];

			const flagsArray = this.#mvfData.properties.flags;
			if (flagsArray != null && flagsArray.length > 0) {
				for (const [flagName, flagDeclaration] of Object.entries(this.#data.navigationFlags)) {
					if (flagsArray.length > flagDeclaration.index && flagsArray[flagDeclaration.index] != null) {
						if (hasFlag({ flags: flagsArray }, flagDeclaration)) {
							flags.push(flagName);
						}
					}
				}
			}

			this.#cache.navigationFlags = Object.freeze(flags);
		}

		return this.#cache.navigationFlags;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Node.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this.coordinate;
	}

	/** @internal */
	get anchorTarget() {
		return this.coordinate;
	}

	/**
	 * Serializes the node data to JSON.
	 *
	 * @returns An object representing the node.
	 */
	toJSON() {
		return {
			__type: Node.__type,
			id: this.id,
			floor: this.floor.id,
			coordinate: this.coordinate.toJSON(),
			neighbors: this.neighbors.map(neighbor => neighbor.id),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Node;
