const regex = /<svg([^>]*)>/g;
export function getLogoIcon(
	iconName: (typeof ICON_NAMES)[number],
	color: string,
	width: number,
	height: number,
): string {
	const icon = ICONS.get(iconName);
	if (!icon) {
		return '';
	}

	return icon.replace(
		regex,
		`<svg$1 width="${width}" height="${height}" xmlns="http://www.w3.org/2000/svg" style="fill: ${color};">`,
	);
}

export default ICONS;
