export type TDrawFn = (ctx: CanvasRenderingContext2D | OffscreenCanvasRenderingContext2D, x: number, y: number) => void;

type TShave = (options: {
	text: string;
	size: number;
	maxWidth: number;
	pixelRatio: number;
	maxLines?: number;
	lineHeight?: number;
	options?: {
		strokeText: boolean;
	};
}) => // maxHeight: number
{
	maxWidth: number;
	maxHeight: number;
	lines: number;
	draw: TDrawFn;
};

export const getMeasureCanvas = (function createMeasureCanvasContext() {
	let measureCanvas: HTMLCanvasElement | undefined;
	let measureCanvasContext: CanvasRenderingContext2D | undefined | null;

	return (pixelRatio: number) => {
		if (!measureCanvasContext || !measureCanvasContext) {
			measureCanvas = document.createElement('canvas');
			measureCanvas.width = 500 * pixelRatio;
			measureCanvas.height = 500 * pixelRatio;
			measureCanvas.style.width = '500px';
			measureCanvas.style.height = '500px';
			measureCanvasContext = measureCanvas.getContext('2d');
		}
		if (!measureCanvasContext) {
			throw new Error('Could not get 2D context');
		}

		return {
			measureCanvas,
			measureCanvasContext,
		};
	};
})();

export const shaveText: TShave = ({
	text,
	size,
	maxWidth,
	pixelRatio,
	maxLines = 2,
	lineHeight = 1.2,
	options = { strokeText: true },
}) => {
	const font = `bold ${size * pixelRatio}px 'InterUI-Regular', Helvetica Neue, Arial, sans-serif`;

	const { measureCanvasContext } = getMeasureCanvas(pixelRatio);

	measureCanvasContext.font = font;

	const lines: string[] = [];

	const words: string[] = text.split(' ');
	let linePtr = 0;
	let wordPtr = 0;

	let maxTextWidth = 0;
	// go through each word

	while (wordPtr < words.length) {
		const word = words[wordPtr];

		// if we exceed the allowed number of lines, append ... and quit
		if (linePtr === maxLines && linePtr > 0) {
			lines[linePtr - 1] += '...';
			break;
		}

		// if the current line is empty, append the current word
		if (lines[linePtr] == null) {
			lines[linePtr] = word;
			wordPtr++;
		}

		if (words.length === 1) {
			lines[linePtr] = word;
			break;
		}

		// if we have no more words, quit
		if (words[wordPtr] == null) {
			break;
		}

		// see if what's currently on this line plus the next word fits within maxWidth bounds
		const { width } = measureCanvasContext.measureText(lines[linePtr] + ' ' + words[wordPtr]);

		if (width <= maxWidth * pixelRatio) {
			// if it does, append the word to the current line
			lines[linePtr] += ' ' + words[wordPtr];
			wordPtr++;
		} else {
			// otherwise, move on to the next line
			linePtr++;
		}
	}

	// find the widest line
	lines.forEach(l => {
		const { width } = measureCanvasContext.measureText(l);

		maxTextWidth = Math.max(width, maxTextWidth);
	});

	const lineHeightPx = size * pixelRatio * lineHeight;

	const maxHeight = lines.length * lineHeightPx;

	const result = {
		maxWidth: maxTextWidth,
		maxHeight,
		lines: lines.length,
		draw: (context, x, y) => {
			// TODO: cleanup at some point and implement more than 2 lines of shave
			// The idea here is to vertically center single line or double line text,
			// With single line text, we render it at 0, 0
			// and two lines, each line gets rendered lineHeight away from the next, while maintaing vertical center to Y
			if (lines.length === 1) {
				if (options.strokeText) {
					context.strokeText(lines[0], x, y);
				}
				context.fillText(lines[0], x, y);
			} else {
				if (options.strokeText) {
					context.strokeText(lines[0], x, y - lineHeightPx / 2);
					context.strokeText(lines[1], x, y + lineHeightPx / 2);
				}
				context.fillText(lines[0], x, y - lineHeightPx / 2);
				context.fillText(lines[1], x, y + lineHeightPx / 2);
			}
		},
	};

	return result;
};
