import { type NodeFeature } from '@packages/internal/geojson-navigator/src/types/node';
import { quadtree, type Quadtree } from 'd3-quadtree';
import type { MapDataInternal } from '../map-data-objects';
import { type CoordinateFeature } from '@packages/internal/geojson-navigator/src/types/coordinate';
import { type SpaceFeature } from '@packages/internal/geojson-navigator/src/types/space';
import { type TQueriables } from './external';
import { equirectangularDistance } from '@packages/internal/common/utils';
import { traverseQuadTree } from './utils';

const MAX_SEARCH_RADIUS_METERS = 1000;
const LINE_OF_SIGHT_BUFFER_RADIUS = 0.1;
const INITIAL_SEARCH_RADIUS_METERS = 5;
const RADIUS_EXPANSION_FACTOR = 1.5;

type SearchTrees<T extends NodeFeature | SpaceFeature | CoordinateFeature> = {
	[mapId: string]: {
		// TODO: think about how the outdoor map works
		// for example if a coordinate is on the base map
		// it should go lookup nodes on every map with the same elevation
		[floorId: number]: Quadtree<T>;
	};
};

export class QueryCache {
	#queryTrees: {
		[type in TQueriables['__type']]?: SearchTrees<NodeFeature>;
	} = {};

	#maxSquareCache = new Map<string, Map<string, number>>();

	/**
	 * Get the maximum search radius for a given type and floorId, this is typically the extent of the tree for the given type and floorId
	 * @param type - The type of feature to search for
	 * @param floorId - The floor to search on
	 * @returns The maximum search radius in meters
	 */
	getMaxSearchRadiusForFloorId = (type: TQueriables['__type'], floorId: string) => {
		let maxSquareSize = this.#maxSquareCache.get(this.#mapDataInternal.mapName)?.get(floorId);
		if (maxSquareSize) {
			return maxSquareSize;
		}

		const tree = this.getNodeTreeByTypeAndFloorId(type, this.#mapDataInternal.mapName, floorId);
		if (typeof tree.extent === 'function') {
			if (tree.extent()) {
				const [[x0, y0], [x1, y1]] = tree.extent()!;
				const width = x1 - x0;
				const height = y1 - y0;
				maxSquareSize = Math.max(width, height);
			} else {
				maxSquareSize = MAX_SEARCH_RADIUS_METERS;
			}
		} else {
			maxSquareSize = MAX_SEARCH_RADIUS_METERS;
		}
		this.#maxSquareCache.set(this.#mapDataInternal.mapName, new Map([[floorId, maxSquareSize]]));
		return maxSquareSize;
	};

	/**
	 * Search for features within a radius of a coordinate using incremental radius expansion.
	 * Uses geometric expansion to search in concentric circles, processing candidates by distance.
	 * @param point - The coordinate to search around
	 * @param type - The type of feature to search for
	 * @param mapName - The name of the map to search on
	 * @param floorId - The floor to search on
	 * @param includedIds - The ids of the features to include in the search
	 * @param maxDistance - The maximum distance to search for features within
	 * @param lineOfSight - Whether to use line of sight checks to filter results
	 * @param limit - The limit of the number of features to return, so an early return is possible
	 * @returns An array of features sorted by distance from the coordinate
	 */
	searchWithIncludedIds(
		point: [number, number],
		type: TQueriables['__type'],
		mapName: string,
		floorId: string,
		includedIds: string[],
		maxDistance: number,
		lineOfSight = false,
		limit = Infinity,
	): {
		node: NodeFeature;
		distance: number;
	}[] {
		const tree = this.getNodeTreeByTypeAndFloorId(type, mapName, floorId);
		const processedNodeIds = new Set<string>();
		const results: { node: NodeFeature; distance: number }[] = [];

		let currentRadius = INITIAL_SEARCH_RADIUS_METERS;
		const effectiveMaxDistance = Math.max(maxDistance, this.getMaxSearchRadiusForFloorId(type, floorId));

		// Expand radius geometrically until we have enough results or reach max distance
		while (results.length < limit && currentRadius <= effectiveMaxDistance) {
			// Find new candidates in the current radius that haven't been processed
			const candidates = this.#findCandidatesInRadius(
				point,
				tree,
				includedIds,
				currentRadius,
				maxDistance,
				processedNodeIds,
			);

			// Sort candidates by distance (closest first)
			candidates.sort((a, b) => a.distance - b.distance);

			// Process candidates in distance order, checking line-of-sight
			for (const candidate of candidates) {
				if (results.length >= limit) break;

				if (lineOfSight) {
					if (
						this.#mapDataInternal.directions.navigator.hasLineOfSight(
							point,
							candidate.node.geometry.coordinates as [number, number],
							floorId,
							LINE_OF_SIGHT_BUFFER_RADIUS,
						)
					) {
						results.push(candidate);
					}
				} else {
					results.push(candidate);
				}
			}

			// Expand radius geometrically for next iteration
			currentRadius = currentRadius * RADIUS_EXPANSION_FACTOR;
		}

		// Results are already sorted by distance due to processing order
		return results;
	}

	/**
	 * Find candidate nodes within the specified radius that haven't been processed yet
	 * @private
	 */
	#findCandidatesInRadius(
		point: [number, number],
		tree: Quadtree<NodeFeature>,
		includedIds: string[],
		radius: number,
		maxDistance: number,
		processedNodeIds: Set<string>,
	): { node: NodeFeature; distance: number }[] {
		const { x, y } = this.#projectOntoCartesianPlane(point);
		const xmin = x - radius;
		const xmax = x + radius;
		const ymin = y - radius;
		const ymax = y + radius;

		const candidates: { node: NodeFeature; distance: number }[] = [];

		traverseQuadTree(tree, (node, x1, y1, x2, y2) => {
			const quadIsOutsideRadius = x1 >= xmax || y1 >= ymax || x2 < xmin || y2 < ymin;

			if ('data' in node) {
				const d = node.data;
				const id = d.properties.parentId || d.properties.id;
				// Check if this node should be included and hasn't been processed
				if (includedIds.includes(id) && !processedNodeIds.has(id)) {
					processedNodeIds.add(id);
					const distance = equirectangularDistance(point, d.geometry.coordinates as [number, number]);
					// Check if the distance condition is met
					if (distance <= maxDistance) {
						candidates.push({
							node: d,
							distance,
						});
					}
				}
			}

			// Skip quadrant if outside radius bounds
			return quadIsOutsideRadius;
		});

		return candidates;
	}

	#mapDataInternal: MapDataInternal;
	getNodeTreeByTypeAndFloorId(type: TQueriables['__type'], mapName: string, floorId: string): Quadtree<NodeFeature> {
		if (!this.#queryTrees[type]) {
			this.#queryTrees[type] = {};
		}

		if (this.#queryTrees[type][mapName]?.[floorId]) {
			return this.#queryTrees[type][mapName][floorId];
		}

		if (!this.#queryTrees[type][mapName]) {
			this.#queryTrees[type][mapName] = {};
		}

		// Overwrite the tree with a new one
		this.#queryTrees[type][mapName][floorId] = quadtree();
		let tree: Quadtree<NodeFeature> = this.#queryTrees[type][mapName][floorId];
		tree.x(node => this.#projectOntoCartesianPlane(node.geometry.coordinates as [number, number]).x);
		tree.y(node => this.#projectOntoCartesianPlane(node.geometry.coordinates as [number, number]).y);
		let targetsOnFloor: any[] = [];

		switch (type) {
			case 'space':
				const destinationNodeIds = this.#mapDataInternal.mvfSpacesByFloorId[floorId].flatMap(
					space => space.properties.destinationNodes,
				);
				targetsOnFloor = destinationNodeIds.map(id => {
					const node = this.#mapDataInternal.mvfNodesById[id];
					if (node) {
						return {
							...node,
							properties: {
								id: node.properties.id,
								parentId: node.properties.space[0],
							},
						};
					}
					return null;
				});
				break;
			case 'node':
				targetsOnFloor =
					this.#mapDataInternal.mvfNodesByFloorId[floorId]?.filter(node => node.properties.neighbors.length > 0) || [];
				break;
			case 'point-of-interest':
				targetsOnFloor = this.#mapDataInternal.mvfPoisByFloorId[floorId] || [];
				break;
			case 'door':
				targetsOnFloor = this.#mapDataInternal.mvfEntrancesByFloorId[floorId] || [];
				break;
			case 'annotation':
				targetsOnFloor = this.#mapDataInternal.mvfAnnotationsByFloorId[floorId] || [];
				break;
		}

		if (targetsOnFloor?.length > 0) {
			tree.addAll(targetsOnFloor);
		}

		return tree;
	}

	#projectOntoCartesianPlane: (point: [number, number]) => { x: number; y: number };

	constructor(
		mapDataInternal: MapDataInternal,
		projectOntoCartesianPlane: (point: [number, number]) => { x: number; y: number },
	) {
		this.#mapDataInternal = mapDataInternal;
		this.#projectOntoCartesianPlane = projectOntoCartesianPlane;
	}
}
