import type { GeometryId } from '@mappedin/mvf-core/identifiers';
import { typedKeys } from '@mappedin/mvf-core/utils';
import type { DefaultStyleCollection } from '@mappedin/mvf-default-style';
import type { Area, AreaId } from '../../extensions/area.js';
import type { WindowFeature, WindowId } from '../../extensions/window.js';
import type { ParsedMVF } from '../../types/bundle.ts';
import type { NodeId, ObstructionFeature, ObstructionId, SpaceFeature, SpaceId } from '../../types/core.js';
import type { Feature, Geometry, Polygon } from '../../types/geojson.js';
import type { BuilderOptions } from '../builderOptions.js';
import { type PartiallyTransformedGeometryFeature, convertNodeToSpaceId, toObstructionId } from '../converterUtils.js';
import { labelCenter } from '../geoCenter.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.ts';

export type V3Geometry = Pick<MVFv2_STANDARD_MVFv3, 'geometry' | 'defaultStyle'>;
export type V2Geometry = Pick<ParsedMVF, 'space' | 'obstruction' | 'area' | 'window'>;

// Helper function to check if a feature has a Polygon geometry
function isPolygonFeature(feature: Feature<Geometry, unknown>): feature is Feature<Polygon, unknown> {
	return feature?.geometry?.type === 'Polygon';
}

export function buildGeometry(
	v3: V3Geometry,
	{
		geometryMaps,
		nodesByGeometryId,
		nodesById,
	}: Pick<BuilderOptions, 'geometryMaps' | 'nodesByGeometryId' | 'nodesById'>,
): V2Geometry {
	const footprintGeometryIds = calculateFootprintGeometry(v3.defaultStyle);
	const spaces: V2Geometry['space'] = {};
	const obstructions: V2Geometry['obstruction'] = {};
	const areas: V2Geometry['area'] = {};
	const windows: V2Geometry['window'] = {};
	for (const floorId of typedKeys(v3.geometry)) {
		const floorFeatureCollection = v3.geometry[floorId];
		if (!floorFeatureCollection) {
			continue;
		}
		const spaceFeatures: (SpaceFeature | null)[] = [];
		const obstructionFeatures: (ObstructionFeature | null)[] = [];
		const areaFeatures: (Area | null)[] = [];
		const windowFeatures: (WindowFeature | null)[] = [];
		for (const feature of floorFeatureCollection.features) {
			const id = feature.properties.id;
			const isFootprint = footprintGeometryIds.has(id);

			const nodes = nodesByGeometryId.get(id) ?? [];

			for (const space of geometryMaps.space.get(id)?.features ?? []) {
				spaceFeatures.push(buildSpace(space, nodes));
			}

			for (const obstruction of geometryMaps.obstruction.get(id)?.features ?? []) {
				obstructionFeatures.push(buildObstruction(obstruction, isFootprint));
				const object = geometryMaps.object.get(id);
				if (object) {
					/**
					 * Objects in MVFv2 don't have destination nodes like spaces do. So in order to navigate to objects in MVFv2,
					 * we need to create entrance spaces for each of its nodes, where the externalId is the ID of the object.
					 */
					const entranceNodeIds = nodesByGeometryId.get(id) ?? [];
					const entranceNodes = entranceNodeIds.map((nodeId) => nodesById.get(nodeId)).filter((x) => x != null);
					for (const node of entranceNodes) {
						const entranceSpace = buildSpace(
							{
								type: 'Feature',
								geometry: node.geometry,
								properties: {
									id: convertNodeToSpaceId(node.properties.id),
									kind: 'desk-entrance', // This identifies these special spaces
									details: {
										externalId: toObstructionId(id),
									},
								},
							},
							entranceNodeIds,
						);
						spaceFeatures.push(entranceSpace);
					}
				}
			}

			for (const area of geometryMaps.area.get(id)?.features ?? []) {
				areaFeatures.push(buildArea(area, nodes));
			}

			for (const window of geometryMaps.window.get(id)?.features ?? []) {
				windowFeatures.push(buildWindow(window));
			}
		}

		if (spaceFeatures.length > 0) {
			spaces[floorId] = {
				type: 'FeatureCollection',
				features: spaceFeatures.filter((x) => x !== null),
			};
		}

		if (obstructionFeatures.length > 0) {
			obstructions[floorId] = {
				type: 'FeatureCollection',
				features: obstructionFeatures.filter((x) => x !== null),
			};
		}

		if (areaFeatures.length > 0) {
			areas[floorId] = {
				type: 'FeatureCollection',
				features: areaFeatures.filter((x) => x !== null),
			};
		}

		if (windowFeatures.length > 0) {
			windows[floorId] = {
				type: 'FeatureCollection',
				features: windowFeatures.filter((x) => x !== null),
			};
		}
	}
	const result: V2Geometry = {
		space: spaces,
		obstruction: obstructions,
	};
	if (Object.keys(areas).length > 0) {
		result.area = areas;
	}
	if (Object.keys(windows).length > 0) {
		result.window = windows;
	}

	return result;
}

export function buildSpace(
	feature: PartiallyTransformedGeometryFeature<SpaceId>,
	nodes: NodeId[],
): SpaceFeature | null {
	switch (feature.geometry.type) {
		case 'MultiPoint':
		case 'MultiLineString':
		case 'MultiPolygon':
			// Multis will have already been split
			return null;
	}
	const space: SpaceFeature = {
		type: 'Feature',
		geometry: feature.geometry,
		properties: {
			destinationNodes: nodes,
			id: feature.properties.id,
			externalId: feature.properties.details?.externalId ?? '',
			kind: feature.properties.kind,
		},
	};
	if (feature.properties.details) {
		space.properties.details = {
			name: feature.properties.details.name,
			description: feature.properties.details.description,
			images: feature.properties.details.icon
				? [
						{
							url: feature.properties.details.icon,
							altText: feature.properties.details.name ? `${feature.properties.details.name} icon` : 'icon',
						},
					]
				: undefined,
		};
	}

	if (isPolygonFeature(space)) {
		space.properties.center = labelCenter(space).unwrapOr(undefined);
	}

	return space;
}

export function buildObstruction(
	feature: PartiallyTransformedGeometryFeature<ObstructionId>,
	footprintComponent: boolean,
): ObstructionFeature | null {
	switch (feature.geometry.type) {
		case 'MultiPoint':
		case 'MultiLineString':
		case 'MultiPolygon':
		case 'Point':
			// Multis will have already been split, point obstructions are meaningless
			return null;
	}

	const properties = feature.properties;
	const obstruction: ObstructionFeature = {
		type: 'Feature' as const,
		geometry: feature.geometry,
		properties: {
			id: properties.id,
			externalId: properties.details?.externalId ?? '',
			entrances: [],
			kind: properties.kind,
			footprintComponent,
		},
	};

	if (isPolygonFeature(obstruction)) {
		obstruction.properties.center = labelCenter(obstruction).unwrapOr(undefined);
	}

	if (properties.details) {
		obstruction.properties.details = {
			name: properties.details.name,
			description: properties.details.description,
			images: properties.details.icon
				? [
						{
							url: properties.details.icon,
							altText: properties.details.name ? `${properties.details.name} icon` : 'icon',
						},
					]
				: undefined,
		};
	}

	return obstruction;
}

export function buildArea(feature: PartiallyTransformedGeometryFeature<AreaId>, nodes: NodeId[]): Area | null {
	if (feature.geometry.type !== 'Polygon') {
		//  Multi polygons are already split. Otherwise, it is nonsense
		return null;
	}
	const properties = feature.properties;
	const area: Area = {
		type: 'Feature',
		geometry: feature.geometry,
		properties: {
			id: properties.id,
			externalId: properties.details?.externalId ?? '',
			destinationNodes: nodes,
		},
	};

	if (isPolygonFeature(area)) {
		area.properties.center = labelCenter(area).unwrapOr(undefined);
	}

	if (properties.details) {
		area.properties.details = {
			name: properties.details.name,
			description: properties.details.description,
			images: properties.details.icon
				? [
						{
							url: properties.details.icon,
							altText: properties.details.name ? `${properties.details.name} icon` : 'icon',
						},
					]
				: undefined,
		};
	}

	return area;
}

export function buildWindow(feature: PartiallyTransformedGeometryFeature<WindowId>): WindowFeature | null {
	if (feature.geometry.type !== 'LineString') {
		return null;
	}
	const window: WindowFeature = {
		type: 'Feature',
		geometry: feature.geometry,
		properties: {
			id: feature.properties.id,
		},
	};
	return window;
}

export function calculateFootprintGeometry(styles?: DefaultStyleCollection): Set<GeometryId> {
	const geometries = new Set<GeometryId>((styles?.ExteriorWalls?.geometryAnchors ?? []).map((a) => a.geometryId));
	return geometries;
}
