import type {
	Coordinate,
	TAddPathOptions,
	TClickPayload,
	TDirectionInstruction,
	TDirectionInstructionAction,
} from '../';
import Logger from '../../../packages/common/Mappedin.Logger';
import { ConnectionMarker, DEPARTURE_COLOR, DESTINATION_COLOR, DroppedPin } from './markers';
import { getHighlightedPathSegments, ms } from './utils';
import type { Directions, GeoJsonApi } from '../api-geojson';
import type { Floor, FloorStack } from '../map-data-objects';
import { Marker, Path } from '../map-view-objects';
import { randomId } from '../utils';
import type { TPathSectionHighlightOptions } from '../types';
import { Tween } from '@tweenjs/tween.js';

export type TCreateMarker = boolean | ((instruction: TDirectionInstruction) => Marker);

export const INACTIVE_PATH_VERTICAL_OFFSET = 0.01;
export const ACTIVE_PATH_VERTICAL_OFFSET = 0.02;

/**
 * Options for navigation.
 */
export type TNavigationOptions = {
	/**
	 * Controls whether the map should be set to the floor of the connection point when a connection point is clicked.
	 * @default true
	 */
	setMapOnConnectionClick?: boolean;
	/**
	 * Controls whether the map should be set to the floor of the departure point when the path is drawn.
	 * @default true
	 */
	setMapToDeparture?: boolean;
	/**
	 * Controls whether markers are created for the departure, destination, and connection points.
	 */
	createMarkers?: {
		/**
		 * Controls whether a marker is created for the departure point and allows a custom marker to be created.
		 * @default true
		 */
		departure?: TCreateMarker;
		/**
		 * Controls whether a marker is created for the destination point and allows a custom marker to be created.
		 * @default true
		 */
		destination?: TCreateMarker;
		/**
		 * Controls whether markers are created for connection points and allows a custom marker to be created.
		 * @default true
		 */
		connection?: TCreateMarker;
	};

	/**
	 * Path Options for the non-current path in multi-destination mode.
	 */
	inactivePathOptions?: Partial<TAddPathOptions>;

	/**
	 * Controls whether the path drawing is animated across floors.
	 * @default true
	 */
	animatePathDrawing?: boolean;

	/**
	 * Options for the path.
	 */
	pathOptions?: Partial<TAddPathOptions>;
	/**
	 * Options for the markers at the departure and destination.
	 */
	markerOptions?: {
		/**
		 * The color of the departure marker.
		 * @default '#1890FF'
		 */
		departureColor?: string;
		/**
		 * The color of the destination marker.
		 * @default '#722ED1'
		 */
		destinationColor?: string;
	};
};

const defaultPathOptions: TNavigationOptions['pathOptions'] = {
	drawDuration: 1000,
	color: '#40A9FF',
	interactive: false,
	accentColor: 'blue',
	width: {
		on: 'zoom-level',
		input: [17, 22],
		output: [2, 0.5],
	},
	displayArrowsOnPath: false,
	animateArrowsOnPath: false,
};

const defaultInactivePathOptions: TNavigationOptions['inactivePathOptions'] = {
	drawDuration: 1000,
	color: '#B3DDFF',
	interactive: false,
	accentColor: '#40A9FF',
	width: {
		on: 'zoom-level',
		input: [17, 22],
		output: [2, 0.5],
	},
	displayArrowsOnPath: false,
	animateArrowsOnPath: false,
};

const defaultOptions: Required<TNavigationOptions> = {
	setMapOnConnectionClick: true,
	setMapToDeparture: true,
	createMarkers: {
		departure: true,
		destination: true,
		connection: true,
	},
	pathOptions: defaultPathOptions,
	inactivePathOptions: defaultInactivePathOptions,
	markerOptions: {
		departureColor: DEPARTURE_COLOR,
		destinationColor: DESTINATION_COLOR,
	},
	animatePathDrawing: true,
};

export type NavigationState = {
	isMultiFloor: boolean;
	floorStacks: FloorStack[];
	floors: Floor[];
	activeDirections?: Directions;
	activePath?: Path;
	paths: Path[];
};

/**
 * Navigation-specific event types.
 */
export type TNavigationEvents = {
	/**
	 * Emitted when the navigation state changes.
	 */
	'navigation-state-change': NavigationState;
	/**
	 * Emitted when a connection point is clicked.
	 */
	'navigation-connection-click': { fromFloor?: Floor; toFloor: Floor; instruction: TDirectionInstruction };
	/**
	 * Emitted when the active path changes.
	 */
	'navigation-active-path-change': { directions: Directions; path: Path };
};

/**
 * When a user needs to get from point A to point B, drawing a path on the map helps them to navigate to their destination. It can help them to visualize the route they'll need to take, like a good treasure map.

 * Navigation is a helper class to display wayfinding easily on the map. Functionality of Navigation could be replicated by drawing the paths using {@link Paths} and adding well designed tooltips at connection points.
 *
 * This class is accessed using {@link MapView.Navigation}.
 *
 * {@link Navigation.draw} allows for easily drawing multiple components that make up a wayfinding illustration. It shows a human figure to mark the start point, a path with animated directional arrows, pulses in the direction of travel and a pin to mark the destination. Each of these components can be customized to match an app's style.
 *
 * Refer to the [Drawing Navigation](https://developer.mappedin.com/web-sdk/wayfinding#drawing-navigation) in the Wayfinding Guide for more information and interactive examples.
 */
export class Navigation {
	#markers = new Map<string | number, { instruction: TDirectionInstruction; marker: Marker } | undefined>();
	#paths = new Map<string, Path>();
	#geoJSONApi: GeoJsonApi;
	#options: Required<TNavigationOptions> = defaultOptions;
	#existingDraw?: {
		promise: Promise<void>;
		cancel: () => void;
	};
	#existingDrawSync?: boolean;
	#directions: Directions[] = [];
	#activePath?: Path;
	// Map of directions to paths, INCLUDING internal paths
	#pathsMap: Map<Directions, Path> = new Map();
	// Map of paths to directions, INCLUDING internal paths
	#directionsMap: Map<Path, Directions> = new Map();

	/**
	 * Returns true if the navigation is for a multi-floor path.
	 */
	get isMultiFloor() {
		if (!this.#directions.length) return false;

		const floorIds = new Set<string>();
		for (const direction of this.#directions) {
			for (const coordinate of direction.coordinates) {
				if (coordinate.floorId) {
					floorIds.add(coordinate.floorId);
					if (floorIds.size > 1) {
						return true;
					}
				}
			}
		}

		return false;
	}

	/**
	 * @internal
	 */
	constructor(geoJSONApi: GeoJsonApi) {
		this.#geoJSONApi = geoJSONApi;
	}

	#isVortexAction(action?: TDirectionInstructionAction) {
		return action != null && ['TakeConnection', 'ExitConnection', 'TakeVortex', 'ExitVortex'].includes(action.type);
	}

	/**
	 * @internal
	 */
	getPathById(id: string) {
		return this.#geoJSONApi.Paths.getById(id);
	}

	/**
	 * @internal
	 */
	getMarkerById(id: string) {
		return this.#markers.get(id);
	}

	/**
	 * @internal
	 */
	get paths() {
		// There is a duplicate Path in the paths map for each segment of the path.
		const paths = new Set<Path>();
		this.#paths.forEach(path => paths.add(path));

		return Array.from(paths);
	}

	/**
	 * The currently active directions.
	 */
	get activeDirections() {
		return this.#directionsMap.get(this.#activePath!);
	}

	/**
	 * The currently active path.
	 */
	get activePath() {
		if (!this.#activePath) return undefined;

		return this.#paths.get(this.#activePath.id);
	}

	/**
	 * The current list of floor stacks along the navigation paths.
	 */
	get floorStacks(): FloorStack[] {
		const mapData = this.#geoJSONApi.getMapData();

		if (!this.#directions.length || !mapData) return [];

		const floorStacks = new Set<FloorStack>();
		for (const direction of this.#directions) {
			for (const coordinate of direction.coordinates) {
				if (coordinate.floorId) {
					const floor = mapData.getById('floor', coordinate.floorId);

					if (floor) {
						floorStacks.add(floor.floorStack);
					}
				}
			}
		}

		return Array.from(floorStacks);
	}

	/**
	 * The current list of floors along the navigation paths.
	 */
	get floors(): Floor[] {
		const mapData = this.#geoJSONApi.getMapData();

		if (!this.#directions.length || !mapData) return [];

		const floors = new Set<Floor>();
		for (const direction of this.#directions) {
			for (const coordinate of direction.coordinates) {
				if (coordinate.floorId) {
					const floor = mapData.getById('floor', coordinate.floorId);

					if (floor) {
						floors.add(floor);
					}
				}
			}
		}

		return Array.from(floors);
	}

	/**
	 * Sets the active path by index.
	 */
	setActivePathByIndex(target: number) {
		const directions = this.#directions[target];

		if (!this.#directions.length) {
			Logger.warn('Cannot set an active path before drawing. Call Navigation.draw() first.');

			return;
		}

		if (!this.#directions[target]) {
			Logger.warn(`Direction #${target} does not exist`);

			return;
		}

		const path = this.#pathsMap.get(directions);

		if (!path) {
			Logger.warn('The direction is not a part of muli-direcional path');

			return;
		}

		this.#setActiveDirections(path);
	}

	/**
	 * Sets the active path.
	 */
	setActivePath(target: Path) {
		if (!this.#directions.length) {
			Logger.warn('Cannot set an active path before drawing. Call Navigation.draw() first.');

			return;
		}

		const path = this.#directionsMap.get(target);
		if (!path) {
			Logger.warn(`Path "${target.id}" is not a part of muli-direcional path`);

			return;
		}

		this.#setActiveDirections(target);
	}

	/**
	 * This function ensures that the active path is always above the other paths, so slightly higher than the current adjusted altitude.
	 */
	#reorderPaths() {
		if (!this.#activePath) {
			return;
		}

		const activePath = this.#paths.get(this.#activePath.id);
		if (!activePath) {
			return;
		}

		for (const path of this.#paths.values()) {
			if (!path.id.includes('internal-')) {
				continue;
			}
			if (path.id === this.#activePath.id) {
				for (const segment of activePath.segments) {
					this.#geoJSONApi.updateState(segment, {
						verticalOffset: ACTIVE_PATH_VERTICAL_OFFSET,
					});
				}
			} else {
				this.#geoJSONApi.updateState(path, {
					verticalOffset: INACTIVE_PATH_VERTICAL_OFFSET,
				});
			}
		}
	}

	/**
	 * Sets the active path by directions.
	 */
	setActivePathByDirections(target: Directions) {
		if (!this.#directions.length) {
			Logger.warn(
				'[setActivePathByDirections] Cannot set an active path before drawing. Call Navigation.draw() first.',
			);

			return;
		}

		let path = this.#pathsMap.get(target);
		if (!path) {
			const foundDirections = this.#directions.find(d => d.id === target.id);
			if (!foundDirections) {
				Logger.warn('[setActivePathByDirections] Cannot find directions for id');

				return;
			}
			path = this.#pathsMap.get(foundDirections);
		}

		if (!path) {
			Logger.warn('[setActivePathByDirections] Cannot find path for directions');

			return;
		}

		this.#setActiveDirections(path);
	}

	clearHighlightedPathSection() {
		for (const path of this.paths) {
			for (const segment of path.segments) {
				this.#geoJSONApi.updateState(segment, {
					highlightCompleteFraction: 0.0,
				});
			}
		}
	}

	/**
	 * Highlights path section between two coordinates. This can be used to highlight any section of the path or indicated the currently traveled path.
	 */
	highlightPathSection(from: Coordinate, to: Coordinate, options?: TPathSectionHighlightOptions) {
		const animationDuration = options?.animationDuration ?? 1500;

		// this point will be used to project all coordinates to the same cartesian space for searching
		const projectionOrigin = this.#geoJSONApi.getMapData()!.mapCenter;

		const highlightedPathSegments = getHighlightedPathSegments(this.paths, from, to, projectionOrigin);
		if (!highlightedPathSegments) {
			return;
		}

		this.clearHighlightedPathSection();

		const tweens: Tween[] = highlightedPathSegments.map(h => {
			this.#geoJSONApi.updateState(h.segment, {
				highlightStartFraction: h.startHighlightFraction,
				highlightEndFraction: h.endHighlightFraction,
				highlightColor: options?.color ?? 'red',
				highlightWidthMultiplier: options?.widthMultiplier ?? 1,
			});
			return new Tween({ fraction: 0.0 }).to({ fraction: 1.0 }, animationDuration).onUpdate(value => {
				this.#geoJSONApi.updateState(h.segment, {
					highlightCompleteFraction: value.fraction,
				});
			});
		});

		let resolveFn: () => void = () => undefined;

		const completedPromise = new Promise(res => {
			resolveFn = res as () => void;
		});

		if (tweens.length > 0) {
			tweens.forEach((tween, index) => {
				if (index < tweens.length - 1) {
					tween.chain(tweens[index + 1]);
				}
				this.#geoJSONApi.tweenGroup().add(tweens[index]);
			});
			tweens[0].start();
			tweens[tweens.length - 1].onComplete(() => {
				this.#geoJSONApi.tweenGroup().remove(tweens[tweens.length - 1]);
				resolveFn();
			});
		}
		return {
			animation: completedPromise,
		};
	}

	/**
	 * @internal
	 */
	drawSync(directions: Directions | Directions[], options: TNavigationOptions = {}) {
		if (this.#existingDrawSync) {
			this.clear();
		}

		this.#existingDrawSync = true;

		this.#setupOptions(options);

		const directionsArray = Array.isArray(directions) ? directions : [directions];
		const firstDirection = directionsArray[0];
		const lastDirection = directionsArray[directionsArray.length - 1];
		const firstNode = firstDirection.coordinates[0];
		this.#directions = directionsArray;

		if (firstNode.floorId && this.#options.setMapToDeparture) {
			this.#geoJSONApi.setFloor(firstNode.floorId);
		}

		if (this.#options.createMarkers?.departure) {
			this.#addMarker('departure', firstDirection.instructions[0]);
		}

		for (let i = 0; i < directionsArray.length; i++) {
			const direction = directionsArray[i];
			const pathOptions = i ? { ...this.#options.inactivePathOptions } : { ...this.#options.pathOptions };
			if (direction.coordinates.length < 2) {
				Logger.warn('Directions must have at least 2 coordinates');

				return;
			}

			const path = this.#createPath(direction.coordinates, {
				...pathOptions,
				animateDrawing: this.#options.animatePathDrawing,
				verticalOffset: this.#computeVerticalOffset(i === 0 ? 'active' : 'inactive'),
			});

			this.#pathsMap.set(direction, path);
			this.#directionsMap.set(path, direction);

			if (this.#options.createMarkers?.connection) {
				this.#addMarkers(direction.instructions, 'connection');
			}

			if (i === 0) {
				this.#activePath = this.#pathsMap.get(this.#directions[0])!;
				this.#geoJSONApi.externalPubSub.on('click', this.#handleClick);
			}
		}

		if (this.#options.createMarkers?.destination) {
			this.#addMarker('destination', lastDirection.instructions[lastDirection.instructions.length - 1]);
		}

		this.#geoJSONApi.mapObjects.forEach(mo => {
			mo.processFloorChange(mo.currentFloorId);
		});

		this.#emitNavigationStateChanged();
	}

	#createPath(coordinates: Coordinate[], options: TAddPathOptions & { id?: string }): Path {
		const id = options.id?.toString() ?? randomId();
		const internalPath = this.#geoJSONApi.currentMap.Paths.add(coordinates, options);
		const path = new Path(id, internalPath.animation, coordinates, internalPath.paths);
		this.#paths.set(id, path);
		return path;
	}

	#createMarker(instruction: TDirectionInstruction, contentEl: string, interactive = false): Marker {
		const internalMarker = this.#geoJSONApi.currentMap.Markers.add(instruction.coordinate, contentEl, {
			interactive,
			placement: 'top',
			rank: 'always-visible',
		});
		const el = this.#geoJSONApi.currentMap.Markers.getContentEl(internalMarker.id.toString());
		if (!el) throw new Error('marker component should exist');
		const marker = new Marker(internalMarker.id.toString(), el, instruction.coordinate);
		return marker;
	}

	#computeVerticalOffset(type: 'active' | 'inactive') {
		return type === 'active'
			? ACTIVE_PATH_VERTICAL_OFFSET + (this.#options.pathOptions?.verticalOffset ?? 0)
			: INACTIVE_PATH_VERTICAL_OFFSET + (this.#options.inactivePathOptions?.verticalOffset ?? 0);
	}

	#drawAsync(directionsArray: Directions[]) {
		const firstDirection = directionsArray[0];
		const lastDirection = directionsArray[directionsArray.length - 1];

		let cancelled = false;
		this.#existingDraw = {
			promise: new Promise<void>(async resolve => {
				if (process.env.NODE_ENV !== 'test') {
					await ms(500);
				}

				if (cancelled || !directionsArray.length) {
					resolve();

					return;
				}

				if (this.#options.createMarkers?.departure) {
					this.#addMarker('departure', firstDirection.instructions[0]);
				}

				for (let i = 0; i < directionsArray.length; i++) {
					const direction = directionsArray[i];
					const pathOptions = i === 0 ? { ...this.#options.pathOptions } : { ...this.#options.inactivePathOptions };
					if (direction.coordinates.length < 2) {
						Logger.warn('Directions must have at least 2 coordinates');
						resolve();

						return;
					}

					const path = this.#createPath(direction.coordinates, {
						...pathOptions,
						animateDrawing: this.#options.animatePathDrawing,
						verticalOffset: this.#computeVerticalOffset(i === 0 ? 'active' : 'inactive'),
					});

					this.#pathsMap.set(direction, path);
					this.#directionsMap.set(path, direction);
					if (i === 0) {
						this.#activePath = this.#pathsMap.get(this.#directions[0])!;
						this.#geoJSONApi.externalPubSub.on('click', this.#handleClick);
					}

					if (process.env.NODE_ENV !== 'test') {
						await path.animation;
					}

					if (cancelled) {
						resolve();

						return;
					}

					if (this.#options.createMarkers?.connection) {
						this.#addMarkers(direction.instructions, 'connection');
					}
				}

				if (this.#options.createMarkers?.destination) {
					this.#addMarker('destination', lastDirection.instructions[lastDirection.instructions.length - 1]);
				}

				if (cancelled) {
					resolve();

					return;
				}

				this.#geoJSONApi.mapObjects.forEach(mo => {
					mo.processFloorChange(mo.currentFloorId);
				});

				this.#emitNavigationStateChanged();

				resolve();
			}),
			cancel: () => {
				cancelled = true;
			},
		};

		return this.#existingDraw.promise;
	}

	#setupOptions(options: TNavigationOptions) {
		this.#options = {
			setMapOnConnectionClick: options.setMapOnConnectionClick ?? defaultOptions.setMapOnConnectionClick,
			setMapToDeparture: options.setMapToDeparture ?? defaultOptions.setMapToDeparture,
			pathOptions: {
				...defaultOptions.pathOptions,
				...options.pathOptions,
			},
			inactivePathOptions: {
				...defaultOptions.inactivePathOptions,
				...options.inactivePathOptions,
			},
			createMarkers: {
				...defaultOptions.createMarkers,
				...options.createMarkers,
			},
			markerOptions: {
				...defaultOptions.markerOptions,
				...options.markerOptions,
			},
			animatePathDrawing: options.animatePathDrawing ?? defaultOptions.animatePathDrawing,
		};
	}

	#emitNavigationStateChanged() {
		this.#geoJSONApi.publish('navigation-state-change', {
			isMultiFloor: this.isMultiFloor,
			floorStacks: this.floorStacks,
			floors: this.floors,
			activeDirections: this.activeDirections,
			activePath: this.activePath,
			paths: this.paths,
		});
	}
	/**
	 * Draws the specified directions on the map.
	 * @param directions The directions to be drawn.
	 * @param options Optional additional options for the navigation.
	 */
	async draw(directions: Directions | Directions[], options: TNavigationOptions = {}) {
		if (this.#existingDraw) {
			this.clear();
		}

		this.#setupOptions(options);

		const directionsArray = Array.isArray(directions) ? directions : [directions];
		const firstDirection = directionsArray[0];
		const firstNode = firstDirection.coordinates[0];
		this.#directions = directionsArray;

		if (firstNode.floorId && this.#options.setMapToDeparture) {
			this.#geoJSONApi.setFloor(firstNode.floorId);
		}

		return this.#drawAsync(directionsArray);
	}

	#setActiveDirections(target: Path) {
		this.#activePath = target;

		this.#directions.forEach(directions => {
			const internalPath = this.#pathsMap.get(directions);
			if (internalPath) {
				const options =
					internalPath.id === this.#activePath!.id
						? { ...this.#options.pathOptions }
						: { ...this.#options.inactivePathOptions };
				this.#geoJSONApi.updateState(internalPath, options);
			}
		});

		this.#geoJSONApi.publish('navigation-active-path-change', {
			directions: this.activeDirections!,
			path: this.activePath!,
		});

		this.#reorderPaths();
		this.#emitNavigationStateChanged();
	}

	#addMarker(type: 'departure' | 'destination' | 'connection', instruction: TDirectionInstruction) {
		if (type === 'connection' && instruction) {
			const color =
				instruction.action.type === 'ExitConnection'
					? this.#options.markerOptions.departureColor
					: this.#options.markerOptions.destinationColor;

			let marker: Marker;
			if (typeof this.#options.createMarkers?.connection === 'function') {
				marker = this.#options.createMarkers.connection(instruction);
				this.#geoJSONApi.updateState(marker, {
					interactive: true,
				});
			} else {
				marker = this.#createMarker(
					instruction,
					ConnectionMarker({
						/**
						 * Connections aren't associated correctly in the MVF right now, so sometimes they are undefined.
						 * If we have an explicit connection type, we can temporarily fallback to that even if it should
						 * be deprecated.
						 *
						 * TODO: Remove fallback to a deprecated property when connections are overhauled in the MVF.
						 */
						type: instruction.action.connection?.type ?? instruction.action.connectionType,
						color,
						direction: instruction.action.direction,
					}),
					true,
				);
			}
			this.#markers.set(marker.id, {
				instruction,
				marker,
			});
		} else {
			const color =
				type === 'departure'
					? this.#options.markerOptions.departureColor
					: this.#options.markerOptions.destinationColor;
			let marker: Marker;
			if (typeof this.#options.createMarkers?.departure === 'function' && type === 'departure') {
				marker = this.#options.createMarkers.departure(instruction);
			} else if (typeof this.#options.createMarkers?.destination === 'function' && type === 'destination') {
				marker = this.#options.createMarkers.destination(instruction);
			} else {
				marker = this.#createMarker(instruction, DroppedPin({ type, color }))!;
			}
			this.#markers.set(marker.id, {
				instruction,
				marker,
			});
		}
	}

	#addMarkers(instructions: TDirectionInstruction[], type: 'connection') {
		const connectionInstructions = instructions.filter(instruction => this.#isVortexAction(instruction.action));
		connectionInstructions.forEach(instruction => {
			this.#addMarker(type, instruction);
		});
	}

	#handleClick = (e: TClickPayload) => {
		if (this.#options.createMarkers?.connection && e.markers != null && e.markers.length > 0) {
			const { instruction } = this.#markers.get(e.markers[0].id)!;

			if (instruction?.action.toFloor) {
				if (this.#options.setMapOnConnectionClick) {
					this.#geoJSONApi.setFloor(instruction.action.toFloor.id, 'navigation-connection-click');
				}
				this.#geoJSONApi.publish('navigation-connection-click', {
					instruction,
					fromFloor: instruction.action.fromFloor,
					toFloor: instruction.action.toFloor,
				});
			}
		}
	};

	/**
	 * Clears any drawn navigation paths or directions from the map.
	 */
	clear() {
		this.#markers.forEach(m => {
			if (m?.marker) {
				this.#geoJSONApi.currentMap.Markers.remove(m.marker.id.toString());
			}
		});
		this.#markers.clear();
		this.#paths.forEach(path => {
			this.#geoJSONApi.currentMap.Paths.remove(path.segments.map(s => s.id.toString()));
		});
		this.#paths.clear();

		if (this.#existingDraw) {
			this.#existingDraw.cancel();
			this.#existingDraw = undefined;
		}
		if (this.#existingDrawSync) {
			this.#existingDrawSync = false;
		}
		this.#directions = [];
		this.#pathsMap.clear();
		this.#directionsMap.clear();
		this.#activePath = undefined;
		this.#geoJSONApi.externalPubSub.off('click', this.#handleClick);
		this.#geoJSONApi.mapObjects.forEach(mo => {
			mo.processFloorChange(mo.currentFloorId);
		});

		this.#emitNavigationStateChanged();
	}
}
