import { MAPPEDIN_LAYER_ID, type RendererCore } from '@mappedin/core-sdk';
import { Logger } from '@packages/internal/common';
import type { Map as MapLibreMap } from '../../../packages/outdoor-context-v5';
import type { BBox } from 'geojson';

const DEFAULT_STYLE = { opacity: 1 };

export type HiddenOutdoorGeometry = [
	BBox,
	polygons: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[],
	layers: string[],
];

/**
 * The outdoor map shown around the Mappedin indoor map can be manipulated to show or hide additional information. It can be used to add GeoJSON based geometry, images, deck.gl layers and more.
 *
 * The outdoor map is accessed using {@link MapView.Outdoor}, which returns a maplibregl.Map object.
 * [MapLibre GL JS](https://maplibre.org/maplibre-gl-js/docs/) is a TypeScript library that uses WebGL to render interactive maps from vector tiles in a browser. The Mappedin SDK makes use of it to display the outdoor map.
 *
 * By providing access to the MapLibre map used to draw the outdoor map, the Mappedin SDK enables developers to draw on and manipulate the outdoor map.
 * Developers can use most capabilities of MapLibre GL JS with key exceptions being Interactivity and Camera, which are not available.
 *
 * User touch and click events are handled by Mappedin JS and are not propagated to the outdoor map layer. Camera movement is also handled by Mappedin JS and cannot be manipulated using MapLibre controls.
 *
 * Refer to the [Outdoor Map Guide](https://developer.mappedin.com/web-sdk/outdoor-map) for more information and interactive examples.
 */
export default class Outdoor {
	#core: RendererCore;
	#hiddenOutdoorGeometry: HiddenOutdoorGeometry[] = [];
	#style: { opacity: number | 'initial' } = { opacity: 'initial' };
	#initialStyle = { opacity: new Map<string, [object | number, object | number | undefined]>() };
	#hiddenLayers = new Set<string>();

	/**
	 * @internal
	 */
	constructor(core: RendererCore, hiddenOutdoorGeometry?: HiddenOutdoorGeometry[]) {
		this.#core = core;
		this.#hiddenOutdoorGeometry = hiddenOutdoorGeometry || [];
		const map = this.#core.map;

		if (map) {
			map.on('idle', () => {
				if (this.#initialStyle.opacity.size) return;

				this.#setInitialStyle(map);
			});
		}
	}

	#setInitialStyle(map) {
		const layers = map.getStyle().layers;

		if (layers) {
			layers.forEach(layer => {
				const visibility = map.getLayoutProperty(layer.id, 'visibility');

				if (visibility !== 'none') {
					if (layer.id === 'background') {
						// do nothing
					} else if (layer.type === 'symbol') {
						this.#initialStyle.opacity.set(layer.id, [
							(map.getPaintProperty(layer.id, `icon-opacity`) as number) ?? DEFAULT_STYLE.opacity,
							(map.getPaintProperty(layer.id, `text-opacity`) as number) ?? DEFAULT_STYLE.opacity,
						]);
					} else {
						this.#initialStyle.opacity.set(layer.id, [
							(map.getPaintProperty(layer.id, `${layer.type}-opacity`) as number) ?? DEFAULT_STYLE.opacity,
							undefined,
						]);
					}
				}
			});
		}
	}
	#clearInitialStyle() {
		Object.values(this.#initialStyle).forEach(value => value.clear());
	}

	/**
	 * Set the style of the outdoor map. Use one of [Mappedin's predefined styles](https://developer.mappedin.com/web-sdk/outdoor-map#styles) or link to a custom style.
	 * @param style {any}
	 */
	setStyle(style: any) {
		if (this.#core.map) {
			this.#core.map?.setStyle(style);
			if (this.#hiddenLayers.size) {
				this.#hiddenLayers.clear();
				this.hide();
			}
			this.#clearInitialStyle();
			this.#setInitialStyle(this.#core.map);
			this.setOpacity(this.#style.opacity);
			// move mappedin layer to the top
			this.#core.map?.moveLayer(MAPPEDIN_LAYER_ID);
			for (let [bbox, polygons, layers] of this.#hiddenOutdoorGeometry || []) {
				this.#core.hideOutdoorLayersIntersectingPolygons(bbox, polygons, layers);
			}
		}
	}
	/**
	 * Returns a Maplibre map for advanced usage.
	 *
	 * @returns {object} Maplibre map instance
	 *
	 * Limitations:
	 * - Maplibre interaction events are not supported, use Mappedin JS interaction events.
	 * - Maplibre markers and labels may overlap as they are not integrated with the Mappedin JS collision engine.
	 */
	get map(): MapLibreMap | undefined {
		return this.#core.map;
	}
	/**
	 * Whether the outdoorView is enabled.
	 */
	get enabled() {
		return !!this.#core.map;
	}

	/**
	 * Whether the outdoorView is visible.
	 */
	get visible() {
		return this.#hiddenLayers.size === 0;
	}

	/**
	 * Show the outdoor map.
	 */
	show() {
		const map = this.#core.map;

		if (!map) return;

		const layers = map.getStyle().layers;

		if (!layers) return;

		layers.forEach(layer => {
			if (this.#hiddenLayers.has(layer.id)) {
				map.setLayoutProperty(layer.id, 'visibility', 'visible');
			}
		});

		this.#hiddenLayers.clear();
	}

	/**
	 * Hide the outdoor map.
	 * @param excludedStyleLayerIds {string[]}
	 */
	hide(excludedStyleLayerIds: string[] = []) {
		const map = this.#core.map;

		if (!map) return;

		const layers = map.getStyle().layers;

		if (!layers) return;

		layers.forEach(layer => {
			const visibility = map.getLayoutProperty(layer.id, 'visibility');

			if (!excludedStyleLayerIds.includes(layer.id) && visibility !== 'none') {
				map.setLayoutProperty(layer.id, 'visibility', 'none');
				this.#hiddenLayers.add(layer.id);
			}
		});
	}

	/**
	 * Set outdoor view opacity.
	 * @param targetOpacity {number | 'initial'}
	 * @param excludedStyleLayerIds {string[]}
	 */
	setOpacity(targetOpacity: number | 'initial', excludedStyleLayerIds: string[] = []) {
		const map = this.#core.map;

		if (!map) return;

		// TODO: Not sure why `getStyle` can sometimes be undefined
		const layers = this.#core.map?.getStyle?.()?.layers;

		if (!layers) return;

		map.once('idle', () => {
			layers.forEach(layer => {
				const visibility = map.getLayoutProperty(layer.id, 'visibility');

				if (!excludedStyleLayerIds.includes(layer.id) && visibility !== 'none') {
					try {
						const initialOpacity = this.#initialStyle.opacity.get(layer.id);

						if (initialOpacity !== undefined) {
							if (layer.id === 'background') {
								// do nothing
							} else if (layer.type === 'symbol') {
								map.setPaintProperty(
									layer.id,
									`icon-opacity`,
									targetOpacity === 'initial' ? initialOpacity[0] : targetOpacity,
								);
								map.setPaintProperty(
									layer.id,
									`text-opacity`,
									targetOpacity === 'initial' ? initialOpacity[1] : targetOpacity,
								);
							} else {
								map.setPaintProperty(
									layer.id,
									`${layer.type}-opacity`,
									targetOpacity === 'initial' ? initialOpacity[0] : targetOpacity,
								);
							}
							this.#style.opacity = targetOpacity;
						}
					} catch (error) {
						Logger.warn('Error setting opacity:', error);
					}
				}
			});
		});
	}
}
