import type { Quadtree, QuadtreeInternalNode, QuadtreeLeaf } from 'd3-quadtree';

/**
 * Traverses a quad tree in depth-first order, starting with top-left quadrant
 * and calling a callback function on each leaf node.
 *
 * @param tree - The quad tree to traverse
 * @param callback - Function called on each leaf node. Return true to stop traversal, false to continue
 * @param node - Current node being processed (used internally for recursion)
 * @param x1 - Left boundary of current quadrant (used internally for recursion)
 * @param y1 - Top boundary of current quadrant (used internally for recursion)
 * @param x2 - Right boundary of current quadrant (used internally for recursion)
 * @param y2 - Bottom boundary of current quadrant (used internally for recursion)
 * @returns true if traversal was stopped by callback, false if completed
 *
 * @example
 * ```typescript
 * import { quadtree } from 'd3-quadtree';
 * import { traverseQuadTree } from './utils';
 *
 * // Create a quad tree with some data points
 * const tree = quadtree()
 *   .x(d => d.x)
 *   .y(d => d.y)
 *   .addAll([
 *     { x: 10, y: 10, id: 'point1' },
 *     { x: 20, y: 20, id: 'point2' },
 *     { x: 30, y: 30, id: 'point3' }
 *   ]);
 *
 * // Traverse all leaf nodes and process them
 * traverseQuadTree(tree, (data, x1, y1, x2, y2) => {
 *   console.log('Found leaf node:', data, 'in bounds:', [x1, y1, x2, y2]);
 *   return false; // Continue traversal
 * });
 *
 * // Stop traversal when a specific condition is met
 * traverseQuadTree(tree, (data, x1, y1, x2, y2) => {
 *   if (data.id === 'point2') {
 *     console.log('Found target point:', data);
 *     return true; // Stop traversal
 *   }
 *   return false; // Continue traversal
 * });
 * ```
 */
export function traverseQuadTree<T>(
	tree: Quadtree<T>,
	callback: (
		node: QuadtreeInternalNode<T> | QuadtreeLeaf<T>,
		x1: number,
		y1: number,
		x2: number,
		y2: number,
	) => boolean,
	node?: QuadtreeInternalNode<T> | QuadtreeLeaf<T>,
	x1 = 0,
	y1 = 0,
	x2 = 0,
	y2 = 0,
): boolean {
	// root tree
	if (node === undefined) {
		node = tree.root();
		const extent = tree.extent();
		if (extent) {
			[[x1, y1], [x2, y2]] = extent;
			// check callback for root and stop (which should never happen?)
			if (callback(node, x1, y1, x2, y2)) {
				return true;
			}
		}
		if (!node) return false;
	}

	// debugger;

	// Calculate quadrant bounds: [x1, y1, x2, y2] for each quadrant
	const quadrants = [
		[x1, y1, (x2 + x1) / 2, (y2 + y1) / 2], // top-left
		[(x2 + x1) / 2, y1, x2, (y2 + y1) / 2], // top-right
		[x1, (y2 + y1) / 2, (x2 + x1) / 2, y2], // bottom-left
		[(x2 + x1) / 2, (y2 + y1) / 2, x2, y2], // bottom-right
	];

	// For each quadrant: top-left, top-right, bottom-left, bottom-right
	for (let i = 0; i < 4; i++) {
		const quadrant = node[i];
		if (quadrant) {
			const [qx1, qy1, qx2, qy2] = quadrants[i];

			const shouldNotSearchChildren = callback(quadrant, qx1, qy1, qx2, qy2);
			// Only search children if callback returns true (continue searching)
			if (!shouldNotSearchChildren) {
				traverseQuadTree(tree, callback, quadrant, qx1, qy1, qx2, qy2);
			}
		}
	}

	return false;
}

export function getDistanceFast(x1: number, y1: number, x2: number, y2: number) {
	const xDiff = x2 - x1;
	const yDiff = y2 - y1;
	return Math.sqrt(xDiff * xDiff + yDiff * yDiff);
}
