import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3FloorStacks = Pick<MVFv2_STANDARD_MVFv3, 'floorStacks' | 'outdoors'>;
export type V2FloorStacks = Pick<ParsedMVF, 'floorstack.json' | 'mapstack.geojson'>;
export type V2FloorStack = NonNullable<V2FloorStacks['floorstack.json']>[number];
export type V2MapStack = NonNullable<V2FloorStacks['mapstack.geojson']>[number];
export function buildFloorStacks(v3: V3FloorStacks, _builderOptions: Partial<BuilderOptions> = {}): V2FloorStacks {
	const floorStacks: V2FloorStacks['floorstack.json'] = [];

	for (const floorStack of v3?.floorStacks ?? []) {
		const v2FloorStack: V2FloorStack = {
			id: floorStack.id,
			floors: floorStack.floors,
			maps: floorStack.floors,
			externalId: floorStack.details?.externalId ?? '',
			name: floorStack.details?.name ?? '',
			shortName: floorStack.details?.shortName ?? '',
			type: 'Building',
		};
		if (floorStack.defaultFloor) {
			v2FloorStack.defaultFloor = floorStack.defaultFloor;
		}

		floorStacks.push(v2FloorStack);
	}

	if (v3.outdoors && v3.outdoors.floors.length > 0) {
		const v2Outdoors: V2FloorStack = {
			id: 'outdoors',
			floors: v3.outdoors.floors,
			maps: v3.outdoors.floors,
			type: 'Outdoor',
			name: 'Outdoors',
			externalId: 'outdoors',
		};
		floorStacks.push(v2Outdoors);
	}

	const v2: V2FloorStacks = {
		'floorstack.json': floorStacks,
		'mapstack.geojson': floorStacks,
	};

	return v2;
}
