import type { Rectangle } from '../rectangle';

/**
 * Check if a rectangle contains another rectangle
 * @param rect1 {Rectangle}
 * @param rect2 {Rectangle}
 * @hidden
 */
export function contains(rect1: Rectangle, rect2: Rectangle) {
	return (
		rect2.x + rect2.w < rect1.x + rect1.w &&
		rect2.x > rect1.x &&
		rect2.y > rect1.y &&
		rect2.y + rect2.h < rect1.y + rect1.h
	);
}
