import type { Position, Feature, MultiPolygon, Polygon } from 'geojson';

import { Edge } from '../edge';
import { DEFAULT_BUFFER_RADIUS, NavigationGraph } from '../navigation-graph';
import { hasLineOfSight, getAngle } from '../utils';

import type { NodeCollection } from '../types/node';
import type { ObstructionCollection, ObstructionFeature } from '../types/obstruction';
import type { SpaceCollection, SpaceFeature } from '../types/space';
import type { DirectionsCollection, DirectionProperties } from '../types/directions';
import { type NavigationFlagDeclarations } from '@mappedin/mvf-v2';
import { WELL_KNOWN_FLAGS } from '@mappedin/mvf-v2/no-validator';
import { hasFlag } from './utils';
import { equirectangularDistance } from '../../../common/utils';

/**
 * Options for simplifying directions in the Navigator class.
 */
export type SimplifyDirectionsOptions = {
	/**
	 * Enable or disable simplifying.
	 */
	enabled: boolean;
	/**
	 * The radius of the buffer around the path to consider when simplifying, in meters.
	 * @default 0.7
	 */
	bufferRadius?: number;
};

/**
 * Defines the special zone for navigation operations.
 */
export type DirectionsZone = {
	geometry: Feature<MultiPolygon | Polygon>;
	/**
	 * The additional cost for navigation through the zone.
	 */
	cost: number;
	/**
	 *
	 *  Additional property specific to the navigator based on the 'groupBy' option.
	 */
	[index: string]: any;
};

export class Navigator {
	private groupBy: string;
	graph: NavigationGraph;
	private geometryEdgesByMapId = new Map<string, [number, number][][]>();
	private flagDeclarations: NavigationFlagDeclarations = {};

	// Currently, we only disable nodes without the public flag when the flag is
	// present in declarations. No need to recompute at this time.
	private disabledNodeIds: Set<string>;

	/**
	 * Constructs a Navigator instance to manage pathfinding with optional obstructions and grouping features.
	 *
	 * @param {NodeCollection} nodes - Collection of nodes for the navigation graph.
	 * @param {ObstructionCollection} [obstructions] - Optional collection of obstructions that could block paths.
	 * @param {SpaceCollection} [spaces] - Optional collection of spaces that could block paths.
	 * @param {string} [groupBy] - Optional property name to group nodes and paths for differentiated processing.
	 */
	constructor({
		nodes,
		geojsonCollection,
		groupBy = 'default',
		multiplicativeDistanceWeightScaling,
		flagDeclarations,
	}: {
		nodes: NodeCollection;
		geojsonCollection?: ObstructionCollection | SpaceCollection;
		groupBy?: string;
		multiplicativeDistanceWeightScaling?: boolean;
		flagDeclarations?: NavigationFlagDeclarations;
	}) {
		this.groupBy = groupBy;
		this.graph = new NavigationGraph({ nodes, groupBy: this.groupBy, multiplicativeDistanceWeightScaling });
		let features: ObstructionFeature[] | SpaceFeature[] = geojsonCollection?.features || [];
		this.flagDeclarations = flagDeclarations ?? {};
		this.disabledNodeIds = this.getDisabledNodeIds();

		features.forEach(feature => {
			let coords: [number, number][] | null = null;

			if (feature.geometry.type === 'LineString') {
				coords = feature.geometry.coordinates as [number, number][];
			} else if (feature.geometry.type === 'Polygon') {
				coords = feature.geometry.coordinates.flat() as [number, number][];
			}

			if (coords) {
				const groupId = this.groupBy === 'default' ? this.groupBy : feature.properties[this.groupBy];

				if (!this.geometryEdgesByMapId.has(groupId)) {
					this.geometryEdgesByMapId.set(groupId, []);
				}
				coords.forEach((v, i) => {
					this.geometryEdgesByMapId.get(groupId)!.push([
						v,
						coords[i + 1] || coords[0], // Safely wrap around if at the end of the array.
					]);
				});
			}
		});
	}

	private getDisabledNodeIds(): Set<string> {
		const disabledNodeIds = new Set<string>();

		// Exclude non-public nodes when the well-known public flag is declared
		if (WELL_KNOWN_FLAGS.public in this.flagDeclarations) {
			for (const nodeId in this.graph.nodesById) {
				if (
					!hasFlag(
						this.graph.nodesById[nodeId].properties.flags,
						this.flagDeclarations[WELL_KNOWN_FLAGS.public].index,
						this.flagDeclarations[WELL_KNOWN_FLAGS.public].bit,
					)
				) {
					disabledNodeIds.add(nodeId);
				}
			}
		}

		return disabledNodeIds;
	}

	/**
	 * Calculates and returns a set of directions from origin nodes to destination nodes, including detailed properties.
	 *
	 * @param {DirectionsZone[]} zones - special zones for navigation operations.
	 * @param {string[]} originIds - IDs of origin nodes.
	 * @param {string[]} destinationNodeIds - IDs of destination nodes.
	 * @param {string[]} [disabledConnectionNodeIds] - IDs of connection nodes that are disabled (ie. act as regular nodes).
	 * @param {SimplifyDirectionsOptions} [simplify] - Options to simplify the pathfinding result.
	 * @returns {DirectionsCollection} A collection of directional features representing the path.
	 */
	public getDirections({
		zones: directionsZones,
		originIds,
		destinationNodeIds,
		disabledConnectionNodeIds,
		simplify,
		multiplicativeDistanceWeightScaling,
		overrideEdgeWeights,
	}: {
		originIds: string[];
		destinationNodeIds: string[];
		zones?: DirectionsZone[];
		disabledConnectionNodeIds?: string[];
		simplify?: SimplifyDirectionsOptions;
		multiplicativeDistanceWeightScaling?: boolean;
		overrideEdgeWeights?: Map<Edge, number>;
	}): DirectionsCollection {
		const simplifyEnabled = simplify?.enabled ?? true;
		const zones = directionsZones ?? [];

		let steps = this.graph.aStar({
			originIds,
			destinationNodeIds,
			zones,
			disabledConnectionNodeIds: new Set(disabledConnectionNodeIds),
			overrideEdgeWeights,
			// TODO: We eventually may want to include non-public nodes for emergency pathing or other
			// such controllable options. For now, always exclude non-public nodes from pathing.
			disabledNodeIds: this.disabledNodeIds,
		});

		if (steps.length === 0) {
			return { type: 'FeatureCollection', features: [] };
		}

		if (simplifyEnabled) {
			steps = this.simplifyAllSteps(
				steps,
				simplify?.bufferRadius ?? DEFAULT_BUFFER_RADIUS,
				multiplicativeDistanceWeightScaling,
			);
		}

		return this.generatePath(steps);
	}

	/**
	 * Generates a path from a series of edges, constructing a feature collection of directional steps.
	 *
	 * @param {Edge[]} steps - An array of edges representing the path.
	 * @returns {DirectionsCollection} A collection of directional features.
	 */
	private generatePath = (steps: Edge[]): DirectionsCollection => {
		const { destination } = steps[steps.length - 1];
		const path: DirectionsCollection = {
			type: 'FeatureCollection',
			features: [],
		};

		for (let i = 0; i < steps.length; i++) {
			const { origin, destination } = steps[i];
			const properties: DirectionProperties = {
				id: origin.properties.id,
				angle: steps[i].angle,
				distance: steps[i].distance,
				destination: destination.properties.id,
				edges: this.graph.edges[origin.properties.id],
			};

			if (this.groupBy !== 'default') {
				properties.groupBy = origin.properties[this.groupBy];
			}
			path.features.push({
				type: 'Feature',
				geometry: { type: 'Point', coordinates: [origin.geometry.coordinates[0], origin.geometry.coordinates[1]] },
				properties,
			});
		}

		const properties: DirectionProperties = {
			id: destination.properties.id,
			edges: this.graph.edges[destination.properties.id],
		};

		if (this.groupBy !== 'default') {
			properties.groupBy = destination.properties[this.groupBy];
		}

		path.features.push({
			type: 'Feature',
			geometry: {
				type: 'Point',
				coordinates: [destination.geometry.coordinates[0], destination.geometry.coordinates[1]],
			},
			properties,
		});

		return path;
	};

	/**
	 * Simplifies a sequence of steps by reducing unnecessary nodes using a buffer radius to check for obstructions.
	 *
	 * @param {Edge[]} steps - The steps to simplify.
	 * @param {number} bufferRadius - The buffer radius to use
	 * for simplification.
	 * @returns {Edge[]} An array of simplified edges representing a more direct path.
	 */
	private simplifyAllSteps(steps: Edge[], bufferRadius: number, multiplicativeDistanceWeightScaling?: boolean): Edge[] {
		const processedSteps: Edge[] = [];
		let stepMapSegment: Edge[] = [steps[0]];

		for (let i = 1; i < steps.length; i++) {
			if (
				this.groupBy !== 'default' &&
				steps[i].origin.properties[this.groupBy] !== steps[i - 1].origin.properties[this.groupBy]
			) {
				const groupId = steps[i - 1].origin.properties[this.groupBy];
				const edges = this.geometryEdgesByMapId.get(groupId) ?? [];
				processedSteps.push(
					...this.simplifySteps(stepMapSegment, edges, bufferRadius, multiplicativeDistanceWeightScaling),
				);
				stepMapSegment = [steps[i]];
			} else {
				stepMapSegment.push(steps[i]);
			}
		}

		// Handle the last segment
		const finalStep = steps[steps.length - 1];
		const finalGroupId = this.groupBy === 'default' ? this.groupBy : finalStep.origin.properties[this.groupBy];
		const finalEdges = this.geometryEdgesByMapId.get(finalGroupId) ?? [];
		processedSteps.push(
			...this.simplifySteps(stepMapSegment, finalEdges, bufferRadius, multiplicativeDistanceWeightScaling),
		);

		return processedSteps;
	}

	/**
	 * Finds the nearest nodes on the graph within a given travel distance.
	 *
	 * @param originId - The ID of the origin node.
	 * @param maxDistance - The maximum distance to search.
	 * @param floorId - The ID of the floor.
	 * @param includedNodeIds - The IDs of the nodes to include.
	 * @param useLineOfSight - Whether to use line of sight.
	 * @returns An array of nodes within the travel distance that satisfy the goal function.
	 */
	public findNearestNodesOnGraph = (
		originId: string,
		maxDistance: number,
		floorId: string,
		includedNodeIds: string[],
		useLineOfSight = false,
		limitNumberOfResults = Infinity,
	) => {
		return this.graph.dijkstraFindWithinTravelDistance(
			originId,
			maxDistance,
			includedNodeIds,
			this.geometryEdgesByMapId.get(floorId) ?? [],
			useLineOfSight,
			limitNumberOfResults,
		);
	};

	/**
	 * Checks if there is a line of sight between two points on the map.
	 *
	 * @param origin - The origin point.
	 * @param destination - The destination point.
	 * @param floorId - The ID of the floor.
	 * @param bufferRadius - The buffer radius to use when checking for line of sight.
	 * @returns True if there is a line of sight, false otherwise.
	 */
	public hasLineOfSight = (
		origin: [number, number],
		destination: [number, number],
		floorId: string,
		bufferRadius?: number,
	): boolean => {
		return this.graph.hasLineOfSight(origin, destination, this.geometryEdgesByMapId.get(floorId), bufferRadius);
	};

	/**
	 * Simplifies a section of steps by checking direct lines of sight between steps and eliminating intermediate nodes if unobstructed.
	 *
	 * @param {Edge[]} steps - The steps to potentially simplify.
	 * @param {[number, number][][]} geometryEdges - The geometrical edges of the map used to check for line of sight.
	 * @param {number} bufferRadius - The buffer radius to use when simplifying.
	 * @returns {Edge[]} An array of simplified edges.
	 */
	private simplifySteps(
		steps: Edge[],
		geometryEdges: [number, number][][],
		bufferRadius: number,
		multiplicativeDistanceWeightScaling?: boolean,
	): Edge[] {
		if (steps.length < 3) return steps;

		const simpleSteps: Edge[] = [steps[0]];
		let n = 1;
		for (let m = n + 2; m < steps.length; m++) {
			/*
			 * TODO: Implement edge simplification zone checks.
			 * In some cases, simplification and A* may need to consider zones to ensure the path remains valid.
			 * When edge intersects with a zone, the simplification shouldn't remove the edge.
			 * To implement this, we need have to create multiple equidistant points along the edge and
			 * check if any of them are in the zone.
			 */
			if (
				/**
				 * Some nodes may be generated from ORS and accurately model roads; we don't want to simplify these.
				 * MVFv3s and MVFv2s converted from MVFv3s will specify this flag.
				 */
				('outdoors' in this.flagDeclarations &&
					hasFlag(
						steps[n]?.origin?.properties?.flags ?? [],
						this.flagDeclarations.outdoors.index,
						this.flagDeclarations.outdoors.bit,
					)) ||
				/**
				 * Some nodes may explicitly specify that they should not be smoothed. Native MVFv2s will specify
				 * this flag on outdoor nodes.
				 */
				steps[n].origin.properties.preventSmoothing ||
				/**
				 * Otherwise, do not smooth if a node does not have line of sight to the next node, because the path
				 * would go through obstructions.
				 */
				!hasLineOfSight(steps[n].origin, steps[m].origin, geometryEdges, bufferRadius)
			) {
				const lastVisible = m - 1;
				simpleSteps.push(
					new Edge({
						...steps[n],
						destination: steps[lastVisible].origin,
						distance: equirectangularDistance(
							steps[n].origin.geometry.coordinates,
							steps[lastVisible].origin.geometry.coordinates,
						),
						angle: getAngle(steps[n].origin.geometry.coordinates, steps[lastVisible].origin.geometry.coordinates),
					}),
				);
				n = lastVisible;
			}
		}

		// Add the last two steps leaving the final as is
		// This ensures the path will enter from the hallway node directly in front of the entranceNode
		simpleSteps.push(
			new Edge({
				...steps[n],
				destination: steps[steps.length - 1].origin,
				distance: equirectangularDistance(
					steps[n].origin.geometry.coordinates,
					steps[steps.length - 1].origin.geometry.coordinates,
				),
				angle: getAngle(steps[n].origin.geometry.coordinates, steps[steps.length - 1].origin.geometry.coordinates),
				multiplicativeDistanceWeightScaling: multiplicativeDistanceWeightScaling,
			}),
			steps[steps.length - 1],
		);

		return simpleSteps;
	}

	/**
	 * Calculates the approximate distance between two geographic coordinates on Earth's surface.
	 *
	 * This function uses the equirectangular approximation method to compute the distance, which simplifies
	 * the math and speeds up calculations, but is less accurate over long distances compared to other methods
	 * like the haversine formula.
	 *
	 * @param {Position} point1 - The first point's longitude and latitude as [longitude, latitude].
	 * @param {Position} point2 - The second point's longitude and latitude as [longitude, latitude].
	 * @return
	 * @return {number} The approximate distance between the two points in meters.
	 */
	public getDistance = (point1: Position, point2: Position) => {
		return equirectangularDistance(point1, point2);
	};

	/**
	 * Calculates the angle between two geographic coordinates.
	 *
	 * @param {Position} point1 - The first point's longitude and latitude as [longitude, latitude].
	 * @param {Position} point2 - The second point's longitude and latitude as [longitude, latitude].
	 * @hidden
	 *
	 * @return {number} The angle in radians, calculated clockwise from the north between the two points specified.
	 */
	public getAngle = (point1: Position, point2: Position) => {
		return getAngle(point1, point2);
	};
}
