export const DEFAULT_V6_URI = 'https://app.mappedin.com/';
export const DEFAULT_ENTERPRISE_AUTH_URL = 'https://auth.mappedin.com/';

/**
 * Represents the environment state configuration.
 * @example
 * const mapData = getMapData({
 *  key: '',
 *  secret: '',
 *  environment: 'eu'
 * })
 */
export type Environment = {
	/**
	 * The base URI for the API.
	 */
	baseUri: string;

	/**
	 * The base URI for authentication.
	 */
	baseAuthUri: string;

	/**
	 * The base URI for analytics.
	 */
	analyticsBaseUri: string;

	/**
	 * The URI for the tile server.
	 */
	tileServerUri: string;
};

export type InternalServiceEnvironment =
	| 'us'
	| 'us-enterprise'
	| 'eu'
	| 'us-staging-enterprise'
	| 'us-staging-self-serve';
export type ServiceEnvironment = 'us' | 'eu';

export const US_SELF_SERVE_ENV: Readonly<Environment> = Object.freeze({
	baseUri: 'https://app.mappedin.com/',
	baseAuthUri: 'https://app.mappedin.com/',
	analyticsBaseUri: 'https://api-gateway.mappedin.com/track-analytics/a/',
	tileServerUri: 'https://tiles-cdn.mappedin.com/styles/mappedin/style.json',
});

export const US_ENTERPRISE_ENV: Readonly<Environment> = Object.freeze({
	...US_SELF_SERVE_ENV,
	baseUri: 'https://api-gateway.mappedin.com/',
	baseAuthUri: 'https://auth.mappedin.com/',
});

const EU_ENV_ENTERPRISE: Readonly<Environment> = Object.freeze({
	baseUri: 'https://api-gateway.eu.mappedin.com/',
	baseAuthUri: 'https://auth.eu.mappedin.com/',
	analyticsBaseUri: `https://api-gateway.eu.mappedin.com/track-analytics/a/`,
	tileServerUri: 'https://tiles-cdn.eu.mappedin.com/styles/mappedin/style.json',
});

const US_STAGING_ENV: Readonly<Environment> = Object.freeze({
	baseUri: 'https://api-gateway-staging.mappedin.net/',
	baseAuthUri: 'https://auth-staging.mappedin.net/',
	analyticsBaseUri: `https://api-gateway-staging.mappedin.net/track-analytics/a/`,
	tileServerUri: 'https://tiles-cdn.mappedin.com/styles/mappedin/style.json',
});

const US_SELF_SERVE_STAGING_ENV: Readonly<Environment> = Object.freeze({
	baseUri: 'https://app-staging.mappedin.net/',
	baseAuthUri: 'https://app-staging.mappedin.net/',
	analyticsBaseUri: `https://api-gateway-staging.mappedin.net/track-analytics/a/`,
	tileServerUri: 'https://tiles-cdn.mappedin.com/styles/mappedin/style.json',
});

const ENV_MAP: Record<InternalServiceEnvironment, Readonly<Environment>> = {
	eu: EU_ENV_ENTERPRISE,
	us: US_SELF_SERVE_ENV,
	'us-enterprise': US_ENTERPRISE_ENV,
	'us-staging-enterprise': US_STAGING_ENV,
	'us-staging-self-serve': US_SELF_SERVE_STAGING_ENV,
};

export function getEnvironmentAuthState(env: InternalServiceEnvironment) {
	return ENV_MAP[env];
}
