import { BufferAttribute, BufferGeometry } from 'three';
import type { Matrix4 } from 'three';
import type { EntityBatchedMesh, MeshComponent } from '../../components/mesh';

/**
 * Extract the buffer geometry from the batched geometry.
 * @param meshComponent - The mesh component to extract the buffer geometry from.
 * @returns The buffer geometry.
 */
export function extractBufferGeometryFromBatchedGeometry(meshComponent: MeshComponent) {
	const bufferGeometry = new BufferGeometry();
	const originalGeometry = meshComponent.mesh?.geometry;

	if (!originalGeometry || !meshComponent.batchedProps?.range) {
		return;
	}

	const range = meshComponent.batchedProps.range;
	const start = range.start;
	const count = range.count;

	// Extract position attribute
	const originalPosition = originalGeometry.getAttribute('position');
	if (originalPosition) {
		const extractedPositions = new Float32Array(count * 3);
		for (let i = 0; i < count; i++) {
			const sourceIndex = start + i;
			extractedPositions[i * 3] = originalPosition.array[sourceIndex * 3];
			extractedPositions[i * 3 + 1] = originalPosition.array[sourceIndex * 3 + 1];
			extractedPositions[i * 3 + 2] = originalPosition.array[sourceIndex * 3 + 2];
		}
		bufferGeometry.setAttribute('position', new BufferAttribute(extractedPositions, 3));
	}

	// Extract normal attribute if it exists
	const originalNormal = originalGeometry.getAttribute('normal');
	if (originalNormal) {
		const extractedNormals = new Float32Array(count * 3);
		for (let i = 0; i < count; i++) {
			const sourceIndex = start + i;
			extractedNormals[i * 3] = originalNormal.array[sourceIndex * 3];
			extractedNormals[i * 3 + 1] = originalNormal.array[sourceIndex * 3 + 1];
			extractedNormals[i * 3 + 2] = originalNormal.array[sourceIndex * 3 + 2];
		}
		bufferGeometry.setAttribute('normal', new BufferAttribute(extractedNormals, 3));
	}

	// Extract UV attribute if it exists
	const originalUV = originalGeometry.getAttribute('uv');
	if (originalUV) {
		const extractedUVs = new Float32Array(count * 2);
		for (let i = 0; i < count; i++) {
			const sourceIndex = start + i;
			extractedUVs[i * 2] = originalUV.array[sourceIndex * 2];
			extractedUVs[i * 2 + 1] = originalUV.array[sourceIndex * 2 + 1];
		}
		bufferGeometry.setAttribute('uv', new BufferAttribute(extractedUVs, 2));
	}

	// Extract index if it exists
	if (originalGeometry.index) {
		const originalIndex = originalGeometry.index;
		const extractedIndices = new Uint32Array(count);
		for (let i = 0; i < count; i++) {
			extractedIndices[i] = originalIndex.array[start + i];
		}
		bufferGeometry.setIndex(new BufferAttribute(extractedIndices, 1));
	}

	return bufferGeometry;
}

/**
 * Transfer the geometry from the old batched mesh to the new batched mesh.
 * @param meshComponent - The mesh component to transfer the geometry from.
 * @param newBatchedMesh - The new batched mesh to transfer the geometry to.
 * @returns The instance of the new batched mesh.
 */
export function transferGeometryToNewBatchedMesh(
	meshComponent: MeshComponent,
	newBatchedMesh: EntityBatchedMesh,
	matrix: Matrix4,
) {
	// extract buffer geometry out of the batched mesh
	const bufferGeometry = extractBufferGeometryFromBatchedGeometry(meshComponent);
	if (!bufferGeometry || !meshComponent.mesh) {
		return;
	}

	// preserve the instance matrix
	meshComponent.mesh.getMatrixAt(meshComponent.instanceIndex, matrix);

	// let's remove the instance from the old batched mesh
	meshComponent.mesh.deleteInstance(meshComponent.instanceIndex);
	meshComponent.mesh.optimize();

	// add the geometry to the new batched mesh
	const instance = newBatchedMesh.addGeometry(bufferGeometry);
	newBatchedMesh.addInstance(instance);

	// apply the mesh's matrix to the new instance
	newBatchedMesh.setMatrixAt(instance, matrix);

	// get the geometry id and range of the new batched mesh
	const geometryId = newBatchedMesh.getGeometryIdAt(instance);
	const range = newBatchedMesh.getGeometryRangeAt(geometryId);
	if (range && meshComponent.batchedProps) {
		meshComponent.batchedProps.range = range;
	}

	// and now dispose of the geometry to free up memory
	bufferGeometry.dispose();

	return instance;
}
