export const getMapDataEnterprise = getMapData;

/**
 * @internal
 *
 * Note: had to add this here to avoid a circular reference when I put it in options
 * TODO: investigate
 *
 * Check if the worker urls are valid
 * @returns false if workers have not been set, and an object with the worker urls if they are set
 */
export const checkWorkerUrls = async () => {
	if (collisionWorkerUrl === '' || maplibreWorkerUrl === '') {
		return false;
	}

	// we want to enforce same-origin, as the browser will outright reject remote worker URLs
	const promises = [
		fetch(collisionWorkerUrl, {
			mode: 'same-origin',
		}),
		fetch(maplibreWorkerUrl, {
			mode: 'same-origin',
		}),
	];
	let [collisionResponse, maplibreResponse] = [{ ok: false }, { ok: false }];
	try {
		[collisionResponse, maplibreResponse] = await Promise.all(promises);
	} catch (e) {
		// ignore errors
	}

	return {
		collision: collisionResponse.ok,
		maplibre: maplibreResponse.ok,
	};
};

export const show3dMap = async (el: HTMLElement, mapData: MapData, options?: TShow3DMapOptions): Promise<MapView> => {
	const start = performance.now();
	const optionsWithDefaults = handleShow3DMapOptionDefault(options, mapData);
	if (optionsWithDefaults) validateOptions(optionsWithDefaults);

	// Inject the styles into the document head.
	if (optionsWithDefaults.injectStyles ?? true) {
		injectCSS(styles, { id: 'mappedin__styles' });
	}

	// Should use the outdoorViewToken and not user provided accessToken.
	// Capabilities turn on/off paid features we don't want users to bypass.
	const capabilities = handleTokenCapabilities(mapData.outdoorViewToken ?? '');

	const internal = getMapDataInternal(mapData);
	if (optionsWithDefaults.outdoorView?.style) {
		internal.envControl.updateTileServerBaseUrl(optionsWithDefaults.outdoorView?.style);
	}

	let disableCollisionWorker = false;
	let disableOutdoors = false;
	// let's check if the worker urls are valid
	const workerUrls = await checkWorkerUrls();
	if (workerUrls) {
		// setWorkerURL was called, let's check if the worker urls are properly set up
		if (!workerUrls.collision) {
			// collision worker url is not valid, let's disable it
			disableCollisionWorker = true;
		}
		if (!workerUrls.maplibre) {
			// maplibre worker url is not valid, let's disable it
			disableOutdoors = true;
		}
	}

	const core = await createRenderer(el, {
		center: [mapData.mapCenter.longitude, mapData.mapCenter.latitude],
		bearing: optionsWithDefaults.bearing,
		pitch: optionsWithDefaults.pitch,
		zoomLevel: optionsWithDefaults.zoomLevel,
		backgroundAlpha: optionsWithDefaults.style?.backgroundAlpha,
		backgroundColor: optionsWithDefaults.style?.backgroundColor,
		outdoorView:
			!disableOutdoors && optionsWithDefaults.outdoorView?.token
				? {
						headers: {
							'x-mappedin-tiles-key': optionsWithDefaults.outdoorView?.token,
						},
						style: mapData.getEnv().tileServerUri,
						enabled: !!optionsWithDefaults.outdoorView?.enabled,
						lowDpi: optionsWithDefaults.outdoorView?.lowDpi,
				  }
				: undefined,
		watermark: {
			...optionsWithDefaults.watermark,
			visible: optionsWithDefaults.watermark?.visible ?? (!internal.enterpriseMode && !capabilities.remove_watermark),
			onClick: CUSTOM_WATERMARK_ON_CLICK_FN || undefined,
		},
		attribution: optionsWithDefaults.attribution,
		antialias:
			typeof optionsWithDefaults.antialiasing === 'boolean'
				? optionsWithDefaults.antialiasing
				: Boolean(optionsWithDefaults.antialiasing?.enabled),
		accessToken: mapData.outdoorViewToken,
		occlusionEnabled: true,
		transformImageRequest: !internal.enterpriseMode ? internal.transformImageRequest : undefined,
		naturalBearing: mapData.naturalBearing,
		imagePlacementOptions: optionsWithDefaults.imagePlacementOptions,
		onWebGLContextCreationError: optionsWithDefaults.onWebGLContextCreationError,
		onWebGLContextLost: optionsWithDefaults.onWebGLContextLost,
		onWebGLContextRestored: optionsWithDefaults.onWebGLContextRestored,
		onWebGLRendererError: optionsWithDefaults.onWebGLRendererError,
		useStandaloneCamera: optionsWithDefaults.useStandaloneCamera ?? false,
		useWorkers: !(optionsWithDefaults.disableWorkers ?? false),
		useCollisionWorker: !disableCollisionWorker,
	});

	const mapView3d = new MapView(core);

	await mapView3d.addMap(mapData, optionsWithDefaults);
	internal.Analytics.sendMapViewLoadedEvent({
		firstRenderDuration: performance.now() - start,
		dimension: mapView3d.getDimensions(),
		collisionWorkerDisabled: disableCollisionWorker,
		outdoorsWorkerDisabled: disableOutdoors,
	});

	return mapView3d;
};

