import type { PerspectiveCamera, Vector3, Object3D, Intersection, Object3DEventMap } from 'three';
import { Mesh, Raycaster, Scene, Vector2 } from 'three';
import type { Geometry2D } from '../../entities/geometry2d';
import { QuadTree, Rectangle } from '@packages/internal/quad-tree';
import type { Geometry3D } from '../../entities/geometry3d';
import { Geometry3DComponents } from '../../entities/geometry3d';
import { PubSub } from '../../../../packages/common/pubsub';
import type { EntityMesh } from '../../types';
import { Tap, TapsController } from './tap-controller';
import { MOUSE_CLICK_DISTANCE_THRESHOLD, MOUSE_CLICK_TIME_THRESHOLD, TAP_TIMEOUT } from './constants';
import type { All3DTypes, RendererState } from '../../renderer';
import '../../styles/interactions.scss';
import type { EntityBatchedMesh } from '../../components/mesh';
import type { GroupContainerObject3D } from '../../entities/group-container';
import { getUniqueIntersectedGroupContainers } from './utils';
import { RAYCAST_LAYERS } from '../../utils/constants';
import { arraysEqual } from '@packages/internal/common';

type InteractionPayload = {
	entity2D?: Geometry2D;
	entity3D?: Geometry3D;
	position?: Vector3;
	groupContainers?: GroupContainerObject3D[];
	pointerEvent: Pick<PointerEvent, 'button'>;
};
type InteractionEvents = {
	click: InteractionPayload;
	hover: Omit<InteractionPayload, 'pointerEvent'>;
	'singletap-with-twofinger': PointerEvent;
	'doubletap-with-onefinger': PointerEvent;
};

type CursorTypes = 'grabbing' | 'grab' | 'pointer';
type InteractionState = {
	distanceFromMouseDown: number | undefined;
	isPanning: boolean;
	mouseDownStart:
		| {
				time: number; // Date representation
				timestamp: number; // performance.now representation
				offsetX: number; // container relative coordinates
				offsetY: number; // container relative coordinates
		  }
		| undefined;
};

export type InteractionRequiredCoreStateProps = {
	geometry2DIdsInScene: Set<Geometry2D['id']>;
	geometry3DIdsInScene: Set<Geometry3D['id']>;
}; // Local core state type for separation of concern. Structural typing will figure this out

/**
 * TODO: touch support
 */
export class InteractionSystem extends PubSub<InteractionEvents> {
	// private debug = false;
	raycaster = new Raycaster(); // exposed for testing
	private state = {
		hovered3DEntity: undefined as Geometry3D | undefined,
		hovered2DEntity: undefined as Geometry2D | undefined,
		hovered3DContainers: undefined as GroupContainerObject3D[] | undefined,
		lastHover: undefined as Geometry3D | undefined,
		interaction: {
			mouseDownStart: undefined,
			distanceFromMouseDown: undefined,
			isPanning: false,
		} as InteractionState,
	};

	private coreState: RendererState;
	private camera: PerspectiveCamera;
	private worldPlane: Mesh;
	// exposed for testing conveniences
	_quadtree: QuadTree<{ entityId: Geometry2D['id'] }> = new QuadTree(new Rectangle(0, 0, 0, 0));
	// exposed for testing
	_container: HTMLCanvasElement;
	private lastPointerEvent?: PointerEvent;
	private cursor: CursorTypes = 'grab';
	private touchesCount = 0;
	private isUserInteracting: () => boolean;
	constructor(
		container: HTMLCanvasElement,
		coreState: RendererState,
		camera: PerspectiveCamera,
		worldPlane: Mesh,
		isUserInteracting: () => boolean,
	) {
		super();
		this.isUserInteracting = isUserInteracting;
		// (window as any).debugInteraction = () => {
		// 	this.enableDebug();
		// };

		this.raycaster.layers.enableAll();
		this.raycaster.layers.disable(RAYCAST_LAYERS.OUTLINES);

		this.camera = camera;
		this._container = container;
		this.coreState = coreState;
		this.worldPlane = worldPlane;

		this._container.addEventListener('pointerdown', this.onPointerDown, false);
		// rAF is used to prevent multiple events firing on the same frame, makes a big difference on huge maps
		this._container.addEventListener('pointermove', this.onPointerMoveRaf, false);

		// add class to control cursor
		this._container.classList.add(`mappedin-interactive`);
	}

	// TODO: check for memory leak
	updateQuadtree(takeIT: QuadTree<{ entityId: Geometry2D['id'] }>) {
		this._quadtree = takeIT;
	}

	// this function is called when global hover color is changed
	setHovered3DEntityInteractionComponentDirty() {
		if (this.state.hovered3DEntity) {
			const interactionComponent = this.state.hovered3DEntity.components[Geometry3DComponents.Interaction];
			if (interactionComponent) {
				interactionComponent.dirty = true;
			}
		}
	}

	hasTouched = false;

	#animationFrame?: number = undefined;

	onPointerMoveRaf = (event: PointerEvent) => {
		this.lastPointerEvent = event;

		if (this.#animationFrame) {
			cancelAnimationFrame(this.#animationFrame);
		}

		this.#animationFrame = requestAnimationFrame(() => {
			this.onPointerMove(event);
		});
	};

	private onPointerMove = (event: PointerEvent) => {
		event.preventDefault();
		this.updateInteractionStateIfPanning(event);
		// prevent intersect if camera isn't moving nor user is interacting
		if (event.pointerType !== 'touch' || (event.pointerType === undefined && this.hasTouched)) {
			this.handleHover(event);
		}

		this.handleCursor();
	};

	tapsControl = new TapsController();

	public getThreeDIntersectsFromXY(point: Vector2) {
		this.raycaster.setFromCamera(point, this.camera);
		this.raycaster.layers.enable(0);

		const intersects = this.raycaster
			// needs recursive for paths that have multiple children
			.intersectObjects(this._hitBoxes.entities, true)
			.filter(i => i.object?.name === 'worldPlane' || i.object?.visible);

		// we need to shift the worldPlane to the end of the array, to support underground objects
		// this way we always test it last
		const worldPlaneIndex = intersects.findIndex(i => i.object?.name === 'worldPlane');
		if (worldPlaneIndex !== -1) {
			intersects.push(intersects.splice(worldPlaneIndex, 1)[0]);
		}

		return intersects;
	}

	public getGroupContainerIntersectsFromXY(point: Vector2) {
		const intersects = this.getThreeDIntersectsFromXY(point);

		let intersectedContainers;
		if (intersects != null && intersects.length > 0) {
			intersectedContainers = getUniqueIntersectedGroupContainers(intersects) as GroupContainerObject3D[];
		}

		return intersectedContainers;
	}

	private onPointerDown = (event: PointerEvent) => {
		if (event.target !== this._container) return;
		this._container.addEventListener('pointerup', this.onPointerUp, false);
		this._container.addEventListener('pointercancel', this.onPointerUp, false);

		// update on pointerdown
		this.state.interaction.mouseDownStart = {
			time: Date.now(),
			timestamp: event.timeStamp,
			offsetX: event.offsetX,
			offsetY: event.offsetY,
		};

		this.lastPointerEvent = event;
		this.state.interaction.distanceFromMouseDown = 0;
		this.handleCursor();
		if (event.pointerType === 'touch') {
			this.tapsControl.add(new Tap(event));
			this.touchesCount = Math.max(0, this.touchesCount + 1);
		}
	};

	private clickTimeout = undefined as number | undefined;
	private onPointerUp = (event: PointerEvent) => {
		this.tapsControl.discardOutsideOfWaitWindow(event.timeStamp);
		this.lastPointerEvent = event;
		this.touchesCount = Math.max(0, this.touchesCount - 1);
		if (this.touchesCount === 0) {
			// only discard these event when there are no more touches
			this._container.removeEventListener('pointerup', this.onPointerUp, false);
			this._container.removeEventListener('pointercancel', this.onPointerUp, false);
		}
		this.updateInteractionStateIfPanning(event);
		const { mouseDownStart } = this.state.interaction;
		if (!mouseDownStart || this.state.interaction.distanceFromMouseDown == null) {
			return;
		}
		if (
			Date.now() < mouseDownStart.time + MOUSE_CLICK_TIME_THRESHOLD &&
			(this.tapsControl.isSingleTapWithTwoFingers ||
				this.state.interaction.distanceFromMouseDown <= MOUSE_CLICK_DISTANCE_THRESHOLD)
		) {
			if (event.pointerType === 'touch') {
				if (this.tapsControl.isSingleTapWithTwoFingers) {
					this.publish('singletap-with-twofinger', event);
					this.flush();
				} else if (this.tapsControl.isSingleTapWithOneFinger && this.touchesCount === 0) {
					if (this.clickTimeout) clearTimeout(this.clickTimeout);
					this.clickTimeout = window.setTimeout(() => {
						this.handleClick(event);
						this.flush();
					}, TAP_TIMEOUT);
				} else if (this.tapsControl.isDoubleTapWithOneFinger) {
					this.publish('doubletap-with-onefinger', event);
					this.flush();
				}
			} else {
				this.handleClick(event);
			}
		}
		this.state.interaction.mouseDownStart = undefined;
		this.state.interaction.distanceFromMouseDown = undefined;
		this.state.interaction.isPanning = false;
		this.handleCursor();
	};

	private flush() {
		this.tapsControl.flush();
		if (this.clickTimeout != null) {
			clearTimeout(this.clickTimeout);
			this.clickTimeout = undefined;
		}
	}

	public handleHover(e?: PointerEvent) {
		const event = e || this.lastPointerEvent;

		// we want to ignore hover events when the user is interacting, as this causes jank for larger maps
		// and is unnecessary. We should only accept hover events when the camera is not being interacted with
		if (this.state.interaction.mouseDownStart || !event || this.isUserInteracting()) return;
		const { entity3D, entity2D, position, groupContainers } = this.intersect(event);
		if (
			this.detect3DEntityHover(entity3D) ||
			this.detect2DEntityHover(entity2D) ||
			this.detect3DContainerHover(groupContainers)
		) {
			this.state.hovered3DEntity = entity3D;
			this.state.lastHover = entity3D;
			this.state.hovered2DEntity = entity2D;
			this.state.hovered3DContainers = groupContainers;

			this.publish('hover', {
				entity2D: this.state.hovered2DEntity,
				entity3D: this.state.hovered3DEntity,
				position,
				groupContainers,
			});
		}
		this.handleCursor();
	}

	// ensure to be called at the end of pointer handlers
	private handleCursor() {
		const intersected = !!(
			(this.state.hovered3DEntity && this.state.hovered3DEntity?.components[Geometry3DComponents.Interaction]) ||
			this.state.hovered2DEntity
		);
		const isMouseDown = !!this.state.interaction.mouseDownStart;
		const isPanning = this.state.interaction.isPanning;
		const cursor = processCursor(intersected, isPanning, isMouseDown);
		if (this.cursor === cursor) return;
		this.cursor = cursor;

		// pseudo :active will handle grab/grabbing
		// but we need to handle hover pointer
		if (cursor === 'pointer') {
			this._container.classList.add('mappedin-interaction-hover');
		} else {
			this._container.classList.remove('mappedin-interaction-hover');
		}
	}

	private detect2DEntityHover(entity: Geometry2D | undefined): boolean {
		const dirty = this.state.hovered2DEntity !== entity;

		return dirty;
	}

	private handleClick(event: PointerEvent) {
		const intersection = this.intersect(event);
		this.publish('click', {
			entity2D: intersection.entity2D,
			entity3D: intersection.entity3D,
			position: intersection.position,
			groupContainers: intersection.groupContainers,
			pointerEvent: {
				button: event.button,
			},
		});
	}

	dirty3D = true;

	private cachedHitBoxes: {
		entities: (Object3D | EntityMesh<Geometry3D> | EntityBatchedMesh)[];
	} = {
		entities: [],
	};
	get _hitBoxes() {
		if (this.dirty3D) {
			this.cachedHitBoxes.entities = [];
			let e: All3DTypes | undefined;
			for (let entityId of this.coreState.geometry3DIdsInScene) {
				e = this.coreState.geometry3DMap.get(entityId);
				if (e?.type === 'geometry' || e?.type === 'model' || e?.type === 'path' || e?.type === 'custom-geometry') {
					if (
						e.components[Geometry3DComponents.Mesh].mesh &&
						!this.cachedHitBoxes.entities.includes(e.components[Geometry3DComponents.Mesh].mesh)
					) {
						this.cachedHitBoxes.entities.push(e.components[Geometry3DComponents.Mesh].mesh);
					}
				}
			}
			this.dirty3D = false;

			this.cachedHitBoxes.entities.push(this.worldPlane);

			return this.cachedHitBoxes;
		}

		return this.cachedHitBoxes;
	}

	// normalized device coordinates of pointer position
	private ndcPoint = new Vector2();
	private intersect(event: PointerEvent) {
		// Use offsetX/offsetY (element-relative) instead of clientX/clientY (viewport-relative)
		// to ensure accurate coordinates regardless of page scroll position.
		const { offsetX, offsetY } = event;
		// 2D intersect
		const point = [offsetX, offsetY] as [number, number];
		const result = this._quadtree.queryPoint(point);
		const [intersection] = result.filter(r => r.intersectsPoint(point));

		this.ndcPoint.x = (offsetX / this._container.clientWidth) * 2 - 1;
		this.ndcPoint.y = -(offsetY / this._container.clientHeight) * 2 + 1;

		const intersects = this.getThreeDIntersectsFromXY(this.ndcPoint);

		// first, lets grab the position off any mesh that gets intersected first
		const position = intersects[0]?.point;

		// Sort objects with depthTest=false to the front
		const alwaysOnTopIntersects: Intersection<Object3D<Object3DEventMap>>[] = [];
		const nonDepthTestIntersects: Intersection<Object3D<Object3DEventMap>>[] = [];

		const filteredIntersects = intersects.filter(i => i.object.name !== 'worldPlane');
		for (const intersect of filteredIntersects) {
			if (intersect.object instanceof Mesh && intersect.object.material.depthTest === false) {
				alwaysOnTopIntersects.push(intersect);
			} else {
				nonDepthTestIntersects.push(intersect);
			}
		}

		const firstHit = alwaysOnTopIntersects[0] ?? nonDepthTestIntersects[0] ?? undefined;

		// 3D geometry intersect
		let entity: All3DTypes | undefined;
		let intersected: All3DTypes | undefined;

		// grab first item and check if it's interactive
		if (firstHit != null) {
			// check for models
			entity = findModelParentEntity(firstHit.object, this.coreState.geometry3DMap);
			if (!entity) {
				// check for batched meshes
				const instanceIndex = firstHit?.instanceId ?? firstHit?.batchId;
				entity =
					instanceIndex != null && firstHit?.object.userData.entities
						? this.coreState.geometry3DMap.get(firstHit?.object.userData.entities[instanceIndex])
						: this.coreState.geometry3DMap.get(firstHit?.object.userData.entityId);
			}

			// ensure entity is interactive
			if (entity?.components[Geometry3DComponents.Interaction]) {
				intersected = entity;
			}
		}

		// intersect with group containers
		const containerIntersects = getUniqueIntersectedGroupContainers(filteredIntersects) as GroupContainerObject3D[];

		return {
			entity2D:
				intersection?.userData?.entityId === 'watermark'
					? ({ id: 'watermark' } as Geometry2D)
					: this.coreState.geometry2DMap.get(intersection?.userData.entityId),
			entity3D: intersected as Geometry3D | undefined,
			position: position,
			groupContainers: containerIntersects.length > 0 ? containerIntersects : undefined,
		};
	}

	private detect3DEntityHover(entity: Geometry3D | undefined): boolean {
		if (this.state.lastHover !== entity) {
			if (this.state.lastHover) {
				updateHover(this.state.lastHover, false, this.coreState);
			}
			if (entity) {
				updateHover(entity, true, this.coreState);
			}
		}
		const dirty = this.state.hovered3DEntity !== entity;

		return dirty;
	}

	private detect3DContainerHover(groupContainers: GroupContainerObject3D[] | undefined) {
		const dirty = !arraysEqual(this.state.hovered3DContainers, groupContainers);

		return dirty;
	}

	// update interaction state based on the poiner events
	private updateInteractionStateIfPanning(event: PointerEvent) {
		if (this.state.interaction.mouseDownStart) {
			this.state.interaction.distanceFromMouseDown = Math.sqrt(
				Math.pow(event.offsetX - this.state.interaction.mouseDownStart.offsetX, 2) +
					Math.pow(event.offsetY - this.state.interaction.mouseDownStart.offsetY, 2),
			);
			if (
				!this.state.interaction.isPanning &&
				this.state.interaction.distanceFromMouseDown > MOUSE_CLICK_DISTANCE_THRESHOLD
			) {
				this.state.interaction.isPanning = true;
			}
		}
	}

	// ensure to be called after raycaster is set
	getMouseRayIntersects() {
		return this.raycaster.intersectObjects([this.worldPlane], false);
	}

	destroy() {
		this._container.removeEventListener('pointerdown', this.onPointerDown, false);
		this._container.removeEventListener('pointermove', this.onPointerMoveRaf, false);
		this._container.removeEventListener('pointerup', this.onPointerUp, false);
		this._container.removeEventListener('pointercancel', this.onPointerUp, false);
	}

	// SECTION: DEBUG
	debugPanel: HTMLDivElement | undefined;
	enableDebug() {
		const panel = document.createElement('div');
		panel.style.position = 'absolute';
		panel.style.top = '0';
		panel.style.right = '0';
		panel.style.backgroundColor = 'white';
		panel.style.padding = '10px';
		document.body.append(panel);
		panel.id = 'interaction-debug-panel';
		this.debugPanel = panel;

		this.on('doubletap-with-onefinger', () => {
			this.queueDebugMessage('doubletap-with-onefinger', '#ffadad');
		});

		this.on('singletap-with-twofinger', () => {
			this.queueDebugMessage('singletap-with-twofinger', '#ffd6a5');
		});

		this.on('click', payload => {
			this.queueDebugMessage(
				`CLICK \n3D: ${payload.entity3D?.id} \n2D: ${payload.entity2D?.id} \nPos: ${payload.position
					?.toArray()
					.join(', ')}`,
				'#fdffb6',
			);
		});
		this.on('hover', payload => {
			this.queueDebugMessage(
				`HOVER \n3D: ${payload.entity3D?.id} \n2D: ${payload.entity2D?.id} \nPos: ${payload.position
					?.toArray()
					.join(', ')}`,
				'#caffbf',
			);
		});
	}

	// expose for testing
	get _state() {
		return this.state;
	}

	private queueDebugMessage(msg: string, color: string) {
		const div = document.createElement('pre');
		div.style.maxWidth = '300px';
		div.style.transition = 'opacity 500ms cubic-bezier(0.4, 0, 0.2, 1)';
		div.style.backgroundColor = color;
		div.textContent = msg;
		this.debugPanel?.prepend(div);
		setTimeout(() => {
			div.style.opacity = '0';
			setTimeout(() => {
				div.remove();
			}, 500);
		}, 2500);
	}

	/**
	 * Get the current cursor type determined by the interaction.
	 */
	getCursor() {
		return this.cursor;
	}
}

function updateHover(entity: Geometry3D, hover: boolean, coreState: RendererState) {
	const interactionComponent = entity.components[Geometry3DComponents.Interaction];
	// If hover is true, we don't want to override it because this has been explicitly set via setState
	if (interactionComponent && interactionComponent.hover !== true) {
		interactionComponent.hover = hover ? 'user-interaction' : false;
		interactionComponent.dirty = true;
	}
	/**
	 * Handles text mesh color update when polygon is hovered.
	 * Note: styleComponent color and hoverColor states are set by developer. The final material color is determined by color, hovercolor, globalHoverColor and hoverByPolygon state
	 */
	const labelTextEntityId = entity.components[Geometry3DComponents.Mesh].textMesh?.userData.entityId;
	if (!labelTextEntityId) return;
	const textEntity = coreState.geometry3DMap.get(labelTextEntityId);
	if (textEntity?.type === 'text3d') {
		textEntity.components[Geometry3DComponents.Style].hoverByPolygon = hover;
		textEntity.components[Geometry3DComponents.Style].dirty = true;
	}
}

function processCursor(intersected: boolean, isPanning: boolean, isMouseDown: boolean): CursorTypes {
	if (isPanning) return 'grabbing';
	if (intersected) return 'pointer';
	if (isMouseDown) return 'grabbing';

	return 'grab';
}

// export for testing
export function findModelParent(object: Object3D) {
	let itr = object;

	while (itr?.parent) {
		if (itr instanceof Scene) {
			return;
		}
		if (itr.userData.isSingleModel) {
			return itr;
		}
		itr = itr.parent;
	}

	return undefined;
}

function findModelParentEntity(object: Object3D, map: RendererState['geometry3DMap']) {
	const found = findModelParent(object);
	if (!found) return;
	const geometryGroup = map.get(found.userData.entityId);

	return geometryGroup;
}
