export const DESTINATION_COLOR = '#722ED1';
export const DEPARTURE_COLOR = '#1890FF';
export const DroppedPin = ({ type = 'departure', color = DEPARTURE_COLOR }) => {
	return `<div class="dropped-pin-wrapper">
			${type === 'departure' ? getDeparturePin(color) : getDestinationPin(color)}
		</div>`;
};

const getSize = (state: 'default' | 'selection' | 'navigation') => {
	switch (state) {
		case 'default':
			return 28;
		case 'selection':
			return 28;
		case 'navigation':
			return 32;
	}
};

export const ConnectionMarker = ({ type = 'stairs', color = DESTINATION_COLOR, direction = 'up' }) => {
	const size = getSize('default');
	const iconSize = size / 2 + 2;
	const hasDirection = direction && direction !== 'none';

	return `<div class="mappedin-connection-marker">
			<div class="connection-circle"
				role="button"
                style="background:${color}; width:${size}px; height:${size}px;"
			>
                ${getStyledIcon('connection-' + type + '-16x16', 'white', iconSize) || ''}
			</div>
			${
				hasDirection
					? `<div style="margin-left: -${size * 0.2}px;">
					<div class="bouncey">
						<div class="connection-circle"
							role="button"
							style="width: ${size}px; height: ${size}px;">
							<div class="${direction === 'up' ? '' : 'arrow--down'}">
                                <svg viewBox="0 0 16 16" style="fill: white; width: ${iconSize}px; height: ${iconSize}px">
                                    <path
                                        fillRule="evenodd"
                                        clipRule="evenodd"
                                        d="M8.00015 0.5C8.34685 0.5 8.67835 0.642318 8.91714 0.893677L15.6517 7.98267C15.8441 8.18525 15.8359 8.50548 15.6333 8.69793L14.533 9.7433C14.3304 9.93575 14.0101 9.92754 13.8177 9.72496L9.26497 4.93262V15.4249C9.26497 15.7043 9.03846 15.9308 8.75904 15.9308H7.24125C6.96184 15.9308 6.73532 15.7043 6.73532 15.4249V4.93262L2.1826 9.72496C1.99015 9.92754 1.66992 9.93575 1.46734 9.7433L0.366945 8.69793C0.164367 8.50548 0.156156 8.18525 0.348604 7.98267L7.08315 0.893677C7.32194 0.642318 7.65344 0.5 8.00015 0.5Z"
                                        stroke="none"
                                    />
                                </svg>
							</div>
						</div>
					</div>
				</div>`
					: ''
			}
		</div>`;
};
