import { PubSub } from '@packages/internal/common';
import type { All3DTypes, RendererState } from '../../renderer';
import { MeshComponent } from '../../components/mesh';
import { Geometry3DComponents } from '../../entities';
import { Box3, BoxGeometry, Mesh, MeshBasicMaterial, type PerspectiveCamera, Raycaster, Vector2 } from 'three';
import { GroupContainerComponents } from '../../entities/group-container';
import { FocusableComponent } from '../../components/focusable';
import { destroyObject3D } from '../../utils/object3d-destroy';

/**
 * This is the window after a camera move where geometry-in-focus can be updated. After this window, we assume
 * the camera has settled, and further renders will not change geometry-in-focus until the next camera move.
 * Otherwise, every render will update geometry-in-focus, even if the camera is not moving, and re-trigger
 * dynamic focus on the current floor stack.
 */
const CAMERA_MOVE_BUFFER_THRESHOLD_MS = 250;

export class GeometryInFocusSystem extends PubSub<{
	/**
	 * Returns the list of entities that are likely in focus, sorted by the weight of the raycast.
	 */
	'geometry-in-focus': string[];
}> {
	cameraDirty = true;
	focusablesDirty = true;
	private state: RendererState;
	private camera: PerspectiveCamera;
	private debugEl: HTMLElement | null = null;
	private raycasters: [Raycaster, cameraCoordinate: Vector2, weight: number][] = [];
	private collisions: Map<string, number> = new Map();
	private focusableEntities: Set<Mesh> = new Set();
	private debugRaycasters = false;

	constructor(state: RendererState, camera: PerspectiveCamera) {
		super();
		this.state = state;
		this.camera = camera;
		this.buildRaycasters();
	}

	resize() {
		this.buildRaycasters();
	}

	/**
	 * Build 5 raycasters, one for the center of the screen, and one for each corner of the screen.
	 * the center one has the highest weight, and the corners have the lowest weight.
	 * this allows us to detect focus closer to the center of the screen more easily.
	 */
	private buildRaycasters() {
		if (this.debugEl) {
			this.debugEl.remove();
		}
		const center = new Vector2(this.state.canvasWidth / 2, this.state.canvasHeight / 2);
		const topLeft = new Vector2(center.x / 1.2, center.y / 1.2);
		const topRight = new Vector2(center.x + (center.x - topLeft.x), topLeft.y);
		const bottomLeft = new Vector2(topLeft.x, center.y + (center.y - topLeft.y));
		const bottomRight = new Vector2(topRight.x, bottomLeft.y);
		const centerTop = new Vector2(center.x, center.y / 1.5);
		const centerBottom = new Vector2(center.x, center.y + (center.y - centerTop.y));

		this.debugEl = document.createElement('div');
		Object.assign(this.debugEl.style, {
			position: 'absolute',
			top: '0',
			left: '0',
			width: '100%',
			height: '100%',
			pointerEvents: 'none',
		});
		document.body.append(this.debugEl);

		this.raycasters = [center, centerTop, centerBottom, topLeft, topRight, bottomLeft, bottomRight].map(
			(coordXY, idx) => {
				if (this.debugRaycasters) {
					const el = document.createElement('div');
					el.style.position = 'absolute';
					el.style.top = coordXY.y + 'px';
					el.style.left = coordXY.x + 'px';
					el.style.width = '10px';
					el.style.height = '10px';
					el.style.backgroundColor = 'red';
					this.debugEl!.append(el);
				}

				const _x = (coordXY.x / this.state.canvasWidth) * 2 - 1;
				const _y = -(coordXY.y / this.state.canvasHeight) * 2 + 1;
				const coordinateVector = new Vector2(_x, _y);
				const raycaster = new Raycaster();
				raycaster.setFromCamera(coordinateVector, this.camera);

				return [raycaster, coordinateVector, idx === 0 ? 10 : idx < 7 ? 2 : 1];
			},
		);
	}

	private updateFocusableEntities() {
		this.focusableEntities.clear();
		let meshComponent: All3DTypes['components'][Geometry3DComponents.Mesh];
		let focusableComponent: All3DTypes['components'][Geometry3DComponents.Focusable];
		let entity: All3DTypes | undefined;
		for (const entityId of this.state.geometry3DIdsInScene) {
			entity = this.state.geometry3DMap.get(entityId);
			if (entity == null) {
				continue;
			}
			if (
				entity.type === 'group-container' &&
				entity.components[GroupContainerComponents.Focusable] !== undefined &&
				entity.components[GroupContainerComponents.Focusable] instanceof FocusableComponent
			) {
				focusableComponent = entity.components[GroupContainerComponents.Focusable];
				if (focusableComponent.dirty === true || focusableComponent.focusMesh == null) {
					// For group containers that are focusable let's create a mesh of the bounding box

					if (focusableComponent.focusMesh) {
						// destroy the focusMesh
						destroyObject3D(focusableComponent.focusMesh);
					}
					const box = new Box3();

					box.setFromObject(entity);

					// Validate bounding box dimensions
					const width = box.max.x - box.min.x;
					const height = box.max.y - box.min.y;
					const depth = box.max.z - box.min.z;

					if (
						!isFinite(width) ||
						!isFinite(height) ||
						!isFinite(depth) ||
						isNaN(width) ||
						isNaN(height) ||
						isNaN(depth) ||
						width <= 0 ||
						height <= 0 ||
						depth <= 0
					) {
						continue;
					}

					const geometry = new BoxGeometry(width, height, depth);
					const matProperties: any = {
						transparent: true,
						opacity: this.debugRaycasters ? 1 : 0,
						visible: this.debugRaycasters,
					};
					if (this.debugRaycasters) {
						const colors = ['red', 'green', 'blue', 'yellow', 'purple', 'orange', 'pink', 'brown', 'gray', 'black'];
						matProperties.color = colors[Math.floor(Math.random() * colors.length)];
					}
					const material = new MeshBasicMaterial(matProperties);
					const focusMesh = new Mesh(geometry, material);
					focusMesh.position.set((box.min.x + box.max.x) / 2, (box.min.y + box.max.y) / 2, 0);
					focusMesh.userData = {
						entityId: entity.userData.entityId,
					};
					focusableComponent.focusMesh = focusMesh;
					entity.add(focusMesh);
					entity.components[GroupContainerComponents.Focusable].dirty = false;
				}
				this.focusableEntities.add(focusableComponent.focusMesh);
			} else if (
				entity.type === 'geometry' &&
				entity.components[Geometry3DComponents.Focusable] &&
				entity.components[Geometry3DComponents.Focusable] instanceof FocusableComponent
			) {
				meshComponent = entity.components[Geometry3DComponents.Mesh];
				focusableComponent = entity.components[Geometry3DComponents.Focusable];
				if (
					meshComponent instanceof MeshComponent &&
					meshComponent.geometry != null &&
					(focusableComponent.dirty === true || focusableComponent.focusMesh == null)
				) {
					// batched geometry behaves differently than regular geometry, and we are not able to raycast against it when it's invisible, which causes issues for dynamic focus
					// so let's create a focus mesh that can be used for focus detection
					if (focusableComponent.focusMesh == null) {
						const focusMesh = new Mesh(meshComponent.geometry);
						focusMesh.userData = {
							entityId: entity.id,
						};
						focusableComponent.focusMesh = focusMesh;
					}
					this.focusableEntities.add(focusableComponent.focusMesh);
				}
			}
		}
		this.focusablesDirty = false;
	}

	#animationFrame?: number = undefined;

	update = (cameraIsMoving = false, cameraStoppedMovingTime = Date.now()) => {
		if (this.focusablesDirty) {
			this.updateFocusableEntities();
		}

		if (cameraIsMoving) return;

		if (Date.now() - cameraStoppedMovingTime > CAMERA_MOVE_BUFFER_THRESHOLD_MS) {
			return;
		}

		if (this.#animationFrame) {
			cancelAnimationFrame(this.#animationFrame);
		}

		this.#animationFrame = requestAnimationFrame(() => {
			this.updateRaf();
		});
	};

	updateRaf() {
		if (this.focusableEntities.size === 0) {
			this.publish('geometry-in-focus', []);

			return;
		}

		if (!this.cameraDirty) {
			return;
		}

		this.raycast();
	}

	raycast() {
		this.collisions.clear();

		for (const [raycaster, coordinateVector] of this.raycasters) {
			raycaster.setFromCamera(coordinateVector, this.camera);
		}

		// raycast against all focusable entities, and accumulate the weight of each raycast per entity
		for (const [raycaster, _, weight] of this.raycasters) {
			const cols = raycaster.intersectObjects(Array.from(this.focusableEntities.values()));
			cols.forEach(collision => {
				if (collision?.object != null && 'isMesh' in collision.object && collision?.object.isMesh) {
					const entityId = collision.object.userData.entityId;
					const currentWeight = this.collisions.get(entityId) ?? 0;
					this.collisions.set(entityId, currentWeight + weight);
				}
			});
		}

		// sort the entities by weight, the most likely to be in focus is first
		const sortedEntities = Array.from(this.collisions, ([entityId, weight]) => ({ entityId, weight }))
			.sort((a, b) => b.weight - a.weight)
			.map(({ entityId }) => entityId);

		this.publish('geometry-in-focus', sortedEntities);

		this.cameraDirty = false;
	}

	showRaycasters() {
		this.debugRaycasters = true;
		this.buildRaycasters();
	}

	hideRaycasters() {
		this.debugRaycasters = false;
		this.buildRaycasters();
	}

	destroy() {
		if (this.#animationFrame) {
			cancelAnimationFrame(this.#animationFrame);
		}
		if (this.debugEl) {
			this.debugEl.remove();
		}
		this.collisions.clear();
		this.focusableEntities.clear();
		this.raycasters = [];
	}
}
