import { Geometry3DComponents } from '../../entities';
import type { EntityTypes, RendererState } from '../../renderer';
import type { ClippingPlaneComponent } from '../../components/clipping-plane';
import type { ModelComponent } from '../../components/model';
import {
	AlwaysStencilFunc,
	BackSide,
	type BufferGeometry,
	DecrementWrapStencilOp,
	FrontSide,
	Group,
	IncrementWrapStencilOp,
	Mesh,
	MeshBasicMaterial,
	MeshStandardMaterial,
	NotEqualStencilFunc,
	Plane,
	PlaneGeometry,
	ReplaceStencilOp,
	Vector3,
} from 'three';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils.js';

/**
 * This system is responsible for creating the clipping plane and the hole fill mesh. From: https://threejs.org/examples/#webgl_clipping_stencil
 */

// this is the max size of the plane geometry to be used as the hole fill mesh, ie this should be as big as the cross section of the model from the top
const MAX_PLANE_SIZE = 500;

export class ClippingPlaneSystem {
	rendererState: RendererState;
	dirty = false;
	constructor(rendererState: RendererState) {
		this.rendererState = rendererState;
	}

	update() {
		let entity: EntityTypes | undefined;
		let clippingPlaneComponent: ClippingPlaneComponent | undefined;
		let meshComponent: ModelComponent | undefined;
		for (const entityId of this.rendererState.geometry3DIdsInScene) {
			entity = this.rendererState.geometry3DMap.get(entityId);
			if (entity?.type === 'model' && entity.components[Geometry3DComponents.ClippingPlane] != null) {
				meshComponent = entity.components[Geometry3DComponents.Mesh] as ModelComponent;
				clippingPlaneComponent = entity.components[Geometry3DComponents.ClippingPlane] as ClippingPlaneComponent;

				if (
					clippingPlaneComponent.dirty &&
					meshComponent.mesh != null &&
					clippingPlaneComponent?.verticalOffset !== Infinity
				) {
					if (clippingPlaneComponent.plane == null) {
						clippingPlaneComponent.plane = new Plane(new Vector3(0, 0, -1), clippingPlaneComponent.verticalOffset);
						this.process(meshComponent, clippingPlaneComponent);
					}

					if (clippingPlaneComponent.plane.constant !== clippingPlaneComponent.verticalOffset) {
						clippingPlaneComponent.plane.constant = clippingPlaneComponent.verticalOffset;
					}

					if (
						clippingPlaneComponent.topColor != null &&
						clippingPlaneComponent.holeFillMesh?.material instanceof MeshStandardMaterial
					) {
						clippingPlaneComponent.holeFillMesh.material.color.set(clippingPlaneComponent.topColor);
					}

					if (clippingPlaneComponent.topVisible != null && clippingPlaneComponent.holeFillMesh != null) {
						clippingPlaneComponent.holeFillMesh.visible = clippingPlaneComponent.topVisible;
					}

					clippingPlaneComponent.dirty = false;
				}

				if (
					clippingPlaneComponent.plane != null &&
					clippingPlaneComponent.holeFillMesh != null &&
					clippingPlaneComponent.topVisible === true
				) {
					const plane = clippingPlaneComponent.plane;
					const po = clippingPlaneComponent.holeFillMesh;
					plane.coplanarPoint(po.position);
					po.lookAt(po.position.x - plane.normal.x, po.position.y - plane.normal.y, po.position.z - plane.normal.z);
				}
			}
		}
	}

	private process(meshComponent: ModelComponent, clippingPlaneComponent: ClippingPlaneComponent) {
		if (meshComponent.mesh == null) {
			return;
		}
		const parent = meshComponent.mesh.parent!;

		const geometries: BufferGeometry[] = [];
		// we need to create a merged geometry of all the vertices of this nested mesh
		meshComponent.mesh.traverse(child => {
			if (child instanceof Mesh) {
				// Clone the geometry to avoid modifying the original
				const clonedGeometry = child.geometry.clone() as BufferGeometry;

				// Apply the child mesh's world matrix to preserve position, rotation, and scale
				child.updateWorldMatrix(true, false);
				clonedGeometry.applyMatrix4(child.matrixWorld);

				geometries.push(clonedGeometry);
				clonedGeometry.computeVertexNormals();
				child.material.clippingPlanes = [clippingPlaneComponent.plane!];
				child.material.transparent = false;
			}
		});

		// we need to merge the geometries to create a single geometry that can be used for the hole fill mesh
		const mergedGeometry = BufferGeometryUtils.mergeGeometries(geometries);

		const mainGroup = new Group();
		const clippedObjectAndStencilGroup = new Group();

		const holeFillGroup = this.createHoleFillGeometry(mergedGeometry, clippingPlaneComponent.plane!, 1);
		holeFillGroup.name = 'hole-fill-group';
		const holeFillMaterial = new MeshStandardMaterial({
			color: clippingPlaneComponent.topColor ?? '#E67B5B',
			clippingPlanes: [],

			stencilWrite: true,
			stencilFunc: NotEqualStencilFunc,
			stencilFail: ReplaceStencilOp,
			stencilZFail: ReplaceStencilOp,
			stencilZPass: ReplaceStencilOp,
		});

		const planeGeom = new PlaneGeometry(MAX_PLANE_SIZE, MAX_PLANE_SIZE);
		const holeFillMesh = new Mesh(planeGeom, holeFillMaterial);
		holeFillMesh.name = 'hole-fill-mesh';
		clippingPlaneComponent.holeFillMesh = holeFillMesh;

		holeFillMesh.renderOrder = 1.1;
		clippedObjectAndStencilGroup.add(holeFillGroup);
		clippedObjectAndStencilGroup.add(meshComponent.mesh);

		mainGroup.add(clippedObjectAndStencilGroup);
		mainGroup.add(holeFillMesh);

		parent?.add(mainGroup);
		meshComponent.mesh = mainGroup;
	}

	private createHoleFillGeometry(geometry: BufferGeometry, plane: Plane, renderOrder = 0) {
		const group = new Group();
		const baseMat = new MeshBasicMaterial();
		baseMat.depthWrite = false;
		baseMat.depthTest = false;
		baseMat.colorWrite = false;
		baseMat.stencilWrite = true;
		baseMat.stencilFunc = AlwaysStencilFunc;

		// back faces
		const mat0 = baseMat.clone();
		mat0.side = BackSide;
		mat0.clippingPlanes = [plane];
		mat0.stencilFail = IncrementWrapStencilOp;
		mat0.stencilZFail = IncrementWrapStencilOp;
		mat0.stencilZPass = IncrementWrapStencilOp;

		const mesh0 = new Mesh(geometry, mat0);
		mesh0.renderOrder = renderOrder;
		group.add(mesh0);

		// front faces
		const mat1 = baseMat.clone();
		mat1.side = FrontSide;
		mat1.clippingPlanes = [plane];
		mat1.stencilFail = DecrementWrapStencilOp;
		mat1.stencilZFail = DecrementWrapStencilOp;
		mat1.stencilZPass = DecrementWrapStencilOp;

		const mesh1 = new Mesh(geometry, mat1);
		mesh1.renderOrder = renderOrder;

		group.add(mesh1);

		return group;
	}
}
