import type { ImageState } from '@mappedin/core-sdk';
import { z } from 'zod';
import { Coordinate } from '../map-data-objects';
import type { Complete } from '@packages/internal/common/type-utils';

/**
 * Defines the state for an image when its appearance is updated.
 */
export const IMAGE_STATE_TYPE = 'image3d';

export const imageStateSchema = z.object({
	type: z.literal(IMAGE_STATE_TYPE).default(IMAGE_STATE_TYPE),
	/**
	 * Whether the image is visible.
	 * @default true
	 */
	visible: z.boolean().default(true),
	/**
	 * Opacity of the image.
	 * @default 1
	 */
	opacity: z.number().min(0).max(1).default(1),
	/**
	 * Coordinate position of the image.
	 */
	position: z.custom<Coordinate>(val => val instanceof Coordinate, {
		error: 'Must be a Coordinate instance',
	}),
	/**
	 * Width of the image in meters.
	 * @default 1
	 */
	width: z.number().default(1),
	/**
	 * Height of the image in meters.
	 * @default 1
	 */
	height: z.number().default(1),
	/**
	 * Rotation of the image in degrees clockwise from North. 0 degrees is North, 90 degrees is East, 180 degrees is South, and 270 degrees is West.
	 *
	 * @default 0
	 */
	rotation: z.number().default(0),
	/**
	 * Vertical offset of the image in meters.
	 *
	 * @default 0
	 */
	verticalOffset: z.number().default(0),
	/**
	 * Attempt to keep the image facing the camera as much as possible
	 *
	 * @default false
	 */
	flipImagesToFaceCamera: z.boolean().default(false),
});
export const imageStateSchemaPartial = imageStateSchema.partial();

export type TImage3DState = z.infer<typeof imageStateSchema>;
export type TImage3DUpdateState = z.infer<typeof imageStateSchemaPartial>;

export function getImageState(state: ImageState): TImage3DState {
	const { position } = state;
	return {
		type: IMAGE_STATE_TYPE,
		visible: state.visible ?? true,
		opacity: state.opacity ?? 1,
		position: new Coordinate({
			latitude: position?.[1] ?? 0,
			longitude: position?.[0] ?? 0,
			verticalOffset: position?.[2],
		}),
		width: state.width ?? 1,
		height: state.height ?? 1,
		rotation: state.rotation ?? 0,
		verticalOffset: state.verticalOffset ?? 0,
		flipImagesToFaceCamera: state.flipImageToFaceCamera ?? false,
	} satisfies Complete<TImage3DState>;
}
