import type { Position } from 'geojson';

/**
 * Calculates the angle between two geographic coordinates.
 *
 * @param {Position} position1 - The first point's longitude and latitude as [longitude, latitude].
 * @param {Position} position2 - The second point's longitude and latitude as [longitude, latitude].
 * @hidden
 *
 * @return {number} The angle in radians, calculated clockwise from the north between the two points specified.
 */
export function getAngle([point1Lon, point1Lat]: Position, [point2Lon, point2Lat]: Position): number {
	const xDelta = point1Lat - point2Lat;
	const yDelta = point1Lon - point2Lon;

	// We need to add 3 * PI to the result to make sure the angle is positive
	return (Math.atan2(yDelta, xDelta) + Math.PI * 3) % (Math.PI * 2); // radians;
}
