import { ALWAYS_VISIBLE_RANK_INDEX } from '../../utils/collision-ranking-tier';
import type { TSerializedCollider } from './collider-processor';
import type { PackedBBoxes, PackedCollider, PackedMessage, TMessage } from './system';

export function throttle(func, wait, options) {
	let context;
	let args;
	let result;
	let timeout = null;
	let previous = 0;
	if (!options) options = {};
	let later = function () {
		previous = options.leading === false ? 0 : Date.now();
		timeout = null;
		result = func.apply(context, args);
		if (!timeout) context = args = null;
	};

	return function () {
		let now = Date.now();
		if (!previous && options.leading === false) previous = now;
		let remaining = wait - (now - previous);
		context = globalThis;
		// eslint-disable-next-line
		args = arguments;
		if (remaining <= 0 || remaining > wait) {
			if (timeout) {
				clearTimeout(timeout);
				timeout = null;
			}
			previous = now;
			result = func.apply(context, args);
			if (!timeout) context = args = null;
		} else if (!timeout && options.trailing !== false) {
			// @ts-ignore
			timeout = setTimeout(later, remaining);
		}

		return result;
	};
}

export function debounce(func, wait, immediate) {
	let timeout;

	return function () {
		let context = globalThis;
		// eslint-disable-next-line
		let args = arguments;
		let later = function () {
			timeout = null;
			if (!immediate) func.apply(context, args);
		};
		let callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) func.apply(context, args);
	};
}

export function packCollider(collider: TSerializedCollider): PackedCollider {
	const packedBBoxes: PackedBBoxes = collider.bboxes.map(bbox => [
		bbox.boundingBox[0],
		bbox.boundingBox[1],
		bbox.boundingBox[2],
		bbox.boundingBox[3],
		bbox.index,
	]);

	return [
		packedBBoxes,
		collider.enabledAndVisible ? 1 : 0,
		collider.rank === ALWAYS_VISIBLE_RANK_INDEX ? 1 : 0,
		collider.x,
		collider.y,
		collider.visible && collider.bboxes.length === 1 ? 1 : 0,
		collider.shouldCollideWithScreenEdges ? 1 : 0,
	];
}

// Function to pack the data
export function packMessage(message: TMessage): PackedMessage {
	const packedColliders: PackedCollider[] = message.colliders.map(packCollider);

	return [
		packedColliders,
		message.devicePixelRatio,
		message.totalHeight,
		message.totalWidth,
		message.watermarkWidth,
		message.watermarkHeight,
		message.watermarkPosition,
	];
}

export function unpackMessage(packedMessage: PackedMessage): TMessage {
	const colliders: TSerializedCollider[] = packedMessage[0].map(packedCollider => {
		const bboxes = packedCollider[0].map(bbox => ({
			index: bbox[4],
			boundingBox: [bbox[0], bbox[1], bbox[2], bbox[3]] as [number, number, number, number],
		}));

		return {
			enabledAndVisible: packedCollider[1] === 1,
			rank: packedCollider[2] === 1 ? ALWAYS_VISIBLE_RANK_INDEX : 0,
			x: packedCollider[3],
			y: packedCollider[4],
			shouldCollideWithScreenEdges: packedCollider[5] === 1,
			bboxes: bboxes,
			lockedToStrategyIndex: packedCollider[6],
		};
	});

	return {
		msgId: '-1',
		totalWidth: packedMessage[3],
		totalHeight: packedMessage[2],
		watermarkWidth: packedMessage[4],
		watermarkHeight: packedMessage[5],
		watermarkPosition: packedMessage[6],
		colliders: colliders,
		devicePixelRatio: packedMessage[1],
	};
}
