import type { BufferGeometry } from 'three';
import type { GeometryGroupObject3D } from '../../entities/geometry-group';
import type { RendererState } from '../../renderer';
import { Geometry3DComponents } from '../../entities';

export function hasOutline(entity: GeometryGroupObject3D) {
	return entity.children.some(child => child.userData.isOutline);
}

export function findOutline(entity: GeometryGroupObject3D) {
	return entity.children.find(child => child.userData.isOutline);
}

// TODO: in the future if we want to turn outlines on and off per object, we need to fix this
export function needsOutline(entity: GeometryGroupObject3D, state: RendererState) {
	const firstChildId = entity.userData.entities3D.values().next().value;
	if (!firstChildId) return false;
	return state.geometry3DMap.get(firstChildId)?.components[Geometry3DComponents.Outline] !== undefined;
}

export function updateBufferGeometryHeight(
	bufferGeometry: BufferGeometry,
	ranges: { start: number; count: number },
	height: number,
	altitude: number,
) {
	const positionAttribute = bufferGeometry.getAttribute('position');
	const positions = positionAttribute.array;
	// Find the maximum Z value in this entity's range
	let maxZ = -Infinity;
	for (let i = 0; i < ranges.count; i++) {
		const vertexIndex = ranges.start + i;
		const baseIndex = vertexIndex * 3;
		const z = positions[baseIndex + 2];
		maxZ = Math.max(maxZ, z);
	}

	// For each vertex in this entity's range
	for (let i = 0; i < ranges.count; i++) {
		const vertexIndex = ranges.start + i;
		const baseIndex = vertexIndex * 3;
		const z = positions[baseIndex + 2];

		// If this vertex is at the maximum Z, it's a top vertex
		if (Math.abs(z - maxZ) < 0.0001) {
			// make sure the outline is at least 2mm above the bottom of the geometry so we can use the top calculation
			positions[baseIndex + 2] = Math.max(height + altitude, 0.0002 + altitude);
		} else {
			// Apply altitude to all other vertices
			positions[baseIndex + 2] = altitude;
		}
	}

	bufferGeometry.attributes.position.needsUpdate = true;
}
