import type { RendererCore } from '../..';
import type { CustomGeometryComponent } from '../../components/custom';
import type { StyleComponent } from '../../components/styles/style';
import { Geometry3DComponents } from '../../entities';
import type { RendererState } from '../../renderer';

/**
 * System that renders and updates custom geometry entities. For every custom geometry entity, this calls the builder's
 * setup method once (when it is visible) and then the update method every frame.
 */
export class CustomGeometrySystem {
	private state: RendererState;
	private convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'];

	constructor(state: RendererState, convertTo3DMapPosition: RendererCore['convertTo3DMapPosition']) {
		this.state = state;
		this.convertTo3DMapPosition = convertTo3DMapPosition;
	}

	update() {
		let component: CustomGeometryComponent;
		let style: StyleComponent;
		for (const entityId of this.state.geometry3DIdsInScene) {
			const entity = this.state.geometry3DMap.get(entityId);
			if (entity?.type === 'custom-geometry') {
				component = entity.components[Geometry3DComponents.Mesh] as CustomGeometryComponent;
				style = entity.components[Geometry3DComponents.Style] as StyleComponent;
				// first, build
				if (component.dirty && style.visible !== false) {
					component.position.copy(
						this.convertTo3DMapPosition(
							component.feature.geometry.coordinates[1],
							component.feature.geometry.coordinates[0],
							style.altitude,
						),
					);
					component.builder.setup(component.mesh);
					component.mesh.traverse(child => {
						child.userData = {
							...child.userData, // preserve userData if specified by user. this is used by bluedot to keep track floorId
							entityId,
							type: 'custom-geometry',
						};
					});
					component.dirty = false;
				}
				// next, update
				if (style.visible !== false) {
					component.builder.update(component.mesh);
				}
			}
		}
	}
}
