/**
 * Function to convert radians to degrees and adjust the range from -180 to 180.
 * @param radians - The angle in radians.
 * @returns The angle in degrees, ranging from -180 to 180.
 */
export function radToDegNormalized(radians: number): number {
	let degrees = radians * (180 / Math.PI);
	degrees = degrees % 360;
	if (degrees > 180) {
		degrees -= 360;
	} else if (degrees < -180) {
		degrees += 360;
	}

	return Object.is(degrees, -0) ? 0 : degrees;
}
