	CAMERA_CONSTANTS;

/** This must align with Maplibre's FOV */
const FIELD_OF_VIEW = MathUtils.radToDeg(0.6435011087932844); // ~ 37 degrees

export type Systems = {
	cameraSystem: CameraSystem;
	panBoundsSystem: PanBoundsSystem;
	collisionSystem: CollisionSystem;
	interactionSystem: InteractionSystem;
	meshOptimizationSystem: MeshCreationAndOptimizationSystem;
	domVisiblitySystem: DOMVisibilitySystem;
	domMutationSystem: DOMResizeSystem;
	domDrawSystem: DOMDrawSystem;
	twoDProjectionSystem: TwoDProjectionSystem;
	twoDDrawSystem: TwoDDrawSystem;
	drawSystem: DrawSystem;
	twoDVisiblitySystem: TwoDVisibilitySystem;
	renderSystem: RenderSystem;
	watermarkSystem: WatermarkSystem;
	htmlControlsSystem: HTMLControlsSystem;
	exporterSystem: ExporterSystem;
	customGeometrySystem: CustomGeometrySystem;
	outlineInterpolationSystem: OutlineInterpolationSystem;
	outdoorLayersSystem: OutdoorLayers;
	pathSystem: PathSystem;
	imageSystem: ImageSystem;
	geometryInFocusSystem: GeometryInFocusSystem;
	meshDetachmentSystem: MeshDetachmentSystem;
	meshModificationSystem: MeshModificationSystem;
	text3DSystem: Text3DSystem;
	occlusion2DSystem: Occlusion2DSystem;
	geometryDisposalSystem: GeometryDisposalSystem;
	textureSystem: TextureSystem;
	borderSystem: BorderSystem;
	renderOrderSystem: RenderOrderSystem;
	clippingPlaneSystem: ClippingPlaneSystem;
};

export type MapViewState = {
	readonly type: 'map-view';
	hoverColor: string;
	text3dHoverColor: string;
};

class Core extends PubSub<MapEvent> {
	options: Omit<RendererCoreOptions, 'outdoorView'>;
	container: HTMLElement;
	Debug: Debug;
	#scene?: Scene;
	#cameraObject: PerspectiveCamera;
	#cachedBasicEnvMapPromise: Promise<Texture> | undefined;
	#viewCamera: ThreeCamera;
	/**
	 * The view camera is used for projecting both the outdoor map and our scene
	 */
	get viewCamera() {
		return this.#viewCamera;
	}
	#resolution: Vector2;
	#worldPlane: Mesh<PlaneGeometry, MeshBasicMaterial>;
	#resizeObserver?: ResizeObserver;
	#cameraOrbit: Object3D;
	get canvasWidth() {
		return this.#state.canvasWidth;
	}
	get canvasHeight() {
		return this.#state.canvasHeight;
	}
	rendererDomElement: HTMLCanvasElement;
	/**
	 * Returns pixel ratio of the renderer
	 */
	get resolutionScale(): number {
		// make sure we use maplibre's clamped pixel ratio. Do not ask me why, but this fixes the issue with ultra wide monitors
		// TODO: figure out what clamped pixel ratio is and why it's needed
		return this.map?._getClampedPixelRatio(this.canvasWidth, this.canvasHeight) ?? getPixelRatio();
	}

	/**
	 * Sets pixel ratio of the renderer. In interleaved mode, maplibre will decide what pixel ratio to use based on canvas dimensions
	 * and `this.resolutionScale` will reflect that.
	 */
	#setResolutionScale(value: number) {
		if (this.#mode !== 'outdoors-overlay') {
			this.map?.setPixelRatio(value);
		}
		this.#state.pixelRatio = this.resolutionScale;
	}

	get aspect() {
		return this.canvasWidth / this.canvasHeight;
	}

	#state: RendererState = {
		geometry3DMap: new Map(),
		geometry2DMap: new Map(),
		geometry2DIdsInScene: new Set(),
		geometry3DIdsInScene: new Set(),
		geometryGroupIdsToLoad: new Set(),
		entityScene: new GroupContainerObject3D('__scene'),
		hoverColor: '#f6efff',
		text3dHoverColor: 'white',
		insetsPadding: DEFAULT_INSET_PADDING,
		shouldMeasureCanvas: true,
		pixelRatio: this.resolutionScale,
		canvasWidth: 0,
		canvasHeight: 0,
		internalTweenGroup: new TweenGroup(),
		externalTweenGroup: new TweenGroup(),
		cameraObject: new PerspectiveCamera(),
		naturalBearing: 0,
		containerOffset: [0, 0],
		useStandaloneCamera: false,
	};

	/**
	 * MapLibre event listeners that the renderer is subscribed to.
	 */
	#mapLibreEventListeners: Record<keyof MapLibreMapEventType, ((...args: any[]) => void)[]> = {} as any;

	/**
	 * The abort controller for the renderer.
	 */
	#abortController: AbortController = new AbortController();

	// APIs
	camera: Camera;

	// DOM elements
	#collisionEngineContainerEl!: HTMLDivElement;
	#debugCanvas!: HTMLCanvasElement;
	#colliderCanvas!: HTMLCanvasElement;
	#twoDEntityScene!: Entity2DHTMLDivElementContainer;

	// Services
	#renderer: Renderer;

	// Systems
	#systems: Systems = {} as Systems;

	#matchMediaMinResolution: MediaQueryList = window.matchMedia('screen and (min-resolution: 2dppx)');

	map: MapLibreMap | undefined;

	#invalidate2DEntities = () => {
		if (this.aborted || !this.#systems?.renderSystem) {
			return;
		}
		this.#systems.renderSystem.twoDdirty = true;
	};

	#invalidate3DEntities = () => {
		if (this.aborted || !this.#systems?.renderSystem) {
			return;
		}
		this.#systems.renderSystem.threeDdirty = true;
	};

	#renderEntity2DContainer() {
		this.#collisionEngineContainerEl = document.createElement('div');
		this.#collisionEngineContainerEl.classList.add('mappedin__smart-collision-engine__container');
		this.#debugCanvas = document.createElement('canvas');
		this.#debugCanvas.classList.add('mappedin__smart-collision-engine-debug__container');
		this.container.appendChild(this.#collisionEngineContainerEl);
		this.#colliderCanvas = document.createElement('canvas');
		this.#colliderCanvas.classList.add('mappedin__smart-collision-engine__container');
		this.#colliderCanvas.width = this.#collisionEngineContainerEl.clientWidth * this.resolutionScale;
		this.#colliderCanvas.height = this.#collisionEngineContainerEl.clientHeight * this.resolutionScale;
		this.#colliderCanvas.style.width = this.#collisionEngineContainerEl.clientWidth + 'px';
		this.#colliderCanvas.style.height = this.#collisionEngineContainerEl.clientHeight + 'px';
		this.#collisionEngineContainerEl.appendChild(this.#debugCanvas);
		this.#collisionEngineContainerEl.appendChild(this.#colliderCanvas);
		this.#debugCanvas.width = this.#collisionEngineContainerEl.clientWidth;
		this.#debugCanvas.height = this.#collisionEngineContainerEl.clientHeight;
		this.#colliderCanvas.width = this.#collisionEngineContainerEl.clientWidth * this.resolutionScale;
		this.#colliderCanvas.height = this.#collisionEngineContainerEl.clientHeight * this.resolutionScale;
		this.#colliderCanvas.style.width = this.#collisionEngineContainerEl.clientWidth + 'px';
		this.#colliderCanvas.style.height = this.#collisionEngineContainerEl.clientHeight + 'px';
	}

	#renderEntityDOMContainer() {
		this.#twoDEntityScene = document.createElement('div') as Entity2DHTMLDivElementContainer;
		this.#twoDEntityScene.classList.add('mappedin-2d-entities');
		Object.assign(this.#twoDEntityScene.style, {
			position: 'absolute',
			top: '0',
			left: '0',
			bottom: '0',
			right: '0',
			pointerEvents: 'none',
			'transform-style': 'preserve-3d',
		});
		this.container.appendChild(this.#twoDEntityScene);
	}

	#resizeCollisionCanvas() {
		this.#colliderCanvas.width = this.#collisionEngineContainerEl.clientWidth * this.resolutionScale;
		this.#colliderCanvas.height = this.#collisionEngineContainerEl.clientHeight * this.resolutionScale;
		this.#colliderCanvas.style.width = this.#collisionEngineContainerEl.clientWidth + 'px';
		this.#colliderCanvas.style.height = this.#collisionEngineContainerEl.clientHeight + 'px';
		this.#debugCanvas.width = this.#collisionEngineContainerEl.clientWidth;
		this.#debugCanvas.height = this.#collisionEngineContainerEl.clientHeight;
		this.#colliderCanvas.width = this.#collisionEngineContainerEl.clientWidth * this.resolutionScale;
		this.#colliderCanvas.height = this.#collisionEngineContainerEl.clientHeight * this.resolutionScale;
		this.#colliderCanvas.style.width = this.#collisionEngineContainerEl.clientWidth + 'px';
		this.#colliderCanvas.style.height = this.#collisionEngineContainerEl.clientHeight + 'px';
		this.#systems.collisionSystem.collidersDirty = true;
	}

	#mode: RendererCoreOptions['mode'] = 'standalone';

	/**
	 * Returns the mode the Renderer is in.
	 */
	get mode() {
		return this.#mode;
	}
	/**
	 * @internal
	 */
	constructor(container: HTMLElement, options: RendererCoreOptions = {}) {
		super();

		this.#abortController = new AbortController();

		this.#state.useStandaloneCamera = options.useStandaloneCamera ?? false;
		this.#state.naturalBearing = options.naturalBearing ?? 0;
		this.convertTo3DMapPosition = this.convertTo3DMapPosition.bind(this);
		if (!container.clientWidth || !container.clientHeight) {
			throw new Error('[MappedinJS] Container width and height must be greater than 0');

			return;
		}

		this.convertTo3DMapPosition = this.convertTo3DMapPosition.bind(this);
		if ((options.mode === 'outdoors-interleaved' || options.mode === 'outdoors-overlay') && options.map) {
			this.#mode = options.mode;
			this.map = options.map;
		}
		this.#state.entityScene.addEventListener('childadded', () => {
			this.#invalidate3DEntities();
			this.#invalidate2DEntities();
		});
		this.#state.entityScene.addEventListener('childremoved', () => {
			this.#invalidate3DEntities();
			this.#invalidate2DEntities();
		});

		this.#state.geometry3DMap.set(this.#state.entityScene.userData.entityId, this.#state.entityScene);

		if (options.center) {
			this.#state.center = options.center ? [...options.center] : [0, 0];
		}
		this.options = options;
		this.container = container;
		this.#renderEntity2DContainer();
		this.#renderEntityDOMContainer();

		this.#systems.htmlControlsSystem = new HTMLControlsSystem(this.container, {
			outdoorEnabled: !!this.map,
		});
		if (this.#mode !== 'outdoors-overlay') {
			this.#systems.htmlControlsSystem.addAttributionControl(this.options.attribution);
		}

		if (!(window as any).MappedinDebug) {
			(window as any).MappedinDebug = {};
		}

		// Expose some things on the window object when running integration tests
		if (process.env.NODE_ENV === 'test') {
			(window as any).MappedinDebug.mapViewCoreInstance = this;
		}

		this.#renderer = new Renderer({
			antialias: typeof options.antialias === 'boolean' ? options.antialias : true,
			alpha: false,
			canvas: this.mode !== 'standalone' ? options.gl?.canvas : undefined,
			onWebGLContextCreationError: options.onWebGLContextCreationError,
			onWebGLContextLost: options.onWebGLContextLost,
			onWebGLContextRestored: event => {
				options.onWebGLContextRestored?.(event);
				this.#invalidate2DEntities();
				this.#invalidate3DEntities();
				this.render();
			},
			onWebGLRendererError: options.onWebGLRendererError,
			backgroundColor: options.backgroundColor || '#ffffff',
			backgroundAlpha: options.backgroundAlpha ?? 1,
		});

		// Our 3D scene. The different camera layers holds elements based on
		// their re-rendering frequency.
		this.#scene = new Scene();
		if (this.#mode !== 'standalone') {
			// TODO: replace this with a more robust solution
			this.#scene.add(maplibreFrustrumCullHack());
		}

		// (window as any).scene = this.scene;
		this.#scene.add(this.#state.entityScene);

		// Observe a scene and a renderer for the Three.js Developer Tools extension
		const __THREE_DEVTOOLS__ = window['__THREE_DEVTOOLS__'];
		if (process.env.NODE_ENV === 'dev' && typeof __THREE_DEVTOOLS__ !== 'undefined') {
			__THREE_DEVTOOLS__.dispatchEvent(new CustomEvent('observe', { detail: this.#scene }));
			__THREE_DEVTOOLS__.dispatchEvent(new CustomEvent('observe', { detail: this.#renderer }));
		}
		// The camera object has two other objects that act as joints. Camera orbit is what handles panning and rotation
		// cameraElevation is what controls the angle of the camera off the ground, and zoom is the distance between the camera and cameraElevation.
		// This makes it trivial to anchor the camera's pan motion to the map plane.
		this.#cameraObject = new PerspectiveCamera(FIELD_OF_VIEW, this.aspect, 50, 100000);
		this.#state.cameraObject = this.#cameraObject;
		// the view camera is used for projecting both the outdoor map and our scene
		// we should never touch the view camera
		this.#viewCamera = new ThreeCamera();

		this.#cameraObject.renderOrder = 999;
		let cameraElevation = new Object3D();
		cameraElevation.add(this.#cameraObject);

		this.#cameraOrbit = new Object3D();
		this.#cameraOrbit.add(cameraElevation);

		this.#scene.add(this.#cameraOrbit);

		this.#cameraObject.updateProjectionMatrix();
		this.#cameraObject.updateMatrixWorld(true);

		// @ts-ignore TODO: backwards compat, remove
		this.referenceMap = {
			scale: 1,
			latitude: 0,
			longitude: 0,
			getNorth: () => 0,
			georeference: [
				{
					target: {
						x: 0,
						y: 0,
					},
				},
				{
					target: {
						x: 0,
						y: 0,
					},
				},
				{
					target: {
						x: 0,
						y: 0,
					},
				},
				{
					target: {
						x: 0,
						y: 0,
					},
				},
			],
		};

		this.rendererDomElement = this.#renderer.domElement()!;
		this.#resolution = new Vector2();

		const worldPlaneGeometery = new PlaneGeometry(1000000, 1000000); // Should set this to be the map bounds later
		const worldPlaneMaterial = new MeshBasicMaterial({
			color: 0x000000,
			visible: false,
		});
		this.#worldPlane = new Mesh(worldPlaneGeometery, worldPlaneMaterial);
		this.#worldPlane.name = 'worldPlane';

		this.#scene.add(this.#worldPlane);

		if (!options.gl?.canvas) {
			// Set up all our events
			container.appendChild(this.rendererDomElement);
		}

		window.addEventListener('resize', this.#handleWindowResizeWithEvent, false);
		this.#matchMediaMinResolution.addEventListener('change', this.#handlePixelRatioChange);

		/**
		 * ResizeObserver is only supported on ~92% of browsers, with the notable
		 * exceptions being IE, early Edge versions and early Safari versions
		 * (<= 13.1 on desktop, <= 13.4 on mobile). Fortunately this isn't a deal-
		 * breaker if unsupported, so we can just skip it.
		 */
		if (
			typeof ResizeObserver === 'function' &&
			// [Anzor] ResizeObserver causes issues when running integration tests
			// needs investigation (the height just keeps growing, crashing the browser,
			// I've seen this behavior in a couple other places too)
			process.env?.NODE_ENV !== 'test'
		) {
			// https://stackoverflow.com/questions/49384120/resizeobserver-loop-limit-exceeded
			this.#resizeObserver = new ResizeObserver(
				throttle(a => {
					// this may be a browser bug, but the container dimensions are not updated
					// until the next tick, so we need to use the contentRect to get the correct dimensions
					this.#handleWindowResize(a[0].contentRect.width, a[0].contentRect.height);
				}, 50),
			);
			this.#resizeObserver?.observe(container);
		}

		container.classList.add('mappedin-map');
		// integration tests in iOS are headless, so we have to set fake
		// width and heights here to get it as close to the real thing as possible
		this.rendererDomElement.style.width = globalThis.MappedinDebug?.TEST_IOS ? '1080px' : '100%';
		this.rendererDomElement.style.height = globalThis.MappedinDebug?.TEST_IOS ? '1920px' : '100%';

		if (process.env.NODE_ENV === 'test') this.rendererDomElement.setAttribute('data-testid', 'mappedin-canvas');

		/**
		 * Maplibre adds a tabindex to the canvas because they have keyboard controls.
		 * Since our camera doesn't have keyboard controls, we need to remove it.
		 */
		if (this.mode !== 'outdoors-overlay' && this.rendererDomElement.tabIndex !== undefined) {
			this.rendererDomElement.removeAttribute('tabindex');
		}

		this.#state.canvasWidth = this.rendererDomElement.clientWidth;
		this.#state.canvasHeight = this.rendererDomElement.clientHeight;

		/**
		 * touch-action: manipulation will ensure that double tapping on the
		 * canvas will not result in a browser zoom
		 */
		this.rendererDomElement.style.touchAction = 'manipulation';

		let canvasWidth = this.canvasWidth;
		let canvasHeight = this.canvasHeight;

		// Create and enable the watermark
		this.#systems.watermarkSystem = new WatermarkSystem(this.#cameraObject, this.#state, options?.watermark);
		this.#systems.watermarkSystem.on('texture-loaded', this.render);

		/**
		 * We want to defer the intial canvas measurement until the first render,
		 * to ensure everything in Core has initialized
		 */
		this.#state.shouldMeasureCanvas = true;

		this.#state.pixelRatio = this.resolutionScale;

		this.#setBufferSize(
			Math.floor(canvasWidth * this.resolutionScale),
			Math.floor(canvasHeight * this.resolutionScale),
		);

		/**
		 * These light settings mirror what things look liked in the Mappedin CMS.
		 *
		 * The renderer.useLegacyLights has been set to false in three.js v.0.155.0.
		 * This means that light intensities are no longer internally scaled by factor PI.
		 * Before, this trick made the lights look nicer by keeping their intensity levels close to 1
		 * (so-called artist-friendly light intensity scaling factor).
		 *
		 * @internal
		 * @see https://discourse.threejs.org/t/updates-to-lighting-in-three-js-r155/53733
		 */
		let directionalLight = new DirectionalLight(0xffffff, 0.3 * Math.PI);
		directionalLight.position.set(-150, -150, 350);
		this.#scene.add(directionalLight);

		let ambientLight = new AmbientLight(0xffffff, 0.741 * Math.PI);
		this.#scene.add(ambientLight);

		// Set up the camera controls
		this.#systems.cameraSystem = new CameraSystem(
			this.#cameraObject,
			this.#scene,
			this.#renderer.renderer!,
			this.#state,
			{
				mode: this.mode,
				canvasHeight: canvasHeight,
				canvasWidth: canvasWidth,
			},
		);

		this.#systems.cameraSystem.on('user-dolly-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-pan-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-rotate-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-zoom-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-tilt-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-pedestal-start', this.#aggregateUserEventsStart);
		this.#systems.cameraSystem.on('user-dolly-end', this.#aggregateUserEventsEnd);
		this.#systems.cameraSystem.on('user-pan-end', this.#aggregateUserEventsEnd);
		this.#systems.cameraSystem.on('user-rotate-end', this.#aggregateUserEventsEnd);
		this.#systems.cameraSystem.on('user-zoom-end', this.#aggregateUserEventsEnd);
		this.#systems.cameraSystem.on('user-tilt-end', this.#aggregateUserEventsEnd);
		this.#systems.cameraSystem.on('user-pedestal-end', this.#aggregateUserEventsEnd);

		this.#systems.cameraSystem.on('change', () => {
			this.publish('camera-change', this.camera);
			if (this.#systems.geometryInFocusSystem) {
				this.#systems.geometryInFocusSystem.cameraDirty = true;
			}
			this.render();
		});
		this.onAbort(() => {
			this.#systems.cameraSystem.off('user-dolly-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-pan-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-rotate-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-zoom-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-tilt-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-pedestal-start', this.#aggregateUserEventsStart);
			this.#systems.cameraSystem.off('user-dolly-end', this.#aggregateUserEventsEnd);
			this.#systems.cameraSystem.off('user-pan-end', this.#aggregateUserEventsEnd);
			this.#systems.cameraSystem.off('user-rotate-end', this.#aggregateUserEventsEnd);
			this.#systems.cameraSystem.off('user-zoom-end', this.#aggregateUserEventsEnd);
			this.#systems.cameraSystem.off('user-tilt-end', this.#aggregateUserEventsEnd);
			this.#systems.cameraSystem.off('user-pedestal-end', this.#aggregateUserEventsEnd);
			this.#systems.renderSystem.off('measure-canvas', this.#measureCanvas);
			this.#systems.cameraSystem.off('change', this.render);
			this.#systems.outlineInterpolationSystem.off('needs-render', this.render);
		});

		this.#systems.pathSystem = new PathSystem(
			this.#state,
			this.convertTo3DMapPosition,
			this.getParentContainer,
			this.#systems.cameraSystem.convertZoomLevelToAltitude,
		);
		this.#systems.pathSystem.on('animate:path', this.render);
		this.onAbort(() => {
			this.#systems.pathSystem.off('animate:path', this.render);
		});

		this.#systems.panBoundsSystem = new PanBoundsSystem(this.#state, this.#systems.cameraSystem);
		/* TODO Move the maplibre camera logic into cameraSystem, so we don't need these events. */
		const handlePanBoundsUpdate = () => {
			const { panBounds, fov, convertAltitudeToZoomLevel } = this.#systems.cameraSystem;
			if (this.#systems.panBoundsSystem.autoMinZoomLevel) {
				if (panBounds.radius <= 0) {
					return;
				}
				// Three.js perspective camera uses vertical FOV by default. For landscape orientation (width > height),
				// we can use the FOV directly (clamped at minimum of 1). For portrait orientation (width < height),
				// we need to reduce the effective FOV based on the width-to-height ratio to maintain proper scaling.
				const screenWidthHeightRatio = Math.min(this.#state.canvasWidth / this.#state.canvasHeight, 1);
				const fovInRadians = ((fov * Math.PI) / 180) * screenWidthHeightRatio;
				const tanFovHalf = Math.tan(fovInRadians / 2); // Tangent of half the field of view (used for calculating the visible area spread)

				// See image in: https://github.com/MappedIn/sdk/pull/448
				const { radius: r, center } = this.#systems.cameraSystem.panBounds;
				const d = r / tanFovHalf;
				const cameraPosition = this.getThreeCamera().getWorldPosition(new Vector3());
				const cameraToCenterDistance = center.distanceTo(cameraPosition);
				const cameraToCenterPlane = Math.abs(cameraPosition.z - center.z);
				const y = (d * cameraToCenterPlane) / cameraToCenterDistance;

				const minZoomFromCurrentPanBounds = convertAltitudeToZoomLevel(y);
				if (shouldExpandZoomLevel(minZoomFromCurrentPanBounds, this.camera.minZoomLevel)) {
					this.camera.setMinZoomLevel(minZoomFromCurrentPanBounds);
				}
			}
			this.publish('pan-bounds-change', this.#systems.cameraSystem.panBounds);
		};
		this.#systems.panBoundsSystem.on('update', handlePanBoundsUpdate);
		this.onAbort(() => {
			this.#systems.panBoundsSystem.off('update', handlePanBoundsUpdate);
		});

		this.camera = new Camera(this, this.#systems, this.#state);

		this.camera.setZoomLevel(options.zoomLevel ?? DEFAULT_ZOOM_LEVEL);
		this.camera.setBearing(options.bearing ?? DEFAULT_BEARING);
		this.camera.setPitch(options.pitch ?? DEFAULT_PITCH);
		this.camera.setMaxZoomLevel(DEFAULT_MAX_ZOOM_LEVEL);
		this.camera.setMinZoomLevel(DEFAULT_MIN_ZOOM_LEVEL);

		this.#systems.domVisiblitySystem = new DOMVisibilitySystem(this.#state, this.#twoDEntityScene);
		this.#systems.domMutationSystem = new DOMResizeSystem(this.#state, this.#twoDEntityScene);
		this.#systems.domMutationSystem.on('dimensions-update', () => {
			this.#systems.collisionSystem.collidersDirty = true;
			this.render();
		});
		this.#systems.domDrawSystem = new DOMDrawSystem(this.#state);

		this.#systems.collisionSystem = new CollisionSystem(
			this.#debugCanvas,
			this.#state,
			(options.useCollisionWorker ?? true) && (options.useWorkers ?? true),
		);
		this.#systems.twoDDrawSystem = new TwoDDrawSystem(this.#state, this.#colliderCanvas, options.transformImageRequest);
		this.#systems.twoDDrawSystem.on('img:loaded', this.render);
		this.#systems.interactionSystem = new InteractionSystem(
			this.rendererDomElement,
			this.#state,
			this.#cameraObject,
			this.#worldPlane,
			this.#systems.cameraSystem.isUserInteracting,
		);
		this.#systems.interactionSystem.on('doubletap-with-onefinger', (e: PointerEvent) => {
			if (this.#systems.cameraSystem.enableZoom) {
				this.camera.animateTo({
					center: this.projectScreenXYToCoordinate(e.clientX, e.clientY)?.coordinate,
					zoomLevel: this.camera.zoomLevel + 1,
					duration: TAP_ZOOM_DURATION,
					easing: 'ease-out',
				});
			}
		});
		this.#systems.interactionSystem.on('singletap-with-twofinger', () => {
			if (this.#systems.cameraSystem.enableZoom) {
				this.camera.animateTo({
					zoomLevel: this.camera.zoomLevel - 1,
					duration: TAP_ZOOM_DURATION,
					easing: 'ease-out',
				});
			}
		});
		this.#systems.interactionSystem.on('hover', this.render);
		this.#systems.interactionSystem.on('click', payload => {
			if (
				payload.entity2D?.id === 'watermark' &&
				typeof this.#systems.watermarkSystem.options.onClick === 'function' &&
				this.#systems.watermarkSystem.options.interactive
			) {
				this.#systems.watermarkSystem.options.onClick();

				return;
			}

			const geometry = payload.entity3D ? [payload.entity3D?.id] : [];
			if (payload.entity3D?.parentObject3D?.userData?.entityId) {
				geometry.push(payload.entity3D?.parentObject3D?.userData?.entityId);
			}

			this.publish('click', {
				coordinate: this.convert3DMapPositionToCoordinate(payload.position!),
				geometry,
				paths:
					payload.entity3D?.components[Geometry3DComponents.Mesh] instanceof PathComponent ? [payload.entity3D.id] : [],
				markers:
					payload.entity2D?.components[Geometry2DComponents.UI] instanceof MarkerComponent ? [payload.entity2D.id] : [],
				models:
					payload.entity3D?.components[Geometry3DComponents.Mesh] instanceof ModelComponent
						? [payload.entity3D.id]
						: [],
				labels:
					payload.entity2D?.components[Geometry2DComponents.UI] instanceof LabelComponent ? [payload.entity2D.id] : [],
				containers: Array.isArray(payload.groupContainers)
					? payload.groupContainers.map(container => container.userData.entityId)
					: [],
				pointerEvent: payload.pointerEvent,
			});
		});
		// TODO: possibly refactor with click
		this.#systems.interactionSystem.on('hover', payload => {
			if (!payload.position) return;

			this.publish('hover', {
				coordinate: this.convert3DMapPositionToCoordinate(payload.position!),
				geometry: payload.entity3D ? [payload.entity3D?.id] : [],
				paths:
					payload.entity3D?.components[Geometry3DComponents.Mesh] instanceof PathComponent ? [payload.entity3D.id] : [],
				markers:
					payload.entity2D?.components[Geometry2DComponents.UI] instanceof MarkerComponent ? [payload.entity2D.id] : [],
				models:
					payload.entity3D?.components[Geometry3DComponents.Mesh] instanceof ModelComponent
						? [payload.entity3D.id]
						: [],
				labels:
					payload.entity2D?.components[Geometry2DComponents.UI] instanceof LabelComponent ? [payload.entity2D.id] : [],
				containers: Array.isArray(payload.groupContainers)
					? payload.groupContainers.map(container => container.userData.entityId)
					: [],
			});
		});

		this.#systems.geometryInFocusSystem = new GeometryInFocusSystem(this.#state, this.#cameraObject);
		const handleGeometryInFocus = geometry => {
			this.publish('geometry-in-focus', geometry);
		};
		this.#systems.geometryInFocusSystem.on('geometry-in-focus', handleGeometryInFocus);
		this.onAbort(() => {
			this.#systems.geometryInFocusSystem.off('geometry-in-focus', handleGeometryInFocus);
		});

		this.#systems.twoDVisiblitySystem = new TwoDVisibilitySystem(this.#state);
		this.#systems.drawSystem = new DrawSystem(this.#state, this.convertTo3DMapPosition);

		this.#systems.twoDProjectionSystem = new TwoDProjectionSystem(this.#state, this.#project);
		this.#systems.exporterSystem = new ExporterSystem(this.#state);

		this.#systems.meshOptimizationSystem = new MeshCreationAndOptimizationSystem(
			this.#state,
			this.convertTo3DMapPosition,
		);
		this.#systems.meshOptimizationSystem.on('model-loaded', this.render);
		this.#systems.meshOptimizationSystem.on('geometry-2d-added', this.render);
		this.#systems.meshOptimizationSystem.on('geometry-group-added', this.#setGeometryDisposalDirty);
		this.onAbort(() => {
			this.#systems.meshOptimizationSystem.off('model-loaded', this.render);
			this.#systems.meshOptimizationSystem.off('geometry-2d-added', this.render);
			this.#systems.meshOptimizationSystem.off('geometry-group-added', this.#setGeometryDisposalDirty);
		});
		this.#systems.meshDetachmentSystem = new MeshDetachmentSystem(this.#state);
		this.#systems.meshModificationSystem = new MeshModificationSystem(this.#state);
		this.#systems.geometryDisposalSystem = new GeometryDisposalSystem(this.#state);
		this.#systems.textureSystem = new TextureSystem(this.#state);
		this.#systems.textureSystem.on('texture-added', this.render);
		this.onAbort(() => {
			this.#systems.textureSystem.off('texture-added', this.render);
		});
		this.#systems.borderSystem = new BorderSystem(this.#state, this.convertTo3DMapPosition);
		this.#systems.clippingPlaneSystem = new ClippingPlaneSystem(this.#state);
		this.#systems.renderOrderSystem = new RenderOrderSystem(this.#state);

		this.#systems.customGeometrySystem = new CustomGeometrySystem(this.#state, this.convertTo3DMapPosition);

		this.#systems.outlineInterpolationSystem = new OutlineInterpolationSystem(this.#state);
		this.#systems.outlineInterpolationSystem.on('needs-render', this.render);
		this.#systems.renderSystem = new RenderSystem(
			this.#renderer,
			this.#state,
			this.mode,
			this.#scene,
			this.#cameraObject,
			this.viewCamera,
			this.#systems,
		);
		this.#systems.imageSystem = new ImageSystem(
			this.#state,
			this.convertTo3DMapPosition,
			this.#project,
			options.bearing,
			options.naturalBearing,
			options.imagePlacementOptions,
		);
		this.#systems.text3DSystem = new Text3DSystem(this.#state, this.convertTo3DMapPosition, options.useWorkers ?? true);
		this.#systems.text3DSystem.on('render', this.render);
		this.#systems.imageSystem.on('image-loaded', this.render);

		this.#systems.renderSystem.on('pre-render', () => this.publish('pre-render'));
		this.#systems.renderSystem.on('post-render', () => this.publish('post-render'));
		this.#systems.renderSystem.on('measure-canvas', this.#measureCanvas);
		this.onAbort(() => {
			this.#systems.renderSystem.off('pre-render', () => this.publish('pre-render'));
			this.#systems.renderSystem.off('post-render', () => this.publish('post-render'));
			this.#systems.renderSystem.off('measure-canvas', this.#measureCanvas);
		});
		this.#systems.outdoorLayersSystem = new OutdoorLayers(this.map);
		this.#systems.occlusion2DSystem = new Occlusion2DSystem(this.#state, this.convertTo3DMapPosition, this.#renderer);
		if (options.occlusionEnabled) {
			this.#systems.occlusion2DSystem.enable();
		}

		this.Debug = new Debug(this);

		if (options.environment !== false) {
			this.#cachedBasicEnvMapPromise = this.#loadEnvMap();
		}

		// Perform initial render so canvas is drawn with background color
		this.#renderFull();
	}

	#setGeometryDisposalDirty = () => {
		this.#systems.geometryDisposalSystem.dirty = true;
		this.render();
	};

	#round10 = value => {
		return Math.trunc(value * 10) / 10;
	};

	#handlePixelRatioChange = () => {
		this.#setResolutionScale(getPixelRatio());
		this.#systems.twoDDrawSystem.dirty = true;
		// proceed with the window resize
		this.#handleWindowResize(undefined, undefined, true);
	};

	// make typescript happy
	#handleWindowResizeWithEvent = () => {
		this.#handleWindowResize();
	};

	// Updates the size of the renderer and where everything is when the window is resized
	#handleWindowResize = (width?: number, height?: number, force = false) => {
		const currentWidth = width ?? this.rendererDomElement.clientWidth;
		const currentHeight = height ?? this.rendererDomElement.clientHeight;
		if ((currentWidth === 0 || currentHeight === 0) && !force) {
			return;
		}
		// if the window is resized, we want to measure the canvas again (immediately)
		// and then do a full render, to ensure any deffered render tasks
		// have the updated canvas size
		this.#state.canvasWidth = currentWidth;
		this.#state.canvasHeight = currentHeight;
		this.#setResolutionScale(getPixelRatio());

		this.#measureCanvas();

		this.#resizeCollisionCanvas();
		this.#systems.cameraSystem.resize(this.canvasWidth, this.canvasHeight);
		this.#systems.cameraSystem.dirty = true;
		if (this.map != null) {
			/**
			 * Sync Maplibre's min/max zoom with the camera's, since our camera's zoom
			 * levels are recalculated on canvas resize and may diverge from Maplibre's fixed values.
			 */
			this.map.setMinZoom(this.camera.minZoomLevel);
			this.map.setMaxZoom(this.camera.maxZoomLevel);
		}
		this.#systems.watermarkSystem.resize(this.canvasWidth, this.canvasHeight);
		this.#systems.htmlControlsSystem.resize(this.canvasWidth);
		this.#systems.geometryInFocusSystem.resize();
		this.#systems.occlusion2DSystem.resize();
		const { top, left } = this.container.getBoundingClientRect();
		this.#state.containerOffset = [top, left];
		this.publish('resize');
		this.render();
	};

	// Perform a full rendering pass, synchronously
	#renderFull = () => {
		this.renderSync();
	};

	#tempProjectionVector = new Vector3();

	#project = (projectVector: Vector2, position: Vector3) => {
		const camera = this.mode === 'standalone' ? this.#cameraObject : this.viewCamera;

		// make sure that the object is not behind the camera
		if (this.#cameraObject.position.z + this.camera.elevation > position.z) {
			this.#tempProjectionVector.copy(position);
			const projection = this.#tempProjectionVector.project(camera);
			let x = ((projection.x + 1) / 2) * this.canvasWidth - 0 / 2;
			let y = ((-projection.y + 1) / 2) * this.canvasHeight - 0 / 2;

			let translateX = this.#round10(x);
			let translateY = this.#round10(y);

			projectVector.x = translateX;
			projectVector.y = translateY;
		} else {
			// toss projections out of viewport, until they're in view
			// @TODO: we should be smarter and stop processing these, but that can come later
			// maybe a frustum culling type thing but for 2D entities
			projectVector.x = -1000;
			projectVector.y = -1000;
		}
	};

	#setBufferSize = (width, height) => {
		this.#renderer.setBufferSize(width, height);
		this.#cameraObject.aspect = width / height;
		this.#cameraObject.updateProjectionMatrix();
		this.#cameraObject.updateMatrixWorld(true);
		this.#resolution.x = width;
		this.#resolution.y = height;
	};

	#measureCanvas = () => {
		// look up the size the canvas is being displayed in CSS pixels
		let width = (this.#state.canvasWidth = this.rendererDomElement.clientWidth);
		let height = (this.#state.canvasHeight = this.rendererDomElement.clientHeight);

		// guess the width and height js will use if we ask for that size
		let widthThreeJS = Math.floor(width * this.resolutionScale);
		let heightThreeJS = Math.floor(height * this.resolutionScale);

		// check if our canvas is that widthThreeJS
		// if (this.rendererDomElement.width !== widthThreeJS || this.rendererDomElement.height !== heightThreeJS) {
		// 	if (this.rendererDomElement.width * this.rendererDomElement.height * widthThreeJS * heightThreeJS !== 0) {
		// 		let factor =
		// 			this.rendererDomElement.width !== widthThreeJS
		// 				? this.rendererDomElement.width / widthThreeJS
		// 				: this.rendererDomElement.height / heightThreeJS;
		// 		this.cameraSystem.setZoom(factor * this.cameraSystem.getZoom());
		// 	}
		// 	// it's not the same size so
		// 	// set it to the width and height we want and pray
		// 	// that it matches what js actually uses
		this.#setBufferSize(widthThreeJS, heightThreeJS);
		this.map?.resize();
		// }
		this.#state.shouldMeasureCanvas = false;
	};

	/**
	 * Add a container that can hold other containers, geometry groups, markers, labels and paths. Use this to group entities together.
	 */
	addGroupContainer(
		id: string,
		options?: {
			visible?: boolean;
			altitude?: number;
			interactive?: boolean;
			focusable?: boolean;
			preloadGeometry?: boolean;
		},
		parent?: EntityId<GroupContainerState> | string | number | null,
	) {
		if (!this.#state.geometry3DMap.has(id) && !this.#state.geometry2DMap.has(id)) {
			const containerObject = new GroupContainerObject3D(id);
			containerObject.addEventListener('childadded', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});
			containerObject.addEventListener('childremoved', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});
			if (options?.visible !== undefined) {
				containerObject.visible = options.visible;
			}
			if (options?.altitude !== undefined) {
				containerObject.setAltitude(options.altitude);
			}
			if (options?.interactive !== undefined) {
				updateInteractivity(containerObject, options.interactive, this.#state);
			}
			if (options?.focusable !== undefined) {
				updateFocusable(containerObject, options.focusable);
			}
			if (options?.preloadGeometry !== undefined) {
				containerObject.userData.preloadGeometry = options.preloadGeometry;
			}
			this.#state.geometry3DMap.set(id, containerObject);
			const parentContainer = this.getParentContainer(parent);
			if (parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(containerObject);
			}

			this.#invalidate3DEntities();
			this.render();
		} else {
			Logger.error(`Group container with ID '${id}' already exists. The group container cannot be added.`);
		}

		return { id, type: 'group-container' } as EntityId<GroupContainerState>;
	}

	getParentContainer = (
		parent?: EntityId<GroupContainerState> | EntityId<GeometryState> | string | number | null,
		defaultToScene = true,
	): GroupContainerObject3D | undefined => {
		if (!parent) {
			return defaultToScene ? this.#state.entityScene : undefined;
		}

		if (typeof parent === 'string' || typeof parent === 'number') {
			const parentEntity = this.#state.geometry3DMap.get(parent);

			if (parentEntity instanceof GroupContainerObject3D) {
				return parentEntity;
			} else {
				Logger.warn(
					`Parent (${
						typeof parent === 'object' ? (parent as EntityId<GroupContainerState>).id : parent
					}) is not a valid container, adding to scene`,
				);

				return defaultToScene ? this.#state.entityScene : undefined;
			}
		} else if (parent.type === 'group-container') {
			const parentContainer = this.#state.geometry3DMap.get(parent.id);
			if (parentContainer instanceof GroupContainerObject3D) {
				return parentContainer;
			}
		}

		return defaultToScene ? this.#state.entityScene : undefined;
	};

	/**
	 * Add a custom THREE.js entity to the map. The geometry is placed at the GeoJSON coordinate and includes a `setup` and `update` methods.
	 * Setup is called when the first time the geometry visible, and update is called every render frame.
	 */
	addCustomGeometry(
		id: string,
		feature: Feature<Point>,
		builder: CustomGeometryBuilder,
		style?: { visible?: boolean; altitude?: number; interactive?: boolean },
		parent?: EntityId<GroupContainerState> | string | number | null,
	) {
		if (!this.#state.geometry3DMap.has(id)) {
			const geometry = new CustomGeometryComponent(feature, builder);
			const entity = new Geometry3D(geometry, new StyleComponent(style));

			if (style?.interactive !== undefined) {
				updateInteractivity(entity, style.interactive, this.#state);
			}
			entity.id = id;

			geometry.mesh.userData = {
				entityId: id,
				type: 'custom-geometry',
			};

			this.#state.geometry3DMap.set(entity.id, entity);

			const parentContainer = this.getParentContainer(parent);
			if (parent && parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(entity);
			} else {
				this.#state.entityScene.addEntity(entity);
			}
			this.#invalidate3DEntities();
			this.render();
		} else {
			Logger.error(`Custom geometry with ID '${id}' already exists. The custom geometry cannot be added.`);
		}

		return {
			id,
			type: 'custom-geometry',
		} as EntityId<ShapeState>;
	}

	/**
	 * Add a geometry group from GeoJSON data
	 */
	addGeometryGroup<T extends FeatureCollection<Polygon | MultiPolygon | LineString, any>>(
		id: string,
		geometry: T,
		style?: T extends FeatureCollection<LineString, any> ? LineStyle : PaintStyle,
		parent?: EntityId<GroupContainerState> | string | number | null,
	) {
		if (!this.#state.geometry3DMap.has(id) && !this.#state.geometry2DMap.has(id)) {
			const groupObject = new GeometryGroupObject3D(id);
			groupObject.addEventListener('childadded', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});
			groupObject.addEventListener('childremoved', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});

			groupObject.userData.dirty = true;

			this.#state.geometry3DMap.set(id, groupObject);
			const { visible, ...rest } = style || { color: DEFAULT_COLOR };
			for (const feature of geometry.features) {
				if (feature.geometry == null) {
					continue;
				}
				if (!['Polygon', 'MultiPolygon', 'LineString', 'MultiLineString'].includes(feature.geometry.type)) {
					continue;
				}

				const id = feature?.id || feature?.properties?.id || randomId();

				const entity = this.#systems.meshOptimizationSystem.createEntityFromFeature(id, feature, {
					outline: true, // default to true unless overwritten by the user
					...rest,
				});
				this.#state.geometry3DMap.set(id, entity);
				groupObject.userData.entities3D.add(id);
				this.#systems.textureSystem.dirty = true;
			}
			groupObject.setVisible(visible ?? true);

			const parentContainer = this.getParentContainer(parent);
			if (parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(groupObject);
			}
			this.#invalidate3DEntities();
			this.render();
		} else {
			Logger.error(`Geometry group with ID '${id}' already exists. The geometry group cannot be added.`);
		}

		this.#systems.outlineInterpolationSystem.geometries3DDirty = true;

		return { id, type: 'geometry-group' } as EntityId<GeometryGroupState>;
	}

	addOccluder<T extends Feature<Polygon | MultiPolygon, any>>(
		feature: T,
		parent?: EntityId<GroupContainerState> | string | number | null,
	): number {
		const parentContainer = this.getParentContainer(parent);

		const occluderId = this.#systems.occlusion2DSystem.reserveOccluderId();
		parentContainer?.addOccluderFeature(feature, occluderId);

		return occluderId;
	}

	addImage(
		id: string,
		geometry: Feature<Point, ImageProperties>,
		style: ImageStyle,
		parent?: EntityId<GroupContainerState> | string | null,
	) {
		try {
			validateImage(id, geometry, style);
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}
		if (!this.#state.geometry3DMap.has(id) && !this.#state.geometry2DMap.has(id)) {
			const meshComponent = new ImageComponent(geometry);

			const entity = new Geometry3D(meshComponent, new StyleComponent(style));
			entity.id = id || randomId();
			meshComponent.mesh.userData = {
				entityId: entity.id,
				type: 'path',
			};

			this.#state.geometry3DMap.set(entity.id, entity);

			const parentContainer = this.getParentContainer(parent);
			if (parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(entity);
			}

			this.#invalidate3DEntities();
			this.render();
		} else {
			Logger.error(`Image with ID '${id}' already exists. The image cannot be added.`);
		}

		return { id, type: 'image' } as EntityId<ImageState>;
	}

	/**
	 * Add a group of models from GeoJSON data. These will be instanced automatically for better performance.
	 */
	addModelGroup(
		id: string,
		geometry: FeatureCollection<Point, ModelProperties>,
		style: Partial<InitializeModelState>,
		{
			parent,
			onComplete: onAdd,
		}: {
			parent?: EntityId<GroupContainerState> | string | null;
			onComplete?: () => void;
		} = {},
	) {
		try {
			validateModelGroup(id, geometry, style);
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}
		if (!this.#state.geometry3DMap.has(id) && !this.#state.geometry2DMap.has(id)) {
			const groupObject = new GeometryGroupObject3D(id);
			groupObject.addEventListener('childadded', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});
			groupObject.addEventListener('childremoved', () => {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			});
			this.#state.geometry3DMap.set(id, groupObject);

			groupObject.userData.dirty = true;
			groupObject.userData.modelURL = style.url;
			for (const feature of geometry.features) {
				const id = feature.id || feature.properties.id || randomId();
				const entity = this.#systems.meshOptimizationSystem.createModelFromFeature(id, feature, {
					...style,
				});
				if (feature.properties.interactive !== undefined || style.interactive != null) {
					updateInteractivity(entity, feature.properties.interactive ?? style.interactive, this.#state);
				}
				if (style.clippingPlaneZOffset != null) {
					entity.components[Geometry3DComponents.ClippingPlane] = new ClippingPlaneComponent();
					entity.components[Geometry3DComponents.ClippingPlane].verticalOffset = style.clippingPlaneZOffset;
				}
				// store the parent id on the geometry so we can handle cases where we want to remove the geometry from the group
				// before the mesh is created
				entity.parentId = groupObject.userData.entityId;
				this.#state.geometry3DMap.set(id, entity);
				groupObject.userData.entities3D.add(id);
			}

			const parentContainer = this.getParentContainer(parent);
			if (parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(groupObject);
			}
			this.#invalidate3DEntities();
			this.render()?.then(onAdd);
		} else {
			Logger.error(`Model group with ID '${id}' already exists. The model group cannot be added.`);
		}

		return { id, type: 'geometry-group' } as EntityId<GeometryGroupState>;
	}

	#createEntity2D(
		coordinate: Position,
		component: MarkerComponent | LabelComponent,
		occluderId?: number,
		verticalOffset?: number,
	) {
		const entity = new Geometry2D(
			component,
			this.convertTo3DMapPosition(coordinate[1], coordinate[0], coordinate[2] || 0),
			verticalOffset,
		);
		entity.occluderId = occluderId ?? this.#systems.occlusion2DSystem.backgroundId;
		this.#state.geometry2DMap.set(entity.id, entity);

		return entity;
	}

	/**
	 * Add an HTML Marker at a GeoJSON coordinate.
	 */
	addMarker2D(
		coordinate: Position,
		contentHTML: string,
		options?: AddMarkerOptions,
		parent?: EntityId<GroupContainerState> | string | number | null,
	) {
		let entityId = options?.id;

		try {
			validateMarker(coordinate, contentHTML, options || {});
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}

		if (
			entityId === undefined ||
			(!this.#state.geometry2DMap.has(entityId) && !this.#state.geometry3DMap.has(entityId))
		) {
			const entity = this.#createEntity2D(
				coordinate,
				new MarkerComponent(contentHTML, options),
				options?.occluderId,
				options?.verticalOffset,
			);
			entity.id = entityId || String(entity.id);
			entityId = entity.id;

			// only add an interactive component if the marker's not set to pointer-events-auto
			if (options?.interactive != null && options.interactive !== 'pointer-events-auto') {
				updateInteractivity(entity, options?.interactive, this.#state);
			}
			if (options?.enabled != null) {
				updateEnabled(entity, options.enabled);
			}

			const attachedEntity =
				typeof options?.attachTo === 'string' || typeof options?.attachTo === 'number'
					? this.#state.geometry3DMap.get(options.attachTo)
					: undefined;
			if (attachedEntity instanceof Geometry3D) {
				attachedEntity.attach(entity);
			}

			this.getParentContainer(parent)?.addEntity(entity);
			this.render();
		} else {
			Logger.error(`Marker2D with ID '${entityId}' already exists. The marker cannot be added.`);
		}

		return {
			id: entityId,
			type: 'marker',
		} as EntityId<MarkerState>;
	}

	addText3D(
		id: string,
		geometry: Feature<Point, FloatingFloorTextProperties>,
		options: AddText3DOptions,
		parent?: EntityId<GroupContainerState> | string | null,
	) {
		if (!this.#state.geometry3DMap.has(id) && !this.#state.geometry2DMap.has(id)) {
			const textComponent = new Text3DComponent({
				...geometry,
				properties: {
					...geometry.properties,
					id,
					content: geometry.properties.text,
				},
			});
			const entity = new Geometry3D(textComponent, new Text3DStyleComponent(options?.appearance));
			entity.id = id;

			textComponent.mesh.userData.entityId = id.toString();
			textComponent.mesh.userData.type = 'text3d';

			this.#state.geometry3DMap.set(id, entity);

			const parentContainer = this.getParentContainer(parent);
			if (parentContainer instanceof GroupContainerObject3D) {
				parentContainer.addEntity(entity);
			}

			this.#invalidate3DEntities();
			this.render();
		} else {
			Logger.error(`Text3D with ID '${id}' already exists. The text3d cannot be added.`);
		}

		return { id, type: 'text3d' } as EntityId<Text3DState>;
	}

	/**
	 * Labels an existing 3D geometry with a text area.
	 *
	 * @param meshGeometryId - The ID of the target 3D geometry to label.
	 * @param content - The text content to display.
	 * @param options - Additional options for adding the text label, including:
	 *   - parentId: Optional ID of the parent container.
	 *   - appearance: Optional appearance settings for the text label.
	 *
	 * @returns An object containing the ID and type of the created or existing text label, or undefined if labeling failed. If geometry is already labeld, the same text id will be returned
	 *
	 * @example
	 * ```typescript
	 * const label = renderer.labelText3D('geometry123', 'Hello World', { parentId: 'parent456', appearance: { color: 'red' } });
	 * if (label) {
	 *   console.log(`Label created with ID: ${label.id}`);
	 * }
	 *
	 */
	labelText3D(meshGeometryId: string, content: string, options: AddText3DOptions & { parentId?: string } = {}) {
		try {
			if (!content || !meshGeometryId) {
				throw new Error('content and meshGeometryId are required');
			}
			addText3DOptionsSchema.parse(options);
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}
		const targetGeometry3D = this.#state.geometry3DMap.get(meshGeometryId);
		if (targetGeometry3D?.type !== 'geometry') {
			Logger.error('.labelText3D failed: target incorrect');

			return;
		}
		const targetMeshComponent = targetGeometry3D?.components[Geometry3DComponents.Mesh];

		// Skip labeling due to missing textArea property
		if (!targetMeshComponent.feature.properties.textArea) {
			return;
		}

		// return existing label id if exists
		if (targetMeshComponent.textEntityId) {
			const existing = this.#state.geometry3DMap.get(targetMeshComponent.textEntityId);
			if (existing?.type !== 'text3d') {
				Logger.error('.labelText3D failed: existing text3d not found');

				return;
			}

			return {
				id: existing.id,
				type: existing.type,
			};
		}

		const textComponent = new Text3DComponent({
			type: 'Feature',
			geometry: {
				coordinates: targetMeshComponent.feature.properties.textArea?.position,
				type: 'Point',
			},
			properties: { textArea: targetMeshComponent.feature.properties.textArea, content, id: randomId() },
		});

		textComponent.polygonEntityId = meshGeometryId;
		const entity = new Geometry3D(textComponent, new Text3DStyleComponent(options?.appearance));
		targetMeshComponent.textEntityId = entity.id.toString();
		textComponent.mesh.userData.entityId = entity.id.toString();
		textComponent.mesh.userData.type = 'text3d';

		this.#state.geometry3DMap.set(entity.id.toString(), entity);

		if (options.parentId) {
			this.getParentContainer(options.parentId)?.addEntity(entity);
		}

		this.render();

		return {
			id: entity.id,
			type: 'text3d',
		} as EntityId<Text3DState>;
	}

	/**
	 * Add a 2D label at a GeoJSON coordinate.
	 */
	addLabel2D(
		coordinate: Position,
		text: string,
		options?: AddLabelOptionsInternal,
		parent?: EntityId<GroupContainerState> | string | null,
	) {
		let entityId = options?.id;

		try {
			validateLabel(coordinate, text, options || {});
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}

		if (
			entityId === undefined ||
			(!this.#state.geometry2DMap.has(entityId) && !this.#state.geometry3DMap.has(entityId))
		) {
			const entity = this.#createEntity2D(
				coordinate,
				new LabelComponent(text, options || {}, this.#state),
				options?.occluderId,
				options?.verticalOffset,
			);
			entity.id = entityId || String(entity.id);
			entityId = entity.id;
			if (options?.interactive != null) {
				updateInteractivity(entity, options?.interactive, this.#state);
			}
			if (options?.enabled != null) {
				updateEnabled(entity, options.enabled);
			}

			const attachedEntity =
				typeof options?.attachTo === 'string' || typeof options?.attachTo === 'number'
					? this.#state.geometry3DMap.get(options.attachTo)
					: undefined;
			if (attachedEntity instanceof Geometry3D) {
				attachedEntity.attach(entity);
			}

			this.getParentContainer(parent)?.addEntity(entity);

			this.#invalidate2DEntities();
			this.render();
		} else {
			Logger.error(`Label2D with ID '${entityId}' already exists. The label cannot be added.`);
		}

		return {
			id: entityId,
			type: 'label',
		} as EntityId<LabelState>;
	}

	/**
	 * Add a Path along a set of GeoJSON coordinates that can be animated.
	 */
	addPath(
		geometry: FeatureCollection<Point, PathProperties>,
		options: AddPathOptions = {},
		parent?: EntityId<GroupContainerState> | string | null,
	) {
		try {
			validatePath(geometry, options || {});
		} catch (error: unknown) {
			Logger.error(error);

			return;
		}

		const meshComponent = new PathComponent(geometry, pickPathOptions(options));

		const entity = new Geometry3D(
			meshComponent,
			new StyleComponent({
				color: options?.color ?? '#ff834c',
				altitude: options?.verticalOffset ?? 0.1,
			}),
		);
		entity.id = options?.id || randomId();
		meshComponent.mesh.userData = {
			// @ts-ignore
			entityId: entity.id,
			type: 'path',
		};

		this.#state.geometry3DMap.set(entity.id, entity);
		if (options?.interactive != null) {
			updateInteractivity(entity, options?.interactive, this.#state);
		}

		const parentContainer = this.getParentContainer(parent);
		if (parentContainer instanceof GroupContainerObject3D) {
			parentContainer.addEntity(entity);
		}

		this.#invalidate3DEntities();
		this.render();

		return {
			id: entity.id,
			type: 'path',
		} as EntityId<PathState>;
	}

	/**
	 * Updates the watermark on the map.
	 *
	 * @param options {WatermarkUpdateOptions}
	 */
	updateWatermark(options: WatermarkUpdateOptions) {
		this.#systems.watermarkSystem.options = options;
		this.#systems.watermarkSystem.dirty = true;
		this.#systems.collisionSystem.collidersDirty = true;
		this.render();
	}

	/**
	 * Remove an entity from the renderer and release associated resources.
	 */
	remove(object: string | number): void;
	remove(object: EntityTypes): void;
	remove(object: string | number | EntityTypes) {
		let geometry: EntityTypes | undefined;
		if (typeof object === 'string' || typeof object === 'number') {
			geometry = this.#state.geometry2DMap.get(object);
			if (geometry == null) {
				geometry = this.#state.geometry3DMap.get(object);
			}
		} else if (typeof object.id === 'string' || typeof object.id === 'number') {
			geometry = this.#state.geometry2DMap.get(object.id);
			if (geometry == null) {
				geometry = this.#state.geometry3DMap.get(object.id);
			}
		} else {
			geometry = object as EntityTypes;
		}

		if (geometry == null) {
			return;
		}

		if (geometry.type === 'custom-geometry') {
			this.#state.geometry3DMap.delete(geometry.id);
			if (geometry.object3d) {
				destroyObject3D(geometry.object3d);
			}
		} else if (geometry.type === 'group-container') {
			this.#state.geometry3DMap.delete(geometry.userData.entityId);
			for (const entityId of geometry.childrenIds.values()) {
				this.remove(entityId);
				geometry.userData.entities2D.delete(entityId);
			}
			const focusComponent = geometry.components[GroupContainerComponents.Focusable];
			if (focusComponent && focusComponent.focusMesh) {
				destroyObject3D(focusComponent.focusMesh);
			}
			destroyObject3D(geometry);
		} else if (geometry.type === 'geometry-group') {
			this.#state.geometry3DMap.delete(geometry.userData.entityId);
			for (const entityId of geometry.userData.entities3D.values()) {
				this.remove(entityId);
				geometry.userData.entities3D.delete(entityId);
			}
			destroyObject3D(geometry);
		} else if (geometry.type === 'geometry' || geometry.type === 'model' || geometry.type === 'path') {
			const meshComponent = geometry.components[Geometry3DComponents.Mesh];
			const focusableComponent = geometry.components[Geometry3DComponents.Focusable];
			if (geometry.entities2D.size > 0) {
				// remove all the 2D entities from the parent
				for (const entity of geometry.entities2D.values()) {
					this.remove(entity);
				}
				// and clean up the geometry3D
				geometry.removeAllEntities();
			}
			const parentGroupId = geometry.parentObject3D?.userData.entityId ?? geometry.parentId;
			if (parentGroupId) {
				const group = this.#state.geometry3DMap.get(parentGroupId);
				if (group instanceof GeometryGroupObject3D) {
					group.userData.entities3D.delete(geometry.id);
				}
			}
			if (geometry.object3d) {
				destroyObject3D(geometry.object3d);
			}
			if (geometry.type === 'geometry') {
				const outlineComponent = geometry.components[Geometry3DComponents.Outline];
				if (outlineComponent) {
					outlineComponent.enabled = false;
					outlineComponent.dirty = true;
				}
			}

			if (focusableComponent && 'focusMesh' in focusableComponent && focusableComponent.focusMesh != null) {
				destroyObject3D(focusableComponent.focusMesh);
			}
			if ('textMesh' in meshComponent && meshComponent.textMesh != null) {
				this.remove(meshComponent.textMesh.userData.entityId);
			}
			this.#state.geometry3DMap.delete(geometry.id);
		} else if (geometry.type === 'marker' || geometry.type === 'label') {
			const parentId = geometry.attachedTo;
			if (parentId) {
				const parent = this.#state.geometry3DMap.get(parentId);
				if (parent instanceof Geometry3D) {
					// make sure we remove the entity from the parent
					parent.detach(geometry);
				}
			}
			if (geometry.object3d) {
				destroyObject3D(geometry.object3d);
			}
			if (geometry.type === 'label') {
				delete this.#systems.twoDDrawSystem.textCache[geometry.id];
			}
			this.#state.geometry2DMap.delete(geometry.id);
		} else if (geometry.type === 'image') {
			if (geometry.object3d) {
				destroyObject3D(geometry.object3d);
			}
			this.#state.geometry3DMap.delete(geometry.id);
		} else if (geometry.type === 'text3d') {
			destroyObject3D(geometry.object3d);
			const { batchedText, textMesh, polygonEntityId } = geometry.components[Geometry3DComponents.Mesh];
			if (textMesh) {
				if (batchedText) {
					batchedText.removeText(textMesh);
				}
				destroyObject3D(textMesh);
			}
			if (polygonEntityId) {
				this.#systems.text3DSystem.remove(polygonEntityId);
				const meshGeometry3D = this.#state.geometry3DMap.get(polygonEntityId);
				if (meshGeometry3D?.type === 'geometry') {
					meshGeometry3D.components[Geometry3DComponents.Mesh].textMesh = undefined;
					meshGeometry3D.components[Geometry3DComponents.Mesh].textEntityId = undefined;
				}
			}
			this.#state.geometry3DMap.delete(geometry.id);
		}

		this.#invalidate3DEntities();
		this.#invalidate2DEntities();
		this.render();
	}

	/**
	 * Show collision boxes for 2D entities for debugging
	 */
	showCollisionBoxes = () => {
		this.#systems.collisionSystem.showCollisionBoxes();
		this.render();
	};

	/**
	 * Hide collision boxes for 2D entities for debugging
	 */
	hideCollisionBoxes = () => {
		this.#systems.collisionSystem.hideCollisionBoxes();
		this.render();
	};

	/**
	 * Recursively get the state for the whole scene. Can be an expensive operation.
	 */
	getScene() {
		return this.getState<GroupContainerState>('__scene');
	}

	getThreeScene() {
		return this.#scene;
	}
	getThreeCamera() {
		return this.#cameraObject;
	}

	async setEnvironment(update: EnvMapOptions) {
		if (!this.#scene) return;
		if (update === 'basic') {
			if (!this.#cachedBasicEnvMapPromise) this.#cachedBasicEnvMapPromise = this.#loadEnvMap();
			const texture = await this.#cachedBasicEnvMapPromise;
			this.#scene.environment = texture;
		} else {
			this.#scene.environment = null;
		}
	}

	getEnvironment(): EnvMapOptions {
		return this.#scene?.environment ? 'basic' : false;
	}
	/**
	 * Get the current scene as a GLB file
	 */
	getCurrentSceneGLTF(options: GLTFExportOptions) {
		return this.#systems.exporterSystem.getCurrentSceneGLTF(options);
	}

	/**
	 * Gets the bounding area (4 corners) in lon,lat of a geometry, geomrtry group, or group container. This can be used with Camera to focus on an entity
	 */
	getBoundingArea(geometryOrGeometryId?: string | number | EntityId<EntityState>) {
		if (this.#systems.renderSystem.threeDdirty || this.#systems.renderSystem.twoDdirty) {
			this.renderSync();
		}
		const box = new Box3();
		if (geometryOrGeometryId == null) {
			box.setFromObject(this.#state.entityScene);
		} else {
			const geometryEntity = getGeometryByGeometryId(this.#state, geometryOrGeometryId);
			if (geometryEntity?.type === 'geometry' && geometryEntity?.object3d != null) {
				const component = geometryEntity.components[Geometry3DComponents.Mesh];
				const geometry = component instanceof MeshComponent ? component.geometry : undefined;
				if (component.mesh instanceof BatchedMesh && geometry) {
					// when it's batched meshes we want to compute bounding box of the geometry which contains the data for the geometry itself
					geometry.computeBoundingBox();
					if (geometry.boundingBox) {
						box.union(geometry.boundingBox);
					}
				} else {
					box.setFromObject(geometryEntity.object3d);
				}
			} else if (
				(geometryEntity?.type === 'geometry-group' || geometryEntity?.type === 'group-container') &&
				geometryEntity?.userData.entityId != null
			) {
				box.setFromObject(geometryEntity);
			}
		}
		if (isFiniteBox(box)) {
			return getCornersOfBoundingBox(box).map(this.convert3DMapPositionToCoordinate);
		}
	}

	/**
	 * Check if the state for a given geometry or geometry id exists
	 * @param geometryOrGeometryId - The geometry or geometry id to check
	 * @returns True if the state exists, false otherwise
	 */
	hasState(geometryOrGeometryId: string | number | EntityId<EntityState>) {
		return getGeometryByGeometryId(this.#state, geometryOrGeometryId) != null;
	}

	/**
	 * Get the current state of the map view, or any entity that was added, regardless of whether it is visible in the scene
	 */
	getState(): MapViewState;
	getState<T extends EntityId<EntityState>>(
		geometryOrGeometryId: T,
	): T extends EntityId<LabelState>
		? LabelState
		: T extends EntityId<GeometryState>
		? GeometryState
		: T extends EntityId<MarkerState>
		? MarkerState
		: T extends EntityId<GeometryGroupState>
		? GeometryGroupState
		: T extends EntityId<GroupContainerState>
		? GroupContainerState
		: T extends EntityId<ModelState>
		? ModelState
		: T extends EntityId<PathState>
		? PathState
		: T extends EntityId<ShapeState>
		? ShapeState
		: T extends EntityId<ImageState>
		? ImageState
		: T extends EntityId<Text3DState>
		? Text3DState
		: EntityState;
	getState(geometryOrGeometryId?: Record<string | number, any> | string | number): EntityState;
	getState<T extends EntityState>(
		geometryOrGeometryId: T['id'],
	): T extends LabelState
		? LabelState
		: T extends GeometryState
		? GeometryState
		: T extends MarkerState
		? MarkerState
		: T extends GeometryGroupState
		? GeometryGroupState
		: T extends GroupContainerState
		? GroupContainerState
		: T extends ModelState
		? ModelState
		: T extends PathState
		? PathState
		: T extends ShapeState
		? ShapeState
		: T extends ImageState
		? ImageState
		: EntityState;
	getState(geometryOrGeometryId?: Record<string | number, any> | string | number) {
		if (geometryOrGeometryId == null) {
			return {
				type: 'map-view',
				hoverColor: this.#state.hoverColor,
				text3dHoverColor: this.#state.text3dHoverColor,
			} satisfies MapViewState as MapViewState;
		}
		const geometry = getGeometryByGeometryId(this.#state, geometryOrGeometryId);

		if (geometry?.type === 'custom-geometry') {
			const component = geometry.components[Geometry3DComponents.Mesh] as CustomGeometryComponent;
			const styleComponent = geometry.components[Geometry3DComponents.Style] as StyleComponent;

			return {
				id: geometry.id,
				type: 'custom-geometry',
				visible: styleComponent.visible,
				altitude: styleComponent.altitude,
				parent: geometry.parentObject3D?.userData.entityId,
				position: this.convert3DMapPositionToCoordinate(component.position!),
			} as ShapeState;
		} else if (geometry?.type === 'geometry-group') {
			const children = Array.from(geometry.userData.entities3D.values());
			// leaving this here for backwards compatibility, but will remove it as move towards getting rid of the geometry group as an external entity
			// TODO: remove this once we have removed the geometry group as an external entity
			const firstChildId = children?.[0];
			const firstChildState = firstChildId ? this.getState<GeometryState>(firstChildId) : ({} as GeometryState);

			return {
				id: geometry.userData.entityId,
				type: 'geometry-group',
				visible: geometry.visible,
				altitude: firstChildState?.altitude,
				initialColor: firstChildState?.initialColor,
				color: firstChildState?.color,
				side: firstChildState?.side ?? 'double',
				opacity: firstChildState?.opacity,
				height: firstChildState?.height,
				texture: firstChildState?.texture,
				topColor: firstChildState?.topColor,
				topTexture: firstChildState?.topTexture,
				interactive: Array.from(geometry.userData.entities3D.values())
					.map(entityId => this.#state.geometry3DMap.get(entityId))
					.every(e => e && (e as Geometry3D).components[Geometry3DComponents.Interaction] != null),
				children,
				bevel: firstChildState?.bevel,
			} as GeometryGroupState;
		} else if (geometry?.type === 'group-container') {
			return {
				id: geometry.userData.entityId,
				type: 'group-container',
				visible: geometry.visible,
				altitude: geometry.getWorldPosition(tempVec).z ?? 0, // flooor altittude is always relates to the ground take world position altitude.
				opacity: geometry.opacity,
				interactive: !!geometry.components[GroupContainerComponents.Interaction],
				children: Array.from(geometry.childrenIds.values())
					.map(childId => this.getState(childId))
					.filter(Boolean),
			} as GroupContainerState;
		} else if (geometry?.type === 'label') {
			const component = geometry.components[Geometry2DComponents.UI] as LabelComponent;
			const parentObject = geometry.object3d.parent;
			const parentId = parentObject?.userData.entityId;

			return {
				id: geometry.id,
				type: 'label',
				text: component.text || '',
				parent: {
					id: parentId,
					type: parentObject?.type,
				},
				position: this.convert3DMapPositionToCoordinate(geometry.position),
				enabled: component.enabled,
				visible: component.visible,
				initialRank: convertNumberToCollisionRankingTier(component.initialRank),
				options: {
					rank: convertNumberToCollisionRankingTier(component.rank),
					appearance: component.appearance,
					interactive: geometry.components[Geometry2DComponents.Interaction] != null,
					verticalOffset: geometry.verticalOffset,
					attachTo: geometry.attachedTo,
				},
			} as LabelState;
		} else if (geometry?.type === 'marker') {
			const component = geometry.components[Geometry2DComponents.UI] as MarkerComponent;
			const parentObject = geometry.object3d.parent;
			const parentId = parentObject?.userData.entityId;

			return {
				id: geometry.id,
				type: 'marker',
				enabled: component.enabled,
				visible: component.visible,
				placement: component.options.placement,
				dynamicResize: component.options.dynamicResize,
				parent: {
					id: parentId,
					type: parentObject?.type,
				},
				position: this.convert3DMapPositionToCoordinate(geometry.position),
				element: component.containerEl,
				initialRank: convertNumberToCollisionRankingTier(component.initialRank),
				contentHTML: component.contentHtml,
				options: {
					rank: convertNumberToCollisionRankingTier(component.rank),
					interactive: geometry.components[Geometry2DComponents.Interaction] != null || false,
					verticalOffset: geometry.verticalOffset,
					attachTo: geometry.attachedTo,
					lowPriorityPin: component.options.lowPriorityPin,
				},
			} as MarkerState;
		} else if (geometry?.type === 'geometry') {
			const { color, hoverColor, initialColor, initialTopColor, topColor, altitude } =
				geometry.components[Geometry3DComponents.Style];
			const meshComponent = geometry.components[Geometry3DComponents.Mesh] as MeshComponent;
			const focusableComponent = geometry.components[GroupContainerComponents.Focusable] as FocusableComponent;
			const styleComponent = geometry.components[Geometry3DComponents.Style] as StyleComponent;
			const textureComponent = geometry.components[Geometry3DComponents.TextureComponent] as TextureComponent;
			const interactionComponent = geometry.components[Geometry3DComponents.Interaction] as InteractionComponent;
			const outlineComponent = geometry.components[Geometry3DComponents.Outline] as OutlineComponent;
			const parent = geometry.parentObject3D;

			return {
				id: geometry.id,
				type: 'geometry',
				opacity: styleComponent.opacity,
				initialColor,
				color,
				focusable: focusableComponent != null && focusableComponent.focusMesh != null,
				initialTopColor,
				topColor,
				side: styleComponent.side,
				hoverColor,
				hovered: Boolean(interactionComponent?.hover),
				height: styleComponent.height,
				visible: styleComponent.visible,
				outline: outlineComponent?.enabled,
				showImage: styleComponent.showImage,
				texture: textureComponent?.texture?.path,
				topTexture: textureComponent?.topTexture?.path,
				flipImageToFaceCamera: styleComponent.flipImageToFaceCamera,
				enableImageCollisions: styleComponent.enableImageCollisions,
				parent: {
					id: parent?.userData.entityId,
					type: parent?.type,
				},
				altitude,
				bevel: styleComponent.bevel,
				// TODO: should these be lazy getters? In which case we would only send a single copy of the state
				position: this.convert3DMapPositionToCoordinate(meshComponent.position),
				interactive: geometry.components[Geometry3DComponents.Interaction] != null,
				borderVisible: styleComponent.borderVisible,
				borderColor: styleComponent.borderColor,
				borderWidth: styleComponent.borderWidth,
			} as GeometryState;
		} else if (geometry?.type === 'model') {
			const modelComponent = geometry.components[Geometry3DComponents.Mesh];
			const styleComp = geometry.components[Geometry3DComponents.Style];
			const interactionComponent = geometry.components[Geometry3DComponents.Interaction] as InteractionComponent;

			return {
				id: geometry.id,
				type: 'model',
				position: modelComponent.mesh
					? this.convert3DMapPositionToCoordinate(modelComponent.mesh?.getWorldPosition(new Vector3()))
					: this.convert3DMapPositionToCoordinate(modelComponent.position),
				interactive: interactionComponent != null,
				material: styleComp.material,
				parent: geometry.parentObject3D?.parent
					? {
							id: geometry.parentObject3D.parent.userData.entityId,
							type: geometry.parentObject3D.parent.type,
					  }
					: undefined,
				visible: styleComp.visible,
				verticalOffset: styleComp.verticalOffset,
				color: styleComp.color,
				opacity: styleComp.opacity,
				rotation: styleComp.rotation ?? DEFAULT_ROTATION,
				scale: styleComp.scale ?? DEFAULT_SCALE,
			} as ModelState;
		} else if (geometry?.type === 'path') {
			const pathComponent = geometry.components[Geometry3DComponents.Mesh] as PathComponent;
			const interactionComponent = geometry.components[Geometry3DComponents.Interaction] as InteractionComponent;
			const styleComponent = geometry.components[Geometry3DComponents.Style] as StyleComponent;

			return {
				id: geometry.id,
				type: 'path',
				position: this.convert3DMapPositionToCoordinate(pathComponent.position),
				visible: styleComponent.visible,
				interactive: interactionComponent != null,
				completeFraction: pathComponent.completeFraction,
				color: styleComponent.color,
				accentColor: pathComponent.accentColor,
				width: pathComponent.width,
				animateArrowsOnPath: pathComponent.animateArrowsOnPath,
				displayArrowsOnPath: pathComponent.displayArrowsOnPath,
				verticalOffset: styleComponent.altitude,
				highlightCompleteFraction: pathComponent.highlightCompleteFraction,
				highlightStartFraction: pathComponent.highlightStartFraction,
				highlightEndFraction: pathComponent.highlightEndFraction,
				highlightColor: pathComponent.highlightColor,
				highlightWidthMultiplier: pathComponent.highlightWidthMultiplier,
			} satisfies PathState as PathState;
		} else if (geometry?.type === 'image') {
			const imageComp = geometry.components[Geometry3DComponents.Mesh] as ImageComponent;

			return {
				id: geometry.id,
				type: 'image',
				position: this.convert3DMapPositionToCoordinate(imageComp.position),
				visible: geometry.components[Geometry3DComponents.Style].visible,
				verticalOffset: imageComp.feature.properties.verticalOffset ?? 0,
				opacity: geometry.components[Geometry3DComponents.Style].opacity,
				flipImageToFaceCamera: geometry.components[Geometry3DComponents.Style].flipImageToFaceCamera,
				width: imageComp.feature.properties.width ?? 0,
				height: imageComp.feature.properties.height ?? 0,
				rotation: imageComp.feature.properties.rotation ?? 0,
			} as ImageState;
		} else if (
			geometry?.type === 'text3d' &&
			geometry.components[Geometry3DComponents.Mesh] instanceof Text3DComponent &&
			'textArea' in geometry.components[Geometry3DComponents.Mesh].feature.properties
		) {
			assetGeometryIsText3D(geometry);
			const text3DComp = geometry.components[Geometry3DComponents.Mesh];
			const styleComp = geometry.components[Geometry3DComponents.Style];
			const { textMesh } = text3DComp;

			const parent = text3DComp.mesh?.parent;
			if (parent instanceof GroupContainerObject3D) {
				parent.children.find(v => v.type === 'batchedtext')?.getWorldPosition(this.#batchoffsetTempVec);
			}
			this.#tempVec.set(0, 0, 0);
			const position = textMesh ? textMesh.getWorldPosition(this.#tempVec) : this.#tempVec;

			const state = Object.assign({}, DEFAULT_TEXT_STYLE, styleComp.getState(), {
				id: geometry.id,
				type: 'text3d' as const,
				position: this.convert3DMapPositionToCoordinate(position.add(this.#batchoffsetTempVec)), // text is not a children of batched text need this to get altitude
				visible: text3DComp.visible ?? false,
				content: (text3DComp.feature.properties as Text3DProperties).content ?? '',
			} as Text3DState);

			return state satisfies Text3DState as Text3DState;
		} else {
			return undefined;
		}
	}

	/**
	 * Set the state of the map view or any entity that was added, regardless of whether it is visible in the scene.
	 */
	setState(object: Partial<Omit<MapViewState, 'type'>>): void;
	setState<T extends EntityId<LabelState>>(object: T | T['id'], state: Partial<LabelState>): void;
	setState<T extends EntityId<MarkerState>>(object: T | T['id'], state: Partial<MarkerStateUpdate>): void;
	setState<T extends EntityId<GeometryGroupState>>(object: T | T['id'], state: Partial<GeometryGroupState>): void;
	setState<T extends EntityId<GroupContainerState>>(object: T | T['id'], state: Partial<GroupContainerState>): void;
	setState<T extends EntityId<GeometryState>>(object: T | T['id'], state: Partial<GeometryState>): void;
	setState<T extends EntityId<ShapeState>>(object: T | T['id'], state: Partial<ShapeState>): void;
	setState<T extends EntityId<PathState>>(object: T | T['id'], state: PathUpdateState): void;
	setState<T extends EntityId<ModelState>>(object: T | T['id'], state: Partial<UpdateModelState>): void;
	setState<T extends EntityId<ImageState>>(object: T | T['id'], state: Partial<ImageState>): void;
	setState<T extends EntityId<Text3DState>>(object: T | T['id'], state: Partial<UpdatableText3DState>): void;
	setState<T extends EntityId<PathState>>(object: T | T['id'], state: Partial<PathState>): void;
	setState<T extends EntityState>(object: T | T['id'], state: Partial<T>): void;
	setState(object: Record<string | number, any> | string | number, state?: Record<string, any>) {
		if (typeof object === 'object' && state == null) {
			if ('hoverColor' in object) {
				if (isColor(object.hoverColor)) {
					this.#state.hoverColor = object.hoverColor ?? this.#state.hoverColor;
				} else {
					Logger.warn(`Failed to update global hover color. Invalid color "${object.hoverColor}".`);
				}
			}
			if ('text3dHoverColor' in object) {
				this.#state.text3dHoverColor = object.text3dHoverColor ?? this.#state.text3dHoverColor;
			}

			return;
		}

		const geometry = getGeometryByGeometryId(this.#state, object);

		if (geometry == null) {
			return;
		}

		if (geometry.type === 'custom-geometry') {
			const geometry3D = this.#state.geometry3DMap.get(geometry.id);
			if (
				geometry3D instanceof Geometry3D &&
				geometry3D.components[Geometry3DComponents.Mesh] instanceof CustomGeometryComponent
			) {
				const _state = state as Partial<ShapeState>;
				updateVisibility(geometry, this.#state, _state.visible);
				updateAltitude(geometry, this.#state, _state.altitude);
				updatePosition(geometry, _state.position, this.convertTo3DMapPosition);
				updateParent(geometry, this.getParentContainer(_state.parent, false) as GroupContainerObject3D);
				if (updateInteractivity(geometry, _state.interactive, this.#state)) {
					this.#invalidate3DEntities();
				}
			}
		} else if (geometry.type === 'path') {
			let _state = state as Partial<PathState>;
			if (updateInteractivity(geometry, _state.interactive, this.#state)) {
				this.#invalidate3DEntities();
			}
			updateAltitude(geometry, this.#state, (_state as PathState).verticalOffset);
			handlePathUpdate(geometry, this.#state, _state);
		} else if (geometry.type === 'model') {
			let _state = state as Partial<ModelState>;
			if (updateInteractivity(geometry, _state.interactive, this.#state)) {
				this.#invalidate3DEntities();
			}
			updateVisibility(geometry, this.#state, _state.visible);
			handleModelStyleUpdate(geometry, _state);
			updateClippingPlane(geometry, _state);
		} else if (geometry.type === 'group-container') {
			const _state = state as Partial<GroupContainerState>;
			if (updateVisibility(geometry, this.#state, _state.visible)) {
				this.#systems.pathSystem.verticalDistanceDirty = true;
				this.#systems.geometryInFocusSystem.focusablesDirty = true;
			}
			if (updateAltitude(geometry, this.#state, _state.altitude)) {
				this.#systems.pathSystem.verticalDistanceDirty = true;
			}
			if (updateInteractivity(geometry, _state.interactive, this.#state)) {
				this.#invalidate3DEntities();
			}
			if (updateOpacity(geometry, this.#state, _state.opacity)) {
				this.#systems.outlineInterpolationSystem.outlineOpacitiesDirty = true;
			}
			if (_state.preloadGeometry && geometry.userData.preloadGeometry !== true) {
				geometry.userData.preloadGeometry = true;
				this.#systems.outlineInterpolationSystem.geometries3DDirty = true;
			}
			if (updateFocusable(geometry, _state.focusable)) {
				this.#systems.geometryInFocusSystem.focusablesDirty = true;
			}
		} else if (geometry.type === 'geometry-group') {
			const _state = state as Partial<GeometryGroupState>;
			updateVisibility(geometry, this.#state, _state.visible);
			updateGroupColor(geometry, this.#state, _state);
			updateOutline(geometry, this.#state, _state.outline);
			if (_state.height != null) {
				updateHeight(geometry, this.#state, _state.height);
			}
			updateAltitude(geometry, this.#state, _state.altitude);
			if (updateGroupTexture(geometry, this.#state, _state)) {
				this.#systems.textureSystem.dirty = true;
			}
			updateGroupTexture(geometry, this.#state, _state);
			updateGroupShading(geometry, this.#state, _state.shading);
			if (updateOpacity(geometry, this.#state, _state.opacity)) {
				this.#systems.outlineInterpolationSystem.outlineOpacitiesDirty = true;
			}
			if (updateInteractivity(geometry, _state.interactive, this.#state)) {
				this.#invalidate3DEntities();
				this.#invalidate2DEntities();
			}
			updateSide(geometry, this.#state, _state.side);
		} else if (geometry.type === 'geometry') {
			if (geometry.components[Geometry3DComponents.Mesh] instanceof MeshComponent) {
				const _state = state as Partial<GeometryState>;
				updateVisibility(geometry, this.#state, _state.visible);
				updateOutline(geometry, this.#state, _state.outline);
				updateImageProps(geometry, _state);
				if (updateFocusable(geometry, _state.focusable)) {
					this.#systems.geometryInFocusSystem.focusablesDirty = true;
				}
				if (updateIndividualGeometryRenderOrder(geometry, _state.renderOrder)) {
					this.#systems.meshDetachmentSystem.dirty = true;
				}
				if (updateIndividualGeometryOpacity(geometry, _state.opacity)) {
					this.#systems.meshDetachmentSystem.dirty = true;
					this.#systems.interactionSystem.dirty3D = true;
					this.#systems.outlineInterpolationSystem.outlineOpacitiesDirty = true;
				}
				updateFlipImageToFaceCamera(geometry, _state.flipImageToFaceCamera);
				updateEnableImageCollisions(geometry, _state.enableImageCollisions);
				updateAltitude(geometry, this.#state, _state.altitude);
				if (_state.height != null) {
					updateHeight(geometry, this.#state, _state.height);
				}
				if (updateIndividualGeometryTexture(geometry, _state)) {
					this.#systems.meshDetachmentSystem.dirty = true;
					this.#systems.textureSystem.dirty = true;
				}
				updateColor(geometry, _state);
				updateHoverColor(geometry, _state.hoverColor);
				updateInteractivity(geometry, _state.interactive, this.#state);
				updateHovered(geometry, _state.hovered);
				updateSide(geometry, this.#state, _state.side);
				updateBorder(geometry, _state);
			}
			this.#invalidate3DEntities();
		} else if (geometry.type === 'label' || geometry.type === 'marker') {
			const _state = state as Partial<LabelState | MarkerState>;
			updateLabel(geometry, state as Partial<LabelState>, this.#state);
			if (updateMarker(geometry, state as Partial<MarkerState>, this.#state)) {
				this.#invalidate2DEntities();
				this.#systems.domMutationSystem.dirty = true;
			}

			updatePosition(geometry, _state.position, this.convertTo3DMapPosition);
			if (updateParent(geometry, this.getParentContainer(_state.parent, false) as GroupContainerObject3D)) {
				this.#invalidate2DEntities();
				this.#systems.panBoundsSystem.dirty = true;
			}
			updateEnabled(geometry, _state.enabled);
			updateVisibility(geometry, this.#state, _state.visible);
		} else if (geometry.type === 'image') {
			const _state = state as Partial<ImageState>;
			updateVisibility(geometry, this.#state, _state.visible);
			updateOpacity(geometry, this.#state, _state.opacity);
			updateFlipImageToFaceCamera(geometry, _state.flipImageToFaceCamera);
		} else if (geometry.type === 'text3d') {
			const _state = state as Partial<UpdatableText3DState>;
			handleText3DUpdate(geometry, this.#state, _state);
		}
		this.render();
	}

	/**
	 * Project a clientX/clientY screen coordinate to a geographic coordinate
	 */
	projectScreenXYToCoordinate = (
		clientX: MouseEvent['clientX'],
		clientY: MouseEvent['clientY'],
		useWorldPlane = false,
	) => {
		// Get fresh container position to handle scroll correctly
		const containerRect = this.container.getBoundingClientRect();
		const offsetX = ((clientX - containerRect.left) / this.#state.canvasWidth) * 2 - 1;
		const offsetY = -((clientY - containerRect.top) / this.#state.canvasHeight) * 2 + 1;
		const point = new Vector2(offsetX, offsetY);
		let pointThreeD: Vector3 | undefined;
		let groupContainers: GroupContainerObject3D[] | undefined;
		if (useWorldPlane) {
			raycaster.setFromCamera(point, this.#cameraObject);
			pointThreeD = raycaster.intersectObject(this.#worldPlane, false)[0]?.point;
		} else {
			const intersects = this.#systems.interactionSystem.getThreeDIntersectsFromXY(point);
			pointThreeD = intersects?.[0]?.point;
			groupContainers = this.#systems.interactionSystem.getGroupContainerIntersectsFromXY(point);
		}

		if (pointThreeD) {
			return {
				coordinate: this.convert3DMapPositionToCoordinate(pointThreeD),
				groupContainers,
			};
		}

		return undefined;
	};

	/**
	 * Project a geographic coordinate to a screen coordinate
	 */
	projectCoordinateToScreenXY = (coordinate: Position) => {
		const vector3D = this.convertTo3DMapPosition(coordinate[1], coordinate[0], coordinate[2] || 0);
		const vector2D = new Vector2();

		this.#project(vector2D, vector3D);

		return {
			x: vector2D.x,
			y: vector2D.y,
		};
	};

	/**
	 * Convert a GeoJSON position to a 3D map position.
	 *
	 * @param position - The GeoJSON position to convert.
	 * @param vector - Optional vector to copy the result into instead of creating a new vector.
	 * @returns The converted 3D map position.
	 */
	convertTo3DMapPosition(position: GeoJsonPosition, vector?: Vector3): Vector3;
	/**
	 * Convert latitude and longitude to a 3D map position.
	 *
	 * @param latitude - The latitude to convert.
	 * @param longitude - The longitude to convert.
	 * @param altitude - Optional altitude to include in the conversion. Defaults to 0.
	 * @param vector - Optional vector to copy the result into instead of creating a new vector.
	 * @returns The converted 3D map position.
	 */
	convertTo3DMapPosition(latitude: number, longitude: number, altitude?: number, vector?: Vector3): Vector3;

	convertTo3DMapPosition(
		latitudeOrPosition: number | GeoJsonPosition,
		longitude?: number | Vector3,
		altitude = 0,
		vector?: Vector3,
	) {
		if (Array.isArray(latitudeOrPosition)) {
			return this.convertTo3DMapPosition(
				latitudeOrPosition[1],
				latitudeOrPosition[0],
				latitudeOrPosition[2] ?? altitude,
				typeof longitude === 'function' ? longitude : undefined,
			);
		}

		if (typeof longitude !== 'number') throw new Error('convertTo3DMapPosition failed: longitude must be a number');

		const { x, y } = geographicToCartesian(
			this.#state.center?.[1] ?? 0,
			this.#state.center?.[0] ?? 0,
			latitudeOrPosition,
			longitude,
		);

		if (vector) {
			vector.set(x, y, altitude);

			return vector;
		}

		return new Vector3(x, y, altitude);
	}

	/**
	 * @internal
	 */
	convert3DMapPositionToCoordinate = (v: Vector3): Position => {
		const { lat, lon } = cartesianToGeographic(this.#state.center?.[1] ?? 0, this.#state.center?.[0] ?? 0, v.x, v.y);

		return [lon, lat, v.z];
	};

	/**
	 * Sets the background color of the renderer. Only applies to "standalone mode"
	 */
	setBackgroundColor = (color: string, alpha: number) => {
		this.#renderer.setBackgroundColor(color, alpha);
	};

	/**
	 * Returns  the background color of the renderer. Only applies to "standalone mode"
	 */
	get backgroundColor() {
		if (typeof this.#renderer.backgroundColor === 'string') {
			return this.#renderer.backgroundColor;
		}

		return `#${this.#renderer.backgroundColor.getHexString()}`;
	}

	/**
	 * Returns  the background alpha of the renderer. Only applies to "standalone mode"
	 */
	get backgroundAlpha() {
		return this.#renderer.backgroundAlpha;
	}

	/**
	 * internal
	 */
	getThreeRenderer() {
		return this.#renderer;
	}

	/**
	 * Returns the current scale of the map in meters per pixel.
	 */
	getMetersPerPixel = () => {
		return this.#systems.cameraSystem.getCurrentMetersPerPixel();
	};

	/**
	 * Filters out layers at a certain point on the outdoor map. This can be used to hide the outdoor building footprint underneath the 3D geometry.
	 * @param bbox The bounding box to filter layers under.
	 * @param polygons The polygons to filter layers under.
	 * @param layers The layers to filter out.
	 */
	hideOutdoorLayersIntersectingPolygons = (
		bbox: BBox,
		polygons: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[],
		layers: string[],
	) => {
		this.#systems.outdoorLayersSystem.hideLayersIntersectingPolygons(bbox, polygons, layers);
		this.render();
	};

	/**
	 * Get the center of a geometry or bounding box.
	 * @param geometryOrBoundingBox Geometry, geometry id, or bounding box to get the center of.
	 */
	getCenter(geometryOrBoundingBox: string | number | EntityId<EntityState> | Position[]) {
		const bbox = Array.isArray(geometryOrBoundingBox)
			? geometryOrBoundingBox
			: this.getBoundingArea(geometryOrBoundingBox);

		return bbox ? getBoundingBoxCenter(bbox) : undefined;
	}

	#cameraFrustum = new Frustum();
	#viewProjectionMatrix = new Matrix4();

	/**
	 * Checks if the given entity is within the current camera's view frustum.
	 * This method is useful for determining if an entity is visible on the screen.
	 *
	 * @param target - The entity to check. This can be an object with `id` and `type` properties,
	 *                 a string representing the entity's ID, or an entity state object.
	 * @returns - Returns `true` if the entity is within the camera's view, otherwise `false`.
	 */
	isInView(target: { id: EntityState['id']; type: EntityState['type'] } | string | EntityState) {
		const geometry = getGeometryByGeometryId(this.#state, target);

		this.#viewProjectionMatrix
			.identity()
			.multiply(this.#cameraObject.projectionMatrix)
			.multiply(this.#cameraObject.matrixWorldInverse);
		this.#cameraFrustum.setFromProjectionMatrix(this.#viewProjectionMatrix);

		if (geometry instanceof Geometry2D) {
			return this.#cameraFrustum.containsPoint(geometry.position); // 2D object we use position for now
		} else if (geometry instanceof Geometry3D) {
			const mesh = geometry?.components[Geometry3DComponents.Mesh];
			if (mesh instanceof CustomGeometryComponent || mesh instanceof ModelComponent) {
				return this.#cameraFrustum.containsPoint(this.convertTo3DMapPosition(mesh.feature.geometry.coordinates));
			} else if (mesh instanceof MeshComponent) {
				const box = new Box3(
					this.convertTo3DMapPosition(mesh.featureBbox[1], mesh.featureBbox[0], 0),
					this.convertTo3DMapPosition(mesh.featureBbox[3], mesh.featureBbox[2], 0),
				);

				return this.#cameraFrustum.intersectsBox(box);
			}
		}

		return false;
	}

	/**
	 * Return a list of Geometry2D IDs currently rendered on screen.
	 *
	 * @param options - The options to query.
	 * @param options.screenOffsets - Optional offsets to account for on the edges of the screen.
	 * @param options.fullyContains - Whether to exclude entities that intersect with the screen edges after accounting for offsets.
	 * @returns A list of IDs.
	 */
	queryGeometry2DInView(options?: { screenOffsets?: Partial<InsetPadding>; fullyContains?: boolean }): string[] {
		const { screenOffsets, fullyContains } = options ?? {};

		const [x, y, w, h] = rectFromScreenOffsets(this.container, screenOffsets);
		const rectangle = new Rectangle(x, y, w, h);
		const candidates = this.#systems.collisionSystem.visibleCollidersQTree.queryRect(rectangle);
		const intersects = candidates.filter(c => (fullyContains ? rectangle.contains(c) : rectangle.intersects(c)));
		const entities = intersects
			.map(i => this.#state.geometry2DMap.get(i.userData.entityId))
			.filter(e => e != null)
			.filter(e => {
				if (e.components[Geometry2DComponents.UI] instanceof LabelComponent) {
					return true;
				}
				if (e.components[Geometry2DComponents.UI] instanceof MarkerComponent) {
					return true;
				}
				return false;
			});
		return entities.map(e => e.id.toString());
	}

	#onRenderError = (e: Error) => {
		Logger.error('Render failed: ', e, e.stack ?? 'No stack trace available');
		this.publish('render-error', { error: e });
	};

	/**
	 * Perform a render of the scene. This is called internally when something changes, but can be called when adding external models
	 * The render happens in an animation frame, not matter how frequently it is called
	 * @method render
	 */
	render = () => {
		if (this.#systems?.renderSystem == null || this.aborted) {
			return;
		}

		if (this.map && (this.mode === 'outdoors-interleaved' || this.mode === 'outdoors-overlay')) {
			this.map.triggerRepaint();
		} else {
			return this.#systems.renderSystem.update().catch(this.#onRenderError);
		}
	};

	/**
	 * Perform a synchronous render of the scene. This bypasses the animation frame and renders immediately.
	 *
	 * Note: Only use this if you have an animation frame setup elsewhere
	 */
	renderSync = () => {
		if (this.aborted) {
			return Promise.resolve();
		}

		return this.#systems.renderSystem.update(true).catch(this.#onRenderError);
	};

	#aggregateUserEventsStart = debounce(
		() => {
			this.publish('user-interaction-start', undefined);
		},
		0,
		true,
	);

	#aggregateUserEventsEnd = debounce(
		() => {
			this.publish('user-interaction-end', undefined);
			this.render();
		},
		0,
		true,
	);

	/**
	 * Register a MapLibre event listener
	 */
	onMapLibreEvent(event: LiteralUnion<keyof MapLibreMapEventType, string>, handler: (...args: any[]) => void) {
		if (!this.map) {
			Logger.warn(`Tried to add event listener "${event}" but MapLibre map is not initialized.`);
			return;
		}
		this.map.on(event, handler);
		this.#mapLibreEventListeners[event] = this.#mapLibreEventListeners[event] || [];
		this.#mapLibreEventListeners[event].push(handler);
	}

	/**
	 * Remove a MapLibre event listener
	 */
	offMapLibreEvent(event: keyof MapLibreMapEventType, handler: (...args: any[]) => void) {
		if (!this.map || !Array.isArray(this.#mapLibreEventListeners[event])) {
			return;
		}
		this.map.off(event, handler);
		this.#mapLibreEventListeners[event] = this.#mapLibreEventListeners[event].filter(h => h !== handler);
	}

	/**
	 * Returns true if the renderer has been aborted.
	 */
	get aborted() {
		return this.#abortController.signal.aborted;
	}

	/**
	 * Register a handler once to be called when the renderer is aborted.
	 * The handler will be removed after it is called.
	 */
	onAbort(handler: () => void) {
		this.#abortController.signal.addEventListener('abort', handler, { once: true });
	}

	/**
	 * @internal for testing
	 */
	getSystems() {
		return this.#systems;
	}

	/**
	 * @internal for testing
	 */
	getInternalState() {
		return this.#state;
	}

	/** @internal */
	getInternalTweenGroup() {
		return this.#state.internalTweenGroup;
	}

	getExternalTweenGroup() {
		return this.#state.externalTweenGroup;
	}

	async #loadEnvMap() {
		return TextureLoader.load(blaze_thick_compressed).then(texture => {
			if (!this.#scene) return texture;
			this.#scene.environment?.dispose();
			texture.mapping = EquirectangularReflectionMapping;
			this.#scene.environment = texture;
			this.render();

			return texture;
		});
	}

	computeMinZOffsetBetweenContainers(
		lowerContainer: GroupContainerObject3D,
		upperContainer: GroupContainerObject3D,
		debug?: boolean,
	) {
		if (!lowerContainer || !upperContainer) return -1;

		assertType(lowerContainer, 'group-container');
		assertType(upperContainer, 'group-container');

		const lowerObjectNDCBox = projectToBox2D(lowerContainer, this.getThreeCamera());
		const upperObjectClone = upperContainer.clone();
		const upperObjectNDCBox = projectToBox2D(upperObjectClone, this.getThreeCamera());

		if (!isFiniteBox(lowerObjectNDCBox) || !isFiniteBox(upperObjectNDCBox)) {
			return -1;
		}

		let zOffset = 0;
		let overlaps: boolean;

		if (debug) {
			const debugElements = this.container.querySelectorAll('.debug-box');
			debugElements.forEach(el => el.remove());
		}

		do {
			if (zOffset > 10000) {
				return -1;
			}
			zOffset += 20;
			upperObjectClone.position.z = zOffset;
			upperObjectClone.updateMatrixWorld();
			upperObjectNDCBox.makeEmpty();
			projectToBox2D(upperObjectClone, this.#cameraObject, upperObjectNDCBox);
			overlaps = lowerObjectNDCBox.intersectsBox(upperObjectNDCBox);
			if (debug) {
				// Draw projected boxes
				const bottomBox = drawProjectedBox(lowerObjectNDCBox, 'blue', this.container);
				const topBox = drawProjectedBox(upperObjectNDCBox, overlaps ? 'red' : 'green', this.container);

				// Add debug class for easy removal
				bottomBox.classList.add('debug-box');
				topBox.classList.add('debug-box');
			}
		} while (overlaps);

		return zOffset;
	}

	/**
	 * Subscribe a function to an event.
	 *
	 * @param eventName An event name which, when fired, will call the provided
	 * function.
	 * @param fn A callback that gets called when the corresponding event is fired. The
	 * callback will get passed an argument with a type that's one of event payloads.
	 * @example
	 * // Subscribe to the 'click' event
	 * const handler = (event) => {
	 *  const { coordinate } = event;
	 *  const { latitude, longitude } = coordinate;
	 * 	console.log(`Map was clicked at ${latitude}, ${longitude}`);
	 * };
	 * map.on('click', handler);
	 */
	on = <EventName extends keyof MapEvent>(eventName: EventName, fn: (payload: MapEventPayload<EventName>) => void) => {
		if (this.aborted) {
			return;
		}

		super.on(eventName, fn);

		if (
			['outdoor-view-loaded', 'outdoor-style-loaded'].includes(eventName) &&
			this.mode === 'standalone' &&
			process.env.NODE_ENV !== 'test'
		) {
			Logger.warn(
				`"${eventName}" was subscribed to but outdoor view is disabled. The event has been triggered immediately. Enable outdoor view or remove the listener.`,
			);
			this.publish(eventName);
		}
	};

	/**
	 * Unsubscribe a function previously subscribed with {@link on}
	 *
	 * @param eventName An event name to which the provided function was previously
	 * subscribed.
	 * @param fn A function that was previously passed to {@link on}. The function must
	 * have the same reference as the function that was subscribed.
	 * @example
	 * // Unsubscribe from the 'click' event
	 * const handler = (event) => {
	 * 	console.log('Map was clicked', event);
	 * };
	 * map.off('click', handler);
	 */
	off = <EventName extends keyof MapEvent>(eventName: EventName, fn: (payload: MapEventPayload<EventName>) => void) => {
		super.off(eventName, fn);
	};

	/**
	 * @hidden
	 * Returns the current cursor decided by Mappedin. This is useful when integrating with other overlays to show the correct user interaction.
	 */
	getCursor = () => {
		return this.#systems.interactionSystem.getCursor();
	};

	/**
	 * Clears MapLibre and core event listeners.
	 */
	#cleanupEventListeners = () => {
		this.#state.entityScene.removeEventListener('childadded', this.#invalidate3DEntities);
		this.#state.entityScene.removeEventListener('childadded', this.#invalidate2DEntities);
		this.#state.entityScene.removeEventListener('childremoved', this.#invalidate3DEntities);
		this.#state.entityScene.removeEventListener('childremoved', this.#invalidate2DEntities);
		window.removeEventListener('resize', this.#handleWindowResizeWithEvent, false);
		this.#matchMediaMinResolution.removeEventListener('change', this.#handlePixelRatioChange);
		// Clean up MapLibre event listeners
		if (this.map) {
			for (const event in this.#mapLibreEventListeners) {
				for (const handler of this.#mapLibreEventListeners[event]) {
					this.map.off(event as any, handler as any);
				}
			}
			this.#mapLibreEventListeners = {} as any;
		}
		this['_subscribers'] = {};
	};

	/**
	 * Safe DOM cleanup that handles edge cases
	 */
	#cleanupDOMElements = () => {
		try {
			if (this.#mode !== 'outdoors-overlay') {
				if (this.map) {
					this.map.remove();
				}
				if (this.container && this.rendererDomElement && this.container.contains(this.rendererDomElement)) {
					this.container.removeChild(this.rendererDomElement);
				}
			}

			if (this.container) {
				if (this.#twoDEntityScene && this.container.contains(this.#twoDEntityScene)) {
					this.container.removeChild(this.#twoDEntityScene);
				}
				if (this.#collisionEngineContainerEl && this.container.contains(this.#collisionEngineContainerEl)) {
					this.container.removeChild(this.#collisionEngineContainerEl);
				}
			}
		} catch (error) {
			Logger.warn('Error during DOM cleanup, some elements may not have been removed:', error);
		}
	};

	/**
	 * Destroys instance and frees resources
	 */
	destroy = () => {
		if (!this.#systems) return;
		this.#systems.textureSystem.destroy();
		if (!this.aborted) {
			this.#abortController.abort();
		}
		this.#cleanupEventListeners();
		this.remove('__scene');
		this.#systems.renderSystem.destroy();
		this.#systems.cameraSystem.dispose();
		this.#systems.exporterSystem.destroy();
		this.#systems.outdoorLayersSystem.destroy();

		this.#systems.borderSystem.destroy();
		if (this.#resizeObserver) {
			this.#resizeObserver.disconnect();
		}
		this.#renderer.destroy();
		this.#scene?.traverse(obj => {
			destroyObject3D(obj);
			if ('dispose' in obj && typeof obj.dispose === 'function') {
				obj.dispose();
			}
		});
		this.#scene = undefined;
		this.#systems.htmlControlsSystem.destroy();
		this.#systems.collisionSystem.destroy();
		this.#systems.interactionSystem.destroy();
		this.#systems.domMutationSystem.destroy();
		this.#systems.geometryInFocusSystem.destroy();
		this.#systems.occlusion2DSystem.destroy();
		// @ts-expect-error
		this.#systems = undefined;
		this.#cleanupDOMElements();
		(this.#renderer as any) = undefined;
	};
	#tempVec = new Vector3();
	#batchoffsetTempVec = new Vector3();
}

const tempVec = new Vector3();
export default Core;
