import type { Feature, MultiPolygon, FloorProperties as MVFFloor, Polygon } from '@mappedin/mvf-v2';
import type { Annotation, Connection, Door, MapDataInternal, PointOfInterest, Space, MapObject, FloorStack } from '.';
import type Coordinate from './coordinate';
import type { FloorWithLocale, MVFFloorFeature } from './types';
import BaseMapData from './base-object';
import type { IFocusable } from '../types';
import { getCenterCoordinate } from './utils';

/**
 * A class representing floor data within the map.
 *
 * Floors are used to represent different levels within a map, each containing various map elements.
 */
class Floor extends BaseMapData implements IFocusable {
	/**
	 * @internal
	 */
	static readonly __type = 'floor';

	/**
	 * @internal
	 */
	readonly __type = Floor.__type;

	#data: MapDataInternal;
	#cache: Partial<{
		spaces: Space[];
		objects: MapObject[];
		connections: Connection[];
		doors: Door[];
		annotations: Annotation[];
		pois: PointOfInterest[];
		floorStack: FloorStack;
		center: Coordinate;
	}> = {};
	#mvfData: MVFFloorFeature;

	/**
	 * Checks if the provided instance is of type Floor.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Floor, false otherwise.
	 */
	static is(instance: object): instance is Floor {
		return (instance as { __type: string }).__type === Floor.__type;
	}

	/**
	 * @internal
	 */
	constructor(data: MapDataInternal, options: { mvfData: MVFFloorFeature }) {
		super(options.mvfData.properties.id, data);
		this.#data = data;
		this.#mvfData = options.mvfData;
	}

	#translate<T extends keyof MVFFloor>(prop: T) {
		const value = this.#mvfData.properties[prop];

		return this.#data.getPropTranslation(this.__type, prop, this.id, value as NonNullable<FloorWithLocale>[T]);
	}

	/** @internal */
	get focusTarget() {
		return this;
	}

	/**
	 * Gets the name of the floor.
	 *
	 * @returns {string} The name of the floor.
	 */
	get name(): string {
		// compatibility for outdated mvf data
		if (!('name' in this.#mvfData.properties)) {
			return this.#mvfData.properties.externalId ?? '';
		}

		const name = this.#translate('name');
		return typeof name === 'string' ? name : '';
	}
	/**
	 * Gets the short name of the floor (i.e. "F1", "L, "2", etc.).
	 * Used in space constrained lists and Often aligns with what is displayed on an elevator.
	 *
	 * @returns {string} The short name of the floor.
	 */
	get shortName(): string {
		// compatibility for outdated mvf data
		if (!('shortName' in this.#mvfData.properties)) {
			return this.#mvfData.properties.externalId ?? '';
		}

		const shortName = this.#translate('shortName');
		return typeof shortName === 'string' ? shortName : '';
	}

	/**
	 * Gets the subtitle of the floor.
	 *
	 * @returns {string} The subtitle of the floor.
	 */
	get subtitle(): string {
		const subtitle = this.#translate('subtitle');
		return typeof subtitle === 'string' ? subtitle : '';
	}

	/**
	 * Gets the external ID of the floor.
	 *
	 * @returns {string} The external ID of the floor.
	 */
	get externalId(): string {
		return this.#mvfData.properties.externalId ?? '';
	}

	/**
	 * Gets the elevation of the floor.
	 *
	 * @returns {number} The elevation of the floor.
	 */
	get elevation(): number {
		return this.#mvfData.properties.elevation;
	}

	/**
	 * Gets the max height of the floor if there is one.
	 *
	 * @returns {number | undefined} The max height of the floor.
	 */
	get maxHeight(): number | undefined {
		return this.#mvfData.properties.maxHeight;
	}

	/**
	 * Gets the spaces ({@link Space}) located on this floor.
	 *
	 * @returns {Space[]} An array of Space objects on this floor.
	 */
	get spaces(): Space[] {
		if (this.#cache.spaces == null) {
			const spacesOnThisFloor = this.#data.spaces.filter(space => space.floor.id === this.id);

			this.#cache.spaces = spacesOnThisFloor;
		}

		return this.#cache.spaces;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Space.
	 */
	get geoJSON(): Feature<Polygon | MultiPolygon, null> {
		return {
			type: 'Feature',
			properties: null,
			geometry: this.#mvfData.geometry ?? { type: 'Polygon', coordinates: [] },
		};
	}

	/**
	 * Gets the objects ({@link MapObject}) located on this floor.
	 *
	 * @returns {MapObject[]} An array of MapObject objects on this floor.
	 */
	get objects(): MapObject[] {
		if (this.#cache.objects == null) {
			const objectsOnThisFloor = this.#data.objects.filter(obstruction => obstruction.floor.id === this.id);

			this.#cache.objects = objectsOnThisFloor;
		}

		return this.#cache.objects;
	}

	/**
	 * Gets the connections ({@link Connection}) associated with this floor.
	 *
	 * @returns {Connection[]} An array of Connection objects linked to this floor.
	 */
	get connections(): Connection[] {
		if (this.#cache.connections == null) {
			const connectionsOnFloor = this.#data.connections.filter(connection => connection.floors.includes(this));
			this.#cache.connections = connectionsOnFloor;
		}

		return this.#cache.connections;
	}

	/**
	 * Gets the doors ({@link Door}) located on this floor.
	 *
	 * @returns {Door[]} An array of Door objects on this floor.
	 */
	get doors(): Door[] {
		if (this.#cache.doors == null) {
			const doorsOnFloor = this.#data.doors.filter(door => door.floor.id === this.id);
			this.#cache.doors = doorsOnFloor;
		}

		return this.#cache.doors;
	}

	/**
	 * Gets the annotations ({@link Annotation}) associated with this floor.
	 *
	 * @returns {Annotation[]} An array of Annotation objects linked to this floor.
	 */
	get annotations(): Annotation[] {
		if (this.#cache.annotations == null) {
			const annotationsOnFloor = this.#data.annotations.filter(annotation => annotation.floor.id === this.id);
			this.#cache.annotations = annotationsOnFloor;
		}

		return this.#cache.annotations;
	}

	/**
	 * Gets the points of interest ({@link PointOfInterest}) located on this floor.
	 *
	 * @returns {PointOfInterest[]} An array of PointOfInterest objects on this floor.
	 */
	get pois(): PointOfInterest[] {
		if (this.#cache.pois == null) {
			const poisOnFloor = this.#data.pointsOfInterest.filter(poi => poi.floor.id === this.id);
			this.#cache.pois = poisOnFloor;
		}

		return this.#cache.pois;
	}

	/**
	 * Gets the FloorStack ({@link FloorStack}) that this floor belongs to.
	 *
	 * @returns {FloorStack} The FloorStack that this floor belongs to.
	 */
	get floorStack(): FloorStack {
		if (this.#cache.floorStack == null) {
			const floorStackForFloor = this.#data.floorStacks.find(floorStack => floorStack.floors.includes(this));
			this.#cache.floorStack = floorStackForFloor;
		}

		return this.#cache.floorStack!;
	}

	get center(): Coordinate {
		if (this.#cache.center == null) {
			this.#cache.center = getCenterCoordinate(this.#mvfData, this.id);
		}

		return this.#cache.center;
	}

	/**
	 * Serializes the floor data to JSON.
	 *
	 * @returns An object representing the floor.
	 */
	toJSON() {
		return {
			__type: Floor.__type,
			id: this.id,
			name: this.name,
			shortName: this.shortName,
			subtitle: this.subtitle,
			elevation: this.elevation,
			maxHeight: this.maxHeight,
			spaces: this.spaces.map(space => space.id),
			objects: this.objects.map(object => object.id),
			connections: this.connections.map(connection => connection.id),
			doors: this.doors.map(door => door.id),
			annotations: this.annotations.map(annotation => annotation.id),
			pois: this.pois.map(poi => poi.id),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of globalThis.Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Floor;
