import { notNull } from './not-null';

const control = (function createFetchControl() {
	let fetch: Window['fetch'] | undefined = globalThis.fetch;

	return {
		fetch(...args: Parameters<Window['fetch']>): ReturnType<Window['fetch']> {
			if (!notNull(fetch))
				throw new Error(
					'fetch is not available in your environment. Pass your own function into `setFetchFn` (node-fetch, etc)',
				);

			return fetch?.(...args);
		},

		setFetch(f: Window['fetch']) {
			fetch = f;
		},
	};
})();

export const fetch = control.fetch;
