import type { LineSegments, PlaneGeometry, MeshLambertMaterial } from 'three';
import { Vector3, Mesh } from 'three';
import type { Position } from '../types';
import type { Feature, Point } from 'geojson';
import { Geometry3DObject3D } from '../entities/geometry3d';
import { geoJSONPoint } from '../utils/schema-validation';
import { z } from 'zod';

export class EntityMesh extends Mesh {
	userData: {
		entityId: string | number;
	} = {
		entityId: '',
	};
}

export type ImageStyle = {
	/**
	 * Attempt to keep the image facing the camera as much as possible
	 */
	flipImageToFaceCamera?: boolean;
	/**
	 * url of the image
	 */
	url: string;
};

export type ImageProperties = {
	width: number;
	height: number;
	rotation?: number;
	verticalOffset?: number;
};

export const addImageFeatureStyleSchema = z.object({
	flipImageToFaceCamera: z.boolean().optional(), // Optional boolean
	url: z.string(), // Required string
});

export const addImageFeatureSchema = z.object({
	type: z.enum(['Feature']), // Required enum with 'Feature'
	properties: z.object({
		width: z.number(), // Required number
		height: z.number(), // Required number
		rotation: z.number().min(0).max(360).optional(), // Optional number with min and max
		verticalOffset: z.number().optional(), // Optional number
	}),
	geometry: geoJSONPoint, // Assuming geoJSONPoint would be another Zod schema for validation
});

export function validateImage(id: string | number, feature: Feature<Point, ImageProperties>, style: ImageStyle) {
	z.union([z.number(), z.string()]).parse(id);
	addImageFeatureSchema.parse(feature);
	addImageFeatureStyleSchema.parse(style);
}

/**
 * State representing an Image
 */
export type ImageState = {
	readonly id: string | number;
	readonly type: 'image';
	/**
	 * The position of the image in [lon, lat]
	 */
	readonly position: Position;
	/**
	 * The offset of the image in the z direction
	 */
	readonly verticalOffset: number;
	/**
	 * Whether the image is visible
	 */
	visible: boolean;
	width: number;
	height: number;
	rotation: number;
	/**
	 * Attempt to keep the image facing the camera as much as possible
	 */
	flipImageToFaceCamera: boolean;
	/**
	 * The opacity of the image
	 */
	opacity: number;
};

export class ImageComponent {
	// Pointer to mesh for this component, which is either the mesh itself or a merged geometry mesh
	// if this component was optimized
	mesh = new Geometry3DObject3D();
	imageMesh?: EntityMesh;

	// Pointer to outline mesh for this component, again, either the outline mesh itself or a merged geometry mesh
	outline?: LineSegments;

	readonly type = 'image';

	instanceIndex = -1;
	geometry?: PlaneGeometry;
	material?: MeshLambertMaterial;
	feature: Feature<Point, ImageProperties>;

	constructor(feature: Feature<Point, ImageProperties>) {
		this.feature = feature;
	}

	get visible() {
		return this.mesh?.visible ?? false;
	}

	set visible(visible: boolean) {
		if (this.mesh) {
			this.mesh.visible = visible;
		}
	}

	position = new Vector3();
}
