import type { BatchedMeshGeometryRange, Matrix4, Quaternion, Vector3 } from 'three';
import type { MeshComponent } from '../../components/mesh';
import { updateBufferGeometryHeight } from '../outline-interpolation/utils';

export function updateHeight(component: MeshComponent, height: number, initialHeight: number) {
	if (component.batchedProps?.range && component.mesh) {
		let newHeight = height || 0.0001;
		let repeatScalingFactor = newHeight / Math.max(initialHeight, 0.0001);
		updateBufferGeometryHeight(
			component.mesh.geometry,
			component.batchedProps.range as BatchedMeshGeometryRange,
			newHeight,
			0,
		);
		if (component.material) {
			component.material.repeatYFactor = repeatScalingFactor;
		}
		component.currentHeight = newHeight;

		return true;
	}

	return false;
}

export function updateAltitude(
	component: MeshComponent,
	altitude: number,
	matrix: Matrix4,
	position: Vector3,
	quaternion: Quaternion,
	scale: Vector3,
) {
	if (component.mesh && component.instanceIndex !== -1) {
		component.mesh.getMatrixAt(component.instanceIndex, matrix);
		matrix.decompose(position, quaternion, scale);
		position.z = altitude;
		matrix.compose(position, quaternion, scale);
		component.mesh.setMatrixAt(component.instanceIndex, matrix);

		component.altitude = altitude;

		return true;
	}

	return false;
}
