import type { Group, LineSegments, Material, Object3D, Plane } from 'three';
import { Color, InstancedMesh, MathUtils, Mesh, Vector3 } from 'three';
import type { BatchedStandardMaterial } from '../systems/mesh-creation-and-optimization/batched-material';
import type { Position, EntityId, ModelProperties, ModelGroupStyle } from '../types';
import type { Feature, FeatureCollection, Point } from 'geojson';
import type { Geometry3DObject3D } from '../entities/geometry3d';
import type { GeometryGroupState } from '../entities/geometry-group';
import { geoJSONPoint } from '../utils/schema-validation';
import { z } from 'zod';
import type { ModelStyle } from './styles';
import { interpolate } from '@packages/internal/common';
import { RENDER_LAYERS } from '../utils/constants';

/**
 * State representing a Model, typically loaded via a URL
 */
export type ModelState = {
	readonly id: string;
	readonly type: 'model';
	/**
	 * The parent container of the marker
	 */
	readonly parent?: EntityId<GeometryGroupState> | string | number;
	/**
	 * The position of the model in [lon, lat]
	 */
	readonly position: Position;
	/**
	 * Whether the model is interactive, which means it can be hovered over and clicked on.
	 *
	 * @example
	 * ```javascript
	 * 	renderer.on('click', ({ geometry }) => {});
	 * ```
	 */
	interactive: boolean;
} & Partial<Omit<ModelStyle, 'rotation' | 'scale'>> &
	Pick<ModelStyle, 'rotation' | 'scale'>;

/**
 * initialize model state
 * @interface
 */
export type InitializeModelState = Omit<Partial<ModelState>, 'id' | 'type' | 'parent' | 'position'>;

/**
 * updatable model state
 * @interface
 */
export type UpdateModelState = Omit<InitializeModelState, 'url'> & {
	position?: Position;
};

export const modelPropertiesSchema = z.object({
	rotation: z.array(z.number()).length(3).optional(), // Optional array of 3 numbers
	altitude: z.number().optional(), // Optional number
	scale: z.array(z.number()).length(3).optional(), // Optional array of 3 numbers
	interactive: z.boolean().optional(), // Optional boolean
	verticalOffset: z.number().optional(),
	id: z.union([z.number(), z.string()]).optional(), // Assuming `idTest` is a custom validator, using `z.any()` as placeholder
});

export const featureSchema = z.object({
	type: z.enum(['Feature']), // Required string must be 'Feature'
	geometry: geoJSONPoint, // Required geoJSONPoint schema
	properties: modelPropertiesSchema, // Required modelPropertiesSchema
});

export const addModelFeatureSchema = z.object({
	type: z.enum(['FeatureCollection']), // Required string must be 'FeatureCollection'
	features: z.array(featureSchema), // Required array of featureSchema
});

export const MaterialStyleSchema = z.object({
	color: z.string(),
});

export const modelGroupStyleSchema = z.object({
	url: z.string(), // Required string
	color: z.string().optional(), // Optional string
	opacity: z.number().optional(), // Optional number
	material: z.record(z.string(), MaterialStyleSchema).optional(), // Optional record of string and MaterialStyleSchema
});

export function validateModelGroup(
	id: string | number,
	geometry: FeatureCollection<Point, ModelProperties>,
	style: Partial<ModelGroupStyle>,
) {
	z.union([z.number(), z.string()]).parse(id);
	addModelFeatureSchema.parse(geometry);
	modelGroupStyleSchema.parse(style);
}

export class ModelComponent {
	// Pointer to mesh for this component, which is either the mesh itself or a merged geometry mesh if this component was optimized
	// It can also be a GLTF Scene when loaded inline. see: https://mappedin.atlassian.net/wiki/spaces/SDK/pages/597983234/3D+Asset+Library#Deprecating-FeatureCollection-Support-when-Adding-Model
	mesh?: Geometry3DObject3D | Group;
	readonly type = 'model';

	positionDirty = true;

	// Pointer to outline mesh for this component, again, either the outline mesh itself or a merged geometry mesh
	outline?: LineSegments;

	geometry?: Object3D;
	material?: BatchedStandardMaterial;
	feature: Feature<Point, ModelProperties>;

	instanceIndex?: number = -1;

	constructor(feature: Feature<Point, ModelProperties>) {
		this.feature = feature;
	}

	#visible = true;
	get visible() {
		// Return the mesh's visible state if it exists, otherwise return the stored value
		return this.mesh?.visible ?? this.#visible;
	}

	set visible(value: boolean) {
		this.#visible = value;
		if (this.mesh) {
			this.mesh.visible = value;
		}
	}

	setOpacity() {
		// TODO: implement
	}

	color: Color = new Color();
	setColor() {
		// TODO: implement
	}

	clippingPlane?: Plane;

	position = new Vector3();

	#altitude = 0;
	get altitude() {
		return this.#altitude;
	}

	set altitude(value) {
		if (!this.mesh) {
			return;
		}
		this.mesh.position.z = value;
		this.#altitude = value;
	}

	getRotation() {
		return this.mesh ? [this.mesh.rotation.x, this.mesh.rotation.y, this.mesh.rotation.z] : undefined;
	}
	setRotation(value: [number, number, number]) {
		if (!this.mesh) return;
		this.mesh.rotation.set(MathUtils.degToRad(value[0]), MathUtils.degToRad(value[1]), MathUtils.degToRad(value[2]));
	}

	getScale() {
		return this.mesh ? [this.mesh.scale.x, this.mesh.scale.y, this.mesh.scale.z] : undefined;
	}
	setScale(value: [number, number, number]) {
		if (!this.mesh) return;
		this.mesh.scale.set(value[0], value[1], value[2]);
	}
	#opacity = 1;
	get opacity() {
		return this.#opacity;
	}

	set opacity(value) {
		if (!this.mesh) return;
		if (this.mesh.children[0] instanceof InstancedMesh) {
			for (const child of this.mesh.children) {
				if (child instanceof InstancedMesh) {
					// if the instance already has opacity, we want to interpolate from that value, and not from 1
					if (child.userData?.originalOpacity === undefined) {
						child.userData.originalOpacity = child.material.opacity;
					}
					const targetOpacity = interpolate(value, 0, child.userData?.originalOpacity, 0, 1);
					child.material.opacity = targetOpacity;
					child.material.transparent = targetOpacity < 1;
					child.material.needsUpdate = true;
				}
			}
		} else {
			this.mesh?.traverse(child => {
				if (child instanceof Mesh && child.material) {
					if (Array.isArray(child.material)) {
						child.material.forEach(material => {
							updateMaterialOpacity(material, value);
						});
					} else {
						updateMaterialOpacity(child.material, value);
					}
				}
			});
		}
		this.#opacity = value;
	}

	#renderOrder = RENDER_LAYERS.GEOMETRY;
	get renderOrder() {
		return this.#renderOrder;
	}
	set renderOrder(value: number) {
		if (!this.mesh || value === this.#renderOrder) return;

		this.#renderOrder = value;
		if (this.mesh.children[0] instanceof InstancedMesh) {
			// Set render order on the parent mesh for instanced meshes
			this.mesh.renderOrder = value;
			for (const child of this.mesh.children) {
				if (child instanceof InstancedMesh) {
					child.material.depthWrite = !this.#visibleThroughGeometry;
					child.material.depthTest = !this.#visibleThroughGeometry;
					child.material.needsUpdate = true;
				}
			}
		} else {
			this.mesh?.traverse(child => {
				if (child instanceof Mesh && child.material) {
					child.renderOrder = value;
					if (Array.isArray(child.material)) {
						child.material.forEach(material => {
							material.depthWrite = !this.#visibleThroughGeometry;
							material.depthTest = !this.#visibleThroughGeometry;
							material.needsUpdate = true;
						});
					} else {
						child.material.depthWrite = !this.#visibleThroughGeometry;
						child.material.depthTest = !this.#visibleThroughGeometry;
						child.material.needsUpdate = true;
					}
				}
			});
		}
	}

	#visibleThroughGeometry = false;
	get visibleThroughGeometry() {
		return this.#visibleThroughGeometry;
	}
	set visibleThroughGeometry(value: boolean) {
		this.#visibleThroughGeometry = value;
		// The actual render order assignment is handled by the RenderOrderSystem
	}
}

function updateMaterialOpacity(material: Material, opacity: number) {
	if (material.opacity === opacity) {
		return;
	}
	if (opacity === 1) {
		material.opacity = 1;
		material.transparent = false;
	} else {
		material.opacity = opacity;
		material.transparent = true;
	}
	material.needsUpdate = true;
}
