type AddStyleOptions = Partial<{
	id: string;
}>;

export const injectCSS = (style: string | string[], { id }: AddStyleOptions = {}) => {
	const stylesArr = Array.isArray(style) ? style : [style];
	if (!stylesArr.every(s => typeof s === 'string')) {
		return;
	}

	const styleContent = stylesArr.join('');
	let styleElement: HTMLStyleElement | null = null;

	// Don't add the same style twice
	if (id) {
		const existing = document.getElementById(id);
		if (existing instanceof HTMLStyleElement) {
			styleElement = existing;
		}
	}

	if (styleElement == null) {
		styleElement = document.createElement('style');
	}

	if (id && id !== '') {
		styleElement.id = id;
	}

	// Add the style to the document
	styleElement.textContent = styleContent;
	document.head.appendChild(styleElement);
};
