import { decodeAccessToken, Logger } from '@packages/internal/common';
import type { MapData, TShow3DMapOptions } from '../';
import { DEFAULT_MAP_OPTIONS } from '../constants';

/**
 * Options that are public and should be returned by the `mapView.options` getter.
 */
export const PUBLIC_MAPVIEW_OPTIONS_SET = new Set([
	'outdoorView',
	'bearing',
	'pitch',
	'zoomLevel',
	'screenOffsets',
	'debug',
	'initialFloor',
	'shadingAndOutlines',
	'wallTopColor',
	'style',
	'multiFloorView',
	'occlusion',
	'imagePlacementOptions',
	'flipImagesToFaceCamera',
	'injectStyles',
	'useStandaloneCamera',
	'preloadFloors',
]);

/**
 * Options that are public and should be returned by the `mapView.options` getter.
 */
export const PUBLIC_OUTDOOR_VIEW_OPTIONS_SET = new Set(['style', 'enabled', 'layersHiddenByGeometry', 'lowDpi']);

export function validateOptions(options: TShow3DMapOptions) {
	if (options.outdoorView?.enabled && !options.outdoorView?.token) {
		Logger.error('Enable outdoor view requires a token. Provide token in `options.outdoorView.token`');
	}
}

export function handleShow3DMapOptionDefault(options: TShow3DMapOptions | undefined, mapData: MapData) {
	const token = options?.outdoorView?.token || mapData.outdoorViewToken;

	const stateWithDefault: TShow3DMapOptions = {
		...DEFAULT_MAP_OPTIONS,
		...options,
		outdoorView: {
			enabled: options?.outdoorView?.enabled === undefined ? !!token : options?.outdoorView?.enabled,
			layersHiddenByGeometry: DEFAULT_MAP_OPTIONS.outdoorView?.layersHiddenByGeometry,
			...options?.outdoorView,
		},
		bearing: options?.bearing ?? mapData.naturalBearing ?? 0,
		imagePlacementOptions: {
			...DEFAULT_MAP_OPTIONS.imagePlacementOptions,
			...options?.imagePlacementOptions,
		},
		multiFloorView: {
			...DEFAULT_MAP_OPTIONS.multiFloorView,
			...options?.multiFloorView,
		},
	};

	if (token && stateWithDefault.outdoorView) {
		stateWithDefault.outdoorView.token = token;
	}

	return stateWithDefault;
}

export function handleTokenCapabilities(token: string) {
	const capabilities = { remove_watermark: false };

	if (token) {
		try {
			const decoded = decodeAccessToken(token);
			Object.assign(capabilities, decoded.capabilities);
		} catch (e) {
			Logger.error('Error decoding capabilities from token', e);
		}
	}

	return capabilities;
}

export function getPublicOptions(options: TShow3DMapOptions): TShow3DMapOptions {
	const publicOptions = Object.fromEntries(
		Object.entries(options).filter(([key]) => PUBLIC_MAPVIEW_OPTIONS_SET.has(key)),
	) as TShow3DMapOptions;

	if (publicOptions.outdoorView) {
		publicOptions.outdoorView = Object.fromEntries(
			Object.entries(publicOptions.outdoorView).filter(([key]) => PUBLIC_OUTDOOR_VIEW_OPTIONS_SET.has(key)),
		) as TShow3DMapOptions['outdoorView'];
	}

	return publicOptions;
}
