import { QuadTree, Rectangle } from '@packages/internal/quad-tree';
import { ALWAYS_VISIBLE_RANK_INDEX } from '../../utils/collision-ranking-tier';
import { type WatermarkPosition } from '../watermark/system';

export const STATIC_COLLIDER_INDEX = 9;

export type TSerializedCollider = {
	rank: number;
	enabledAndVisible: boolean;
	x: number;
	y: number;
	bboxes: {
		index: number;
		boundingBox: [number, number, number, number];
		boundingRect?: Rectangle;
	}[];
	shouldCollideWithScreenEdges?: boolean;
	lockedToStrategyIndex?: number;
	visible?: boolean;
};

export type TSerializedColliderResponse = [number, 1 | 0, Rectangle?];

const padding = 200;
const OFFSCREEN_COLLIDER_BUFFER = 10;

export function processColliders(
	colliders: TSerializedCollider[],
	totalWidth,
	totalHeight,
	watermarkWidth,
	watermarkHeight,
	watermarkPosition,
	msgId = '-1',
) {
	// next we create a bounding box around all colliders' bounding boxes
	let minX = Infinity;
	let minY = Infinity;
	let maxX = -Infinity;
	let maxY = -Infinity;

	const leftScreenBoundary = new Rectangle(-OFFSCREEN_COLLIDER_BUFFER, 0, 5, totalHeight);
	const topScreenBoundary = new Rectangle(-OFFSCREEN_COLLIDER_BUFFER, 0, totalWidth, 5);
	const rightScreenBoundary = new Rectangle(totalWidth - 5, 0, totalWidth + OFFSCREEN_COLLIDER_BUFFER, totalHeight);
	const bottomScreenBoundary = new Rectangle(0, totalHeight - 5, totalWidth, totalHeight + OFFSCREEN_COLLIDER_BUFFER);
	const watermarkBoundary = getWatermarkBoundary(
		totalWidth,
		totalHeight,
		watermarkWidth,
		watermarkHeight,
		watermarkPosition,
	);

	const screenBoundColliders = [leftScreenBoundary, topScreenBoundary, rightScreenBoundary, bottomScreenBoundary];

	const screenRectangle = new Rectangle(-padding, -padding, totalWidth + padding * 2, totalHeight + padding * 2);

	for (const collider of colliders) {
		let boundingBox;
		let boundingIndex;

		for (let i = 0; i < collider.bboxes.length; i++) {
			if (collider.bboxes[i] == null) {
				continue;
			}
			boundingBox = collider.bboxes[i].boundingBox;
			boundingIndex = collider.bboxes[i].index;
			if (collider.lockedToStrategyIndex !== -1 && boundingIndex !== collider.lockedToStrategyIndex) {
				continue;
			}

			const [y1, y2, x1, x2] = boundingBox;
			const [y11, y21, x11, x21] = [collider.y - y1, collider.y + y2, collider.x - x1, collider.x + x2];
			const x = x11;
			const y = y11;
			const w = x21 - x11;
			const h = y21 - y11;
			minX = Math.min(minX, x) - padding;
			minY = Math.min(minY, y) - padding;
			maxX = Math.max(maxX, x + w) + padding;
			maxY = Math.max(maxY, y + h) + padding;
			collider.bboxes[i].boundingRect = new Rectangle(x, y, w, h);
		}
	}

	// create a quad tree with the bounding box
	const qTree = new QuadTree(new Rectangle(minX, minY, maxX - minX, maxY - minY));

	const result: {
		msgId: string;
		colliders: TSerializedColliderResponse[];
		staticColliders: Rectangle[];
		debug: { x: number; y: number; w: number; h: number }[];
	} = {
		msgId: msgId,
		colliders: new Array(colliders.length),
		staticColliders: [],
		debug: [],
	};

	let staticColliderIndex = 0;
	// insert static colliders
	for (let i = 0; i < colliders.length; i++) {
		const collider = colliders[i];
		if (collider.bboxes?.[0]?.index === STATIC_COLLIDER_INDEX) {
			result.staticColliders.push(collider.bboxes[0].boundingRect!);
			qTree.insert(collider.bboxes[0].boundingRect!);
			staticColliderIndex++;
		}
	}

	// we now need to trim the static colliders from the colliders array, using the index
	colliders = colliders.slice(staticColliderIndex, colliders.length);

	let collider;
	for (let idx = 0; idx < colliders.length; idx++) {
		collider = colliders[idx];
		result.colliders[idx] = [-1, 0];
		if (collider.enabledAndVisible) {
			let [minIntersectionsForCollider, strategyIndex, rectangle]: [number, number, Rectangle | undefined] = [
				Infinity,
				-1,
				undefined,
			];

			for (const { index, boundingRect: rect } of collider.bboxes) {
				if (rect == null) {
					continue;
				}
				let intersections: Rectangle[] = [];
				const collisions = qTree.queryRect(rect);
				intersections = collisions.filter(c => rect.intersects(c));

				if (collider.shouldCollideWithScreenEdges) {
					screenBoundColliders.forEach(c => {
						if (rect.intersects(c)) {
							intersections.push(c);
						}
					});
				}

				if (rect.intersects(watermarkBoundary)) {
					result.colliders[idx] = [-1, 0, rect];
					break;
				}

				// for colliders that we do want to insert, regardless of collisions,
				// such as floor change tooltips,
				// we find the strategy with the least number of collisions and pop it in there
				if (intersections.length <= minIntersectionsForCollider) {
					rectangle = rect;
					strategyIndex = index;
					minIntersectionsForCollider = intersections.length;
				}

				if (intersections.length === 0) {
					qTree.insert(rect);
					result.colliders[idx] = [index, screenRectangle.contains(rect) ? 0 : 1, rect];
					break;
				}
			}

			if (collider.rank === ALWAYS_VISIBLE_RANK_INDEX && rectangle != null) {
				qTree.insert(rectangle);
				result.colliders[idx] = [strategyIndex, screenRectangle.contains(rectangle) ? 0 : 1, rectangle];
			}
		} else {
			result.colliders[idx] = [-1, 0];
		}
	}

	return result;
}

export function getWatermarkBoundary(
	totalWidth: number,
	totalHeight: number,
	watermarkWidth: number,
	watermarkHeight: number,
	watermarkPosition: WatermarkPosition,
) {
	switch (watermarkPosition) {
		case 'top-left':
			return new Rectangle(0, 0, watermarkWidth, watermarkHeight);
		case 'top-right':
			return new Rectangle(totalWidth - watermarkWidth, 0, watermarkWidth, watermarkHeight);
		case 'bottom-left':
			return new Rectangle(0, totalHeight - watermarkHeight, watermarkWidth, watermarkHeight);
		case 'bottom-right':
			return new Rectangle(totalWidth - watermarkWidth, totalHeight - watermarkHeight, watermarkWidth, watermarkHeight);
		case 'center':
			return new Rectangle(
				(totalWidth - watermarkWidth) / 2,
				(totalHeight - watermarkHeight) / 2,
				watermarkWidth,
				watermarkHeight,
			);
		case 'top':
			return new Rectangle((totalWidth - watermarkWidth) / 2, 0, watermarkWidth, watermarkHeight);
		case 'bottom':
			return new Rectangle(
				(totalWidth - watermarkWidth) / 2,
				totalHeight - watermarkHeight,
				watermarkWidth,
				watermarkHeight,
			);
		case 'left':
			return new Rectangle(0, (totalHeight - watermarkHeight) / 2, watermarkWidth, watermarkHeight);
		case 'right':
			return new Rectangle(
				totalWidth - watermarkWidth,
				(totalHeight - watermarkHeight) / 2,
				watermarkWidth,
				watermarkHeight,
			);
		default:
			// Fallback for unknown positions - should not happen with proper typing
			return new Rectangle(0, totalHeight - watermarkHeight, watermarkWidth, watermarkHeight);
	}
}
