import type { Quaternion, Object3D, BufferAttribute } from 'three';
import {
	Vector3,
	Euler,
	Scene,
	DoubleSide,
	BatchedMesh,
	DirectionalLight,
	MeshStandardMaterial,
	MeshBasicMaterial,
	LineSegments,
	Mesh,
	Box3,
	Color,
	BufferGeometry,
} from 'three';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import type { RendererState } from '../../renderer';
import type { Geometry3DTypes } from '../../entities';
import { Geometry3D, Geometry3DComponents } from '../../entities';
import { ModelStyleComponnet, StyleComponent, Text3DStyleComponent } from '../../components/styles';

export type GLTFExportOptions = {
	onlyVisible?: boolean;
	binary?: boolean;
	scale?: number;
	light?: boolean;
};

/**
 * Class for exporting a three.js scene as a GLTF file.
 */
export class ExporterSystem {
	#glTFExporter: any = null;

	#state: RendererState;

	/**
	 * @hidden
	 */
	constructor(state: RendererState) {
		this.#state = state;
		this.#state.entityScene = state.entityScene;
		this.#state.geometry3DMap = state.geometry3DMap;
	}

	/**
	 * Exports the current scene as a GLTF file.
	 * @param {GLTFExportOptions} userOptions - User-defined options for exporting.
	 * @returns {Promise<Blob>} - A promise that resolves to a Blob containing the GLTF data.
	 */
	getCurrentSceneGLTF = async (userOptions: GLTFExportOptions): Promise<Blob> => {
		const options: Required<GLTFExportOptions> = {
			onlyVisible: true,
			binary: true,
			light: true,
			scale: 1,
			...userOptions,
		};

		const gltfExporter = await this.#getGLTFExporter();
		const newScene = this.#getExportedScene(options);

		const gltf = await gltfExporter.parseAsync(newScene, {
			trs: false,
			maxTextureSize: Infinity,
			onlyVisible: options.onlyVisible,
			binary: options.binary,
			scale: options.scale,
		});

		let blob: Blob;
		if (gltf instanceof ArrayBuffer) {
			blob = new Blob([gltf], { type: 'application/octet-stream' });
		} else {
			const output = JSON.stringify(gltf, null, 2);
			blob = new Blob([output], { type: 'text/plain' });
		}

		return blob;
	};

	/**
	 * Creates a new scene with the current scene's objects, new light, and converts all the coordinates from XYZ to XZY.
	 * @param {GLTFExportOptions} options - The options for exporting the scene.
	 * @returns {Scene | null} - The new scene with transformed coordinates.
	 * @hidden
	 */
	#getExportedScene = (options: Required<GLTFExportOptions>): Scene => {
		const { scale, light, onlyVisible } = options;
		const newScene = new Scene();

		const bbox = new Box3().setFromObject(this.#state.entityScene);

		this.#processObject3D(this.#state.entityScene, newScene, onlyVisible);

		if (light) {
			const directionalLight = new DirectionalLight(0xffffff, 0.3 * Math.PI);
			directionalLight.position.set(0, Math.max(bbox.max.x, bbox.max.y) * scale * 0.5, bbox.max.y * scale * 4);
			directionalLight.lookAt(0, 0, -1);
			directionalLight.target.position.set(0, 0, -1);
			directionalLight.add(directionalLight.target);

			newScene.add(directionalLight);
		}

		return newScene;
	};

	/**
	 * Loads the GLTF exporter module if it is not already loaded.
	 * @returns {Promise<any>} - The GLTF exporter instance.
	 * @hidden
	 */
	async #getGLTFExporter() {
		if (!this.#glTFExporter) {
			const { GLTFExporter } = await import('three/examples/jsm/exporters/GLTFExporter.js');
			this.#glTFExporter = new GLTFExporter();
		}

		return this.#glTFExporter;
	}

	/**
	 * Processes the venue object and its children to convert their properties.
	 * @param {Object3D} object - The object to process.
	 * @param {Scene} newScene - The new scene to add the processed object to.
	 * @param {boolean} onlyVisible - Whether to process only visible objects.
	 * @hidden
	 */
	#processObject3D = (object: Object3D, newScene: Scene, onlyVisible: boolean) => {
		if ((object as Mesh).material) {
			if (object instanceof BatchedMesh) {
				this.#processBatchedMesh(object).forEach(mesh => newScene.add(mesh));
			} else if (object instanceof LineSegments) {
				newScene.add(this.#processLineSegments(object));
			}
		}
		object.children.forEach(child => {
			if (!onlyVisible || child.visible) {
				this.#processObject3D(child, newScene, onlyVisible);
			}
		});
	};

	#createStandardMaterial = (color: string, opacity: number, transparent: boolean): MeshStandardMaterial => {
		const newMaterial = new MeshStandardMaterial({ color: new Color(color), metalness: 0 });
		newMaterial.opacity = opacity;
		newMaterial.transparent = transparent;

		return newMaterial;
	};

	/**
	 * Processes batched mesh and converts its properties.
	 * @param {BatchedMesh} batchedMesh - The batched mesh to process.
	 * @returns {Mesh} - The merged mesh.
	 * @hidden
	 */
	#processBatchedMesh(batchedMesh: BatchedMesh): Mesh[] {
		const result: Mesh[] = [];
		const entityIDs: string[] = batchedMesh.userData?.entities ? Object.values(batchedMesh.userData?.entities) : [];
		const entities = entityIDs.map(id => this.#state.geometry3DMap.get(id));
		const colorMap = new Map<string, Geometry3DTypes[]>();

		entities.forEach(entity => {
			if (!entity || !(entity instanceof Geometry3D)) return;
			const style = entity.components[Geometry3DComponents.Style];
			const interaction = entity.components[Geometry3DComponents.Interaction];

			if (
				!style.visible ||
				(style instanceof StyleComponent && style.opacity === 0) ||
				style instanceof Text3DStyleComponent ||
				style instanceof ModelStyleComponnet
			)
				return;

			const color = interaction?.hover ? style.hoverColor || this.#state.hoverColor : style.color;
			const key = `${color}-${style.opacity}-${Number(style.visible)}`;

			if (!colorMap.has(key)) {
				colorMap.set(key, []);
			}

			colorMap.get(key)?.push(entity);
		});

		colorMap.forEach((entities, key) => {
			const [color, opacity, visible] = key.split('-');
			const geometries = entities.reduce((acc, entity) => {
				if (entity.type === 'custom-geometry') {
					return acc;
				}
				const mesh = entity.components[Geometry3DComponents.Mesh];

				if ('geometry' in mesh && mesh.geometry instanceof BufferGeometry) {
					acc.push(mesh.geometry);
				}

				return acc;
			}, [] as BufferGeometry[]);

			const material = this.#createStandardMaterial(color, Number(opacity), batchedMesh.material.transparent);
			const mergedGeometry = BufferGeometryUtils.mergeGeometries(geometries, true);
			const mergedMesh = new Mesh(mergedGeometry, material);

			mergedMesh.position.copy(batchedMesh.position);
			mergedMesh.rotation.copy(batchedMesh.rotation);
			mergedMesh.scale.copy(batchedMesh.scale);
			mergedMesh.visible = Boolean(visible);

			this.#convertObjectProperties(
				mergedMesh.position,
				mergedMesh.scale,
				mergedMesh.rotation as Euler,
				mergedMesh.quaternion,
			);
			result.push(this.#convertGeometry(mergedMesh));
		});

		return result;
	}

	/**
	 * Processes line segments and converts its properties.
	 * @param {LineSegments} mesh - The line segments to process.
	 * @returns {Mesh} - The merged mesh.
	 * @hidden
	 */
	#processLineSegments(mesh: LineSegments): Mesh {
		const geometry = mesh.geometry.clone();
		const material = mesh.material as MeshBasicMaterial;
		const newMaterial = new MeshBasicMaterial({ color: material.color, side: DoubleSide });
		newMaterial.opacity = material.opacity;
		newMaterial.transparent = material.transparent;

		const clone = new LineSegments(geometry, newMaterial);
		clone.position.copy(mesh.position);
		clone.rotation.copy(mesh.rotation);
		clone.scale.copy(mesh.scale);
		clone.visible = mesh.visible;

		return this.#convertGeometry(clone) as Mesh;
	}

	/**
	 * Converts the geometry of an object to switch from XYZ to XZY coordinate system.
	 * @param object - The object to convert.
	 * @hidden
	 */
	#convertGeometry = object => {
		const { geometry, material, position, rotation, quaternion, scale } = object;
		if (!geometry?.isBufferGeometry) return object;

		const positionAttribute = geometry.attributes.position;
		const normalAttribute = geometry.attributes.normal;
		const uvAttribute = geometry.attributes.uv;

		this.#convertAttributes(positionAttribute, normalAttribute);
		this.#convertObjectProperties(position, scale, rotation, quaternion);

		if (uvAttribute) {
			this.#convertUVs(uvAttribute);
		}

		if (material?.map && geometry.type === 'PlaneGeometry') {
			object.side = DoubleSide;
		}

		return object;
	};

	/**
	 * Converts position and normal attributes of a geometry.
	 * @param {BufferAttribute} positionAttribute - The position attribute to convert.
	 * @param {BufferAttribute} normalAttribute - The normal attribute to convert.
	 * @hidden
	 */
	#convertAttributes = (positionAttribute: BufferAttribute, normalAttribute: BufferAttribute): void => {
		const vertex = new Vector3();
		const normal = new Vector3();

		for (let i = 0; i < positionAttribute.count; i++) {
			vertex.fromBufferAttribute(positionAttribute, i);
			this.#convertVector(vertex);
			positionAttribute.setXYZ(i, vertex.x, vertex.y, vertex.z);

			if (normalAttribute) {
				normal.fromBufferAttribute(normalAttribute, i);
				this.#convertVector(normal);
				normalAttribute.setXYZ(i, normal.x, normal.y, normal.z);
			}
		}
	};

	/**
	 * Converts a vector from XYZ to XZY coordinate system.
	 * @param {Vector3} vector - The vector to convert.
	 * @hidden
	 */
	#convertVector = (vector: Vector3): void => {
		const tempY = vector.y;
		vector.y = vector.z;
		vector.z = -tempY;
	};

	/**
	 * Converts the position, scale, and rotation properties of an object.
	 * @param {Vector3} position - The position to convert.
	 * @param {Vector3} scale - The scale to convert.
	 * @param {Euler} rotation - The rotation to convert.
	 * @param {Quaternion} quaternion - The quaternion to convert.
	 * @hidden
	 */
	#convertObjectProperties = (position: Vector3, scale: Vector3, rotation: Euler, quaternion: Quaternion): void => {
		const tempPosY = position.y;
		position.y = position.z;
		position.z = -tempPosY;

		const tempScaleY = scale.y;
		scale.y = scale.z;
		scale.z = tempScaleY;

		if (rotation instanceof Euler) {
			rotation.order = 'YXZ'; // Ensure the rotation order is set
			rotation.set(rotation.x, rotation.y, rotation.z, rotation.order);
		} else {
			const tempQuatY = quaternion.y;
			quaternion.set(quaternion.x, quaternion.z, -tempQuatY, quaternion.w);
		}
	};

	/**
	 * Converts the UV attributes of a geometry.
	 * @param {BufferAttribute} uvAttribute - The UV attribute to convert.
	 * @hidden
	 */
	#convertUVs = (uvAttribute: BufferAttribute): void => {
		for (let i = 0; i < uvAttribute.count; i++) {
			const u = uvAttribute.getX(i);
			const v = uvAttribute.getY(i);
			uvAttribute.setXY(i, u, v);
		}
	};

	/**
	 * Cleans up resources used by the ExporterSystem.
	 */
	destroy(): void {
		this.#glTFExporter = null;
	}
}
