import type { Details, Feature as MVFFeature, Geometry } from '@mappedin/mvf-v2';
import type LocationProfile from './location-profile';
import Hyperlink from './hyperlink';
import type { MapDataInternal } from '.';
import ImageMetaData from './image';
import BaseMapData from './base-object';

type PropertiesWithDetails = {
	id: string;
	externalId?: string;
	details?: Details;
};

export default abstract class DetailedMapData<
	MVFData extends MVFFeature<Geometry, PropertiesWithDetails>,
> extends BaseMapData {
	#data: MapDataInternal;
	#mvfData: MVFData;
	#cache: Partial<{ images: ImageMetaData[]; links: Hyperlink[] }> = {};

	constructor(data: MapDataInternal, mvfData: MVFData) {
		super(mvfData.properties.id, data);
		this.#data = data;
		this.#mvfData = mvfData;
	}

	/**
	 * Gets the external identifier of the map feature.
	 *
	 * @returns {string} The external ID of the map feature, or an empty string if no external ID exists.
	 */
	get externalId(): string {
		return this.#mvfData.properties.externalId ?? '';
	}

	/**
	 * Gets the name of the map feature.
	 *
	 * @returns {string} The name of the map feature.
	 */
	get name(): string {
		// compatibility for outdated mvf data
		let name = this.#mvfData.properties.details?.name ?? '';

		if (name === '') {
			name = this.locationProfiles.find(profile => profile.name && profile.name.length > 0)?.name ?? '';
		}

		return name;
	}

	/**
	 * Gets the description of the map feature.
	 *
	 * @returns {string} The description of the map feature, or an empty string if no description exists.
	 */
	get description(): string {
		let description = this.#mvfData.properties.details?.description ?? '';
		if (description === '') {
			description =
				this.locationProfiles.find(profile => profile.description && profile.description.length > 0)?.description ?? '';
		}

		return description;
	}

	/**
	 * Gets the array of {@link ImageMetaData}s associated with this map feature.
	 *
	 * @returns {ImageMetaData[]} An array of Image objects, or an empty array if no images exist.
	 */
	get images(): ImageMetaData[] {
		if (this.#cache.images != null) {
			return this.#cache.images;
		}

		let images =
			this.#mvfData.properties.details?.images?.map(
				image =>
					new ImageMetaData({
						mvfData: image,
						mapData: this.#data,
					}),
			) ?? [];
		if (images == null || images.length === 0) {
			images = this.locationProfiles.find(profile => profile.images.length > 0)?.images ?? [];
		}

		this.#cache.images = images;

		return images;
	}

	/**
	 * Gets the array of {@link Hyperlink}s associated with this map feature.
	 *
	 * @returns {Hyperlink[]} An array of Hyperlink objects, or an empty array if no links exist.
	 */
	get links(): Hyperlink[] {
		if (this.#cache.links != null) {
			return this.#cache.links;
		}

		let links = this.#mvfData.properties.details?.links?.map(link => new Hyperlink({ mvfData: link })) ?? [];
		if (links == null || links.length === 0) {
			links = this.locationProfiles.find(profile => profile.links.length > 0)?.links ?? [];
		}

		this.#cache.links = links;

		return links;
	}

	/**
	 * Gets the {@link LocationProfile}s attached to this feature.
	 *
	 * @returns {LocationProfile[]} An array of location profiles.
	 */
	get locationProfiles(): LocationProfile[] {
		return this.#data.locationProfilesByAttachedFeatureId[this.id] ?? [];
	}
}
