import type { CategoryId, Category as MVFCategory } from '@mappedin/mvf-v2';
import BaseMetaData from './base-metadata-object';
import type { LocationProfile, MapDataInternal } from '.';

class LocationCategory extends BaseMetaData implements Omit<MVFCategory, 'parent'> {
	/**
	 * Checks if the provided instance is of type EnterpriseLocation.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a EnterpriseLocation, false otherwise.
	 */
	static is(instance: object): instance is LocationCategory {
		return (instance as { __type: string }).__type === LocationCategory.__type;
	}

	/**
	 * @internal
	 */
	static readonly __type = 'location-category';

	/**
	 * @internal
	 */
	readonly __type = LocationCategory.__type;
	id: CategoryId = 'cat_';
	name = '';

	/**
	 * The category's icon
	 *
	 * @format uri
	 */
	icon = '';

	#data: MapDataInternal;
	#cache: Partial<{
		parent: LocationCategory | undefined;
		locationProfiles: LocationProfile[];
		children: LocationCategory[];
	}> = {};
	#mvfData: MVFCategory;

	constructor(data: MapDataInternal, options: { mvfData: MVFCategory }) {
		super(options.mvfData.id);
		this.#data = data;
		this.#mvfData = options.mvfData;
		for (const prop in options.mvfData) {
			if (!Object.getOwnPropertyDescriptor(Object.getPrototypeOf(this), prop)) {
				Object.defineProperty(this, prop, {
					enumerable: true,
					get() {
						return this.#mvfData[prop];
					},
				});
			}
		}
	}

	/**
	 * Gets the parent {@link LocationCategory}.
	 *
	 * @returns {LocationCategory | undefined} The parent location category.
	 */
	get parent(): LocationCategory | undefined {
		if (this.#cache.parent) {
			return this.#cache.parent;
		}

		this.#cache.parent = this.#mvfData.parent
			? this.#data.getById('location-category', this.#mvfData.parent)
			: undefined;

		return this.#cache.parent;
	}

	/**
	 * Gets the children {@link LocationCategory}s.
	 */
	get children(): LocationCategory[] {
		if (this.#cache.children) {
			return this.#cache.children;
		}

		this.#cache.children = this.#data.locationCategoriesByParentId[this.id] || [];

		return this.#cache.children;
	}

	/**
	 * Gets the {@link LocationProfile}s that are associated with this category.
	 */
	get locationProfiles(): LocationProfile[] {
		if (this.#cache.locationProfiles) {
			return this.#cache.locationProfiles;
		}

		const profiles: LocationProfile[] = [];

		for (const profile of this.#data.locationProfilesByCategoryId[this.id] || []) {
			const locationProfile = this.#data.getById('location-profile', profile.id);
			if (locationProfile) {
				profiles.push(locationProfile);
			}
		}

		this.#cache.locationProfiles = profiles;

		return profiles;
	}

	toJSON() {
		return {
			__type: LocationCategory.__type,
			id: this.id,
			name: this.name,
		};
	}
}

export default LocationCategory;
