import type { AreaCollection } from '@mappedin/mvf-v2';
import type Coordinate from './coordinate';
import type { MapDataInternal } from '.';
import type Floor from './floor';
import type { IGeoJSONData } from './types';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * An Area represents some grouping of multiple pieces of geometry, not
 * necessarily bounded by walls or any other physical feature of the map.
 *
 * Areas are currently in a preview state, and may have changes to existing
 * functionality or new features added in the future.
 */
class Area
	extends DetailedMapData<AreaCollection['features'][number]>
	implements IGeoJSONData, IFocusable, IAnchorable
{
	/**
	 * @internal
	 */
	static readonly __type = 'area';

	/**
	 * @internal
	 */
	readonly __type = Area.__type;

	#data: MapDataInternal;
	#floorId: string;
	#cache: Partial<{ center: Coordinate }> = {};
	#mvfData: AreaCollection['features'][number];

	/**
	 * Checks if the provided instance is of type Area.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Area, false otherwise.
	 */
	static is(instance: object): instance is Area {
		return (instance as { __type: string }).__type === Area.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: AreaCollection['features'][number];
		},
	) {
		super(data, options.mvfData);
		this.#data = data;
		this.#floorId = options.floorId;
		this.#mvfData = options.mvfData;
	}

	/**
	 * Gets the center {@link Coordinate} of the area.
	 *
	 * @returns {Coordinate} The area's center coordinate.
	 */
	get center(): Coordinate {
		if (this.#cache.center == null) {
			this.#cache.center = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.center;
	}

	/**
	 * Gets the {@link Floor} object associated with the area.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Area.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this;
	}

	/** @internal */
	get anchorTarget() {
		return this.center;
	}

	/**
	 * Serializes the space data to JSON.
	 *
	 * @returns An object representing the space.
	 */
	toJSON() {
		return {
			__type: Area.__type,
			id: this.id,
			name: this.name,
			floor: this.floor.id,
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {}
}

export default Area;
