import type { EntranceFeature } from '@mappedin/mvf-v2';
import type Coordinate from './coordinate';
import type Floor from './floor';
import type { MapDataInternal } from '.';
import type { IGeoJSONData } from './types';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * A class representing door data within the map.
 *
 * Doors are hidden by default. Doors can be made visible by setting their `visible` property to `true`.
 *
 * ```typescript
 * //Make interior doors visible and brown.
 * mapView.updateState(DOORS.Interior, {
 * 	visible: true,
 * 	color: '#5C4033',
 * 	opacity: 0.6,
 * });

 * //Make exterior doors visible and black.
 * mapView.updateState(DOORS.Exterior, {
 * 	visible: true,
 * 	color: 'black',
 * 	opacity: 0.6,
 * });
 * ```
 * Doors can also be made visible on an individual basis by passing in a {@link Door} object to {@link MapView.updateState}.
 *
 * Refer to the [Door Guide](https://developer.mappedin.com/web-sdk/spaces#doors) for more information and interactive examples.
 *
 * Door appearance can be customized by changing the color or adding a texture to the top or sides of a door.
 * Refer to the [Textures & Colors Guide](https://developer.mappedin.com/web-sdk/images-textures#textures--colors) for more information and interactive examples.
 *
 */
class Door extends DetailedMapData<EntranceFeature> implements IGeoJSONData, IFocusable, IAnchorable {
	/**
	 * @internal
	 */
	static readonly __type = 'door';

	/**
	 * @internal
	 */
	readonly __type = Door.__type;

	#data: MapDataInternal;
	#cache: Partial<{ center: Coordinate }>;
	#floorId: string;
	#mvfData: EntranceFeature;

	/**
	 * Checks if the provided instance is of type Door.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Door, false otherwise.
	 */
	static is(instance: object): instance is Door {
		return (instance as { __type: string }).__type === Door.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: EntranceFeature;
		},
	) {
		super(data, options.mvfData);
		this.#cache = {};
		this.#mvfData = options.mvfData;
		this.#data = data;
		this.#floorId = options.floorId;
	}

	/**
	 * Gets the {@link Floor} object associated with the door.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Calculates and gets the center {@link Coordinate} of the door.
	 *
	 * @returns {Coordinate} The center coordinate.
	 */
	get center(): Coordinate {
		if (this.#cache.center == null) {
			this.#cache.center = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.center;
	}

	/**
	 * Gets whether this door is an exterior door.
	 */
	get isExterior(): boolean {
		if (this.#mvfData.properties.isExterior) {
			return true;
		}

		const obstructionId = this.#data.obstructionIdByEntranceId[this.id];
		const obstruction = this.#data.getMVFFeatureById('obstruction', obstructionId);

		return obstruction?.properties.footprintComponent ?? false;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Door.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this.center;
	}

	/** @internal */
	get anchorTarget() {
		return this.center;
	}

	/**
	 * Converts the door information to a JSON object.
	 *
	 * @returns An object representing the door.
	 */
	toJSON() {
		return {
			__type: Door.__type,
			id: this.id,
			name: this.name,
			floor: this.floor.id,
			center: this.center.toJSON(),
			/* TODO: Uncomment this code when the adjacentSpaces getter is fixed */
			// adjacentSpaces: this.adjacentSpaces.map(space => space.id),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default Door;
