import type { Floor } from '../map-data-objects';
import type { TFloorState } from '../states';
import { parseFloorGap } from './utils';

export type VisibilityState = {
	outdoorOpacity: number;
	floorStates: {
		floor?: Floor;
		state: TFloorState;
	}[];
};

export function getMultiFloorState(
	floors: Floor[],
	currentFloor: Floor,
	floorGap: number | 'auto' = 'auto',
	floorIdsInNavigation: Floor['id'][],
	floorsVisualHeightMap: Map<number, { altitude: number; effectiveHeight: number }> = new Map(),
): VisibilityState {
	const state: VisibilityState = {
		outdoorOpacity: 1,
		floorStates: [],
	};

	const sortedFloors = floors.sort((f1, f2) => f1.elevation - f2.elevation);

	// Check if there's no ground floor (elevation 0)
	const hasGroundFloor = floors.some(floor => floor.elevation === 0);
	let firstNonGroundFloorProcessed = true;

	for (const floor of sortedFloors) {
		const footprintHeight =
			floorsVisualHeightMap.get(floor.elevation)?.effectiveHeight ?? parseFloorGap(floorGap, floor.maxHeight);
		const currentFloorState = {
			floor,
			state: {
				type: 'floor',
				visible: false,
				geometry: {
					visible: false,
				},
				labels: {
					enabled: false,
				},
				markers: {
					enabled: false,
				},
				footprint: {
					visible: false,
				},
				occlusion: {
					enabled: false,
				},
				areas: {
					visible: false,
				},
			} as Required<TFloorState>,
		};
		state.floorStates.push(currentFloorState);

		if (floor.id === currentFloor.id) {
			currentFloorState.state.visible = true;
			currentFloorState.state.geometry.visible = true;
			currentFloorState.state.labels.enabled = true;
			currentFloorState.state.markers.enabled = true;
			currentFloorState.state.occlusion.enabled = true;
			currentFloorState.state.areas.visible = true;
			if (floor.elevation < 0) {
				currentFloorState.state.footprint.height = footprintHeight;
				currentFloorState.state.footprint.visible = true;
				state.outdoorOpacity = 0.2;
			}
		} else if (floorIdsInNavigation.includes(floor.id) && floor.elevation < currentFloor.elevation) {
			currentFloorState.state.visible = true;
			currentFloorState.state.geometry.visible = true;
			currentFloorState.state.markers.enabled = true;
			currentFloorState.state.occlusion.enabled = true;
			currentFloorState.state.footprint.visible = true;
			currentFloorState.state.footprint.height = footprintHeight;
			if (floor.elevation < 0) {
				state.outdoorOpacity = 0.2;
			}
		} else if (currentFloor.elevation > 0 && floor.elevation < currentFloor.elevation && floor.elevation >= 0) {
			currentFloorState.state.visible = true;
			currentFloorState.state.footprint.visible = true;
			currentFloorState.state.footprint.height = footprintHeight;
		}

		// If we dont have a ground floor, dont make the footprint visible for that floor
		if (
			!hasGroundFloor &&
			floor.elevation > 0 &&
			firstNonGroundFloorProcessed &&
			currentFloor.elevation === floor.elevation
		) {
			firstNonGroundFloorProcessed = false;
			currentFloorState.state.footprint.visible = false;
		}
	}
	return state;
}
