import type { GeojsonApiMapObject } from './map-object';
import type { GLTFExportOptions } from '../types';
import { Logger } from '@packages/internal/common';

/**
 * API to export the scene.
 *
 * @hidden
 */
export class Exporter {
	#getCurrentMap: CurrentMapGetter;
	/**
	 * @internal
	 */
	get currentMap() {
		return this.#getCurrentMap();
	}
	/**
	 * @internal
	 */
	constructor({ currentMapGetter }: { currentMapGetter: CurrentMapGetter }) {
		this.#getCurrentMap = currentMapGetter;
	}

	/**
	 * Exports the current scene as a GLTF file.
	 *
	 * @hidden
	 */
	async getCurrentSceneGLTF(userOptions: GLTFExportOptions): Promise<Blob> {
		const map = this.#getCurrentMap();

		if (!map) {
			Logger.error('There is no map to export. Please load a map first.');

			return new Blob();
		}

		return map?.Exporter.getCurrentSceneGLTF(userOptions);
	}
}
type CurrentMapGetter = () => GeojsonApiMapObject | undefined;
