import {
	AlwaysStencilFunc,
	Box3,
	BufferAttribute,
	CatmullRomCurve3,
	Color,
	KeepStencilOp,
	Mesh,
	NotEqualStencilFunc,
	RepeatWrapping,
	ReplaceStencilOp,
	SRGBColorSpace,
	ShaderMaterial,
	Texture,
	TextureLoader,
	TubeGeometry,
	Vector2,
	Vector3,
} from 'three';
// @ts-ignore
import PathVertexShader from '../../shaders/Path.Vertex.glsl';
// @ts-ignore
import PathFragmentShader from '../../shaders/Path.Fragment.glsl';
import StencilFragmentShader from '../../shaders/Path.Stencil.Fragment.glsl';

// @ts-ignore
import arrow from '../../icons/mappedin-icon_arrow.png';

import type { PathComponent } from '../../components/path';
import type { PathMaterial, PathUniforms } from '../../entities/geometry3d';
import { RENDER_LAYERS } from '../../utils/constants';

const textureLoader = new TextureLoader();

type BuildPathOptions = Partial<{
	color: string;
	altitude: number;
	isVertical: boolean;
}>;

export function buildPath(points: Vector3[], component: PathComponent, options: BuildPathOptions) {
	const {
		breakpoint,
		width,
		accentColor: pulseColor,
		completeFraction,
		isVertical,
		visibleThroughGeometry,
		dashed,
		highlightCompleteFraction,
		highlightStartFraction,
		highlightEndFraction,
		highlightColor,
		highlightWidthMultiplier,
	} = component;
	const { color } = options;

	/*
    Default near radius of 0.2 meters for finer control near the camera.
    Chosen to be less than half the average doorway width (0.85 meters)
    to ensure the path can navigate through doorways on the zoom levels close to nearZoom.
    */
	const animateDrawing = true;
	/*
    Default far radius of 0.4 meters for a wider view as the path extends
    farther from the camera. This value is approximately half the average
    doorway width, providing a comfortable margin for the zoom levels close to farZoom.
    */
	const nearZoom = 0;
	const farZoom = Infinity;
	const displayArrowsOnPath = component.displayArrowsOnPath;
	const showPulse = true;
	const showArrow = displayArrowsOnPath;
	const nearRadius = typeof width === 'object' ? Number(width.output[breakpoint]) / 2 : width / 2;
	const farRadius =
		typeof width === 'object' ? Number(width.output[breakpoint + 1] ?? width.output[breakpoint]) / 2 : width / 2;

	const pathZ = (options.altitude ?? 0) + ALTITUDE_ADJUSTMENT;
	// Compute the total length of the path.
	let pathLength = 0;

	points[0].z += pathZ;
	for (let i = 1; i < points.length; ++i) {
		points[i].z += pathZ;
		const dx = points[i].x - points[i - 1].x;
		const dz = points[i].z - points[i - 1].z;
		if (isVertical) {
			// If the path is a connection path, we ignore slope toward and
			// away from the camera, as this would merely compress the arrow
			// texture visually.
			pathLength += Math.sqrt(dx * dx + dz * dz);
		} else {
			const dy = points[i].y - points[i - 1].y;
			pathLength += Math.sqrt(dx * dx + dy * dy + dz * dz);
		}
	}

	if (!isVertical) {
		const firstPoint = points[0];
		const lastPoint = points[points.length - 1];

		// 0.01 is subtracted to point end cap downward
		points.push(lastPoint.clone().setZ(lastPoint.z - 0.01));
		points.unshift(firstPoint.clone().setZ(firstPoint.z - 0.01));
	}

	let arrowTexture = new Texture();
	if (showArrow) {
		arrowTexture = textureLoader.load(arrow);
		arrowTexture.colorSpace = SRGBColorSpace;
	}
	const colorThree = new Color(color);
	const pulseColorThree = new Color(pulseColor);

	const uniforms = {
		vertexes: {
			type: 'f',
			value: 1.0,
		},
		resolution: {
			type: 'v2',
			value: new Vector2(1.0, 1.0),
		},
		cameraParameters: {
			type: 'v2',
			value: new Vector2(nearZoom, 0),
		},
		complete: {
			type: 'f',
			value: animateDrawing ? 0.0 : 1.0,
		},
		color: {
			type: 'c',
			value: colorThree,
		},
		pathLength: {
			type: 'f',
			value: pathLength,
		},
		nearRadius: {
			type: 'f',
			value: nearRadius,
		},
		farRadius: {
			type: 'f',
			value: farRadius,
		},
		nearZoom: {
			type: 'f',
			value: nearZoom,
		},
		farZoom: {
			type: 'f',
			value: farZoom,
		},
		pulseColor: {
			type: 'c',
			value: pulseColorThree,
		},
		pulse: {
			type: 'f',
			value: 0.0,
		},
		pulseLength: {
			type: 'f',
			value: 0.075,
		},
		pathIsVertical: {
			type: 'b',
			value: isVertical,
		},
		arrowAnimationTimer: {
			type: 'f',
			value: 0.0,
		},
		arrowTexture: {
			type: 't',
			value: arrowTexture,
		},
		displayArrowsOnPath: {
			type: 'b',
			value: displayArrowsOnPath,
		},
		showPulse: {
			type: 'b',
			value: showPulse,
		},
		opacityMultiplier: {
			type: 'f',
			value: 1.0,
		},
		dashed: {
			type: 'b',
			value: dashed,
		},
		highlightCompleteFraction: {
			type: 'f',
			value: highlightCompleteFraction,
		},
		highlightStartFraction: {
			type: 'f',
			value: highlightStartFraction,
		},
		highlightEndFraction: {
			type: 'f',
			value: highlightEndFraction,
		},
		highlightColor: {
			type: 'c',
			value: new Color(highlightColor),
		},
		highlightWidthMultiplier: {
			type: 'f',
			value: highlightWidthMultiplier,
		},
	} satisfies PathUniforms;

	const pathCurve: any = new CatmullRomCurve3(points);

	pathCurve.curveType = 'catmullrom';
	pathCurve.tension = 0.0;

	const TUBULAR_SEGMENTS_PER_UNIT_LENGTH = 30;
	const RADIAL_SEGMENTS = 8;

	const tubularSegments = TUBULAR_SEGMENTS_PER_UNIT_LENGTH * points.length;
	const geometry = new TubeGeometry(pathCurve, tubularSegments, nearRadius, RADIAL_SEGMENTS, false);

	const vertexIndex = new Float32Array(geometry.attributes.position.count);
	for (let i = points.length; i < vertexIndex.length; i++) {
		vertexIndex[i] = i;
	}
	geometry.setAttribute('vertexIndex', new BufferAttribute(vertexIndex, 1));

	// An attribute that contains a value from 0.0 up to 1.0 depending on
	// whether the vertex is at the beginning or the end of the path.
	const pathDistance = new Float32Array(geometry.attributes.position.count);
	const pathDistanceAttribute = new BufferAttribute(pathDistance, 1);
	for (let i = 0; i <= tubularSegments; i++) {
		// Take careful note of the <= here -- threejs generates an
		// extra vertex per segment...
		for (let j = 0; j <= RADIAL_SEGMENTS; ++j) {
			pathDistanceAttribute.setX(i * (RADIAL_SEGMENTS + 1) + j, i / tubularSegments);
		}
	}
	geometry.setAttribute('pathDistance', pathDistanceAttribute);

	// An attribute that contains the tangent vector for the path at the
	// current point. This vector will point "forward" along the path at
	// any point.
	const pathTangents = new Float32Array(geometry.attributes.normal.count * 3);
	const pathTangentAttribute = new BufferAttribute(pathTangents, 3);
	for (let i = 0; i < tubularSegments; i++) {
		const tangent = geometry.tangents[i];
		for (let j = 0; j <= RADIAL_SEGMENTS; ++j) {
			pathTangentAttribute.setXYZ(i * (RADIAL_SEGMENTS + 1) + j, tangent.x, tangent.y, tangent.z);
		}
	}
	geometry.setAttribute('pathTangent', pathTangentAttribute);

	// An attribute that points toward the "top" of the path, which is a
	// vector that's perpendicular to the path and points as closely to the
	// positive z-axis as possible (for normal paths; for connection paths,
	// the "front" is used, which is the negative y-axis.)
	const pathNormals = new Float32Array(geometry.attributes.normal.count * 3);
	const pathNormalAttribute = new BufferAttribute(pathNormals, 3);
	for (let i = 0; i < tubularSegments; i++) {
		// N = normalize(U - T(T dot U))
		const tangent = geometry.tangents[i];
		var normalX;
		var normalY;
		var normalZ;

		if (isVertical) {
			// U = (0, -1, 0)
			normalX = tangent.x * tangent.y;
			normalY = -1 + tangent.y * tangent.y;
			normalZ = tangent.z * tangent.y;
		} else {
			// U = (0, 0, 1)
			normalX = -tangent.x * tangent.z;
			normalY = -tangent.y * tangent.z;
			normalZ = 1 - tangent.z * tangent.z;
		}

		let normalR = Math.sqrt(normalX * normalX + normalY * normalY + normalZ * normalZ);
		/**
		 * Guard against NaNs produced by producing normalized tangents.
		 * There may be some issues with the above normal calculations
		 * that we don't fully understand but this guard works for now.
		 */
		if (normalR === 0) {
			normalR = 1;
		}
		const unitNormalX = normalX / normalR;
		const unitNormalY = normalY / normalR;
		const unitNormalZ = normalZ / normalR;
		for (let j = 0; j <= RADIAL_SEGMENTS; ++j) {
			pathNormalAttribute.setXYZ(i * (RADIAL_SEGMENTS + 1) + j, unitNormalX, unitNormalY, unitNormalZ);
		}
	}
	geometry.setAttribute('pathNormal', pathNormalAttribute);

	// Sweet shader material
	const material = new ShaderMaterial({
		uniforms: uniforms,
		vertexShader: PathVertexShader,
		fragmentShader: PathFragmentShader,
	}) as PathMaterial;

	material.transparent = true;
	material.alphaTest = 0.5;

	uniforms.resolution.value.x = 1.0; // width
	uniforms.resolution.value.y = 1.0; // height

	uniforms.vertexes.value = vertexIndex.length;
	uniforms.arrowTexture.value.wrapS = RepeatWrapping;
	uniforms.arrowTexture.value.wrapT = RepeatWrapping;
	uniforms.arrowTexture.value.anisotropy = 16;

	const pathObject = new Mesh(geometry, material);
	const xRayObject = pathObject.clone();
	const xRayUniforms = {
		...uniforms,
		opacityMultiplier: {
			type: 'f',
			value: 0.25,
		},
		pathIsVertical: {
			type: 'b',
			value: isVertical,
		},
	};

	xRayUniforms.complete.value = 1.0;

	xRayObject.material = new ShaderMaterial({
		uniforms: xRayUniforms,
		vertexShader: PathVertexShader,
		fragmentShader: PathFragmentShader,
	}) as PathMaterial;
	xRayObject.material.transparent = true;
	xRayObject.material.alphaTest = 0.5;
	xRayObject.material.depthWrite = false;
	xRayObject.material.depthTest = false;

	xRayUniforms.resolution.value.x = 1.0; // width
	xRayUniforms.resolution.value.y = 1.0; // height

	xRayUniforms.vertexes.value = vertexIndex.length;
	xRayUniforms.arrowTexture.value.wrapS = RepeatWrapping;
	xRayUniforms.arrowTexture.value.wrapT = RepeatWrapping;
	xRayUniforms.arrowTexture.value.anisotropy = 16;

	xRayUniforms.complete.value = completeFraction;

	pathObject.userData = xRayObject.userData = component.mesh.userData;

	const mesh = component.mesh;

	if (!visibleThroughGeometry) {
		const stencilMaterial = new ShaderMaterial({
			uniforms: uniforms,
			// we need the same vertex shader as the path shader so the path is flattened the same way
			vertexShader: PathVertexShader,
			fragmentShader: StencilFragmentShader,
		}) as PathMaterial;
		// create a stencil mesh that will be used to mask any visible through geometry paths
		const stencilMesh = new Mesh(geometry, stencilMaterial);
		stencilMesh.name = 'stencil';
		stencilMesh.material.colorWrite = false;
		stencilMesh.material.stencilWrite = true;
		stencilMesh.material.stencilRef = 1;
		stencilMesh.material.stencilFunc = AlwaysStencilFunc;
		stencilMesh.material.stencilZPass = ReplaceStencilOp;
		stencilMesh.material.stencilZFail = ReplaceStencilOp;
		stencilMesh.material.stencilFail = ReplaceStencilOp;
		stencilMesh.material.depthWrite = false;
		stencilMesh.material.depthTest = false;

		stencilMesh.userData = component.mesh.userData;

		mesh.add(stencilMesh);
		mesh.add(xRayObject);
		/**
		 * Note: the render order ensures that the regular path is rendered above the xray path, always, preventing transparency fighting at
		 * different camera angles. We don't care about the x-ray being under regular path, because we only need it in places where the regular path is occluded by geometry.
		 * This issue shows up when rendering multiple paths above each other (step by step directions path segment highlight)
		 */
		// make sure xray is rendered first
		xRayObject.renderOrder = 0;
		// make sure path is rendered last
		pathObject.renderOrder = 1;
	}

	mesh.add(pathObject);

	if (isVertical) {
		// ensure we can move the object back to the original position
		// after centring its geometry which is required for billboarding
		const boundingBox = new Box3().setFromObject(pathObject);
		const center = new Vector3();
		boundingBox.getCenter(center);

		mesh.children.forEach(child => {
			child.geometry.center();
			child.position.copy(center);
		});
	}

	// always show path on top of everything else, without opacity (xray)
	if (visibleThroughGeometry) {
		// read 1 from stencil buffer to hide any pixels covered by the stencil mesh above
		pathObject.material.stencilWrite = true;
		pathObject.material.stencilRef = 1;
		pathObject.material.stencilFunc = NotEqualStencilFunc;
		pathObject.material.stencilZPass = KeepStencilOp;
		pathObject.material.stencilZFail = KeepStencilOp;
		pathObject.material.stencilFail = KeepStencilOp;
		pathObject.material.needsUpdate = true;
		pathObject.layers.disableAll();
		pathObject.layers.enable(RENDER_LAYERS.ALWAYS_ON_TOP_PATHS);
	}

	geometry.computeBoundingBox();
	const center = new Vector3();
	geometry.boundingBox!.getCenter(center);

	return { mesh, material, center };
}

// This adjustment separates xray path from path.
// TODO: Fix this properly without a hard coded offset value
const ALTITUDE_ADJUSTMENT = 0.15;
