import type { AnnotationId } from '@mappedin/mvf-annotations';
import { typedEntries } from '@mappedin/mvf-core/utils';
import type { Category } from '../../extensions/category.js';
import type { Location, LocationAnnotationMapping, LocationEntranceMapping } from '../../extensions/location.js';
import type { ParsedMVF } from '../../types/bundle.js';
import { Connection, type ConnectionId, type EntranceId } from '../../types/core.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';
import { isLineDoor } from './connectionBuilder.js';

export type V3Locations = Pick<
	MVFv2_STANDARD_MVFv3,
	'locationCategories' | 'locations' | 'annotations' | 'connections'
>;
export type V2Locations = Pick<ParsedMVF, 'category.json' | 'location.json'>;

/**
 * Builds locations and categories, primarily based on the Locations extension
 * @param v3
 * @returns
 */
export function buildLocations(
	v3: V3Locations,
	{ geometryMaps, geometryFeatureById }: Pick<BuilderOptions, 'geometryMaps' | 'geometryFeatureById'>,
): V2Locations {
	if (!v3.locationCategories && !v3.locations) {
		return {};
	}

	const v2: V2Locations = {};

	if (v3.locationCategories?.length) {
		const v2Categories: Category[] = [];
		for (const v3Category of v3.locationCategories) {
			const v2Category: Category = {
				id: v3Category.id,
				name: v3Category.details.name,
				icon: v3Category.details.icon ?? '',
			};
			if (v3Category.parent) {
				v2Category.parent = v3Category.parent;
			}
			v2Categories.push(v2Category);
		}
		v2['category.json'] = v2Categories;
	}

	if (v3.locations?.length) {
		const attachmentMaps = buildLocationAttachmentMaps(v3, { geometryFeatureById });

		const v2Locations: Location[] = [];
		for (const v3Location of v3.locations) {
			const v2Location: Location = {
				id: v3Location.id,
				name: v3Location.details.name,
				categories: v3Location.categories,
				spaces: [],
				obstructions: [],
				areas: [],
				annotations: [],
				connections: [],
				entrances: [],
				links: v3Location.links,
				openingHoursSpecification: v3Location.openingHours ?? [],
				pictures: v3Location.images,
				social: v3Location.social,
				description: v3Location.details.description,
				externalId: v3Location.details.externalId,
				icon: v3Location.details.icon,
				logo: v3Location.logo,
				phone: v3Location.phone,
				website: v3Location.website,
			};
			v2Locations.push(v2Location);

			for (const { geometryId, floorId } of v3Location.geometryAnchors) {
				v2Location.spaces.push(
					...(geometryMaps.space.get(geometryId)?.ids.map((id) => ({ id, floor: floorId })) ?? []),
				);
				v2Location.obstructions.push(
					...(geometryMaps.obstruction.get(geometryId)?.ids.map((id) => ({ id, floor: floorId })) ?? []),
				);
				v2Location.areas.push(...(geometryMaps.area.get(geometryId)?.ids.map((id) => ({ id, floor: floorId })) ?? []));

				v2Location.entrances.push(...(attachmentMaps.entrancesByGeometryId.get(geometryId) ?? []));
				v2Location.connections.push(...(attachmentMaps.connectionsByGeometryId.get(geometryId) ?? []));
				v2Location.annotations.push(...(attachmentMaps.annotationsByGeometryId.get(geometryId) ?? []));
			}
		}
		v2['location.json'] = v2Locations;
	}
	return v2;
}

export type LocationAttachmentMaps = {
	annotationsByGeometryId: Map<string, LocationAnnotationMapping[]>;
	connectionsByGeometryId: Map<string, ConnectionId[]>;
	entrancesByGeometryId: Map<string, LocationEntranceMapping[]>;
};
export function buildLocationAttachmentMaps(
	v3: Pick<V3Locations, 'annotations' | 'connections'>,
	{ geometryFeatureById }: Pick<BuilderOptions, 'geometryFeatureById'>,
): LocationAttachmentMaps {
	const maps: LocationAttachmentMaps = {
		annotationsByGeometryId: new Map(),
		connectionsByGeometryId: new Map(),
		entrancesByGeometryId: new Map(),
	};

	if (v3.annotations) {
		for (const [floorId, annotations] of typedEntries(v3.annotations)) {
			for (const annotation of annotations ?? []) {
				const geometryId = annotation.geometryId;
				const annotationId = annotation.id;
				const annotationIds = maps.annotationsByGeometryId.get(geometryId) ?? [];
				annotationIds.push({ floor: floorId, id: annotationId });
				maps.annotationsByGeometryId.set(geometryId, annotationIds);
			}
		}
	}

	if (v3.connections) {
		for (const connection of v3.connections) {
			const isEntrance = isLineDoor(connection, geometryFeatureById);
			// Make a unique array of geometryIds for the connection
			// Create a unique array of geometry anchors by comparing geometryId and floorId
			const geometryAnchors = Array.from(
				new Map(
					[...connection.entrances, ...connection.exits].map((anchor) => [
						`${anchor.geometryId}_${anchor.floorId}`,
						anchor,
					]),
				).values(),
			);
			for (const anchor of geometryAnchors) {
				const geometryId = anchor.geometryId;
				if (isEntrance) {
					const entranceIds = maps.entrancesByGeometryId.get(geometryId) ?? [];
					entranceIds.push({ floor: anchor.floorId, id: connection.id });
					maps.entrancesByGeometryId.set(geometryId, entranceIds);
				} else {
					const connectionIds = maps.connectionsByGeometryId.get(geometryId) ?? [];
					connectionIds.push(connection.id);
					maps.connectionsByGeometryId.set(geometryId, connectionIds);
				}
			}
		}
	}

	return maps;
}
