import {
	type LegacyFilterSpecification,
	type Map,
	type MapStyleDataEvent,
	Point,
} from '@packages/internal/outdoor-context-v5';
import { type BBox, type Position as GeoJSONPosition } from 'geojson';
import { multiPolygon, polygon } from '@turf/helpers';
import booleanIntersects from '@turf/boolean-intersects';
import { IdleQueue } from '../../utils/idle-queue';

// there's no reliable way to know when maplibre tiles have all come in, so we'll give up after a set amount of time
// so we stop trying to hide layers after a set amount of time
const TRYING_TIMEOUT_MS = 5000;

const DEBUG_CONTAINER_ID = 'outdoor-system-debug-container';

// Set to true to see the debug container used to query tileset features
const DEBUG = false;

export class OutdoorLayers {
	#dirty = true;
	#map?: Map;
	idleQueue = new IdleQueue();
	#stylesheetId = '';
	#userHiddenLayers: Set<string> = new Set();
	#hiddenLayersInStyle: Set<string> = new Set();
	#bbox?: BBox;
	#polygons?: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[];
	#timer: NodeJS.Timeout | null = null;
	#loaded = false;

	#getScreenPoints = (bbox: BBox): [Point, Point] => {
		if (!this.#map) {
			throw new Error('Map not initialized');
		}

		/**
		 * We want to take the bbox and produce a screen aligned rectangle fully containing the bbox. The bbox will likely
		 * not be aligned to the screen, so we project each corner onto the screen and find the box that fully contains
		 * each corner.
		 */
		const bboxPoints: [number, number][] = [
			[bbox[0], bbox[1]],
			[bbox[2], bbox[1]],
			[bbox[2], bbox[3]],
			[bbox[0], bbox[3]],
		];
		const screenPoints = bboxPoints.map(point => this.#map!.project(point));
		const maxX = Math.max(...screenPoints.map(p => p.x));
		const minX = Math.min(...screenPoints.map(p => p.x));
		const maxY = Math.max(...screenPoints.map(p => p.y));
		const minY = Math.min(...screenPoints.map(p => p.y));

		return [new Point(minX, minY), new Point(maxX, maxY)];
	};

	#stopTrying = () => {
		if (this.#timer) {
			clearTimeout(this.#timer);
			this.#timer = null;
			this.#dirty = false;
		}
	};

	#startTrying = () => {
		if (this.#timer) {
			clearTimeout(this.#timer);
			this.#timer = null;
		}
		this.#timer = setTimeout(() => {
			this.#stopTrying();
		}, TRYING_TIMEOUT_MS);
	};

	hideLayersIntersectingPolygons(
		bbox: BBox,
		polygons: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[],
		layers: string[],
	) {
		if (this.#timer) {
			clearTimeout(this.#timer);
		}
		this.#startTrying();

		this.#bbox = bbox;
		this.#polygons = polygons.reduce<GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[]>((acc, p) => {
			if (p.geometry.type === 'Polygon') {
				const poly = polygon(p.geometry.coordinates as GeoJSONPosition[][]);
				acc.push(poly);
			} else if (p.geometry.type === 'MultiPolygon') {
				acc.push(...p.geometry.coordinates.map(c => polygon(c as GeoJSONPosition[][])));
			}

			return acc;
		}, []);

		for (let layerId of layers) {
			this.#userHiddenLayers.add(layerId);
		}
		this.#dirty = true;
		this.#filterStylesInMap();
		this.#handleRender();
	}

	constructor(map?: Map) {
		if (map) {
			this.#map = map;
			this.#map.on('styledata', this.#handleStyleChange);
			this.#map.on('render', this.#handleRender);
			this.#loaded = this.#map.loaded();
			this.#map.on('load', () => {
				this.#loaded = true;
			});
		}

		if (DEBUG) {
			this.#addDebugContainer();
		}
	}

	#filterStylesInMap() {
		this.#hiddenLayersInStyle = new Set();
		if (this.#map) {
			for (let layerId of this.#userHiddenLayers) {
				if (this.#map.getLayer(layerId)) {
					this.#hiddenLayersInStyle.add(layerId);
				}
			}
		}
	}

	#handleRender = async () => {
		if (DEBUG) {
			if (this.#bbox) {
				const points = this.#getScreenPoints(this.#bbox);
				this.#updateDebugBBox(points);
			}
		}

		if (!this.#map || !this.#loaded) {
			return;
		}

		if (!this.#dirty) {
			return;
		}

		if (!this.#hiddenLayersInStyle || this.#hiddenLayersInStyle.size === 0) {
			return;
		}

		if (this.#running) {
			this.#cancelJobs();
		}

		if (this.#bbox && this.#polygons) {
			const points = this.#getScreenPoints(this.#bbox);
			const features = this.#map.queryRenderedFeatures(points, {
				layers: Array.from(this.#hiddenLayersInStyle),
			});

			// TODO: one issue remaning where a second render will trigger a second set of jobs,
			// some of which were already processed. Wish we had a more predictable way to know when all tiles are loaded.
			// I won't blow up this PR with this, but it's a TODO to fix.
			if (features.length > 0) {
				/**
				 * First, push the final job that will run. This job consumes the current filter expression and updates
				 * the layer filters. This job must run last, because it will override any previously set filters.
				 */
				this.idleQueue.onComplete(() => {
					if (this.#expr.length > 0) {
						this.#hiddenLayersInStyle.forEach(layer => {
							this.#map?.setFilter(layer, ['all', ...this.#expr]);
						});
						this.#dirty = false;
					}
					this.#expr = [];
				});

				for (const feature of features) {
					this.idleQueue.add(() => {
						if (feature.geometry != null && 'coordinates' in feature.geometry) {
							if (this.#polygons == null) {
								return;
							}
							let poly: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any> | undefined;

							if (feature.geometry.type === 'Polygon') {
								poly = polygon(feature.geometry.coordinates);
							} else if (feature.geometry.type === 'MultiPolygon') {
								poly = multiPolygon(feature.geometry.coordinates);
							}
							if (!poly) {
								return;
							}
							for (const p of this.#polygons) {
								const intersection = booleanIntersects(p, poly);

								if (intersection) {
									// @ts-ignore
									this.#expr.push(['!=', ['id'], feature.id] as LegacyFilterSpecification);

									return;
								}
							}
						}
					});
				}
			}
		}
	};

	#expr: LegacyFilterSpecification[] = [];

	get #running() {
		return this.idleQueue.size !== 0;
	}

	#cancelJobs = () => {
		this.idleQueue.clearAll();
	};

	#handleStyleChange = (e: MapStyleDataEvent) => {
		if (this.#map && e && e.dataType === 'style' && e.type === 'styledata' && this.#userHiddenLayers.size > 0) {
			// @ts-ignore - e.style.stylesheet does not exist in the type
			// if we can find the id we'll use it, otherwise assume it has changed
			const newStylesheetId = e?.style?.stylesheet?.id;
			if (!newStylesheetId || newStylesheetId !== this.#stylesheetId) {
				this.#stylesheetId = newStylesheetId;
				this.#filterStylesInMap();
				this.#dirty = true;
			}
		}
	};

	#addDebugContainer() {
		if (document.getElementById(DEBUG_CONTAINER_ID)) {
			return;
		}

		if (this.#map) {
			const container = document.createElement('div');
			container.id = DEBUG_CONTAINER_ID;
			container.style.position = 'absolute';
			container.style.top = '0';
			container.style.left = '0';
			container.style.bottom = '0';
			container.style.right = '0';
			container.style.pointerEvents = 'none';
			container.style.zIndex = '1000';
			this.#map.getContainer().appendChild(container);
		}
	}

	#updateDebugBBox(bbox: [Point, Point]) {
		const container = document.getElementById(DEBUG_CONTAINER_ID);
		if (!container) {
			return;
		}

		let el = document.getElementById('bbox');
		if (el == null) {
			el = document.createElement('div');
			el.id = 'bbox';
			el.style.position = 'absolute';
			container.appendChild(el);
		}

		const topLeftPoint = [Math.min(bbox[0].x, bbox[1].x), Math.min(bbox[0].y, bbox[1].y)];
		const bottomRightPoint = [Math.max(bbox[0].x, bbox[1].x), Math.max(bbox[0].y, bbox[1].y)];

		el.style.top = topLeftPoint[1] + 'px';
		el.style.left = topLeftPoint[0] + 'px';
		el.style.width = bottomRightPoint[0] - topLeftPoint[0] + 'px';
		el.style.height = bottomRightPoint[1] - topLeftPoint[1] + 'px';
		el.style.border = `2px solid ${this.#dirty ? 'red' : 'blue'}`;
	}

	destroy() {
		if (this.#map) {
			this.#map.off('styledata', this.#handleStyleChange);
			this.#map.off('render', this.#handleRender);
		}
		this.idleQueue.clearAll();
	}
}
