import type { Vector2, Vector3 } from 'three';
import type { RendererState } from '../../renderer';
import { type Geometry2D, Geometry2DComponents } from '../../entities/geometry2d';
import { Geometry3D, Geometry3DComponents } from '../../entities';
import { type StyleComponent } from '../../components/styles/index';
import type LabelComponent from '../../components/label';
import type { MarkerComponent } from '../../components/marker';

export class TwoDProjectionSystem {
	state: RendererState;
	project: (projection: Vector2, position: Vector3) => void;
	constructor(state: RendererState, project: (projection: Vector2, position: Vector3) => void) {
		this.state = state;
		this.project = project;
	}

	update() {
		let entity: Geometry2D;
		let height = 0;
		let altitude = 0;
		let attachedEntityStyle: StyleComponent;
		let component: LabelComponent | MarkerComponent;
		for (const entityId of this.state.geometry2DIdsInScene) {
			entity = this.state.geometry2DMap.get(entityId) as Geometry2D;
			if (entity) {
				component = entity.components[Geometry2DComponents.UI];
				// find the geometry3D that this is attached to
				const attachedEntity = entity.attachedTo ? this.state.geometry3DMap.get(entity.attachedTo) : undefined;
				// if the entity is a geometry3D, then we can use the style of the entity to set the height, opacity, and visible of the 2D entity
				if (attachedEntity instanceof Geometry3D) {
					if (this.state.geometry3DIdsInScene.has(attachedEntity.id)) {
						attachedEntityStyle = attachedEntity.components[Geometry3DComponents.Style] as StyleComponent;
						component = entity.components[Geometry2DComponents.UI];
						if (attachedEntityStyle.dirty || entity.components[Geometry2DComponents.UI].attachedDirty) {
							height = attachedEntityStyle.height;
							altitude = attachedEntityStyle.altitude;
							entity.setAltitude(height + altitude);
						}

						// attached entity is in the scene, but it could be hidden
						const shouldBeVisible = attachedEntityStyle.visible;
						if (component.visible !== shouldBeVisible) {
							component.visible = shouldBeVisible;
						}
					} else if (component.visible === true) {
						// attached entity is not in the scene, hide the attached 2D entity
						component.visible = false;
					}
				}
				this.project(entity.components[Geometry2DComponents.UI].projection, entity.position);
				entity.components[Geometry2DComponents.UI].attachedDirty = false;
			}
		}
	}
}
