import { Geometry3D, Geometry3DComponents } from '../../entities';
import type { MeshComponent } from '../../components/mesh';
import type { StyleComponent } from '../../components/styles/style';
import type { EntityTypes, RendererState } from '../../renderer';
import { updateAltitude, updateHeight } from './utils';
import { Matrix4, Quaternion, Vector3 } from 'three';

/**
 * Any physical changes to geometry should go in here. This includes height changes, etc.
 * This is to ensure that physical changes apply before any other systems need to do their work,
 * such as the outline system.
 *
 * TODO: For now, this piggybacks of style.dirty, but we may want to create a flag indicating geometry dirty.
 */
export class MeshModificationSystem {
	rendererState: RendererState;
	dirty = false;
	constructor(rendererState: RendererState) {
		this.rendererState = rendererState;
	}

	#meshComponentBeingProcessed?: MeshComponent;
	#styleComponentBeingProcessed?: StyleComponent;
	// these are reused for updating the geometry position
	#processingMatrix = new Matrix4();
	#processingPosition = new Vector3();
	#processingQuaternion = new Quaternion();
	#processingScale = new Vector3();

	// I am starting to add `updateOne` methods, so that in the future a huge optimization
	// will be to have a single loop running in the render system and calling all the `updateOne`
	// methods of all the systems. This way we loop once through all geometries per frame
	// we can leave the `update` method for unit testing
	updateOne(entity?: EntityTypes) {
		if (
			entity instanceof Geometry3D &&
			entity.type === 'geometry' &&
			entity.components[Geometry3DComponents.Style]?.dirty
		) {
			this.#meshComponentBeingProcessed = entity.components[Geometry3DComponents.Mesh] as MeshComponent;
			this.#styleComponentBeingProcessed = entity.components[Geometry3DComponents.Style] as StyleComponent;

			if (entity.type === 'geometry') {
				let modified = false;
				if (this.#styleComponentBeingProcessed.height !== this.#meshComponentBeingProcessed.currentHeight) {
					modified = updateHeight(
						this.#meshComponentBeingProcessed,
						this.#styleComponentBeingProcessed.height,
						this.#styleComponentBeingProcessed.initialHeight,
					);
				}
				if (this.#styleComponentBeingProcessed.altitude !== this.#meshComponentBeingProcessed.altitude) {
					modified = updateAltitude(
						this.#meshComponentBeingProcessed,
						this.#styleComponentBeingProcessed.altitude,
						this.#processingMatrix,
						this.#processingPosition,
						this.#processingQuaternion,
						this.#processingScale,
					);
				}

				if (modified && this.#meshComponentBeingProcessed.mesh != null) {
					// we must recompute the bounding sphere for frustrum culling to handle the updated geometry position
					this.#meshComponentBeingProcessed.mesh.computeBoundingSphere();
				}
			}
		}
	}

	update() {
		let entity: EntityTypes | undefined;
		for (const entityId of this.rendererState.geometry3DIdsInScene) {
			entity = this.rendererState.geometry3DMap.get(entityId);
			this.updateOne(entity);
		}
		this.cleanup();
	}

	cleanup() {
		this.#meshComponentBeingProcessed = undefined;
		this.#styleComponentBeingProcessed = undefined;
	}
}
