import { Shape, Path, ExtrudeGeometry } from 'three';
import type { Feature, MultiPolygon } from 'geojson';
import type { PaintStyle } from '../types';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import { createExtrudeSettings } from '../utils/constants';

const DEFAULT_COLOR = 'gray';
const DEFAULT_HEIGHT = 0.1;

export function renderMultiPolygonGeometry(
	feature: Feature<MultiPolygon, any>,
	convertTo3DMapPosition,
	style?: PaintStyle,
) {
	let { color = DEFAULT_COLOR, opacity = 1, altitude = 0, height = DEFAULT_HEIGHT } = style || {};
	if (Array.isArray(opacity)) {
		opacity = 1;
	}

	const boundingBox = {
		min: { x: Infinity, y: Infinity, z: altitude },
		max: { x: -Infinity, y: -Infinity, z: height + altitude },
	};

	// Use StyleComponent bevel as single source of truth
	const extrudeSettings = createExtrudeSettings(style?.bevel, height);

	const geometries: ExtrudeGeometry[] = [];

	feature.geometry.coordinates.forEach(coordinates => {
		const shape = new Shape();
		const cartesianCoordinates = coordinates[0].map(coordinate => {
			const coord = convertTo3DMapPosition(coordinate[1], coordinate[0]);
			boundingBox.min.x = Math.min(boundingBox.min.x, coord.x);
			boundingBox.min.y = Math.min(boundingBox.min.y, coord.y);
			boundingBox.max.x = Math.max(boundingBox.max.x, coord.x);
			boundingBox.max.y = Math.max(boundingBox.max.y, coord.y);

			return coord;
		});

		shape.moveTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);
		cartesianCoordinates.slice(1).forEach(coord => shape.lineTo(coord.x, coord.y));
		shape.lineTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);
		const holes = coordinates.slice(1);

		shape.holes = holes.map(hole => {
			const cartesianCoordinates = hole.map(coordinate => {
				return convertTo3DMapPosition(coordinate[1], coordinate[0]);
			});
			const path = new Path();
			path.moveTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);
			cartesianCoordinates.slice(1).forEach(coord => path.lineTo(coord.x, coord.y));
			path.lineTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);

			return path;
		});
		const geometry = new ExtrudeGeometry(shape, extrudeSettings);
		geometries.push(geometry);
	});

	const geometry = BufferGeometryUtils.mergeGeometries(geometries);

	// Dispose individual geometries after merging to prevent memory leaks
	geometries.forEach(geom => geom.dispose());

	geometry.translate(0, 0, altitude || 0);

	geometry.userData = {
		style: {
			color,
			opacity,
		},
		usedBevelSettings: extrudeSettings,
	};

	return geometry;
}
