import type { GeometryGroupState, GeometryState } from '@mappedin/core-sdk';
import z from 'zod';
import { parseCompleteState } from './state-utils';

export const DOORS_STATE_TYPE = 'doors';

export const doorsStateSchema = z.object({
	type: z.literal(DOORS_STATE_TYPE).default(DOORS_STATE_TYPE),
	/**
	 * Color of the doors.
	 * @default #ffffff
	 */
	color: z.string().default('#ffffff'),
	/**
	 * Color of the top of doors, if not set, defaults to the color.
	 */
	topColor: z.string().optional(),
	/** Texture to apply to the geometry element. */
	texture: z.object({ url: z.string().optional() }).optional(),
	/** Top texture to apply to the geometry element. */
	topTexture: z.object({ url: z.string().optional() }).optional(),
	/**
	 * Visibility of the doors.
	 * @default false
	 */
	visible: z.boolean().default(false),
	/**
	 * Opacity of the doors.
	 * @default 1
	 */
	opacity: z.number().default(1),
});

export const doorsStateSchemaPartial = doorsStateSchema.partial();
const doorsStateSchemaStrict = z.strictObject(doorsStateSchema.shape);

export type TDoorsState = z.infer<typeof doorsStateSchemaStrict>;
export type TDoorsUpdateState = z.infer<typeof doorsStateSchemaPartial>;

export function getDoorsState(geometryGroupState: GeometryGroupState): TDoorsState {
	const { color, topColor, texture, topTexture, visible, opacity } = geometryGroupState;

	return parseCompleteState(doorsStateSchema, {
		visible,
		color,
		topColor,
		opacity,
		texture: texture ? { url: texture } : undefined,
		topTexture: topTexture ? { url: topTexture } : undefined,
	});
}

export function translateDoorsStateToGeojsonCore(
	state: TDoorsUpdateState,
	currentState: Partial<GeometryState>,
): Partial<GeometryState> {
	const update: Partial<GeometryState> = {};
	if (currentState == null) {
		return update;
	}
	if (state.color) {
		if (state.color === 'initial') {
			update.color = currentState.initialColor;
		} else {
			update.color = state.color;
		}
	}
	if (state.topColor) {
		if (state.topColor === 'initial') {
			update.topColor = currentState.initialTopColor;
		} else {
			update.topColor = state.topColor;
		}
	}
	if (state.visible !== undefined) {
		update.visible = state.visible;
	}
	if (state.texture?.url !== undefined) {
		update.texture = state.texture.url;
	}
	if (state.topTexture?.url !== undefined) {
		update.topTexture = state.topTexture.url;
	}

	if (state.opacity !== undefined) {
		update.opacity = state.opacity;
	}

	return update;
}
