import type { ParsedMVF, SpaceCollection } from '@mappedin/mvf-v2';
import type {
	DirectionFeature,
	DirectionsCollection,
	ObstructionCollection,
} from '@packages/internal/geojson-navigator';
import { Navigator } from '@packages/internal/geojson-navigator';
import type { Connection, MapDataInternal } from '../../map-data-objects';
import { Coordinate } from '../../map-data-objects';
import type { TDirectionZone, TNavigationTarget } from '../../types';
import type { Edge } from '@packages/internal/geojson-navigator/src/edge';
import { Directions } from './directions';
import {
	getDepartureFromDirections,
	getDestinationFromDirections,
	getNodesFromTarget,
	getNodesFromTargetSync,
} from './utils';

export class DirectionsInternal {
	navigator: Navigator;
	private readonly connections: ParsedMVF['connection.json'];
	//
	/**
	 * @hidden
	 */
	constructor({
		nodes,
		geojsonCollection,
		connections,
		groupBy,
		multiplicativeDistanceWeightScaling,
		flagDeclarations,
	}: {
		nodes: ParsedMVF['node.geojson'];
		geojsonCollection: ParsedMVF['obstruction'] | ParsedMVF['space'];
		connections: ParsedMVF['connection.json'];
		groupBy?: string;
		multiplicativeDistanceWeightScaling?: boolean;
		flagDeclarations?: ParsedMVF['navigationFlags.json'];
	}) {
		this.navigator = new Navigator({
			nodes,
			geojsonCollection: this.createCollection(geojsonCollection),
			groupBy,
			multiplicativeDistanceWeightScaling,
			flagDeclarations,
		});
		this.connections = connections;
	}

	processTargets(
		fromNodesByTarget: Map<TNavigationTarget, string[]>,
		toNodesByTarget: Map<TNavigationTarget, string[]>,
		mapData: MapDataInternal,
	): {
		originIds: string[];
		destinationNodeIds: string[];
		featureToNodeIdsMap: Map<TNavigationTarget, string[]>;
		/**
		 * Node ID for early return when origin and destination share same space.
		 */
		earlyReturnNodeId?: string;
	} {
		const originIds: string[] = [];
		const destinationNodeIds: string[] = [];
		const featureToNodeIdsMap = new Map<TNavigationTarget, string[]>();
		/**
		 * Track the space ids we're departing from. If a destination has the same space id we can return early.
		 */
		const nodeSpaceIdsSet = new Set<string>();

		for (const [target, nodeIds] of fromNodesByTarget) {
			featureToNodeIdsMap.set(target, nodeIds);
			for (const nodeId of nodeIds) {
				originIds.push(nodeId);
				if (mapData.nodesById[nodeId]?.space?.id) {
					nodeSpaceIdsSet.add(mapData.nodesById[nodeId].space.id);
				}
			}
		}
		for (const [target, nodeIds] of toNodesByTarget) {
			featureToNodeIdsMap.set(target, nodeIds);
			for (const nodeId of nodeIds) {
				destinationNodeIds.push(nodeId);
				if (mapData.nodesById[nodeId]?.space && nodeSpaceIdsSet.has(mapData.nodesById[nodeId]?.space?.id)) {
					// A departure and destination share a space id. The distance is 0.
					return {
						originIds,
						destinationNodeIds,
						featureToNodeIdsMap,
						earlyReturnNodeId: nodeId,
					};
				}
			}
		}

		return {
			originIds,
			destinationNodeIds,
			featureToNodeIdsMap,
		};
	}

	public getDirections = async (
		from: TNavigationTarget[],
		to: TNavigationTarget[],
		options: {
			accessible: boolean;
			smoothing: { enabled: boolean; radius: number };
			zones: TDirectionZone[];
			excludedConnections: Connection[];
			connectionIdWeightMap: Record<string, number>;
		},
		mapData: MapDataInternal,
	): Promise<Directions | undefined> => {
		const fromNodesByTarget: Map<TNavigationTarget, string[]> = new Map();
		const toNodesByTarget: Map<TNavigationTarget, string[]> = new Map();

		for (const target of from) {
			fromNodesByTarget.set(target, await getNodesFromTarget(target, mapData, mapData.enterpriseMode));
		}

		for (const target of to) {
			toNodesByTarget.set(target, await getNodesFromTarget(target, mapData, mapData.enterpriseMode));
		}

		const { originIds, destinationNodeIds, featureToNodeIdsMap, earlyReturnNodeId } = this.processTargets(
			fromNodesByTarget,
			toNodesByTarget,
			mapData,
		);

		if (earlyReturnNodeId) {
			return this.#execDirections(
				from,
				to,
				[earlyReturnNodeId],
				[earlyReturnNodeId],
				options,
				mapData,
				featureToNodeIdsMap,
			);
		}

		return this.#execDirections(from, to, originIds, destinationNodeIds, options, mapData, featureToNodeIdsMap);
	};

	/** @deprecated use getDirections instead */
	public getDirectionsSync = (
		from: TNavigationTarget[],
		to: TNavigationTarget[],
		options: {
			accessible: boolean;
			smoothing: { enabled: boolean; radius: number };
			zones: TDirectionZone[];
			excludedConnections: Connection[];
			connectionIdWeightMap: Record<string, number>;
		},
		mapData: MapDataInternal,
	): Directions | undefined => {
		const fromNodesByTarget: Map<TNavigationTarget, string[]> = new Map(
			from.map(target => [target, getNodesFromTargetSync(target, mapData, mapData.enterpriseMode)]),
		);
		const toNodesByTarget: Map<TNavigationTarget, string[]> = new Map(
			to.map(target => [target, getNodesFromTargetSync(target, mapData, mapData.enterpriseMode)]),
		);

		const { originIds, destinationNodeIds, featureToNodeIdsMap, earlyReturnNodeId } = this.processTargets(
			fromNodesByTarget,
			toNodesByTarget,
			mapData,
		);

		if (earlyReturnNodeId) {
			return this.#execDirections(
				from,
				to,
				[earlyReturnNodeId],
				[earlyReturnNodeId],
				options,
				mapData,
				featureToNodeIdsMap,
			);
		}

		return this.#execDirections(from, to, originIds, destinationNodeIds, options, mapData, featureToNodeIdsMap);
	};

	#execDirections = (
		from: TNavigationTarget[],
		to: TNavigationTarget[],
		originIds: string[],
		destinationNodeIds: string[],
		options: {
			accessible: boolean;
			smoothing: { enabled: boolean; radius: number };
			zones: TDirectionZone[];
			excludedConnections: Connection[];
			connectionIdWeightMap: Record<string, number>;
		},
		mapData: MapDataInternal,
		/**
		 * A pre-computed map of feature to node ids
		 */
		featureToNodeIdsMap: Map<TNavigationTarget, string[]>,
	) => {
		// TODO: This should probably disable the edges not the two nodes of the connection but needs testing on Platform maps
		// we could also consider using the navigationFlags in MVF
		const disabledConnectionNodeIds = this.getDisabledConnectionNodeIds(options.accessible).filter(
			nodeId => !originIds.includes(nodeId) && !destinationNodeIds.includes(nodeId),
		);

		const overrideEdgeWeights = new Map<Edge, number>();
		// Merge the connectionIdWeightMap with the excludedConnections
		const connectionWeights = { ...options.connectionIdWeightMap };
		for (const connection of options.excludedConnections) {
			// Override with Infinity to disable the connection
			connectionWeights[connection.id] = Infinity;
		}
		for (const connectionId in connectionWeights) {
			const connection = mapData.getById('connection', connectionId);
			if (!connection) {
				continue;
			}
			this.#setConnectionWeight(connection, connectionWeights[connectionId], overrideEdgeWeights);
		}

		const directions = this.navigator.getDirections({
			zones: options.zones.map(zone => ({ geometry: zone.geometry, cost: zone.cost, map: zone.floor?.id })),
			originIds,
			destinationNodeIds,
			disabledConnectionNodeIds,
			simplify: {
				enabled: options.smoothing.enabled,
				bufferRadius: options.smoothing.radius,
			},
			overrideEdgeWeights,
			multiplicativeDistanceWeightScaling: !mapData.enterpriseMode,
		});
		this.addCoordinateDirections(directions, from, to);

		const departure = getDepartureFromDirections(directions, from, featureToNodeIdsMap);
		const destination = getDestinationFromDirections(directions, to, featureToNodeIdsMap);

		const makerDirections = new Directions(directions, mapData, departure, destination);

		/**
		 * If the distance is 0, but the from and to targets do not share any nodes, then we can assume that the
		 * directions are invalid and we should return undefined.
		 */
		if (
			makerDirections.distance === 0 &&
			new Set([...originIds, ...destinationNodeIds]).size === originIds.length + destinationNodeIds.length
		) {
			return undefined;
		}

		return makerDirections;
	};

	/**
	 * Get the node IDs of connections that do not match the accessibility setting provided.
	 * A disabled connection node is a connection node that acts as a regular node
	 * (ie. the edges that would cause a floor change are ignored).
	 *
	 * @hidden
	 * @param accessible {boolean}
	 */
	getDisabledConnectionNodeIds = (accessible: boolean): string[] => {
		if (accessible === false) return [];

		return this.connections.reduce((acc, connection) => {
			if (connection.accessible) return acc;

			return acc.concat(connection.nodes);
		}, [] as string[]);
	};

	/**
	 * Set the weight for all edges between a connection's nodes.
	 */
	#setConnectionWeight = (connection: Connection, weight: number, overrideEdgeWeights: Map<Edge, number>) => {
		if (connection.nodes.length < 2) {
			return;
		}

		const connectionNodeIds = new Set(connection.nodes.map(n => n.id));

		for (const sourceNode of connection.nodes) {
			const sourceEdges = this.navigator.graph.edges[sourceNode.id];
			if (!sourceEdges) continue;

			for (const edge of sourceEdges) {
				if (!connectionNodeIds.has(edge.destination.properties.id)) continue;

				// Set weight for this specific edge (nodeA -> nodeB is different from nodeB -> nodeA)
				overrideEdgeWeights.set(edge, weight);
			}
		}
	};

	/**
	 * If the navigation targets are coordinates, make sure to include them in the final directions. For other
	 * data types (e.g. POIs, Doors, Spaces), the coordinate should have a coincidental node generated in the
	 * navigation graph already, so this is unnecessary.
	 *
	 * @hidden
	 * @param directions
	 * @param from {TNavigationTarget[]}
	 * @param to {TNavigationTarget[]}
	 */
	private addCoordinateDirections = (
		directions: DirectionsCollection,
		[from]: TNavigationTarget[],
		[to]: TNavigationTarget[],
	) => {
		if (!directions.features.length) {
			return;
		}

		if (Coordinate.is(from)) {
			const direction = this.createDirectionFeature(
				from,
				this.navigator.getAngle([from.longitude, from.latitude], directions.features[0].geometry.coordinates),
				this.navigator.getDistance([from.longitude, from.latitude], directions.features[0].geometry.coordinates),
			);
			directions.features.splice(0, 0, direction);
		}
		if (Coordinate.is(to)) {
			const direction = this.createDirectionFeature(to, 0, 0);
			const lastDirection = directions.features[directions.features.length - 1];
			lastDirection.properties.distance = this.navigator.getDistance(lastDirection.geometry.coordinates, [
				to.longitude,
				to.latitude,
			]);
			lastDirection.properties.angle = this.navigator.getAngle(lastDirection.geometry.coordinates, [
				to.longitude,
				to.latitude,
			]);
			directions.features.push(direction);
		}
	};

	/**
	 * Create an geojson collection from the parsed MVF.
	 *
	 * @hidden
	 * @param obstructions
	 */
	private createCollection(
		collection: ParsedMVF['obstruction'] | ParsedMVF['space'],
	): ObstructionCollection | SpaceCollection {
		return {
			type: 'FeatureCollection',
			features: Object.entries(collection).reduce((acc: any, [mapID, obstructions]) => {
				return acc.concat(
					obstructions!.features.map(feature => {
						feature.properties.map = mapID;

						return feature;
					}),
				);
			}, [] as ObstructionCollection['features'] | SpaceCollection['features']),
		};
	}

	/**
	 * Create a direction feature.
	 *
	 * @hidden
	 * @param coordinate
	 * @param angle
	 * @param distance
	 */
	private createDirectionFeature = (coordinate: Coordinate, angle: number, distance: number): DirectionFeature => {
		return {
			type: 'Feature',
			geometry: {
				type: 'Point',
				coordinates: [coordinate.longitude, coordinate.latitude],
			},
			properties: { groupBy: coordinate.floorId, id: coordinate.id, angle, distance, edges: [] },
		};
	};
}
