import type { Map } from '../../../../packages/outdoor-context-v5';
import '../../styles/html-controls.scss';

const MAPPEDIN_ATTRIBUTION = '© Mappedin';
const FEEDBACK_LINK = 'https://info.mappedin.com/feedback';

/**
 * All possible control anchor positions.
 */
export const ATTRIBUTION_POSITIONS = ['top-left', 'top-right', 'bottom-left', 'bottom-right'] as const;
/**
 * The threshold at which the attribution control will switch between compact and full mode.
 */
const MAX_COMPACT_CANVAS_WIDTH = 640;

const getAttributionButtonIcon =
	() => `<svg xmlns='http://www.w3.org/2000/svg' width='15' height='15' fill-rule='evenodd' viewBox='0 0 20 20'>
						<circle cx='10' cy='10' r='5.9' style='fill:rgba(255,255,255,1);'/>
						<path style='fill:rgba(0,0,0,0.7);' d='M4 10a6 6 0 1 0 12 0 6 6 0 1 0-12 0m5-3a1 1 0 1 0 2 0 1 1 0 1 0-2 0m0 3a1 1 0 1 1 2 0v3a1 1 0 1 1-2 0'/>
					</svg>`;

export type AttributionPosition = (typeof ATTRIBUTION_POSITIONS)[number];

export type AttributionControlOptions = {
	custom?: string[];
	position?: AttributionPosition;
	feedback?: boolean;
};

const DEFAULT_ATTRIB_OPTIONS = {
	custom: [],
	position: 'bottom-right',
	feedback: true,
} satisfies AttributionControlOptions;

export type HTMLControlsSystemOptions = {
	outdoorEnabled?: boolean;
};

export class HTMLControlsSystem {
	controlContainerEl: HTMLDivElement;
	controlPositions: Record<AttributionPosition, HTMLDivElement>;
	compact: boolean | undefined = undefined;
	#outdoorEnabled: boolean;

	attribContainerEl?: HTMLDetailsElement;
	attribInnerEl?: HTMLDivElement;
	attribButtonEl?: HTMLElement;
	feedbackLinkEl?: HTMLAnchorElement;
	attribHTML = MAPPEDIN_ATTRIBUTION;
	customAttributions: string[] = [];

	constructor(container: HTMLElement, options: HTMLControlsSystemOptions = {}) {
		this.#outdoorEnabled = !!options.outdoorEnabled;
		this.controlContainerEl = document.createElement('div');
		this.controlContainerEl.classList.add('mappedin-control-container');
		container.appendChild(this.controlContainerEl);
		this.controlPositions = ATTRIBUTION_POSITIONS.reduce((a, v) => {
			const el = document.createElement('div');
			el.classList.add(`mappedin-ctrl-${v}`);
			this.controlContainerEl.appendChild(el);
			a[v] = el;

			return a;
		}, {} as Record<AttributionPosition, HTMLDivElement>);
	}

	addAttributionControl(options: AttributionControlOptions = DEFAULT_ATTRIB_OPTIONS) {
		const position =
			options.position && ATTRIBUTION_POSITIONS.includes(options.position)
				? options.position
				: DEFAULT_ATTRIB_OPTIONS.position;
		this.attribContainerEl = document.createElement('details');
		this.attribContainerEl.classList.add('mappedin-ctrl-attrib', `mappedin-ctrl-attrib-${position}`);
		this.attribContainerEl.open = true;
		this.attribContainerEl.ariaLabel = 'Attribution';
		this.controlPositions[position].appendChild(this.attribContainerEl);

		this.attribButtonEl = document.createElement('summary');
		this.attribButtonEl.addEventListener('click', this.toggleAttribution);
		this.attribButtonEl.classList.add('mappedin-ctrl-attrib-button');
		this.attribButtonEl.title = 'Toggle attribution';
		this.attribButtonEl.ariaLabel = 'Toggle attribution';
		this.attribButtonEl.tabIndex = 0; // Tab index here so you can open and close the attribution control with the keyboard
		this.attribButtonEl.innerHTML = getAttributionButtonIcon();
		this.attribContainerEl.appendChild(this.attribButtonEl);

		this.attribInnerEl = document.createElement('div');
		this.attribInnerEl.classList.add('mappedin-ctrl-attrib-inner');
		// Set initial attribution based on outdoor state
		this.attribHTML = this.#outdoorEnabled ? MAPPEDIN_ATTRIBUTION : '';
		this.attribInnerEl.innerHTML = this.attribHTML;
		this.attribContainerEl.appendChild(this.attribInnerEl);

		const feedback = options.feedback ?? DEFAULT_ATTRIB_OPTIONS.feedback;
		if (feedback) {
			this.feedbackLinkEl = document.createElement('a');
			this.feedbackLinkEl.href = FEEDBACK_LINK;
			this.feedbackLinkEl.target = '_blank';
			this.feedbackLinkEl.rel = 'noopener';
			this.feedbackLinkEl.textContent = 'Provide Feedback';
			this.feedbackLinkEl.classList.add('mappedin-ctrl-attrib-inner');
			this.feedbackLinkEl.classList.add('mappedin-ctrl-attrib-inner-bold');
			this.attribContainerEl.appendChild(this.feedbackLinkEl);
		}

		if (options.custom) {
			this.customAttributions = options.custom;
		}
		this.updateAttributionDisplay();
	}

	toggleAttribution = (e: MouseEvent) => {
		if (this.attribContainerEl == null) return;
		e.preventDefault();
		if (this.attribContainerEl.classList.contains('mappedin-compact')) {
			if (this.attribContainerEl.classList.contains('mappedin-compact-show')) {
				this.attribContainerEl.removeAttribute('open');
				this.attribContainerEl.classList.remove('mappedin-compact-show');
			} else {
				this.attribContainerEl.classList.add('mappedin-compact-show');
				this.attribContainerEl.setAttribute('open', '');
			}
		}
	};

	/**
	 * Updates the attribution based on sources in the map style.
	 */
	private updateAttribution(map: Map) {
		if (!map.style) {
			// map style has not loaded yet
			return;
		}

		if (!this.attribInnerEl || !this.attribContainerEl) {
			return;
		}

		let attributions: string[] = this.#outdoorEnabled ? [MAPPEDIN_ATTRIBUTION] : [];

		const sourceCaches = map.style.sourceCaches;
		for (const id in sourceCaches) {
			const sourceCache = sourceCaches[id];

			if (sourceCache.used || sourceCache.usedForTerrain) {
				const source = sourceCache.getSource();
				if (source.attribution && attributions.indexOf(source.attribution) < 0) {
					attributions.push(source.attribution);
				}
			}
		}

		// remove any entries that are whitespace
		attributions = attributions.filter(e => String(e).trim());

		// remove any entries that are substrings of another entry.
		attributions = attributions.filter((attrib, i) => {
			for (let j = i + 1; j < attributions.length; j++) {
				if (attributions[j].indexOf(attrib) >= 0) {
					return false;
				}
			}

			return true;
		});

		// Store updated attributions for merging
		this.attribHTML = attributions.join(' ');

		// Update attribution display with map and custom attributions
		this.updateAttributionDisplay();
	}

	/**
	 * Combines custom and map attributions and updates the display.
	 */
	private updateAttributionDisplay() {
		const combinedAttributions = [...new Set([this.attribHTML, ...this.customAttributions])].filter(Boolean).join(' ');
		if (this.attribInnerEl) {
			this.attribInnerEl.innerHTML = combinedAttributions;
		}
	}

	private resizeAttribution(canvasWidth: number) {
		if (!this.attribContainerEl) {
			return;
		}
		// if canvas is small the compact button will appear
		if (canvasWidth <= MAX_COMPACT_CANVAS_WIDTH || this.compact) {
			if (this.compact === false) {
				this.attribContainerEl.setAttribute('open', '');
			} else if (
				!this.attribContainerEl.classList.contains('mappedin-compact') &&
				!this.attribContainerEl.classList.contains('mappedin-attrib-empty')
			) {
				this.attribContainerEl.setAttribute('open', '');
				this.attribContainerEl.classList.add('mappedin-compact', 'mappedin-compact-show');
			}
		} else {
			this.attribContainerEl.setAttribute('open', '');
			if (this.attribContainerEl.classList.contains('mappedin-compact')) {
				this.attribContainerEl.classList.remove('mappedin-compact', 'mappedin-compact-show');
			}
		}
	}

	destroyAttributionControl() {
		if (this.attribButtonEl) {
			this.attribButtonEl.removeEventListener('click', this.toggleAttribution);
		}

		if (this.attribContainerEl) {
			this.attribContainerEl.remove();
			this.attribContainerEl = undefined;
			this.attribInnerEl = undefined;
			this.attribButtonEl = undefined;
		}
	}

	resize(canvasWidth: number) {
		this.resizeAttribution(canvasWidth);
	}

	/**
	 * This should fire when Maplibre data changes.
	 */
	updateData(map: Map) {
		this.updateAttribution(map);
	}

	destroy() {
		this.destroyAttributionControl();
		this.controlContainerEl.remove();
		this.compact = undefined;
	}
}
