const FORCE_IDLE_CALLBACK_TIMEOUT = 3000;
// Minimum time buffer (in ms) required before starting the next callback
// This prevents starting expensive operations when we're almost out of idle time
const MIN_TIME_REMAINING = 2;

/**
 * A queue that processes callbacks during browser idle time.
 * Supports tracking queue size and running callbacks when all jobs complete.
 */
export class IdleQueue {
	#queue: (() => void)[] = [];
	#isProcessing = false;
	#onCompleteCallback?: () => void;
	#currentCallbackId?: number;

	/**
	 * Returns the current number of pending jobs in the queue
	 */
	get size(): number {
		return this.#queue.length;
	}

	/**
	 * Adds a callback to the queue.
	 * The queue will process callbacks during idle time to avoid blocking the main thread.
	 * @param callback - The callback to execute during idle time
	 */
	add(callback: () => void): void {
		this.#queue.push(callback);

		// Only start processing if we're not already processing
		if (!this.#isProcessing) {
			this.#processQueue();
		}
	}

	/**
	 * Sets a callback to run when all jobs in the queue have completed.
	 * This callback will be invoked after the last job finishes processing.
	 * @param callback - The callback to execute when queue is empty
	 */
	onComplete(callback: () => void): void {
		this.#onCompleteCallback = callback;
	}

	/**
	 * Clears the completion callback
	 */
	clearOnComplete(): void {
		this.#onCompleteCallback = undefined;
	}

	/**
	 * Processes items from the queue during idle time
	 */
	#processQueue(): void {
		this.#isProcessing = true;
		this.#currentCallbackId = requestIdleCallback(
			deadline => {
				let processedCount = 0;
				// Process items while we have sufficient idle time remaining
				// We require MIN_TIME_REMAINING to avoid starting expensive operations when almost out of time
				while (this.#queue.length > 0 && deadline.timeRemaining() > MIN_TIME_REMAINING) {
					const callback = this.#queue.shift();
					if (callback) {
						callback();
						processedCount++;
					}
				}

				// If we didn't process any jobs due to insufficient idle time,
				// process at least one job to ensure progress and prevent infinite rescheduling
				// Only do this when there's truly no idle time (< MIN_TIME_REMAINING)
				// This handles both real timeouts and polyfill cases where didTimeout is always false
				if (processedCount === 0 && this.#queue.length > 0 && deadline.timeRemaining() < MIN_TIME_REMAINING) {
					const callback = this.#queue.shift();
					if (callback) {
						callback();
					}
				}

				// Clear the callback ID since this callback has finished
				this.#currentCallbackId = undefined;

				// Reschedule if there are still items in the queue
				if (this.#queue.length > 0) {
					this.#processQueue();
				} else {
					// Queue is empty, mark as not processing and invoke completion callback
					this.#isProcessing = false;
					if (this.#onCompleteCallback) {
						this.#onCompleteCallback();
					}
				}
			},
			{ timeout: FORCE_IDLE_CALLBACK_TIMEOUT },
		);
	}

	/**
	 * Clears all pending jobs and the completion callback.
	 * Cancels any in-progress idle callback.
	 * Useful when resetting the queue completely.
	 */
	clearAll(): void {
		this.#queue = [];
		this.#isProcessing = false;
		this.#onCompleteCallback = undefined;
		if (this.#currentCallbackId !== undefined) {
			cancelIdleCallback(this.#currentCallbackId);
			this.#currentCallbackId = undefined;
		}
	}
}
