import type { Intersection, Object3D } from 'three';
import { GroupContainerComponents, GroupContainerObject3D } from '../../entities/group-container';

/** Takes an array of raycast intersects and traverses the parents to determine the intersected group-containers */
export function getUniqueIntersectedGroupContainers(intersects: Intersection<Object3D>[]): GroupContainerObject3D[] {
	const groupContainers = new Set<GroupContainerObject3D>();
	for (const intersect of intersects) {
		let object = intersect.object;

		while (
			object.visible &&
			object.parent &&
			object.parent.visible === true &&
			!groupContainers.has(object.parent as GroupContainerObject3D)
		) {
			object = object.parent;

			if (
				object instanceof GroupContainerObject3D &&
				object.type === 'group-container' &&
				object.visible &&
				object.components[GroupContainerComponents.Interaction]
			) {
				groupContainers.add(object);
			}
		}
	}

	return Array.from(groupContainers);
}
