import { Geometry3DComponents, type Geometry3D } from '../../entities/geometry3d';
import { MeshComponent } from '../../components/mesh';
import type { EntityTypes, Position, RendererState } from '../../renderer';
import type { InteractionComponent } from '../../components/interaction';
import type { Vector3 } from 'three';
import { DEFAULT_TEXT_STYLE } from '../../components/styles';
import type { Text3DStyleComponent } from '../../components/styles';
import { applyModelGeometryStyle, applyModelPosition, getMaterial, getMaterialSide, setMaterialSide } from './utils';
import { getGroupContainerOpacity } from '../../utils';
import { TextureComponent } from '../../components/texture';

function processColor(entity: Geometry3D, globalHoverColor: string) {
	const interactionComponent = entity.components[Geometry3DComponents.Interaction];
	const styleComponent = entity.components[Geometry3DComponents.Style];

	// if we have a hover, return that
	if (interactionComponent?.hover) {
		return [styleComponent.hoverColor || globalHoverColor, styleComponent.hoverColor || globalHoverColor];
	}

	return [styleComponent.color, styleComponent.topColor || styleComponent.color];
}

export class DrawSystem {
	state: RendererState;
	convertTo3DMapPosition: (position: Position) => Vector3;
	constructor(state: RendererState, convertTo3DMapPosition: (position: Position) => Vector3) {
		this.convertTo3DMapPosition = convertTo3DMapPosition;
		this.state = state;
	}

	determineTextureVisiblity(
		meshComponent: MeshComponent,
		textureComponent: TextureComponent,
		interactionComponent?: InteractionComponent,
	) {
		const material = getMaterial(meshComponent);
		if (material?.texture != null || material?.topTexture != null) {
			if (interactionComponent?.hover && material.texturesVisible) {
				material.hideTextures(meshComponent.instanceIndex);
			} else if (!material.texturesVisible) {
				material.showTextures(meshComponent.instanceIndex);
			}

			if (textureComponent.texture == null) {
				material.removeSideTexture(meshComponent.instanceIndex);
			}

			if (textureComponent.topTexture == null) {
				material.removeTopTexture(meshComponent.instanceIndex);
			}
		}
	}

	update() {
		let component: EntityTypes['components'][0];
		let interactionComponent: InteractionComponent | undefined;
		let entity: EntityTypes | undefined;
		for (const entityId of this.state.geometry3DIdsInScene) {
			entity = this.state.geometry3DMap.get(entityId);

			if (
				entity &&
				(entity.type === 'geometry' || entity.type === 'custom-geometry') &&
				(entity.components[Geometry3DComponents.Style]?.dirty ||
					entity.components[Geometry3DComponents.Interaction]?.dirty)
			) {
				const component = entity.components[Geometry3DComponents.Mesh];
				const styleComponent = entity.components[Geometry3DComponents.Style];
				interactionComponent = entity.components[Geometry3DComponents.Interaction];
				const color = processColor(entity as Geometry3D, this.state.hoverColor);
				component.setColor(color[0], color[1]);
				const material = getMaterial(component);
				if (styleComponent.visible !== component.visible) {
					component.visible = styleComponent.visible;
					if (component instanceof MeshComponent && component.textMesh) {
						const textEntity = this.state.geometry3DMap.get(component.textMesh.userData.entityId);
						if (
							textEntity?.type === 'text3d' &&
							textEntity.components[Geometry3DComponents.Mesh].polygonEntityId != null
						) {
							// should be setting the .visible on mesh instead of setting the style componnets since this is where the effect happens
							textEntity.components[Geometry3DComponents.Mesh].visible = styleComponent.visible;
						}
					}
				}
				if (styleComponent.altitude !== component.altitude) {
					component.altitude = styleComponent.altitude;
				}
				const opacity = styleComponent.opacity * getGroupContainerOpacity(entity);
				if (opacity !== component.opacity) {
					component.opacity = opacity;
				}
				if (styleComponent.renderOrder !== component.renderOrder) {
					component.renderOrder = styleComponent.renderOrder;
				}
				if (material && styleComponent.side !== getMaterialSide(material)) {
					setMaterialSide(material, styleComponent.side);
				}
				if (
					component instanceof MeshComponent &&
					styleComponent.shading != null &&
					(styleComponent.shading?.start !== getMaterial(component)?.getGradientShading().start ||
						styleComponent.shading?.end !== getMaterial(component)?.getGradientShading().end ||
						styleComponent.shading?.intensity !== getMaterial(component)?.getGradientShading().intensity)
				) {
					getMaterial(component)?.setGradientShading(
						styleComponent.shading?.start ?? 0,
						styleComponent.shading?.end ?? 0,
						styleComponent.shading?.intensity ?? 0,
					);
				}
				styleComponent.dirty = false;

				if (component instanceof MeshComponent) {
					const textureComponent = entity.components[Geometry3DComponents.TextureComponent];
					if (textureComponent instanceof TextureComponent) {
						this.determineTextureVisiblity(component, textureComponent, interactionComponent);
					}
				}

				if (entity.components[Geometry3DComponents.Interaction]) {
					entity.components[Geometry3DComponents.Interaction].dirty = false;
				}
			} else if (entity?.type === 'model') {
				if (entity.components[Geometry3DComponents.Style].dirty) {
					applyModelGeometryStyle(entity);
					entity.components[Geometry3DComponents.Style].dirty = false;
				}
				if (entity.components[Geometry3DComponents.Mesh].positionDirty) {
					applyModelPosition(entity, this.convertTo3DMapPosition);
					entity.components[Geometry3DComponents.Mesh].positionDirty = false;
				}
			} else if (entity?.type === 'image' && entity.components[Geometry3DComponents.Style]?.dirty) {
				component = entity.components[Geometry3DComponents.Mesh];
				const styleComp = entity.components[Geometry3DComponents.Style];
				if (styleComp.visible !== component.visible) {
					component.visible = styleComp.visible;
				}
				styleComp.dirty = false;
			} else if (
				entity?.type === 'text3d' &&
				entity.components[Geometry3DComponents.Style]?.dirty &&
				entity.components[Geometry3DComponents.Mesh].polygonEntityId != null
			) {
				component = entity.components[Geometry3DComponents.Mesh];
				const styleComp = entity.components[Geometry3DComponents.Style] as Text3DStyleComponent;
				if (styleComp.visible != null && styleComp.visible !== component.visible) {
					component.visible = styleComp.visible;
				}
				const { textMesh } = component;
				if (textMesh) {
					Object.assign(textMesh, styleComp.getState());
					// NOTE: prioritize hoverColor if exists, fallback to globalHoverColor
					const hoverColor = styleComp.hoverColor ?? this.state.text3dHoverColor;
					textMesh.color = styleComp.hoverByPolygon ? hoverColor : styleComp.color ?? DEFAULT_TEXT_STYLE.color;
					textMesh.outlineColor = styleComp.hoverByPolygon
						? hoverColor
						: styleComp.outlineColor ?? DEFAULT_TEXT_STYLE.outlineColor;
					textMesh.strokeColor = styleComp.hoverByPolygon
						? hoverColor
						: styleComp.strokeColor ?? DEFAULT_TEXT_STYLE.strokeColor;
				}
				styleComp.dirty = false;
			}
		}
	}
}
