import type { Vector3 } from 'three';
import { FrontSide, BackSide, Mesh, DoubleSide } from 'three';
import type { ModelGeometry3D } from '../../entities';
import { Geometry3DComponents } from '../../entities';
import type { MaterialSide, ModelStyle } from '../../components/styles';
import type { Position } from '../../renderer';
import { MeshComponent } from '../../components/mesh';
import type { BatchedStandardMaterial } from '../mesh-creation-and-optimization/batched-material';
import type { CustomGeometryComponent } from '../../components/custom';

export function applyModelGeometryStyle(model: ModelGeometry3D) {
	const modelComp = model.components[Geometry3DComponents.Mesh];
	const styleComponent = model.components[Geometry3DComponents.Style];
	if (styleComponent.opacity != null && styleComponent.opacity !== modelComp.opacity) {
		modelComp.opacity = styleComponent.opacity;
	}
	const materialStyle = styleComponent.material;
	const color = styleComponent.color;
	if (materialStyle || color) {
		const materialState = consolidateColorAndMaterial(color, materialStyle);
		modelComp.mesh?.traverse(child => {
			if (child instanceof Mesh && child.material) {
				if (Array.isArray(child.material)) {
					child.material.forEach(material => {
						if (materialState[material.name]) {
							material.color.set(materialState[material.name].color);
							// Disable vertex colors when material-specific colors are provided
							material.vertexColors = false;
						} else {
							material.color.set(color);
						}
					});
				} else {
					if (materialState[child.material.name]) {
						child.material.color.set(materialState[child.material.name].color);
						// Disable vertex colors when material-specific colors are provided
						child.material.vertexColors = false;
					} else {
						child.material.color.set(color);
					}
				}
			}
		});
	}

	const meshPositionZOffset = modelComp.feature.geometry.coordinates[2] ?? 0;
	const targetAltitude = styleComponent.verticalOffset + meshPositionZOffset;
	if (modelComp.altitude !== targetAltitude) {
		modelComp.altitude = targetAltitude;
	}

	if (styleComponent.visible !== modelComp.visible) {
		modelComp.visible = styleComponent.visible;
	}
	if (styleComponent.rotation) {
		modelComp.setRotation(styleComponent.rotation);
	}
	if (styleComponent.scale) {
		modelComp.setScale(styleComponent.scale);
	}
	if (styleComponent.visibleThroughGeometry !== modelComp.visibleThroughGeometry) {
		modelComp.visibleThroughGeometry = styleComponent.visibleThroughGeometry;
	}
}

/**
 * consolidates the color update and material
 * color updates
 * @param color
 * @param materialOverwrite
 * @returns
 */
export function consolidateColorAndMaterial(
	color?: string,
	materialOverwrite?: ModelStyle['material'],
): ModelStyle['material'] {
	const update: ModelStyle['material'] = {};

	// Apply material-specific colors first (these take precedence)
	if (materialOverwrite) {
		for (const name in materialOverwrite) {
			if (materialOverwrite[name].color) {
				update[name] = { color: materialOverwrite[name].color };
			}
		}
	}

	// Apply general color to default materials only if no material-specific color was provided
	if (color) {
		const defaultMaterials = ['Default', 'Fabric', 'Mpdn_Logo', 'CouchLogo'];
		for (const materialName of defaultMaterials) {
			if (!update[materialName]) {
				update[materialName] = { color };
			}
		}
	}

	return update;
}

export function applyModelPosition(model: ModelGeometry3D, convertTo3DMapPosition: (position: Position) => Vector3) {
	const modelComp = model.components[Geometry3DComponents.Mesh];
	const styleComponent = model.components[Geometry3DComponents.Style];
	const verticalOffset = styleComponent.verticalOffset ?? 0;
	const position = modelComp.feature.geometry.coordinates.slice() as Position;
	position[2] = (position[2] ?? 0) + verticalOffset;
	if (modelComp.mesh) {
		modelComp.mesh.position.copy(convertTo3DMapPosition(position));
	} else {
		modelComp.position.copy(convertTo3DMapPosition(position));
	}
}

export function getMaterial(meshComponent: MeshComponent | CustomGeometryComponent) {
	if (meshComponent instanceof MeshComponent) {
		return meshComponent.material as BatchedStandardMaterial;
	}
	return null;
}

export function getMaterialSide(material: BatchedStandardMaterial) {
	return material.side === BackSide ? 'back' : material.side === FrontSide ? 'front' : 'double';
}

export function setMaterialSide(material: BatchedStandardMaterial, side: MaterialSide) {
	material.side = side === 'back' ? BackSide : side === 'front' ? FrontSide : DoubleSide;
}
