import type { GroupContainerState } from '@mappedin/core-sdk';
import { z } from 'zod';
import { parseCompleteState } from './state-utils';

export const FACADE_STATE_TYPE = 'facade';

export const facadeStateSchema = z.object({
	type: z.literal(FACADE_STATE_TYPE).default(FACADE_STATE_TYPE),
	/**
	 * The opacity of the facade.
	 * @default 1
	 */
	opacity: z.number().default(1),
	/**
	 * Whether the facade is visible.
	 * @default true
	 */
	visible: z.boolean().default(true),
});

export const facadeStateSchemaPartial = facadeStateSchema.partial();
const facadeStateSchemaStrict = z.strictObject(facadeStateSchema.shape);

export type TFacadeState = z.infer<typeof facadeStateSchemaStrict>;
export type TFacadeUpdateState = z.infer<typeof facadeStateSchemaPartial>;

export function getFacadeState(state: GroupContainerState): TFacadeState {
	return parseCompleteState(facadeStateSchema, {
		opacity: state.opacity,
		visible: state.visible,
	});
}
