/* eslint-disable @typescript-eslint/ban-ts-comment */
import type { Image as MVFImage } from '@mappedin/mvf-v2';
import { randomId } from '../utils/randomId';
import BaseMetaData from './base-metadata-object';
import type { MapDataInternal } from '.';
import { parseCdnAssetUrl } from '../utils/misc';
import { Logger } from '@packages/internal/common';

/**
 * A class representing image link data within the map.
 */
class ImageMetaData extends BaseMetaData {
	/**
	 * @internal
	 */
	static readonly __type = 'image';

	/**
	 * @internal
	 */
	readonly __type = ImageMetaData.__type;

	#cache: Partial<object> = {};
	#mvfData: MVFImage;
	#data: MapDataInternal;

	/**
	 * Checks if the provided instance is of type Image.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Image, false otherwise.
	 */
	static is(instance: object): instance is ImageMetaData {
		return (instance as { __type: string }).__type === ImageMetaData.__type;
	}

	/**
	 * @internal
	 */
	constructor(options: { mvfData: MVFImage; mapData: MapDataInternal }) {
		super(String(randomId()));
		this.#mvfData = options.mvfData;
		this.#data = options.mapData;
	}

	/**
	 * Gets the url of the image.
	 *
	 * @returns {string | undefined} The url of the image.
	 */
	get url(): string | undefined {
		/**
		 * We don't want to cache any of this because we want this getter to return new values when the token manager
		 * refreshes the SAS tokens.
		 */
		const url = this.#mvfData.url;
		if (this.#data.tokenManager == null) {
			return url;
		}

		const parsed = parseCdnAssetUrl(this.#mvfData.url);
		if (parsed == null) {
			// Don't need a SAS token for a non-cdn image
			return url;
		}

		const mapId = parsed.mapId;
		try {
			if (this.#data.tokenManager.isEnterpriseMode()) {
				return url;
			}

			const sasToken = this.#data.tokenManager.getSasToken(mapId).token;

			if (sasToken == null || sasToken === '') {
				throw new Error('SAS token empty!');
			}

			return `${url}?${sasToken}`;
		} catch (error) {
			// We have some public SVGs that don't need SAS tokens, but it's difficult to distinguish them from private SVGs.
			if (parsed.fileType !== 'svg') {
				Logger.error('Error getting SAS token for image', { error, url });
			}

			return url;
		}
	}

	/**
	 * Gets the alt text of the image.
	 *
	 * @returns {string | undefined } The alt text of the image.
	 * @deprecated Use {@link altText} instead.
	 */
	get name(): string | undefined {
		return this.#mvfData.altText;
	}

	/**
	 * Gets the alt text of the image.
	 *
	 * @returns {string | undefined } The alt text of the image.
	 */
	get altText(): string | undefined {
		return this.#mvfData.altText;
	}

	/**
	 * Serializes the image data to JSON.
	 *
	 * @returns An object representing the image.
	 */
	toJSON() {
		return {
			__type: ImageMetaData.__type,
			id: this.id,
			url: this.url,
			name: this.name,
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of globalThis.Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export default ImageMetaData;
