export function convertCoordinateToPosition(coord: Coordinate): Position {
	if (coord.verticalOffset === 0) return [coord.longitude, coord.latitude];

	return [coord.longitude, coord.latitude, coord.verticalOffset];
}
export function convertPositionToCoordinate(coord: Position): Coordinate {
	return new Coordinate(coord[1], coord[0]);
}

function getEntranceMap(features: EntranceCollection['features']) {
	return new Map<string, EntranceFeature>(
		features.map(feature => {
			const { properties } = feature;
			const { id } = properties;

			return [id, feature];
		}),
	);
}

export const cutEntrancesFromLineStrings = (
	lineStrings: ObstructionCollection['features'],
	entranceCollection: EntranceCollection['features'],
) => {
	const entranceMap = getEntranceMap(entranceCollection);

	const output: {
		entrances: FeatureCollection<LineString, any>;
		walls: Feature<MultiLineString, any>;
	} = {
		entrances: {
			type: 'FeatureCollection',
			features: [],
		},
		walls: {
			type: 'Feature',
			properties: {
				id: lineStrings[0].properties.id,
			},
			geometry: {
				type: 'MultiLineString',
				coordinates: [],
			},
		},
	};
	for (const feature of lineStrings) {
		let result = [feature.geometry.coordinates] as Position[][];

		const entrancesList = (feature.properties.entrances || []).reduce((acc, name) => {
			if (entranceMap.has(name)) {
				acc.push(entranceMap.get(name)!);
			}

			return acc;
		}, [] as EntranceFeature[]);

		if (entrancesList.length) {
			// @TODO: fix types
			// @ts-ignore
			const { entrances, obstructions } = findAndSplitObstructionByAllEntrances(feature as any, entrancesList);
			result = obstructions.map(f => f.geometry.coordinates);
			output.entrances.features.push(...entrances);
		}

		output.walls.geometry.coordinates.push(...result);
	}

	return output;
};

export function differenceBetweenAngles(a: number, b: number): number {
	return ((((b - a + Math.PI) % (Math.PI * 2)) + Math.PI * 2) % (Math.PI * 2)) - Math.PI;
}

export function getRelativeBearing(relativeBearingAngle: number, threshold: number): TBearingType {
	let relativeBearing: TBearingType = BEARING_TYPE.Left;

	if (relativeBearingAngle <= -Math.PI / 4.0) {
		relativeBearing = BEARING_TYPE.Right;
	} else if (relativeBearingAngle <= -threshold) {
		relativeBearing = BEARING_TYPE.SlightRight;
	} else if (relativeBearingAngle <= threshold) {
		relativeBearing = BEARING_TYPE.Straight;
	} else if (relativeBearingAngle <= Math.PI / 4.0) {
		relativeBearing = BEARING_TYPE.SlightLeft;
	} else {
		relativeBearing = BEARING_TYPE.Left;
	}

	return relativeBearing;
}

const rankToNumber = ['', '', 'medium', 'high', 'always-visible'];

export function convertCollisionRankingTeirToNumber(tier: CollisionRankingTier): number {
	return rankToNumber.indexOf(tier as string);
}
export type StyleCollectionWithId = {
	[groupId: string]: WithId<PolygonStyle> | WithId<LineStringStyle> | WithId<PointStyle> | undefined;
};

export type AggregatedStyleMap = { [id: string]: StyleCollectionWithId[keyof StyleCollectionWithId] };
export function extractStyleMap(styles: StyleCollection) {
	const styleMap: AggregatedStyleMap = {};
	for (const styleGroupId in styles) {
		const groupStyle:
			| ((PolygonStyle | LineStringStyle | PointStyle) & {
					id?: string;
			  })
			| undefined = styles[styleGroupId];
		if (groupStyle == null) {
			continue;
		}
		groupStyle.id = styleGroupId;
		if ('polygons' in groupStyle) {
			for (const polygonId of groupStyle.polygons) {
				styleMap[polygonId] = groupStyle;
			}
		} else if ('lineStrings' in groupStyle) {
			for (const lineId of groupStyle.lineStrings) {
				styleMap[lineId] = groupStyle;
			}
		} else if ('points' in groupStyle) {
			for (const pointId of groupStyle.points) {
				styleMap[pointId] = groupStyle;
			}
		}
	}

	return styleMap;
}

export type WithId<T> = T & { id?: string };

export function groupGeometryByStyle(
	features: Feature<LineString | Polygon | Point, PolygonFeatureProperties<SpaceProperties | ObstructionProperties>>[],
	styleMap: AggregatedStyleMap,
) {
	return features.reduce((acc, feature) => {
		const style = styleMap[feature.properties.id];
		if (style == null) {
			return acc;
		}
		// If the feature has textures, we need to create a new style for it
		// otherwise, the texture will be applied to every geometry that shares the same style
		if (
			'textures' in feature.properties &&
			Array.isArray(feature.properties.textures) &&
			feature.properties.textures.length > 0
		) {
			const newStyle = { ...style, id: style.id + '-textures' };
			acc.set(newStyle, [feature]);
		} else {
			if (!acc.has(style)) {
				acc.set(style, []);
			}
			acc.get(style)!.push(feature);
		}

		return acc;
	}, new Map() as Map<WithId<PolygonStyle> | WithId<LineStringStyle> | WithId<PointStyle>, Feature<LineString | Polygon | Point, PolygonFeatureProperties<SpaceProperties | ObstructionProperties>>[]>);
}

export function translateToCoreStyle(
	style: PolygonStyle | LineStringStyle,
	userOptions: TShow3DMapOptions = {},
): PaintStyle | LineStyle {
	const coreStyle: PaintStyle | LineStyle = {
		...style,
		flipImageToFaceCamera: userOptions?.flipImagesToFaceCamera ?? true,
	};

	const shading = userOptions?.style?.shading ?? userOptions?.shadingAndOutlines;
	if (shading === true) {
		// turn on default shading
		coreStyle.shading = {
			start: 0.1,
			end: 0.15,
			intensity: 0.4,
		};
	} else if (typeof shading === 'object') {
		coreStyle.shading = {
			start: shading.start,
			end: shading.end,
			intensity: shading.intensity,
		};
	}

	// handle outlines
	coreStyle.outline = userOptions?.style?.outlines ?? userOptions?.shadingAndOutlines;

	// linestring specific styles
	if ('lineStrings' in style) {
		const wallTopColor = userOptions?.style?.wallTopColor ?? userOptions?.wallTopColor;
		if (wallTopColor != null) {
			coreStyle.topColor = wallTopColor;
		}
	}

	return coreStyle;
}

export const getTargetID = <T extends TMapElements | string>(target: T, api: GeoJsonApi): string | undefined => {
	const currentFloor = api.currentMap?.currentFloor;
	const geometryLayer = currentFloor.layers.get(GEOMETRY_LAYER);

	switch (target) {
		case WALLS.Interior:
			return geometryLayer?.layers.get(WALLS_ID);
		case WALLS.Exterior:
			return geometryLayer?.layers.get(EXTERIOR_WALLS_ID);
		case DOORS.Interior:
			return geometryLayer?.layers.get(INTERIOR_DOORS_ID);
		case DOORS.Exterior:
			return geometryLayer?.layers.get(EXTERIOR_DOORS_ID);
		default:
			return typeof target === 'string' ? target : target?.id;
	}
};

export function tweenToPromise(tween: Tween) {
	return new Promise<void>(resolve => {
		tween.onComplete(resolve);
		tween.onStop(resolve);
	});
}

export function getBoundingBox(geometry: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>): BBox {
	if (geometry.bbox) {
		return geometry.bbox;
	}
	const bbox = turfBbox(geometry);

	return bbox;
}

export function expandBbox(bbox: BBox, bboxToExpand: BBox) {
	return [
		Math.min(bbox[0], bboxToExpand[0]),
		Math.min(bbox[1], bboxToExpand[1]),
		Math.max(bbox[2], bboxToExpand[2]),
		Math.max(bbox[3], bboxToExpand[3]),
	];
}

export function unique<T>(array: T[]): T[] {
	return [...new Set(array)];
}

export function getTargetFloorObject(
	coordinate: Coordinate,
	floorsById: Map<string, FloorObject>,
	defaultFloor: FloorObject,
) {
	return coordinate.floorId
		? floorsById.has(coordinate.floorId)
			? floorsById.get(coordinate.floorId)!
			: defaultFloor
		: defaultFloor;
}

/** Gets the ID of a geometry that 2D entities can attach to */
export function getAttachableGeometryId(target: IAnchorable) {
	if (Space.is(target) || MapObject.is(target)) {
		return target.id;
	}

	return undefined;
}

export function shouldAnimatePathDrawing(
	path: EntityId<PathState> & {
		coordinates: Coordinate[];
	},
	mapData: MapData,
	api: GeoJsonApi,
): boolean {
	const isConnectionPath = path.coordinates.length === 2 && path.coordinates[0].floorId !== path.coordinates[1].floorId;

	let shouldAnimate = true;

	if (isConnectionPath && path.coordinates[0].floorId != null && path.coordinates[1].floorId != null) {
		const fromFloor = mapData?.getById('floor', path.coordinates[0].floorId);
		const toFloor = mapData?.getById('floor', path.coordinates[1].floorId);
		if (fromFloor && toFloor) {
			const fromFloorState = api.getState(fromFloor);
			const toFloorState = api.getState(toFloor);
			if (fromFloorState != null && toFloorState != null) {
				// For connections, if the floors are visible and the altitude is different, we should animate
				shouldAnimate =
					fromFloorState.visible && toFloorState.visible && fromFloorState.altitude !== toFloorState.altitude;
			}
		}
	} else {
		const floor =
			path.coordinates[0].floorId != null ? mapData?.getById('floor', path.coordinates[0].floorId) : undefined;

		shouldAnimate = floor ? api.getState(floor)?.visible ?? true : true;
	}

	return shouldAnimate;
}

export function generateImageId(feature: FloorImageFeature, imageIdx: number) {
	// let's base64 decode the path
	const pathSplit = feature.properties.path.split('/');
	const fileName = pathSplit[pathSplit.length - 1].split('.')[0];
	const decodedFileName = atob(fileName);

	return 'img_' + cyrb53(decodedFileName).toString() + '-' + imageIdx;
}

export function parseFloorGap(floorGap: number | 'auto' = 'auto', height?: number): number {
	if (floorGap === 'auto') {
		return height ? Math.round(height * FLOOR_GAP_MULTIPLIER) : DEFAULT_FLOOR_HEIGHT;
	}

	// A number was provided, so return it
	return floorGap;
}

/**
 * This method computes the altitude and effective height for each floor
 * If there are missing floors, the previous floor before the gap will be extended to fill the gap
 * using that floor's max height (or floor gap if no max height is provided)
 * @param floors - All the floors in a building
 * @param floorGap - The floor gap of the current floor, defaults to 'auto'
 * @returns A map of floor elevation to altitude and effective height
 */
export function computeFloorAltitudes(
	floors: Floor[] | FloorObject[],
	floorGap: number | 'auto' = 'auto',
): Map<number, { altitude: number; effectiveHeight: number }> {
	if (!floors.length) {
		return new Map();
	}

	const orderedFloors = floors.sort((a, b) => a.elevation - b.elevation);
	let firstAboveGroundFloorIndex = orderedFloors.findIndex(floor => floor.elevation >= 0);
	// If there are no above ground floors, set the index to the length of the array
	if (firstAboveGroundFloorIndex === -1) {
		firstAboveGroundFloorIndex = orderedFloors.length;
	}
	let altitude = 0;
	const result = new Map<number, { altitude: number; effectiveHeight: number }>();

	// Processing above ground floors
	for (let i = firstAboveGroundFloorIndex; i < orderedFloors.length; i += 1) {
		const curFloor = orderedFloors[i];
		const nextFloor = orderedFloors[i + 1];
		const curElevation = curFloor.elevation;
		const nextElevation = nextFloor ? nextFloor.elevation : undefined;

		const gapsAfter = nextElevation !== undefined ? Math.max(0, nextElevation - curElevation - 1) : 0;
		const missingToGround = i === firstAboveGroundFloorIndex ? curElevation : 0;

		const effectiveHeight = parseFloorGap(floorGap, curFloor.maxHeight) * (1 + gapsAfter + missingToGround);
		const baseAltitude = altitude;

		result.set(curFloor.elevation, { altitude: baseAltitude, effectiveHeight: effectiveHeight });
		altitude += effectiveHeight;
	}

	// Processing below ground floors
	const norm = (lvl: number) => -lvl - 1;
	altitude = 0;
	for (let i = firstAboveGroundFloorIndex - 1; i >= 0; i -= 1) {
		const curFloor = orderedFloors[i];
		const nextFloor = orderedFloors[i - 1];
		const curElevation = norm(curFloor.elevation);
		const nextElevation = nextFloor ? norm(nextFloor.elevation) : undefined;

		const gapsAfter = nextElevation !== undefined ? Math.max(0, nextElevation - curElevation - 1) : 0;
		const missingToGround = i === firstAboveGroundFloorIndex - 1 ? curElevation : 0;

		const effectiveHeight = parseFloorGap(floorGap, curFloor.maxHeight) * (1 + gapsAfter + missingToGround);
		const baseAltitude = -(altitude + effectiveHeight);

		result.set(curFloor.elevation, { altitude: baseAltitude, effectiveHeight: effectiveHeight });
		altitude += effectiveHeight;
	}

	return result;
}

export function convertPanBoundsToCameraBounds(
	panBounds: PanBounds,
	convert3DMapPositionToCoordinate: RendererCore['convert3DMapPositionToCoordinate'],
): CameraBounds {
	const center = convert3DMapPositionToCoordinate(panBounds.center);
	return {
		center: new Coordinate({
			latitude: center[1],
			longitude: center[0],
		}),
		radius: panBounds.radius,
	};
}
