import type { GeometryFeature } from '@mappedin/mvf-core';
import { typedEntries } from '@mappedin/mvf-core/utils';
import type { LineStringStyle, PointStyle, PolygonStyle, StyleCollection } from '../../extensions/style.js';
import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Styles = Pick<MVFv2_STANDARD_MVFv3, 'defaultStyle'>;
export type V2Styles = Pick<ParsedMVF, 'styles.json'>;
export type V2Style = StyleCollection[string];

type GeometryType = GeometryFeature['geometry']['type'];
export type GeometryByType = Map<string, GeometryType>;

export function groupGeometryByType(
	geometryIds: string[],
	geometryByType: GeometryByType,
): Partial<Record<GeometryType, string[]>> {
	const grouped: Partial<Record<GeometryType, string[]>> = {};

	for (const id of geometryIds) {
		const type = geometryByType.get(id);
		if (type) {
			grouped[type] ??= [];
			grouped[type].push(id);
		}
	}

	return grouped;
}
export function buildStyles(v3: V3Styles, { geometryMaps }: Pick<BuilderOptions, 'geometryMaps'>): V2Styles {
	if (!v3.defaultStyle) {
		return {};
	}
	const v2: V2Styles = {};

	const styles: StyleCollection = {};
	v2['styles.json'] = styles;

	const spacesAndObstructions = [
		...Array.from(geometryMaps.space.values()).map((space) => space.features),
		...Array.from(geometryMaps.obstruction.values()).map((obstruction) => obstruction.features),
	];

	const geometryTypeById: GeometryByType = new Map(
		spacesAndObstructions.flatMap((feature) =>
			feature.map((geometry) => [geometry.properties.id, geometry.geometry.type]),
		),
	);

	for (const [groupId, v3Style] of typedEntries(v3.defaultStyle)) {
		const grouped = groupGeometryByType(
			v3Style.geometryAnchors.flatMap((anchor) => [
				...(geometryMaps.space.get(anchor.geometryId)?.ids ?? []),
				...(geometryMaps.obstruction.get(anchor.geometryId)?.ids ?? []),
			]),
			geometryTypeById,
		);

		if (grouped.Point && grouped.Point.length > 0) {
			const pointStyle: PointStyle = {
				points: Array.from(grouped.Point.values()),
				showImage: v3Style.extra?.showImage !== undefined ? v3Style.extra?.showImage === true : v3Style.opacity !== 0,
			};
			styles[`${groupId}.Point`] = pointStyle;
		}

		if (grouped.LineString && grouped.LineString.length > 0) {
			const lineStringStyle: LineStringStyle = {
				lineStrings: Array.from(grouped.LineString.values()),
				altitude: v3Style.altitude,
				color: v3Style.color,
				height: v3Style.height,
				opacity: v3Style.opacity,
				width: v3Style.buffer,
			};
			styles[`${groupId}.LineString`] = lineStringStyle;
		}

		if (grouped.Polygon && grouped.Polygon.length > 0) {
			const polygonStyle: PolygonStyle = {
				polygons: Array.from(grouped.Polygon.values()),
				altitude: v3Style.altitude,
				color: v3Style.color,
				height: v3Style.height,
				opacity: v3Style.opacity,
			};
			if (v3Style.extra?.showImage !== undefined) {
				polygonStyle.showImage = v3Style.extra.showImage === true;
			}
			styles[`${groupId}.Polygon`] = polygonStyle;
		}
	}

	return v2;
}
