import MapData from './map-data';
import type { TGetMapDataOptions } from '../../packages/mvf-utils';
import type { ParsedMVF as TMVF, ParsedMVFLocalePack } from '@mappedin/mvf-v2';
import { MapDataInternal } from './map-data-objects';
import { Logger } from '@packages/internal/common';
import type { LanguagePackHydrationItem } from './types';
import {
	createEnvControl,
	getBuildingIds,
	parseLanguagePack,
	unzipAndParseMVFv2,
} from '@packages/internal/mvf-utils/mvf-utils';
import type { TMapDataInternalOptions } from './map-data-objects/types';
import { TokenManager } from '@packages/internal/mvf-utils/tokens/token-manager';
import type { AnalyticsUpdateOptions } from './analytics';
import { shouldForceEnterpriseLocations } from '.';

export type THydrateMapDataBundle =
	| {
			type: 'binary';
			options?: {
				version?: '2.0.0' | '3.0.0';
				enterprise?: boolean;
			};
			languagePacks?: {
				language: { code: string; name: string };
				localePack: Uint8Array;
			}[];
			main: Uint8Array;
	  }
	| {
			type: 'json';
			options?: {
				version?: '2.0.0' | '3.0.0';
				enterprise?: boolean;
			};
			languagePacks?: {
				language: { code: string; name: string };
				localePack: ParsedMVFLocalePack;
			}[];
			main: TMVF;
	  };

/**
 * Load a MapData instance from a backup including language packs. Pass in userOptions to ensure outdoor view is available.
 */
export const hydrateMapData = async (
	backup: THydrateMapDataBundle | TMVF,
	userOptions?: TGetMapDataOptions,
): Promise<MapData> => {
	const mvfVersion =
		('mvfVersion' in (userOptions ?? {}) ? userOptions?.mvfVersion : undefined) ??
		('options' in backup && 'version' in (backup.options ?? {}) ? backup.options?.version : undefined);
	const userOptionsWithDefaults: TGetMapDataOptions = {
		prefetchTokens: true,
		...(userOptions || ({} as TGetMapDataOptions)),
		mvfVersion,
	};
	let mvf: TMVF;
	const env = createEnvControl();
	env.updateByUserOption(userOptionsWithDefaults);
	const internalOptions: TMapDataInternalOptions = {};

	if ('type' in backup && typeof backup.type === 'string') {
		internalOptions.enterprise = backup.options?.enterprise;

		if (backup.type === 'binary' && backup.main instanceof Uint8Array) {
			mvf = await unzipAndParseMVFv2(backup.main, userOptionsWithDefaults.mvfVersion);
			internalOptions.languagePacks = [];
			for (const lp of backup.languagePacks ?? []) {
				if (lp.localePack instanceof Uint8Array) {
					internalOptions.languagePacks.push({
						language: lp.language,
						localePack: await parseLanguagePack(lp.localePack),
					});
				}
			}
		} else if (backup.type === 'json') {
			mvf = backup.main as TMVF;
			internalOptions.languagePacks = (backup.languagePacks as LanguagePackHydrationItem[]) ?? [];
		} else {
			throw new Error('Invalid backup type');
		}

		if (backup.type === 'binary' && backup.main instanceof Uint8Array) {
			internalOptions.binaryBundle = backup.main;
		}
	} else {
		mvf = backup as TMVF;
		internalOptions.enterprise = mvf.enterprise != null;
	}

	env.setEnterprise(internalOptions.enterprise ?? false);

	const floorStackIds = getBuildingIds(mvf);
	const mapIds = floorStackIds.length > 0 ? floorStackIds : [userOptionsWithDefaults.mapId];
	// baseAuthUri needs to be set before tokenManager gets instantiated
	userOptionsWithDefaults.baseAuthUri = env.getBaseAuthUri();
	const tokenManager = new TokenManager(mapIds, {
		...userOptionsWithDefaults,
		enterprise: internalOptions.enterprise,
	});
	let accessToken: string | undefined;
	if ('accessToken' in userOptionsWithDefaults && userOptionsWithDefaults.accessToken) {
		accessToken = userOptionsWithDefaults.accessToken;
	} else if ('key' in userOptionsWithDefaults && 'secret' in userOptionsWithDefaults) {
		let tokenData;
		try {
			tokenData = await tokenManager.getAccessTokenAsync();
		} catch (e) {
			Logger.error('Failed getting token during hydrateMapData', e);
		}
		accessToken = tokenData?.token || '';
	}

	internalOptions.outdoorViewToken = accessToken;
	internalOptions.env = env;
	internalOptions.getMapDataOptions = userOptionsWithDefaults;

	if (userOptionsWithDefaults.prefetchTokens) {
		try {
			await tokenManager.readySync();
		} catch (e) {
			Logger.error('Failed to ready token manager!', e);
		}
	}

	if (shouldForceEnterpriseLocations()) {
		if (internalOptions.enterprise) {
			Logger.warn('forceEnterpriseLocations is enabled, but the map is already an enterprise map!');
		}

		if (userOptionsWithDefaults.mvfVersion !== '3.0.0') {
			Logger.warn("mvfVersion must be '3.0.0' when forceEnterpriseLocations is enabled!");
		}
	}

	const mapDataInternal = new MapDataInternal(mvf, internalOptions);
	if (accessToken) {
		mapDataInternal.Analytics.init({
			accessToken,
			mapId: userOptionsWithDefaults.mapId,
			key: 'key' in userOptionsWithDefaults ? userOptionsWithDefaults.key : undefined,
			secret: 'secret' in userOptionsWithDefaults ? userOptionsWithDefaults.secret : undefined,
			...userOptionsWithDefaults.analytics,
			baseUri: env.getAnalyticsBaseUri(),
			context: userOptionsWithDefaults.analytics?.context as AnalyticsUpdateOptions['context'],
		});
	}

	const mapData = new MapData(mapDataInternal);

	const language = mapDataInternal.findPreferredLanguage({
		languageCode: userOptionsWithDefaults.language,
		fallbackToNavigatorLanguage: false,
	});
	if (language) {
		try {
			await mapData.changeLanguage(language.code);
		} catch (e) {
			/* empty */
		}
	}

	if (typeof userOptions?.onMVFParsed === 'function') {
		userOptions.onMVFParsed(mvf);
	}

	return mapData;
};
