import { typedEntries } from '@mappedin/mvf-core/utils';
import type { AnnotationCollection, AnnotationProperties } from '../../extensions/annotation.js';
import type { ParsedMVF } from '../../types/bundle.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Annotations = Pick<MVFv2_STANDARD_MVFv3, 'annotations' | 'annotationSymbols' | 'geometry'>;
export type V2Annotations = Pick<ParsedMVF, 'annotation'>;

export function buildAnnotations(v3: V3Annotations, _: Record<string, unknown>): V2Annotations {
	if (!v3.annotations || !v3.annotationSymbols || !v3.geometry) {
		return {};
	}

	const v2Annotations: V2Annotations['annotation'] = {};

	for (const [floorId, annotations] of typedEntries(v3.annotations)) {
		if (
			!annotations ||
			annotations.length === 0 ||
			!v3.geometry[floorId] ||
			v3.geometry[floorId].features.length === 0
		) {
			continue;
		}

		const geometryById = new Map(v3.geometry[floorId].features.map((g) => [g.properties.id, g]));

		const v2AnnotationCollection: AnnotationCollection = {
			type: 'FeatureCollection',
			features: [],
		};

		for (const v3Annotation of annotations) {
			const geometry = geometryById.get(v3Annotation.geometryId);
			if (!geometry || geometry.geometry.type !== 'Point') {
				continue;
			}

			const v2AnnotationProperties: AnnotationProperties = {
				id: v3Annotation.id,
				externalId: v3Annotation.externalId,
				symbolId: v3Annotation.symbolKey,
			};

			v2AnnotationCollection.features.push({
				type: 'Feature',
				geometry: geometry.geometry,
				properties: v2AnnotationProperties,
			});
		}

		v2Annotations[floorId] = v2AnnotationCollection;
	}

	return {
		annotation: v2Annotations,
	};
}
