	get mapName(): string {
		return this.mvf['manifest.geojson'].features[0].properties.name;
	}

	get organizationId(): string {
		return this.mvf['manifest.geojson'].features[0].properties.org_id ?? '';
	}

	#mapCenter: Coordinate = new Coordinate(0, 0, 'initializing');

	get mapCenter(): Coordinate {
		if (this.#mapCenter.floorId === 'initializing') {
			this.#mapCenter = new Coordinate(
				this.mvf['manifest.geojson'].features[0].geometry.coordinates[1],
				this.mvf['manifest.geojson'].features[0].geometry.coordinates[0],
			);
		}

		return this.#mapCenter;
	}

	get naturalBearing(): number {
		return this.mvf['manifest.geojson'].features[0].properties.naturalBearing ?? 0;
	}

