	getByType(type: 'floor'): Floor[];
	getByType(type: 'floor-stack'): FloorStack[];
	getByType(type: 'connection'): Connection[];
	getByType(type: 'object'): MapObject[];
	getByType(type: 'point-of-interest'): PointOfInterest[];
	getByType(type: 'annotation'): Annotation[];
	getByType(type: 'node'): Node[];
	getByType(type: 'enterprise-location'): EnterpriseLocation[];
	getByType(type: 'enterprise-category'): EnterpriseCategory[];
	getByType(type: 'enterprise-venue'): EnterpriseVenue | undefined;
	getByType(type: 'facade'): Facade[];
	getByType(type: string): object[] | object | undefined {
		switch (type) {
			case 'location-profile':
				return this.locationProfiles;
			case 'location-category':
				return this.locationCategories;
			case 'area':
				return this.areas;
			case 'node':
				return this.nodes;
			case 'space':
				return this.spaces;
			case 'door':
				return this.doors;
			case 'floor':
				return this.floors;
			case 'floor-stack':
				return this.floorStacks;
			case 'connection':
				return this.connections;
			case 'object':
				return this.objects;
			case 'point-of-interest':
				return this.pointsOfInterest;
			case 'annotation':
				return this.annotations;
			case 'enterprise-location':
				return this.locations;
			case 'enterprise-category':
				return this.categories;
			case 'enterprise-venue':
				return this.venue;
			case 'facade':
				return this.facades;
			default:
				return [];
		}
	}

	/**
	 * Retrieves an object by its type and ID.
	 *
	 * @param type The type of the object (e.g., 'space', 'door').
	 * @param id The ID of the object.
	 * @returns The requested object, or undefined if not found.
	 */
	getById<T extends keyof Omit<TMapDataObjectTypes, 'enterprise-venue'> | 'unknown'>(
		type: T,
		id: string,
	):
		| (T extends keyof TMapDataObjectTypes ? TMapDataObjectTypes[T] : TMapDataObjectTypes[keyof TMapDataObjectTypes])
		| undefined;
	getById<T extends keyof Omit<TMapDataObjectTypes, 'enterprise-venue'>>(
		type: T,
		id: string,
	): TMapDataObjectTypes[T] | undefined;
	getById<T extends keyof Omit<TMapDataObjectTypes, 'enterprise-venue'> | 'unknown'>(
		type: 'unknown',
		id: string,
	):
		| (T extends keyof TMapDataObjectTypes ? TMapDataObjectTypes[T] : TMapDataObjectTypes[keyof TMapDataObjectTypes])
		| undefined;
	getById<T extends keyof Omit<TMapDataObjectTypes, 'enterprise-venue'> | 'unknown'>(
		type: T,
		id: string,
	):
		| (T extends keyof TMapDataObjectTypes ? TMapDataObjectTypes[T] : TMapDataObjectTypes[keyof TMapDataObjectTypes])
		| undefined {
		const collections = {
			'location-profile': this.locationProfilesById,
			'location-category': this.locationCategoriesById,
			space: this.spacesById,
			floor: this.floorsById,
			'floor-stack': this.floorStacksById,
			facade: this.facadesById,
			node: this.nodesById,
			door: this.doorsById,
			connection: this.connectionsById,
			'mvf-connection': (id: string) => this.#getConnectionFromMVFConnection(this.mvfConnectionsById[id]),
			object: this.objectsById,
			'point-of-interest': this.pointsOfInterestById,
			annotation: this.annotationsById,
			area: this.areasById,
			'enterprise-location': this.locationsById,
			'enterprise-category': this.categoriesById,
		};

		const getItemFromCollection = (
			type: keyof typeof collections,
		): TMapDataObjectTypes[keyof TMapDataObjectTypes] | undefined => {
			const collection = collections[type];
			if (collection == null) {
				return undefined;
			}
			if (typeof collection === 'function') {
				return collection(id);
			}
			return collection[id];
		};

		const getItemFromAllCollections = (): ReturnType<typeof getItemFromCollection> | undefined => {
			for (const collection of Object.keys(collections) as (keyof typeof collections)[]) {
				const item = getItemFromCollection(collection);
				if (item != null) {
					return item;
				}
			}
		};

		if (type === 'unknown') {
			return getItemFromAllCollections() as T extends keyof TMapDataObjectTypes
				? TMapDataObjectTypes[T]
				: TMapDataObjectTypes[keyof TMapDataObjectTypes];
		}

		return getItemFromCollection(type) as T extends keyof TMapDataObjectTypes
			? TMapDataObjectTypes[T]
			: TMapDataObjectTypes[keyof TMapDataObjectTypes];
	}

