/**
 * Determines smoothing configuration based on enterprise mode and user input
 * @internal
 */
export const getSmoothingConfig = (
	enterpriseMode: boolean,
	opt?: TGetDirectionsOptions,
): { enabled: boolean; radius: number } => {
	// Determine default smoothing based on enterprise mode
	const defaultSmoothingEnabled = !enterpriseMode; // false for enterprise, true for non-enterprise
	const smoothing = opt?.smoothing;

	// Check if user explicitly provided smoothing option
	const userProvidedSmoothing = smoothing != null;

	// Use user-provided smoothing or enterprise-aware default
	const smoothingEnabled = userProvidedSmoothing
		? typeof smoothing === 'object'
			? smoothing.enabled ?? defaultSmoothingEnabled // if smoothing is not specified in the options, we fallback to default
			: smoothing
		: defaultSmoothingEnabled; // if user do not provide smoothing option, we fallback to default

	const smoothingRadius = typeof smoothing === 'object' ? smoothing.radius : undefined;

	return {
		enabled: smoothingEnabled,
		radius: smoothingRadius ?? 0.75,
	};
};
