import type { RendererState } from '../../renderer';
import { MarkerComponent } from '../../components/marker';
import { Geometry2DComponents } from '../../entities/geometry2d';
import type { Entity2DHTMLDivElementContainer } from '../../types';

export class DOMVisibilitySystem {
	state: RendererState;
	dirty = true;
	domTree: Entity2DHTMLDivElementContainer;
	constructor(state: RendererState, domTree: Entity2DHTMLDivElementContainer) {
		this.state = state;
		this.domTree = domTree;
	}

	update() {
		if (this.dirty) {
			let entity;

			for (const child of Array.from(this.domTree.children)) {
				entity = this.state.geometry2DMap.get(child.userData.entityId);
				if (entity) {
					const component = entity.components[Geometry2DComponents.UI];
					if (
						!component.enabled ||
						!component.visible ||
						!this.state.geometry2DIdsInScene.has(child.userData.entityId)
					) {
						this.domTree.removeChild(child);
					}
				} else {
					this.domTree.removeChild(child);
				}
			}

			for (const entityId of this.state.geometry2DIdsInScene) {
				entity = this.state.geometry2DMap.get(entityId);
				if (entity) {
					const component = entity.components[Geometry2DComponents.UI];
					if (component instanceof MarkerComponent) {
						if (component.enabled && component.markerContainer && !this.domTree.contains(component.markerContainer)) {
							// @TODO: remove in favor of attribute
							component.markerContainer.userData = {
								entityId: entity.id,
							};
							this.domTree.appendChild(component.markerContainer);
							component.updateDimensions();
						}
					}
				}
			}

			this.dirty = false;
		}
	}
}
