import type { GeometryGroupState, GeometryState } from '@mappedin/core-sdk';
import z from 'zod';
import { parseCompleteState } from './state-utils';

export const WALLS_STATE_TYPE = 'walls';

export const wallsStateSchema = z.object({
	type: z.literal(WALLS_STATE_TYPE).default(WALLS_STATE_TYPE),
	/**
	 * Color of the walls.
	 * @default #ffffff
	 */
	color: z.string().default('#ffffff'),
	/**
	 * Color of the top of walls, if not set, defaults to the color.
	 */
	topColor: z.string().optional(),
	/**
	 * Texture to apply to the geometry element.
	 */
	texture: z.object({ url: z.string() }).or(z.undefined()),
	/**
	 * Top texture to apply to the geometry element.
	 */
	topTexture: z.object({ url: z.string() }).or(z.undefined()),
	/**
	 * Visibility of the walls.
	 * @default true
	 */
	visible: z.boolean().default(true),
	/**
	 * Height of the walls.
	 * @default 0
	 */
	height: z.number().default(0),
	/**
	 * Opacity of the walls.
	 * @default 1
	 */
	opacity: z.number().default(1),
});

export const wallsStateSchemaPartial = wallsStateSchema.partial();
const wallsStateSchemaStrict = z.strictObject(wallsStateSchema.shape);

export type TWallsState = z.infer<typeof wallsStateSchemaStrict>;
export type TWallsUpdateState = z.infer<typeof wallsStateSchemaPartial>;

export function getWallsState(geometryGroupState: GeometryGroupState): TWallsState {
	const { color, topColor, texture, topTexture, visible, height, opacity } = geometryGroupState;

	return parseCompleteState(wallsStateSchema, {
		visible,
		color,
		topColor,
		texture: texture ? { url: texture } : undefined,
		topTexture: topTexture ? { url: topTexture } : undefined,
		height,
		opacity,
	});
}

export function translateWallsStateToGeojsonCore(
	state: TWallsUpdateState,
	currentState: Partial<GeometryState>,
): Partial<GeometryState> {
	const update: Partial<GeometryState> = {};
	if (currentState == null) {
		return update;
	}
	if (state.color) {
		if (state.color === 'initial') {
			update.color = currentState.initialColor;
		} else {
			update.color = state.color;
		}
	}
	if (state.topColor) {
		if (state.topColor === 'initial') {
			update.topColor = currentState.initialTopColor;
		} else {
			update.topColor = state.topColor;
		}
	}
	if (state.visible !== undefined) {
		update.visible = state.visible;
	}
	if (state.texture?.url !== undefined) {
		update.texture = state.texture.url;
	}
	if (state.topTexture?.url !== undefined) {
		update.topTexture = state.topTexture.url;
	}
	if (state.height !== undefined) {
		update.height = state.height;
	}
	if (state.opacity !== undefined) {
		update.opacity = state.opacity;
	}

	return update;
}
