import type { PerspectiveCamera, Scene, Intersection, WebGLRenderer } from 'three';

import {
	Object3D,
	Euler,
	MOUSE,
	PlaneGeometry,
	Matrix4,
	MeshBasicMaterial,
	Mesh,
	Raycaster,
	Vector2,
	Vector3,
	Clock,
	Box3,
	MathUtils,
	InstancedMesh,
	BoxGeometry,
} from 'three';
import { Easing, Tween } from '@tweenjs/tween.js';

// TODO Replace with the local version
import { cartesianToGeographic, geographicToCartesian, getProjectionScaleFactor } from '../../utils';
import { debounce } from '../../utils/async';
import { PubSub } from '../../../../packages/common/pubsub';

import { differenceBetweenAngles, getScrollDelta, getTouchPos, TouchAnchor, InputSet } from './helpers';
import * as CAMERA_CONSTANTS from './constants';

import type {
	AnimateCameraTarget,
	CameraControlsOptions,
	CameraControlsCoords,
	CameraControlsTouch,
	CameraControlsTweenPoint,
	CameraFocusOnOptions,
	PanBounds,
} from './types';
import type { InsetPadding, RendererState } from '../../renderer';
import { DEFAULT_MIN_ZOOM_LEVEL } from './constants';
import { destroyObject3D } from '../../utils/object3d-destroy';
import { EARTH_RADIUS_M } from '@packages/internal/common/constants';
import { shortestTweenRotation } from '@packages/internal/common';

const DEBUG_CAMERA_PLANE = false;
const DEBUG_FOCUS_ON = false;

export enum CameraControlsState {
	NONE = -1,
	ROTATE = 0,
	DOLLY = 1,
	PAN = 2,
	WHEEL_ZOOM = 3,
	TOUCH_TILT = 4,
	TOUCH_DOLLY = 5,
	TOUCH_PAN = 6,
	MULTI = 7,
	PEDESTAL = 8,
	TOUCH_PEDESTAL = 9,
}

enum CameraControlsViewState {
	SINGLE_FLOOR = 'single_floor',
	MULTI_FLOOR = 'multi_floor',
}

type CameraEventName = (typeof CAMERA_CONSTANTS.EVENTS)[number];

type SpecificCameraEvents = {
	'multi-start': { zooming: boolean; rotating: boolean; tilting: boolean };
	'multi-end': { zooming: boolean; rotating: boolean; tilting: boolean };
	'multi-cancel': { zooming: boolean; rotating: boolean; tilting: boolean };
	'pedestal-change': {
		pedestal: number;
		scrolledToTop?: boolean;
		scrolledToBottom?: boolean;
		scrollPercent?: number;
	};
};

export type CameraSystemState = {
	center: [number, number];
	zoomLevel: number;
	minZoomLevel: number;
	maxZoomLevel: number;
	pitch: number;
	minPitch: number;
	maxPitch: number;
	bearing: number;
	elevation: number;
};

type CameraEvents = {
	[Event in CameraEventName]: Event extends keyof SpecificCameraEvents ? SpecificCameraEvents[Event] : undefined;
};

/**
 * The advanced, manual camera controls.
 * This class will let you do things like change the min/max zoom, tilt, and pan, attach to camera events, and move/animate the camera to specifc points.
 *
 * The camera works by setting an anchor point on the scene at ground level and pointing the camera at it.
 * The camera will always be pointed directly at the anchor point.
 */
class CameraSystem extends PubSub<CameraEvents> {
	public dirty = false;
	public zoomDirty = false;
	public rotationDirty = false;
	public panDirty = false;
	public stateDirty = false;

	#defaultMinZoomLevel = DEFAULT_MIN_ZOOM_LEVEL;

	/**
	 * Factor that controls how fast zooming in and out happens in response to mouse wheel events
	 *
	 * @property zoomSpeed {Float}
	 * @default 5.0
	 */
	public zoomSpeed = 5.0;

	/**
	 * Factor to multiple mouse movement by to get tilt/rotation.
	 *
	 * @property rotateSpeed {Float}
	 * @default 100
	 */
	public rotateSpeed = 100;

	/**
	 * Disable or re-enable user input.
	 *
	 * @property enabled {Boolean}
	 * @default true
	 */
	public enabled = true;

	/**
	 * Disable or re-enable user zoom.
	 *
	 * @property enableZoom {Boolean}
	 * @default true
	 */
	public enableZoom = true;

	/**
	 * Disable or re-enable user pan.
	 *
	 * @property enablePan {Boolean}
	 * @default true
	 */
	public enablePan = true;

	/**
	 * Disable or re-enable user pedestal.
	 *
	 * @property enablePedestal {Boolean}
	 * @default false
	 */
	public enablePedestal = false;

	/**
	 * Max amount to allow scrolling maps down
	 * (In Z-axis units, at the origin, down is positive)
	 *
	 * @property maxPedestal {Number}
	 * @default Infinity
	 */
	public maxPedestal = Infinity;

	/**
	 * Max amount to allow scrolling maps up
	 * (In Z-axis units, at the origin, up is negative)
	 *
	 * @property minPedestal {Number}
	 * @default 0
	 */
	public minPedestal = 0;

	/**
	 * Disable or re-enable user tilt/rotation.
	 *
	 * @property enableRotate {Boolean}
	 * @default true
	 */
	public enableRotate = true;

	/**
	 * This is actually the minium distance the camera can get from it's anchor on the ground. May be worth changing if your map has very tall buildings to avoid the camera clipping through them.
	 *
	 * @property minZoomAltitude {Number}
	 * @default 0
	 */
	public minZoomAltitude = 0;

	/**
	 * Maximum distance the camera can get from it's anchor on the ground. Setting this too high will result in parts of the map falling out of the camera's clipping plane and disappearing.
	 *
	 * @property maxZoomAltitude {Number}
	 * @default Infinity
	 */
	public maxZoomAltitude = Infinity;

	/**
	 * How far the camera can zoom in towards the ground.
	 *
	 * This is equivalent to the minZoomAltitude property in mercator zoom level units.
	 * @default 0
	 */
	public get maxZoomLevel() {
		return this.convertAltitudeToZoomLevel(this.minZoomAltitude);
	}

	/**
	 * Sets distance the camera can zoom in towards the ground.
	 * @default Infinity
	 */
	public setMaxZoomLevel(zoomLevel: number) {
		this.minZoomAltitude = this.convertZoomLevelToAltitude(zoomLevel);
		this.stateDirty = true;
	}

	/**
	 * The default minimum zoom level of the camera in mercator zoom levels.
	 */
	get defaultMinZoomLevel() {
		return this.#defaultMinZoomLevel;
	}

	/**
	 * How far the camera can zoom out away from the ground.
	 *
	 * This is equivalent to the maxZoomAltitude property in mercator zoom level units.
	 */
	public get minZoomLevel() {
		return this.convertAltitudeToZoomLevel(this.maxZoomAltitude);
	}

	/**
	 * Sets distance the camera can zoom away from the ground.
	 */
	public setMinZoomLevel(zoomLevel: number) {
		this.maxZoomAltitude = this.convertZoomLevelToAltitude(zoomLevel);
		this.stateDirty = true;
	}

	/**
	 * ignoreZoomLimits; use with caution for special effects
	 * @default false
	 */
	public ignoreZoomLimits = false;

	/**
	 * Multiplier for min and max zoom, for convenience.
	 *
	 * @hidden
	 * @property zoomFactor {Number}
	 * @default 1
	 */
	public zoomFactor = 1;

	/**
	 * Constrains the camera from panning to far away from the scene. It's set automatically based on the size of the map.
	 * If you want to change anything, you probably want to change the margin property, which is the factor the min and max in
	 * each dimension are multiplied by to give the true bounds. For example, on a truely huge venue a 1.25 margin could get you
	 * way out into space when zoomed in.
	 *
	 * @property panBounds {Object}
	 @property panBounds.min {Object} An x, y pair representing the bounds of one corner of the map.
	 @property panBounds.max {Object} An x, y pair representing the bounds of the other corner of the map.
	 */
	public panBounds: PanBounds = {
		min: new Vector3(Infinity, Infinity),
		max: new Vector3(-Infinity, -Infinity),
		center: new Vector3(0, 0, 0),
		radius: Infinity,
	};

	/**
	 * Minium camera tilt, in radians. If it's anything other than 0, you won't be able to look at the venue from the top down perspective.
	 *
	 * @property minTilt {Number}
	 * @default 0.0
	 */
	public minTilt = 0.0;

	/**
	 * Minium camera tilt, in radians. If you set it too high, the camera will be able to tilt down through the geometery of the scene, which will produce clipping issues.
	 *
	 * @property maxTilt {Number}
	 * @default 1.2
	 */
	public maxTilt = 1.2;

	/**
	 * If you would really prefer to pan with the right mouse button and tilt/rotate with the left, you can swap the values here to achieve that.
	 *
	 * @property MOUSE_BUTTONS {Object}
	 * @hidden
	 @property mouseButtons.ORBIT=MOUSE.RIGHT {MOUSE} The button to use for tilt/rotation. Defaults to `MOUSE.RIGHT`.
	 @property mouseButtons.ZOOM=MOUSE.MIDDLE {MOUSE} The button to use for zoom behaviour. Don't change
	 @property mouseButtons.PAN=MOUSE.LEFT {MOUSE} The button to use for panning the camera. Defaults to `MOUSE.LEFT`.
	 */
	private readonly MOUSE_BUTTONS = {
		ORBIT: MOUSE.RIGHT,
		ZOOM: MOUSE.MIDDLE,
		PAN: MOUSE.LEFT,
	};
	private readonly camera: PerspectiveCamera;
	private readonly scene: Scene;
	private readonly renderer: WebGLRenderer;
	private readonly elevation: Object3D;
	private readonly orbit: Object3D;
	private readonly cameraPlane: Mesh;
	private readonly raycaster = new Raycaster();
	private readonly rendererState: RendererState;

	private readonly options: CameraControlsOptions;

	public state: CameraControlsState = CameraControlsState.NONE;
	private intersection?: Intersection<Object3D>;
	private viewState: CameraControlsViewState | null = null;
	private tweens = new Map<
		Tween,
		{ cancelledCallback: ((...args: any[]) => void) | undefined; interruptible: boolean }
	>();
	private clock = new Clock(true);
	private coords: CameraControlsCoords = {
		mouse: new Vector2(),
		rotateStart: new Vector2(),
		rotateEnd: new Vector2(),
		rotateDelta: new Vector2(),
		panStart: new Vector3(),
		panCameraStart: new Vector3(),
		panEnd: new Vector3(),
		panDelta: new Vector3(),
		initialShiftPos: new Vector2(),
		initialPedestal: 0,
		floorAnchor: new Vector3(),
	};

	private touch: CameraControlsTouch = {
		origin: { offsetLeft: 0, offsetTop: 0 },
		touches: [],
		anchor1: null,
		anchor2: null,
		inputs: null,
	};

	private scrollTimer: null | number = null;
	private lastWheelTime = 0;
	private resetZoom = false; // Indicates whether to reset the camera position and zoom level when panning while mouse zooming
	private zoomStart: number;
	private pedestalScaleFactor = 1;

	private isUserZooming = false;
	// If true, the controls will not be limited by min and max values.
	private stayInsideBounds = true;
	private userInteracting = false;
	private cameraMoving = false;
	private lastCameraMoveTime = Date.now();

	constructor(
		camera: PerspectiveCamera,
		scene: Scene,
		renderer: WebGLRenderer,
		rendererState: RendererState,
		options: CameraControlsOptions,
	) {
		super();

		this.camera = camera;
		this.renderer = renderer;
		this.rendererState = rendererState;
		this.scene = scene;
		this.options = options;
		this.elevation = camera.parent!;
		this.orbit = this.elevation.parent!;
		this.cameraPlane = this.createCameraPlane();
		this.zoomStart = camera.position.z;

		if (options.mode !== 'outdoors-overlay') {
			this.addEventListeners();
		}
	}

	/**
	 * The amount the camera is shifted up/down
	 * (corresponds to scroll amount in multifloor mode)
	 * 100% == camera is as far up as it will go
	 * 0% == camera is as far down as it will go
	 */
	get scrollPercent(): number {
		const min = this.minPedestal;
		const max = this.maxPedestal;
		let pedestal = this.getPedestal();
		pedestal = Math.min(pedestal, max);
		pedestal = Math.max(pedestal, min);
		const offset = pedestal - min;
		const portion = offset / (max - min);

		return portion * 100;
	}

	/**
	 * Returns current field of view(FOV) in degrees
	 */
	get fov(): number {
		return this.camera.fov;
	}

	/**
	 * Returns whether the maps are scrolled to bottom
	 */
	get scrolledToBottom(): boolean {
		return this.minPedestal === 0 || Math.floor(this.getPedestal()) <= this.minPedestal;
	}

	/**
	 * Returns whether the maps are scrolled to top
	 */
	get scrolledToTop(): boolean {
		return this.maxPedestal === 0 || Math.ceil(this.getPedestal()) >= this.maxPedestal;
	}

	/**
	 * The camera's current center in [longitude, latitude] degrees.
	 */
	get center(): [number, number] {
		const v = this.getPosition();

		const { lat, lon } = cartesianToGeographic(
			this.rendererState.center?.[1] ?? 0,
			this.rendererState.center?.[0] ?? 0,
			v.x,
			v.y,
		);

		return [lon, lat];
	}

	/**
	 * @param center The center in [longitude, latitude] degrees.
	 */
	setCenter(longitude: number, latitude: number) {
		const { x, y } = geographicToCartesian(
			this.rendererState.center?.[1] ?? 0,
			this.rendererState.center?.[0] ?? 0,
			latitude,
			longitude,
		);

		this.setPosition(x, y);
	}

	/**
	 * @hidden
	 */
	public raycastToFloor = (pos: Vector2): Vector3 => {
		this.raycaster.setFromCamera(pos, this.camera);

		let intersection = this.raycaster.intersectObject(this.cameraPlane, false)[0];
		if (intersection) {
			return intersection.point;
		} else {
			return new Vector3();
		}
	};

	/**
	 * Returns true if the the user is currently manipulating the camera.
	 *
	 * @return {Boolean} True if the user is currently manipulating the camera, false otherwise.
	 */
	public isUserInteracting = (): boolean => {
		return this.userInteracting;
	};

	/**
	 * Returns the current camera position.
	 *
	 * @return {Object} An {x, y} object of the current camera postion.
	 */
	public getPosition = (): Vector3 => {
		return new Vector3(this.orbit.position.x, this.orbit.position.y, this.orbit.position.z);
	};

	/**
	 * Returns the current camera pedestal zoom level.
	 */
	public getPedestal = (): number => {
		return this.orbit.position.z;
	};

	/**
	 * Pans the camera right and down from the current position
	 *
	 * @param right {Number} The units to move right. Negative will pan left. This is in relation to the global coordinate system, not the current camera rotation.
	 * @param down {Number} The units to move down. Negative will pan up. This is in relation to the global coordinate system, not the current camera rotation.
	 */
	public pan = (right: number, down: number) => {
		if (isNaN(right) || isNaN(down)) {
			return;
		}

		this.setPosition(this.orbit.position.x + right, this.orbit.position.y + down);
		this.publish('position-updated');
	};

	/**
	 * Anything we need to do after we render the camera, like update anchors for the mouse/touch controls.
	 *
	 * @hidden
	 */
	public postRender = () => {
		if (this.state === CameraControlsState.PAN || this.state === CameraControlsState.TOUCH_PAN) {
			this.raycaster.setFromCamera(this.coords.mouse, this.camera);
			this.intersection = this.raycaster.intersectObject(this.cameraPlane, false)[0];
			if (this.intersection) {
				this.coords.panStart.set(this.intersection.point.x, this.intersection.point.y, 0);
				this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);
			}
		}

		if (this.state === CameraControlsState.WHEEL_ZOOM && this.resetZoom) {
			this.resetZoom = false;
			this.zoomStart = this.getZoomAltitude();
			this.coords.floorAnchor = this.raycastToFloor(this.coords.mouse);
			this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);
		}

		if (this.state === CameraControlsState.MULTI && this.isAnimating === false) {
			this.state = CameraControlsState.NONE;
			this.publish('multi-end');
		}
	};

	/**
	 * Returns true if the camera is currently animating.
	 * @return {Boolean} True if the camera is animating, false otherwise.
	 */
	public get isAnimating() {
		return this.rendererState.internalTweenGroup.getAll().some(value => (value as any).cameraAnimation);
	}

	/**
	 * Returns true if the camera is currently moving (it's animating, the user is manipulating it).
	 *
	 * @return {Boolean} True if the camera is moving, false otherwise.
	 */
	public isCameraMoving = (): boolean => {
		return this.cameraMoving;
	};

	/**
	 * Returns the most recent time the camera stopped moving.
	 *
	 * @returns {Number} The time in ms the camera stopped moving.
	 */
	public lastCameraStoppedMovingTime = (): number => {
		return this.lastCameraMoveTime;
	};

	/**
	 * Sets the camera anchor to a specifc x/y positon, in the global reference frame. 0,0 will be roughly the middle of the map, and panBounds holds the min/max points.
	 *
	 * @param x {Number} The x position to move the camera to. +x will take you right, from the default camera rotation of 0.
	 * @param y {Number} The y position to move the camera to. +y will take you down (towards the viewer) in the default camera rotation of 0.
	 */
	public setPosition = (x: number, y: number) => {
		if (x === undefined || y === undefined || isNaN(x) || isNaN(y)) {
			return;
		}

		if (this.state === CameraControlsState.NONE) {
			this.publish('pan-start');
		}

		this.updateCameraPosition(x, y);

		this.publish('position-updated');

		this.publish('change');
		if (this.state === CameraControlsState.NONE) {
			this.publish('pan-end');
		}
		this.dirty = true;
		this.stateDirty = true;
	};

	private updateCameraPosition = (x: number, y: number) => {
		const dx = x - this.panBounds.center.x;
		const dy = y - this.panBounds.center.y;
		const targetDistanceFromBounds = Math.sqrt(dx * dx + dy * dy);
		if (this.stayInsideBounds && targetDistanceFromBounds > this.panBounds.radius) {
			const angle = Math.atan2(dy, dx);
			const target = {
				x: Math.cos(angle) * this.panBounds.radius + this.panBounds.center.x,
				y: Math.sin(angle) * this.panBounds.radius + this.panBounds.center.y,
			};
			this.orbit.position.x = target.x;
			this.orbit.position.y = target.y;
		} else {
			this.orbit.position.x = x;
			this.orbit.position.y = y;
		}
	};

	/**
	 * Tilts the camera up or down by some number of radians. Bounded by minTilt and maxTilt.
	 *
	 * @param radians {Number} Number of radians to increase or decrease the current tilt by.
	 */
	public tilt = (radians: number) => {
		if (isNaN(radians)) {
			return;
		}

		if (this.state === CameraControlsState.NONE) {
			this.publish('rotate-start');
		}
		const newTilt = this.stayInsideBounds
			? Math.max(Math.min(radians + this.getTilt(), this.maxTilt), this.minTilt)
			: radians + this.getTilt();
		if (newTilt !== this.elevation.rotation.x) {
			this.elevation.rotation.x = newTilt;
			this.publish('tilt-updated');
		}
		this.publish('change');

		if (this.state === CameraControlsState.NONE) {
			this.publish('rotate-end');
		}
		this.stateDirty = true;
	};

	/**
	 * Zooms the camera in on the center of the current view.
	 *
	 * @param duration {Number} The duration of the zoom animation, in ms.
	 * @param curve {Function} The easing function to use for the zoom animation.
	 * @param callback {Function} A callback that will be executed when the zoom animation is done.
	 */
	public zoomIn = (duration: number, curve: (n: number) => number, callback: (...args) => void) => {
		let zoom = this.getZoomAltitude();
		this.animateCamera(
			{
				zoom: zoom / 1.5,
			},
			duration,
			curve,
			callback,
		);
	};

	/**
	 * Zooms the camera out from the center of the current view.
	 *
	 * @param duration
	 * @param curve
	 * @param callback
	 */
	public zoomOut = (duration: number, curve: (n: number) => number, callback: (...args) => void) => {
		let zoom = this.getZoomAltitude();
		this.animateCamera(
			{
				zoom: zoom * 1.5,
			},
			duration,
			curve,
			callback,
		);
	};

	/**
	 * Returns the current camera rotation.
	 *
	 * @return {Number} The current rotation of the camera, in radians.
	 */
	public getRotation = (): number => {
		// return rotation normalized to 0-360
		return (-this.orbit.rotation.z + Math.PI * 2) % (Math.PI * 2);
	};

	/**
	 * Cancel animation- for the new API
	 */
	public cancelAnimation = () => {
		const tweensArray = Array.from(this.tweens.keys());
		if (tweensArray.length < 1) {
			return;
		}
		let lastTween: Tween = tweensArray[tweensArray.length - 1];
		// Make sure to emit an event if we are cancelling a tween
		if ((lastTween as any).state === CameraControlsState.MULTI) {
			this.publish('multi-cancel', {
				zooming: true,
				rotating: true,
				tilting: true,
			});
		}
		this.completeTween(lastTween);
	};

	/**
	 * Animates the camera from it's current position to the state specified in target. You only
	 * need to specify the properties you actually want to change.
	 *
	 * You can also specify a duration, animation curve, and a callback for when it's done.
	 *
	 * @param target {Object} A list of optional parameters you can set that represent the camera state.
	 @param [target.position] {Object} An {x, y, z} object representing the position to move to.
	 @param [target.zoom] {Number} The zoom level to end at.
	 @param [target.tilt] {Number} The tilt to end at, in radians.
	 @param [target.rotation] {Number} The rotation to end at, in radians.
	 @param [target.doNotAutoStart=false] {Boolean} Set this to true if you want to start the tween yourself.
	 * @param [duration] {Number} The duration to animate the camera for, in ms.
	 * @param [curve] {Mappedin.Easing} The animation curve to use for the animation.
	 * @param [callback] {Function} A callback that will be executed when the animation is done.
	 * @param [options] {Object} An Options object
	 * @param [options.interruptible=true] {Boolean} Determines if this animation must complete before any new animations start
	 it will cancel and omit any previous animations. Set to "chain" to chain instead
	 *
	 * @return {Mappedin.Tween} The tween being used, if you want to do anything to control it manually. Do not overide it's events.
	 */
	public animateCamera = (
		target: AnimateCameraTarget,
		duration: number,
		curve?: (n: number) => number,
		callback?: (...args) => void,
		cancelledCallback?: (...args) => void,
		options?: { interruptible?: boolean },
	): Promise<void> => {
		const _options = { interruptible: true, ...options };
		let tweenStart: CameraControlsTweenPoint = {};
		let tweenEnd: CameraControlsTweenPoint = {};

		let position = this.getPosition();

		if (target.position != null) {
			if (target.position.x !== undefined) {
				tweenStart.x = position.x;
				tweenEnd.x = target.position.x;
			}
			if (target.position.y !== undefined) {
				tweenStart.y = position.y;
				tweenEnd.y = target.position.y;
			}
			if (target.position.z !== undefined) {
				tweenStart.z = position.z;
				tweenEnd.z = target.position.z;
			}
		}

		if (target.zoom !== undefined) {
			tweenStart.zoom = this.stayInsideBounds
				? Math.min(Math.max(this.getZoomAltitude(), this.getZoomScaledMin()), this.getZoomScaledMax())
				: this.getZoomAltitude();
			if (this.viewState === CameraControlsViewState.MULTI_FLOOR || !this.stayInsideBounds) {
				tweenEnd.zoom = target.zoom;
			} else {
				tweenEnd.zoom = Math.min(Math.max(target.zoom, this.getZoomScaledMin()), this.getZoomScaledMax());
			}
		}

		if (target.rotation !== undefined) {
			const { start, end } = shortestTweenRotation(this.getRotation(), target.rotation);
			tweenStart.rotation = start;
			tweenEnd.rotation = end;
		}

		if (target.tilt !== undefined) {
			tweenStart.tilt = this.getTilt();
			if (this.viewState === CameraControlsViewState.MULTI_FLOOR || !this.stayInsideBounds) {
				tweenEnd.tilt = target.tilt;
			} else {
				tweenEnd.tilt = Math.max(Math.min(target.tilt, this.maxTilt), this.minTilt);
			}
		}

		const zooming = tweenStart.zoom !== tweenEnd.zoom;
		const rotating = tweenStart.rotation !== tweenEnd.rotation;
		const tilting = tweenStart.tilt !== tweenEnd.tilt;

		let tween = new Tween(tweenStart)
			.to(tweenEnd, process.env.TESTING ? 0 : duration)
			.onUpdate(between => {
				this.setMulti(
					{
						x: between.x,
						y: between.y,
						z: between.z,
					},
					between.zoom,
					between.rotation,
					between.tilt,
				);
			})
			.onComplete(() => {
				this.completeTween(tween);
				this.publish('multi-end', {
					zooming,
					rotating,
					tilting,
				});
				if (callback) {
					callback();
				}
			})
			.onStart(() => {
				if (this.state !== CameraControlsState.MULTI) {
					this.state = CameraControlsState.MULTI;
					(tween as any).state = CameraControlsState.MULTI;
					this.publish('multi-start', {
						zooming,
						rotating,
						tilting,
					});
				}
				this.publish('change');
			});
		this.rendererState.internalTweenGroup.add(tween);

		// check for ongoing tweens
		const tweensArray = Array.from(this.tweens.entries());
		let lastTween = tweensArray[tweensArray.length - 1];

		let wait = false;

		if (lastTween != null) {
			// if the last tween can be interrupted
			if (lastTween[1].interruptible) {
				// cancel it to play this one
				if (typeof lastTween[1].cancelledCallback === 'function') {
					lastTween[1].cancelledCallback();
				}
				// and finally remove the last tween
				this.completeTween(lastTween[0]);
			} else {
				// otherwise chain this tween
				wait = true;
				lastTween[0].chain(tween);
			}
		}

		this.tweens.set(tween, { cancelledCallback, interruptible: _options.interruptible });

		if (curve) {
			tween.easing(curve);
		} else {
			tween.easing(Easing.Quadratic.In);
		}

		if (!target.doNotAutoStart && !wait) {
			tween.start();
			this.publish('change');
		}

		(tween as any).cameraAnimation = true;

		// TODO: this should resolve when the tween completes
		// @ts-expect-error
		return tween;
	};

	/**
	 * Allows you to set any of the Camera's position, zoom, rotation and tilt at once, with one function.
	 *
	 * @param [position] {Object} an {x, y, z} object representing the new position.
	 * @param [zoom] {Number} The new zoom distance.
	 * @param [rotation] {Number} The new rotation, in radians.
	 * @param [tilt] {Number} The new tilt, in radians.
	 */
	public setMulti = (
		position?: { x?: number; y?: number; z?: number },
		zoom?: number,
		rotation?: number,
		tilt?: number,
		publishChangeEvent = true,
	) => {
		if (this.state === CameraControlsState.NONE) {
			this.publish('multi-start');
		}

		let positionUpdated = false;

		if (position != null) {
			if (position.x !== undefined && !isNaN(position.x) && position.y !== undefined && !isNaN(position.y)) {
				this.updateCameraPosition(position.x, position.y);
				positionUpdated = true;
			}

			if (position.z !== undefined && !isNaN(position.z)) {
				this.setPedestal(position.z, true, false); // Don't emit camera-state-change here, setMulti will emit it
				positionUpdated = true;
			}
		}
		if (positionUpdated) {
			this.publish('position-updated');
		}

		const payload = { zooming: false, tilting: Boolean(zoom), rotating: false };

		if (zoom !== undefined && !isNaN(zoom)) {
			const targetZoom = this.stayInsideBounds
				? Math.min(Math.max(zoom, this.getZoomScaledMin()), this.getZoomScaledMax())
				: zoom;

			if (targetZoom !== this.camera.position.z) {
				payload.zooming = true;
				this.camera.position.z = targetZoom;
				this.publish('zoom-updated');
				this.zoomDirty = true;

				// adjust camera near / far to prevent Z-fighting when extremely zoomed out
				// this is only likely to happen in multifloor views, when FOV is reduced

				if (this.viewState === CameraControlsViewState.MULTI_FLOOR) {
					if (zoom > CAMERA_CONSTANTS.CLIPPING_RADIUS) {
						this.camera.near = zoom - CAMERA_CONSTANTS.CLIPPING_RADIUS;
						this.camera.far = zoom + CAMERA_CONSTANTS.CLIPPING_RADIUS;
					}
				} else {
					this.camera.near = zoom / CAMERA_CONSTANTS.ZRANGE;
					this.camera.far = zoom * CAMERA_CONSTANTS.ZRANGE;
				}
				this.camera.updateProjectionMatrix();
			}
		}

		if (rotation !== undefined && !isNaN(rotation)) {
			if (rotation !== this.getRotation()) {
				payload.rotating = true;
				this.orbit.rotation.z = -rotation;
				this.publish('rotation-updated');
			}
		}

		if (tilt !== undefined && !isNaN(tilt)) {
			const newTilt = this.stayInsideBounds ? Math.max(Math.min(tilt, this.maxTilt), this.minTilt) : tilt;
			if (newTilt !== this.elevation.rotation.x) {
				payload.tilting = true;
				this.elevation.rotation.x = newTilt;
				this.publish('tilt-updated');
			}
		}

		if (publishChangeEvent) {
			this.publish('change');
		}

		if (this.state === CameraControlsState.NONE) {
			this.publish('multi-end', payload);
		}

		this.stateDirty = true;
	};

	/**
	 * Returns the actual zoom minimum based on real-world distance.
	 *
	 * @return {Number} The actual zoom minimum in map units.
	 */
	public getZoomScaledMin = (): number => {
		return this.minZoomAltitude * this.zoomFactor;
	};

	/**
	 * Returns the actual zoom maximum based on visible map size.
	 *
	 * @return {Number} The actual zoom maximum in map units.
	 */
	public getZoomScaledMax = (): number => {
		let min = this.minZoomAltitude * this.zoomFactor;

		return Math.max(this.maxZoomAltitude, min);
	};

	/**
	 * Sets the rotation to a specific orientation, clockwise in radians. Mostly useful to orient the map a certain way for a physical directory.
	 *
	 * @param radians {Number} Absolute rotation to set the camera to, in radians. 0 in the starting point.
	 * @param silent {Boolean} Whether to avoid emitting events
	 */
	public setRotation = (radians: number, silent?: boolean) => {
		if (isNaN(radians)) {
			return;
		}

		if (this.state === CameraControlsState.NONE && !silent) {
			this.publish('rotate-start');
		}

		// Negate the value to rotate clockwise instead of threejs counterclockwise rotation
		const newRotation = -(radians % CAMERA_CONSTANTS.TWO_PI);
		if (newRotation !== this.orbit.rotation.z) {
			this.orbit.rotation.z = newRotation;
			if (!silent) {
				this.publish('rotation-updated');
			}
		}
		if (!silent) {
			this.publish('change');
		}

		if (this.state === CameraControlsState.NONE && !silent) {
			this.publish('rotate-end');
		}

		this.dirty = true;
		this.rotationDirty = true;
		if (!silent) {
			this.stateDirty = true;
		}
	};

	/**
	 * Sets the camera to be a certain distance from the anchor point, along it's tilt and rotation.
	 * Keeps it inside minZoom and maxZoom.
	 *
	 * @param zoom {Number} The distance to set the camera to.
	 */
	public setZoomAltitude = (zoom: number) => {
		if (isNaN(zoom)) {
			return;
		}

		if (this.state === CameraControlsState.NONE) {
			this.publish('zoom-start');
		}

		let z = this.stayInsideBounds ? Math.min(Math.max(zoom, this.getZoomScaledMin()), this.getZoomScaledMax()) : zoom;

		if (z !== this.camera.position.z) {
			this.camera.position.z = z;
			this.publish('zoom-updated');
			this.zoomDirty = true;
		}

		this.camera.near = z / CAMERA_CONSTANTS.ZRANGE;
		this.camera.far = z * CAMERA_CONSTANTS.ZRANGE;
		this.camera.updateProjectionMatrix();

		this.publish('change');

		if (this.state === CameraControlsState.NONE) {
			this.publish('zoom-end');
		}

		this.dirty = true;
		this.stateDirty = true;
	};

	/**
	 * Set the camera's zoom level in mercator zoom level units.
	 */
	public setZoomLevel = (zoomLevel: number) => {
		this.setZoomAltitude(this.convertZoomLevelToAltitude(zoomLevel));
	};

	/**
	 * Returns the current camera zoom
	 *
	 * @return {Number} The distance of the camera from the anchor.
	 */
	public getZoomAltitude = (): number => {
		return this.camera.position.z;
	};

	/**
	 * Current camera zoom in mercator zoom level units.
	 */
	public getZoomLevel = (): number => {
		return this.convertAltitudeToZoomLevel(this.getZoomAltitude());
	};

	/**
	 * Sets the tilt to a specific level, in radians. 0 is top down. Bounded by minTilt and maxTilt.
	 *
	 * @param radians {Number} Tilt to set the camera to, in radians.
	 * @param silent {Boolean} Whether to avoid emitting events.
	 */
	public setTilt = (radians: number, silent?: boolean) => {
		if (isNaN(radians)) {
			return;
		}

		if (this.state === CameraControlsState.NONE && !silent) {
			this.publish('rotate-start');
		}
		const newTilt = this.stayInsideBounds ? Math.max(Math.min(radians, this.maxTilt), this.minTilt) : radians;
		if (newTilt !== this.elevation.rotation.x) {
			this.elevation.rotation.x = newTilt;
			if (!silent) {
				this.publish('tilt-updated');
			}
		}
		if (!silent) {
			this.publish('change');
		}

		if (this.state === CameraControlsState.NONE && !silent) {
			this.publish('rotate-end');
		}

		this.dirty = true;
		if (!silent) {
			this.stateDirty = true;
		}
	};

	/**
	 * Returns the current camera tilt
	 *
	 * @return {Number} The current tilt of the camera, in radians.
	 */
	public getTilt = (): number => {
		return this.elevation.rotation.x;
	};

	/**
	 * @param elevation {number}
	 * @hidden
	 */
	public setCameraPlaneElevation = (elevation: number) => {
		this.cameraPlane.position.z = elevation;
	};

	/**
	 * Moves the camera towards or away from the camera by a set amount. Positive will zoom in (bringing the distance closer to 0).
	 *
	 * @param zoom {Number} The distance to increase or decrease the zoom.
	 */
	public zoom = (zoomDelta: number) => {
		if (isNaN(zoomDelta)) {
			return;
		}

		if (this.state === CameraControlsState.NONE) {
			this.publish('zoom-start');
		}

		let newZoom = this.getZoomAltitude() - zoomDelta;
		this.camera.position.z = this.stayInsideBounds
			? Math.min(Math.max(newZoom, this.minZoomAltitude * this.zoomFactor), this.maxZoomAltitude * this.zoomFactor)
			: newZoom;
		this.publish('zoom-updated');
		this.zoomDirty = true;
		this.publish('change');

		if (this.state === CameraControlsState.NONE) {
			this.publish('zoom-end');
		}
		this.stateDirty = true;
	};

	/**
	 * Returns the current projection scale factor.
	 * @param FOV {number} Field of view
	 * @param canvasHeight {number} Height of the canvas
	 * @param zoom{number} Zoom level
	 */
	public getProjectionScaleFactor = (FOV: number, canvasHeight: number, zoom: number): number => {
		return getProjectionScaleFactor(FOV, canvasHeight, zoom);
	};

	/**
	 * Should probably be "pre-render". Anything we need to do before rendering the scene.
	 *
	 * @hidden
	 */
	public update = () => {
		this.orbit.updateMatrixWorld();
		if (
			this.lastWheelTime > 0 &&
			this.state === CameraControlsState.WHEEL_ZOOM &&
			this.clock.getElapsedTime() - this.lastWheelTime > 0.2
		) {
			this.lastWheelTime = 0;
			this.state = CameraControlsState.NONE;
			this.publish('zoom-end');

			this.removeMouseMoveEventListeners();
		}
	};

	public setPedestal = (z: number, emitEvent = false, emitCameraStateChanged = true) => {
		if (typeof z === 'number' && !isNaN(z)) {
			this.orbit.position.z = z;
			if (emitEvent) {
				this.publishPedestalChangeEvent(z);
				this.publish('pedestal-change', { pedestal: z });
			}
			this.publish('change');

			this.dirty = true;
			if (emitCameraStateChanged) {
				this.stateDirty = true;
			}
		}
	};

	/**
	 * Disposes of the camera and all of it's events.
	 *
	 * @hidden
	 */
	public dispose = () => {
		for (let eventName of CAMERA_CONSTANTS.EVENTS) {
			if (eventName.endsWith('start')) {
				this.off(eventName, this.setCameraMoving);
				if (eventName.startsWith('user')) {
					this.off(eventName, this.setUserInteracting);
				}
			} else if (eventName.endsWith('end') || eventName.endsWith('cancel')) {
				this.off(eventName, this.unsetCameraMoving);
				if (eventName.startsWith('user')) {
					this.off(eventName, this.unsetUserInteracting);
				}
			}
		}
		this.removeEventListeners();
		this.scene.remove(this.cameraPlane);
		this.destroy();
	};

	private createCameraPlane = () => {
		const size = DEBUG_CAMERA_PLANE ? 2000 : 10000000000;
		const segments = DEBUG_CAMERA_PLANE ? 100 : 1;
		let planeGeometry = new PlaneGeometry(size, size, segments, segments); // Should set this to be the map bounds later
		let cameraPlaneMaterial = new MeshBasicMaterial({
			color: 0x000000,
			wireframe: DEBUG_CAMERA_PLANE,
			visible: DEBUG_CAMERA_PLANE,
		});
		const cameraPlane = new Mesh(planeGeometry, cameraPlaneMaterial);

		this.scene.add(cameraPlane);

		return cameraPlane;
	};

	private addEventListeners = () => {
		const { domElement } = this.renderer;
		for (let eventName of CAMERA_CONSTANTS.EVENTS) {
			if (eventName.endsWith('start')) {
				this.on(eventName, this.setCameraMoving);
				if (eventName.startsWith('user')) {
					this.on(eventName, this.setUserInteracting);
				}
			} else if (eventName.endsWith('end') || eventName.endsWith('cancel')) {
				this.on(eventName, this.unsetCameraMoving);
				if (eventName.startsWith('user')) {
					this.on(eventName, this.unsetUserInteracting);
				}
			}
		}

		domElement.addEventListener('contextmenu', this.onContextMenu, false);
		domElement.addEventListener('mousedown', this.onMouseDown, false);
		domElement.addEventListener('wheel', this.onMouseWheel, { passive: false });

		domElement.addEventListener('touchstart', this.onTouchStart, false);
		domElement.addEventListener('touchend', this.onTouchEnd, false);
		domElement.addEventListener('touchmove', this.onTouchMove, false);
	};

	private addMouseEventListeners = () => {
		this.addMouseMoveEventListeners();
		window.addEventListener('mouseup', this.onMouseUp, false);
		window.addEventListener('mouseout', this.onMouseUp, false);
	};

	private addMouseMoveEventListeners = () => {
		this.renderer.domElement.addEventListener('mousemove', this.onMouseMove, false);
	};

	private removeMouseMoveEventListeners = () => {
		this.renderer.domElement.removeEventListener('mousemove', this.onMouseMove, false);
	};

	private removeEventListeners = () => {
		const { domElement } = this.renderer;

		domElement.removeEventListener('contextmenu', this.onContextMenu, false);
		domElement.removeEventListener('mousedown', this.onMouseDown, false);
		domElement.removeEventListener('wheel', this.onMouseWheel, false);

		domElement.removeEventListener('touchstart', this.onTouchStart, false);
		domElement.removeEventListener('touchend', this.onTouchEnd, false);
		domElement.removeEventListener('touchmove', this.onTouchMove, false);

		domElement.removeEventListener('mousemove', this.onMouseMove, false);
		window.removeEventListener('mouseup', this.onMouseUp, false);
		window.removeEventListener('mouseout', this.onMouseUp, false);
	};

	private removeMouseUpEventListeners = () => {
		this.renderer.domElement.removeEventListener('mousemove', this.onMouseMove, false);
		window.removeEventListener('mouseup', this.onMouseUp, false);
		window.removeEventListener('mouseout', this.onMouseUp, false);
	};

	private onContextMenu = (event: MouseEvent) => {
		event.preventDefault();
	};

	private onMouseUp = () => {
		if (this.enabled === false) return;

		this.removeMouseUpEventListeners();

		switch (this.state) {
			case CameraControlsState.PAN:
				this.publish('pan-end');
				this.publish('user-pan-end');
				break;
			case CameraControlsState.DOLLY:
				this.publish('zoom-end');
				this.publish('user-dolly-end');
				break;
			case CameraControlsState.ROTATE:
				this.publish('rotate-end');
				this.publish('user-rotate-end');
				break;
			case CameraControlsState.PEDESTAL:
				this.publish('pedestal-end');
				this.publish('user-pedestal-end');
				break;
		}

		this.state = CameraControlsState.NONE;
		this.panDirty = false;
	};

	private onMouseMove = (event: MouseEvent) => {
		if (this.enabled === false) return;
		event.preventDefault();
		this.mouseToScene(event);

		if (this.state === CameraControlsState.ROTATE) {
			if (this.enableRotate === false) return;

			this.handleMouseMoveRotate(event);
		} else if (this.state === CameraControlsState.PAN) {
			if (this.enablePan === false) return;

			this.handleMouseMovePan();
		} else if (this.state === CameraControlsState.WHEEL_ZOOM) {
			this.resetZoom = true;
			this.state = CameraControlsState.NONE;
		} else if (this.state === CameraControlsState.PEDESTAL) {
			this.handleMouseMovePedestal(event);
		}
	};

	private onTouchStart = (event: TouchEvent) => {
		if (this.enabled === false) return;

		this.updateTouchOrigin();
		this.touchToScene(event);
		switch (event.touches.length) {
			case 1: // one-fingered touch: pan/pedestal
				// note - they are mutually exclusive as they use the same event
				// pedestal takes precedence
				if (this.enablePedestal) {
					this.handleTouchStartPedestal(event);
					this.state = CameraControlsState.TOUCH_PEDESTAL;
				} else if (this.enablePan) {
					this.handleTouchStartPan(event);
					this.state = CameraControlsState.TOUCH_PAN;
				}

				break;

			case 2: // two-fingered touch: dolly/tilt
				this.touch.inputs = new InputSet(this.touch.touches[0], this.touch.touches[1]);
				break;

			default:
				this.state = CameraControlsState.NONE;
		}

		if (this.state !== CameraControlsState.NONE) {
			// Probably don't need to do anything
		}
	};

	private onTouchMove = (event: TouchEvent) => {
		if (this.enabled === false) return;

		this.touchToScene(event);
		switch (event.touches.length) {
			case 2: // two-fingered touch: dolly/tilt
				// Two finger touch should prevent default behavior
				event.preventDefault();
				event.stopPropagation();
				const prevFirst = { ...this.touch.inputs!.first };
				const prevSecond = { ...this.touch.inputs!.second };
				const first = new Vector2(this.touch.touches[0].x, this.touch.touches[0].y);
				const second = new Vector2(this.touch.touches[1].x, this.touch.touches[1].y);
				this.touch.inputs!.update(this.touch.touches[0], this.touch.touches[1]);

				if (this.enableRotate && this.state === CameraControlsState.TOUCH_TILT) {
					this.handleTouchMoveTilt(event);
				} else if (this.state === CameraControlsState.TOUCH_DOLLY) {
					this.handleTouchMoveDolly();
				} else if (
					(this.state as CameraControlsState) !== CameraControlsState.TOUCH_DOLLY &&
					this.touch.inputs!.totalDistance > 0.05
				) {
					const movingUp = prevFirst.y < first.y && prevSecond.y < second.y;
					const movingDown = prevFirst.y > first.y && prevSecond.y > second.y;

					if (this.enableRotate && this.state !== CameraControlsState.TOUCH_TILT && (movingDown || movingUp)) {
						this.handleTouchStartTilt(event);
						this.state = CameraControlsState.TOUCH_TILT;
					} else {
						this.handleTouchStartDolly();
						this.state = CameraControlsState.TOUCH_DOLLY;
					}
				}

				break;

			case 1: // one-fingered touch: pan/pedestal
				// One finger touch should only prevent default behavior if pan is enabled
				if (this.enablePedestal || this.enablePan) {
					event.preventDefault();
					event.stopPropagation();
				}
				if (this.enablePedestal && this.state === CameraControlsState.TOUCH_PEDESTAL) {
					this.handleTouchMovePedestal(event);
				} else if (this.enablePan && this.state === CameraControlsState.TOUCH_PAN) {
					this.handleTouchMovePan(event);
				}
				break;

			default:
				this.state = CameraControlsState.NONE;
		}
	};

	private onTouchEnd = (event: TouchEvent) => {
		if (this.enabled === false) return;

		this.handleTouchEnd();

		switch (this.state) {
			case CameraControlsState.PAN:
			case CameraControlsState.TOUCH_PAN:
				this.publish('user-pan-end');
				break;
			case CameraControlsState.TOUCH_TILT:
				this.publish('pan-end');
				this.publish('user-tilt-end');
				break;
			case CameraControlsState.DOLLY:
			case CameraControlsState.TOUCH_DOLLY:
				this.publish('zoom-end');
				this.publish('user-dolly-end');
				break;
			case CameraControlsState.ROTATE:
				this.publish('rotate-end');
				this.publish('user-rotate-end');
				break;
			case CameraControlsState.PEDESTAL:
			case CameraControlsState.TOUCH_PEDESTAL:
				this.publish('pedestal-end');
				this.publish('user-pedestal-end');
				break;
		}

		if (event.touches.length) {
			// user is still interacting, determine the new touch state
			this.onTouchStart(event);
		} else {
			this.state = CameraControlsState.NONE;
		}
	};

	private handleTouchMovePan = (event: TouchEvent | MouseEvent) => {
		const vectorToCast = Object.hasOwn(event, 'x')
			? new Vector2((event as MouseEvent).x, (event as MouseEvent).y)
			: this.touch.touches[0];
		// Not sure why we do need event.x here but let's keep it for now
		let pos = this.raycastToFloor(vectorToCast);
		this.coords.panEnd.set(pos.x, pos.y, 0);

		this.coords.panDelta.subVectors(this.coords.panEnd, this.coords.panStart);

		this.setPosition(
			this.coords.panCameraStart.x - this.coords.panDelta.x,
			this.coords.panCameraStart.y - this.coords.panDelta.y,
		);

		this.publish('change');
		this.panDirty = true;
	};

	private handleTouchStartDolly = () => {
		let dx = this.touch.touches[0].x - this.touch.touches[1].x;
		let dy = this.touch.touches[0].y - this.touch.touches[1].y;
		this.zoomStart = this.getZoomAltitude();
		let pos = new Vector2(this.touch.touches[0].x - dx / 2, this.touch.touches[0].y - dy / 2);
		this.coords.floorAnchor = this.raycastToFloor(pos);

		this.orbit.updateMatrixWorld();

		this.touch.anchor1 = new TouchAnchor(this.touch.touches[0], this.camera, this.cameraPlane);

		this.touch.anchor2 = new TouchAnchor(this.touch.touches[1], this.camera, this.cameraPlane);

		this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);

		this.publish('user-dolly-start');
		this.publish('zoom-start');
		this.publish('multi-start');
	};

	private handleMouseMovePan = () => {
		let pos = this.raycastToFloor(this.coords.mouse);
		this.coords.panEnd.set(pos.x, pos.y, 0);

		this.coords.panDelta.subVectors(this.coords.panEnd, this.coords.panStart);
		this.setPosition(
			this.coords.panCameraStart.x - this.coords.panDelta.x,
			this.coords.panCameraStart.y - this.coords.panDelta.y,
		);

		this.publish('change');
		this.panDirty = true;
	};

	private handleTouchStartTilt = (event: TouchEvent) => {
		this.coords.rotateStart.set(event.touches[1].clientX, event.touches[1].clientY);
		this.publish('user-tilt-start');
		this.publish('rotate-start');
	};

	private handleTouchMovePedestal = (event: TouchEvent) => {
		this.handleMouseMovePedestal(event.touches[0]);
	};

	private handleTouchMoveTilt = (event: TouchEvent) => {
		this.coords.rotateEnd.set(event.touches[1].clientX, event.touches[1].clientY);
		this.coords.rotateDelta.subVectors(this.coords.rotateEnd, this.coords.rotateStart);

		this.setTilt(this.getTilt() - this.coords.rotateDelta.y / this.rotateSpeed);

		this.coords.rotateStart.copy(this.coords.rotateEnd);
	};

	private handleTouchMoveDolly = () => {
		const first = this.touch.inputs!.first;
		const second = this.touch.inputs!.second;

		this.touch.anchor1!.viewCoordinate = { x: first.x, y: first.y };
		this.touch.anchor2!.viewCoordinate = { x: second.x, y: second.y };

		let newMatrix = this.makeTransformFromTouchAnchors(this.touch.anchor1!, this.touch.anchor2!);
		if (newMatrix) {
			this.setCameraFromTransformMatrix(newMatrix);
		}

		this.orbit.updateMatrixWorld();
		this.touch.anchor1!.updateAnchorState(this.camera, first);
		this.touch.anchor2!.updateAnchorState(this.camera, second);
	};

	private handleTouchEnd = () => {
		if (this.state === CameraControlsState.TOUCH_TILT || this.state === CameraControlsState.TOUCH_DOLLY) {
			this.publish('multi-end');
		} else if (this.state === CameraControlsState.TOUCH_PAN) {
			this.publish('pan-end');
		}
		this.panDirty = false;
	};

	private handleTouchStartPan = (event: TouchEvent | MouseEvent) => {
		const vectorToCast = Object.hasOwn(event, 'x')
			? new Vector2((event as MouseEvent).x, (event as MouseEvent).y)
			: this.touch.touches[0];
		// Not sure why we do need event.x here but let's keep it for now
		let pos = this.raycastToFloor(vectorToCast);

		this.coords.panStart.set(pos.x, pos.y, 0);
		this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);

		this.publish('user-pan-start');
		this.publish('pan-start');
	};

	private handleTouchStartPedestal = (event: TouchEvent) => {
		this.handleMouseDownPedestal(event.touches[0]);
	};

	private onMouseDown = (event: MouseEvent) => {
		if (this.enabled === false) return;
		this.mouseToScene(event);
		event.preventDefault();

		if (this.state === CameraControlsState.WHEEL_ZOOM) {
			this.publish('zoom-end');
		}

		if (event.button === this.MOUSE_BUTTONS.ORBIT) {
			if (this.enableRotate === false) return;

			this.handleMouseDownRotate(event);

			this.state = CameraControlsState.ROTATE;
		} else if (event.button === this.MOUSE_BUTTONS.PAN) {
			if (this.enablePan) {
				this.handleMouseDownPan();
				this.state = CameraControlsState.PAN;
			} else if (this.enablePedestal) {
				this.handleMouseDownPedestal(event);
				this.state = CameraControlsState.PEDESTAL;
			}
		}

		if (this.state !== CameraControlsState.NONE) {
			this.addMouseEventListeners();
		}
	};

	private onMouseWheel = (event: WheelEvent) => {
		if (
			this.enabled === false ||
			this.enableZoom === false ||
			(this.state !== CameraControlsState.NONE && this.state !== CameraControlsState.WHEEL_ZOOM)
		)
			return;

		event.preventDefault();
		event.stopPropagation();

		this.mouseToScene(event);

		if (this.canScrollZoom(event)) {
			this.lastWheelTime = this.clock.getElapsedTime();
			this.publishUserZoomEvent();
		}
		if (this.state !== CameraControlsState.WHEEL_ZOOM && this.canScrollZoom(event)) {
			this.addMouseMoveEventListeners();
			this.state = CameraControlsState.WHEEL_ZOOM;
			this.zoomStart = this.getZoomAltitude();
			this.coords.floorAnchor = this.raycastToFloor(this.coords.mouse);
			this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);

			this.publish('zoom-start');
		}
		if (this.scrollTimer != null) {
			clearTimeout(this.scrollTimer);
		}

		this.scrollTimer = setTimeout(() => {
			this.publish('zoom-end');
		}, 50) as unknown as number;

		this.handleMouseWheel(event);
	};

	private handleMouseWheel = (event: WheelEvent) => {
		this.publish('zoom-start');

		if (!this.canScrollZoom(event)) {
			if (this.lastWheelTime > 0) {
				this.publish('zoom-end');
			}
			this.lastWheelTime = 0;
			this.state = CameraControlsState.NONE;

			return;
		}

		let currentZoom = this.getZoomAltitude();

		let zoom =
			currentZoom - ((getScrollDelta(event) * currentZoom) / CAMERA_CONSTANTS.WHEEL_ZOOM_MULTIPLIER) * this.zoomSpeed;
		this.setZoomAltitude(zoom);

		if (this.enablePan) {
			let zoomPercent = zoom / this.zoomStart;

			this.coords.panDelta
				.subVectors(this.coords.floorAnchor, this.coords.panCameraStart)
				.multiplyScalar(1 - zoomPercent);

			this.setPosition(
				this.coords.panCameraStart.x + this.coords.panDelta.x,
				this.coords.panCameraStart.y + this.coords.panDelta.y,
			);
		}

		this.publish('change');
	};

	/*
	Set the rotation, position and zoom of the camera system from a matrix
	representing the desired world transform of the camera's point.
	*/
	private setCameraFromTransformMatrix = transform => {
		this.orbit.updateMatrixWorld();

		let newAngle = new Euler();
		newAngle.setFromRotationMatrix(transform, 'ZYX');

		let zoom = (transform.elements[14] - this.cameraPlane.position.z) / Math.cos(newAngle.x);
		let rotation = newAngle.z;

		let vectorToGround = new Vector3(0, 0, 1);
		vectorToGround.applyEuler(newAngle);
		vectorToGround.setLength(zoom);

		let groundPosition = new Vector3();
		groundPosition.setFromMatrixPosition(transform);
		groundPosition.sub(vectorToGround);

		if (this.enablePan) {
			this.setPosition(groundPosition.x, groundPosition.y);
		}
		if (this.enableZoom) {
			this.setZoomAltitude(zoom);
		}
		if (this.enableRotate) {
			// Since setRotation expects clockwise from North we need to invert the rotation from our matrix
			this.setRotation(-rotation);
		}
	};

	/*
	From two touch anchors, determine a world-transform matrix for the camera
	that will keep those anchors attached to the same world position.

	Ported from iOS camera controls' move function.
	TODO: Document this better; see CE-750.
	*/
	private makeTransformFromTouchAnchors = (touchAnchor1: TouchAnchor, touchAnchor2: TouchAnchor) => {
		this.orbit.updateMatrixWorld();
		this.camera.updateProjectionMatrix();

		let p = this.camera.projectionMatrix.clone();

		let w1 = touchAnchor1.worldCoordinate;
		let w2 = touchAnchor2.worldCoordinate;

		let nw1 = touchAnchor1.reUnproject();
		let nw2 = touchAnchor2.reUnproject();

		if (!nw1 || !nw2 || !w1 || !w2) {
			return null;
		}

		let s1 = touchAnchor1.viewCoordinate;
		let s2 = touchAnchor2.viewCoordinate;

		let xd1 = w2.x - w1.x;
		let yd1 = w2.y - w1.y;
		let angle1 = Math.atan2(yd1, xd1);

		let xd2 = nw2.x - nw1.x;
		let yd2 = nw2.y - nw1.y;
		let angle2 = Math.atan2(yd2, xd2);

		let zAngleDelta = differenceBetweenAngles(angle1, angle2);

		let yawAdjustment = new Matrix4();
		yawAdjustment.makeRotationZ(-zAngleDelta);
		let t = yawAdjustment.clone();
		t.multiply(touchAnchor1.snapHolderMatrix);

		// Matrix subscript helper
		let m = (o, a, b) => o.elements[(a - 1) * 4 + b - 1];

		let x1 = m(p, 1, 1) * m(t, 1, 1) - s1.x * m(p, 3, 4) * m(t, 3, 1);
		let y1 = m(p, 1, 1) * m(t, 1, 2) - s1.x * m(p, 3, 4) * m(t, 3, 2);
		let z1 = m(p, 1, 1) * m(t, 1, 3) - s1.x * m(p, 3, 4) * m(t, 3, 3);
		let c1 = w1.x * x1 + w1.y * y1 + w1.z * z1;

		let x2 = m(p, 2, 2) * m(t, 2, 1) - s1.y * m(p, 3, 4) * m(t, 3, 1);
		let y2 = m(p, 2, 2) * m(t, 2, 2) - s1.y * m(p, 3, 4) * m(t, 3, 2);
		let z2 = m(p, 2, 2) * m(t, 2, 3) - s1.y * m(p, 3, 4) * m(t, 3, 3);
		let c2 = w1.x * x2 + w1.y * y2 + w1.z * z2;

		let x3 = m(p, 1, 1) * m(t, 1, 1) - s2.x * m(p, 3, 4) * m(t, 3, 1);
		let y3 = m(p, 1, 1) * m(t, 1, 2) - s2.x * m(p, 3, 4) * m(t, 3, 2);
		let z3 = m(p, 1, 1) * m(t, 1, 3) - s2.x * m(p, 3, 4) * m(t, 3, 3);
		let c3 = w2.x * x3 + w2.y * y3 + w2.z * z3;

		let xb = m(p, 2, 2) * m(t, 2, 1) - s2.y * m(p, 3, 4) * m(t, 3, 1);
		let yb = m(p, 2, 2) * m(t, 2, 2) - s2.y * m(p, 3, 4) * m(t, 3, 2);
		let zb = m(p, 2, 2) * m(t, 2, 3) - s2.y * m(p, 3, 4) * m(t, 3, 3);
		let cb = w2.x * xb + w2.y * yb + w2.z * zb;

		let dx1 = Math.abs(x3 - x1);
		let dy1 = Math.abs(y3 - y1);
		let dz1 = Math.abs(z3 - z1);
		let dx2 = Math.abs(x3 - x2);
		let dy2 = Math.abs(y3 - y2);
		let dz2 = Math.abs(z3 - z2);

		let dx3 = Math.abs(xb - x1);
		let dy3 = Math.abs(yb - y1);
		let dz3 = Math.abs(zb - z1);
		let dx4 = Math.abs(xb - x2);
		let dy4 = Math.abs(yb - y2);
		let dz4 = Math.abs(zb - z2);

		let min1 = Math.min(dx1 + dy1 + dz1, dx2 + dy2 + dz2);
		let min2 = Math.min(dx3 + dy3 + dz3, dx4 + dy4 + dz4);

		if (min1 < min2) {
			x3 = xb;
			y3 = yb;
			z3 = zb;
			c3 = cb;
		}

		let x4 = x2 * z1 - x1 * z2;
		let y4 = y2 * z1 - y1 * z2;
		let c4 = c2 * z1 - c1 * z2;

		let x5 = x3 * z2 - x2 * z3;
		let y5 = y3 * z2 - y2 * z3;
		let c5 = c3 * z2 - c2 * z3;

		let x = (c5 * y4 - c4 * y5) / (x5 * y4 - x4 * y5);
		let y = (c5 * x4 - c4 * x5) / (y5 * x4 - y4 * x5);

		let y6 = y2 * x1 - y1 * x2;
		let z6 = z2 * x1 - z1 * x2;
		let c6 = c2 * x1 - c1 * x2;

		let y7 = y3 * x2 - y2 * x3;
		let z7 = z3 * x2 - z2 * x3;
		let c7 = c3 * x2 - c2 * x3;

		let z = (c7 * y6 - c6 * y7) / (z7 * y6 - z6 * y7);

		let finalTransformMatrix = t.clone();
		finalTransformMatrix.elements[12] = x;
		finalTransformMatrix.elements[13] = y;
		finalTransformMatrix.elements[14] = z;
		if (z > 0) {
			return finalTransformMatrix;
		} else {
			return null;
		}
	};

	private updateTouchOrigin = () => {
		let touchOriginElement = this.renderer.domElement;
		this.touch.origin.offsetLeft = touchOriginElement.getBoundingClientRect().left;
		this.touch.origin.offsetTop = touchOriginElement.getBoundingClientRect().top;
	};

	private touchToScene = (event: TouchEvent) => {
		this.touch.touches = [];
		for (let i = 0, iLen = event.touches.length; i < iLen; i++) {
			let touch = event.touches[i];
			let pos = getTouchPos(touch, this.touch.origin);
			this.touch.touches.push(
				new Vector2(
					(pos.x / this.renderer.domElement.width) * this.rendererState.pixelRatio * 2 - 1,
					-((pos.y / this.renderer.domElement.height) * this.rendererState.pixelRatio) * 2 + 1,
				),
			);
		}
		this.coords.mouse = this.touch.touches[0];
	};

	private canScrollZoom = (event: MouseEvent | WheelEvent) => {
		let delta = getScrollDelta(event);

		let currentZoom = this.getZoomAltitude();

		return !(
			(delta < 0 && currentZoom === this.getZoomScaledMax()) ||
			(delta > 0 && currentZoom === this.getZoomScaledMin())
		);
	};

	private mouseToScene = (event: MouseEvent | WheelEvent) => {
		let pos = this.getMousePos(event);
		this.coords.mouse.x = (pos.x / this.renderer.domElement.width) * this.rendererState.pixelRatio * 2 - 1;
		this.coords.mouse.y = -((pos.y / this.renderer.domElement.height) * this.rendererState.pixelRatio) * 2 + 1;

		return this.coords.mouse;
	};

	private getMousePos = (event: MouseEvent | WheelEvent) => {
		return {
			x: event.offsetX,
			y: event.offsetY,
		};
	};

	private handleMouseDownRotate = (event: MouseEvent) => {
		this.coords.rotateStart.set(event.clientX, event.clientY);
		this.publish('rotate-start');
		this.publish('user-rotate-start');
	};

	private handleMouseDownPan = () => {
		let pos = this.raycastToFloor(this.coords.mouse);

		this.coords.panStart.set(pos.x, pos.y, 0);
		this.coords.panCameraStart.set(this.orbit.position.x, this.orbit.position.y, 0);
		this.publish('pan-start');
		this.publish('user-pan-start');
	};

	private handleMouseDownPedestal = (event: Touch | MouseEvent) => {
		const pos = new Vector2(event.clientX, event.clientY);
		this.coords.initialShiftPos = pos;
		this.coords.initialPedestal = this.getPedestal();
		this.pedestalScaleFactor = getProjectionScaleFactor(
			this.camera.fov,
			this.renderer.domElement.clientHeight,
			this.getZoomAltitude(),
		);
		this.publish('pedestal-start');
		this.publish('user-pedestal-start');
	};

	private handleMouseMovePedestal = (event: Touch | MouseEvent) => {
		const pos = { x: event.clientX, y: event.clientY };
		let newZ = this.coords.initialPedestal + this.pedestalScaleFactor * (pos.y - this.coords.initialShiftPos.y);

		// clamp to min/max
		if (this.stayInsideBounds) {
			if (newZ < this.minPedestal) {
				newZ = Math.max(newZ, this.minPedestal);
			} else if (newZ > this.maxPedestal) {
				newZ = Math.min(newZ, this.maxPedestal);
			}
		}

		if (newZ === this.getPedestal()) {
			return;
		}
		this.setPedestal(newZ, true);
	};

	private handleMouseMoveRotate = (event: MouseEvent) => {
		this.coords.rotateEnd.set(event.clientX, event.clientY);
		this.coords.rotateDelta.subVectors(this.coords.rotateEnd, this.coords.rotateStart);

		this.setRotation(this.getRotation() + this.coords.rotateDelta.x / this.rotateSpeed);
		this.setTilt(this.getTilt() - this.coords.rotateDelta.y / this.rotateSpeed);

		this.coords.rotateStart.copy(this.coords.rotateEnd);

		this.publish('change');
	};

	private publishUserZoomDebounced = debounce(() => {
		// publish user zoom end
		this.isUserZooming = false;
		this.publish('user-zoom-end');
	}, 250);

	private publishUserZoomEvent = () => {
		if (this.isUserZooming) {
			this.publishUserZoomDebounced();
		} else {
			this.publish('user-zoom-start');
			this.isUserZooming = true;
			this.publishUserZoomDebounced();
		}
	};

	private setCameraMoving = () => {
		this.cameraMoving = true;
	};

	private unsetCameraMoving = () => {
		this.cameraMoving = false;
		this.lastCameraMoveTime = Date.now();
	};

	private setUserInteracting = () => {
		this.userInteracting = true;
	};

	private unsetUserInteracting = () => {
		this.userInteracting = false;
	};

	private publishPedestalChangeEvent = (newZ: number) => {
		this.publish('pedestal-change', {
			pedestal: newZ,
			scrolledToTop: this.scrolledToTop,
			scrolledToBottom: this.scrolledToBottom,
			scrollPercent: this.scrollPercent,
		});
	};

	/**
	 * Converts the altitude to the mercator zoom level.
	 * @param altitude {number} The altitude in meters.
	 */
	convertAltitudeToZoomLevel = (altitude: number): number => {
		// [Anzor] OK, so it's not technically altitude, because we return the camera Z distance
		// but this should do for now
		const [, lat] = this.center;

		const metersPerPixel = this.computeMetersPerPixelFromAltitude(altitude);
		const mapPixelWidthAtZoom = (Math.cos(MathUtils.degToRad(lat)) * 2 * Math.PI * EARTH_RADIUS_M) / metersPerPixel;

		const zoomLevel = MathUtils.clamp(
			Math.log2(mapPixelWidthAtZoom / CAMERA_CONSTANTS.MAPLIBRE_TILE_SIZE),
			CAMERA_CONSTANTS.MIN_MERCATOR_ZOOM_LEVEL,
			CAMERA_CONSTANTS.MAX_MERCATOR_ZOOM_LEVEL,
		);

		return zoomLevel;
	};

	/**
	 * Converts a mercator zoom level to an altitude in meters.
	 * @param zoomLevel{number} The mercator zoom level
	 */
	convertZoomLevelToAltitude = (zoomLevel: number) => {
		const metersTall = this.computeMetersPerPixelFromZoomLevel(zoomLevel) * this.options.canvasHeight;

		const altitude = metersTall / 2 / Math.tan(MathUtils.degToRad(this.camera.fov) / 2);

		return altitude;
	};

	private completeTween = (tween: Tween) => {
		if (tween.isPlaying()) {
			tween.stop();
		}
		this.rendererState.internalTweenGroup.remove(tween);
		this.tweens.delete(tween);
	};

	static CAMERA_FRAME_PADDING_SIDES = ['top', 'bottom', 'left', 'right'] as const;

	debugContainer = new Object3D();

	getCameraFrameForCoordinates(pointCloud: Vector3[], options: CameraFocusOnOptions & { padding: InsetPadding }) {
		const minZoom = options.minZoom !== undefined ? options.minZoom : this.minZoomAltitude;
		const maxZoom = options.maxZoom !== undefined ? options.maxZoom : this.maxZoomAltitude;

		const canvasWidth = this.options.canvasWidth;
		const canvasHeight = this.options.canvasHeight;

		const currentRotation = this.getRotation();
		const currentTilt = this.getTilt();
		const position = this.getPosition();

		this.setRotation(options.rotation ?? currentRotation, true);
		this.setTilt(options.tilt ?? currentTilt, true);

		const frame = getCameraFrameForPoints(pointCloud, {
			canvasWidth,
			canvasHeight,
			paddingOption: options.padding,
			verticalPadding: options.verticalPadding,
			minZoom,
			maxZoom,
			position,
			camera: this.camera,
			orbit: this.orbit,
		});

		this.setRotation(currentRotation, true);
		this.setTilt(currentTilt, true);

		return {
			center: frame.position,
			zoomLevel: this.convertAltitudeToZoomLevel(frame.zoom),
		};
	}

	focusOn(pointCloud: Vector3[], options: CameraFocusOnOptions & { padding: InsetPadding }) {
		if (DEBUG_FOCUS_ON) {
			if (this.debugContainer) {
				destroyObject3D(this.debugContainer);
			}

			// Create instanced mesh for all points
			const geometry = new BoxGeometry(4, 4, 4);
			const material = new MeshBasicMaterial({ color: 0x00ff00, depthTest: false });
			const instancedMesh = new InstancedMesh(geometry, material, pointCloud.length);

			// Set position for each instance
			const matrix = new Matrix4();
			pointCloud.forEach((point, index) => {
				matrix.setPosition(point);
				instancedMesh.setMatrixAt(index, matrix);
			});

			instancedMesh.instanceMatrix.needsUpdate = true;
			this.debugContainer.add(instancedMesh);
			this.scene.add(this.debugContainer);
		}

		const curve = options.curve;
		const callback = options.callback;
		const cancelledCallback = options.cancelledCallback;

		const minZoom = options.minZoom !== undefined ? options.minZoom : this.minZoomAltitude;
		const maxZoom = options.maxZoom !== undefined ? options.maxZoom : this.maxZoomAltitude;

		const canvasWidth = this.options.canvasWidth;
		const canvasHeight = this.options.canvasHeight;

		let duration;
		if (options.duration !== undefined) {
			duration = options.duration;
		} else if (process.env.TESTING === 'true') {
			duration = 0;
		} else {
			duration = CAMERA_CONSTANTS.DEFAULT_ANIMATION_DURATION;
		}

		const padding = options.padding;
		const currentRotaion = this.getRotation();
		const currentTilt = this.getTilt();

		if (options.rotation != null) {
			this.setRotation(options.rotation);
		}
		if (options.tilt != null) {
			this.setTilt(options.tilt);
		}

		const frame = getCameraFrameForPoints(pointCloud, {
			canvasHeight,
			paddingOption: padding,
			canvasWidth,
			minZoom,
			maxZoom,
			camera: this.camera,
			orbit: this.orbit,
			position: this.getPosition(),
			verticalPadding: options.verticalPadding,
		});

		this.setRotation(currentRotaion);
		this.setTilt(currentTilt);

		this.animateCamera(
			{
				position: frame.position,
				tilt: options.tilt,
				rotation: options.rotation,
				zoom: frame.zoom,
			},
			duration,
			curve,
			callback,
			cancelledCallback,
			options,
		);
	}

	resize(canvasWidth: number, canvasHeight: number) {
		this.options.canvasWidth = canvasWidth;
		this.options.canvasHeight = canvasHeight;
	}

	computeMetersPerPixelFromZoomLevel(zoomLevel: number) {
		const [, lat] = this.center;

		const constrainedZoom = MathUtils.clamp(
			zoomLevel,
			CAMERA_CONSTANTS.MIN_MERCATOR_ZOOM_LEVEL,
			CAMERA_CONSTANTS.MAX_MERCATOR_ZOOM_LEVEL,
		);

		const constrainedScale = Math.pow(2, constrainedZoom);
		const constrainedLatitude = MathUtils.clamp(lat, CAMERA_CONSTANTS.MIN_LAT, CAMERA_CONSTANTS.MAX_LAT);
		const worldSize = constrainedScale * CAMERA_CONSTANTS.MAPLIBRE_TILE_SIZE;

		return (Math.cos(MathUtils.degToRad(constrainedLatitude)) * Math.PI * 2 * EARTH_RADIUS_M) / worldSize;
	}

	computeMetersPerPixelFromAltitude(altitude: number) {
		const metersTall = altitude * 2 * Math.tan(MathUtils.degToRad(this.camera.fov) / 2);
		return metersTall / this.options.canvasHeight;
	}

	getCurrentMetersPerPixel() {
		return this.computeMetersPerPixelFromAltitude(this.getZoomAltitude());
	}
}
export { CameraSystem };

export function getCameraFrameForPoints(
	pointCloud: Vector3[],
	{
		canvasWidth,
		orbit,
		paddingOption,
		verticalPadding = 0,
		canvasHeight,
		minZoom,
		maxZoom,
		camera,
		position,
	}: {
		paddingOption: InsetPadding;
		canvasWidth: number;
		canvasHeight: number;
		minZoom: number;
		maxZoom: number;
		orbit: Object3D;
		camera: PerspectiveCamera;
		position: Vector3;
		verticalPadding?: number;
	},
): { position: Vector3; zoom: number } {
	const results = {
		zoom: minZoom,
		position,
	};

	if (pointCloud.length === 0) {
		return results;
	}

	for (let side of CameraSystem.CAMERA_FRAME_PADDING_SIDES) {
		if (typeof paddingOption[side] !== 'number') {
			paddingOption[side] = 0;
		}
	}

	const padding: InsetPadding =
		paddingOption.type === 'portion'
			? {
					top: paddingOption.top * canvasHeight,
					bottom: paddingOption.bottom * canvasHeight,
					left: paddingOption.left * canvasWidth,
					right: paddingOption.right * canvasWidth,
					type: 'pixel',
			  }
			: paddingOption;

	if (padding.left + padding.right >= canvasWidth || padding.top + padding.bottom >= canvasHeight) {
		return results;
	}

	orbit.updateMatrixWorld();
	const camMatrixWorldInverse = camera.matrixWorldInverse;
	let halfFOV = (camera.fov * (Math.PI / 180)) / 2;
	let yFov = halfFOV;
	let xFov_OLD = Math.atan(Math.tan(halfFOV) * (canvasWidth / canvasHeight));
	let xFov = xFov_OLD;

	let min = new Vector3(Infinity, Infinity, Infinity);
	let max = new Vector3(-Infinity, -Infinity, -Infinity);

	let originalBbox = new Box3();

	const point = new Vector3();
	for (let i = 0, iLen = pointCloud.length; i < iLen; i++) {
		point.copy(pointCloud[i]);
		originalBbox.expandByPoint(point);
		point.applyMatrix4(camMatrixWorldInverse);
		min.min(point);
		max.max(point);

		point.copy(pointCloud[i]);
		point.z -= verticalPadding;
		point.applyMatrix4(camMatrixWorldInverse);
		min.min(point);
		max.max(point);

		point.copy(pointCloud[i]);
		point.z += verticalPadding;
		point.applyMatrix4(camMatrixWorldInverse);
		min.min(point);
		max.max(point);
	}

	let originalBoundsWidth = max.x - min.x;
	let originalBoundsHeight = max.y - min.y;

	let containmentWidth = canvasWidth - padding.left - padding.right;
	let containmentHeight = canvasHeight - padding.top - padding.bottom;

	let originalBoundsCenterX = (max.x + min.x) / 2;
	let originalBoundsCenterY = (max.y + min.y) / 2;

	let widthRatio = originalBoundsWidth / containmentWidth;
	let heightRatio = originalBoundsHeight / containmentHeight;

	let minZoomDistance = minZoom;
	let maxZoomDistance = maxZoom;

	let minBoundsWidth = minZoomDistance * Math.tan(xFov) * 2;
	let maxBoundsWidth = maxZoomDistance * Math.tan(xFov) * 2;
	let minimumScaleRatio = minBoundsWidth / canvasWidth;
	let maximumScaleRatio = maxBoundsWidth / canvasWidth;

	let scaleRatio = Math.max(widthRatio, heightRatio);

	scaleRatio = MathUtils.clamp(scaleRatio, minimumScaleRatio, maximumScaleRatio);

	let paddedBoundsWidth = containmentWidth * scaleRatio;
	let paddedBoundsHeight = containmentHeight * scaleRatio;

	let boundsForScreen = {
		min: {
			x: originalBoundsCenterX - paddedBoundsWidth / 2 - padding.left * scaleRatio,
			y: originalBoundsCenterY - paddedBoundsHeight / 2 - padding.bottom * scaleRatio,
		},
		max: {
			x: originalBoundsCenterX + paddedBoundsWidth / 2 + padding.right * scaleRatio,
			y: originalBoundsCenterY + paddedBoundsHeight / 2 + padding.top * scaleRatio,
		},
	};

	let centerX = (boundsForScreen.min.x + boundsForScreen.max.x) / 2;
	let centerY = (boundsForScreen.min.y + boundsForScreen.max.y) / 2;

	const center = originalBbox.getCenter(new Vector3());

	let deltaX = Math.abs(centerX - boundsForScreen.min.x);
	let deltaY = Math.abs(centerY - boundsForScreen.min.y);

	let distanceX = deltaX / Math.tan(xFov);
	let distanceY = deltaY / Math.tan(yFov);

	let cameraTarget = new Vector3();

	cameraTarget.set(centerX, centerY, max.z + Math.max(distanceX, distanceY));
	cameraTarget.applyMatrix4(camera.matrixWorld);

	let cameraAnchor = new Vector3();
	cameraAnchor.set(centerX, centerY, max.z);
	cameraAnchor.applyMatrix4(camera.matrixWorld);

	// Calculate the direction vector between the two points
	let direction = new Vector3().subVectors(cameraAnchor, cameraTarget);

	// Calculate the distance between pointA and the z plane
	let distanceToZPlane = (center.z - cameraTarget.z) / direction.z;

	// Calculate the next point in line where pointA intersects the z plane
	let nextPoint = new Vector3().copy(cameraTarget).add(direction.clone().multiplyScalar(distanceToZPlane));

	cameraAnchor.set(nextPoint.x, nextPoint.y, nextPoint.z);

	cameraTarget.sub(cameraAnchor);

	return {
		position: nextPoint,
		zoom: cameraTarget.length(),
	};
}
