import type { Space, MapDataInternal, FloorStack } from '.';
import { Coordinate } from '.';
import BaseMapData from './base-object';
import type { Facade as MVFFacade, SpaceCollection } from '@mappedin/mvf-v2';
import center from '@turf/center';
import type { IAnchorable, IFocusable } from '../types';
import { MappedinError } from '@packages/internal/common/errors';

/**
 * A Facade is a collection of spaces that make up the exterior representation of a Floor Stack ({@link FloorStack}).
 */
class Facade extends BaseMapData implements IFocusable, IAnchorable {
	static readonly __type = 'facade';
	readonly __type = Facade.__type;

	/**
	 * Checks if the provided instance is of type Floor.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Floor, false otherwise.
	 */
	static is(instance: object): instance is Facade {
		return (instance as { __type: string }).__type === Facade.__type;
	}

	#mvfData: MVFFacade;
	#data: MapDataInternal;
	#floorStackId: string;
	#cache: { spaces?: Space[]; center?: Coordinate } = {};

	/** @internal */
	constructor(data: MapDataInternal, options: { mvfData: MVFFacade; floorStackId: string }) {
		super(options.mvfData.id, data);
		this.#mvfData = options.mvfData;
		this.#data = data;
		this.#floorStackId = options.floorStackId;
	}

	/**
	 * Gets the floor stack that this Facade represents.
	 */
	get floorStack(): FloorStack {
		return this.#data.getById('floor-stack', this.#floorStackId)!;
	}

	/**
	 * Gets the collection of spaces that make up this Facade.
	 */
	get spaces(): Space[] {
		if (!this.#cache.spaces) {
			this.#cache.spaces = [];
			for (const facadeSpace of this.#mvfData.spaces) {
				const space = this.#data.getById('space', facadeSpace.spaceId);
				if (space) {
					this.#cache.spaces.push(space);
				}
			}
		}

		return this.#cache.spaces;
	}

	/**
	 * Gets the center {@link Coordinate} of the Facade.
	 */
	get center(): Coordinate {
		if (!this.#cache.center) {
			const featureCollection: SpaceCollection = {
				type: 'FeatureCollection',
				features: [],
			};
			for (const space of this.spaces) {
				const mvfSpace = this.#data.mvfSpacesById[space.id];
				if (mvfSpace) {
					featureCollection.features.push(mvfSpace);
				}
			}

			if (featureCollection.features.length === 0) {
				throw new MappedinError('No geometry found for facade!');
			}

			const centerResult = center(featureCollection);
			this.#cache.center = new Coordinate({
				latitude: centerResult.geometry.coordinates[1],
				longitude: centerResult.geometry.coordinates[0],
				floorId: this.#mvfData.spaces[0].floorId,
			});
		}

		return this.#cache.center;
	}

	/** @internal */
	get focusTarget() {
		return this.spaces;
	}

	/** @internal */
	get anchorTarget() {
		return this.center;
	}

	toJSON() {
		return {
			id: this.id,
			__type: Facade.__type,
			floorStackId: this.#floorStackId,
			floorStack: this.floorStack,
			spaces: this.spaces,
		};
	}
}

export default Facade;
