import type { StyleCollection } from '@mappedin/mvf-v2';
import type { GeojsonApiMapObject } from './map-object';
import { EXTERIOR_WALLS_ID, FACADES_LAYER, GEOMETRY_LAYER, WALLS_ID } from '../constants';
export class Style {
	#getCurrentMap: CurrentMapGetter;
	/**
	 * @internal
	 */
	get currentMap() {
		return this.#getCurrentMap();
	}
	/**
	 * @internal
	 */
	constructor({ currentMapGetter }: { currentMapGetter: CurrentMapGetter }) {
		this.#getCurrentMap = currentMapGetter;
	}

	setFromStyleCollection(styleCollection: StyleCollection): void {
		for (const styleId in styleCollection) {
			for (const floor of this.currentMap?.floors || []) {
				if (styleCollection[styleId] == null) {
					continue;
				}
				if (!floor.layers.has(GEOMETRY_LAYER)) {
					continue;
				}
				const geometryLayer = floor.layers.get(GEOMETRY_LAYER);

				if (geometryLayer?.layers.has(styleId) && 'color' in styleCollection[styleId]) {
					const layer = geometryLayer.layers.get(styleId);
					if (layer) {
						this.currentMap?.renderer.setState(layer, {
							color: styleCollection[styleId].color,
						});
					}
				}
				// only update exterior wall style with wall style when there's no explicit exterior wall id in the stylecollection
				if (geometryLayer && styleId === WALLS_ID && !(EXTERIOR_WALLS_ID in styleCollection)) {
					const exteriorId = geometryLayer.layers.get(EXTERIOR_WALLS_ID);
					if (exteriorId && 'color' in styleCollection[styleId]) {
						this.currentMap?.renderer.setState(exteriorId, {
							color: styleCollection[styleId].color,
						});
					}
				}

				if (!floor.layers.has(FACADES_LAYER)) {
					continue;
				}

				const facadeLayer = floor.layers.get(FACADES_LAYER)!;
				// Get the facades that share geometry with this styleId
				const facades = floor.facadesByStyleId.get(styleId);
				if (facades && facades.length > 0) {
					for (const facadeId of facades) {
						const groupId = facadeLayer.layers.get(`${facadeId}-${styleId}`);
						if (groupId && 'color' in styleCollection[styleId]) {
							this.currentMap?.renderer.setState(groupId, {
								color: styleCollection[styleId].color,
							});
						}
					}
				}
			}
		}
	}
}
type CurrentMapGetter = () => GeojsonApiMapObject | undefined;
