import type { GeometryGroupState, GroupContainerState, RendererCore } from '@mappedin/core-sdk';
import { Logger } from '@packages/internal/common';
import { z } from 'zod';
import type { FloorObject } from '../api-geojson/floor-object';
import {
	FOOTPRINT_LAYER,
	OCCLUSION_LAYER,
	GEOMETRY_LAYER,
	MARKERS_LAYER,
	LABELS_LAYER,
	IMAGES_LAYER,
	PATHS_LAYER,
	AREAS_LAYER,
} from '../constants';
import { parseCompleteState } from './state-utils';

const FLOOR_STATE_TYPE = 'floor';
const FOOTPRINT_STATE_TYPE = `${FLOOR_STATE_TYPE}.footprint`;
const GEOMETRY_STATE_TYPE = `${FLOOR_STATE_TYPE}.geometry`;
const LABELS_STATE_TYPE = `${FLOOR_STATE_TYPE}.labels`;
const MARKERS_STATE_TYPE = `${FLOOR_STATE_TYPE}.markers`;
const IMAGES_STATE_TYPE = `${FLOOR_STATE_TYPE}.images`;
const OCCLUSION_STATE_TYPE = `${FLOOR_STATE_TYPE}.occlusion`;
const PATHS_STATE_TYPE = `${FLOOR_STATE_TYPE}.paths`;
const AREAS_STATE_TYPE = `${FLOOR_STATE_TYPE}.areas`;

export const footprintStateSchema = z.object({
	type: z.literal(FOOTPRINT_STATE_TYPE).default(FOOTPRINT_STATE_TYPE),
	/**
	 * Altitude of the footprint relative to the floor.
	 * @default 0
	 */
	altitude: z.number().default(0),
	/**
	 * Whether the footprint is visible.
	 * @default false
	 */
	visible: z.boolean().default(false),
	/**
	 * Height of the footprint relative to altitude.
	 * @default 0
	 */
	height: z.number().default(0),
	/**
	 * Opacity of the footprint.
	 * @default 1
	 */
	opacity: z.number().default(1),
	/**
	 * Material side of the footprint.
	 * @internal
	 */
	side: z.enum(['double', 'front', 'back']),
	/**
	 * Color of the footprint.
	 * @default #ffffff
	 */
	color: z.string().default('#ffffff'),
});

export const footprintStateSchemaPartial = footprintStateSchema.partial();

export const floorGeometryStateSchema = z.object({
	type: z.literal(GEOMETRY_STATE_TYPE).default(GEOMETRY_STATE_TYPE),
	/**
	 * Opacity of the floor geometry.
	 * @default 1
	 */
	opacity: z.number().default(1),
	/**
	 * Visibility of floor geometry.
	 * @default true
	 */
	visible: z.boolean().default(true),
});

export const floorGeometryStateSchemaPartial = floorGeometryStateSchema.partial();

export const floorLabelsStateSchema = z.object({
	type: z.literal(LABELS_STATE_TYPE).default(LABELS_STATE_TYPE),
	/**
	 * Whether the labels are enabled to be shown when the floor is visible.
	 * @default true
	 */
	enabled: z.boolean().default(true),
});

export const floorLabelsStateSchemaPartial = floorLabelsStateSchema.partial();

export const floorMarkersStateSchema = z.object({
	type: z.literal(MARKERS_STATE_TYPE).default(MARKERS_STATE_TYPE),
	/**
	 * Whether the markers are enabled to be shown when the floor is visible.
	 * @default true
	 */
	enabled: z.boolean().default(true),
});

export const floorMarkersStateSchemaPartial = floorMarkersStateSchema.partial();

export const floorImagesStateSchema = z.object({
	type: z.literal(IMAGES_STATE_TYPE).default(IMAGES_STATE_TYPE),
	/**
	 * Visibility of the images on the floor.
	 * @default true
	 */
	visible: z.boolean().default(true),
});

export const floorImagesStateSchemaPartial = floorImagesStateSchema.partial();

export const floorOcclusionStateSchema = z.object({
	type: z.literal(OCCLUSION_STATE_TYPE).default(OCCLUSION_STATE_TYPE),
	/**
	 * @internal
	 * Whether this floor should occlude labels and markers on other floors
	 * @default true
	 */
	enabled: z.boolean().default(true),
});

export const floorOcclusionStateSchemaPartial = floorOcclusionStateSchema.partial();

export const floorPathsStateSchema = z.object({
	type: z.literal(PATHS_STATE_TYPE).default(PATHS_STATE_TYPE),
	/**
	 * Visibility of the paths on the floor.
	 * @default true
	 */
	visible: z.boolean().default(true),
});

export const floorPathsStateSchemaPartial = floorPathsStateSchema.partial();

export const floorSchema = z.object({
	type: z.literal(FLOOR_STATE_TYPE).default(FLOOR_STATE_TYPE),
	/**
	 * The visibility of the floor, including the geometry, labels, and markers.
	 * @default false
	 */
	visible: z.boolean().default(false),
	/**
	 * The altitude of the floor.
	 * @default 0
	 */
	altitude: z.number().default(0),
});

export const areasStateSchema = z.object({
	type: z.literal(AREAS_STATE_TYPE).default(AREAS_STATE_TYPE),
	/**
	 * Visibility of the areas on the floor.
	 * @default true
	 */
	visible: z.boolean().default(true),
});

export const areasStateSchemaPartial = areasStateSchema.partial();

export const floorStateSchema = z.object({
	...floorSchema.shape,
	geometry: floorGeometryStateSchema,
	labels: floorLabelsStateSchema,
	markers: floorMarkersStateSchema,
	images: floorImagesStateSchema,
	footprint: footprintStateSchema,
	occlusion: floorOcclusionStateSchema,
	paths: floorPathsStateSchema,
	areas: areasStateSchema,
});

export const floorSchemaPartial = floorSchema.partial();

const floorStateSchemaPartial = z
	.object({
		...floorSchema.shape,
		/**
		 * State of the geometry on the floor.
		 */
		geometry: floorGeometryStateSchema.partial(),
		labels: floorLabelsStateSchema.partial(),
		markers: floorMarkersStateSchema.partial(),
		images: floorImagesStateSchema.partial(),
		footprint: footprintStateSchema.partial(),
		occlusion: floorOcclusionStateSchema.partial(),
		paths: floorPathsStateSchema.partial(),
		areas: areasStateSchema.partial(),
	})
	.partial();
const floorStateSchemaStrict = z.strictObject({
	...floorSchema.shape,
	geometry: z.strictObject(floorGeometryStateSchema.shape),
	labels: z.strictObject(floorLabelsStateSchema.shape),
	markers: z.strictObject(floorMarkersStateSchema.shape),
	images: z.strictObject(floorImagesStateSchema.shape),
	footprint: z.strictObject(footprintStateSchema.shape),
	occlusion: z.strictObject(floorOcclusionStateSchema.shape),
	paths: z.strictObject(floorPathsStateSchema.shape),
	areas: z.strictObject(areasStateSchema.shape),
});

export type TFloorState = z.infer<typeof floorStateSchemaStrict>;
export type TFloorUpdateState = z.infer<typeof floorStateSchemaPartial>;

export function getFloorState(
	state: GroupContainerState,
	floor?: FloorObject,
	renderer?: RendererCore,
): TFloorState | undefined {
	if (!floor) {
		Logger.error(`Floor is not defined. Cannot get floor state.`);
		return;
	}
	const geometryLayer = floor.layers.get(GEOMETRY_LAYER);
	const geometryLayerState = geometryLayer
		? renderer?.getState<GroupContainerState>(geometryLayer.containerId)
		: undefined;

	const labelLayer = floor.layers.get(LABELS_LAYER);
	const markersLayer = floor.layers.get(MARKERS_LAYER);
	const footprintLayer = floor.layers.get(FOOTPRINT_LAYER);
	const footprintLayerState = footprintLayer
		? renderer?.getState<GeometryGroupState>(footprintLayer.containerId)
		: undefined;
	const occlusionLayer = floor.layers.get(OCCLUSION_LAYER);
	const occlusionLayerState = occlusionLayer
		? renderer?.getState<GeometryGroupState>(occlusionLayer.containerId)
		: undefined;
	const imagesLayer = floor.layers.get(IMAGES_LAYER);
	const imagesLayerState = imagesLayer ? renderer?.getState<GeometryGroupState>(imagesLayer.containerId) : undefined;
	const pathsLayer = floor.layers.get(PATHS_LAYER);
	const pathsLayerState = pathsLayer ? renderer?.getState<GeometryGroupState>(pathsLayer.containerId) : undefined;
	const areasLayer = floor.layers.get(AREAS_LAYER);
	const areasLayerState = areasLayer ? renderer?.getState<GeometryGroupState>(areasLayer.containerId) : undefined;
	return parseCompleteState(floorStateSchema, {
		geometry: {
			opacity: geometryLayerState?.opacity,
			visible: geometryLayerState?.visible,
		},
		labels: {
			enabled: labelLayer?.visible,
		},
		markers: {
			enabled: markersLayer?.visible,
		},
		footprint: {
			altitude: footprintLayerState?.altitude,
			visible: footprintLayerState?.visible,
			height: footprintLayerState?.height,
			opacity: footprintLayerState?.opacity,
			side: footprintLayerState?.side,
			color: footprintLayerState?.color,
		},
		images: {
			visible: imagesLayerState?.visible,
		},
		occlusion: {
			enabled: occlusionLayerState?.visible,
		},
		paths: {
			visible: pathsLayerState?.visible,
		},
		areas: {
			visible: areasLayerState?.visible,
		},
		visible: state.visible,
		altitude: state.altitude,
	});
}
