import type { Vector3, Camera, Object3D, Matrix4 } from 'three';
import { Vector2, Raycaster } from 'three';

/**
 * Represents a touch event anchored to a point along the z=0 plane.
 */
class TouchAnchor {
	touch: Vector2;
	camera: Camera;
	cameraPlane: Object3D;
	viewCoordinate: { x: number; y: number };
	snapHolderMatrix: Matrix4;
	snapCamera: Camera;
	cameraPosition?: Vector3;
	raycaster?: Raycaster;
	worldCoordinate?: Vector3 | null;
	snapshotted = false;

	constructor(touch: Vector2, camera: Camera, cameraPlane: Object3D) {
		this.touch = touch;
		this.camera = camera;
		this.cameraPlane = cameraPlane;
		this.viewCoordinate = { x: touch.x, y: touch.y };
		this.camera.updateMatrixWorld();
		this.camera.updateMatrix();
		this.snapHolderMatrix = this.camera.matrixWorld.clone();
		this.snapCamera = this.camera.clone();
		this.snapshotCameraState();
	}

	/**
	 * Calculate and set the world position of this touch anchor.
	 */
	snapshotCameraState() {
		this.camera.updateMatrix();
		this.camera.updateMatrixWorld();

		this.cameraPosition = this.camera.position.clone();

		const coordinateVector = new Vector2(this.viewCoordinate.x, this.viewCoordinate.y);
		this.raycaster = this.raycaster || new Raycaster();
		this.raycaster.setFromCamera(coordinateVector, this.snapCamera);

		const point = this.raycaster.intersectObject(this.cameraPlane, true);
		this.worldCoordinate = point && point.length > 0 ? point[0].point : null;

		this.snapshotted = true;
	}

	/**
	 * Update anchor state with a new camera and coordinate.
	 */
	updateAnchorState(camera: Camera, coord: { x: number; y: number }) {
		this.camera = camera;
		this.snapCamera = camera.clone();
		this.snapHolderMatrix = camera.matrixWorld.clone();
		this.updateViewCoordinate(coord);
		this.snapshotCameraState();
	}

	/**
	 * Set the current screen touch coordinate for this anchor.
	 */
	updateViewCoordinate(coord: { x: number; y: number }) {
		this.viewCoordinate = { x: coord.x, y: coord.y };
	}

	/**
	 * Find the world position for the current touch position using the original camera matrices.
	 */
	reUnproject(): Vector3 | undefined {
		const coordinateVector = new Vector2(this.viewCoordinate.x, this.viewCoordinate.y);

		this.raycaster?.setFromCamera(coordinateVector, this.snapCamera);
		const intersection = this.raycaster?.intersectObject(this.cameraPlane, true)[0];

		return intersection ? intersection.point : undefined;
	}
}

export { TouchAnchor };
