export function createImage(icon: string) {
	const img = new Image();
	if (icon) {
		// This regex detects SVG content by looking for an <svg> tag anywhere in the string.
		// - <svg: Matches the opening SVG tag
		// - [\s\S]*: Matches any character including newlines
		// - >: Matches the closing bracket of the tag
		// - i flag: Makes the match case-insensitive
		// This pattern will match SVGs even when they have XML declarations or comments before the SVG tag.
		const isInlineSvg = /<svg[\s\S]*>/i.test(icon);
		if (isInlineSvg) {
			img.src = `data:image/svg+xml;base64,${btoa(icon)}`;
		} else {
			img.src = icon;
		}
	}

	return img;
}
