import { Box3, BoxGeometry, Mesh, MeshBasicMaterial, Object3D, Scene, Sphere, SphereGeometry, Vector3 } from 'three';
import type { CameraSystem } from '../camera';
import type { RendererState } from '../../renderer';
import { PubSub } from '@packages/internal/common';

// 100 meters margin around any geometry in the scene
const PAN_BOUNDS_MARGIN = 100;
const DEBUG = false;

export class PanBoundsSystem extends PubSub<{
	update: void;
}> {
	private currentTotalBoundingBox?: Box3;
	private state: RendererState;
	private cameraSystem: CameraSystem;
	private debugMesh: Object3D | null = null;
	dirty = true;
	autoMinZoomLevel = true;

	constructor(state: RendererState, cameraSystem: CameraSystem) {
		super();
		this.state = state;
		this.cameraSystem = cameraSystem;
	}

	/**
	 * Recomputes the pan bounds based on the current 2D and 3D entities in the scene
	 * This is only additive, meaning removing entities will not shrink the pan bounds
	 */
	update() {
		if (this.dirty) {
			if (this.currentTotalBoundingBox == null) {
				this.currentTotalBoundingBox = new Box3();
				if (this.state.entityScene.children.length === 0) {
					// expand from center point outwards when scene is empty
					this.currentTotalBoundingBox.setFromCenterAndSize(
						new Vector3(0, 0, 0),
						new Vector3(PAN_BOUNDS_MARGIN * 2, PAN_BOUNDS_MARGIN * 2, 0),
					);
				} else {
					// otherwise use rendered geometry
					this.currentTotalBoundingBox.setFromObject(this.state.entityScene);
					this.currentTotalBoundingBox.expandByScalar(PAN_BOUNDS_MARGIN);
				}
			}
			// @TODO: speed this up by keeping track of the bounding box as objects are added to the scene
			const box = new Box3().setFromObject(this.state.entityScene);
			this.currentTotalBoundingBox.union(box);
			const boxSize = box.getSize(new Vector3());
			const maxDimention = Math.max(boxSize.x, boxSize.y, boxSize.z);

			// make sure we take 2D entities into account when computing the pan bounds
			for (let entity of this.state.geometry2DMap.values()) {
				if (entity.positionDirty === true) {
					this.currentTotalBoundingBox.expandByPoint(entity.position);
					entity.positionDirty = false;
				}
			}

			/**
			 * We want expand bounds relative to the size of the map instead of hardcoded 100 meters.
			 * Otherwise, user can't zoom out in larger maps.
			 */
			const radius = expandedByPaddingBox
				.copy(this.currentTotalBoundingBox)
				.expandByScalar(Math.max(maxDimention, PAN_BOUNDS_MARGIN))
				.getBoundingSphere(expandedByPaddingSphere).radius;

			this.cameraSystem.panBounds = {
				center: this.currentTotalBoundingBox.getCenter(new Vector3()),
				min: expandedByPaddingBox.min,
				max: expandedByPaddingBox.max,
				radius,
			};
			this.publish('update');

			this.cameraSystem.dirty = true;
			this.dirty = false;

			if (DEBUG && this.state.entityScene.parent instanceof Scene) {
				if (this.debugMesh) {
					this.state.entityScene.parent.remove(this.debugMesh);
					this.debugMesh = null;
				}

				this.debugMesh = new Object3D();

				this.debugMesh.add(
					new Mesh(
						new SphereGeometry(this.cameraSystem.panBounds.radius),
						new MeshBasicMaterial({ color: 0xff0000, transparent: true, opacity: 0.2 }),
					),
				);
				this.debugMesh.position.copy(this.cameraSystem.panBounds.center);
				this.state.entityScene.parent.add(this.debugMesh);

				// Create a visualization of the bounding box
				const size = new Vector3();
				expandedByPaddingBox.getSize(size);
				const boxGeometry = new BoxGeometry(size.x, size.y, size.z);
				const boxMaterial = new MeshBasicMaterial({
					color: 0x00ff00,
					wireframe: true,
					transparent: true,
					opacity: 0.5,
				});
				const box = new Mesh(boxGeometry, boxMaterial);
				box.position.copy(this.currentTotalBoundingBox.getCenter(new Vector3()));
				this.debugMesh.add(box);
			}
		}
	}
}

const expandedByPaddingBox = new Box3();
const expandedByPaddingSphere = new Sphere();
