import z from 'zod';
import type { Text3DState } from '../../../geojson/src/components/text3d';
import {
	text3DStateSchema as GeoJsonText3DStateSchema,
	updateText3DStateSchema,
} from '../../../geojson/src/components/text3d';
import { Coordinate } from '../map-data-objects';
import { parseCompleteState } from './state-utils';

const TEXT3D_STATE_TYPE = 'text3d';

const commonText3DStateSchema = z.object({
	type: z.literal(TEXT3D_STATE_TYPE).default(TEXT3D_STATE_TYPE),
	position: z.custom<Coordinate>(val => val instanceof Coordinate, {
		error: 'Must be a Coordinate instance',
	}),
});

export const text3DStateSchema = GeoJsonText3DStateSchema.extend({
	...commonText3DStateSchema.shape,
});

export const text3DUpdateStateSchema = updateText3DStateSchema
	.extend({
		...commonText3DStateSchema.shape,
	})
	.partial();

const text3DStateSchemaStrict = z.strictObject(text3DStateSchema.shape);

export type TText3DState = z.infer<typeof text3DStateSchemaStrict>;

export type TText3DUpdateState = z.infer<typeof text3DUpdateStateSchema>;

export function getText3DState(state: Text3DState): TText3DState {
	const {
		position,
		visible,
		color,
		flipToFaceCamera,
		font,
		fontSize,
		outlineColor,
		outlineOpacity,
		outlineBlur,
		outlineWidth,
		outlineOffsetX,
		outlineOffsetY,
		strokeWidth,
		strokeOpacity,
		strokeColor,
		fillOpacity,
		hoverColor,
	} = state;
	return parseCompleteState(text3DStateSchema, {
		position: new Coordinate({
			latitude: position?.[1] ?? 0,
			longitude: position?.[0] ?? 0,
		}),
		visible,
		color,
		flipToFaceCamera,
		font,
		fontSize,
		outlineColor,
		outlineOpacity,
		outlineBlur,
		outlineWidth,
		outlineOffsetX,
		outlineOffsetY,
		strokeWidth,
		strokeOpacity,
		strokeColor,
		fillOpacity,
		hoverColor,
	});
}
