// @ts-ignore
import collisionWorker from 'inline-worker:./worker';
import { QuadTree, Rectangle } from '@packages/internal/quad-tree';
import '../../styles/collisions.scss';
import { throttle } from './utils';
import type { Geometry2D } from '../../entities/geometry2d';
import { Geometry2DComponents } from '../../entities/geometry2d';
import type { MarkerComponent } from '../../components/marker';
import type LabelComponent from '../../components/label';
import { PubSub } from '../../../../packages/common/pubsub';
import type { RendererState } from '../../renderer';
import {
	STATIC_COLLIDER_INDEX,
	type TSerializedCollider,
	type TSerializedColliderResponse,
} from './collider-processor';
import type { WatermarkPosition } from '../watermark/system';
import { ALWAYS_VISIBLE_RANK_INDEX } from '../../utils/collision-ranking-tier';
import { SyncWorker } from './sync-worker';
import { collisionWorkerUrl } from '../..';
import type { Point2D } from '@packages/internal/quad-tree/src/types';

let DEBUG = false;

export type TMessageEvent = MessageEvent<{
	msgId: string;
	colliders: TSerializedColliderResponse[];
	debug: { x: number; y: number; w: number; h: number }[];
}>;

export type TMessage = {
	msgId: string;
	totalWidth: number;
	totalHeight: number;
	watermarkWidth: number;
	watermarkHeight: number;
	watermarkPosition: WatermarkPosition;
	colliders: TSerializedCollider[];
	devicePixelRatio: number;
};

type TCollisionSystemEvent = {
	'visibility-changed': undefined;
};

const THROTTLE_TIME = 150;

export type PackedBBox = [x: number, y: number, w: number, h: number, index: number];
export type PackedPolygon = Point2D[];
export type PackedBBoxes = PackedBBox[];
export type PackedCollider = [
	bboxes: PackedBBoxes,
	enabled: 0 | 1,
	alwaysVisible: 0 | 1,
	x: number,
	y: number,
	shouldCollideWithScreenEdges?: 0 | 1,
	onlyExposeStrategyIndex?: number,
];
export type PackedMessage = [
	colliders: PackedCollider[],
	devicePixelRatio: number,
	totalHeight: number,
	totalWidth: number,
	watermarkWidth: number,
	watermarkHeight: number,
	watermarkPosition: WatermarkPosition,
];
export type PackedJsonMessage = {
	x: number;
	y: number;
	enabled: boolean;
	rank: number;
	bboxes: PackedBBoxes;
	shouldCollideWithScreenEdges: boolean;
	lockedToStrategyIndex: number;
};

export class CollisionSystem extends PubSub<TCollisionSystemEvent, keyof TCollisionSystemEvent> {
	private worker: Worker | SyncWorker;
	private debugContext: CanvasRenderingContext2D;
	private debugCanvas: HTMLCanvasElement;
	collidersDirty = true;

	packedMessage: PackedMessage = [[], 0, 0, 0, 0, 0, 'bottom-left'];

	visibleCollidersQTree = new QuadTree<{ entityId: string | number }>(
		new Rectangle<{ entityId: string | number }>(0, 0, 0, 0),
	);
	interactiveCollidersQTree = new QuadTree<{ entityId: string | number }>(
		new Rectangle<{ entityId: string | number }>(0, 0, 0, 0),
	);

	coreState: RendererState;

	constructor(debugCanvas: HTMLCanvasElement, coreState: RendererState, useWorker = true) {
		super();
		this.worker =
			useWorker === false
				? new SyncWorker()
				: collisionWorkerUrl !== ''
				? new Worker(collisionWorkerUrl)
				: new Worker(URL.createObjectURL(new Blob([collisionWorker], { type: 'text/javascript' })));

		// In Chrome >= 139, worker sends an error event if CSP rules prohibit it from loading
		this.worker.onerror = () => {
			this.worker = new SyncWorker();
			this.worker.onmessage = e => this.resolve(e);
		};

		this.coreState = coreState;
		this.debugCanvas = debugCanvas;
		this.debugContext = debugCanvas.getContext('2d')!;

		this.worker.onmessage = e => this.resolve(e);
		this.postMessage = throttle(this._postMessage, THROTTLE_TIME, false);
		if (DEBUG) {
			this.showCollisionBoxes();
		} else {
			this.hideCollisionBoxes();
		}
	}

	postMessage: () => void;

	showCollisionBoxes = () => {
		DEBUG = true;
		this.debugCanvas.style.display = 'block';
	};

	hideCollisionBoxes = () => {
		DEBUG = false;
		this.debugContext.clearRect(0, 0, this.debugCanvas.width, this.debugCanvas.height);
		this.debugCanvas.style.display = 'none';
	};

	currentMsgId = '';
	working = false;

	private _postMessage = () => {
		this.worker.postMessage(this.packedMessage);
	};

	componentArray: (MarkerComponent | LabelComponent)[] = [];

	update = (
		watermarkWidth: number,
		watermarkHeight: number,
		watermarkPosition: WatermarkPosition = 'bottom-left',
		isPanning = false,
		staticColliders: Rectangle[] = [],
	) => {
		let needsSort = false;
		if (this.collidersDirty) {
			this.packedMessage = [[], 0, 0, 0, 0, 0, 'bottom-left'];
			this.componentArray = [];
			let component: MarkerComponent | LabelComponent;
			for (const entityId of this.coreState.geometry2DIdsInScene) {
				const entity = this.coreState.geometry2DMap.get(entityId);
				if (entity) {
					component = entity.components[Geometry2DComponents.UI];
					this.componentArray.push(component as MarkerComponent | LabelComponent);
				}
			}

			this.resize(watermarkWidth, watermarkHeight, watermarkPosition);
			this.collidersDirty = false;
			needsSort = true;
		} else {
			needsSort = this.componentArray.some(c => c.collisionDirty === true);
		}

		if (needsSort) {
			this.componentArray.sort((a, b) => b.rank - a.rank);
		}

		let currentIndex = 0;
		// let's add the static colliders
		for (const staticCollider of staticColliders) {
			if (this.packedMessage[0][currentIndex] == null) {
				this.packedMessage[0][currentIndex] = [] as unknown as PackedCollider;
			}
			if (this.packedMessage[0][currentIndex][0] == null) {
				this.packedMessage[0][currentIndex][0] = [];
			}

			// simulate a regular collider with a single bbox, with a static collider index
			// so that we can tell what these are in the collision worker
			// TODO: we need a more friendly way to add colliders
			this.packedMessage[0][currentIndex][0] = [
				[staticCollider.h / 2, staticCollider.h / 2, staticCollider.w / 2, staticCollider.w / 2, STATIC_COLLIDER_INDEX],
			];
			this.packedMessage[0][currentIndex][1] = 1;
			this.packedMessage[0][currentIndex][2] = 1;
			this.packedMessage[0][currentIndex][3] = staticCollider.x;
			this.packedMessage[0][currentIndex][4] = staticCollider.y;
			this.packedMessage[0][currentIndex][5] = 0;
			this.packedMessage[0][currentIndex][6] = -1;
			currentIndex++;
		}

		let componentIndex = 0;
		while (currentIndex < this.componentArray.length + staticColliders.length) {
			if (this.packedMessage) {
				const json = this.componentArray[componentIndex].toPackedMessage(isPanning && !needsSort);
				if (this.packedMessage[0][currentIndex] == null) {
					this.packedMessage[0][currentIndex] = [] as unknown as PackedCollider;
				}
				if (this.packedMessage[0][currentIndex][0] == null) {
					this.packedMessage[0][currentIndex][0] = [];
				}
				this.packedMessage[0][currentIndex][0] = json.bboxes;
				this.packedMessage[0][currentIndex][1] =
					json.enabled && this.componentArray[componentIndex].visible && !this.componentArray[componentIndex].isOccluded
						? 1
						: 0;
				this.packedMessage[0][currentIndex][2] = json.rank === ALWAYS_VISIBLE_RANK_INDEX ? 1 : 0;
				this.packedMessage[0][currentIndex][3] = json.x;
				this.packedMessage[0][currentIndex][4] = json.y;
				this.packedMessage[0][currentIndex][5] = json.shouldCollideWithScreenEdges ? 1 : 0;
				this.packedMessage[0][currentIndex][6] = json.lockedToStrategyIndex;
			}
			currentIndex++;
			componentIndex++;
		}

		if (this.working) {
			return;
		}

		this.postMessage();
	};

	resize(watermarkWidth: number, watermarkHeight: number, watermarkPosition: WatermarkPosition) {
		this.packedMessage[6] = watermarkPosition;
		this.packedMessage[5] = watermarkHeight;
		this.packedMessage[4] = watermarkWidth;
		this.packedMessage[3] = this.debugCanvas.width;
		this.packedMessage[2] = this.debugCanvas.height;
	}

	/**
	 * Resolve collisions
	 */
	resolve = (
		e: MessageEvent<{
			msgId: string;
			colliders: TSerializedColliderResponse[];
			staticColliders: Rectangle[];
		}>,
	) => {
		this.working = false;

		if (this.collidersDirty || !e.data) {
			return;
		}

		const visibleColliders = e.data.colliders;
		let strategyIndex = -1;
		let rectangle: Rectangle | undefined;
		let entity: Geometry2D | undefined;
		let boundingRect: Rectangle<{ entityId: string | number }>;
		const canvasRect = new Rectangle(0, 0, this.debugCanvas.width, this.debugCanvas.height);
		this.visibleCollidersQTree = new QuadTree<any>(canvasRect);
		this.interactiveCollidersQTree = new QuadTree<any>(canvasRect);
		if (DEBUG) {
			// insert static colliders
			for (const staticCollider of e.data.staticColliders) {
				this.visibleCollidersQTree.insert(
					new Rectangle(staticCollider.x, staticCollider.y, staticCollider.w, staticCollider.h),
				);
			}
		}

		for (let i = 0; i < this.componentArray.length; i++) {
			const component = this.componentArray[i];
			if (visibleColliders[i] == null) {
				continue;
			}

			strategyIndex = visibleColliders[i][0];
			rectangle = visibleColliders[i][2];
			component.offscreen = visibleColliders[i][1] === 1;

			if (strategyIndex !== -1 && rectangle != null) {
				boundingRect = new Rectangle(rectangle.x, rectangle.y, rectangle.w, rectangle.h, {
					entityId: component.id,
				});
				this.visibleCollidersQTree.insert(boundingRect);
				entity = this.coreState.geometry2DMap.get(component.id)!;
				if (entity?.components[Geometry2DComponents.Interaction] != null) {
					this.interactiveCollidersQTree.insert(boundingRect);
				}
				if (component.canShow === false || component.visibilityNeedsUpdate !== false) {
					if ('visibilityNeedsUpdate' in component) {
						component.visibilityNeedsUpdate = 'show';
					}
				}

				if (strategyIndex !== -1 && strategyIndex !== component.currentStrategyIndex) {
					component.activeBoundingBox = boundingRect;
					component.onStrategySelected(strategyIndex);
				}
			} else {
				if (component.canShow === true || component.visibilityNeedsUpdate !== false) {
					if ('visibilityNeedsUpdate' in component) {
						component.visibilityNeedsUpdate = 'hide';
					}
				}
				if (strategyIndex !== -1 && strategyIndex !== component.currentStrategyIndex) {
					component.onStrategySelected(strategyIndex);
				}
			}
		}

		if (DEBUG) {
			this.drawDebug();
		}

		this.publish('visibility-changed');
	};

	drawDebug = () => {
		if (DEBUG && this.visibleCollidersQTree) {
			this.debugContext.clearRect(0, 0, this.debugCanvas.width, this.debugCanvas.height);
			this.debugContext.beginPath();
			this.visibleCollidersQTree.drawObjects(this.debugContext);
		}
	};

	destroy = () => {
		this.worker.terminate();
	};
}
