import type { MarkerState } from '@mappedin/core-sdk';
import z from 'zod';
import type { TCollisionRankingTier } from '../types';
import type { Marker } from '../map-view-objects';
import { Coordinate } from '../map-data-objects';
import type { Complete } from '@packages/internal/common/type-utils';
import { collisionRankingTierSchema } from '../../../geojson/src/utils/collision-ranking-tier';
import { MARKER_PLACEMENT_VALUES } from '../../../geojson/src/components/marker-utils';

export const MARKER_STATE_TYPE = 'marker';

export const markerStateSchema = z.object({
	type: z.literal(MARKER_STATE_TYPE).default(MARKER_STATE_TYPE),
	/**
	 * Determines the collision ranking tier of the marker, which influences its visibility in relation to other colliders.
	 * @default 'medium'
	 */
	rank: z.custom<TCollisionRankingTier | 'initial'>(val => {
		return collisionRankingTierSchema.safeParse(val).success || val === 'initial';
	}),
	/**
	 * Specifies the placement of the marker, determining its position relative to its coordinates.
	 * Can be a single placement or an array of placements.
	 */
	placement: z.union([z.enum(MARKER_PLACEMENT_VALUES), z.array(z.enum(MARKER_PLACEMENT_VALUES))]).optional(),
	/**
	 * Whether the Marker should be clickable.
	 * @default false
	 */
	interactive: z.union([z.boolean(), z.literal('pointer-events-auto')]).default(false),
	/**
	 * When true, the marker's collision boundary automatically adjusts when the marker's content or size changes.
	 * @default true
	 */
	dynamicResize: z.boolean().default(true),
	/**
	 * Whether the marker is enabled.
	 * @default true
	 */
	enabled: z.boolean().default(true),
	/**
	 * The z-index of the marker.
	 */
	zIndex: z.number().optional(),
	/** Configuration for the low priority pin fallback strategy. */
	lowPriorityPin: z
		.object({
			/** Whether to enable low priority pin strategy when all anchor positions have collisions. */
			enabled: z.boolean().optional(),
			/** Size of the low priority pin in pixels. */
			size: z.number().positive().optional(),
			/** Color of the low priority pin. */
			color: z.string().optional(),
		})
		.optional(),
	/** HTML element for the marker. */
	element: z.custom<HTMLElement>(val => val instanceof HTMLElement, { error: 'Must be an HTMLElement instance' }),
	/** HTML content of the marker as text */
	contentHTML: z.string(),
});

export const markerStateSchemaPartial = markerStateSchema
	.omit({
		element: true,
	})
	.partial();

export type TMarkerState = z.infer<typeof markerStateSchema>;
export type TMarkerUpdateState = z.infer<typeof markerStateSchemaPartial>;

export function getMarkerState(state: MarkerState, marker?: Marker): TMarkerState {
	// we want to use the coordinate that the marker was created with
	let coordinate = marker?.target ? marker.target.anchorTarget : undefined;

	const { enabled, placement, element, dynamicResize, contentHTML, options, position, parent } = state;
	const parentId = typeof parent === 'object' ? parent.id : parent;

	if (coordinate == null) {
		coordinate = new Coordinate({
			latitude: position?.[1] ?? 0,
			longitude: position?.[0] ?? 0,
			floorId: parentId?.toString(),
		});
	}

	return {
		type: MARKER_STATE_TYPE,
		rank: (options?.rank as TCollisionRankingTier) ?? 'medium',
		interactive: options?.interactive ?? false,
		enabled: enabled ?? true,
		element: element,
		placement: placement ?? [],
		zIndex: options?.zIndex,
		dynamicResize: dynamicResize ?? true,
		lowPriorityPin: options?.lowPriorityPin,
		contentHTML: contentHTML ?? '',
	} satisfies Complete<TMarkerState>;
}

export function translateMarkerStateToGeojsonCore(
	state: TMarkerUpdateState,
	currentState: Partial<MarkerState>,
): Partial<MarkerState> {
	const update = {} as Partial<MarkerState>;
	update.options = {} as MarkerState['options'];

	if (state.rank != null) {
		if (state.rank === 'initial') {
			update.options.rank = currentState.initialRank || 2;
		} else {
			update.options.rank = state.rank;
		}
	}
	if (state.dynamicResize != null) {
		update.options.dynamicResize = state.dynamicResize;
	}
	if (state.enabled != null) {
		update.enabled = state.enabled;
	}
	if (state.interactive != null) {
		update.options.interactive = state.interactive;
	}
	if (state.enabled !== null) {
		update.enabled = state.enabled;
	}
	if (state.contentHTML !== undefined) {
		update.contentHTML = state.contentHTML;
	}
	if (state.placement != null) {
		update.placement = state.placement;
	}

	return update;
}
