import type Core from '../renderer';

/**
 * Default debug state configuration with all debug features disabled by default.
 */
const DEFAULT_DEBUG_STATE = {
	/** Shows the text area mesh used for polygon label rendering */
	showPolygonLabelTextAreaMesh: false,
	/** Enables the basic environment map for lighting and reflections */
	showEnvMap: false,
	/** Shows collision detection boxes for debugging spatial interactions */
	showCollisionBoxes: false,
};

/**
 * Type definition for the debug state configuration.
 * Represents all available debug options that can be toggled.
 */
export type DebugState = typeof DEFAULT_DEBUG_STATE;

/**
 * Debug utility class for toggling various debug visualization features.
 *
 * This class provides a centralized way to control debug features like:
 * - Text area mesh visualization for polygon labels
 * - Environment map rendering
 * - Collision box visualization
 *
 * @example
 * ```typescript
 * const debug = new Debug(core);
 *
 * // Enable collision boxes and environment map
 * debug.update({
 *   showCollisionBoxes: true,
 *   showEnvMap: true
 * });
 *
 * // Disable all debug features
 * debug.update({
 *   showCollisionBoxes: false,
 *   showEnvMap: false,
 *   showPolygonLabelTextAreaMesh: false
 * });
 * ```
 */
export class Debug {
	/** Current debug state configuration */
	state = DEFAULT_DEBUG_STATE;
	/** Reference to the core renderer instance */
	core: Core;

	/**
	 * Creates a new Debug instance.
	 *
	 * @param core - The core renderer instance to control debug features for
	 */
	constructor(core: Core) {
		this.core = core;
	}

	/**
	 * Updates the debug state with new configuration options.
	 * Only changed values will trigger updates to the corresponding systems.
	 *
	 * @param updateState - Partial debug state with the options to update
	 *
	 * @example
	 * ```typescript
	 * // Enable only collision boxes
	 * debug.update({ showCollisionBoxes: true });
	 *
	 * // Enable multiple features at once
	 * debug.update({
	 *   showEnvMap: true,
	 *   showPolygonLabelTextAreaMesh: true
	 * });
	 * ```
	 */
	update(updateState: Partial<DebugState>) {
		const diff: Partial<DebugState> = {};
		for (const [key, value] of Object.entries(updateState)) {
			if (key in this.state && this.state[key] !== updateState[key]) {
				this.state[key as keyof DebugState] = value;
				diff[key] = value;
			}
		}
		this.handleUpdate(diff);
	}

	/**
	 * Handles the actual system updates based on changed debug state values.
	 * This method applies the debug state changes to the appropriate renderer systems.
	 *
	 * @param diff - Object containing only the debug state properties that changed
	 * @private
	 */
	private handleUpdate(diff: Partial<DebugState>) {
		// Toggle text area mesh visibility for polygon labels
		if (diff.showPolygonLabelTextAreaMesh != null) {
			if (diff.showPolygonLabelTextAreaMesh) {
				this.core.getSystems().text3DSystem.showTextAreaMesh();
			} else {
				this.core.getSystems().text3DSystem.hideTextAreaMesh();
			}
		}

		// Toggle environment map rendering
		if (diff.showEnvMap != null) {
			if (diff.showEnvMap) {
				this.core.setEnvironment('basic');
			} else {
				this.core.setEnvironment(false);
			}
		}

		// Toggle collision box visualization
		if (diff.showCollisionBoxes != null) {
			if (diff.showCollisionBoxes) {
				this.core.getSystems().collisionSystem.showCollisionBoxes();
				this.core.render(); // needed to re-render the debug dom
			} else {
				this.core.getSystems().collisionSystem.hideCollisionBoxes();
			}
		}
	}
}
