import type { GeometryFeature, GeometryId } from '@mappedin/mvf-core';
import type { NavigationFlagDeclarations } from '@mappedin/mvf-navigation-flags';
import type { NodeFeature, NodeId, NodeNeighbor } from '@mappedin/mvf-nodes';
import { type GeometryMaps, createGeometryMaps } from './converterUtils.js';
import type { MVFv2_STANDARD_MVFv3 } from './index.js';

export type BuilderOptions = {
	geometryMaps: GeometryMaps;
	nodesByGeometryId: Map<GeometryId, NodeId[]>;
	geometryFeatureById: Map<GeometryId, GeometryFeature>;
	navigationFlags: NavigationFlagDeclarations;
	incomingEdgesByNodeId: Map<NodeId, Array<NodeNeighbor>>;
	nodesById: Map<NodeId, NodeFeature>;
};
export function buildBuilderOptions(v3: Partial<MVFv2_STANDARD_MVFv3>): BuilderOptions {
	const { nodesByGeometryId, incomingEdgesByNodeId, nodesById } = buildNodeMaps(v3);
	const geometryMaps = createGeometryMaps(v3);
	const geometryFeatureById = buildGeometryFeatureById(v3);
	const navigationFlags = v3.navigationFlags ?? {};
	const options: BuilderOptions = {
		geometryMaps,
		nodesByGeometryId,
		geometryFeatureById,
		navigationFlags,
		incomingEdgesByNodeId,
		nodesById,
	};

	return options;
}

export function buildNodeMaps(v3: Pick<MVFv2_STANDARD_MVFv3, 'nodes'>): {
	nodesByGeometryId: Map<GeometryId, NodeId[]>;
	incomingEdgesByNodeId: Map<NodeId, Array<NodeNeighbor>>;
	nodesById: Map<NodeId, NodeFeature>;
} {
	const nodesByGeometryId: Map<GeometryId, NodeId[]> = new Map();
	const incomingEdgesByNodeId: Map<NodeId, Array<NodeNeighbor>> = new Map();
	const nodesById: Map<NodeId, NodeFeature> = new Map();
	for (const floorNodes of Object.values(v3.nodes ?? {})) {
		for (const node of floorNodes?.features ?? []) {
			nodesById.set(node.properties.id, node);
			if (node.properties.geometryIds.length > 0) {
				for (const geometryId of node.properties.geometryIds) {
					const existingNodes = nodesByGeometryId.get(geometryId) ?? [];
					existingNodes.push(node.properties.id);
					nodesByGeometryId.set(geometryId, existingNodes);
				}
			}
			if (node.properties.neighbors.length > 0) {
				for (const neighbor of node.properties.neighbors) {
					const existing = incomingEdgesByNodeId.get(neighbor.id);
					if (existing != null) {
						existing.push(neighbor);
					} else {
						incomingEdgesByNodeId.set(neighbor.id, [neighbor]);
					}
				}
			}
		}
	}
	return { nodesByGeometryId, incomingEdgesByNodeId, nodesById };
}

export function buildGeometryFeatureById(
	v3: Partial<Pick<MVFv2_STANDARD_MVFv3, 'geometry'>>,
): Map<GeometryId, GeometryFeature> {
	const geometryFeatureById: Map<GeometryId, GeometryFeature> = new Map();
	for (const floorGeometry of Object.values(v3.geometry ?? {})) {
		for (const geometry of floorGeometry?.features ?? []) {
			geometryFeatureById.set(geometry.properties.id, geometry);
		}
	}
	return geometryFeatureById;
}
