import type { Position } from '@mappedin/core-sdk';
import type { Coordinate } from '../map-data-objects';
import { equirectangularDistance } from '@packages/internal/common/utils';

/**
 * Place to put random utility functions
 */
export function getId<T extends { id: string }>(item: string | T): string {
	return typeof item === 'string' ? item : item.id;
}

export function getMidpoint(lonLat1: [number, number], lonLat2: [number, number]): [number, number] {
	const [lon1, lat1] = lonLat1;
	const [lon2, lat2] = lonLat2;

	const midLon = (lon1 + lon2) / 2;
	const midLat = (lat1 + lat2) / 2;

	return [midLon, midLat];
}

export function isPosition(thing: unknown): thing is Position {
	return Array.isArray(thing) && Number.isFinite(thing[0]) && Number.isFinite(thing[1]);
}

const PRECISION = 8; // This is on the scale of a 1mm, and lets us account for floating point precision issues

export const serializeCoords = (coordinates: number | Position | Position[] | Position[][]): string => {
	if (typeof coordinates === 'number') {
		return coordinates.toFixed(PRECISION);
	}

	if (isPosition(coordinates)) {
		return coordinates[1].toFixed(PRECISION) + '-' + coordinates[0].toFixed(PRECISION);
	}

	return coordinates.map(coord => serializeCoords(coord)).join('-');
};

const PROD_PUBLIC_CDN_REGEX = /^https:\/\/cdn\.mappedin\.com\/category-symbols\/.+$/;
const STAGING_PUBLIC_CDN_REGEX = /^https:\/\/cdn-staging\.mappedin\.net\/category-symbols\/.+$/;
export const isPublicCdnAsset = (url: string): boolean => {
	return PROD_PUBLIC_CDN_REGEX.test(url) || STAGING_PUBLIC_CDN_REGEX.test(url);
};

const PROD_PRIV_CDN_REGEX = /^https:\/\/cdn\.mappedin\.com\/[^/]+\/[^/]+\.[a-zA-Z0-9]+$/;
const STAGING_PRIV_CDN_REGEX = /^https:\/\/cdn-staging\.mappedin\.net\/[^/]+\/[^/]+\.[a-zA-Z0-9]+$/;
export const isPrivateCdnAsset = (url: string): boolean => {
	if (isPublicCdnAsset(url)) {
		return false;
	}

	return PROD_PRIV_CDN_REGEX.test(url) || STAGING_PRIV_CDN_REGEX.test(url);
};

export const parseCdnAssetUrl = (
	url: string,
): {
	origin: string;
	mapId: string;
	fileName: string;
	fileType: string;
} | null => {
	if (!isPrivateCdnAsset(url)) {
		return null;
	}

	let protocol = '';
	if (url.startsWith('https://')) {
		protocol = 'https://';
		url = url.slice(8);
	} else if (url.startsWith('http://')) {
		protocol = 'http://';
		url = url.slice(7);
	}

	const [origin, mapId, fileName] = url.split('/');
	const fileType = fileName.split('.').pop() ?? '';

	return { origin: protocol + origin, mapId, fileName, fileType };
};

/**
 * Computes the total distance between a list of coordinates using the equirectangular distance formula.
 * This is a fast approximation of the distance, but it is not as accurate as the haversine distance formula.
 */
export function computeTotalDistanceBetweenCoordinates(coordinates: Coordinate[]) {
	return coordinates.reduce((acc, c, index) => {
		if (index === 0) {
			return acc;
		}
		return acc + equirectangularDistance(coordinates[index - 1].toGeoJSON().coordinates, c.toGeoJSON().coordinates);
	}, 0);
}
