import { Vector2 } from 'three';
export const differenceBetweenAngles = (a: number, b: number): number => {
	return ((b - a + Math.PI) % (Math.PI * 2)) + ((Math.PI * 2) % (Math.PI * 2)) - Math.PI;
};

export const getTouchPos = (touch: Touch, origin: { offsetLeft: number; offsetTop: number }): Vector2 => {
	return new Vector2(touch.clientX - origin.offsetLeft, touch.clientY - origin.offsetTop);
};

export const angle = (first: Vector2, second: Vector2): number => {
	return Math.atan2(second.y - first.y, second.x - first.x);
};

export const distance = (first: Vector2, second: Vector2): number => {
	return Math.sqrt((second.y - first.y) ** 2 + (second.x - first.x) ** 2);
};

export const average = (first: Vector2, second: Vector2): Vector2 => {
	return new Vector2((first.x + second.x) / 2, (first.y + second.y) / 2);
};

export const getScrollDelta = (event: MouseEvent | WheelEvent): number => {
	let delta = 0;
	// the wheelDelta property isn't defined in the MouseEvent interface, it's deprecated
	const { wheelDelta, detail } = event as any;

	if (wheelDelta !== undefined) {
		delta = wheelDelta;
	} else if (detail !== undefined) {
		delta = -detail;
	}

	return delta;
};
