import { isBrowser, Logger, PubSub } from '../../common';
import { ACCESS_TOKEN_REFRESH_BUFFER_MS, getAccessToken } from './access-token';
import { SAS_TOKEN_REFRESH_BUFFER_MS, getSasToken } from './sas-token';

const TWO_HOURS_MS = 1000 * 60 * 60 * 2;

type TokenWithExpiration = {
	token: string;
	expires?: number;
};

type TokenManagerOptions = {
	baseUri?: string;
	baseAuthUri?: string;
	customAuthUri?: string;
	key?: string;
	secret?: string;
	accessToken?: string;
	enterprise?: boolean;
};

type TokenManagerEvents = {
	'access-token-refreshed': {
		accessToken: TokenWithExpiration;
	};
	'sas-tokens-refreshed': {
		sasTokens: Record<string, TokenWithExpiration>;
	};
};

type TokenManagerPubSub = PubSub<TokenManagerEvents>;

/**
 * Class for retrieving and refreshing tokens.
 */
export class TokenManager {
	#pubsub: TokenManagerPubSub = new PubSub();
	#mapIds: string[];
	#options: TokenManagerOptions;

	// Dedupe requests by saving promises
	#accessTokenPromise: Promise<TokenWithExpiration> | undefined;
	#sasTokensPromise: Promise<{ tokens: Record<string, TokenWithExpiration>; expires: number }> | undefined;

	// Save results to allow synchronous access
	#accessToken: TokenWithExpiration | undefined;
	#sasTokens: { tokens: Record<string, TokenWithExpiration>; expires: number } | undefined;

	// Refresh tokens on an interval
	#accessTokenRefresh: ReturnType<typeof setInterval> | undefined;
	#sasTokensRefresh: ReturnType<typeof setInterval> | undefined;

	constructor(mapIds: string[], options: TokenManagerOptions) {
		this.#mapIds = mapIds;
		this.#options = options;
	}

	#handleWindowFocus = () => {
		if ('key' in this.#options && 'secret' in this.#options) {
			this.#refreshAccessToken();
		}
		this.#refreshSasTokens();
	};

	#setAccessToken(accessToken: TokenWithExpiration) {
		const isDifferent = accessToken.token !== this.#accessToken?.token;
		this.#accessToken = accessToken;
		if (isDifferent) {
			this.#pubsub.publish('access-token-refreshed', { accessToken });
		}
	}

	#setSasTokens(sasTokens: { tokens: Record<string, TokenWithExpiration>; expires: number }) {
		const isDifferent = JSON.stringify(sasTokens.tokens) !== JSON.stringify(this.#sasTokens?.tokens);
		this.#sasTokens = sasTokens;
		if (isDifferent) {
			this.#pubsub.publish('sas-tokens-refreshed', { sasTokens: sasTokens.tokens });
		}
	}

	async #refreshAccessToken(forceRefresh = false) {
		if ('accessToken' in this.#options && typeof this.#options.accessToken === 'string') {
			const accessToken = {
				token: this.#options.accessToken,
				// We don't actually know the expiry so we assume something short, like 2 hours
				expires: Date.now() + 1000 * 60 * 60 * 2,
			};
			this.#accessToken = accessToken;

			return accessToken;
		}

		if (
			!('key' in this.#options) ||
			!('secret' in this.#options) ||
			typeof this.#options.key !== 'string' ||
			typeof this.#options.secret !== 'string'
		) {
			throw new Error('No API keys or access token provided!');
		}

		if (this.#accessTokenPromise != null) {
			return this.#accessTokenPromise;
		}

		if (
			this.#accessToken != null &&
			this.#accessToken.expires != null &&
			this.#accessToken.expires > Date.now() &&
			!forceRefresh
		) {
			return this.#accessToken;
		}

		this.#accessTokenPromise = getAccessToken(
			{
				...this.#options,
				key: this.#options.key,
				secret: this.#options.secret,
			},
			this.#options.enterprise ?? false,
		)
			.then(token => {
				const accessToken = {
					token: token.accessToken,
					// Access token expiry is in seconds
					expires: token.expiresTimestamp * 1000,
				};
				this.#setAccessToken(accessToken);

				return accessToken;
			})
			.catch(error => {
				throw error; // Re-throw for proper error propagation
			})
			.finally(() => {
				// Clear the promise after it has resolved or rejected so we don't return expired token promises
				this.#accessTokenPromise = undefined;
			});

		return this.#accessTokenPromise;
	}

	async #refreshSasTokens(forceRefresh = false) {
		if (this.isEnterpriseMode()) {
			return {
				tokens: {},
				expires: Infinity,
			};
		}

		if (this.#sasTokensPromise != null) {
			return this.#sasTokensPromise;
		}

		if (this.#sasTokens != null && this.#sasTokens.expires > Date.now() && !forceRefresh) {
			return this.#sasTokens;
		}

		this.#sasTokensPromise = new Promise((resolve, reject) => {
			this.#refreshAccessToken()
				.then(accessToken => {
					const sasTokens: Record<string, TokenWithExpiration> = {};
					const sasTokenPromises = this.#mapIds.map(mapId =>
						getSasToken({ ...this.#options, mapId }, accessToken.token)
							.then(token => {
								sasTokens[mapId] = {
									token: token.token,
									expires: token.expires,
								};
							})
							.catch(error => {
								Logger.error(`Error getting SAS token for map ${mapId}:`, error);
							}),
					);
					Promise.all(sasTokenPromises).then(() => {
						const res = {
							tokens: sasTokens,
							expires: Math.min(...Object.values(sasTokens).map(token => token.expires ?? Infinity)),
						};
						this.#setSasTokens(res);
						resolve(res);
					});
				})
				.catch(error => {
					reject(error);
				});
		});

		// Clear the promise after it has resolved so we don't return expired token promises
		this.#sasTokensPromise
			.catch(error => {
				throw error;
			})
			.finally(() => {
				this.#sasTokensPromise = undefined;
			});

		return this.#sasTokensPromise;
	}

	#setAccessTokenRefresh(token: TokenWithExpiration) {
		// If we don't have API keys, we can't refresh the access token
		if (!('key' in this.#options) || !('secret' in this.#options)) {
			return;
		}

		const expiresIn = token.expires != null ? token.expires - Date.now() : TWO_HOURS_MS;
		if (isFinite(expiresIn)) {
			this.#accessTokenRefresh = setTimeout(() => {
				this.#refreshAccessToken(true).then(token => {
					this.#setAccessTokenRefresh(token);
				});
			}, expiresIn - ACCESS_TOKEN_REFRESH_BUFFER_MS);
		}
	}

	#setSasTokensRefresh(tokens: { tokens: Record<string, TokenWithExpiration>; expires: number }) {
		const expiresIn = tokens.expires - Date.now();
		if (isFinite(expiresIn)) {
			this.#sasTokensRefresh = setTimeout(() => {
				this.#refreshSasTokens(true).then(tokens => {
					this.#setSasTokensRefresh(tokens);
				});
			}, expiresIn - SAS_TOKEN_REFRESH_BUFFER_MS);
		}
	}

	#stopRefreshing() {
		if (this.#accessTokenRefresh != null) {
			clearTimeout(this.#accessTokenRefresh);
		}
		if (this.#sasTokensRefresh != null) {
			clearTimeout(this.#sasTokensRefresh);
		}
	}

	/**
	 * Set the map IDs that the token manager will manage.
	 */
	setMapIds(mapIds: string[]) {
		this.#mapIds = mapIds;
	}

	/**
	 * Set the enterprise flag.
	 */
	setEnterprise(enterprise: boolean) {
		this.#options.enterprise = enterprise;
	}

	/**
	 * Preload tokens so they can be retrieved synchronously. This will also refresh tokens on an
	 * interval so they remain valid synchronously.
	 */
	async readySync() {
		if ((!('key' in this.#options) || !('secret' in this.#options)) && !('accessToken' in this.#options)) {
			Logger.error('No API keys or access token provided! Cannot preload tokens.');

			return;
		}

		const promises: [
			Promise<TokenWithExpiration>,
			Promise<{ tokens: Record<string, TokenWithExpiration>; expires: number }> | null,
		] = [this.#refreshAccessToken(), null];
		if (!this.#options.enterprise) {
			promises[1] = this.getSasTokensAsync();
		}
		const [accessToken, sasTokens] = await Promise.all(promises);

		// Refresh tokens before they expire so they are always synchronously available
		this.#stopRefreshing();
		this.#setAccessTokenRefresh(accessToken);
		if (sasTokens != null) {
			this.#setSasTokensRefresh(sasTokens);
		}

		if (isBrowser()) {
			globalThis.addEventListener?.('focus', this.#handleWindowFocus);
		}
	}

	/**
	 * Get a promise that resolves when the tokens are refreshed, but does not trigger token
	 * refreshing.
	 */
	async refreshing() {
		const promises: Promise<unknown>[] = [];
		if (this.#accessTokenPromise != null) {
			promises.push(this.#accessTokenPromise);
		}
		if (this.#sasTokensPromise != null) {
			promises.push(this.#sasTokensPromise);
		}

		return Promise.all(promises);
	}

	/**
	 * Get an access token for the current set of API keys. If an access token was passed in, that
	 * will be returned instead.
	 */
	getAccessTokenAsync() {
		return this.#refreshAccessToken();
	}

	/**
	 * Get SAS tokens for all maps. If any SAS token are expired, all SAS tokens will be refreshed.
	 */
	getSasTokensAsync() {
		return this.#refreshSasTokens();
	}

	/**
	 * Get an access token synchronously. `readySync()` must be called first.
	 */
	getAccessToken(): TokenWithExpiration {
		if (this.#accessToken == null) {
			Logger.error('Tokens must be loaded before reading synchronously! Try awaiting readySync() first.');

			return {
				token: '',
			};
		}

		return this.#accessToken;
	}

	/**
	 * Get a SAS token for a specific map synchronously. `readySync()` must be called first.
	 */
	getSasToken(mapId: string): TokenWithExpiration {
		if (this.isEnterpriseMode()) {
			Logger.error('SAS tokens are not available in the enterprise mode!');

			return {
				token: '',
			};
		}

		if (this.#sasTokens == null) {
			Logger.error(`Tokens must be loaded before reading synchronously! Try awaiting readySync() first.`);

			return {
				token: '',
			};
		}

		if (!(mapId in this.#sasTokens.tokens)) {
			Logger.error(`SAS token for map ${mapId} not found!`);

			return {
				token: '',
			};
		}

		return this.#sasTokens.tokens[mapId];
	}

	/**
	 * Check if the token manager is in enterprise mode.
	 */
	isEnterpriseMode(): boolean {
		return this.#options.enterprise ?? false;
	}

	/**
	 * Subscribe to events emitted by the token manager.
	 */
	on: TokenManagerPubSub['on'] = (eventName, fn) => {
		return this.#pubsub.on(eventName, fn);
	};

	/**
	 * Unsubscribe from events emitted by the token manager.
	 */
	off: TokenManagerPubSub['off'] = (eventName, fn) => {
		return this.#pubsub.off(eventName, fn);
	};

	/**
	 * Clean up the token manager.
	 */
	destroy() {
		this.#stopRefreshing();
		this.#pubsub.destroy();
		if (isBrowser()) {
			globalThis.removeEventListener('focus', this.#handleWindowFocus);
		}
	}
}
