import { Geometry3DComponents } from '../../entities';
import type { EntityTypes, RendererState } from '../../renderer';
import type { RendererCore } from '../..';
import { buildPath } from './utils';
import { destroyObject3D } from '../../utils/object3d-destroy';
import { getInterpolationBreakpoint, Logger, PubSub } from '@packages/internal/common';
import type { ShaderMaterial, Vector3 } from 'three';
import type { GroupContainerObject3D } from '../../entities/group-container';
import { PathComponent } from '../../components/path';
import type { CameraSystem } from '../camera';
import { coordinatesEqual } from '../../utils';

const VERTICAL_THRESHOLD_METERS = 0.5;

/**
 * A system for drawing and controlling the visual state of paths
 */
export class PathSystem extends PubSub<{
	'animate:path': undefined;
}> {
	private rendererState: RendererState;
	verticalDistanceDirty = true;

	convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'];
	getParentContainer: RendererCore['getParentContainer'];
	convertZoomLevelToAltitude: CameraSystem['convertZoomLevelToAltitude'];
	constructor(
		rendererState: RendererState,
		convertTo3DMapPosition: RendererCore['convertTo3DMapPosition'],
		getParentContainer: RendererCore['getParentContainer'],
		convertZoomLevelToAltitude: CameraSystem['convertZoomLevelToAltitude'],
	) {
		super();
		this.rendererState = rendererState;
		this.convertTo3DMapPosition = convertTo3DMapPosition;
		this.getParentContainer = getParentContainer;
		this.convertZoomLevelToAltitude = convertZoomLevelToAltitude;
	}

	update(minZoomAltitude: number, maxZoomAltitude: number, currentZoomAltitude: number) {
		let entity: EntityTypes | undefined;
		let shouldAnimate = false;
		// this is run once per geometry group, to generate an path from geojson points
		for (const entityId of this.rendererState.geometry3DIdsInScene) {
			entity = this.rendererState.geometry3DMap.get(entityId);
			if (entity && entity.type === 'path' && entity.components[Geometry3DComponents.Mesh].type === 'path') {
				const component = entity.components[Geometry3DComponents.Mesh];
				const style = entity.components[Geometry3DComponents.Style];
				const componentVerticalDistanceDirty = component.isVertical && this.verticalDistanceDirty;

				if (!(component instanceof PathComponent)) continue;

				let points: Vector3[] = [];

				if (component.dirty || style.dirty || componentVerticalDistanceDirty) {
					if (component.mesh.parent != null) {
						for (let i = component.mesh.children.length - 1; i >= 0; i--) {
							destroyObject3D(component.mesh.children[0]);
						}
					}

					// vertical paths must have two points with parentIds
					if (
						component.feature.features.length === 2 &&
						component.feature.features[0].properties.parentId &&
						component.feature.features[1].properties.parentId
					) {
						const fromGeoJSONPoint = component.feature.features[0];
						const toGeoJSONPoint = component.feature.features[component.feature.features.length - 1];
						let fromParent: GroupContainerObject3D | undefined;
						let toParent: GroupContainerObject3D | undefined;
						let fromAltitude = fromGeoJSONPoint.geometry.coordinates[2] || 0;
						let toAltitude = toGeoJSONPoint.geometry.coordinates[2] || 0;

						if (fromGeoJSONPoint.properties.parentId) {
							fromParent = this.getParentContainer(
								fromGeoJSONPoint.properties.parentId,
								true,
							) as GroupContainerObject3D;
							if (fromParent != null && fromParent.altitude != null) {
								fromAltitude += fromParent.altitude;
								component.isVertical = true;
							}
						}

						if (toGeoJSONPoint.properties.parentId) {
							toParent = this.getParentContainer(toGeoJSONPoint.properties.parentId, true) as GroupContainerObject3D;
							if (toParent != null && toParent.altitude != null) {
								toAltitude += toParent.altitude;
								component.isVertical = true;
							}
						}

						const fromPosition = this.convertTo3DMapPosition(
							fromGeoJSONPoint.geometry.coordinates[1],
							fromGeoJSONPoint.geometry.coordinates[0],
							fromAltitude,
						)!;

						const toPosition = this.convertTo3DMapPosition(
							toGeoJSONPoint.geometry.coordinates[1],
							toGeoJSONPoint.geometry.coordinates[0],
							toAltitude,
						)!;

						const verticalDistance = Math.abs(fromPosition.z - toPosition.z);

						if (
							verticalDistance > VERTICAL_THRESHOLD_METERS &&
							fromParent != null &&
							fromParent.visible &&
							toParent != null &&
							toParent.visible
						) {
							points = [fromPosition, toPosition];
						}
					} else {
						// otherwise we have a multi-point horizontal path
						const geojsonPoints = component.feature.features;

						for (let i = 0; i < geojsonPoints.length; i++) {
							// skip coordinates that are the same as the previous one
							// this can happen in v6-compat when converting from v5 path to v6
							// and it will cause the TubeGeometry to implode
							if (
								i === 0 ||
								!coordinatesEqual(geojsonPoints[i].geometry.coordinates, geojsonPoints[i - 1].geometry.coordinates)
							) {
								const convertedPoint = this.convertTo3DMapPosition(
									geojsonPoints[i].geometry.coordinates[1],
									geojsonPoints[i].geometry.coordinates[0],
									geojsonPoints[i].geometry.coordinates[2] || 0,
								)!;
								points.push(convertedPoint);
							}
						}
					}

					if (points.length > 1) {
						const { mesh, material, center } = buildPath(points, component, {
							color: style.color,
							altitude: style.altitude,
							isVertical: component.isVertical,
						});
						component.mesh = mesh;
						component.material = material;
						component.position = center;
					}

					if (component.dirty) {
						if (typeof component.width === 'object' && component.width.on === 'zoom-level') {
							component.altitudeBreakpoints = component.width.input.map(zoomLevel =>
								this.convertZoomLevelToAltitude(zoomLevel),
							);
						}
					}
					component.dirty = false;
				}

				if (style.dirty) {
					component.mesh.children.forEach(child => {
						if (child.name === 'stencil') {
							return;
						}
						child.material.uniforms.color.value.set(style.color);
						child.material.visible = style.visible;
						child.material.needsUpdate = true;
					});
					component.mesh.position.z = style.altitude;
					style.dirty = false;
				}

				if (component.material) {
					component.mesh.children.forEach(child => {
						const uniforms = child.material.uniforms;
						if (child.name === 'stencil') {
							if (component.materialDirty) {
								updateHighlightPathUniforms(component, child.material);
							}
							return;
						}
						// these have to happen every frame
						if (typeof component.width === 'object' && component.width.on === 'zoom-level') {
							if (component.altitudeBreakpoints && component.altitudeBreakpoints.length > 2) {
								/**
								 * Ben Liu, 2025-07-17:
								 *
								 * For some reason, adding extra interpolations causes the path with the jump absuptly. I suspect it has
								 * something to do with creating the path geometry with an initial set of breakpoints and then changing the
								 * breakpoints later, but I'm not totally sure.
								 *
								 * For now, I don't think many people will want multiple breakpoints for the path, and we need to change the
								 * API before stabilizing V6, so I'm going to leave this warning.
								 *
								 * TODO: Figure out why this isn't working.
								 */
								Logger.warn(
									`Path width interpolation does not yet support more than 2 breakpoints! This will be added in a future version. Path width will only use the first two breakpoints.`,
								);
							}
							const breakpoint = getInterpolationBreakpoint(
								currentZoomAltitude,
								component.altitudeBreakpoints?.slice(0, 2) ?? [minZoomAltitude, maxZoomAltitude],
							);
							component.breakpoint = breakpoint;

							uniforms.nearZoom.value = component.altitudeBreakpoints?.[breakpoint] ?? minZoomAltitude;
							uniforms.farZoom.value = component.altitudeBreakpoints?.[breakpoint + 1] ?? maxZoomAltitude;
							uniforms.nearRadius.value = Number(component.width.output[breakpoint]) / 2;
							uniforms.farRadius.value =
								Number(component.width.output[breakpoint + 1] ?? component.width.output[breakpoint]) / 2;
						} else {
							// If the width is static, it only needs to be updated when the state changes
							// See below
						}
						uniforms.cameraParameters.value.x = currentZoomAltitude;
						// and these only when we make a change to the path state
						if (component.materialDirty) {
							uniforms.displayArrowsOnPath.value = component.displayArrowsOnPath;
							uniforms.pulseColor.value.set(component.accentColor);
							uniforms.complete.value = component.completeFraction;
							updateHighlightPathUniforms(component, child.material);

							if (typeof component.width === 'number') {
								uniforms.nearZoom.value = minZoomAltitude;
								uniforms.farZoom.value = maxZoomAltitude;
								uniforms.nearRadius.value = Number(component.width) / 2;
								uniforms.farRadius.value = Number(component.width) / 2;
							} else {
								// If the width is an interpolation, we potentially need to update the uniforms every frame
								// See above
							}
						}
					});

					if (component.displayArrowsOnPath && component.animateArrowsOnPath) {
						component.mesh.children.forEach(child => {
							if (child.name === 'stencil') {
								return;
							}
							child.material.uniforms.arrowAnimationTimer.value = performance.now() / 3000;
							child.material.needsUpdate = true;
						});
						shouldAnimate = true;
					}
					component.materialDirty = false;
				}
			}
		}
		this.verticalDistanceDirty = false;
		if (shouldAnimate) {
			this.publish('animate:path');
		}
	}
}

function updateHighlightPathUniforms(component: PathComponent, material: ShaderMaterial) {
	const uniforms = material.uniforms;
	uniforms.highlightCompleteFraction.value = component.highlightCompleteFraction;
	uniforms.highlightStartFraction.value = component.highlightStartFraction;
	uniforms.highlightEndFraction.value = component.highlightEndFraction;
	uniforms.highlightWidthMultiplier.value = component.highlightWidthMultiplier;
	uniforms.highlightColor.value.set(component.highlightColor);
}
