import type { ParsedMVF, PolygonStyle } from '@mappedin/mvf-v2';
import type { FeatureCollection, LineString, MultiPolygon, Polygon, Feature, Point } from 'geojson';
import { Logger, PubSub } from '@packages/internal/common';
import {
	type Position,
	type AddLabelOptionsInternal,
	type RendererCore,
	type PathState,
	type GroupContainerState,
	type MarkerState,
	type LabelAppearance,
	type LineStyle,
	type PaintStyle,
	type EntityId,
	type CollisionRankingTier,
	type InitializeModelState,
	type Text3DState,
	ANIMATION_TWEENS,
} from '@mappedin/core-sdk';
import { FloorObject } from './floor-object';
import type { TAddMarkerOptions, TAddPathOptions, TShow3DMapOptions } from '..';
import type {
	GLTFExportOptions,
	TAnimationOptions,
	TAddModelOptions,
	TAddImageOptions,
	TAddText3DOptions,
	TCollisionRankingTier,
	IAnchorable,
	TAddLabelOptions,
	TMultiFloorViewOptions,
} from '../types';
import {
	type AggregatedStyleMap,
	convertCoordinateToPosition,
	shouldAnimatePathDrawing,
	extractStyleMap,
	getAttachableGeometryId,
	getTargetFloorObject,
	tweenToPromise,
	computeFloorAltitudes,
	parseFloorGap,
} from './utils';
import type { TFloorChangeReason } from '../events';
import { getId, randomId } from '../utils';
import type { Shape } from '../map-view-objects';
import type { MapDataInternal } from '../map-data-objects';
import { Space, Coordinate } from '../map-data-objects';
import { type GeoJsonApi } from './api';
import { Tween } from '@tweenjs/tween.js';
import { FloorStackObject } from './floor-stack-object';
import { getMultiFloorState } from './multi-floor';
import { LABELS_LAYER, MARKERS_LAYER, PATHS_LAYER } from '../constants';
import { MappedinError } from '@packages/internal/common/errors';

export type InternalPath = {
	paths: (EntityId<PathState> & {
		coordinates: Coordinate[];
	})[];
	animation: Promise<void>;
};

export class GeojsonApiMapObject extends PubSub<{
	'floor-change': { reason?: TFloorChangeReason; floorId: string; previousFloorId: string };
	'floor-change-start': { reason?: TFloorChangeReason; floorId: string; previousFloorId: string };
}> {
	floorStacksById: Map<string, FloorStackObject> = new Map();
	floorsById: Map<string, FloorObject> = new Map();
	startingFloorId = '';
	currentFloorId = '';
	currentFloorStackId = '';
	outdoorFloorStacks: Set<string> = new Set();
	id: string;
	renderer: RendererCore;
	api: GeoJsonApi;
	mvf: ParsedMVF;
	options: TShow3DMapOptions = {};
	styleMap: AggregatedStyleMap;
	private animations: Map<string, Tween> = new Map();
	private floorStackAltitudeMap: Map<string, Map<number, { altitude: number; effectiveHeight: number }>> = new Map();

	get currentFloorStack() {
		const floorStack = this.floorStacksById.get(this.currentFloorStackId);
		if (!floorStack) {
			throw new MappedinError(`Current floor stack with id "${this.currentFloorStackId}" not found`);
		}

		return floorStack!;
	}

	setFloorStack(floorStackId: string, reason?: TFloorChangeReason) {
		if (this.currentFloorStackId === floorStackId) {
			return;
		}
		const floorStack = this.floorStacksById.get(floorStackId);
		if (floorStack == null) {
			Logger.error(
				`Invalid floor stack passed to setFloorStack (${floorStackId}), the floor stack was not changed. Current floor stack is still ${this.currentFloorStack.id}.`,
			);

			return;
		}

		if (floorStack.floorObjects.length === 0) {
			Logger.error(
				`Floor stack (${floorStackId}) has no floors. The floor stack was not changed. Current floor stack is still ${this.currentFloorStack.id}.`,
			);

			return;
		}

		this.setFloor(floorStack.defaultFloor.id, reason);
	}

	get currentFloor() {
		const floor = this.floorsById.get(this.currentFloorId);
		if (!floor) {
			throw new MappedinError(`Floor with id "${this.currentFloorId}" not found`);
		}

		return floor!;
	}

	/** @deprecated use `currentFloorStack.floorObjects` or `floorsById` instead */
	get floors() {
		return Array.from(this.floorsById.values());
	}

	processFloorChange(floorId: string) {
		if (this.api.manualFloorVisibility === true) {
			return;
		}
		const currentFloor = this.api.getMapDataInternal()?.getById('floor', floorId);
		const currentFloorStack = this.api.getMapDataInternal()?.getById('floor-stack', this.currentFloorStackId);
		if (currentFloor && currentFloorStack) {
			if (!this.#multiFloorView?.enabled || currentFloor.floorStack.id !== currentFloorStack?.id) {
				for (const floorStack of this.floorStacksById.values()) {
					floorStack.floorObjects.forEach(floor => {
						if (floor.id === floorId) {
							return;
						}

						floor.setVisible(false);
					});
				}
			} else {
				const state = getMultiFloorState(
					currentFloorStack.floors,
					currentFloor,
					this.#multiFloorView.floorGap,
					this.api.Navigation?.floors?.map(f => f.id) || [],
					this.getFloorStackAltitudeData(currentFloorStack.id),
				);

				this.api?.Outdoor?.setOpacity(state.outdoorOpacity);
				state.floorStates.forEach(f => {
					if (f.floor) {
						const floor = this.api.getMapData()?.getById('floor', f.floor.id);

						if (floor) {
							this.api.updateState(floor, f.state);
						}
					}
				});
				if (this.#multiFloorView.updateCameraElevationOnFloorChange) {
					const floorAltitudes = this.floorStackAltitudeMap.get(currentFloorStack.id)!;
					this.api.Camera.setElevation(
						floorAltitudes.get(currentFloor.elevation)?.altitude ??
							currentFloor.elevation * parseFloorGap(this.#multiFloorView.floorGap, currentFloor.maxHeight),
					);
				}
			}
		}
	}

	setFloor(floorId: string, reason?: TFloorChangeReason) {
		if (this.currentFloorId === floorId) {
			return;
		}

		const newFloor = this.floorsById.get(floorId);
		if (!newFloor) {
			Logger.error(
				`Invalid floor passed to setFloor (${floorId}), the floor was not changed. Current floor is still ${this.currentFloor.id}`,
			);

			return;
		}

		// Warning: this.currentFloor throw here on first load
		const previousFloorId = this.currentFloorId;

		this.publish('floor-change-start', {
			previousFloorId,
			floorId,
			reason,
		});

		if (this.currentFloorStack) {
			this.processFloorChange(floorId);
		}

		// Then show the current floor
		this.currentFloorId = floorId;
		this.currentFloorStackId = this.currentFloor.floorStackId;
		this.currentFloor.setVisible(true);

		// Legacy behavior for compat
		this.updatePathLayersVisibility();

		this.publish('floor-change', {
			reason,
			floorId: this.currentFloor.id,
			previousFloorId,
		});
	}

	/**
	 * Updates the visibility of path layers across all floors based the value of hidePathsNotOnCurrentFloor
	 * and whether multi-floor view is enabled.
	 */
	updatePathLayersVisibility() {
		const shouldHideOtherFloors = this.api.hidePathsNotOnCurrentFloor === true && !this.options.multiFloorView?.enabled;

		for (const floor of this.floorsById.values()) {
			const pathLayer = floor.layers.get(PATHS_LAYER);
			if (!pathLayer) continue;

			const shouldShowLayer = !shouldHideOtherFloors || floor.id === this.currentFloor.id;
			pathLayer.setVisible(shouldShowLayer);
		}
	}

	getFloorStackAltitudeData(floorStackId: string) {
		return this.floorStackAltitudeMap.get(floorStackId)!;
	}

	private cancelAnimation(id: string) {
		const animation = this.animations.get(id);
		if (animation) {
			animation.stop();
			this.animations.delete(id);
			this.renderer.getInternalTweenGroup().remove(animation);
		}
	}

	Models = {
		add: (id: string, targets: Coordinate[], opts: TAddModelOptions & { floorId?: string; url: string }) => {
			const { floorId, scale, ...rest } = opts;
			const floor = floorId ? this.floorsById.get(floorId) : undefined;
			const params: InitializeModelState = { ...rest };
			if (scale != null) {
				params.scale = Array.isArray(opts.scale)
					? opts.scale
					: ([opts.scale, opts.scale, opts.scale] as [number, number, number]);
			}
			const model = this.renderer.addModelGroup(
				id,
				{
					type: 'FeatureCollection',
					features: targets.map(target => {
						return {
							type: 'Feature',
							properties: {
								id: randomId(),
								rotation: opts.rotation,
								scale: params.scale,
								interactive: opts.interactive,
							},
							geometry: {
								type: 'Point',
								coordinates: convertCoordinateToPosition(target) as number[],
							},
						};
					}),
				},
				params,
				{
					parent: floor?.id,
				},
			);

			if (model == null) {
				throw new MappedinError('error creating model');
			}

			return this.renderer.getState(model).children;
		},
		remove: (_id: string, _groupId: string) => {
			this.renderer.remove(_id);
			const group = this.renderer.getState<GroupContainerState>(_groupId);

			if (group.children.length === 0) {
				this.renderer.remove(_groupId);
			}
		},
	};

	Image3D = {
		add: (target: Position, url: string, opts: TAddImageOptions & { floorId?: string }) => {
			const { floorId, flipImageToFaceCamera, ...properties } = opts;
			const floor = floorId ? this.floorsById.get(floorId)! : this.currentFloor;
			const geometry: Feature<Point, Omit<TAddImageOptions, 'id' | 'flipImageToFaceCamera'>> = {
				type: 'Feature',
				properties,
				geometry: { type: 'Point', coordinates: target as [number, number] },
			};

			return this.renderer.addImage(
				opts.id ?? randomId(),
				geometry,
				{ url, flipImageToFaceCamera: flipImageToFaceCamera ?? false },
				floor.id,
			);
		},
		remove: (id: string) => {
			this.renderer.remove(id);
		},
		removeAll: () => {
			// Already implemented globally, no need to implement here for now
		},
	};

	Markers = {
		add: (
			coordinate: Coordinate,
			html: string,
			opts: TAddMarkerOptions & { attachTo?: string },
		): EntityId<MarkerState> => {
			const floor = getTargetFloorObject(
				coordinate,
				this.floorsById,
				this.api.mapObjects[0].floorsById.get('default-floor')!,
			);
			const { attachTo, ...rest } = opts;
			const _opts = rest as Omit<TAddMarkerOptions, 'rank'> & {
				rank: TCollisionRankingTier;
			};
			if (rest.rank === 'initial') {
				_opts.rank = 'medium';
			}

			const markerLayer = floor.layers.get(MARKERS_LAYER);

			const marker = this.renderer.addMarker2D(
				convertCoordinateToPosition(coordinate),
				html,
				{ ..._opts, occluderId: floor?.occluderId, attachTo },
				markerLayer?.containerId,
			);

			if (!marker) {
				throw new MappedinError('Failed to create Marker');
			}

			return marker;
		},
		remove: (id: string) => {
			this.renderer.remove(id);
		},
		getContentEl: (id: string) => {
			const markerState = this.renderer.getState(id);
			if (markerState?.type === 'marker') {
				return markerState.element;
			}

			return undefined;
		},
		removeAll: () => {
			// Already implemented globally, no need to implement here for now
		},
		setPosition: (id: string, coordinate: Position, targetFloorId: string) => {
			const markerState = this.renderer.getState(id);
			const floor = targetFloorId ? this.floorsById.get(targetFloorId) : this.currentFloor;

			if (!markerState) {
				Logger.error(`Failed to retrieve state for Marker with id "${id}".`);

				return;
			}
			if (markerState.type !== 'marker') {
				Logger.error(`Geometry with id "${id}" is not of type "marker".`);

				return;
			}

			this.renderer.setState<MarkerState>(markerState, {
				position: coordinate,
				parent: floor?.layers.get(MARKERS_LAYER)?.containerId,
			});
		},
		animateTo: (id: string, coordinate: Position, targetFloorId: string, options?: TAnimationOptions) => {
			const markerState = this.renderer.getState(id);
			const floor = targetFloorId ? this.floorsById.get(targetFloorId) : this.currentFloor;

			if (!markerState) {
				Logger.error(`Failed to retrieve state for Marker with id "${id}".`);

				return Promise.resolve();
			}
			if (markerState.type !== 'marker') {
				Logger.error(`Geometry with id "${id}" is not of type "marker".`);

				return Promise.resolve();
			}
			const optionsWithDefaults = {
				duration: 1000,
				easing: 'ease-in-out',
				...options,
			};

			this.cancelAnimation(id);

			const tween = new Tween({ lon: markerState.position[0], lat: markerState.position[1] })
				.to({ lon: coordinate[0], lat: coordinate[1] }, optionsWithDefaults.duration)
				.easing(ANIMATION_TWEENS[optionsWithDefaults.easing])
				.onUpdate(({ lon, lat }) => {
					const state = this.renderer.getState(id);
					if (!state) {
						this.cancelAnimation(id);
					} else {
						const layerContainerId = floor?.layers.get(MARKERS_LAYER)?.containerId;
						if (layerContainerId) {
							this.Markers.setPosition(id, [lon, lat], layerContainerId);
						}
					}
				})
				.start();
			this.renderer.getInternalTweenGroup().add(tween);
			this.renderer.render();

			this.animations.set(id, tween);

			return tweenToPromise(tween).then(() => {
				this.animations.delete(id);
			});
		},
	};

	Exporter = {
		getCurrentSceneGLTF: async (options: GLTFExportOptions) => {
			return this.renderer.getCurrentSceneGLTF(options);
		},
	};

	Shapes = {
		add: <T extends FeatureCollection<Polygon | MultiPolygon | LineString, any>>(
			geometry: T,
			style: T extends FeatureCollection<LineString, any> ? LineStyle : PaintStyle,
			opts: { floorId?: string },
		) => {
			const floor = opts?.floorId ? this.floorsById.get(opts?.floorId)! : this.currentFloor;
			const id = 'shape-' + randomId();
			this.renderer.addGeometryGroup(id, geometry, style, floor.id);

			return id;
		},

		remove: (customGeometry: Shape) => {
			this.renderer.remove(customGeometry.id);

			return customGeometry.id;
		},
	};

	Text3D = {
		label: (target: Space, content?: string, options?: TAddText3DOptions): Text3DInfo | undefined => {
			if (!this.api.getMapDataInternal()?.enterpriseMode) {
				Logger.warn('Enterprise mode is not enabled, skipping label creation');

				return;
			}
			const textInfo = labelSpace(this.renderer, target, content, options);

			if (textInfo) {
				return {
					textInfo,
					target: target,
				};
			}
		},
		labelAll: (option: TAddText3DOptions = {}): Text3DInfo[] => {
			if (!this.api.getMapDataInternal()?.enterpriseMode) {
				Logger.warn('Enterprise mode is not enabled, skipping label creation');

				return [];
			}

			const mapData = this.api.getMapData();
			if (!mapData) return [];

			const result: Text3DInfo[] = [];

			mapData.getByType('space').forEach(space => {
				if (space.locations[0]) {
					const text3d = labelSpace(this.renderer, space, space.locations[0]?.name ?? space.name, option);
					if (text3d) {
						result.push({
							textInfo: text3d,
							target: space,
						});
					}
				}
			});

			return result;
		},
		remove: (id: string) => {
			if (!this.api.getMapDataInternal()?.enterpriseMode) {
				Logger.warn('Remove text label only works in enterprise mode');

				return;
			}

			this.renderer.remove(id);

			return id;
		},
	};
	Labels = {
		all: ({
			onCreate,
			labelOptions = {},
		}: {
			onCreate: (labelId: string | number, text: string, target: IAnchorable) => void;
			labelOptions?: TAddLabelOptions;
		}) => {
			for (const entry of Object.entries(this.mvf.space)) {
				const [floorId, val] = entry;
				if (!val) continue;
				val.features.forEach(feature => {
					const space = this.api.getMapData()?.getById('space', feature.properties.id);
					if (!space) return;
					const hasImageLabel = (this.styleMap[feature.properties.id] as PolygonStyle)?.showImage;
					const featureImages = feature.properties.details?.images;
					const icon = hasImageLabel && featureImages?.[0]?.url ? { icon: featureImages[0].url } : {};
					const appearance: LabelAppearance = {
						icon: icon.icon,
						...labelOptions.appearance,
					};
					const floor = floorId ? this.floorsById.get(floorId)! : this.currentFloor;

					const labelLayer = floor.layers.get(LABELS_LAYER);

					if (feature.properties.center && space.name !== '') {
						const label = this.renderer.addLabel2D(
							feature.properties.center,
							space.name,
							{
								id: feature.properties.id + '-label',
								...labelOptions,
								appearance,
								rank: labelOptions.rank as CollisionRankingTier,
								attachTo: getAttachableGeometryId(space),
							},
							Space.is(space) ? space.id : labelLayer?.containerId,
						);

						if (label) {
							onCreate(label.id, space.name, space);
						}
					}
				});
			}
		},
		add: (
			coordinate: Coordinate,
			text: string,
			opts: AddLabelOptionsInternal & { attachTo?: string; verticalOffset?: number } = {},
		): { id: string | number } => {
			const { attachTo, ...rest } = opts;
			const floor = getTargetFloorObject(
				coordinate,
				this.floorsById,
				this.api.mapObjects[0].floorsById.get('default-floor')!,
			);

			const labelLayer = floor.layers.get(LABELS_LAYER)!;

			const label = this.renderer.addLabel2D(
				convertCoordinateToPosition(coordinate),
				text,
				{ ...rest, occluderId: floor.occluderId, attachTo },
				labelLayer?.containerId,
			);

			if (!label) {
				throw new MappedinError('label creating failed');
			}

			return label;
		},
		remove: ({ entityId }: { entityId: string }) => {
			this.renderer.remove(entityId);
		},

		removeAll: () => {
			// TODO: currently removeall is to remove all labels that's not exclusive this map.
			// When we ever get the multi map support, we should implement the logic to only remove
			// the map in here
		},
	};

	Paths: {
		add: (
			coordinates: Coordinate[],
			options?: TAddPathOptions & { id?: string },
		) => { paths: (EntityId<PathState> & { coordinates: Coordinate[] })[]; animation: Promise<void> };
		remove: (entityIds: string[]) => void;
	} = {
		add: (coordinates: Coordinate[], options?: TAddPathOptions & { id?: string }): InternalPath => {
			const animateDrawing = options?.animateDrawing ?? true;
			const segments: {
				floorId: string;
				coordinates: Coordinate[];
			}[] = [];
			let currentFloorId = coordinates[0].floorId || 'root';
			let segmentIndex = 0;
			for (const coord of coordinates) {
				const { floorId = 'root', latitude, longitude } = coord;

				// Detect floor changes and iterate to the next segment
				if (floorId !== currentFloorId) {
					segmentIndex++;
					currentFloorId = floorId;
				}

				// Create a new segment if it doesn't exist
				if (segments[segmentIndex] == null) {
					segments.push({ floorId, coordinates: [] });
				}

				// Add the coordinate to the current segment
				const coordinate = floorId === coord.floorId ? coord : new Coordinate({ longitude, latitude, floorId });
				segments[segmentIndex].coordinates.push(coordinate);
			}

			const paths = segments.reduce<(EntityId<PathState> & { coordinates: Coordinate[] })[]>(
				(acc, { floorId, coordinates }, idx) => {
					if (idx !== 0) {
						const fromSegment = segments[idx - 1];
						const fromCoordinate = fromSegment.coordinates[fromSegment.coordinates.length - 1];
						const toCoordinate = coordinates[0];
						const fromPosition = convertCoordinateToPosition(fromCoordinate);
						const toPosition = convertCoordinateToPosition(toCoordinate);

						// Between each path segment create a connection path which will stretch in stacked maps
						const connectionPath = this.renderer.addPath(
							{
								type: 'FeatureCollection',
								features: [
									{
										type: 'Feature',
										properties: {
											parentId: fromSegment.floorId,
										},
										geometry: {
											type: 'Point',
											coordinates: fromPosition,
										},
									},
									{
										type: 'Feature',
										properties: {
											parentId: floorId,
										},
										geometry: {
											type: 'Point',
											coordinates: toPosition,
										},
									},
								],
							},
							options,
							this.id,
						);

						if (connectionPath) {
							if (animateDrawing) {
								this.renderer.setState<PathState>(connectionPath.id, {
									completeFraction: 0,
								});
							}
							acc.push({ ...connectionPath, coordinates: [fromCoordinate, toCoordinate] });
						}
					}

					/**
					 * Sometimes, only a single coordinate of a path exists on a floor. This usually happens just before a connection.
					 * For example, if a path begins at a door on the outdoor map, the path segment outdoors will be a single coord
					 * at the door, and the next path segment will be two coordinates (connecting the door coordinate outdoor to the
					 * door coordinate indoors). If there is only a single coordinate in a path segment, there is nothing to draw.
					 */
					if (coordinates.length < 2) {
						return acc;
					}

					// Create the path for the current floor segment
					const pathsLayerId = this.floorsById.get(floorId)?.layers.get(PATHS_LAYER)?.containerId;
					const path = this.renderer.addPath(
						{
							type: 'FeatureCollection',
							features: coordinates.map(coord => ({
								type: 'Feature',
								properties: {},
								geometry: { type: 'Point', coordinates: convertCoordinateToPosition(coord) },
							})),
						},
						options,
						// attempt to add to the floor's paths layer
						// if pathsLayerId is not found, then the floorId is probably 'root'
						pathsLayerId ?? floorId,
					);

					if (path) {
						if (animateDrawing) {
							this.renderer.setState<PathState>(path.id, {
								completeFraction: 0,
							});
						}
						acc.push({ ...path, coordinates });
					}

					return acc;
				},
				[],
			);

			if (animateDrawing) {
				const tweens: Tween[] = [];
				const mapData = this.api.getMapData();

				paths.forEach(path => {
					const shouldAnimate = shouldAnimatePathDrawing(path, mapData!, this.api);

					tweens.push(
						new Tween({ fraction: 0.0 })
							// do not animate if the floor is not visible
							.to({ fraction: 1.0 }, shouldAnimate ? options?.drawDuration ?? 1500 : 0)
							.onUpdate(value => {
								this.renderer.setState<PathState>(path.id, {
									completeFraction: value.fraction,
								});
							}),
					);
				});

				tweens.forEach((tween, index) => {
					if (index < tweens.length - 1) {
						tween.chain(tweens[index + 1]);
					}
					this.renderer.getInternalTweenGroup().add(tween);
				});

				tweens[0].start();

				this.renderer.render();
				this.animations.set(paths[0].id.toString(), tweens[0]);

				return {
					paths,
					animation: tweenToPromise(tweens[tweens.length - 1]).then(() => {
						this.animations.delete(paths[0].id.toString());
					}),
				};
			} else {
				return {
					paths,
					animation: Promise.resolve(),
				};
			}
		},
		remove: (entityIds: string[]) => {
			entityIds.forEach(id => {
				this.cancelAnimation(id);
				this.renderer.remove(id);
			});
		},
	};

	#multiFloorView: Required<TMultiFloorViewOptions> = {
		enabled: false,
		floorGap: 'auto',
		updateCameraElevationOnFloorChange: true,
	};

	constructor(
		id: string,
		mapDataInternal: MapDataInternal | undefined,
		options: TShow3DMapOptions | undefined,
		api: GeoJsonApi,
	) {
		super();
		this.renderer = api.core;
		this.api = api;
		this.id = id;
		this.renderer.addGroupContainer(id, {}, 'root');
		this.options = options ?? {};
		this.mvf = mapDataInternal?.mvf ?? ({} as ParsedMVF);
		const styleMap = extractStyleMap(this.mvf['styles.json'] || {});
		this.styleMap = styleMap;
		this.#multiFloorView = {
			enabled: false,
			floorGap: 'auto',
			updateCameraElevationOnFloorChange: true,
			...options?.multiFloorView,
		};

		if (!mapDataInternal) {
			if (this.id !== 'outdoors-container') {
				Logger.warn(`Map (${this.id}) created with no data.`);
			}

			const defaultStack = new FloorStackObject(
				{
					id: 'default-stack',
					externalId: 'default-stack',
					name: 'Default Stack',
					maps: ['default-floor'],
				},
				this.id,
				this.renderer,
			);
			this.floorStacksById.set(defaultStack.id, defaultStack);
			this.currentFloorStackId = defaultStack.id;
			const defaultFloor = new FloorObject(
				defaultStack.id,
				{
					id: 'default-floor',
					externalId: 'default-floor',
					name: 'Default Floor',
					elevation: 0,
				},
				this,
				this.#multiFloorView,
				options ?? {},
			);
			this.floorsById.set(defaultFloor.id, defaultFloor);
			defaultStack.addFloor(defaultFloor);
			this.setFloor(defaultFloor.id);

			return;
		}

		const floorStacks = mapDataInternal.getMVFFeatureByType('floor-stack');
		if (floorStacks == null || floorStacks.length === 0) {
			throw new MappedinError('MVF does not contain any Floor Stacks.');
		}

		for (const floorStack of floorStacks) {
			const floorStackInternal = new FloorStackObject(
				floorStack,
				this.id,
				this.renderer,
				this.mvf['facade']?.[floorStack.id],
			);
			for (const floorId of floorStackInternal.floorIds) {
				const floor = mapDataInternal.getMVFFeatureById('floor', floorId);

				if (floor != null) {
					const floorObject = new FloorObject(
						floorStackInternal.id,
						floor,
						this,
						this.#multiFloorView,
						options ?? {},
						this.mvf,
						styleMap,
						mapDataInternal,
					);
					this.floorsById.set(floorObject.id, floorObject);
					floorStackInternal.addFloor(floorObject);
				}
			}
			if (floorStackInternal.floorObjects.length < 1) {
				// We don't currently support a floor stack with no floors. Downstream logic expects this to exist
				Logger.warn(`Floor stack ${floorStack.id} has no floors. It will not be loaded.`);
				continue;
			}
			this.floorStacksById.set(floorStack.id, floorStackInternal);
			if (!this.currentFloorStackId) {
				this.currentFloorStackId = floorStack.id;
			}
			this.floorStackAltitudeMap.set(
				floorStack.id,
				computeFloorAltitudes(floorStackInternal.floorObjects, this.#multiFloorView.floorGap),
			);
		}

		for (const [spaceId] of Object.entries(mapDataInternal.facadesBySpaceId)) {
			// We can assume that the facade space is rendered outdoors
			// We need to keep track of these floor stacks and keep them visible
			const space = mapDataInternal.getById('space', spaceId);
			if (space) {
				this.outdoorFloorStacks.add(space.floor.floorStack.id);
				this.floorsById.get(space.floor.id)?.setVisible(true);
			}
		}

		let floorId = this.mvf.enterprise?.venue.defaultMap;

		if (options?.initialFloor) {
			const pickedFloor = getId(options.initialFloor);
			if (pickedFloor && this.floorsById.has(pickedFloor)) {
				floorId = pickedFloor;
			} else {
				Logger.error(`Invalid initial floor specified (${options.initialFloor}), using default behavior instead`);
			}
		}

		if (!floorId) {
			// If no floorId is provided, use the default floor of the first floor stack
			floorId = this.currentFloorStack.defaultFloor.id;
		}

		this.startingFloorId = floorId;
	}

	loadAllFloors() {
		for (const floorStack of this.floorStacksById.values()) {
			floorStack.loadAllFloors();

			// We get the corresponding floor to the space and use the floor's data to calculate the altitude and height
			// Currently, this works since the space altitude are incrementally increasing by the same amount since we are
			// using a default altitude for all spaces. When the mvf export correctly exports the altitude for each space, we can
			// remove this logic and use the space's altitude and height directly.
			let altitudeData = this.floorStackAltitudeMap.get(floorStack.id);
			floorStack.facade?.spaces.forEach(space => {
				let spaceStyle = this.styleMap[space.spaceId] as any;
				if (spaceStyle && spaceStyle.altitude >= 0) {
					let spaceAltitudeData = altitudeData?.get(spaceStyle.altitude / 2);
					if (spaceAltitudeData) {
						this.renderer.setState(space.spaceId, {
							altitude: spaceAltitudeData.altitude,
							height: spaceAltitudeData.effectiveHeight,
						});
					}
				}
			});
		}
	}
}

function labelSpace(
	core: RendererCore,
	space: Space,
	content?: string,
	options?: TAddText3DOptions,
): EntityId<Text3DState> | undefined {
	return core.labelText3D(space.id, content ?? space.name, { ...options, parentId: space.floor.id });
}
export type Text3DInfo = {
	target: Space;
	textInfo: EntityId<Text3DState>;
};
