import type { RendererState } from '../../renderer';
import { PubSub } from '../../../../packages/common/pubsub';
import LabelComponent from '../../components/label';
import { Geometry2DComponents } from '../../entities/geometry2d';
import { easeIn, interpolate, linearEase } from '@packages/internal/common';

export class TwoDVisibilitySystem extends PubSub<{
	'draw:2d': undefined;
	'animate:2d': undefined;
}> {
	state: RendererState;
	constructor(state: RendererState) {
		super();
		this.state = state;
		this.playAnimations = this._playAnimations;
	}

	animating: Map<LabelComponent, [number, number]> = new Map();
	isAnimating = false;
	startTime = -1;
	showDuration = 300;
	hideDuration = 250;

	update = () => {
		this.animating.clear();
		let entity;
		let component;
		for (const entityId of this.state.geometry2DIdsInScene) {
			entity = this.state.geometry2DMap.get(entityId);
			if (entity) {
				component = entity.components[Geometry2DComponents.UI];
				if (component instanceof LabelComponent) {
					if (component.visibilityNeedsUpdate === 'show') {
						component.canShow = true;
						this.animating.set(component, [component.currentOpacity, component.appearance.opacity ?? 1]);
					} else if (component.visibilityNeedsUpdate === 'hide') {
						this.animating.set(component, [component.currentOpacity, 0]);
					}
				}
			}
		}

		this.startTime = performance.now();
		this.publish('animate:2d');
	};

	playAnimations: () => void;
	_playAnimations() {
		const frameTime = performance.now();
		for (let [component, [startingOpacity, endOpacity]] of this.animating.entries()) {
			const duration = endOpacity === 1 ? this.showDuration : this.hideDuration;
			const percentComplete = (frameTime - this.startTime) / duration;

			component.currentOpacity = interpolate(
				percentComplete,
				0,
				1,
				startingOpacity,
				endOpacity,
				endOpacity === 0 ? linearEase : easeIn,
			);

			if (frameTime - this.startTime >= duration) {
				if (endOpacity === 1) {
					component.canShow = true;
				} else if (endOpacity === 0) {
					component.canShow = false;
				}
				component.visibilityNeedsUpdate = false;
				this.animating.delete(component);
			}
		}

		this.publish('draw:2d');
		if (this.animating.size > 0) {
			this.publish('animate:2d');
		}
	}
}
