const PERCENT_OF_SCREEN_PADDING = 0.05;

export class GeoJsonApi extends PubSub<TNavigationEvents> {
	core: RendererCore;
	mapObjects: GeojsonApiMapObject[] = [];
	id: string;

	mapDataExternal: { [key in string]: MapData } = {};
	private mapsByFloorId: { [floorId: string]: GeojsonApiMapObject } = {};
	mapData?: MapData;
	currentMap: GeojsonApiMapObject;
	hiddenOutdoorGeometries: [
		BBox,
		polygons: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[],
		layers: string[],
	][] = [];
	private lastFacadeIntersected?: Facade;
	#manualFloorVisibility = false;
	#hidePathsNotOnCurrentFloor = false;
	mapView: MapView;
	/**
	 * External event bus for MapView - bridges internal API events to public API.
	 *
	 * Internal systems publish events here which are then exposed to developers via
	 * MapView.on() and MapView.off().
	 */
	externalPubSub: PubSub<TEvents>;

	get manualFloorVisibility(): boolean {
		return this.#manualFloorVisibility;
	}

	set manualFloorVisibility(value: boolean) {
		this.#manualFloorVisibility = value;
		this.externalPubSub.publish('state-change-internal', {
			manualFloorVisibility: value,
		});
	}

	get hidePathsNotOnCurrentFloor(): boolean {
		return this.#hidePathsNotOnCurrentFloor;
	}

	set hidePathsNotOnCurrentFloor(value: boolean) {
		this.#hidePathsNotOnCurrentFloor = value;
		this.currentMap?.updatePathLayersVisibility();
	}

	Camera: Camera;
	Labels: Labels;
	Text3D: Text3D;
	Markers: Markers;
	Models: Models;
	Paths: Paths;
	Exporter: Exporter;
	Navigation: Navigation;
	Outdoor: Outdoor;
	Shapes: Shapes;
	Style: Style;
	Image3D: Image3D;

	constructor(rendererCore: RendererCore, mapView: MapView, externalPubSub: PubSub<TEvents>) {
		super();
		this.core = rendererCore;
		this.mapView = mapView;
		this.externalPubSub = externalPubSub;
		this.Camera = new Camera(this);
		this.id = this.core.addGroupContainer('root').id.toString();

		const defaultMap = new GeojsonApiMapObject('outdoors-container', undefined, undefined, this);
		this.mapObjects.push(defaultMap);
		this.currentMap = defaultMap;

		this.core.on('pre-render', () => {
			externalPubSub.publish('pre-render');
		});
		this.core.on('post-render', () => {
			externalPubSub.publish('post-render');
		});
		this.core.on('resize', () => {
			externalPubSub.publish('resize');
		});
		this.core.on('render-error', ({ error }) => {
			externalPubSub.publish('render-error', {
				error: new MappedinRenderError(error instanceof Error ? error.message : 'Unknown error'),
			});
		});
		const getFacadeFromEntityId = (entityId: string) => {
			const geometry = this.getMapDataObject(entityId) as Facade | Floor;

			switch (geometry?.__type) {
				case 'facade':
					return geometry;
				case 'floor':
					/**
					 * Floors are focusable too and it'll give back the facade that it belongs to.
					 * This allows multi-floor buildings to prevent the raycast from going through the floor
					 * and switching the focus constantly.
					 */
					return geometry.floorStack.facade;
				default:
					return null;
			}
		};
		this.core.on('geometry-in-focus', entities => {
			const uniqueFacades = new Set<Facade>();
			for (const entityId of entities) {
				const facade = getFacadeFromEntityId(entityId);
				if (facade) {
					uniqueFacades.add(facade);
				}
			}
			externalPubSub.publish('facades-in-view-change', {
				facades: Array.from(uniqueFacades),
			});
		});

		this.core.getSystems().renderSystem.on('camera-state-change', () => {
			externalPubSub.publish('state-change-internal', {
				Camera: this.getCameraState(),
			});
		});

		// Create CameraTransform with centimeter precision by default
		const cameraTransform = new CameraTransform(this.Camera, { precision: 7 });
		this.core.on('camera-change', () => {
			externalPubSub.publish('camera-change', cameraTransform);
		});
		this.core.on('pan-bounds-change', panBounds => {
			externalPubSub.publish(
				'camera-bounds-change',
				convertPanBoundsToCameraBounds(panBounds, this.core.convert3DMapPositionToCoordinate),
			);
		});
		const createHandler = (event: 'hover' | 'click') => (payload: ClickPayload | HoverPayload) => {
			const labels: Label[] = [];
			const markers: Marker[] = [];
			const models: Model[] = [];
			const paths: Path[] = [];
			payload.labels.forEach(l => {
				const found = this.Labels.getById(l.toString());
				if (found) labels.push(found.label);
			});
			payload.markers.forEach(m => {
				const found = this.Markers.getById(m.toString()) || this.Navigation.getMarkerById(m.toString());
				if (found) markers.push(found.marker);
			});
			payload.models.forEach(m => {
				const found = this.Models.getById(m.toString());
				if (found) models.push(found.model);
			});
			payload.paths.forEach(p => {
				const found = this.Paths.getById(p.toString());
				if (found) paths.push(found);
			});

			const geometryIntersectIds = payload.geometry;
			let geometryIntersected: Space | MapObject | Area | Shape | undefined;

			// TODO: refactor to getMapDataById ?
			if (geometryIntersectIds.length > 0) {
				geometryIntersected = geometryIntersectIds
					.map(id => this.getMapDataInternal()?.getById('space', id as string))
					.filter(Boolean)[0];

				if (!geometryIntersected) {
					geometryIntersected = geometryIntersectIds
						.map(id => this.getMapDataInternal()?.getById('object', id as string))
						.filter(Boolean)[0];
				}
				if (!geometryIntersected) {
					geometryIntersected = geometryIntersectIds
						.map(id => this.getMapDataInternal()?.getById('area', id as string))
						.filter(Boolean)[0];
				}
				if (!geometryIntersected) {
					geometryIntersected = geometryIntersectIds.map(id => this.Shapes.getById(id as string)).filter(Boolean)[0];
				}
			}

			const floorsIntersected = payload.containers.reduce((acc: Floor[], id) => {
				const floor = this.getMapDataInternal()?.getById('floor', id as string);

				if (floor && Floor.is(floor)) {
					acc.push(floor);
				}

				return acc;
			}, []);

			let floorId: string | undefined;
			let verticalOffset = 0;
			if (floorsIntersected.length > 0) {
				floorId = floorsIntersected[0].id;

				// Get the vertical offset of the coordinate relative to the floor's altitude
				const floorState = this.core.getState<GroupContainerState>(floorId);
				verticalOffset = payload.coordinate[2] ? payload.coordinate[2] - floorState.altitude : verticalOffset;
			}

			// Handle facade hover state
			let facadeIntersected: Facade | undefined;
			if (this.lastFacadeIntersected) {
				this.lastFacadeIntersected.spaces.forEach(space => {
					this.core.setState<GeometryState>(space.id, {
						hovered: false,
					});
				});
			}
			if (geometryIntersected && Space.is(geometryIntersected)) {
				facadeIntersected = geometryIntersectIds
					.map(id => this.getMapDataInternal()?.facadesBySpaceId[id as string])
					.filter(Boolean)[0];
				if (facadeIntersected) {
					facadeIntersected.spaces.forEach(space => {
						this.core.setState<GeometryState>(space.id, {
							hovered: true,
						});
					});
				}
			}
			this.lastFacadeIntersected = facadeIntersected;

			const publicPayload: THoverPayload | TClickPayload = {
				coordinate: new Coordinate({
					latitude: payload.coordinate[1],
					longitude: payload.coordinate[0],
					floorId,
					verticalOffset,
				}),
				spaces: geometryIntersected && Space.is(geometryIntersected) ? [geometryIntersected] : [],
				objects: geometryIntersected && MapObject.is(geometryIntersected) ? [geometryIntersected] : [],
				areas: geometryIntersected && Area.is(geometryIntersected) ? [geometryIntersected] : [],
				shapes: geometryIntersected && Shape.is(geometryIntersected) ? [geometryIntersected] : [],
				markers,
				labels,
				models,
				paths,
				floors: floorsIntersected,
				facades: facadeIntersected ? [facadeIntersected] : [],
			};
			if ('pointerEvent' in payload) {
				(publicPayload as TClickPayload).pointerEvent = payload.pointerEvent;
			}
			externalPubSub.publish(event, publicPayload);
		};

		this.core.on('hover', createHandler('hover'));
		this.core.on('click', createHandler('click'));

		this.core.on('user-interaction-start', () => {
			externalPubSub.publish('user-interaction-start');
		});

		this.core.on('user-interaction-end', () => {
			externalPubSub.publish('user-interaction-end');
		});

		const currentMapGetter = {
			currentMapGetter: () => {
				return this.currentMap;
			},
		};
		this.Markers = new Markers(currentMapGetter);
		this.Models = new Models(currentMapGetter);
		this.Labels = new Labels(currentMapGetter);
		this.Text3D = new Text3D(currentMapGetter);
		this.Exporter = new Exporter(currentMapGetter);
		this.Paths = new Paths(currentMapGetter);
		this.Navigation = new Navigation(this);
		this.on('navigation-state-change', e => {
			this.externalPubSub.publish('state-change-internal', {
				Navigation: e,
			});
		});
		this.on('navigation-connection-click', e => {
			this.externalPubSub.publish('navigation-connection-click', e);
		});
		this.on('navigation-active-path-change', e => {
			this.externalPubSub.publish('navigation-active-path-change', e);
		});
		this.Outdoor = new Outdoor(this.core, this.hiddenOutdoorGeometries);

		if (this.Outdoor.enabled) {
			this.core.on('outdoor-view-loaded', () => {
				externalPubSub.publish('outdoor-view-loaded');
			});

			this.core.on('outdoor-style-loaded', () => {
				externalPubSub.publish('outdoor-style-loaded');
			});
		}

		this.Image3D = new Image3D(currentMapGetter);
		this.Shapes = new Shapes(currentMapGetter);
		this.Style = new Style(currentMapGetter);
	}

	preloadFloors(floors: Floor[]) {
		floors.forEach(floor => {
			if (floor instanceof Floor) {
				this.core.setState<GroupContainerState>(floor.id, { preloadGeometry: true });
			}
		});
	}

	updateState<T extends MapElementsWithState>(target: T, state: TUpdateState<T>): void;
	updateState<T extends string & NonNullable<unknown>>(target: T, state: ValueOf<MapElementToUpdateState>): void;
	updateState<T extends MapElementsWithState | string>(target: T, state: TUpdateState<T>): void;
	updateState(target: MapElementsWithState | string, state: TUpdateState<MapElementsWithState | string>): void {
		if (state == null || typeof state !== 'object') {
			// no state to update
			return;
		}

		if (target === WALLS.Interior) {
			this.currentMap.floors.forEach(floor => {
				const geometryLayer = floor.layers.get(GEOMETRY_LAYER);
				if (geometryLayer) {
					const wallsId = geometryLayer.layers.get(WALLS_ID);
					if (wallsId && this.core.hasState(wallsId)) {
						this.core.setState(
							wallsId,
							translateWallsStateToGeojsonCore(
								parsePartialState(wallsStateSchemaPartial, state, { warn: true }),
								this.core.getState<GeometryState>(wallsId),
							),
						);
					}
				}
			});

			return;
		}
		if (target === WALLS.Exterior) {
			this.currentMap.floors.forEach(floor => {
				const geometryLayer = floor.layers.get(GEOMETRY_LAYER);
				if (geometryLayer) {
					const exteriorWallsId = geometryLayer.layers.get(EXTERIOR_WALLS_ID);
					if (exteriorWallsId && this.core.hasState(exteriorWallsId)) {
						this.core.setState(
							exteriorWallsId,
							translateWallsStateToGeojsonCore(
								parsePartialState(wallsStateSchemaPartial, state, { warn: true }),
								this.core.getState<GeometryState>(exteriorWallsId),
							),
						);
					}
				}
			});

			return;
		}

		if (target === DOORS.Interior) {
			this.currentMap.floors.forEach(floor => {
				const geometryLayer = floor.layers.get(GEOMETRY_LAYER);
				if (geometryLayer) {
					const interiorDoorsId = geometryLayer.layers.get(INTERIOR_DOORS_ID);
					if (interiorDoorsId && this.core.hasState(interiorDoorsId)) {
						this.core.setState(
							interiorDoorsId,
							translateDoorsStateToGeojsonCore(
								parsePartialState(doorsStateSchemaPartial, state, { warn: true }),
								this.core.getState<GeometryState>(interiorDoorsId),
							),
						);
					}
				}
			});

			return;
		}

		if (target === DOORS.Exterior) {
			this.currentMap.floors.forEach(floor => {
				const geometryLayer = floor.layers.get(GEOMETRY_LAYER);
				if (geometryLayer) {
					const exteriorDoorsId = geometryLayer.layers.get(EXTERIOR_DOORS_ID);
					if (exteriorDoorsId && this.core.hasState(exteriorDoorsId)) {
						this.core.setState(
							exteriorDoorsId,
							translateDoorsStateToGeojsonCore(
								parsePartialState(doorsStateSchemaPartial, state, { warn: true }),
								this.core.getState<GeometryState>(exteriorDoorsId),
							),
						);
					}
				}
			});

			return;
		}

		const dataObject = typeof target === 'string' ? this.getMapDataObject(target) : target;
		if (!dataObject) return;

		// TODO: why don't paths have state?
		if (dataObject.__type !== Path.__type && !this.core.hasState(dataObject.id)) {
			Logger.warn(`Failed to update state for "${dataObject.id}". No ${dataObject.__type} found with that ID.`);
			return;
		}

		if (Label.is(dataObject)) {
			return this.core.setState(
				dataObject.id,
				translateLabelStateToGeojsonCore(
					parsePartialState(labelStateSchemaPartial, state, { warn: true }),
					this.core.getState<LabelState>(dataObject.id),
				),
			);
		}

		if (Marker.is(dataObject)) {
			return this.core.setState(
				dataObject.id,
				translateMarkerStateToGeojsonCore(
					parsePartialState(markerStateSchemaPartial, state, { warn: true }),
					this.core.getState<MarkerState>(dataObject.id),
				),
			);
		}

		if (Space.is(dataObject) || MapObject.is(dataObject)) {
			const currentState = this.core.getState<GeometryState>(dataObject.id);
			if (currentState != null) {
				this.core.setState(
					dataObject.id,
					translateGeometryStateToGeojsonCore(
						parsePartialState(geometryStateSchemaPartial, state, { warn: true }),
						currentState,
					),
				);
			}
		}

		if (Area.is(dataObject)) {
			this.core.setState(dataObject.id, parsePartialState(geometryStateSchemaPartial, state, { warn: true }));
		}

		if (Image3DView.is(dataObject)) {
			this.core.setState(dataObject.id, parsePartialState(imageStateSchemaPartial, state, { warn: true }));
		}

		if (Door.is(dataObject)) {
			this.core.setState(
				dataObject.id,
				translateDoorsStateToGeojsonCore(
					parsePartialState(doorsStateSchemaPartial, state, { warn: true }),
					this.core.getState<GeometryState>(dataObject.id),
				),
			);
		}

		if (Shape.is(dataObject)) {
			this.core.setState(
				dataObject.id,
				translateShapeStateToGeojsonCore(parsePartialState(shapeStateSchemaPartial, state, { warn: true })),
			);
		}

		if (Model.is(dataObject)) {
			const {
				position: target,
				scale,
				rotation,
				...rest
			} = parsePartialState(modelStateUpdateSchema, state, { warn: true });
			const update: GeoJsonUpdateModelState = rest;
			if (target) {
				update.position = [target.longitude, target.latitude, target.verticalOffset] as [number, number, number];
			}
			if (scale != null) {
				if (Array.isArray(scale)) {
					update.scale = scale;
				} else {
					update.scale = [scale, scale, scale];
				}
			}
			if (rotation != null) {
				if (Array.isArray(rotation)) {
					update.rotation = rotation;
				} else {
					update.rotation = [rotation, rotation, rotation];
				}
			}
			if (target) {
				dataObject.updateTarget(target);
			}
			this.core.setState(dataObject.id, update);
		}

		if (Floor.is(dataObject)) {
			const floorObject = this.currentMap.floorsById.get(dataObject.id);
			if (floorObject == null) {
				return;
			}
			const parsedState = parsePartialState(floorSchemaPartial, state, { warn: true });

			this.core.setState(dataObject.id, parsedState);

			const geometryLayer = floorObject.layers.get(GEOMETRY_LAYER);
			if (geometryLayer != null && 'geometry' in state && state.geometry) {
				this.core.setState(
					geometryLayer.containerId,
					parsePartialState(floorGeometryStateSchemaPartial, state.geometry, { warn: true }),
				);
			}
			const labelsLayer = floorObject.layers.get(LABELS_LAYER);
			if (labelsLayer != null && 'labels' in state && state.labels) {
				const labelsState = parsePartialState(floorLabelsStateSchemaPartial, state.labels, { warn: true });
				if ('enabled' in labelsState && typeof labelsState.enabled === 'boolean') {
					labelsLayer.setVisible(labelsState.enabled);
				}
			}
			const markersLayer = floorObject.layers.get(MARKERS_LAYER);
			if (markersLayer != null && 'markers' in state && state.markers) {
				const markersState = parsePartialState(floorMarkersStateSchemaPartial, state.markers, { warn: true });
				if ('enabled' in markersState && typeof markersState.enabled === 'boolean') {
					markersLayer.setVisible(markersState.enabled);
				}
			}
			const footprintLayer = floorObject.layers.get(FOOTPRINT_LAYER);
			if (footprintLayer != null && 'footprint' in state && state.footprint) {
				this.core.setState(
					footprintLayer.containerId,
					parsePartialState(footprintStateSchemaPartial, state.footprint, { warn: true }),
				);
			}
			const occlusionLayer = floorObject.layers.get(OCCLUSION_LAYER);
			if (occlusionLayer != null && 'occlusion' in state && state.occlusion) {
				const occlusionState = parsePartialState(floorOcclusionStateSchemaPartial, state.occlusion, { warn: true });
				if ('enabled' in occlusionState && typeof occlusionState.enabled === 'boolean') {
					occlusionLayer.setVisible(occlusionState.enabled);
				}
			}

			const imagesLayer = floorObject.layers.get(IMAGES_LAYER);
			if (imagesLayer != null && 'images' in state && state.images) {
				const imagesState = parsePartialState(floorImagesStateSchemaPartial, state.images, { warn: true });
				if ('visible' in imagesState && typeof imagesState.visible === 'boolean') {
					imagesLayer.setVisible(imagesState.visible);
				}
			}

			const pathsLayer = floorObject.layers.get(PATHS_LAYER);
			if (pathsLayer != null && 'paths' in state && state.paths != null) {
				const pathsState = parsePartialState(floorPathsStateSchemaPartial, state.paths, { warn: true });
				if (pathsState.visible != null && typeof pathsState.visible === 'boolean') {
					pathsLayer.setVisible(pathsState.visible);
				}
			}

			const areasLayer = floorObject.layers.get(AREAS_LAYER);
			if (areasLayer != null && 'areas' in state && state.areas != null) {
				const areasState = parsePartialState(areasStateSchemaPartial, state.areas, { warn: true });
				if (areasState.visible != null && typeof areasState.visible === 'boolean') {
					areasLayer.setVisible(areasState.visible);
				}
			}
		}

		if (Facade.is(dataObject)) {
			const facadeContainerState = this.core.getState<GroupContainerState>(dataObject.id);
			for (const facadeGeometryContainer of facadeContainerState.children) {
				if (facadeGeometryContainer.type === 'group-container') {
					this.core.setState(
						facadeGeometryContainer.id,
						parsePartialState(facadeStateSchemaPartial, state, { warn: true }),
					);
				}
			}
		}

		if (Text3DView.is(dataObject)) {
			this.core.setState(dataObject.id, parsePartialState(text3DUpdateStateSchema, state, { warn: true }));
		}

		if (Path.is(dataObject)) {
			for (const segment of dataObject.segments) {
				this.core.setState(segment.id, parsePartialState(pathStateSchemaPartial, state, { warn: true }));
			}
		}

		if (PathSegment.is(dataObject)) {
			this.core.setState(dataObject.id, parsePartialState(pathStateSchemaPartial, state, { warn: true }));
		}

		return;
	}

	private getMapDataObject(id: string) {
		return this.getMapDataInternal()?.getMapDataById(id);
	}

	update = () => {
		this.core.render();
	};

	getMapDataInternal() {
		if (!this.mapData) return;

		return getMapDataInternal(this.mapData);
	}

	getMapData() {
		return this.mapData;
	}

	addMap(mapData: MapData, options?: TShow3DMapOptions): Promise<MapData> {
		return new Promise<MapData>(resolve => {
			let data: MapDataInternal;
			try {
				data = getMapDataInternal(mapData);
			} catch (e) {
				Logger.error('Error adding map', e);
				resolve(mapData);

				return;
			}

			this.mapDataExternal[mapData.mapName] = mapData;
			this.mapData = mapData;

			const { mvf } = data;
			const mvfCenter = mvf['manifest.geojson'].features[0].geometry.coordinates as Position;
			if (this.core.mode !== 'outdoors-overlay') {
				// in overlay mode, we are not in charge of where the camera starts
				this.Camera.set({
					center: new Coordinate(mvfCenter[1], mvfCenter[0]),
				});
			}

			const id = mvf['manifest.geojson'].features[0].properties.name;
			const mapObject = new GeojsonApiMapObject(`mapObject-${id}`, data, options, this);
			this.currentMap = mapObject;
			this.currentMap.loadAllFloors();
			this.currentMap.setFloor(this.currentMap.startingFloorId);

			// We can't reliably use the MVF manifest center coordinate for hiding outdoor layers.
			// The MVF uses the center of editor's initial base map image. We need the geometry center.

			const boundingBox = this.core.getBoundingArea(mapObject.currentFloor.id)!;

			if (boundingBox == null) {
				Logger.error('Could not determine bounding box for map:', mapObject.currentFloor.id);
			}
			const userHiddenLayers = options?.outdoorView?.layersHiddenByGeometry ?? [];
			if (userHiddenLayers.length > 0) {
				let totalBbox: BBox | undefined;
				const polygons: GeoJSON.Feature<GeoJSON.Polygon | GeoJSON.MultiPolygon, any>[] = [];
				// go through each floor-stack, grab the footprint of the default floor
				// and hide the outdoor layers that intersect with the footprint
				for (const floorStack of mapObject.floorStacksById.values()) {
					if (
						floorStack.defaultFloor.footprint &&
						floorStack.defaultFloor.footprint.geometry != null &&
						floorStack.defaultFloor.footprint.geometry?.coordinates.length > 0
					) {
						const footprint = floorStack.defaultFloor.footprint as Feature<Polygon | MultiPolygon, any>;
						const bbox = getBoundingBox(footprint);
						if (totalBbox == null) {
							totalBbox = bbox;
						} else {
							expandBbox(totalBbox, bbox);
						}
						polygons.push(footprint);
					}
				}
				if (polygons.length > 0 && totalBbox != null) {
					this.core.hideOutdoorLayersIntersectingPolygons(totalBbox, polygons, userHiddenLayers);
					this.hiddenOutdoorGeometries.push([totalBbox, polygons, userHiddenLayers]);
				}
			}
			this.mapObjects.push(mapObject);
			data.getMVFFeatureByType('floor').forEach(feature => {
				this.mapsByFloorId[feature.id] = mapObject;
			});
			mapObject.on('floor-change-start', e => {
				this.externalPubSub.publish('floor-change-start', {
					reason: e.reason,
					floor: this.getMapDataInternal()?.getById('floor', e.floorId) ?? this.currentFloor,
					previousFloor: this.getMapDataInternal()?.getById('floor', e.previousFloorId) ?? this.currentFloor,
				});
			});
			mapObject.on('floor-change', e => {
				this.externalPubSub.publish('floor-change', {
					reason: e.reason,
					floor: this.currentFloor,
					previousFloor: this.getMapDataInternal()?.getById('floor', e.previousFloorId) ?? this.currentFloor,
				});
				this.externalPubSub.publish('state-change-internal', {
					currentFloor: this.currentFloor,
					currentFloorStack: this.currentFloorStack,
				});
			});

			// if zoomLevel is specifed we don't do anything here since it's handled in renderer constructor
			if (options?.zoomLevel != null) {
				this.core.camera.setZoomLevel(options.zoomLevel);
				resolve(mapData);
			} else {
				// otherwise we focusOn the map that's just added
				if (this.core.mode !== 'outdoors-overlay' && boundingBox) {
					const insets = options?.screenOffsets ?? {
						top: PERCENT_OF_SCREEN_PADDING * this.core.canvasHeight,
						left: PERCENT_OF_SCREEN_PADDING * this.core.canvasWidth,
						right: PERCENT_OF_SCREEN_PADDING * this.core.canvasWidth,
						bottom: PERCENT_OF_SCREEN_PADDING * this.core.canvasHeight,
					};
					this.core.camera
						.focusOn(boundingBox, {
							duration: ADD_MAP_FOCUS_ON_DURATION,
							padding: insets,
						})
						.then(() => {
							// TODO: focusOn should have option not to change elevation
							this.Camera.setElevation(this.currentFloor.elevation);
							resolve(mapData);
						});
				} else {
					resolve(mapData);
				}
			}
		});
	}

	setFloor(floor: Floor | string, reason?: TFloorChangeReason) {
		return this.currentMap.setFloor(getId(floor), reason);
	}

	setFloorStack(floorStack: FloorStack | string, reason?: TFloorChangeReason) {
		return this.currentMap.setFloorStack(getId(floorStack), reason);
	}

	updateWatermark(options: WatermarkUpdateOptions) {
		this.core.updateWatermark(options);
	}

	get currentFloorStack() {
		const currentFloorStackId = this.currentMap.currentFloorStack.id;
		if (!currentFloorStackId) throw new MappedinError('Missing currentFloorStack.');

		const floorStack = this.mapData?.getById('floor-stack', currentFloorStackId);
		if (!floorStack) throw new MappedinError('FloorStack was not found in map data.');

		return floorStack;
	}

	get currentFloor() {
		// properly handle the floorId missing instead of asking compiler to trust us
		const currentFloorId = this.currentMap.currentFloor.id;
		if (!currentFloorId) throw new MappedinError('Missing currentFloor object from GeoJsonApi');
		// TODO: currentFloor can technically be undefined should we reflect that in typings. Does it make sense for currentFloor to be undefined ever
		const currentFloor = this.mapData?.getById('floor', currentFloorId);
		if (!currentFloor) throw new MappedinError('Missing currentFloor');

		return currentFloor;
	}

	getState<T extends MapElementsWithState | string>(target: T): TGetState<T>;
	getState(target: MapElementsWithState | string): TGetState<MapElementsWithState | string> | undefined {
		if (typeof target === 'object' && Path.is(target)) {
			Logger.warn(
				'Cannot get state of a Path directly. Use getState() on individual segments within Path.segments[] instead.',
			);
		}
		const targetId = getTargetID(target, this);
		const state = this.core.getState(targetId);
		if (!state) return undefined;

		if ([WALLS.Interior, WALLS.Exterior].includes(target as WALLS) && state.type === 'geometry-group') {
			return getWallsState(state);
		} else if ([DOORS.Interior, DOORS.Exterior].includes(target as DOORS) && state.type === 'geometry-group') {
			return getDoorsState(state);
		} else if (state.type === 'geometry') {
			return getGeometryState(state);
		} else if (
			state.type === 'label' &&
			state.options.appearance &&
			state.options.rank !== undefined &&
			state.options.interactive !== undefined
		) {
			return getLabelState(state);
		} else if (
			state.type === 'marker' &&
			state.options?.rank !== undefined &&
			state.options.interactive !== undefined
		) {
			return getMarkerState(state, this.Markers.getById(state.id.toString())?.marker);
		} else if (state.type === 'model') {
			return getModelState(state);
		} else if (state.type === 'image') {
			return getImageState(state);
		} else if (state.type === 'text3d') {
			return getText3DState(state);
		} else if (state.type === 'geometry-group' && target && typeof target !== 'string' && Shape.is(target)) {
			return getShapeState(state);
		} else if (state.type === 'path') {
			return getPathState(state);
		} else if (state.type === 'group-container' && typeof target !== 'string' && Floor.is(target)) {
			return getFloorState(state, this.currentMap.floorsById.get(target.id), this.core);
		} else if (
			state.type === 'group-container' &&
			state.children.length > 0 &&
			typeof target !== 'string' &&
			Facade.is(target)
		) {
			// for facades lets update the child state but leave the container visible so that it can be focusable
			const facadeGeometryState = this.core.getState<GroupContainerState>(state.children[0].id);

			return getFacadeState(facadeGeometryState);
		}

		return undefined;
	}

	setHoverColor(c: string) {
		this.core.setState({ hoverColor: c });
	}
	getHoverColor() {
		const state = this.core.getState();

		return state?.type === 'map-view' ? state.hoverColor : undefined;
	}

	/**
	 * Create a coordinate from a screen coordinate
	 * @param x - The x coordinate of the screen
	 * @param y - The y coordinate of the screen
	 * @param floor - The floor to use for the coordinate
	 * @returns The coordinate
	 */
	createCoordinateFromScreenCoordinate(x: number, y: number, floor?: Floor) {
		// for legacy support, we can pass in a floor object
		// which uses the world plane
		if (floor != null) {
			const coord = this.core.projectScreenXYToCoordinate(x, y, true);

			return coord ? new Coordinate(coord.coordinate[1], coord.coordinate[0], floor.id) : undefined;
		}

		// determine the floor and use all geometry to project instead of just the world plane
		const result = this.core.projectScreenXYToCoordinate(x, y);
		if (result == null || result.coordinate == null) return undefined;

		const { coordinate, groupContainers } = result;

		const floorId = groupContainers
			?.find(c => {
				const containerId = c.userData.entityId.toString();

				return containerId.includes('f_') || containerId.includes('m_');
			})
			?.userData.entityId.toString();

		return new Coordinate(coordinate[1], coordinate[0], floorId);
	}

	getScreenCoordinateFromCoordinate(coordinate: Coordinate) {
		let altitude = 0;
		if (coordinate.floorId != null) {
			const floor = this.mapData?.getById('floor', coordinate.floorId);
			if (floor != null) {
				altitude = this.getState(floor)?.altitude ?? 0;
			}
		}

		return this.core.projectCoordinateToScreenXY([coordinate.longitude, coordinate.latitude, altitude]);
	}

	isInView(target: Space | MapObject | Label | Marker | string) {
		return this.core.isInView(typeof target === 'string' ? target : target.id);
	}

	getInView<T extends string>(type: T, options?: TGetInViewOptions): MapFeatureOfType<T>[] {
		const ids = this.core.queryGeometry2DInView({
			screenOffsets: options?.screenOffsets,
			fullyContains: options?.fullyContains,
		});

		if (ids.length === 0) return [];

		let result: unknown[] = [];
		switch (type) {
			case Label.__type:
				result = ids
					.map(id => this.Labels.getById(id))
					.filter(l => l != null)
					.map(l => l.label);
				break;

			case Marker.__type:
				result = ids
					.map(id => this.Markers.getById(id))
					.filter(m => m != null)
					.map(m => m.marker);
		}

		return result as MapFeatureOfType<T>[];
	}

	async __EXPERIMENTAL__auto(): Promise<{ labels: Label[] }> {
		const mapData = this.getMapDataInternal();
		if (!mapData) {
			throw new Error('Map data not found');
		}
		return await auto(mapData, this);
	}

	tween<T extends Record<string, unknown>>(object: T) {
		const tween = new Tween<T>(object);
		this.core.getExternalTweenGroup().add(tween);
		this.core.render();

		return tween;
	}

	removeTween(tween: Tween<any>) {
		this.core.getExternalTweenGroup().remove(tween);
	}

	tweenGroup() {
		return this.core.getExternalTweenGroup();
	}

	/**
	 * @internal
	 */
	get __core() {
		return this.core;
	}

	/**
	 * Get camera state for synchronization
	 * @private
	 */
	private getCameraState(): TStateChangedInternalPayload['Camera'] {
		return this.core.camera.getState() satisfies TStateChangedInternalPayload['Camera'];
	}

	/**
	 * Get navigation state for synchronization
	 * @private
	 */
	private getNavigationState() {
		return {
			isMultiFloor: this.Navigation.isMultiFloor,
			floorStacks: this.Navigation.floorStacks,
			floors: this.Navigation.floors,
			activeDirections: this.Navigation.activeDirections,
			activePath: this.Navigation.activePath,
			paths: this.Navigation.paths,
		};
	}

	/**
	 * Get all current state information for synchronization
	 * @internal
	 */
	getAllState() {
		const state: TStateChangedInternalPayload = {
			currentFloor: this.currentFloor,
			currentFloorStack: this.currentFloorStack,
			Camera: this.getCameraState(),
			Navigation: this.getNavigationState(),
			manualFloorVisibility: this.manualFloorVisibility,
		} as TStateChangedInternalPayload;

		return state;
	}

	/**
	 * Publish all current state information
	 * @internal
	 */
	publishAllState() {
		this.externalPubSub.publish('state-change-internal', this.getAllState());
	}

	clear() {
		this.Navigation.clear();
		this.Shapes.removeAll();
		this.Paths.removeAll();
		this.Models.removeAll();
		this.Markers.removeAll();
		this.Image3D.removeAll();
		this.Labels.removeAll();
	}

	destroy() {
		this.clear();
		this.core.destroy();
	}

	getOptimalVisualDistanceBetweenFloors(floors: Floor[], debug = false) {
		const currentMap = this.currentMap;
		if (!currentMap) {
			Logger.error(`Cannot compute optimal visual distance between floors. Missing current map.`);
			return;
		}

		if (floors.length < 2) {
			Logger.warn(`Cannot compute optimal visual distance between floors. Must provide at least 2 floors.`);
			return;
		}

		const floor = currentMap.renderer.getInternalState().geometry3DMap.get(floors[0].id);
		const floor2 = currentMap.renderer.getInternalState().geometry3DMap.get(floors[1].id);
		if (!floor || !floor2) {
			Logger.warn(`Cannot compute optimal visual distance between floors. Floors not found in current map.`);
			return;
		}

		assertType(floor, 'group-container');
		assertType(floor2, 'group-container');

		if (debug) {
			// Visualize bounding boxes of floors
			const floorBox = new Box3();
			floorBox.setFromObject(floor);
			const floor2Box = new Box3();
			floor2Box.setFromObject(floor2);

			// Remove existing debug boxes if any
			floor.children.forEach(child => {
				if (child instanceof Mesh && child.userData.debugBox) {
					// floor.remove(child);
					destroyObject3D(child);
				}
			});

			floor2.children.forEach(child => {
				if (child instanceof Mesh && child.userData.debugBox) {
					// floor2.remove(child);
					destroyObject3D(child);
				}
			});

			if (!isFiniteBox(floorBox) || !isFiniteBox(floor2Box)) {
				return;
			}

			// Create mesh for floor bounding box
			const floorBoxGeometry = new BoxGeometry(
				floorBox.max.x - floorBox.min.x,
				floorBox.max.y - floorBox.min.y,
				floorBox.max.z - floorBox.min.z,
			);
			const floorBoxMaterial = new MeshBasicMaterial({ color: 0x0000ff, transparent: true, opacity: 0.5 });
			const floorBoxMesh = new Mesh(floorBoxGeometry, floorBoxMaterial);
			floorBoxMesh.position.set(
				(floorBox.min.x + floorBox.max.x) / 2,
				(floorBox.min.y + floorBox.max.y) / 2,
				(floorBox.min.z + floorBox.max.z) / 2,
			);
			floorBoxMesh.userData.debugBox = true;

			// Create mesh for floor2 bounding box
			const floor2BoxGeometry = new BoxGeometry(
				floor2Box.max.x - floor2Box.min.x,
				floor2Box.max.y - floor2Box.min.y,
				floor2Box.max.z - floor2Box.min.z,
			);
			const floor2BoxMaterial = new MeshBasicMaterial({ color: 'cyan', transparent: true, opacity: 0.5 });
			const floor2BoxMesh = new Mesh(floor2BoxGeometry, floor2BoxMaterial);
			floor2BoxMesh.position.set(
				(floor2Box.min.x + floor2Box.max.x) / 2,
				(floor2Box.min.y + floor2Box.max.y) / 2,
				(floor2Box.min.z + floor2Box.max.z) / 2,
			);
			floor2BoxMesh.userData.debugBox = true;

			// Add meshes to the floor objects instead of scene
			floor.add(floorBoxMesh);
			floor2.add(floor2BoxMesh);
		}

		const result = currentMap.renderer.computeMinZOffsetBetweenContainers(floor, floor2);
		if (result === -1) {
			return;
		}

		return result;
	}
}

/**
 * Sets the base URL directory where worker scripts are hosted for CSP compatibility.
 *
 * This function configures both the MapLibre and collision system workers to load from
 * external URLs instead of using inline blob URLs. The SDK expects two specific worker
 * files to be available in the provided directory:
 * - `maplibre-worker.csp.js` - For MapLibre map rendering
 * - `collision-worker.csp.js` - For the collision detection system
 *
 * Using this approach enables compatibility with strict Content Security Policies
 * that block unsafe-eval and blob: URLs.
 *
 * @param baseUrl - Base URL directory where worker scripts are hosted (without trailing slash)
 *                  Example: "https://cdn.example.com/workers"
 *
 * @example
 * ```typescript
 * import { setWorkersUrl } from '@mappedin/mappedin-js';
 *
 * // Call before initializing any maps
 * setWorkersUrl('https://cdn.example.com/workers');
 * // This will load:
 * // - https://cdn.example.com/workers/maplibre-worker.csp.js
 * // - https://cdn.example.com/workers/collision-worker.csp.js
 * ```
 *
 * @remarks
 * - The worker files can be found in the published package at:
 *   `node_modules/@mappedin/mappedin-js/lib/esm/workers/`
 * - For deployment, copy these files to your web server or CDN
 * - A better approach is to add these files to your build process to ensure
 *   they're always in sync with your application
 * - Call this function before creating any map instances
 */
export function setWorkersUrl(baseUrl: string) {
	if (baseUrl === '') {
		setCollisionWorkerUrl('');
		setMaplibreWorkerUrl('');
		return;
	}

	// ensure the file name matches the build script in build.mjs
	setMaplibreWorkerUrl(`${baseUrl}/${MAPLIBRE_WORKER_PATH}`);
	setCollisionWorkerUrl(`${baseUrl}/${COLLISION_WORKER_PATH}`);
}
