import type { EnterpriseCategoryId, EnterpriseLocationId, EnterpriseLocationInstanceId } from '@mappedin/mvf-cms';
import type { GeometryFeature, GeometryId } from '@mappedin/mvf-core';
import { typedEntries, typedKeys } from '@mappedin/mvf-core/utils';
import type { LocationCategoryId, LocationId, LocationInstanceId } from '@mappedin/mvf-locations';
import type { AreaId } from '../extensions/area.js';
import type { WindowId } from '../extensions/window.js';
import type { NodeId, ObstructionId } from '../types/core.js';
import type { SpaceId } from '../types/core.js';
import type { MVFv2_STANDARD_MVFv3 } from './index.js';

export function toSpaceId(id: GeometryId): SpaceId {
	return id.replace(/^g_/, 's_');
}

export function toObstructionId(id: GeometryId): ObstructionId {
	return id.replace(/^g_/, 'o_');
}
export function convertGeometryId(id: GeometryId, obstructionIds: Set<GeometryId>): SpaceId | ObstructionId {
	if (obstructionIds.has(id)) {
		return toObstructionId(id);
	}
	return toSpaceId(id);
}

export function toWindowId(id: GeometryId): WindowId {
	return id.replace(/^g_/, 'w_');
}

export function convertLocationInstanceId(id: LocationInstanceId): EnterpriseLocationInstanceId {
	return id.replace(/^loci_/, 'eli_') as EnterpriseLocationInstanceId;
}

export function convertLocationId(id: LocationId): EnterpriseLocationId {
	return id.replace(/^loc_/, 'el_') as EnterpriseLocationId;
}

export function convertLocationCategoryId(id: LocationCategoryId): EnterpriseCategoryId {
	return id.replace(/^lcat_/, 'ec_') as EnterpriseCategoryId;
}

export type GeometryIdConverter = (id: GeometryId) => SpaceId | ObstructionId;

export function generateIdConverter(obstructionIds: Set<GeometryId>): GeometryIdConverter {
	return (id: GeometryId) => convertGeometryId(id, obstructionIds);
}

export function convertAreaId(id: GeometryId): AreaId {
	return id.replace(/^g_/, 'ar_');
}

export function convertNodeToSpaceId(id: NodeId): SpaceId {
	return id.replace(/^n_/, 's_');
}

export function splitMultiPartGeometryFeatureWithNewId<T extends string>(
	feature: GeometryFeature,
	newId: T,
	kind?: string,
): PartiallyTransformedGeometryFeature<T>[] {
	switch (feature.geometry.type) {
		case 'MultiPoint':
		case 'MultiLineString':
		case 'MultiPolygon': {
			const result: PartiallyTransformedGeometryFeature<T>[] = [];
			let index = 0;
			const type = feature.geometry.type.replace('Multi', '') as 'Point' | 'LineString' | 'Polygon';
			for (const coordinates of feature.geometry.coordinates) {
				result.push({
					...feature,
					geometry: { type, coordinates } as GeometryFeature['geometry'],
					properties: { ...feature.properties, id: `${newId}-${index++}` as T, kind },
				});
			}
			return result;
		}
		default:
			return [{ ...feature, properties: { ...feature.properties, id: newId, kind } }];
	}
}

/**
 * A geometry feature that has been partially transformed into v2. In particular,
 * the `id` has been converted to the new ID type, and the `kind` has been added.
 * These are also probably not multi geometry features, but this is not actually enforced.
 *
 */
export type PartiallyTransformedGeometryFeature<T> = Omit<GeometryFeature, 'properties'> & {
	properties: Omit<GeometryFeature['properties'], 'id'> & { id: T; kind: string | undefined };
};

export type MappedGeometry<T> = Map<
	GeometryId,
	{
		ids: T[];
		features: PartiallyTransformedGeometryFeature<T>[];
		anchors: {
			geometryId: T;
			floorId: string;
		}[];
	}
>;

export function mapGeometryToOtherIds<T extends string>(
	geometry: { feature: GeometryFeature; floorId: string }[],
	xIds: { has: (id: GeometryId) => boolean }, // Probably a set, but can be any object with that function
	convertToX: (id: GeometryId) => T,
	kindsMap: Map<GeometryId, string | undefined>,
): MappedGeometry<T> {
	const result: MappedGeometry<T> = new Map();
	for (const { feature, floorId } of geometry) {
		const id = feature.properties.id;
		const kind = kindsMap.get(id);
		if (xIds.has(id)) {
			const converted = splitMultiPartGeometryFeatureWithNewId(feature, convertToX(id), kind);
			const ids = converted.map((x) => x.properties.id);
			const anchors = ids.map((geometryId) => ({
				geometryId,
				floorId,
			}));
			result.set(id, { ids, features: converted, anchors });
		}
	}
	return result;
}

export type GeometryMaps = {
	space: MappedGeometry<SpaceId>;
	obstruction: MappedGeometry<ObstructionId>;
	area: MappedGeometry<AreaId>;
	window: MappedGeometry<WindowId>;
	object: MappedGeometry<ObstructionId>;
};

export function createGeometryMaps(
	v3: Partial<Pick<MVFv2_STANDARD_MVFv3, 'geometry' | 'kinds' | 'nonwalkable' | 'walkable' | 'connections'>>,
): GeometryMaps {
	const result: GeometryMaps = {
		space: new Map(),
		obstruction: new Map(),
		area: new Map(),
		window: new Map(),
		object: new Map(),
	};
	if (!v3.geometry) {
		return result;
	}

	const kindsMap = createKindsMap(v3);

	const flatFeatures = typedEntries(v3.geometry)
		.flatMap(([floor, featureCollection]) =>
			(featureCollection?.features ?? []).map((x) => ({ feature: x, floorId: floor })),
		)
		.flat();

	const walkableIds = new Set(
		Object.values(v3.walkable ?? {})
			.filter((x) => x !== undefined)
			.flatMap(typedKeys),
	);
	const obstructionIds = new Set(
		Object.values(v3.nonwalkable ?? {})
			.filter((x) => x !== undefined)
			.flatMap(typedKeys),
	);

	const areaIds = new Set(
		Array.from(kindsMap.entries())
			.filter(([_, kind]) => kind === 'area')
			.map(([id]) => id),
	);

	const objectsIds = new Set(
		Array.from(kindsMap.entries())
			.filter(([_, kind]) => kind === 'object')
			.map(([id]) => id),
	);

	const windowIds = new Set(
		Array.from(kindsMap.entries())
			.filter(([_, kind]) => kind === 'window')
			.map(([id]) => id),
	);

	if (walkableIds.size > 0) {
		result.space = mapGeometryToOtherIds(flatFeatures, walkableIds, toSpaceId, kindsMap);
	}
	if (obstructionIds.size > 0) {
		result.obstruction = mapGeometryToOtherIds(flatFeatures, obstructionIds, toObstructionId, kindsMap);
	}
	if (areaIds.size > 0) {
		result.area = mapGeometryToOtherIds(flatFeatures, areaIds, convertAreaId, kindsMap);
	}
	if (objectsIds.size > 0) {
		result.object = mapGeometryToOtherIds(flatFeatures, objectsIds, toObstructionId, kindsMap);
	}

	// I don't think anyone actually cares about window ids to keep the MVFv3 ones vs re-writing them like this
	if (windowIds.size > 0) {
		result.window = mapGeometryToOtherIds(flatFeatures, windowIds, toWindowId, kindsMap);
	}

	// Incase we have no information about the geometry, we will just map everything to a space
	// This is the Enterprise case.
	// If Enterprise ever produces v3 data with areas, it will need to fill in walkable to get spaces again.
	if (walkableIds.size === 0 && obstructionIds.size === 0 && areaIds.size === 0) {
		result.space = mapGeometryToOtherIds(
			flatFeatures,
			new Set(flatFeatures.map((x) => x.feature.properties.id)),
			toSpaceId,
			kindsMap,
		);
	}

	return result;
}

export function createKindsMap(v3: Pick<MVFv2_STANDARD_MVFv3, 'kinds' | 'connections'>): Map<GeometryId, string> {
	const kindsMap = new Map(
		Object.values(v3.kinds ?? {})
			.filter((x) => x !== undefined)
			.flatMap(typedEntries),
	) as Map<GeometryId, string>;

	// All connections (except doors) should override the kind of their geometry to connection.<type>
	for (const connection of v3.connections?.filter((c) => c.type !== 'door') ?? []) {
		for (const entrance of connection.entrances) {
			kindsMap.set(entrance.geometryId, `connection.${connection.type}`);
		}
		for (const exit of connection.exits) {
			kindsMap.set(exit.geometryId, `connection.${connection.type}`);
		}
	}
	return kindsMap;
}
