import type { NodeFeature } from '../types/node';
import type { Position } from 'geojson';
import { getLineIntersection } from './get-line-intersection';
import { getOffsetLine } from './get-offset-line';
import { getAngle } from './get-angle';
import { radToDegNormalized } from './rad-to-deg-normalized';

const KM_PER_DEGREE_LAT = 111000;

/**
 * Determines if there is a clear line of sight between two nodes, considering possible obstructions.
 * The function uses a buffer radius to create a corridor around the direct line of sight and checks for
 * intersections with given edges that might block the view. The function can handle both single and dual
 * raycast approaches depending on the buffer radius provided.
 *
 * @param {NodeFeature} originFeature - The starting node, which includes geographic coordinates.
 * @param {NodeFeature} destinationFeature - The ending node, which includes geographic coordinates.
 * @param {Position[][]} edges - An array of line segments ([Position, Position]) representing potential obstructions.
 * @param {number} bufferRadius - The radius to create a corridor around the line of sight. A radius of 0 uses a single raycast.
 * @hidden
 * @returns {boolean} True if there is a clear line of sight, false if obstructed.
 */
export function hasLineOfSight(
	originFeature: NodeFeature,
	destinationFeature: NodeFeature,
	edges: Position[][] = [],
	bufferRadius = 0,
) {
	let isVisible = true;
	const origin = originFeature.geometry.coordinates as Position;
	const destination = destinationFeature.geometry.coordinates as Position;
	const angleLeft = radToDegNormalized(getAngle(origin, destination) - Math.PI / 2);
	const angleRight = radToDegNormalized(getAngle(origin, destination) + Math.PI / 2);

	const metersToDegreesLat = bufferRadius / KM_PER_DEGREE_LAT; // 111km per degree of latitude
	const metersToDegreesLon = bufferRadius / (KM_PER_DEGREE_LAT * Math.cos((origin[1] * Math.PI) / 180)); // Adjust for latitude

	// optimize by using an axis aligned bounding box to skip any edges that don't intersect with the AABB of the ray
	const minX = Math.min(origin[1], destination[1]) - metersToDegreesLat;
	const maxX = Math.max(origin[1], destination[1]) + metersToDegreesLat;
	const minY = Math.min(origin[0], destination[0]) - metersToDegreesLon;
	const maxY = Math.max(origin[0], destination[0]) + metersToDegreesLon;

	for (const edge of edges) {
		if (
			Math.max(edge[0][1], edge[1][1]) < minX ||
			Math.min(edge[0][1], edge[1][1]) > maxX ||
			Math.max(edge[0][0], edge[1][0]) < minY ||
			Math.min(edge[0][0], edge[1][0]) > maxY
		) {
			continue;
		}

		if (bufferRadius > 0) {
			// Create offset lines to the left and right of the original line by the buffer radius
			const parallelLeft = getOffsetLine(origin, destination, bufferRadius, angleLeft);
			const parallelRight = getOffsetLine(origin, destination, bufferRadius, angleRight);

			// Check for intersections with the buffered lines
			if (
				getLineIntersection(parallelLeft[0], parallelLeft[1], edge[0], edge[1]) ||
				getLineIntersection(parallelRight[0], parallelRight[1], edge[0], edge[1])
			) {
				isVisible = false;

				return false; // Exit loop early if an intersection is found
			}
		} else {
			// Check for an intersection with a single direct line
			if (getLineIntersection(origin, destination, edge[0], edge[1])) {
				isVisible = false;

				return false; // Exit loop early if an intersection is found
			}
		}
	}

	return isVisible;
}
