import type {
	Feature,
	Flags,
	NavigationFlagDeclaration,
	ObstructionProperties,
	ParsedMVFLocalePack,
	Point,
	Polygon,
	SpaceProperties,
} from '@mappedin/mvf-v2';
import type { FloorStackWithLocale, FloorWithLocale, LanguagePack, MVFFeature } from './types';
import Coordinate from './coordinate';
import turfCenter from '@turf/center';
import { Logger } from '@packages/internal/common';

export function parseLocalePackToLanguagePack(downloaded: ParsedMVFLocalePack): LanguagePack {
	const locationsById =
		downloaded.enterprise?.locations?.reduce((acc, cur) => {
			acc[cur.id] = cur;

			return acc;
		}, {}) || {};

	// combine locations and location instances into one object for the language pack
	for (const instance of downloaded.enterprise?.locationInstances || []) {
		locationsById[instance.id] = instance;
	}

	const floorStacksById: Record<string, FloorStackWithLocale> = {};
	for (const floorStack of (downloaded['floorstack.json'] || []) as FloorStackWithLocale[]) {
		floorStacksById[floorStack.id] = floorStack;
	}

	const floorsById: Record<string, FloorWithLocale> = {};
	for (const floor of downloaded['floor.geojson']?.features || []) {
		if (floor.properties && floor.properties.id) {
			floorsById[floor.properties.id] = floor.properties;
		}
	}

	return {
		type: 'downloaded',
		data: downloaded,
		optimized: {
			'enterprise-location': locationsById,
			'enterprise-category':
				downloaded.enterprise?.categories?.reduce((acc, cur) => {
					acc[cur.id] = cur;

					return acc;
				}, {}) || {},
			'floor-stack': floorStacksById,
			floor: floorsById,
		},
	} satisfies LanguagePack;
}

export function isMappedinCdnUrl(url: string) {
	return /^https?:\/\/cdn\.mappedin\.com/.test(url);
}

export function hasSasToken(url: string) {
	const params = url.split('?')[1];

	return Boolean(params?.includes('sv='));
}

export function getCenterCoordinate(mvfData: MVFFeature, floorId?: Coordinate['floorId']): Coordinate {
	const dataId = mvfData?.properties?.id || 'unknown';

	if (mvfData.geometry == null) {
		Logger.error(`Cannot get the center coordinate for "${dataId}" because it has no geometry.`);

		return new Coordinate({
			latitude: 0,
			longitude: 0,
			floorId,
		});
	}

	try {
		if (mvfData.geometry.type === 'LineString') {
			// Since lineStrings are usually small, we can assume the earth is locally flat to find the center
			const [[startLon, startLat], [endLon, endLat]] = mvfData.geometry.coordinates;
			const longitude = (startLon + endLon) / 2;
			const latitude = (startLat + endLat) / 2;

			return new Coordinate({
				latitude,
				longitude,
				floorId,
			});
		} else if (mvfData.geometry.type === 'Point') {
			const [longitude, latitude] = mvfData.geometry.coordinates;

			return new Coordinate({
				latitude,
				longitude,
				floorId,
			});
		} else if (mvfData.geometry.type === 'Polygon') {
			const polygonFeature = mvfData as Feature<Polygon, SpaceProperties | ObstructionProperties>;

			if (polygonFeature.properties.center != null) {
				const [longitude, latitude] = polygonFeature.properties.center;

				return new Coordinate({
					latitude,
					longitude,
					floorId,
				});
			}
		}
	} catch (e) {
		// do nothing, fallback to turf
	}

	try {
		// Fallback: try to get the center with turf for any geometry type
		const [longitude, latitude] = (turfCenter(mvfData) as Feature<Point, never>).geometry.coordinates;

		return new Coordinate({
			latitude,
			longitude,
			floorId,
		});
	} catch (e) {
		Logger.error(`Failed to get center coordinate for "${dataId}" ${e instanceof Error ? e.stack : ''}`);

		return new Coordinate({
			latitude: 0,
			longitude: 0,
			floorId,
		});
	}
}

/**
 * Checks if a flag is set in an object WithFlags.
 *
 * First find the NavigationFlagDeclaration using the key, then check
 * a flagged object with this function to see if it's present. You can
 * safely pass an undefined flag (the check will return false). This is useful when
 * trying to use a well-known flag, which may or may not be actually present in the MVF.
 *
 * @category Utils
 * @example
 * ```typescript
 * const obj: WithFlags = { flags: [1] };
 * const flag: NavigationFlagDeclaration = { index: 0, bit: 0 };
 * const hasAccessible = hasFlag(obj, flag); // true
 * ```
 *
 * @param withFlags - The WithFlags object to check
 * @param flag - The flag to check for
 * @returns true if the flag is set, false otherwise
 * @see {@link NavigationFlagDeclaration}
 * @see {@link WithFlags}
 */
export function hasFlag(withFlags: { flags: Flags }, flag?: NavigationFlagDeclaration) {
	/**
	 * This was copied from the @mappedin/mvf-navigation-flags package.
	 *
	 * TODO: we should use the export from @mappedin/mvf-navigation-flags
	 * but jest can't resolve that module and it's too much of a headache to fix right now.
	 */
	return flag && (withFlags.flags[flag.index] & (1 << flag.bit)) !== 0;
}
