import type { BBox, Feature, LineString, MultiPolygon, Point, Polygon } from 'geojson';
import turfBbox from '@turf/bbox';
import type { MapDataInternal } from '.';

export default abstract class BaseMapData {
	// TODO: abstract __type, is()
	/**
	 * The identity of the map data.
	 */
	readonly id: string;

	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	constructor(id: string, _data: MapDataInternal) {
		this.id = id;
	}

	#geoJSONBoundingBox?: BBox = undefined;

	/**
	 * Gets the bounding box of the geoJSON geometry.
	 *
	 * @returns {BBox} The bounding box of the geoJSON geometry
	 */
	get geoJSONBoundingBox() {
		if (this.#geoJSONBoundingBox) {
			return this.#geoJSONBoundingBox;
		}

		if ('bbox' in this.geoJSON.geometry && this.geoJSON.geometry.bbox) {
			return this.geoJSON.geometry.bbox;
		}

		this.#geoJSONBoundingBox = turfBbox(this.geoJSON as Feature<Polygon | MultiPolygon>);

		return this.#geoJSONBoundingBox;
	}

	get geoJSON(): Feature<Polygon | MultiPolygon | LineString | Point, null> {
		return { properties: null, type: 'Feature', geometry: { type: 'Polygon', coordinates: [[]] } };
	}
}
