import {
	Annotation,
	Coordinate,
	Door,
	MapObject,
	PointOfInterest,
	Space,
	Node,
	EnterpriseLocation,
	Area,
	Facade,
} from '../map-data-objects';

export function getCoordinateFromStuffs(
	thing:
		| Space
		| Door
		| Coordinate
		| MapObject
		| PointOfInterest
		| Annotation
		| Node
		| EnterpriseLocation
		| Area
		| Facade,
) {
	let coordinate: Coordinate;
	if (Space.is(thing) || Door.is(thing) || MapObject.is(thing) || Area.is(thing) || Facade.is(thing)) {
		coordinate = thing.center;
	} else if (PointOfInterest.is(thing) || Annotation.is(thing)) {
		coordinate = thing.coordinate;
	} else if (Coordinate.is(thing)) {
		coordinate = thing;
	} else if (Node.is(thing)) {
		coordinate = thing.coordinate;
	} else if (EnterpriseLocation.is(thing)) {
		coordinate = thing.coordinates[0];
	} else {
		return undefined;
	}

	return coordinate;
}
