import { LineBasicMaterial } from 'three';
import vertexShader from './outline-vertex.glsl';
import fragmentShader from './outline-fragment.glsl';

/**
 * Custom LineBasicMaterial that supports per-vertex visibility and opacity.
 */
export class OutlineLineMaterial extends LineBasicMaterial {
	constructor(parameters?: any) {
		super(parameters);

		this.onBeforeCompile = shader => {
			shader.vertexShader = vertexShader;
			shader.fragmentShader = fragmentShader;
		};
	}
}
