import { Coordinate } from '../map-data-objects';
import type { Path } from '../map-view-objects';
import { PathSegment } from '../map-view-objects';

const EARTH_RADIUS_M = 6371008.8;

// cache the projected coordinates for each segment and origin coordinate
const cartesianToCoordinateCache = new WeakMap<PathSegment, Map<Coordinate, { x: number; y: number }[]>>();

export const ms = async (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export function computePathCoordinateFraction(segment: PathSegment, coordinate: Coordinate, origin: Coordinate) {
	const totalDistance = segment.distance;

	const remainingPathSegment = getRemainingPathSegment(segment, coordinate, origin);

	if (!remainingPathSegment) {
		return 0;
	}

	return (totalDistance - remainingPathSegment?.distance) / totalDistance;
}

/**
 * Projects an array of geographic coordinates to a local Cartesian plane where 1 unit = 1 meter.
 * Uses the specified origin coordinate as (0, 0) and projects all coordinates relative to it.
 *
 * This is particularly useful for indoor mapping where you need precise distance calculations
 * in a local coordinate system, and you want to control where the origin point is located.
 *
 * @param coordinates Array of Coordinate objects to project
 * @param origin The coordinate to use as the origin (0, 0) point
 * @returns Array of {x, y} points in meters relative to the origin coordinate
 * @example
 * ```typescript
 * const origin = new Coordinate({ latitude: 43.8613, longitude: -78.9459 });
 * const coords = [
 *   new Coordinate({ latitude: 43.8613, longitude: -78.9459 }),
 *   new Coordinate({ latitude: 43.8612, longitude: -78.9458 })
 * ];
 * const projected = projectToCartesianPlane(coords, origin);
 * // Returns: [{ x: 0, y: 0 }, { x: 7.84, y: -11.12 }] (approximate)
 * ```
 */
// TODO: weakmap cache each segment so we don't have to project the same segment multiple times
export function projectSegmentToCartesianPlane(segment: PathSegment, origin: Coordinate): { x: number; y: number }[] {
	if (segment.coordinates.length === 0) {
		return [];
	}

	if (cartesianToCoordinateCache.has(segment) && cartesianToCoordinateCache.get(segment)!.has(origin)) {
		return cartesianToCoordinateCache.get(segment)!.get(origin)!;
	}

	const projectedCoords = segment.coordinates.map(coord => projectCoordinateToCartesianPlane(coord, origin));
	cartesianToCoordinateCache.set(segment, new Map([[origin, projectedCoords]]));
	return projectedCoords;
}

function projectCoordinateToCartesianPlane(coordinate: Coordinate, origin: Coordinate): { x: number; y: number } {
	// Use the provided origin coordinate as the reference point
	const originLat = origin.latitude;
	const originLon = origin.longitude;

	const deltaLat = coordinate.latitude - originLat;
	const deltaLon = coordinate.longitude - originLon;

	// Convert to meters using approximate Earth radius (6,378,137 meters)
	// For latitude: 1 degree ≈ 111,320 meters (constant)
	// For longitude: 1 degree ≈ 111,320 * cos(latitude) meters (varies by latitude)

	const metersPerDegreeLatitude = (Math.PI * EARTH_RADIUS_M) / 180;
	const metersPerDegreeLongitude = metersPerDegreeLatitude * Math.cos((originLat * Math.PI) / 180);

	// Convert to x,y coordinates in meters
	const x = deltaLon * metersPerDegreeLongitude;
	const y = deltaLat * metersPerDegreeLatitude;

	return { x, y };
}

/**
 * Finds the closest intersection point on path segments to a given coordinate.
 * Projects all coordinates to Cartesian space using the point as origin, then finds
 * the closest point on any segment and converts it back to geographic coordinates.
 *
 * @param segments Array of PathSegment objects to search through
 * @param point The coordinate to use as reference point (origin in Cartesian space)
 * @param method Method to use for finding intersection ('distance' finds closest point)
 * @returns The closest coordinate on any segment, or undefined if no segments provided
 * @example
 * ```typescript
 * const segments = [pathSegment1, pathSegment2];
 * const userLocation = new Coordinate({ latitude: 43.8613, longitude: -78.9459 });
 * const closest = findSegmentIntersection(segments, userLocation, 'distance');
 * // Returns the closest point on any path segment to the user's location
 * ```
 */
export function findSegmentIntersection(segments: PathSegment[], point: Coordinate): Coordinate | undefined {
	if (segments.length === 0) {
		return undefined;
	}

	let closestPoint: { x: number; y: number } | undefined;
	let closestDistance = Infinity;

	// Process each segment
	for (let segmentIndex = 0; segmentIndex < segments.length; segmentIndex++) {
		const segment = segments[segmentIndex];

		// Project segment coordinates to Cartesian plane using point as origin
		const projectedCoords = projectSegmentToCartesianPlane(segment, point);

		// Check each line segment within this path segment
		for (let i = 0; i < projectedCoords.length - 1; i++) {
			const start = projectedCoords[i];
			const end = projectedCoords[i + 1];

			// Find closest point on this line segment to origin (0, 0)
			const closest = getClosestPointOnLineSegment(start, end, { x: 0, y: 0 });

			// Calculate distance from origin to this closest point
			const distance = Math.sqrt(closest.x * closest.x + closest.y * closest.y);

			// Keep track of the overall closest point
			if (distance < closestDistance) {
				closestDistance = distance;
				closestPoint = closest;
			}
		}
	}

	if (!closestPoint) {
		return undefined;
	}

	// Convert the closest Cartesian point back to geographic coordinates
	return cartesianToCoordinate(closestPoint, point);
}

/**
 * Finds the closest point on a line segment to a given point.
 * Uses vector projection to find the point on the line segment AB that is closest to point P.
 *
 * @param start Start point of the line segment
 * @param end End point of the line segment
 * @param point Point to find closest point to
 * @returns The closest point on the line segment
 */
function getClosestPointOnLineSegment(
	start: { x: number; y: number },
	end: { x: number; y: number },
	point: { x: number; y: number },
): { x: number; y: number } {
	// Vector from start to end of line segment
	const segmentX = end.x - start.x;
	const segmentY = end.y - start.y;

	// Vector from start to the point
	const pointX = point.x - start.x;
	const pointY = point.y - start.y;

	// Calculate the projection parameter t
	const segmentLengthSq = segmentX * segmentX + segmentY * segmentY;

	// Handle degenerate case where start and end are the same point
	if (segmentLengthSq === 0) {
		return { x: start.x, y: start.y };
	}

	const t = (pointX * segmentX + pointY * segmentY) / segmentLengthSq;

	// Clamp t to [0, 1] to stay within the line segment
	const clampedT = Math.max(0, Math.min(1, t));

	// Calculate the closest point
	return {
		x: start.x + clampedT * segmentX,
		y: start.y + clampedT * segmentY,
	};
}

/**
 * Converts a Cartesian coordinate back to geographic coordinates.
 * Reverses the projection done by projectToCartesianPlane.
 *
 * @param cartesian The Cartesian point to convert
 * @param origin The original origin coordinate used for projection
 * @returns Geographic coordinate
 */
function cartesianToCoordinate(cartesian: { x: number; y: number }, origin: Coordinate): Coordinate {
	// Earth radius and conversion factors (same as in projectToCartesianPlane)
	const earthRadius = EARTH_RADIUS_M; // meters
	const metersPerDegreeLatitude = (Math.PI * earthRadius) / 180;
	const metersPerDegreeLongitude = metersPerDegreeLatitude * Math.cos((origin.latitude * Math.PI) / 180);

	// Convert meters back to decimal degrees
	const deltaLat = cartesian.y / metersPerDegreeLatitude;
	const deltaLon = cartesian.x / metersPerDegreeLongitude;

	// Add to origin to get final coordinates
	const latitude = origin.latitude + deltaLat;
	const longitude = origin.longitude + deltaLon;

	return new Coordinate({
		latitude,
		longitude,
		floorId: origin.floorId,
		verticalOffset: origin.verticalOffset,
	});
}

/**
 * Creates a new PathSegment starting from the closest intersection point to the given coordinate
 * and continuing to the end of the original segment. This is useful for navigation scenarios
 * where you want to calculate remaining distance from a user's current position.
 *
 * @param segment The original path segment to modify
 * @param point The coordinate to find intersection with and use as new starting point
 * @returns A new PathSegment starting from the intersection point, or undefined if no intersection found
 * @example
 * ```typescript
 * const pathSegment = new PathSegment('route1', coordinates, totalDistance);
 * const userLocation = new Coordinate({ latitude: 43.8613, longitude: -78.9459 });
 * const remainingPath = getRemainingPathSegment(pathSegment, userLocation);
 * // Returns a new path segment from user's projected position to the end
 * ```
 */
export function getRemainingPathSegment(
	segment: PathSegment,
	point: Coordinate,
	origin: Coordinate,
): PathSegment | undefined {
	// Find the closest intersection point on this segment
	const intersectionPoint = findSegmentIntersection([segment], point);

	if (!intersectionPoint) {
		return undefined;
	}

	// Project all coordinates to Cartesian space for distance calculations
	const projectedCoords = projectSegmentToCartesianPlane(segment, origin);
	const projectedIntersection = projectCoordinateToCartesianPlane(intersectionPoint, origin);

	// Find which line segment the intersection point lies on
	let segmentIndex = -1;
	let closestDistance = Infinity;

	for (let i = 0; i < projectedCoords.length - 1; i++) {
		const start = projectedCoords[i];
		const end = projectedCoords[i + 1];

		// Find closest point on this line segment to the intersection
		const closest = getClosestPointOnLineSegment(start, end, projectedIntersection);

		// Calculate distance from intersection to this closest point
		const dx = closest.x - projectedIntersection.x;
		const dy = closest.y - projectedIntersection.y;
		const distance = Math.sqrt(dx * dx + dy * dy);

		// If this is very close (within 1 meter), this is our segment
		if (distance < 1 && distance < closestDistance) {
			closestDistance = distance;
			segmentIndex = i;
		}
	}

	// If we couldn't find which segment, use the closest endpoint
	if (segmentIndex === -1) {
		let closestEndpointIndex = 0;
		let closestEndpointDistance = Infinity;

		for (let i = 0; i < projectedCoords.length; i++) {
			const coord = projectedCoords[i];
			const dx = coord.x - projectedIntersection.x;
			const dy = coord.y - projectedIntersection.y;
			const distance = Math.sqrt(dx * dx + dy * dy);

			if (distance < closestEndpointDistance) {
				closestEndpointDistance = distance;
				closestEndpointIndex = i;
			}
		}
		segmentIndex = Math.max(0, closestEndpointIndex - 1);
	}

	// Create new coordinates array starting from intersection point
	const remainingCoordinates: Coordinate[] = [intersectionPoint];

	// Add all coordinates from the segment after the intersection point
	for (let i = segmentIndex + 1; i < segment.coordinates.length; i++) {
		remainingCoordinates.push(segment.coordinates[i]);
	}

	// Create and return the new path segment
	return new PathSegment(`${segment.id}_remaining`, remainingCoordinates);
}

/**
 * Finds the closest path segment to a given coordinate.
 * Projects all coordinates to Cartesian space and finds the segment containing the closest point.
 *
 * @param segments Array of PathSegment objects to search through
 * @param coordinate The coordinate to find the closest segment to
 * @returns The closest PathSegment, or undefined if no segments provided
 * @example
 * ```typescript
 * const segments = [pathSegment1, pathSegment2, pathSegment3];
 * const userLocation = new Coordinate({ latitude: 43.8613, longitude: -78.9459 });
 * const closestSegment = findClosestSegment(segments, userLocation);
 * // Returns the path segment that contains the point closest to the user's location
 * ```
 */
export function findClosestSegment(
	segments: PathSegment[],
	coordinate: Coordinate,
	origin: Coordinate,
): PathSegment | undefined {
	if (segments.length === 0) {
		return undefined;
	}

	let closestSegment: PathSegment | undefined;
	let closestDistance = Infinity;

	const projectedCoordinate = projectCoordinateToCartesianPlane(coordinate, origin);

	// Process each segment to find the one with the closest point
	for (const segment of segments) {
		// Project segment coordinates to Cartesian plane using origin
		const projectedCoords = projectSegmentToCartesianPlane(segment, origin);

		let segmentMinDistance = Infinity;

		// Check each line segment within this path segment
		for (let i = 0; i < projectedCoords.length - 1; i++) {
			const start = projectedCoords[i];
			const end = projectedCoords[i + 1];

			// Find closest point on this line segment to the projected coordinate
			const closest = getClosestPointOnLineSegment(start, end, projectedCoordinate);

			// Calculate distance from projected coordinate to this closest point
			const dx = closest.x - projectedCoordinate.x;
			const dy = closest.y - projectedCoordinate.y;
			const distance = Math.sqrt(dx * dx + dy * dy);

			if (distance < segmentMinDistance) {
				segmentMinDistance = distance;
			}

			// Keep track of the segment with the overall closest point
			if (distance < closestDistance) {
				closestDistance = distance;
				closestSegment = segment;
			}
		}
	}

	return closestSegment;
}

export function getHighlightedPathSegments(
	paths: Path[],
	from: Coordinate,
	to: Coordinate,
	projectionOrigin: Coordinate,
) {
	// Find closest segments for both from and to coordinates in a single pass
	let fromSegment: PathSegment | undefined;
	let toSegment: PathSegment | undefined;
	let fromClosestDistance = Infinity;
	let toClosestDistance = Infinity;

	const projectedFrom = projectCoordinateToCartesianPlane(from, projectionOrigin);
	const projectedTo = projectCoordinateToCartesianPlane(to, projectionOrigin);

	// Single pass through all segments
	for (const path of paths) {
		for (const segment of path.segments) {
			// Skip connection segments
			if (segment.isConnectionSegment) {
				continue;
			}

			// Only process segments on the same floor as the target coordinates
			const segmentFloorId = segment.coordinates[0]?.floorId;
			const isFromFloor = segmentFloorId === from.floorId;
			const isToFloor = segmentFloorId === to.floorId;

			if (!isFromFloor && !isToFloor) {
				continue;
			}

			// Project segment coordinates once for both calculations
			const projectedCoords = projectSegmentToCartesianPlane(segment, projectionOrigin);

			// Check distance to 'from' coordinate if on same floor
			if (isFromFloor) {
				let segmentMinDistanceFrom = Infinity;

				for (let i = 0; i < projectedCoords.length - 1; i++) {
					const start = projectedCoords[i];
					const end = projectedCoords[i + 1];

					const closest = getClosestPointOnLineSegment(start, end, projectedFrom);
					const dx = closest.x - projectedFrom.x;
					const dy = closest.y - projectedFrom.y;
					const distance = Math.sqrt(dx * dx + dy * dy);

					if (distance < segmentMinDistanceFrom) {
						segmentMinDistanceFrom = distance;
					}
				}

				if (segmentMinDistanceFrom < fromClosestDistance) {
					fromClosestDistance = segmentMinDistanceFrom;
					fromSegment = segment;
				}
			}

			// Check distance to 'to' coordinate if on same floor
			if (isToFloor) {
				let segmentMinDistanceTo = Infinity;

				for (let i = 0; i < projectedCoords.length - 1; i++) {
					const start = projectedCoords[i];
					const end = projectedCoords[i + 1];

					const closest = getClosestPointOnLineSegment(start, end, projectedTo);
					const dx = closest.x - projectedTo.x;
					const dy = closest.y - projectedTo.y;
					const distance = Math.sqrt(dx * dx + dy * dy);

					if (distance < segmentMinDistanceTo) {
						segmentMinDistanceTo = distance;
					}
				}

				if (segmentMinDistanceTo < toClosestDistance) {
					toClosestDistance = segmentMinDistanceTo;
					toSegment = segment;
				}
			}
		}
	}

	if (!fromSegment || !toSegment) {
		return;
	}

	const segmentHighlights: {
		segment: PathSegment;
		startHighlightFraction: number;
		endHighlightFraction: number;
	}[] = [];

	if (fromSegment.id === toSegment.id) {
		segmentHighlights.push({
			segment: fromSegment,
			startHighlightFraction: computePathCoordinateFraction(fromSegment, from, projectionOrigin),
			endHighlightFraction: computePathCoordinateFraction(toSegment, to, projectionOrigin),
		});
		return segmentHighlights;
	}

	for (const path of paths) {
		for (const segment of path.segments) {
			if (segment.id === fromSegment.id) {
				segmentHighlights.push({
					segment,
					startHighlightFraction: computePathCoordinateFraction(segment, from, projectionOrigin),
					endHighlightFraction: 1.0,
				});
			} else if (segment.id === toSegment.id) {
				segmentHighlights.push({
					segment,
					startHighlightFraction: 0.0,
					endHighlightFraction: computePathCoordinateFraction(segment, to, projectionOrigin),
				});
			} else {
				segmentHighlights.push({
					segment,
					startHighlightFraction: 0.0,
					endHighlightFraction: 1.0,
				});
			}
		}
	}

	return segmentHighlights;
}
