import type { RendererState } from '../../renderer';
import {
	ShaderMaterial,
	WebGLRenderTarget,
	GLSL3,
	type BufferGeometry,
	type WebGLRenderer,
	Color,
	RGBAFormat,
	UnsignedByteType,
	BufferAttribute,
	type Scene,
	Mesh,
	type Vector3,
} from 'three';
import { Geometry2DComponents } from '../../entities';
import type { Renderer } from '../../services/renderer';
import { GroupContainerObject3D } from '../../entities/group-container';
import type { Feature, MultiPolygon, Polygon, Position as GeoJsonPosition } from 'geojson';
import { renderPolygonGeometry } from '../../geometry/polygons';
import { renderMultiPolygonGeometry } from '../../geometry/multi-polygon';
import { Logger } from '@packages/internal/common';

const OCCLUSION_CAMERA_LAYER = 9;

const DEBUG = false;

const DEFAULT_OCCLUDER_HEIGHT = 0.1;

export function fillArrayWithColor(array: Float32Array | Uint8Array, count: number, color: number) {
	for (let i = 0; i < count; i++) {
		// convert numeric (hex) value to rgb:
		const r = (color & 0xff0000) >> 16;
		const g = (color & 0x00ff00) >> 8;
		const b = color & 0x0000ff;
		array[i * 3] = r / 255;
		array[i * 3 + 1] = g / 255;
		array[i * 3 + 2] = b / 255;
	}
}

export class Occlusion2DSystem {
	private state: RendererState;
	private renderer: WebGLRenderer;
	private renderService: Renderer;
	private pickingTexture?: WebGLRenderTarget;
	private pickingMaterial: ShaderMaterial;
	private pixelBuffer?: Uint8Array;
	private width = 0;
	private height = 0;
	private scale = 1 / 2;
	private color = 0x000001;
	private convertTo3DMapPosition: (position: GeoJsonPosition) => Vector3;
	private destroyed = false;
	private enabled = false;
	backgroundId = 0xffffff;

	/**
	 * Applies the id to the geometry which is encoded as a color
	 */
	private applyId(geometry: BufferGeometry, color: number) {
		const position = geometry.attributes.position;
		const array = new Float32Array(position.count * 3);
		fillArrayWithColor(array, position.count, color);

		geometry.setAttribute('color', new BufferAttribute(array, 3));
	}

	private createOccluderMesh(geometry: BufferGeometry, occluderId: number) {
		const mesh = new Mesh(geometry, this.pickingMaterial);
		mesh.layers.set(OCCLUSION_CAMERA_LAYER);
		this.applyId(mesh.geometry, occluderId);

		mesh.userData.occluderId = occluderId;

		return mesh;
	}

	constructor(
		state: RendererState,
		convertTo3DMapPosition: (position: GeoJsonPosition) => Vector3,
		renderer?: Renderer,
	) {
		this.state = state;
		this.convertTo3DMapPosition = convertTo3DMapPosition;
		if (renderer?.renderer == null) {
			throw new Error('No Renderer');
		}
		this.renderService = renderer;
		this.renderer = renderer.renderer;
		this.resize();
		this.pickingMaterial = new ShaderMaterial({
			glslVersion: GLSL3,

			vertexShader: /* glsl */ `
				attribute vec3 color;
				varying vec3 vColor;
				void main() {
					vColor = color;
					gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
				}
			`,

			fragmentShader: /* glsl */ `
				varying vec3 vColor;
				out vec4 fragColor;

				void main() {
					fragColor = vec4(vColor, 1.0);
				}
			`,
		});
	}

	reserveOccluderId() {
		this.color++;

		return this.color;
	}

	enable() {
		this.enabled = true;
	}

	disable() {
		this.enabled = false;
	}

	resize = () => {
		const dpr = this.state.pixelRatio;

		this.width = Math.floor((this.renderer.domElement.width / dpr) * this.scale);
		this.height = Math.floor((this.renderer.domElement.height / dpr) * this.scale);
		this.pixelBuffer = new Uint8Array(4 * this.width * this.height);

		this.pickingTexture = new WebGLRenderTarget(this.width, this.height, {
			format: RGBAFormat,
			type: UnsignedByteType,
			internalFormat: 'RGBA',
		});
	};

	private shouldShowByXY = (x: number, y: number, id: number) => {
		if (this.pixelBuffer == null) {
			return false;
		}
		const pixelIndex = x + (this.height - y - 1) * this.width;

		// let's convert the pixel buffer rgb to a numeric color value
		const r = this.pixelBuffer[pixelIndex * 4];
		const g = this.pixelBuffer[pixelIndex * 4 + 1];
		const b = this.pixelBuffer[pixelIndex * 4 + 2];
		const color = (r << 16) | (g << 8) | b;

		if (color === id || color === this.backgroundId) {
			return true;
		}

		return false;
	};

	// this ensures if an occluder is not active (ie not in the scene),
	// we don't hide all colliders associated with this occluder
	activeOccluders = new Set();

	update = () => {
		if (!this.enabled || this.destroyed) {
			return;
		}
		this.activeOccluders.clear();
		this.activeOccluders.add(this.backgroundId);
		if (!this.pickingTexture || !this.pixelBuffer) {
			return;
		}

		let feature: Feature<Polygon | MultiPolygon, any>;
		let mesh: Mesh;

		for (const entityId of this.state.geometry3DIdsInScene) {
			const entity = this.state.geometry3DMap.get(entityId);
			if (
				entity &&
				entity instanceof GroupContainerObject3D &&
				entity.userData.occluderFeature != null &&
				entity.userData.occluderId != null
			) {
				// if (entity.userData.occluderId !== this.backgroundId) {
				this.activeOccluders.add(entity.userData.occluderId);
				// }

				// populate and create occluders
				if (entity.userData.occluderDirty) {
					feature = entity.userData.occluderFeature;
					if (feature.geometry.type === 'Polygon') {
						const geometry = renderPolygonGeometry(feature as Feature<Polygon, any>, this.convertTo3DMapPosition, {
							color: 'white',
							height: DEFAULT_OCCLUDER_HEIGHT,
						});
						mesh = this.createOccluderMesh(geometry, entity.userData.occluderId);
						entity.add(mesh);
					} else if (feature.geometry.type === 'MultiPolygon') {
						const geometry = renderMultiPolygonGeometry(
							feature as Feature<MultiPolygon, any>,
							this.convertTo3DMapPosition,
							{
								color: 'white',
								height: DEFAULT_OCCLUDER_HEIGHT,
							},
						);
						mesh = this.createOccluderMesh(geometry, entity.userData.occluderId);
						entity?.add(mesh);
					}

					entity.userData.occluderDirty = false;
				}
			}
		}

		// set the render target to the picking texture
		this.renderer.setRenderTarget(this.pickingTexture);
		// clear the render target and set the clear color to black, which we will use as the "background" color
		const clearColor = new Color(this.backgroundId);
		this.renderer.setClearColor(clearColor);
		this.renderer.clear();
		// disable all layers
		this.state.cameraObject.layers.disableAll();
		// enable the occlusion layer
		this.state.cameraObject.layers.enable(OCCLUSION_CAMERA_LAYER);
		// render the scene
		this.renderer.render(this.state.entityScene.parent as Scene, this.state.cameraObject);
		// reset the render target
		this.renderer.setRenderTarget(null);
		this.state.cameraObject.layers.disable(OCCLUSION_CAMERA_LAYER);

		if (DEBUG) {
			// enable the occlusion layer
			this.renderService.enabledLayers.add(OCCLUSION_CAMERA_LAYER);
		} else {
			// disable the occlusion layer
			this.renderService.enabledLayers.delete(OCCLUSION_CAMERA_LAYER);
		}

		// read the pixels from the picking texture
		this.renderer
			.readRenderTargetPixelsAsync(this.pickingTexture, 0, 0, this.width, this.height, this.pixelBuffer)
			.then(() => {
				if (this.destroyed) {
					return;
				}
				for (const entityId of this.state.geometry2DIdsInScene) {
					const entity = this.state.geometry2DMap.get(entityId);
					if (entity && entity?.occluderId != null && this.activeOccluders.has(entity?.occluderId)) {
						const projection = entity.components[Geometry2DComponents.UI].projection;
						const x = Math.floor(projection.x * this.scale);
						const y = Math.floor(projection.y * this.scale);
						// check if the entity is occluded by the picking texture
						const shouldShow = this.shouldShowByXY(x, y, entity.occluderId);
						entity.components[Geometry2DComponents.UI].isOccluded = !shouldShow;
					}
				}
			})
			.catch(error => {
				if (this.destroyed) {
					// Expected error when system is being destroyed, ignore silently
					return;
				}
				Logger.error(error);
			});
	};

	destroy() {
		this.destroyed = true;
		this.pickingTexture?.dispose();
		this.pickingMaterial.dispose();
		this.pickingTexture = undefined;
		this.pixelBuffer = undefined;
	}
}
