import { Logger } from '@packages/internal/common';
import type MapData from '../map-data';
import type { InternalSearch, SearchOptions, SuggestOptions } from './internal';
import type { MapDataInternal } from '../map-data-objects';
import type { Suggestion } from 'minisearch';

/**
 * Search allows users to search for locations, categories, and other points of interest within the venue.
 *
 * Refer to the [Search Guide](https://developer.mappedin.com/web-sdk/enterprise-data#enterprise-search) for more information and interactive examples.
 */
export class Search {
	#mapData: MapData;
	private searchInstance?: Promise<InternalSearch>;
	#mapDataInternal: MapDataInternal;
	#languageChangeHandler: () => void;

	/**
	 * Whether the search is enabled.
	 * @default false
	 */
	enabled = false;

	/**
	 * @internal
	 */
	constructor(mapData: MapData, mapDataInternal: MapDataInternal, { enabled = false }: { enabled?: boolean } = {}) {
		this.#mapData = mapData;
		this.#mapDataInternal = mapDataInternal;
		this.#languageChangeHandler = () => {
			this.searchInstance?.then(search => search.populate());
		};

		if (enabled) {
			this.enable();
		}
	}

	/**
	 * Use query to search for locations based on a string input:
	 *
	 * - {@link EnterpriseLocation}: Specific places such as stores, restaurants, or washrooms.
	 * - {@link EnterpriseCategory}: Groups of locations, such as "Food Court" or "Electronics."
	 * - {@link Places}: Any main objects that can be searched for such as Space, Door, Point of Interest
	 *
	 * Search query returns a list of matching {@	link SearchResult} based on the input string.
	 *
	 * {@link SearchResult} include information about the type of match, the score (relevance), and detailed metadata about the matching items.
	 *
	 * @param term - The search term.
	 * @param options - The search options.
	 * @returns The search results.
	 * @example
	 * ```ts
	 * const results = await search.query('Coffee Shop');
	 * console.log(results.locations);
	 * ```
	 */
	async query(term: string, options: SearchOptions = {}): ReturnType<typeof InternalSearch.prototype.search> {
		if (!this.enabled) {
			await this.enable();
		}
		if (!this.searchInstance) {
			Logger.error('Search should be enabled by now');

			return { places: [], enterpriseLocations: [], enterpriseCategories: [] };
		}

		try {
			return await (await this.searchInstance).search(term, options);
		} catch (e) {
			Logger.error('Search.query failed due to', e);

			return { places: [], enterpriseLocations: [], enterpriseCategories: [] };
		}
	}

	/**
	 * Suggests the names of places, locations, and categories based on partial input. This is useful for creating an autocomplete feature for a search bar.
	 * @param term - The search term.
	 * @param options - The suggest options.
	 * @returns The search suggestions.
	 * @example
	 * ```ts
	 * const suggestions = await search.suggest('Coffee');
	 * console.log(suggestions);
	 * ```
	 */
	async suggest(term: string, options: SuggestOptions = {}): Promise<Suggestion[]> {
		if (!this.enabled) {
			await this.enable();
		}

		if (!this.searchInstance) {
			Logger.error('Search should be enabled by now');

			return [];
		}

		try {
			return await (await this.searchInstance).suggest(term, options);
		} catch (e) {
			Logger.error('Search.suggest failed due to', e);

			return [];
		}
	}

	/**
	 * Enables the search.
	 */
	async enable(): Promise<void> {
		if (this.enabled) {
			return;
		}
		this.enabled = true;
		if (!this.searchInstance) {
			this.searchInstance = import('./internal').then(
				({ InternalSearch: SearchClass }) => new SearchClass(this.#mapData),
			);
		}
		this.#mapDataInternal.on('language-change', this.#languageChangeHandler);

		await this.searchInstance;
	}
}

export type SearchState = {
	/**
	 * Whether the search is enabled.
	 */
	enabled: boolean;
};
