import type { SpaceFeature } from '@mappedin/mvf-v2';
import type Coordinate from './coordinate';
import type { EnterpriseLocation, MapDataInternal } from '.';
import type Floor from './floor';
import type { IGeoJSONData } from './types';
import type Door from './door';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';
import type { Writable } from 'type-fest';

/**
 * Represents the various types of spaces that can be defined within a map.
 * - 'room': A standard room or enclosed area.
 * - 'hallway': A passageway connecting rooms.
 * - 'exterior': An outdoor area.
 * - 'void': An undefined or non-specific space.
 */
export type TSpaceType = 'room' | 'hallway' | 'exterior' | 'void' | 'gate';

/**
 * A Space represents an area enclosed by walls, such as a hall or room. Spaces can be Interactive and have Labels and Markers added to them.
 * Spaces can also be customized with a color, texture and hover color.
 *
 * Refer to the [Spaces Guide](https://developer.mappedin.com/web-sdk/spaces) for more information and interactive examples.
 */
class Space extends DetailedMapData<SpaceFeature> implements IGeoJSONData, IFocusable, IAnchorable {
	/**
	 * @internal
	 */
	static readonly __type = 'space';

	/**
	 * @internal
	 */
	readonly __type = Space.__type;

	#data: MapDataInternal;
	#floorId: string;
	#cache: Partial<Writable<Space>> = {};
	#mvfData: SpaceFeature;

	/**
	 * Checks if the provided instance is of type Space.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Space, false otherwise.
	 */
	static is(instance: object): instance is Space {
		return (instance as { __type: string }).__type === Space.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: SpaceFeature;
		},
	) {
		super(data, options.mvfData);
		this.#data = data;
		this.#floorId = options.floorId;
		this.#mvfData = options.mvfData;
	}

	/**
	 * Gets the type/kind of the space.
	 *
	 * @returns {TSpaceType} The type of the space.
	 */
	get type(): TSpaceType {
		return validateSpaceType(this.#mvfData.properties.kind);
	}

	/**
	 * @internal
	 */
	get locations(): EnterpriseLocation[] {
		if (this.#cache.locations) {
			return this.#cache.locations;
		}

		this.#cache.locations = [];
		const enterpriseLocations = this.#data.mvfLocationsByGeometryId[this.#mvfData.properties.id];
		if (enterpriseLocations?.length > 0) {
			for (const enterpriseLocation of enterpriseLocations) {
				const location = this.#data.getById('enterprise-location', enterpriseLocation.id);
				if (location) {
					this.#cache.locations.push(location);
				}
			}
		}

		return this.#cache.locations;
	}

	/**
	 * Gets the {@link Floor} object associated with the space.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Gets the array of {@link Door}s associated with the space.
	 *
	 * @returns {Door[]} The doors array.
	 */
	get doors(): Door[] {
		if (this.#cache.doors) {
			return this.#cache.doors;
		}

		this.#cache.doors = [];
		const entranceNodeIds = this.#data.entranceNodeIdsBySpaceId?.[this.id] ?? [];
		for (const nodeId of entranceNodeIds) {
			const door = this.#data.doorsByNodeId[nodeId];
			if (door) this.#cache.doors.push(door);
		}

		return this.#cache.doors;
	}

	/**
	 * Gets the center {@link Coordinate} of the space.
	 *
	 * @returns {Coordinate} The space's center coordinate.
	 */
	get center(): Coordinate {
		if (this.#cache.center == null) {
			this.#cache.center = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.center;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Space.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get focusTarget() {
		return this.geoJSON.geometry.type === 'Polygon' ? this : this.center;
	}

	/** @internal */
	get anchorTarget() {
		return this.center;
	}

	/**
	 * Serializes the space data to JSON.
	 *
	 * @returns An object representing the space.
	 */
	toJSON() {
		return {
			__type: Space.__type,
			id: this.id,
			name: this.name,
			type: this.type,
			floor: this.floor.id,
			center: this.center.toJSON(),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of Object.keys(this.#cache)) {
			delete this.#cache[key];
		}
	}
}

export const VALID_SPACE_TYPES: TSpaceType[] = ['room', 'hallway', 'exterior', 'void', 'gate'];
/**
 * validates space type and fallback to room
 */
export function validateSpaceType(input: string | null | undefined): TSpaceType {
	if (input == null || input === 'default') {
		return 'room';
	}
	const inputLowerCase = input.toLowerCase();
	if (VALID_SPACE_TYPES.includes(inputLowerCase as TSpaceType)) {
		// If the input is valid, return it as a TSpaceType
		return inputLowerCase as TSpaceType;
	} else {
		// fallback
		return 'room';
	}
}

export default Space;
