import { Shape, Path, ExtrudeGeometry, type Vector2 } from 'three';
import type { Feature, Polygon } from 'geojson';
import type { PaintStyle } from '../types';
import ClipperLib from '@packages/internal/clipper-lib/ClipperLib';
import type { PolygonFeatureProperties } from '@packages/internal/mvf-utils';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import { createExtrudeSettings } from '../utils/constants';

const DEFAULT_COLOR = 'gray';
const DEFAULT_HEIGHT = 0.1;
const OFFSET_QUALITY = 100;

const offsetter = new ClipperLib.ClipperOffset();

function offset(vertices: Vector2[], offset: number): Vector2[][] {
	offsetter.Clear();
	offset /= 2;
	const paths = vertices.map(v => ({ X: v.x * OFFSET_QUALITY, Y: v.y * OFFSET_QUALITY }));

	offsetter.AddPath(paths, ClipperLib.JoinType.jtMiter, ClipperLib.EndType.etClosedPolygon);
	const offsettedPaths = new ClipperLib.Paths();

	offsetter.Execute(offsettedPaths, offset * OFFSET_QUALITY);
	if (offsettedPaths.length) {
		const newPaths = offsettedPaths.map(p =>
			p.map(v => ({
				x: v.X / OFFSET_QUALITY,
				y: v.Y / OFFSET_QUALITY,
			})),
		);

		return newPaths;
	}

	return [vertices];
}

export function renderPolygonGeometry(
	feature: Feature<Polygon, PolygonFeatureProperties<any>>,
	convertTo3DMapPosition,
	style?: PaintStyle,
) {
	let { color = DEFAULT_COLOR, opacity = 1, altitude = 0, height = DEFAULT_HEIGHT } = style || {};
	if (Array.isArray(opacity)) {
		opacity = 1;
	}

	const boundingBox = {
		min: { x: Infinity, y: Infinity, z: altitude },
		max: { x: -Infinity, y: -Infinity, z: height + altitude },
		center: { x: 0, y: 0, z: (height + altitude) / 2 },
	};

	const cartesianCoordinates = feature.geometry.coordinates[0].map(coordinate => {
		const coord = convertTo3DMapPosition(coordinate[1], coordinate[0]);
		boundingBox.min.x = Math.min(boundingBox.min.x, coord.x);
		boundingBox.min.y = Math.min(boundingBox.min.y, coord.y);
		boundingBox.max.x = Math.max(boundingBox.max.x, coord.x);
		boundingBox.max.y = Math.max(boundingBox.max.y, coord.y);

		return coord;
	});

	let clippedCoordinates = cartesianCoordinates;

	/**
	 * If a polygon has enterprise edge offsets / infills we use clipper to offset the polygon.
	 * Otherwise, two polygons will occupy the same space and the map will render with tons of z-fighting.
	 *
	 * Additionally, if the polygon has an image attached we use clipper with an offset of 0.
	 * This replicates V5 behaviour & many such polygons have bad vertex data
	 * because they're expecting to be offset in the renderer.
	 */
	if (
		(feature.properties?.style?.edgeOffset != null && feature.properties.style.edgeOffset !== 0) ||
		feature.properties?.image != null
	) {
		clippedCoordinates = offset(clippedCoordinates, feature.properties?.style?.edgeOffset || 0);
	} else {
		clippedCoordinates = [clippedCoordinates];
	}

	const geometries: ExtrudeGeometry[] = [];

	for (const polygon of clippedCoordinates) {
		const shape = new Shape();

		shape.moveTo(polygon[0].x, polygon[0].y);
		polygon.slice(1).forEach(coord => shape.lineTo(coord.x, coord.y));
		shape.lineTo(polygon[0].x, polygon[0].y);
		const holes = feature.geometry.coordinates.slice(1);

		shape.holes = holes.map(hole => {
			const cartesianCoordinates = hole.map(coordinate => {
				return convertTo3DMapPosition(coordinate[1], coordinate[0]);
			});
			const path = new Path();
			path.moveTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);
			cartesianCoordinates.slice(1).forEach(coord => path.lineTo(coord.x, coord.y));
			path.lineTo(cartesianCoordinates[0].x, cartesianCoordinates[0].y);

			return path;
		});

		// Use StyleComponent bevel as single source of truth
		const extrudeSettings = createExtrudeSettings(style?.bevel, height);

		const geometry = new ExtrudeGeometry(shape, extrudeSettings);

		geometry.userData = {
			style: {
				color,
				opacity,
			},
			usedBevelSettings: extrudeSettings,
		};

		geometries.push(geometry);
	}

	if (geometries.length === 1) {
		return geometries[0];
	}

	const mergedGeometry = BufferGeometryUtils.mergeGeometries(geometries);

	// Dispose individual geometries after merging to prevent memory leaks
	geometries.forEach(geometry => geometry.dispose());

	// For merged geometries, use the settings from the first geometry
	const extrudeSettings = createExtrudeSettings(style?.bevel, height);
	mergedGeometry.userData = {
		style: {
			color,
			opacity,
		},
		usedBevelSettings: extrudeSettings,
		_split: true,
	};

	return mergedGeometry;
}
