import type { ObstructionFeature } from '@mappedin/mvf-v2';
import type Coordinate from './coordinate';
import type { EnterpriseLocation, MapDataInternal } from '.';
import type Floor from './floor';
import type { IGeoJSONData } from './types';
import type { IAnchorable, IFocusable } from '../types';
import DetailedMapData from './detailed-map-data';
import { getCenterCoordinate } from './utils';

/**
 * A class representing generic map object data within the map.
 *
 * It could represent various objects on the map, such as desks, chairs, etc.
 *
 * MapObject appearance can be customized by changing the color or adding a texture to its top or sides.
 * Refer to the [Textures & Colors Guide](https://developer.mappedin.com/web-sdk/images-textures#textures--colors) for more information and interactive examples.
 */
export class MapObject extends DetailedMapData<ObstructionFeature> implements IGeoJSONData, IFocusable, IAnchorable {
	/**
	 * @internal
	 */
	static readonly __type = 'object';

	/**
	 * @internal
	 */
	readonly __type = MapObject.__type;

	#data: MapDataInternal;
	#floorId: string;
	#cache: Partial<{ center: Coordinate; locations: EnterpriseLocation[] }> = {};
	#mvfData: ObstructionFeature;

	/**
	 * Checks if the provided instance is of type MapObject.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a MapObject, false otherwise.
	 */
	static is(instance: object): instance is MapObject {
		return (instance as { __type: string }).__type === MapObject.__type;
	}

	/**
	 * @internal
	 */
	constructor(
		data: MapDataInternal,
		options: {
			floorId: string;
			mvfData: ObstructionFeature;
		},
	) {
		super(data, options.mvfData);
		this.#data = data;
		this.#floorId = options.floorId;
		this.#mvfData = options.mvfData;
	}

	/**
	 * Gets the type of the MapObject.
	 *
	 * @returns {string} The kind of the object.
	 */
	get type(): string {
		let objectKind = this.#mvfData.properties.kind;
		if (objectKind == null) {
			objectKind = 'object';
		}
		// For some reason MVF does not use the same casing for desks, we should normalize it
		if (objectKind === 'Desks') {
			objectKind = 'desk';
		}

		return objectKind.toLowerCase();
	}

	/**
	 * @internal
	 */
	get locations(): EnterpriseLocation[] {
		if (this.#cache.locations) {
			return this.#cache.locations;
		}

		this.#cache.locations = [];
		const enterpriseLocations = this.#data.mvfLocationsByGeometryId[this.#mvfData.properties.id];
		if (enterpriseLocations?.length > 0) {
			for (const enterpriseLocation of enterpriseLocations) {
				const location = this.#data.getById('enterprise-location', enterpriseLocation.id);
				if (location) {
					this.#cache.locations.push(location);
				}
			}
		}

		return this.#cache.locations;
	}

	/**
	 * Gets the {@link Floor} associated with the MapObject.
	 *
	 * @returns {Floor} The floor object.
	 * @throws Will throw an error if the floor is not found.
	 */
	get floor(): Floor {
		const _floor = this.#data.getById('floor', this.#floorId);
		if (_floor == null) {
			throw new Error(`Floor ${this.#floorId} not found!`);
		}

		return _floor;
	}

	/**
	 * Gets the center {@link Coordinate} of the MapObject.
	 *
	 * @returns {Coordinate} The object's center coordinate.
	 */
	get center(): Coordinate {
		if (this.#cache.center == null) {
			this.#cache.center = getCenterCoordinate(this.#mvfData, this.#floorId);
		}

		return this.#cache.center;
	}

	/**
	 * Gets the underlying GeoJSON Feature representation of this Object.
	 */
	get geoJSON() {
		return { ...this.#mvfData, properties: null };
	}

	/** @internal */
	get anchorTarget() {
		return this.center;
	}

	/** @internal */
	get focusTarget() {
		return this.geoJSON.geometry.type === 'Polygon' ? this : this.center;
	}

	/**
	 * Serializes the MapObject data to JSON.
	 *
	 * @returns An object representing the MapObject.
	 */
	toJSON() {
		return {
			__type: MapObject.__type,
			id: this.id,
			name: this.name,
			type: this.type,
			floor: this.floor.id,
			center: this.center.toJSON(),
		};
	}

	/**
	 * Cleans up resources used by the instance.
	 *
	 * @internal
	 */
	destroy() {
		for (const key of globalThis.Object.keys(this.#cache)) {
			this.#cache[key] = null;
		}
	}
}

export default MapObject;
