import { appendItems } from './utils';
import type { Point2D } from './types';
import { Rectangle } from './rectangle';

/**
 * This is a QuadTree implementation that can be used to store rectangles
 * and query them based on their position.
 *
 * @template T - The type of data stored in the QuadTree.
 */
export class QuadTree<T = unknown> {
	/**
	 * The top-left quadrant of this QuadTree.
	 */
	public topLeft!: QuadTree<T>;

	/**
	 * The top-right quadrant of this QuadTree.
	 */
	public topRight!: QuadTree<T>;

	/**
	 * The bottom-left quadrant of this QuadTree.
	 */
	public bottomLeft!: QuadTree<T>;

	/**
	 * The bottom-right quadrant of this QuadTree.
	 */
	public bottomRight!: QuadTree<T>;

	/**
	 * Whether this QuadTree has been subdivided.
	 */
	public divided = false;
	/**
	 * The boundary of this QuadTree.
	 */
	public readonly boundary: Rectangle<T>;

	/**
	 * The maximum number of objects a QuadTree node can hold before subdividing.
	 */
	public capacity = 16;

	/**
	 * The list of objects in this QuadTree node.
	 */
	public readonly objects: Rectangle<T>[] = [];

	/**
	 * The parent QuadTree node.
	 */
	public readonly parent?: QuadTree<T>;

	/**
	 * Creates an instance of QuadTree.
	 *
	 * @param {Rectangle<T>} boundary - The boundary of this QuadTree.
	 * @param {QuadTree<T>} [parent] - The parent QuadTree node.
	 */
	constructor(boundary: Rectangle<T>, parent?: QuadTree<T>) {
		this.parent = parent;
		this.boundary = boundary;
	}

	/**
	 * Gets the total number of objects in this QuadTree.
	 *
	 * @returns {number} The total number of objects.
	 */
	public getSize(): number {
		if (!this.divided) {
			return this.objects.length;
		} else {
			return (
				this.objects.length +
				this.topLeft.getSize() +
				this.topRight.getSize() +
				this.bottomLeft.getSize() +
				this.bottomRight.getSize()
			);
		}
	}

	/**
	 * Subdivides this QuadTree into four quadrants.
	 */
	public subdivide(): void {
		const { x, y, w, h } = this.boundary;

		this.topLeft = new QuadTree(new Rectangle(x, y, w / 2, h / 2), this);
		this.topRight = new QuadTree(new Rectangle(x + w / 2, y, w / 2, h / 2), this);
		this.bottomLeft = new QuadTree(new Rectangle(x, y + h / 2, w / 2, h / 2), this);
		this.bottomRight = new QuadTree(new Rectangle(x + w / 2, y + h / 2, w / 2, h / 2), this);
		this.divided = true;
	}

	/**
	 * Queries this QuadTree for rectangles that intersect the given rectangle.
	 *
	 * @param {Rectangle} rectangle - The rectangle to query.
	 * @returns {Rectangle<T>[]} The list of intersecting rectangles.
	 */
	public queryRect(rectangle: Rectangle): Rectangle<T>[] {
		let result: Rectangle<T>[] = [];

		// If rectangle is not overlapping this quadrant, return an empty array
		if (!this.boundary.intersects(rectangle)) {
			return [];
		} else {
			result.push(...this.objects);
		}
		if (this.divided) {
			appendItems(result, this.topLeft.queryRect(rectangle));
			appendItems(result, this.topRight.queryRect(rectangle));
			appendItems(result, this.bottomLeft.queryRect(rectangle));
			appendItems(result, this.bottomRight.queryRect(rectangle));
		}

		return result;
	}

	/**
	 * Queries this QuadTree for rectangles that contain the given point.
	 *
	 * @param {Point2D} point - The point to query.
	 * @returns {Rectangle<T>[]} The list of rectangles containing the point.
	 */
	public queryPoint(point: Point2D): Rectangle<T>[] {
		const result: Rectangle<T>[] = [];

		// If the target point is not inside of the rectangle, return an empty array
		if (!this.boundary.intersectsPoint(point)) {
			return [];
		} else {
			result.push(...this.objects);
		}

		if (this.divided) {
			appendItems(result, this.topLeft.queryPoint(point));
			appendItems(result, this.topRight.queryPoint(point));
			appendItems(result, this.bottomLeft.queryPoint(point));
			appendItems(result, this.bottomRight.queryPoint(point));
		}

		return result;
	}

	/**
	 * Inserts a rectangle into this QuadTree.
	 *
	 * @param {Rectangle<T>} rectangle - The rectangle to insert.
	 * @returns {boolean} Whether the rectangle was successfully inserted.
	 */
	public insert(rectangle: Rectangle<T>): boolean {
		// The root quad tree can accept any rectangle that intersects with the boundary
		// while subdivisions only accept rectangles that fit entirely within them
		if (this.parent ? !this.boundary.contains(rectangle) : !this.boundary.intersects(rectangle)) {
			return false;
		}

		// Otherwise, let's see if we're at capacity
		if (this.objects.length < this.capacity) {
			// If we're good, push rectangle
			this.objects.push(rectangle);

			return true;
		} else {
			// Otherwise divide into 4 quadrants
			if (!this.divided) {
				this.subdivide();
			}
			// Try inserting into each quadrant
			const result =
				this.topLeft.insert(rectangle) ||
				this.topRight.insert(rectangle) ||
				this.bottomLeft.insert(rectangle) ||
				this.bottomRight.insert(rectangle);

			// If we can't (meaning it likely overlaps an edge of each quadrant)
			if (result === false) {
				// Just add it to the parent's objects list
				this.objects.push(rectangle);
			}

			// Always return success here
			return true;
		}
	}

	/**
	 * Draws all objects in this QuadTree on the given canvas context.
	 *
	 * @param {CanvasRenderingContext2D} context - The canvas context to draw on.
	 */
	public drawObjects(context: CanvasRenderingContext2D): void {
		this.objects.forEach(o => o.draw(context));
		if (this.divided) {
			this.topLeft.drawObjects(context);
			this.topRight.drawObjects(context);
			this.bottomLeft.drawObjects(context);
			this.bottomRight.drawObjects(context);
		}
	}

	/**
	 * Draws the boundary of this QuadTree on the given canvas context.
	 *
	 * @param {CanvasRenderingContext2D} context - The canvas context to draw on.
	 */
	public draw(context: CanvasRenderingContext2D): void {
		context.rect(this.boundary.x, this.boundary.y, this.boundary.w, this.boundary.h);
		if (this.divided) {
			this.topLeft.draw(context);
			this.topRight.draw(context);
			this.bottomLeft.draw(context);
			this.bottomRight.draw(context);
		}
		context.strokeStyle = 'black';
		context.lineWidth = 4;
		context.stroke();
	}
}
