import type { LabelAppearance, LabelState } from '@mappedin/core-sdk';
import z from 'zod';
import type { TCollisionRankingTier } from '../types';
import {
	DEFAULT_LABEL_TEXT_PLACEMENTS,
	LABEL_TEXT_PLACEMENT_VALUES,
	labelAppearanceSchemaBase,
	parseLabelAppearance,
} from '../../../geojson/src/components/label-utils';
import type { Complete } from '@packages/internal/common/type-utils';
import { collisionRankingTierSchema } from '../../../geojson/src/utils/collision-ranking-tier';

export const LABEL_STATE_TYPE = 'label';

export const labelStateSchema = z.object({
	type: z.literal(LABEL_STATE_TYPE).default(LABEL_STATE_TYPE),
	/** Text content of the label */
	text: z.string(),
	/**
	 * Determines the collision ranking tier of the label, which influences its visibility in relation to other colliders.
	 * @default 'medium'
	 */
	rank: z.custom<TCollisionRankingTier | 'initial'>(val => {
		return collisionRankingTierSchema.safeParse(val).success || val === 'initial';
	}),
	/** Customize the appearance of the Label and its pin */
	appearance: z.custom<LabelAppearance>(val => {
		return labelAppearanceSchemaBase.safeParse(val).success;
	}),
	/**
	 * Whether the Label should be clickable.
	 * @default false
	 */
	interactive: z.boolean().default(false),
	/**
	 * Whether the label is enabled.
	 * @default true
	 */
	enabled: z.boolean().default(true),
	/**
	 * The placement of the text relative to the pin.
	 */
	textPlacement: z
		.union([z.enum(LABEL_TEXT_PLACEMENT_VALUES), z.array(z.enum(LABEL_TEXT_PLACEMENT_VALUES))])
		.default(DEFAULT_LABEL_TEXT_PLACEMENTS),
});

export const labelStateSchemaPartial = labelStateSchema.partial();

export type TLabelState = z.infer<typeof labelStateSchema>;

export type TLabelUpdateState = z.infer<typeof labelStateSchemaPartial>;

export function getLabelState(state: LabelState): TLabelState {
	const { text, enabled, options } = state;
	return {
		type: LABEL_STATE_TYPE,
		text: text ?? '',
		appearance: parseLabelAppearance(options?.appearance ?? {}),
		rank: (options?.rank as TCollisionRankingTier) ?? 'medium',
		enabled: enabled ?? true,
		interactive: options?.interactive ?? false,
		textPlacement: options?.textPlacement ?? DEFAULT_LABEL_TEXT_PLACEMENTS,
	} satisfies Complete<TLabelState>;
}

export function translateLabelStateToGeojsonCore(
	state: TLabelUpdateState,
	currentState: Partial<LabelState>,
): Partial<LabelState> {
	const update: Partial<LabelState> = { options: {} } as Partial<LabelState>;
	if (!update.options) throw new Error('options should exist');
	if (state.text) {
		update.text = state.text;
	}
	if (state.appearance) {
		update.options.appearance = state.appearance;
	}
	if (state.textPlacement) {
		update.options.textPlacement = state.textPlacement;
	}
	if (state.rank != null) {
		if (state.rank === 'initial') {
			update.options.rank = currentState.initialRank || 2;
		} else {
			update.options.rank = state.rank;
		}
	}
	if (state.enabled !== undefined) {
		update.enabled = state.enabled;
	}
	if (state.interactive !== undefined) {
		update.options.interactive = state.interactive;
	}
	if (state.enabled !== undefined) {
		update.enabled = state.enabled;
	}

	return update;
}
