import type { IAnchorable } from '../types';

/**
 * Class representing an Image on the {@link MapView}.
 */
export class Image3DView {
	/**
	 * The image's id
	 */
	#id: string;

	/**
	 * The image's url
	 */
	#url: string;
	/**
	 * The image's target
	 */
	readonly target: IAnchorable;

	/**
	 * @internal
	 */
	static readonly __type = 'image-3d';

	/**
	 * @internal
	 */
	readonly __type = Image3DView.__type;

	/**
	 * Checks if the provided instance is of type Image"
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Image, false otherwise.
	 */
	static is(instance: object): instance is Image3DView {
		return (instance as { __type: string }).__type === Image3DView.__type;
	}

	/**
	 * @internal
	 */
	constructor(id: string, url: string, target: IAnchorable) {
		this.#id = id;
		this.#url = url;
		this.target = target;
	}

	/**
	 * The image's id
	 */
	get id() {
		return this.#id;
	}

	/**
	 * The image's url
	 */
	get url() {
		return this.#url;
	}
}
