import type { Rectangle } from '../rectangle';
import type { Point2D } from '../types';

/**
 * Check if a rectangle contains a point
 * @param rect1 {Rectangle}
 * @param point {Point2D}
 *
 * @hidden
 */
export function intersectPoint(rect1: Rectangle, point: Point2D) {
	const [tx, ty] = point;

	return tx >= rect1.x && tx <= rect1.x + rect1.w && ty >= rect1.y && ty <= rect1.y + rect1.h;
}
