import { Node } from '../map-data-objects';
import type { Coordinate, MapDataInternal, Door, PointOfInterest, Annotation, Space } from '../map-data-objects';
import { QueryCache } from './cache';
import { getCoordinateFromStuffs } from '../utils';
import z from 'zod';
import { Logger } from '@packages/internal/common';
import { getNodesFromTarget } from '../api-geojson/directions';
import { getNodesFromTargetSync } from '../api-geojson/directions/utils';

const SEARCH_RADIUS_METERS = 50;
const METERS_PER_DEGREE_LAT = 111319.9; // circumference of the earth at the equator in meters / 360
const DEFAULT_MAX_RESULTS = 1;

/**
 * We're turning off auto line of sight until we resolve the following issues:
 * On platform, line of sight does not go through doors/entrances.
 * On CMS, line of sight assumes all polygons are obstructing, which should not be the case.
 */
const SHOULD_USE_LINE_OF_SIGHT = false;

const findNearestOptionsSchema = z.object({
	limit: z.enum(['same-floor', 'same-elevation']).optional(),
	mode: z.enum(['travel-distance', 'absolute-distance']).optional(),
	lineOfSight: z.boolean().optional().or(z.literal('auto')),
	maxDistance: z.number().optional(),
	maxResults: z.number().optional(),
});

export type TQueriables = PointOfInterest | Door | Annotation | Node | Space;

const defaultOptions: Required<TFindNearestOptions> = {
	lineOfSight: SHOULD_USE_LINE_OF_SIGHT,
	mode: 'travel-distance',
	maxDistance: SEARCH_RADIUS_METERS,
	limit: 'same-floor',
	maxResults: DEFAULT_MAX_RESULTS,
};

/**
 * Query allows users to query for nodes, locations, categories, and other points of interest within the venue.
 */
export class Query {
	#mapDataInternal: MapDataInternal;
	#cache: QueryCache;
	/**
	 * @internal
	 */
	constructor(mapDataInternal: MapDataInternal) {
		this.#mapDataInternal = mapDataInternal;
		this.#cache = new QueryCache(this.#mapDataInternal, this.#projectOntoCartesianPlane);
	}

	/**
	 * Project a longitude/latitude onto a cartesian plane, where y is up and x is right, and the passed in center is 0,0 on the cartesian plane
	 */
	#projectOntoCartesianPlane = ([lon, lat]: [number, number]): { x: number; y: number } => {
		const [centerLon, centerLat] = [
			this.#mapDataInternal.mapCenter.longitude,
			this.#mapDataInternal.mapCenter.latitude,
		];

		const metersPerDegreeLat = METERS_PER_DEGREE_LAT;
		const metersPerDegreeLon = METERS_PER_DEGREE_LAT * Math.cos((lat * Math.PI) / 180);

		const x = (lon - centerLon) * metersPerDegreeLon;
		const y = (lat - centerLat) * metersPerDegreeLat;

		return {
			x,
			y,
		};
	};

	/**
	 * @internal
	 */
	__LEGACY_NEAREST_SYNC = <T extends TQueriables>(
		origin: Coordinate | PointOfInterest | Door | Annotation | Node,
		include: T[],
		options?: TFindNearestOptions,
	) => {
		const parsedOptions: Required<TFindNearestOptions> = { ...defaultOptions, ...options };
		try {
			findNearestOptionsSchema.parse(parsedOptions);
		} catch (error: unknown) {
			Logger.error(error);

			return undefined;
		}

		const coordinate = getCoordinateFromStuffs(origin);
		if (!coordinate) {
			return undefined;
		}

		// default to querying the floor the origin is on
		let floorIds = this.#getFloorIds(coordinate, parsedOptions);

		let results: TFindNearestResult<T> = [];

		const nodeToFeatureMap: Record<string, TQueriables[]> = {};

		const includedNodeIds: string[] = [];
		// iterate through the targets, and get their nodes as searches can only be performed via nodes
		for (const feature of include) {
			const targetNodeIds = getNodesFromTargetSync(
				feature,
				this.#mapDataInternal,
				this.#mapDataInternal.enterpriseMode,
			);
			if (targetNodeIds) {
				for (const targetNodeId of targetNodeIds) {
					if (!nodeToFeatureMap[targetNodeId]) {
						nodeToFeatureMap[targetNodeId] = [];
					}
					nodeToFeatureMap[targetNodeId].push(feature);
					includedNodeIds.push(targetNodeId);
				}
			}
		}

		if (options?.mode === 'absolute-distance') {
			results = this.#getAbsoluteDistance(coordinate, floorIds, include, parsedOptions);
		} else {
			results = this.#getTravelDistanceSync(origin, floorIds, includedNodeIds, nodeToFeatureMap, parsedOptions);
		}

		return results.sort((a, b) => a.distance - b.distance);
	};

	/**
	 * Find the nearest feature to the origin
	 * @param origin - The origin object
	 * @param include - The features to include in the query
	 * @param options - The options for the query
	 * @returns Objects with the distance and the feature, sorted by distance
	 */
	async nearest<T extends TQueriables>(
		origin: Coordinate | PointOfInterest | Door | Annotation | Node,
		include: T[],
		options?: TFindNearestOptions,
	): Promise<TFindNearestResult<T> | undefined> {
		const parsedOptions: Required<TFindNearestOptions> = { ...defaultOptions, ...options };

		try {
			findNearestOptionsSchema.parse(parsedOptions);
		} catch (error: unknown) {
			Logger.error(error);

			return undefined;
		}

		const coordinate = getCoordinateFromStuffs(origin);
		if (!coordinate) {
			return undefined;
		}

		// default to querying the floor the origin is on
		let floorIds = this.#getFloorIds(coordinate, parsedOptions);

		let results: TFindNearestResult<T> = [];

		const nodeToFeatureMap: Record<string, TQueriables[]> = {};

		const includedNodeIds: string[] = [];
		const promises: Promise<void>[] = [];

		if (options?.mode === 'absolute-distance') {
			results = this.#getAbsoluteDistance(coordinate, floorIds, include, parsedOptions);
		} else {
			// iterate through the targets, and get their nodes as searches can only be performed via nodes
			for (const feature of include) {
				promises.push(
					getNodesFromTarget(feature, this.#mapDataInternal, this.#mapDataInternal.enterpriseMode).then(
						targetNodeIds => {
							if (targetNodeIds) {
								for (const targetNodeId of targetNodeIds) {
									if (!nodeToFeatureMap[targetNodeId]) {
										nodeToFeatureMap[targetNodeId] = [];
									}
									nodeToFeatureMap[targetNodeId].push(feature);
									includedNodeIds.push(targetNodeId);
								}
							}
						},
					),
				);
			}
			await Promise.all(promises);

			results = await this.#getTravelDistance(origin, floorIds, includedNodeIds, nodeToFeatureMap, parsedOptions);
		}

		return results.sort((a, b) => a.distance - b.distance);
	}

	#getFloorIds(coordinate: Coordinate, options?: Required<TFindNearestOptions>) {
		// default to querying the floor the origin is on
		let floorIds = coordinate.floorId ? [coordinate.floorId] : [];

		if (coordinate.floorId == null) {
			// If no floorId provided, we query all default floors on all floors-stacks (likely using GPS? coordinates)
			for (const fs of this.#mapDataInternal.getByType('floor-stack')) {
				const floor = fs.defaultFloor;
				if (floor) {
					floorIds.push(floor.id);
				}
			}
		} else if (options?.limit === 'same-elevation') {
			// If limit is same-elevation, we query all floors in all floors-stacks at the same elevation as the origin (useful for multi-building venues)
			const floor = this.#mapDataInternal.floorsById[coordinate.floorId];
			if (floor) {
				const elevation = floor?.elevation;
				if (elevation) {
					floorIds = this.#mapDataInternal
						.getByType('floor-stack')
						.map(fs => fs.floors.find(f => f.elevation === elevation)?.id)
						.filter(Boolean) as string[];
				}
			}
		}

		return floorIds;
	}

	#getAbsoluteDistance = <T extends TQueriables>(
		coordinate: Coordinate,
		floorIds: string[],
		includedFeatures: TQueriables[],
		options?: Required<TFindNearestOptions>,
	) => {
		const results: TFindNearestResult<T> = [];

		const types = new Set(includedFeatures.map(f => f.__type));

		for (const floorId of floorIds) {
			for (const type of types) {
				const nearestResults = this.#cache.searchWithIncludedIds(
					[coordinate.longitude, coordinate.latitude],
					type,
					this.#mapDataInternal.mapName,
					floorId,
					includedFeatures.filter(f => f.__type === type).map(f => f.id),
					options?.maxDistance ?? SEARCH_RADIUS_METERS,
					options?.lineOfSight === 'auto' ? SHOULD_USE_LINE_OF_SIGHT : options?.lineOfSight,
					options?.maxResults ?? 1,
				);

				if (nearestResults) {
					for (const result of nearestResults) {
						results.push({
							distance: result.distance,
							feature: (this.#mapDataInternal.getById(type, result.node.properties.parentId) ||
								this.#mapDataInternal.getById(type, result.node.properties.id)) as T,
						});
					}
				}
			}
		}

		return results;
	};

	#getTravelDistance = async <T extends TQueriables>(
		origin: Coordinate | PointOfInterest | Door | Annotation | Node,
		floorIds: string[],
		includedNodeIds: string[],
		nodeToFeatureMap: Record<string, TQueriables[]>,
		options?: TFindNearestOptions,
	) => {
		const results: TFindNearestResult<T> = [];

		for (const floorId of floorIds) {
			// find nearest node if origin isn't one
			let node: Node | undefined;
			let distanceToNode = 0;
			if (!Node.is(origin)) {
				const results = this.#getAbsoluteDistance(
					origin.anchorTarget,
					[floorId],
					this.#mapDataInternal.getByType('node').filter(n => n.floor.id === floorId),
					{
						limit: 'same-floor',
						lineOfSight: options?.lineOfSight ?? true,
						mode: 'absolute-distance',
						maxDistance: options?.maxDistance ?? SEARCH_RADIUS_METERS,
						maxResults: 1, // hardcode for the first search, since we need one node to do the rest of travel
					},
				);
				if (results.length > 0) {
					node = results[0].feature as Node;
					distanceToNode = results[0].distance;
				}
			}

			if (node) {
				// we only want unique features, as a space may have multiple nodes that get sorted by distance, but we only want to return
				// the closest to the single space
				const uniqueFeatureIds = new Set();
				const searchResults = this.#mapDataInternal.directions.navigator.findNearestNodesOnGraph(
					node.id,
					(options?.maxDistance ?? SEARCH_RADIUS_METERS) - distanceToNode,
					floorId,
					includedNodeIds,
					false,
					options?.maxResults ?? DEFAULT_MAX_RESULTS,
				);

				for (const result of searchResults) {
					for (const feature of nodeToFeatureMap[result.feature.properties.id]) {
						if (uniqueFeatureIds.has(feature.id)) {
							continue;
						}
						results.push({
							distance: result.distance + distanceToNode,
							feature: feature as T,
						});
						uniqueFeatureIds.add(feature.id);
					}
				}
			}
		}

		return results;
	};

	#getTravelDistanceSync = <T extends TQueriables>(
		origin: Coordinate | PointOfInterest | Door | Annotation | Node,
		floorIds: string[],
		includedNodeIds: string[],
		nodeToFeatureMap: Record<string, TQueriables[]>,
		options?: TFindNearestOptions,
	) => {
		const results: TFindNearestResult<T> = [];

		for (const floorId of floorIds) {
			// find nearest node if origin isn't one
			let node: Node | undefined;
			if (!Node.is(origin)) {
				node = this.__LEGACY_NEAREST_SYNC(origin, this.#mapDataInternal.getByType('node'), {
					lineOfSight: options?.lineOfSight ?? true,
					mode: 'absolute-distance',
					maxDistance: options?.maxDistance ?? SEARCH_RADIUS_METERS,
					maxResults: 1, // hardcode for the first search, since we need one node to do the rest of travel
				})?.[0].feature;
			}
			if (node) {
				const searchResults = this.#mapDataInternal.directions.navigator.findNearestNodesOnGraph(
					node.id,
					options?.maxDistance ?? SEARCH_RADIUS_METERS,
					floorId,
					includedNodeIds,
					options?.lineOfSight === 'auto' ? SHOULD_USE_LINE_OF_SIGHT : options?.lineOfSight,
					options?.maxResults ?? DEFAULT_MAX_RESULTS,
				);

				for (const result of searchResults) {
					for (const feature of nodeToFeatureMap[result.feature.properties.id]) {
						results.push({
							distance: result.distance,
							feature: feature as T,
						});
					}
				}
			}
		}

		return results;
	};
}

export type TFindNearestResult<T extends TQueriables> = {
	distance: number;
	feature: T;
}[];

export type TFindNearestOptions = {
	/**
	 * Limit query to a set of floors. Only applies if the origin has floor information. If it doesn't, it limits the query to all floor-stacks' default floors.
	 *
	 * @default 'same-floor'
	 */
	limit?: /**
	 * Limit query to current floor of origin
	 */
	| 'same-floor'
		/**
		 * Limit query to all floors at the same elevation as the origin
		 */
		| 'same-elevation';
	/**
	 * Whether to use line of sight checks to filter results. This ensures that results aren't obstructed by walls, etc.
	 * When 'auto', heuristics are used to determine whether to use line of sight checks. These are currently experimental, and are subject to change.
	 * @experimental
	 * @default 'auto'
	 */
	lineOfSight?: boolean | 'auto';
	/**
	 * The mode to use for the query
	 */
	mode?: 'travel-distance' | 'absolute-distance';
	/**
	 * The max distance to search for features within
	 */
	maxDistance?: number;

	/**
	 * The maximum number of results to return, the lower the number, the faster the query will be
	 * @default 1
	 */
	maxResults?: number;
};
