import type { ModelState } from '@mappedin/core-sdk';
import { Coordinate } from '../map-data-objects';
import { z } from 'zod';
import { MaterialStyleSchema, modelPropertiesSchema } from '../../../geojson/src/components/model';
import { parseCompleteState } from './state-utils';

export const MODEL_STATE_TYPE = 'model';

/**
 * Schema for validating model state properties.
 */
export const modelStateSchema = modelPropertiesSchema.extend({
	type: z.literal(MODEL_STATE_TYPE).default(MODEL_STATE_TYPE),

	/**
	 * Color property designed for use with @mappedin/3d-assets.
	 * Updates the accent color of 3d assets by applying the color to materials named
	 * ['Default', 'Fabric', 'Mpdn_Logo', 'Fabric_Logo'].
	 * For custom colors on non-Mappedin models, use the {@link ModelStyle.material | material} property.
	 * If both the `material` and `color` properties are provided, `material` property updates will take higher precedence than `color` property updates.
	 */
	color: z.string().optional(),

	/**
	 * The position of the model as a Coordinate.
	 */
	position: z.custom<Coordinate>(val => Coordinate.is(val as any), {
		error: 'Expected a Coordinate',
	}),

	/**
	 * Whether the model is visible.
	 * @default true
	 */
	visible: z.boolean().default(true),

	/**
	 * The rotation of the model.
	 * @default [0, 0, 0]
	 */
	rotation: z.tuple([z.number(), z.number(), z.number()]).default([0, 0, 0]),

	/**
	 * The scale of the model.
	 * @default [1, 1, 1]
	 */
	scale: z.tuple([z.number(), z.number(), z.number()]).default([1, 1, 1]),

	/**
	 * The vertical offset of the model.
	 * @default 0
	 */
	verticalOffset: z.number().default(0),

	/**
	 * The opacity of the model.
	 * @default 1
	 */
	opacity: z.number().default(1),

	/**
	 * The material state by name.
	 */
	material: z.record(z.string(), MaterialStyleSchema).optional(),

	/**
	 * Whether the model is interactive.
	 * @default false
	 */
	interactive: z.boolean().default(false),

	/**
	 * The clipping plane z offset of the model. 0 is the bottom of the model, and positive values are above the model in meters. Clips the model vertically above the clipping plane.
	 *
	 * Note: models need to be manifold for this to work
	 *
	 * @experimental
	 * @default Infinity
	 */
	clippingPlaneZOffset: z.number().default(Infinity),

	/**
	 * The color of the top of the clipping plane.
	 * @experimental
	 */
	clippingPlaneTopColor: z.string().default('#E67B5B'),

	/**
	 * Whether the clipping plane is visible.
	 * @experimental
	 * @default true
	 */
	clippingPlaneTopVisible: z.boolean().default(true),
});

export const modelStateUpdateSchema = modelStateSchema
	.extend({
		scale: z.union([z.number(), z.tuple([z.number(), z.number(), z.number()])]),
		rotation: z.union([z.number(), z.tuple([z.number(), z.number(), z.number()])]),
	})
	.partial();

const modelStateSchemaStrict = z.strictObject(modelStateSchema.shape);

/**
 * Represents the state of a model.
 */
export type TModelState = z.infer<typeof modelStateSchemaStrict>;
export type TModelUpdateState = z.infer<typeof modelStateUpdateSchema>;

export function getModelState(state: ModelState): TModelState {
	const {
		color,
		position,
		rotation,
		scale,
		visible,
		interactive,
		opacity,
		material,
		verticalOffset,
		parent,
		clippingPlaneZOffset,
		clippingPlaneTopVisible,
	} = state;
	const floorId = typeof parent === 'object' ? parent?.id?.toString() : parent?.toString();

	return parseCompleteState(modelStateSchema, {
		color,
		position: new Coordinate({
			latitude: position?.[1] ?? 0,
			longitude: position?.[0] ?? 0,
			verticalOffset: position?.[2],
			floorId,
		}),
		rotation,
		scale,
		visible,
		interactive,
		opacity,
		material,
		verticalOffset,
		clippingPlaneZOffset,
		clippingPlaneTopVisible,
	});
}
