import type { ParsedMVF } from '../../types/bundle.ts';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.ts';

export type V3Floors = Pick<MVFv2_STANDARD_MVFv3, 'floors'>;
export type V2Floors = Pick<ParsedMVF, 'floor.geojson' | 'map.geojson'>;

export function buildFloors(v3: V3Floors, _builderOptions: Partial<BuilderOptions> = {}): V2Floors {
	const floors: V2Floors['floor.geojson'] = {
		type: 'FeatureCollection',
		features: [],
	};

	const maps: V2Floors['map.geojson'] = [];

	for (const floor of v3.floors.features) {
		maps.push({
			id: floor.properties.id,
			externalId: floor.properties.details?.externalId ?? '',
			elevation: floor.properties.elevation,
			name: floor.properties.details?.name ?? '',
			shortName: floor.properties.details?.shortName ?? '',
			subtitle: floor.properties.subtitle,
			maxHeight: floor.properties.maxHeight,
		});

		floors.features.push({
			type: 'Feature',
			geometry: floor.geometry,
			properties: {
				id: floor.properties.id,
				externalId: floor.properties.details?.externalId ?? '',
				elevation: floor.properties.elevation,
				name: floor.properties.details?.name ?? '',
				shortName: floor.properties.details?.shortName ?? '',
				subtitle: floor.properties.subtitle,
				maxHeight: floor.properties.maxHeight,
			},
		});
	}
	return {
		'floor.geojson': floors,
		'map.geojson': maps,
	};
}
