import type { GeometryGroupState } from '@mappedin/core-sdk';
import { Logger } from '@packages/internal/common';
import { z } from 'zod';
import { parseCompleteState, parsePartialState } from './state-utils';

export const SHAPE_STATE_TYPE = 'shape';

/**
 * Schema for validating shape state properties.
 */
export const shapeStateSchema = z.object({
	/**
	 * The type of the shape.
	 */
	type: z.literal(SHAPE_STATE_TYPE).default(SHAPE_STATE_TYPE),

	/**
	 * Whether Shape is visible
	 * @default true
	 */
	visible: z.boolean().default(true),

	/**
	 * The altitude of the shape.
	 * @default 0
	 */
	altitude: z.number().default(0),

	/**
	 * The color of the shape.
	 * @default #ffffff
	 */
	color: z.string().default('#ffffff'),

	/**
	 * The height of the shape.
	 * Height cannot be updated at this time
	 */
	height: z.number().default(0),

	/**
	 * The opacity of the shape.
	 * @default 1
	 */
	opacity: z.number().default(1),

	/**
	 * Whether the shape is interactive.
	 * @default false
	 */
	interactive: z.boolean().default(false),

	/**
	 * Bevel configuration for extruded shapes.
	 */
	bevel: z
		.object({
			enabled: z.boolean().optional(),
			thickness: z.number().optional(),
			size: z.number().optional(),
			offset: z.number().optional(),
			segments: z.number().optional(),
		})
		.optional(),
});

export const shapeStateSchemaPartial = shapeStateSchema.partial();
const shapeStateSchemaStrict = z.strictObject(shapeStateSchema.shape);

/**
 * Represents the state of a shape.
 */
export type TShapeState = z.infer<typeof shapeStateSchemaStrict>;
export type TShapeUpdateState = z.infer<typeof shapeStateSchemaPartial>;

export function getShapeState(state: GeometryGroupState): TShapeState | undefined {
	const { altitude, visible, color, opacity, interactive, height, children, bevel } = state;

	if (children == null || children.length < 1) {
		Logger.error(`Expected at least one child for Shape (${state.id}). Cannot get shape state.`);
		return;
	}

	return parseCompleteState(shapeStateSchema, {
		altitude,
		visible,
		color,
		height,
		opacity,
		interactive,
		bevel,
	});
}

/**
 * Translates a shape state update to the core renderer state format.
 * Handles geometry updates by passing them through to the core renderer.
 *
 * @param state The partial shape state update
 * @returns The translated state for the core renderer
 */
export function translateShapeStateToGeojsonCore(state: TShapeUpdateState): Partial<GeometryGroupState> {
	const parsedState = parsePartialState(shapeStateSchemaPartial, state, { warn: true });

	// Start with all the standard properties
	const translatedState: Partial<GeometryGroupState> = {};

	if (parsedState.visible !== undefined) {
		translatedState.visible = parsedState.visible;
	}
	if (parsedState.color !== undefined) {
		translatedState.color = parsedState.color;
	}
	if (parsedState.opacity !== undefined) {
		translatedState.opacity = parsedState.opacity;
	}
	if (parsedState.altitude !== undefined) {
		translatedState.altitude = parsedState.altitude;
	}
	if (parsedState.height !== undefined) {
		translatedState.height = parsedState.height;
	}
	if (parsedState.interactive !== undefined) {
		translatedState.interactive = parsedState.interactive;
	}
	if (parsedState.bevel !== undefined) {
		// Convert our bevel object to match the expected type
		const bevelState = {
			enabled: parsedState.bevel.enabled || false,
			thickness: parsedState.bevel.thickness,
			size: parsedState.bevel.size,
			offset: parsedState.bevel.offset,
			segments: parsedState.bevel.segments,
		};
		translatedState.bevel = bevelState as any;
	}

	return translatedState;
}
