import type { ParsedMVF, RawMVF } from './types/bundle.js';

export type TextDecoderFn = typeof TextDecoder.prototype.decode;

// eslint-disable-next-line @typescript-eslint/no-unused-vars
type FolderKey<S extends string> = S extends `${infer _}.geojson` ? never : S extends `${infer _}.json` ? never : S;

// eslint-disable-next-line @typescript-eslint/no-unused-vars
type FileKey<S extends string> = S extends `${infer _}.geojson` ? S : S extends `${infer _}.json` ? S : never;

type ExhaustiveArray<T extends readonly unknown[], U> = [U] extends [T[number]] ? T : [...T, Exclude<U, T[number]>];

const defaultEntry = <K extends FolderKey<keyof RawMVF>>(folder: K): ParsedMVF[K] => {
	// Per the spec, these are required to exist, but could potentially be empty.
	if (folder === 'space' || folder === 'obstruction' || folder === 'entrance') {
		return {} as unknown as ParsedMVF[K];
	}
	return undefined as unknown as ParsedMVF[K];
};

const FILE_EXTENSION = /\.json|\.geojson$/;

const rawFolderToParsed = <K extends FolderKey<keyof RawMVF>>(
	raw: RawMVF,
	folder: K,
	decode: TextDecoderFn,
): ParsedMVF[K] => {
	const rawFolder = raw[folder];
	if (typeof rawFolder !== 'object' || Object.keys(rawFolder).length === 0) {
		return defaultEntry(folder);
	}

	const contents: Record<string, string> = {};
	for (const [key, value] of Object.entries(rawFolder)) {
		const floorId = key.replace(FILE_EXTENSION, '');
		contents[floorId] = JSON.parse(decode(value));
	}
	return contents as unknown as ParsedMVF[K];
};

// This oddity is a way to enforce a compiler error if either of these arrays
// is missing a file or folder that should be mentioned.

type OptionalKeys<O> = { [K in keyof O]-?: undefined extends O[K] ? K : never }[keyof O];
type OptionalFiles = FileKey<OptionalKeys<RawMVF>>;
const _OPTIONAL_FILES = [
	'floorstack.json',
	'styles.json',
	'shapes.json',
	'mapstack.json',
	'floor.geojson',
	'tileset.json',
	'location.json',
	'category.json',
	'navigationFlags.json',
] as const;
const OPTIONAL_FILES = _OPTIONAL_FILES satisfies ExhaustiveArray<typeof _OPTIONAL_FILES, OptionalFiles>;

type OptionalFolders = FolderKey<OptionalKeys<RawMVF>>;
const _OPTIONAL_FOLDERS = [
	'annotation',
	'textAreas',
	'enterprise',
	'floorImages',
	'shapeInstances',
	'modelInstances',
	'window',
	'area',
	'facade',
] as const;
const OPTIONAL_FOLDERS = _OPTIONAL_FOLDERS satisfies ExhaustiveArray<typeof _OPTIONAL_FOLDERS, OptionalFolders>;

export const rawToParsedMVF = (raw: RawMVF): ParsedMVF => {
	const decoder = new TextDecoder('utf-8');
	const decode = decoder.decode.bind(decoder) satisfies TextDecoderFn;

	const parsedMVF: ParsedMVF = {
		'manifest.geojson': JSON.parse(decode(raw['manifest.geojson'])),
		'mapstack.geojson': JSON.parse(decode(raw['mapstack.geojson'])),
		'map.geojson': JSON.parse(decode(raw['map.geojson'])),
		'node.geojson': JSON.parse(decode(raw['node.geojson'])),
		'connection.json': JSON.parse(decode(raw['connection.json'])),
		space: rawFolderToParsed(raw, 'space', decode),
		obstruction: rawFolderToParsed(raw, 'obstruction', decode),
		entrance: rawFolderToParsed(raw, 'entrance', decode),
	};

	for (const key of OPTIONAL_FILES) {
		if (raw[key]) {
			parsedMVF[key] = JSON.parse(decode(raw[key]));
		}
	}

	for (const key of OPTIONAL_FOLDERS) {
		if (raw[key]) {
			// @ts-expect-error since the type of each of these keys when parsed
			// is different.
			parsedMVF[key] = rawFolderToParsed(raw, key, decode);
		}
	}

	return parsedMVF;
};
