import type { ManifestCollection, ManifestFeature, ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Manifest = Pick<MVFv2_STANDARD_MVFv3, 'manifest'>;
export type V2Manifest = Pick<ParsedMVF, 'manifest.geojson'>;

export function buildManifest(v3: V3Manifest, _builderOptions: Partial<BuilderOptions>): V2Manifest {
	const v2Manifest: ManifestCollection = {
		features: [],
		type: 'FeatureCollection',
	};
	const v2: V2Manifest = {
		'manifest.geojson': v2Manifest,
	};
	const v3Manifest = v3.manifest;
	if (v3Manifest.features.length > 0) {
		const v3ManifestFeature = v3Manifest.features[0];

		const properties = v3ManifestFeature.properties;
		const v2ManifestFeature: ManifestFeature = {
			type: 'Feature',
			geometry: v3ManifestFeature.geometry,
			properties: {
				// Intentionally empty
				folder_struct: [],
				version: '2.0.0',
				time: properties.time,
				name: properties.name,
				naturalBearing: properties.naturalBearing,
				tzid: properties.tzid,
				map: properties.mapId,
				org_id: properties.orgId,
				language: properties.language,
			},
		};

		v2Manifest.features.push(v2ManifestFeature);
	}
	return v2;
}
