import type { PerspectiveCamera } from 'three';
import { BoxGeometry, Mesh, MeshBasicMaterial, PlaneGeometry, ShaderMaterial, Vector4 } from 'three';
import WatermarkVertexShader from './watermark-vertex.glsl';
import WatermarkFragmentShader from './watermark-fragment.glsl';
import { PubSub } from '@packages/internal/common';
import type { Geometry2D } from '../../entities';
import type { QuadTree } from '@packages/internal/quad-tree';
import { Rectangle } from '@packages/internal/quad-tree';
import {
	WATERMARK_LOGO_ASPECT_RATIO,
	WATERMARK_ICON_DEFAULT_WIDTH,
	WATERMARK_ICON_MIN_WIDTH,
	WATERMARK_LOGO_DEFAULT_WIDTH,
	WATERMARK_LOGO_MIN_WIDTH,
	WATERMARK_ICON_ASPECT_RATIO,
} from './constants';
import type { RendererState } from '../../renderer';
import { getLogoIcon } from './logos';
import { TextureLoader } from '../../utils/texture-loader';
import { destroyObject3D } from '../../utils/object3d-destroy';

const DEBUG = false;

export type WatermarkPosition =
	| 'top-left'
	| 'top-right'
	| 'bottom-left'
	| 'bottom-right'
	| 'center'
	| 'top'
	| 'bottom'
	| 'left'
	| 'right';

export type WatermarkOptions = {
	/**
	 * Padding in pixels. Can be a number or an object with top, right, bottom, and left properties.
	 * @default 16
	 */
	padding?: number | { top?: number; right?: number; bottom?: number; left?: number };
	/**
	 * Position of the watermark on the screen.
	 * @default 'bottom-left'
	 */
	position?: WatermarkPosition;
	/**
	 * Show only the Mappedin icon without text.
	 * @default false
	 */
	iconOnly?: boolean;
	/**
	 * Color of the watermark.
	 * @default 'dark'
	 */
	color?: 'light' | 'dark';
	/**
	 * Scale of the watermark between 0.5 and 1.5.
	 * @default 1
	 */
	scale?: number;
	/**
	 * Callback when the watermark is clicked.
	 * @hidden
	 */
	onClick?: () => void;
	/**
	 * Whether the watermark is interactive.
	 * @default true
	 */
	interactive?: boolean;
	/**
	 * Whether the watermark should be visible.
	 * @hidden
	 * @default true
	 */
	visible?: boolean;
};

export type WatermarkUpdateOptions = Omit<WatermarkOptions, 'onClick' | 'visible'> & {
	/** Show the Mappedin watermark. */
	visible?: boolean;
	/** Set the interactivity of the watermark. */
	interactive?: boolean;
};

export class WatermarkSystem extends PubSub<{
	'texture-loaded': void;
}> {
	#options: Required<WatermarkOptions> = {
		padding: 16,
		scale: 1,
		iconOnly: false,
		position: 'bottom-left',
		color: 'dark',
		onClick: () => {
			if (window != null) {
				window.open('https://www.mappedin.com');
			}
		},
		interactive: true,
		visible: true,
	};
	#rendererState: RendererState;
	#cameraObject: PerspectiveCamera;
	#mesh?: Mesh;
	#material?: ShaderMaterial = undefined;
	#collider?: Rectangle<{ entityId: 'watermark' }> = undefined;
	#width = 0;
	#height = 0;
	dirty = true;

	private get color() {
		return this.#options.color === 'light' ? 'white' : '#293136';
	}

	private get iconName() {
		return this.#options.iconOnly ? 'icon' : 'horizontal';
	}

	private get minWidth() {
		return this.#options.iconOnly ? WATERMARK_ICON_MIN_WIDTH : WATERMARK_LOGO_MIN_WIDTH;
	}

	private get defaultWidth() {
		return this.#options.iconOnly ? WATERMARK_ICON_DEFAULT_WIDTH : WATERMARK_LOGO_DEFAULT_WIDTH;
	}

	private get aspectRatio() {
		return this.#options.iconOnly ? WATERMARK_ICON_ASPECT_RATIO : WATERMARK_LOGO_ASPECT_RATIO;
	}

	get options(): Required<WatermarkOptions> {
		return { ...this.#options };
	}

	set options(options: WatermarkUpdateOptions) {
		this.#options = {
			padding: options.padding || this.#options.padding,
			position: options.position || this.#options.position,
			iconOnly: options.iconOnly || this.#options.iconOnly,
			color: options.color || this.#options.color,
			scale: options.scale || this.#options.scale,
			onClick: this.#options.onClick,
			interactive: options.interactive ?? this.#options.interactive,
			visible: options.visible ?? this.#options.visible,
		};
	}

	get icon() {
		return getLogoIcon(
			this.iconName,
			this.color,
			this.#width * this.#rendererState.pixelRatio,
			this.#height * this.#rendererState.pixelRatio,
		);
	}

	constructor(cameraObject: PerspectiveCamera, rendererState: RendererState, options?: Partial<WatermarkOptions>) {
		super();

		if (options) {
			// Assign options but fallback to defaults if undefined
			Object.entries(options).forEach(([key, value]) => {
				if (value !== undefined) {
					(this.#options as any)[key] = value;
				}
			});
		}
		this.#cameraObject = cameraObject;
		this.#rendererState = rendererState;
	}

	private setSize(w: number, h: number) {
		if (!this.#material) return;
		this.#material.uniforms.uSize.value.x = w;
		this.#material.uniforms.uSize.value.y = h;
	}

	private setXY(x: number, y: number) {
		if (!this.#material) return;
		this.#material.uniforms.uSize.value.w = x;
		this.#material.uniforms.uSize.value.z = y;
	}

	get width() {
		return this.#width;
	}

	get height() {
		return this.#height;
	}

	getPositionAlignedOffset(width: number, height: number) {
		const offsets: Record<
			WatermarkPosition,
			{
				x: number;
				y: number;
			}
		> = {
			'top-left': { x: 0, y: 0 },
			'top-right': { x: -width, y: 0 },
			'bottom-left': { x: 0, y: -height },
			'bottom-right': { x: -width, y: -height },
			center: { x: -(width / 2), y: -(height / 2) },
			top: { x: -(width / 2), y: 0 },
			bottom: { x: -(width / 2), y: -height },
			left: { x: 0, y: -(height / 2) },
			right: { x: -width, y: -(height / 2) },
		};

		return offsets[this.#options.position];
	}

	getPositionAlignedCanvasCoordinates = (canvasWidth: number, canvasHeight: number) => {
		const coords: Record<
			WatermarkPosition,
			{
				x: number;
				y: number;
			}
		> = {
			'top-left': { x: 0, y: 0 },
			'top-right': { x: canvasWidth, y: 0 },
			'bottom-left': { x: 0, y: canvasHeight },
			'bottom-right': { x: canvasWidth, y: canvasHeight },
			center: { x: canvasWidth / 2, y: canvasHeight / 2 },
			top: { x: canvasWidth / 2, y: 0 },
			bottom: { x: canvasWidth / 2, y: canvasHeight },
			left: { x: 0, y: canvasHeight / 2 },
			right: { x: canvasWidth, y: canvasHeight / 2 },
		};

		return coords[this.#options.position];
	};

	getPositionAlignedPadding = () => {
		const padding = this.#options.padding;
		if (typeof padding === 'number') {
			const alignedPaddings: Record<
				WatermarkPosition,
				{
					x: number;
					y: number;
				}
			> = {
				'top-left': { x: padding, y: padding },
				'top-right': { x: -padding, y: padding },
				'bottom-left': { x: padding, y: -padding },
				'bottom-right': { x: -padding, y: -padding },
				center: { x: 0, y: 0 },
				top: { x: 0, y: padding },
				bottom: { x: 0, y: -padding },
				left: { x: padding, y: 0 },
				right: { x: -padding, y: 0 },
			};

			return alignedPaddings[this.#options.position];
		}

		const paddings = {
			top: padding['top'] ?? 0,
			right: padding['right'] ?? 0,
			bottom: padding['bottom'] ?? 0,
			left: padding['left'] ?? 0,
		};

		return { x: paddings['left'] - paddings['right'], y: paddings['top'] - paddings['bottom'] };
	};

	insertIntoQuadTree(quadTree: QuadTree<{ entityId: Geometry2D['id'] }>) {
		if (this.#collider != null && typeof this.#options.onClick === 'function' && this.#options.interactive) {
			quadTree.insert(this.#collider);
		}
	}

	resize(canvasWidth: number, canvasHeight: number) {
		if (this.#options.visible !== true) {
			return;
		}

		// Get the canvas start position and padding based on watermark position option
		const canvasPosition = this.getPositionAlignedCanvasCoordinates(canvasWidth, canvasHeight);
		const padding = this.getPositionAlignedPadding();
		// Get the height/width offset based on watermark size
		const offset = this.getPositionAlignedOffset(this.#width, this.#height);

		// Keep the watermark within the canvas bounds
		const x = Math.min(canvasWidth - this.#width, Math.max(0, canvasPosition.x + padding.x + offset.x));
		const y = Math.min(canvasHeight - this.#height, Math.max(0, canvasPosition.y + padding.y + offset.y));

		// Update the texture
		this.setSize(this.#width / canvasWidth, this.#height / canvasHeight);
		this.setXY(x / canvasWidth, y / canvasHeight);

		this.#collider = new Rectangle(x, y, this.#width, this.#height, {
			entityId: 'watermark',
		});
	}

	update() {
		if (!this.dirty) {
			return;
		}

		if (this.#options.visible === true) {
			/**
			 * We want to avoid the developer hiding our watermark so we clamp the width minimum to some visible size,
			 * and maximum to the canvas width. It may warn and shrink the watermark if the canvas is too small.
			 */
			this.#width = Math.min(
				this.#rendererState.canvasWidth,
				Math.max(this.minWidth, this.defaultWidth * this.#options.scale),
			);
			this.#height = this.#width / this.aspectRatio;

			TextureLoader.getLoader().load(`data:image/svg+xml;base64,${btoa(this.icon)}`, imageData => {
				this.#material = new ShaderMaterial({
					uniforms: {
						uTexture: { value: imageData },
						uSize: { value: new Vector4(0, 0, 0, 0) }, // x, y, w, h
						uOpacity: { value: 0.7 },
					},
					vertexShader: WatermarkVertexShader,
					fragmentShader: WatermarkFragmentShader,
					depthWrite: false,
					depthTest: false,
					defines: { debug: DEBUG },
					wireframe: DEBUG,
					transparent: true,
					visible: true,
				});

				if (!this.#mesh) {
					const planeGeometry = new PlaneGeometry(2, 2, 1, 1);
					this.#mesh = new Mesh(planeGeometry, this.#material);
					this.#mesh.position.set(0, 0, -1);
					this.#mesh.frustumCulled = false;
					// We want the mesh to be rendered the last with depthWrite and depthTest disabled
					// It allows the watermark to be rendered on top of everything else
					this.#mesh.renderOrder = 999;
					// HACK: Add a transparent box so that the watermark doesn't disappear when the camera
					// moves away from any geometry in the scene. Actual reason for this happening is unknown
					// but likely to do with interleaved rendering with mapLibre
					const box = new BoxGeometry(1, 1, 1);
					const mesh = new Mesh(
						box,
						new MeshBasicMaterial({ transparent: true, opacity: 0, depthTest: false, depthWrite: false }),
					);
					mesh.frustumCulled = false;
					mesh.layers.set(1);
					this.#mesh.add(mesh);
					this.#cameraObject.add(this.#mesh);
				} else {
					this.#mesh.visible = true;
					this.#mesh.material = this.#material;
					this.#mesh.material.needsUpdate = true;
				}
				this.resize(this.#rendererState.canvasWidth, this.#rendererState.canvasHeight);
				this.publish('texture-loaded');
			});
		} else {
			if (this.#mesh) {
				this.#mesh.visible = false;
			}
		}

		this.dirty = false;
	}

	destroy() {
		if (this.#mesh) {
			this.#cameraObject.remove(this.#mesh);
			destroyObject3D(this.#mesh);
		}
		this.#mesh = undefined;
		if (this.#material) {
			this.#material.dispose();
		}
		this.#material = undefined;
		this.#collider = undefined;
	}
}
