import merge from 'lodash.merge';
import { isColor, Logger, rgbArrayToString, stringToRgbArray } from '@packages/internal/common';
import type { PartialDeep } from 'type-fest';

/**
 * Extracts only the properties from object1 that have corresponding keys in object2.
 * Maintains the same nested structure but only includes properties that will be animated.
 */
export const extractProperties = <T extends object>(object1: T, object2: PartialDeep<T>): Partial<T> => {
	const result: Partial<T> = {};

	const processObject = (object1: any, object2: any, target: any) => {
		for (const key in object2) {
			const object1Value = object1[key];
			const object2Value = object2[key];

			if (
				typeof object2Value === 'object' &&
				!Array.isArray(object2Value) &&
				object2Value !== null &&
				typeof object1Value === 'object' &&
				!Array.isArray(object1Value) &&
				object1Value !== null
			) {
				// For nested objects, create a new object and process recursively
				target[key] = {};
				processObject(object1Value, object2Value, target[key]);
			} else {
				// For leaf values, copy the initial value
				target[key] = object1Value;
			}
		}
	};

	processObject(object1, object2, result);

	return result;
};

export const animationFactory = <T extends object>(initialState: T, finalValues: PartialDeep<T>) => {
	const baseState = extractProperties(initialState, finalValues);
	const finalState = merge({}, baseState, finalValues);

	// Create one intermediate state object we will continously update to avoid creating new objects every frame
	const intermediateState = merge({}, baseState);

	// Define interpolation function once
	const interpolate = (initial: object, final: object, percent: number, target: object = intermediateState) => {
		for (const key in final) {
			const currentValue = initial[key];
			const finalValue = final[key];

			if (typeof finalValue === 'number' && typeof currentValue === 'number') {
				// Interpolate numbers
				target[key] = currentValue + (finalValue - currentValue) * percent;
			} else if (typeof finalValue === 'string' && typeof currentValue === 'string') {
				if (isColor(currentValue) && isColor(finalValue)) {
					const currentColor = stringToRgbArray(currentValue);
					const finalColor = stringToRgbArray(finalValue);

					if (currentColor && finalColor) {
						target[key] = rgbArrayToString([
							currentColor[0] + (finalColor[0] - currentColor[0]) * percent,
							currentColor[1] + (finalColor[1] - currentColor[1]) * percent,
							currentColor[2] + (finalColor[2] - currentColor[2]) * percent,
						]);
					} else {
						Logger.warn(`Color "${key}" could not be animated!`);
					}
				}
			} else if (
				typeof finalValue === 'object' &&
				finalValue !== null &&
				typeof currentValue === 'object' &&
				currentValue !== null
			) {
				// Recursively interpolate nested objects
				interpolate(currentValue, finalValue, percent, target[key]);
			} else {
				Logger.warn(`Value "${key}" could not be animated!`);
			}
		}
	};

	return (percent: number) => {
		percent = Math.min(Math.max(percent, 0), 1);

		if (percent === 0) {
			return initialState;
		}

		if (percent === 1) {
			return finalState;
		}

		interpolate(baseState, finalValues, percent);

		return intermediateState;
	};
};
