import { Box3, MathUtils, Vector3 } from 'three';
import type { RendererCore } from '.';
import type { Position as GeoJSONPosition } from 'geojson';
import type { InsetPadding, Position, All3DTypes } from '../src/types';
import { Easing, type Tween } from '@tweenjs/tween.js';
import type { InsetPaddingOption, RendererState, Systems } from './renderer';
import { type LngLatBoundsLike } from '@packages/internal/outdoor-context-v5';
import { clampWithWarning, Logger, type EasingCurve } from '@packages/internal/common';
import { CAMERA_CONSTANTS } from './systems/camera';
import { DEFAULT_INSET_PADDING } from './utils/constants';
import { getVerticesOfEntity } from './utils/ecs';
import { GroupContainerObject3D } from './entities/group-container';

// the default values, but also the valid range
const defaultMinPitch = 0;
const defaultMaxPitch = 90;

const DEBUG_FOCUSON_BOUNDS = process.env.NODE_ENV === 'test' ? false : false;

export type AnimationOptions = {
	duration?: number;
	easing?: EasingCurve;
};

export type AnimateElevationOptions = AnimationOptions & {
	interruptible?: boolean;
};

export type FocusOnOptions = AnimationOptions & {
	bearing?: number;
	pitch?: number;
	padding?: InsetPaddingOption;
	minZoomLevel?: number;
	maxZoomLevel?: number;
	interruptible?: boolean;
	verticalPadding?: number;
};

export type AnimateToOptions = AnimationOptions & {
	center?: Position;
	zoomLevel?: number;
	bearing?: number;
	pitch?: number;
	elevation?: number;
	interruptible?: boolean;
};

export const ANIMATION_TWEENS = {
	linear: Easing.Linear.None,
	'ease-in': Easing.Quadratic.In,
	'ease-out': Easing.Quadratic.Out,
	'ease-in-out': Easing.Quadratic.InOut,
};

/**
 * Fixes the issue with underground floors disappearing when viewed from above (in meters)
 */
const MAX_UNDERGROUND_DEPTH = -50;

export class Camera {
	#core: RendererCore;
	#systems: Systems;
	#state: RendererState;
	/**
	 * @internal
	 */
	constructor(core: RendererCore, systems: Systems, state: RendererState) {
		this.#core = core;
		this.#systems = systems;
		this.#state = state;
		if (this.#core.map) {
			this.#core.map.transform.setMinElevationForCurrentTile(MAX_UNDERGROUND_DEPTH);
		}
		this.#systems.cameraSystem.on('pedestal-change', ({ pedestal: elevation }) => {
			this.#systems.cameraSystem.setCameraPlaneElevation(elevation);
		});
	}

	/**
	 * The camera's current center in [longitude, latitude] degrees.
	 */
	get center() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map.getCenter?.().toArray() as Position;
		}

		return this.#systems.cameraSystem.center;
	}

	/**
	 * @param center The center in [longitude, latitude] degrees.
	 */
	setCenter(center: Position) {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			this.#core.map?.setCenter(center as [number, number]);
			this.#systems.interactionSystem.handleHover();

			return;
		}
		this.#systems.cameraSystem.setCenter(center[0], center[1]);
		this.#systems.interactionSystem.handleHover();
	}

	/**
	 * Current camera zoom in mercator zoom level units.
	 */
	get zoomLevel() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map?.getZoom();
		}

		return this.#systems.cameraSystem.getZoomLevel();
	}

	/**
	 * Set the camera's zoom level in mercator zoom level units.
	 */
	setZoomLevel(zoomLevel: number) {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			this.#core.map?.setZoom(zoomLevel);
		}

		this.#systems.cameraSystem.setZoomLevel(
			clampWithWarning(
				zoomLevel,
				this.minZoomLevel,
				this.maxZoomLevel,
				'zoomLevel was clamped to minZoomLevel/maxZoomLevel bounds. Use mapView.Camera.setMinZoomLevel() and mapView.Camera.setMaxZoomLevel() to adjust limits.',
			),
		);
	}

	getState() {
		return {
			zoomLevel: this.zoomLevel,
			minZoomLevel: this.minZoomLevel,
			maxZoomLevel: this.maxZoomLevel,
			minPitch: this.minPitch,
			maxPitch: this.maxPitch,
			autoMinZoomLevel: this.autoMinZoomLevel,
			panMode: this.panMode,
			screenOffsets: this.insetsPadding,
			center: this.center as [number, number],
			bearing: this.bearing,
			pitch: this.pitch,
			elevation: this.elevation,
		};
	}

	/**
	 * Whether the camera is animating.
	 */
	get isAnimating() {
		return this.#systems.cameraSystem.isAnimating;
	}

	/**
	 * Cancel the last animation.
	 */
	cancelAnimation() {
		this.#systems.cameraSystem.cancelAnimation();
	}

	/**
	 * How far the camera can zoom in towards the ground
	 */
	get maxZoomLevel() {
		return this.#systems.cameraSystem.maxZoomLevel;
	}

	/**
	 * How far the camera can zoom out away from the ground
	 */
	get minZoomLevel() {
		return this.#systems.cameraSystem.minZoomLevel;
	}

	/**
	 * Toggle the mode of the camera to automatically set the minimum zoom level based on the size of the scene.
	 * It will be automatically disabled when the minimum zoom level is set manually.
	 * @param value The new value for the auto min zoom level mode.
	 */
	setAutoMinZoomLevel(value: boolean) {
		this.#systems.panBoundsSystem.autoMinZoomLevel = value;
		if (value) {
			this.#systems.panBoundsSystem.dirty = true;
			this.#systems.panBoundsSystem.update();
		}
	}

	/**
	 * The mode of the camera to automatically set the minimum zoom level based on the size of the scene.
	 */
	get autoMinZoomLevel() {
		return this.#systems.panBoundsSystem.autoMinZoomLevel;
	}

	/**
	 * How far out the camera can zoom
	 *
	 * @param zoomLevel  The minimum zoom level in mercator zoom level units.
	 * @default 0
	 */
	setMinZoomLevel(zoomLevel: number, disableAutoMinZoomLevel = false) {
		if (disableAutoMinZoomLevel) this.setAutoMinZoomLevel(false);

		const targetMinZoom = clampWithWarning(
			zoomLevel,
			CAMERA_CONSTANTS.MIN_MERCATOR_ZOOM_LEVEL,
			this.maxZoomLevel,
			`setMinZoomLevel was clamped. minZoomLevel must be between ${CAMERA_CONSTANTS.MIN_MERCATOR_ZOOM_LEVEL} and maxZoomLevel.`,
		);
		this.#systems.cameraSystem.setMinZoomLevel(targetMinZoom);
		// make sure that we snap to the nearest zoom level if zoom level is below the new min zoom level
		if (this.zoomLevel < targetMinZoom) {
			this.setZoomLevel(targetMinZoom);
		}
		this.#core.map?.setMinZoom(targetMinZoom);
	}

	/**
	 * How far in the camera can zoom
	 *
	 * @param zoomLevel The maximum zoom level in mercator zoom level units.
	 * @default Infinity
	 */
	setMaxZoomLevel(zoomLevel: number) {
		const targetMaxZoom = clampWithWarning(
			zoomLevel,
			this.minZoomLevel,
			CAMERA_CONSTANTS.MAX_MERCATOR_ZOOM_LEVEL,
			`setMaxZoomLevel was clamped. maxZoomLevel must be between minZoomLevel and ${CAMERA_CONSTANTS.MAX_MERCATOR_ZOOM_LEVEL}.`,
		);
		this.#systems.cameraSystem.setMaxZoomLevel(targetMaxZoom);
		// make sure that we snap to the nearest zoom level if zoom level is above the new max zoom level
		if (this.zoomLevel > targetMaxZoom) {
			this.setZoomLevel(targetMaxZoom);
		}
		this.#core.map?.setMaxZoom(targetMaxZoom);
	}

	/**
	 * The maximum pitch in degrees.
	 */
	get maxPitch() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map?.getMaxPitch?.();
		}

		return MathUtils.radToDeg(this.#systems.cameraSystem.maxTilt);
	}

	/**
	 * The minimum pitch in degrees.
	 */
	get minPitch() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map?.getMinPitch?.();
		}

		return MathUtils.radToDeg(this.#systems.cameraSystem.minTilt);
	}

	/**
	 * @param pitch The minimum pitch in degrees.
	 */
	setMinPitch(pitch: number) {
		const targetMinPitch = Math.max(defaultMinPitch, pitch);
		this.#core.map?.setMinPitch(targetMinPitch);
		this.#systems.cameraSystem.minTilt = MathUtils.degToRad(targetMinPitch);
	}

	/**
	 * @param pitch The maximum pitch in degrees.
	 */
	setMaxPitch(pitch: number) {
		const targetMaxPitch = Math.min(defaultMaxPitch, pitch);
		this.#core.map?.setMaxPitch(targetMaxPitch);
		this.#systems.cameraSystem.maxTilt = MathUtils.degToRad(targetMaxPitch);
	}

	/**
	 * The camera's current clockwise rotation in degrees from north.
	 */
	get bearing() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map?.getBearing();
		}

		return MathUtils.radToDeg(this.#systems.cameraSystem.getRotation());
	}

	/**
	 * @param bearing The bearing in degrees from north.
	 */
	setBearing(bearing: number) {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			this.#core.map?.setBearing(bearing);

			return;
		}

		this.#systems.cameraSystem.setRotation(MathUtils.degToRad(bearing));
	}

	/**
	 * The camera's current pitch in degrees.
	 */
	get pitch() {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			return this.#core.map?.getPitch();
		}

		return MathUtils.radToDeg(this.#systems.cameraSystem.getTilt());
	}

	/**
	 *
	 * @param pitch The pitch in degrees.
	 */
	setPitch(pitch: number) {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			this.#core.map?.setPitch(pitch);

			return;
		}

		this.#systems.cameraSystem.setTilt(
			MathUtils.degToRad(
				clampWithWarning(
					pitch,
					this.minPitch,
					this.maxPitch,
					'pitch was clamped to minPitch/maxPitch bounds. Use mapView.Camera.setMinPitch() and mapView.Camera.setMaxPitch() to adjust limits.',
				),
			),
		);
	}

	/**
	 * The camera's current elevation in meters from the ground.
	 */
	get elevation() {
		return this.#systems.cameraSystem.getPedestal();
	}

	/**
	 * @param elevation The elevation in meters.
	 */
	setElevation(elevation: number) {
		this.#systems.cameraSystem.setPedestal(elevation, true);
	}

	#elevationAnimation?: Tween;

	/**
	 * Animate the camera's elevation to a new value.
	 * @param elevation The new elevation in meters.
	 * @param options The animation options.
	 */
	animateElevation(elevation: number, options?: AnimateElevationOptions) {
		if (this.#elevationAnimation) {
			this.#elevationAnimation.stop();
		}

		return new Promise<void>(resolve => {
			this.#systems.cameraSystem.animateCamera(
				{
					// Only animate the z position, allowing the user to pan while the camera moves vertically
					position: { z: elevation },
				},
				options?.duration || 300,
				ANIMATION_TWEENS[options?.easing || 'ease-in-out'],
				resolve,
				resolve,
				{
					interruptible: options?.interruptible ?? true,
				},
			);
		});
	}

	/**
	 * The padding around the edges of the map when focusing on a set of coordinates.
	 */
	get insetsPadding() {
		return this.#state.insetsPadding;
	}

	/**
	 * The camera's current pan mode.
	 */
	get panMode() {
		return this.#systems.cameraSystem.enablePan === false && this.#systems.cameraSystem.enablePedestal === true
			? ('elevation' as const)
			: ('default' as const);
	}

	/**
	 * Set the camera's pan mode. 'elevation' moves the camera up and down, while 'default' allows the camera to pan along the ground.
	 */
	setPanMode(panMode: 'default' | 'elevation') {
		if (panMode !== 'default' && panMode !== 'elevation') {
			throw new Error('Invalid pan mode');
		}
		if (panMode === this.panMode) {
			return;
		}
		if (panMode === 'default') {
			this.#systems.cameraSystem.enablePan = true;
			this.#systems.cameraSystem.enablePedestal = false;
		} else {
			this.#systems.cameraSystem.enablePan = false;
			this.#systems.cameraSystem.enablePedestal = true;
		}
	}

	/**
	 *
	 * @param padding The padding around the edges of the map when focusing on a set of coordinates (in screen pixels).
	 */
	setInsetPadding(padding: Partial<InsetPadding>) {
		this.#state.insetsPadding = Object.assign({}, this.#state.insetsPadding, padding);
	}

	animateTo(options: AnimateToOptions) {
		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			this.#core.map?.flyTo({
				center: (options.center ?? this.center) as [number, number],
				zoom: options.zoomLevel ?? this.zoomLevel,
				bearing: options.bearing ?? this.bearing,
				pitch: options.pitch ?? this.pitch,
				duration: options.duration ?? 600,
				// TODO: add easing
				// easing: options.easing,
			});

			return Promise.resolve();
		}

		const position = options.center
			? this.#core.convertTo3DMapPosition(options.center[1], options.center[0], options.center[2] || this.elevation)
			: undefined;
		const zoom =
			options.zoomLevel !== undefined
				? this.#systems.cameraSystem.convertZoomLevelToAltitude(
						clampWithWarning(
							options.zoomLevel,
							this.minZoomLevel,
							this.maxZoomLevel,
							'zoomLevel was clamped to minZoomLevel/maxZoomLevel bounds. Use mapView.Camera.setMinZoomLevel() and mapView.Camera.setMaxZoomLevel() to adjust limits.',
						),
				  )
				: undefined;
		const rotation = options.bearing !== undefined ? MathUtils.degToRad(options.bearing) : undefined;
		const tilt =
			options.pitch !== undefined
				? MathUtils.degToRad(
						clampWithWarning(
							options.pitch,
							this.minPitch,
							this.maxPitch,
							'pitch was clamped to minPitch/maxPitch bounds. Use mapView.Camera.setMinPitch() and mapView.Camera.setMaxPitch() to adjust limits.',
						),
				  )
				: undefined;

		return new Promise<void>(resolve => {
			this.#systems.cameraSystem.animateCamera(
				{ position, zoom, rotation, tilt },
				options.duration || 0,
				ANIMATION_TWEENS[options.easing || 'ease-in-out'],
				() => {
					this.#systems.interactionSystem.handleHover();
					resolve();
				},
				resolve,
				{
					interruptible: options?.interruptible ?? true,
				},
			);
		});
	}

	getCameraFrameForCoordinates(coordinates: (GeoJSONPosition | Position | string)[], options?: FocusOnOptions) {
		const _options = this.#convertToLegacy(options || {}, () => Promise.resolve());

		const { pointCloud } = processTargets(coordinates, this.#core, this.#state.geometry3DMap);
		if (pointCloud.length === 0) {
			Logger.error('No valid coordinates found in targets:', coordinates);

			return {
				center: this.center,
				zoomLevel: this.zoomLevel,
			};
		}

		const { center, zoomLevel } = this.#systems.cameraSystem.getCameraFrameForCoordinates(pointCloud, _options);

		return {
			center: this.#core.convert3DMapPositionToCoordinate(center),
			zoomLevel: zoomLevel,
		};
	}

	#convertToLegacy(options: FocusOnOptions, resolve: (value: void | PromiseLike<void>) => void) {
		return {
			minZoom:
				options?.maxZoomLevel === undefined
					? undefined
					: this.#systems.cameraSystem.convertZoomLevelToAltitude(
							clampWithWarning(
								options.maxZoomLevel,
								this.minZoomLevel,
								this.maxZoomLevel,
								'maxZoomLevel was clamped to minZoomLevel/maxZoomLevel bounds. Use mapView.Camera.setMaxZoomLevel() to adjust limits.',
							),
					  ),
			maxZoom:
				options?.minZoomLevel === undefined
					? undefined
					: this.#systems.cameraSystem.convertZoomLevelToAltitude(
							clampWithWarning(
								options.minZoomLevel,
								this.minZoomLevel,
								this.maxZoomLevel,
								'minZoomLevel was clamped to minZoomLevel/maxZoomLevel bounds. Use mapView.Camera.setMinZoomLevel() to adjust limits.',
							),
					  ),

			padding: options?.padding ? Object.assign({}, DEFAULT_INSET_PADDING, options?.padding) : this.insetsPadding,
			curve: ANIMATION_TWEENS[options?.easing || 'ease-in-out'],
			rotation: options?.bearing === undefined ? undefined : MathUtils.degToRad(options.bearing),
			tilt:
				options?.pitch !== undefined
					? MathUtils.degToRad(
							clampWithWarning(
								options.pitch,
								this.minPitch,
								this.maxPitch,
								'pitch was clamped to minPitch/maxPitch bounds. Use mapView.Camera.setMinPitch() and mapView.Camera.setMaxPitch() to adjust limits.',
							),
					  )
					: undefined,
			duration: options?.duration,
			callback: () => {
				this.#systems.interactionSystem.handleHover();
				resolve();
			},
			cancelledCallback: resolve,
			interruptible: options?.interruptible ?? true,
			verticalPadding: options?.verticalPadding,
		};
	}

	/**
	 *  Focuses the camera on a set of coordinates, with optional animation
	 *
	 * @param target list of [longitude, latitude, altitude?] points
	 * @param options {FocusOnOptions}
	 * @returns Promise<void>
	 */
	focusOn(target: (GeoJSONPosition | Position | string)[], options?: FocusOnOptions) {
		const { pointCloud, geoBounds, firstPosition } = processTargets(target, this.#core, this.#state.geometry3DMap);
		if (pointCloud.length === 0) {
			Logger.error('No valid coordinates found in targets:', target);

			return Promise.resolve();
		}

		if (this.#core.mode === 'outdoors-overlay' && this.#core.map) {
			if (pointCloud.length === 1 && firstPosition) {
				this.#core.map?.flyTo({
					center: [firstPosition[0], firstPosition[1]],
					duration: options?.duration || this.#core.map.getBearing(),
					animate: true,
					// padding: options?.padding,
					bearing: options?.bearing === undefined ? this.#core.map.getBearing() : options?.bearing,
					pitch: options?.pitch === undefined ? this.#core.map.getPitch() : options?.pitch,
					zoom: options?.maxZoomLevel || this.#core.map.getMaxZoom(),
				});
			} else {
				if (DEBUG_FOCUSON_BOUNDS) {
					drawBounds(this.#core, geoBounds);
				}

				// FIXME: bring back animation
				this.#core.map?.fitBounds(geoBounds, {
					duration: options?.duration ?? CAMERA_CONSTANTS.DEFAULT_ANIMATION_DURATION,
					bearing: options?.bearing === undefined ? this.#core.map.getBearing() : options?.bearing,
					pitch: options?.pitch === undefined ? this.#core.map.getPitch() : options?.pitch,
					maxZoom: options?.maxZoomLevel ?? this.#core.map.getMaxZoom(),
				});
			}
			this.#systems.interactionSystem.handleHover();

			return Promise.resolve();
		}

		return new Promise<void>(resolve => {
			this.#systems.cameraSystem.focusOn(pointCloud, this.#convertToLegacy(options || {}, resolve));
		});
	}
}

function updateBounds(bounds: LngLatBoundsLike, coord: Position | GeoJSONPosition) {
	bounds[0] = Math.min(bounds[0], coord[0]);
	bounds[1] = Math.min(bounds[1], coord[1]);
	bounds[2] = Math.max(bounds[2], coord[0]);
	bounds[3] = Math.max(bounds[3], coord[1]);
}

export function processTargets(
	targets: (GeoJSONPosition | Position | string)[],
	core: RendererCore,
	geometry3DMap: Map<string | number, All3DTypes>,
): { pointCloud: Vector3[]; geoBounds: LngLatBoundsLike; firstPosition: (Position | GeoJSONPosition) | null } {
	const pointCloud: Vector3[] = [];
	const geoBounds: LngLatBoundsLike = [Infinity, Infinity, -Infinity, -Infinity];
	let firstPosition: (Position | GeoJSONPosition) | null = null;

	// Early validation of input
	if (!targets || !Array.isArray(targets) || targets.length === 0) {
		return { pointCloud, geoBounds, firstPosition };
	}

	// Check if render state is dirty
	const renderSystem = core.getSystems().renderSystem;

	for (const coord of targets) {
		if (typeof coord === 'string') {
			// we only need a render for non coordinate targets
			if (renderSystem.threeDdirty || renderSystem.twoDdirty) {
				core.renderSync();
			}
			const entity = geometry3DMap.get(coord);
			if (!entity) {
				Logger.error('Entity not found with id:', coord);
				continue;
			}

			const boundingBox = new Box3();
			getVerticesOfEntity(
				core,
				'userData' in entity ? entity.userData.entityId ?? entity.id : entity.id,
				pointCloud,
				boundingBox,
				entity instanceof GroupContainerObject3D ? entity.position.z : undefined,
			);
			const min = core.convert3DMapPositionToCoordinate(boundingBox.min);
			const max = core.convert3DMapPositionToCoordinate(boundingBox.max);
			updateBounds(geoBounds, min);
			updateBounds(geoBounds, max);
			if (!firstPosition) {
				firstPosition = core.convert3DMapPositionToCoordinate(boundingBox.getCenter(new Vector3()));
			}
		} else {
			pointCloud.push(core.convertTo3DMapPosition(coord[1], coord[0], coord[2] || 0));
			updateBounds(geoBounds, coord);
			if (!firstPosition) {
				firstPosition = coord;
			}
		}
	}

	return { pointCloud, geoBounds, firstPosition };
}

function drawBounds(core: RendererCore, geoBounds: LngLatBoundsLike) {
	// Draw bounds on the map for debugging
	const boundsCoordinates = [
		[geoBounds[0], geoBounds[1]], // Southwest corner
		[geoBounds[0], geoBounds[3]], // Northwest corner
		[geoBounds[2], geoBounds[3]], // Northeast corner
		[geoBounds[2], geoBounds[1]], // Southeast corner
		[geoBounds[0], geoBounds[1]], // Back to Southwest to close the polygon
	];

	// Remove previous bounds layer if it exists
	if (core.map?.getLayer('bounds-outline')) {
		core.map.removeLayer('bounds-outline');
	}
	if (core.map?.getSource('bounds-source')) {
		core.map.removeSource('bounds-source');
	}

	// Add the bounds as a source and layer
	core.map?.addSource('bounds-source', {
		type: 'geojson',
		data: {
			type: 'Feature',
			properties: {},
			geometry: {
				type: 'LineString',
				coordinates: boundsCoordinates,
			},
		},
	});

	core.map?.addLayer({
		id: 'bounds-outline',
		type: 'line',
		source: 'bounds-source',
		layout: {},
		paint: {
			'line-color': '#FF0000',
			'line-width': 2,
			'line-opacity': 0.8,
		},
	});
}
