import tinycolor from 'tinycolor2';
import type Core from '../../../src/renderer';
import type { RendererCore, WatermarkUpdateOptions } from '../../../src';

export function mountSceneGraphVisualizer(mv: Core, gui) {
	const parseChild = createParseChild(mv, gui);
	parseChild(mv.getScene(), gui);

	createWatermarkChild(mv, gui);

	const xp = gui.addFolder('Export');
	xp.add(
		{
			export: async () => {
				const gltf = await mv.getCurrentSceneGLTF({
					binary: true,
				});
				const blob = new Blob([gltf], { type: 'application/octet-stream' });
				var a = document.createElement('a');
				document.body.appendChild(a);
				a.style.display = 'none';
				const url = window.URL.createObjectURL(blob);
				a.href = url;
				a.download = 'scene.glb';
				a.click();
				window.URL.revokeObjectURL(url);
			},
		},
		'export',
	);

	const camera = gui.addFolder('Camera');
	const state = {
		get bearing() {
			return mv.camera.bearing;
		},
		set bearing(value) {
			mv.camera.setBearing(value);
		},
		get pitch() {
			return mv.camera.pitch;
		},
		set pitch(value) {
			mv.camera.setPitch(value);
		},
		get zoomLevel() {
			return mv.camera.zoomLevel;
		},
		set zoomLevel(value) {
			mv.camera.setZoomLevel(value);
		},
	};
	camera.add(state, 'bearing', -359, 359, 1);
	camera.add(state, 'pitch', mv.camera.minPitch, mv.camera.maxPitch, 1);
	camera.add(state, 'zoomLevel', mv.camera.minZoomLevel, mv.camera.maxZoomLevel, 0.1);
}

function createParseChild(mv: RendererCore, root) {
	const gui = root;

	return function parseChild(obj, parent) {
		const group = parent.addFolder(`${[obj.type]} ${obj.id.slice(0, 16)}`);
		parent.close();
		if (obj.type === 'geometry' || obj.type === 'model' || obj.type === 'path' || obj.type === 'image') {
			const state = {
				get height() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' ? state.height : 0;
				},
				set height(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry') {
						mv.setState(obj, {
							height: value,
						});
					}
				},
				set flipImageToFaceCamera(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'image') {
						mv.setState(obj, {
							flipImageToFaceCamera: value,
						});
					}
				},
				get flipImageToFaceCamera() {
					const state = mv.getState(obj.id);

					return state?.type === 'image' ? state.flipImageToFaceCamera : false;
				},
				set visible(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry' || state?.type === 'path' || state?.type === 'image') {
						mv.setState(obj, {
							visible: value,
						});
					}
				},
				get visible() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' || state?.type === 'path' || state?.type === 'image'
						? state.visible
						: false;
				},
				set outline(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry') {
						mv.setState(obj, {
							outline: value,
						});
					}
				},
				get outline() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' ? state.outline : false;
				},
				get opacity() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' ? state.opacity : 1;
				},
				set opacity(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry') {
						mv.setState(state, {
							opacity: value,
						});
					}
				},
				get color() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' && state.color ? tinycolor(state.color).toHexString() : '#ffffff';
				},
				set color(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry') {
						mv.setState(state, {
							color: value,
						});
					}
				},
				set interactive(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry' || state?.type === 'model' || state?.type === 'path') {
						mv.setState(obj, {
							interactive: value,
						});
						gui.controllersRecursive().forEach(c => c.updateDisplay());
					}
				},
				get interactive() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry' || state?.type === 'model' || state?.type === 'path'
						? state.interactive
						: false;
				},
				get completeFraction() {
					const state = mv.getState(obj.id);

					return state?.type === 'path' ? state.completeFraction : 1;
				},
				set completeFraction(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'path') {
						mv.setState(state, {
							completeFraction: value,
						});
					}
				},
				focus: () => {},
			};
			group.add(state, 'focus').onChange(() => {
				const state = mv.getState(obj.id);

				if (state?.type === 'model' || state?.type === 'path' || state?.type === 'image') {
					mv.camera.focusOn([state.position]);
				} else if (state?.type === 'geometry') {
					mv.camera.focusOn(mv.getBoundingArea(state)!);
				}
			});
			group.add(state, 'visible');
			if (obj.type === 'image') {
				group.add(state, 'flipImageToFaceCamera');
			} else {
				group.add(state, 'interactive');

				if (obj.type === 'path') {
					group.add(state, 'completeFraction', 0, 1, 0.1);
				}
				if (obj.type !== 'model') {
					group.addColor(state, 'color');
					group.add(state, 'outline');
				}
			}
			if (obj.type === 'geometry') {
				group.add(state, 'height');
				group.add(state, 'opacity', 0, 1, 0.1);
			}
		} else if (obj.type === 'marker') {
			const state = {
				get interactive() {
					const state = mv.getState(obj.id);

					return state?.type === 'marker' ? state.options?.interactive : false;
				},
				set interactive(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'marker') {
						mv.setState(state, {
							options: {
								interactive: value,
							},
						});
						gui.controllersRecursive().forEach(c => c.updateDisplay());
					}
				},
				focus: () => {},
			};
			group.add(state, 'focus').onChange(() => {
				const state = mv.getState(obj.id);

				if (state?.type === 'marker') {
					mv.camera.focusOn([state.position]);
				}
			});
			group.add(state, 'interactive');
		} else if (obj.type === 'label') {
			const state = {
				get interactive() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options?.interactive : false;
				},
				set interactive(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'label') {
						mv.setState(state, {
							options: {
								interactive: value,
							},
						});
						gui.controllersRecursive().forEach(c => c.updateDisplay());
					}
				},
				get text() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.text : '';
				},
				set text(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, { text: value });
					}
				},
				get 'Text Color'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.textColor : '#000000';
				},
				set 'Text Color'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(obj.id, {
							text: state.text,
							options: {
								appearance: {
									textColor: value,
								},
							},
						});
					}
				},
				get 'Text Size'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.textSize : 10;
				},
				set 'Text Size'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									textSize: value,
								},
							},
						});
					}
				},
				get 'Icon Size'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.iconSize : 10;
				},
				set 'Icon Size'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									iconSize: value,
								},
							},
						});
					}
				},
				get 'Pin Color'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.pinColor : '#000000';
				},
				set 'Pin Color'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									pinColor: value,
								},
							},
						});
					}
				},
				get 'Pin Color Inactive'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.pinColorInactive : '#000000';
				},
				set 'Pin Color Inactive'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									pinColorInactive: value,
								},
							},
						});
					}
				},
				get 'Pin Outline Color'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.pinOutlineColor : '#000000';
				},
				set 'Pin Outline Color'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									pinOutlineColor: value,
								},
							},
						});
					}
				},
				get 'Pin Outline Color Inactive'() {
					const state = mv.getState(obj.id);

					return state?.type === 'label' ? state.options.appearance?.pinOutlineColorInactive : '#000000';
				},
				set 'Pin Outline Color Inactive'(value) {
					const currentState = mv.getState(obj.id);

					if (currentState?.type === 'label') {
						mv.setState(currentState, {
							text: state.text,
							options: {
								appearance: {
									pinOutlineColorInactive: value,
								},
							},
						});
					}
				},
				focus: () => {},
			};
			group.add(state, 'focus').onChange(() => {
				const state = mv.getState(obj.id);

				if (state?.type === 'label') {
					mv.camera.focusOn([state.position]);
				}
			});
			group.add(state, 'interactive');
			group.add(state, 'text');
			group.add(state, 'Text Size', 10, 50);
			group.add(state, 'Icon Size', 10, 50);
			group.addColor(state, 'Text Color');
			group.addColor(state, 'Active Background Color');
			group.addColor(state, 'Inactive Background Color');
			group.addColor(state, 'Active Foreground Color');
			group.addColor(state, 'Inactive Foreground Color');
		} else if (obj.type === 'geometry-group') {
			const state = {
				get opacity() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.opacity : 1;
				},
				set opacity(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(obj, {
							opacity: value,
						});
					}
				},
				set visible(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(obj, {
							visible: value,
						});
					}
				},
				get visible() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.visible : false;
				},
				set outline(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(obj, {
							outline: value,
						});
					}
				},
				get outline() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.outline : false;
				},
				set altitude(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(obj, {
							altitude: value,
						});
					}
				},
				get altitude() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.altitude : 0;
				},
				set interactive(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(obj, {
							interactive: value,
						});
						gui.controllersRecursive().forEach(c => c.updateDisplay());
					}
				},
				get interactive() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.interactive : false;
				},
				get shadingStart() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.shading?.start ?? 0 : 0;
				},
				set shadingStart(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(state, {
							shading: {
								start: value,
							},
						});
					}
				},
				get shadingEnd() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.shading?.end ?? 0 : 0;
				},
				set shadingEnd(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(state, {
							shading: {
								end: value,
							},
						});
					}
				},
				get shadingIntensity() {
					const state = mv.getState(obj.id);

					return state?.type === 'geometry-group' ? state.shading?.intensity ?? 0 : 0;
				},
				set shadingIntensity(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'geometry-group') {
						mv.setState(state, {
							shading: {
								intensity: value,
							},
						});
					}
				},
			};
			group.add(state, 'visible');
			group.add(state, 'opacity', 0, 1, 0.1);
			group.add(state, 'interactive');
			group.add(state, 'outline');
			group.add(state, 'altitude', 0, 100, 1);
			group.add(state, 'shadingStart', 0, 1, 0.1);
			group.add(state, 'shadingEnd', 0, 1, 0.1);
			group.add(state, 'shadingIntensity', 0, 1, 0.1);
		} else if (obj.type === 'group-container') {
			const state = {
				set visible(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'group-container') {
						mv.setState(obj, {
							visible: value,
						});
					}
				},
				get visible() {
					const state = mv.getState(obj.id);

					return state?.type === 'group-container' ? state.visible : false;
				},
				set interactive(value) {
					const state = mv.getState(obj.id);
					if (state?.type === 'group-container') {
						mv.setState(state, {
							interactive: value,
						});
						gui.controllersRecursive().forEach(c => c.updateDisplay());
					}
				},
				get interactive() {
					const state = mv.getState(obj.id);

					return state?.type === 'group-container' ? state.interactive : false;
				},
			};
			group.add(state, 'visible');
			group.add(state, 'interactive');
		}
		group.close();
		if (obj.children) {
			for (const child of obj.children.values()) {
				parseChild(child, group);
			}
		}
	};
}

function createWatermarkChild(mv: Core, gui) {
	const folder = gui.addFolder('Watermark');

	const controller: WatermarkUpdateOptions = {
		padding: 16,
		iconOnly: false,
		position: 'bottom-left',
		color: 'dark',
		scale: 1,
	};

	folder.add(controller, 'padding', 0, 100, 1);
	folder.add(controller, 'scale', 0.5, 1.5, 0.1);
	folder.add(controller, 'color', ['dark', 'light']);
	folder.add(controller, 'position', [
		'top-left',
		'top-right',
		'bottom-left',
		'bottom-right',
		'center',
		'top',
		'bottom',
		'left',
		'right',
	]);
	folder.add(controller, 'iconOnly');

	folder.onChange(() => {
		mv.updateWatermark(controller);
	});
}
