import { round } from '@packages/internal/common';
import { Coordinate } from '../map-data-objects';
import type { Camera } from '../api-geojson';

/**
 * Configuration options for CameraTransform precision.
 */
export interface CameraTransformOptions {
	/**
	 * Number of decimal places for coordinate precision.
	 * Default: 7 (centimeter accuracy)
	 * Set to -1 to disable coordinate rounding.
	 *
	 * Note: ZoomLevel is always rounded to 5 decimal places for consistency
	 */
	precision?: number;
}

/**
 * Precision constant for centimeter-level coordinate accuracy.
 * 7 decimal places provides approximately 1cm accuracy for lat/lng coordinates.
 */
const CENTIMETER_PRECISION = 7;

/**
 * Class representing camera transformation data.
 */
export class CameraTransform {
	#internalCamera: Camera;
	#precision: number;

	/**
	 * @internal
	 */
	constructor(camera: Camera, options?: CameraTransformOptions) {
		this.#internalCamera = camera;
		this.#precision = options?.precision ?? CENTIMETER_PRECISION;
	}

	toJSON() {
		return {
			center: this.center,
			bearing: this.bearing,
			pitch: this.pitch,
			zoomLevel: this.zoomLevel,
		};
	}

	/**
	 * Getter for the center coordinate of the camera.
	 *
	 * @returns Center Coordinate with precision applied (default: 7 decimal places for centimeter accuracy).
	 */
	get center() {
		const originalCenter = this.#internalCamera.center;

		// If precision is disabled (-1), return original coordinates
		if (this.#precision === -1) {
			return originalCenter;
		}

		// Round coordinates to specified precision and create new Coordinate instance
		const roundedLat = round(originalCenter.latitude, this.#precision);
		const roundedLng = round(originalCenter.longitude, this.#precision);

		return new Coordinate({
			latitude: roundedLat,
			longitude: roundedLng,
			floorId: originalCenter.floorId,
			verticalOffset: originalCenter.verticalOffset,
		});
	}

	/**
	 * Getter for the camera's bearing in degrees.
	 *
	 * @returns Camera bearing in degrees.
	 */
	get bearing() {
		return this.#internalCamera.bearing;
	}

	/**
	 * Getter for the camera's pitch in degrees.
	 *
	 * @returns Camera pitch in degrees.
	 */
	get pitch() {
		return this.#internalCamera.pitch;
	}

	/**
	 * Getter for the camera's zoom level in mercator zoom levels.
	 * Rounded to 5 decimal places for consistency.
	 *
	 * @see https://wiki.openstreetmap.org/wiki/Zoom_levels
	 *
	 * @returns Zoom level in mercator zoom levels, rounded to 5 decimal places.
	 */
	get zoomLevel() {
		// Round to 5 decimal places (same precision as BlueDot implementation). This prevents tiny zoom level changes from affecting camera-change events
		return round(this.#internalCamera.zoomLevel, 5);
	}
}
