import type { LocationId, LocationSocial, Location as MVFLocation } from '@mappedin/mvf-v2';
import BaseMetaData from './base-metadata-object';
import type {
	Annotation,
	Connection,
	Door,
	LocationCategory,
	MapDataInternal,
	Space,
	MapObject,
	PointOfInterest,
	Area,
} from '.';
import type { TFocusTarget, TNavigationTarget, IFocusable, INavigatable } from '../types';
import { OpeningHours } from './opening-hours';
import Hyperlink from './hyperlink';
import ImageMetaData from './image';
import { serializeCoords } from '../utils';

// This type helps us know we have all the properties we need
type LocationData = Omit<
	MVFLocation,
	| 'categories'
	| 'spaces'
	| 'obstructions'
	| 'entrances'
	| 'shapes'
	| 'connections'
	| 'annotations'
	| 'areas'
	| 'openingHoursSpecification'
	| 'links'
	| 'pictures'
	| 'website'
	| 'icon'
> & {
	// These types are transformed to match the other SDK types
	links: Hyperlink[];
	images: ImageMetaData[];
	icon?: ImageMetaData;
};

class LocationProfile extends BaseMetaData implements LocationData, IFocusable, INavigatable {
	/**
	 * Checks if the provided instance is of type EnterpriseLocation.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a EnterpriseLocation, false otherwise.
	 */
	static is(instance: object): instance is LocationProfile {
		return (instance as { __type: string }).__type === LocationProfile.__type;
	}

	/**
	 * @internal
	 */
	static readonly __type = 'location-profile';

	/**
	 * @internal
	 */
	readonly __type = LocationProfile.__type;
	id: LocationId = 'loc_';
	name = '';
	externalId?: string;

	description?: string;

	/**
	 * The location's logo
	 *
	 * @format uri
	 */
	logo?: string;

	phone?: string;

	/**
	 * The location's social media links.
	 */
	social: LocationSocial[] = [];

	/**
	 * The location's pictures
	 */
	images: ImageMetaData[] = [];

	/**
	 * Web links for the location
	 */
	links: Hyperlink[] = [];

	/**
	 * The location's website
	 */
	website?: Hyperlink;

	/**
	 * The location's icon to display. Will be either a logo, category, or image
	 *
	 * @format uri
	 */
	icon?: ImageMetaData;

	#data: MapDataInternal;
	#cache: Partial<{
		areas: Area[];
		spaces: Space[];
		doors: Door[];
		points: PointOfInterest[];
		connections: Connection[];
		annotations: Annotation[];
		categories: LocationCategory[];
		mapObjects: MapObject[];
		focusTarget: TFocusTarget[];
		navigationTarget: TNavigationTarget[];
		openingHours: OpeningHours;
	}> = {};
	#mvfData: MVFLocation;

	constructor(data: MapDataInternal, options: { mvfData: MVFLocation }) {
		super(options.mvfData.id);
		this.#data = data;
		this.#mvfData = options.mvfData;
		for (const prop in options.mvfData) {
			// Handle links and images special because there is a small type mismatch in MVF
			// TODO: Fix this when we move to MVFv3
			if (prop === 'links') {
				this.links = options.mvfData.links.map(link => new Hyperlink({ mvfData: link }));
			} else if (prop === 'pictures') {
				this.images = options.mvfData.pictures
					.map(image =>
						image.url
							? new ImageMetaData({
									mvfData: {
										...image,
										url: image.url ?? '',
									},
									mapData: this.#data,
							  })
							: undefined,
					)
					.filter(image => image != null);
			} else if (prop === 'website') {
				if (options.mvfData.website) {
					this.website = new Hyperlink({ mvfData: options.mvfData.website });
				}
			} else if (prop === 'icon') {
				if (options.mvfData.icon) {
					this.icon = new ImageMetaData({ mvfData: { url: options.mvfData.icon }, mapData: this.#data });
				}
			} else if (!Object.getOwnPropertyDescriptor(Object.getPrototypeOf(this), prop)) {
				Object.defineProperty(this, prop, {
					enumerable: true,
					get() {
						return this.#mvfData[prop];
					},
				});
			}
		}
	}

	/**
	 * Gets the {@link Space}s associated with the location.
	 *
	 * @returns {Space[]} The spaces array.
	 */
	get spaces(): Space[] {
		if (this.#cache.spaces) {
			return this.#cache.spaces;
		}

		if (this.#mvfData.spaces == null) {
			this.#cache.spaces = [];

			return this.#cache.spaces;
		}

		this.#cache.spaces = [];
		for (let i = 0; i < this.#mvfData.spaces.length; i++) {
			const space = this.#data.getById('space', this.#mvfData.spaces[i].id);
			if (space) {
				this.#cache.spaces.push(space);
			}
		}

		return this.#cache.spaces;
	}

	/**
	 * Gets the {@link PointOfInterest}s associated with the location.
	 *
	 * @returns {PointOfInterest[]} The points of interest array.
	 */
	get points(): PointOfInterest[] {
		if (this.#cache.points) {
			return this.#cache.points;
		}

		if (this.#mvfData.spaces == null) {
			this.#cache.points = [];

			return this.#cache.points;
		}

		this.#cache.points = [];
		// POIs are also spaces in the MVF data
		for (let i = 0; i < this.#mvfData.spaces.length; i++) {
			const point = this.#data.getById('point-of-interest', this.#mvfData.spaces[i].id);
			if (point) {
				this.#cache.points.push(point);
			}
		}

		return this.#cache.points;
	}

	/**
	 * Gets the {@link Door}s associated with the location.
	 *
	 * @returns {Door[]} The doors array.
	 */
	get doors(): Door[] {
		if (this.#cache.doors) {
			return this.#cache.doors;
		}

		if (this.#mvfData.entrances == null) {
			this.#cache.doors = [];

			return this.#cache.doors;
		}

		this.#cache.doors = [];
		for (let i = 0; i < this.#mvfData.entrances.length; i++) {
			const door = this.#data.getById('door', this.#mvfData.entrances[i].id);
			if (door) {
				this.#cache.doors.push(door);
			}
		}

		return this.#cache.doors;
	}

	/**
	 * Gets the {@link Connection}s associated with the location.
	 *
	 * @returns {Connection[]} The connections array.
	 */
	get connections(): Connection[] {
		if (this.#cache.connections) {
			return this.#cache.connections;
		}

		if (this.#mvfData.connections == null) {
			this.#cache.connections = [];

			return this.#cache.connections;
		}

		/**
		 * The location metadata references connections by their group ID. In order to get the
		 * connection instances, we need to do a reverse lookup by node IDs.
		 *
		 * TODO: Fix this when MVFv3 fixes connections.
		 */
		const uniqueConnections = new Set<Connection>();
		for (let i = 0; i < this.#mvfData.connections.length; i++) {
			const connectionGroupId = this.#mvfData.connections[i];
			const mvfConnection = this.#data.mvfConnectionsById[connectionGroupId];
			const nodeIds = mvfConnection == null ? [] : mvfConnection.nodes;
			const nodes = nodeIds.map(id => this.#data.getById('node', id)).filter(Boolean);
			for (const node of nodes) {
				if (node == null) {
					continue;
				}
				const latLonString = serializeCoords([node.coordinate.longitude, node.coordinate.latitude]);
				const maybeConnectionIds = this.#data.connectionSpaceIdsByLatLon[latLonString] ?? [];
				const connections = maybeConnectionIds.map(id => this.#data.connectionsById[id]).filter(c => c != null);
				for (const connection of connections) {
					uniqueConnections.add(connection);
				}
			}
		}
		this.#cache.connections = Array.from(uniqueConnections);

		return this.#cache.connections;
	}

	/**
	 * Gets the {@link Annotation}s associated with the location.
	 *
	 * @returns {Annotation[]} The annotations array.
	 */
	get annotations(): Annotation[] {
		if (this.#cache.annotations) {
			return this.#cache.annotations;
		}

		if (this.#mvfData.annotations == null) {
			this.#cache.annotations = [];

			return this.#cache.annotations;
		}

		this.#cache.annotations = [];
		for (let i = 0; i < this.#mvfData.annotations.length; i++) {
			const annotation = this.#data.getById('annotation', this.#mvfData.annotations[i].id);
			if (annotation) {
				this.#cache.annotations.push(annotation);
			}
		}

		return this.#cache.annotations;
	}

	/**
	 * Gets the {@link LocationCategory}s associated with the location.
	 *
	 * @returns {LocationCategory[]} The location categories array.
	 */
	get categories(): LocationCategory[] {
		if (this.#cache.categories) {
			return this.#cache.categories;
		}

		if (this.#mvfData.categories == null) {
			this.#cache.categories = [];

			return this.#cache.categories;
		}

		this.#cache.categories = this.#mvfData.categories
			.map(cId => this.#data.getById('location-category', cId))
			.filter(c => c != null);

		return this.#cache.categories;
	}

	/**
	 * Gets the {@link MapObject}s associated with the location.
	 *
	 * @returns {MapObject[]} The map objects array.
	 */
	get mapObjects(): MapObject[] {
		if (this.#cache.mapObjects) {
			return this.#cache.mapObjects;
		}

		if (this.#mvfData.obstructions == null) {
			this.#cache.mapObjects = [];

			return this.#cache.mapObjects;
		}

		this.#cache.mapObjects = this.#mvfData.obstructions
			.map(o => this.#data.getById('object', o.id))
			.filter(o => o != null);

		return this.#cache.mapObjects;
	}

	/**
	 * Gets the {@link Area}s associated with the location.
	 *
	 * @returns {Area[]} The areas array.
	 */
	get areas(): Area[] {
		if (this.#cache.areas) {
			return this.#cache.areas;
		}

		if (this.#mvfData.areas == null) {
			this.#cache.areas = [];

			return this.#cache.areas;
		}

		this.#cache.areas = this.#mvfData.areas.map(a => this.#data.getById('area', a.id)).filter(a => a != null);

		return this.#cache.areas;
	}

	/** @internal */
	get focusTarget() {
		if (this.#cache.focusTarget) {
			return this.#cache.focusTarget;
		}

		const targets: TFocusTarget[] = [];
		for (const space of this.spaces) {
			targets.push(space);
		}
		for (const connection of this.connections) {
			targets.push(...connection.coordinates);
		}
		for (const door of this.doors) {
			targets.push(door.focusTarget);
		}
		for (const mapObject of this.mapObjects) {
			targets.push(mapObject.focusTarget);
		}
		for (const annotation of this.annotations) {
			targets.push(annotation.focusTarget);
		}
		for (const point of this.points) {
			targets.push(point.focusTarget);
		}
		for (const area of this.areas) {
			targets.push(area.focusTarget);
		}

		this.#cache.focusTarget = targets;

		return targets;
	}

	/**
	 * Gets the {@link OpeningHours} associated with the location.
	 *
	 * @returns {OpeningHours} The opening hours.
	 */
	get openingHours(): OpeningHours {
		if (this.#cache.openingHours) {
			return this.#cache.openingHours;
		}

		this.#cache.openingHours = new OpeningHours(this.#mvfData.openingHoursSpecification ?? []);

		return this.#cache.openingHours;
	}

	/**
	 * @internal
	 */
	get navigationTarget(): TNavigationTarget[] {
		if (this.#cache.navigationTarget) {
			return this.#cache.navigationTarget;
		}

		const targets: TNavigationTarget[] = [];
		for (const space of this.spaces) {
			targets.push(space);
		}
		for (const connection of this.connections) {
			targets.push(...connection.coordinates);
		}
		for (const door of this.doors) {
			targets.push(door.focusTarget);
		}
		for (const mapObject of this.mapObjects) {
			targets.push(mapObject.focusTarget);
		}
		for (const annotation of this.annotations) {
			targets.push(annotation.focusTarget);
		}
		for (const point of this.points) {
			targets.push(point.focusTarget);
		}

		this.#cache.navigationTarget = targets;

		return targets;
	}

	toJSON() {
		return {
			__type: LocationProfile.__type,
			id: this.id,
			name: this.name,
		};
	}
}

export default LocationProfile;
