import type { NodeFeature } from '../types/node';

const DEFAULT_WEIGHT = 0;

/**
 * Represents an edge in a graph, containing data about its endpoints and metrics like distance and angle.
 * @hidden
 */
export class Edge {
	/** The originating node of the edge */
	public origin: NodeFeature;
	/** The destination node of the edge */
	public destination: NodeFeature;
	/** The distance between the origin and the destination nodes */
	public distance: number;
	/** The angle of the edge with respect to some reference, in degrees */
	public angle: number;
	/** A composite weight of the edge, combining distance and a custom path weight */
	public weight: number;

	/**
	 * Constructs a new Edge instance.
	 * @param {NodeFeature} origin - The originating node of the edge.
	 * @param {NodeFeature} destination - The destination node of the edge.
	 * @param {number} [distance=0] - The physical distance between the origin and destination.
	 * @param {number} [angle=0] - The angle of the edge in degrees.
	 * @param {number} [pathWeight=0] - An additional weight factor for the path.
	 */
	constructor({
		origin,
		destination,
		distance = 0,
		angle = 0,
		pathWeight = DEFAULT_WEIGHT,
		multiplicativeDistanceWeightScaling = true,
	}: {
		origin: NodeFeature;
		destination: NodeFeature;
		distance?: number;
		angle?: number;
		pathWeight?: number;
		multiplicativeDistanceWeightScaling?: boolean;
	}) {
		this.origin = origin;
		this.destination = destination;
		this.distance = distance;
		this.angle = angle;
		// The only case we directly use the path weight is when floor was changed
		this.weight = pathWeight || DEFAULT_WEIGHT;

		if (multiplicativeDistanceWeightScaling) {
			// TODO: Improve distance handling for connections
			// Currently, connection nodes have zero distance but high weight. We need to enhance our algorithm to calculate more accurate distances for connection nodes.
			// - The distance is zero because it's computed entirely based on coordinates. ie we could consider vertical distance and transition time between floors.
			// - The weightValue is coming from mvf directly, for self serve the floor distance value is factored into weightValue
			if (origin.properties.floor === destination.properties.floor) {
				// see graph in: https://github.com/MappedIn/sdk/pull/461
				this.weight = (this.weight >= 0 ? this.distance * this.weight : DEFAULT_WEIGHT) + this.distance;
			}
		} else {
			if (origin.properties.map === destination.properties.map) {
				// fallback to V5 weight calculation approach to avoid surprises
				this.weight = this.weight >= 0 ? this.distance + this.weight : DEFAULT_WEIGHT;
			}
		}

		if (isNaN(this.angle)) {
			throw new Error('Invalid angle: Expected a numeric value, but received NaN.');
		}
	}
}
