import type { RendererState, TransformImageRequest } from '../../renderer';
import { shaveText } from '@packages/internal/shave-text';
import { cyrb53, shouldDisableOffscreenCanvas } from '../../utils';
import LabelComponent from '../../components/label';
import { Geometry2DComponents } from '../../entities/geometry2d';
import { createImage } from './utils';
import { interpolateMulti, Logger, PubSub, round } from '@packages/internal/common';
import {
	INACTIVE_PIN_SIZE_NO_ICON,
	PIN_SHADOW_BLUR,
	PIN_SHADOW_OFFSET,
	PIN_STROKE_SIZE,
	PINVISIBILITY,
	TEXTALIGN,
	TEXT_STROKE_SIZE,
} from './constants';

export class TwoDDrawSystem extends PubSub<{ 'img:loaded': undefined }> {
	// does a complete redraw of the 2d scene
	dirty = false;
	state: RendererState;
	colliderContext: CanvasRenderingContext2D;
	colliderCanvas: HTMLCanvasElement;
	transformRequestFunc?: TransformImageRequest;
	constructor(state: RendererState, colliderCanvas: HTMLCanvasElement, transformImageRequest?: TransformImageRequest) {
		super();
		this.state = state;
		this.colliderCanvas = colliderCanvas;
		// new WebGL2D(colliderCanvas);
		this.colliderContext = colliderCanvas.getContext('2d')!;
		this.transformRequestFunc = transformImageRequest;
	}

	transformRequest = async (url: string) => {
		if (this.transformRequestFunc) {
			return await this.transformRequestFunc(url);
		}

		return { url };
	};

	pinCache: {
		[key in number]: [HTMLCanvasElement | OffscreenCanvas, HTMLCanvasElement | OffscreenCanvas];
	} = {};

	/** Stores the loaded images ready to be drawn. */
	imageCache: {
		[key in number]: HTMLImageElement | ImageBitmap;
	} = {};

	/** Stores the promises for each image fetch. */
	imagePromiseCache: {
		[key in number]: Promise<HTMLImageElement | ImageBitmap>;
	} = {};

	imageDimensionsCache: {
		[key in number]: { width: number; height: number };
	} = {};

	textCache: {
		// component id
		[key in string]: {
			left?: OffscreenCanvas | HTMLCanvasElement;
			center?: OffscreenCanvas | HTMLCanvasElement;
			right?: OffscreenCanvas | HTMLCanvasElement;
		};
	} = {};

	update = (zoomLevel: number) => {
		const pixelRatio = this.state.pixelRatio;

		this.colliderContext.clearRect(0, 0, this.colliderCanvas.width, this.colliderCanvas.height);
		let labelCacheId: string;
		let foregroundColor: { active: string; inactive: string };
		let backgroundColor: { active: string; inactive: string };
		let pinSize: number;
		let iconVisible: boolean | number | undefined;
		let component: LabelComponent;

		let entity;

		for (const entityId of this.state.geometry2DIdsInScene) {
			entity = this.state.geometry2DMap.get(entityId);
			if (entity == null) {
				continue;
			}
			component = entity.components[Geometry2DComponents.UI];

			if (component instanceof LabelComponent && !entity.disposed) {
				iconVisible = component.appearance.iconVisible;
				if (component.appearance.icon != null && iconVisible != null) {
					// Determine if icon should be visible based on type
					const shouldBeVisible = typeof iconVisible === 'boolean' ? iconVisible : zoomLevel >= iconVisible;

					if (shouldBeVisible !== component.iconVisible) {
						// If the icon visibility changed the components size recalculation and re-drawing
						component.iconVisible = shouldBeVisible;
						component.dirty = true;
					}
				}

				if (component.dirty || this.dirty) {
					// If the component is dirty, we need to update the zoom proportion and calculate the pin size.
					// All the following logic relies on the pin size being up to date.
					component.calculatePinSize();
				}

				foregroundColor = { active: component.appearance.pinColor, inactive: component.appearance.pinColorInactive };
				backgroundColor = {
					active: component.appearance.pinOutlineColor,
					inactive: component.appearance.pinOutlineColorInactive,
				};
				// Round the pin size to 2 decimal places to avoid cache misses
				pinSize = round(component.pinSize, 2);
				labelCacheId = `${foregroundColor.active}-${foregroundColor.inactive}-${backgroundColor.active}-${backgroundColor.inactive}-${pinSize}-${component.iconVisible}`;

				component.labelCacheId = labelCacheId;

				if (!this.pinCache[labelCacheId] || this.dirty) {
					this.prepare(component, labelCacheId, this.colliderContext);
					component.dirty = true;
				}

				if ((component.appearance.icon && (!component.imageHash || component.pinDirty)) || this.dirty) {
					this.cacheImage(component);
				}

				if (this.textCache[component.id] == null || component.textDirty || this.dirty) {
					component.dirty = true;
					this.textCache[component.id] = {};

					const { textDrawFn, width, height } = this.processText(
						component.text,
						component.appearance.textSize,
						component.appearance.maxWidth,
						component.appearance.maxLines,
						component.appearance.lineHeight,
					);
					[TEXTALIGN.LEFT, TEXTALIGN.CENTER, TEXTALIGN.RIGHT].forEach((textAlign: TEXTALIGN) => {
						this.textCache[component.id][textAlign] = this.textToCanvas(
							textDrawFn,
							component.appearance.textSize,
							textAlign,
							width,
							height,
							component.appearance.textColor,
							component.appearance.textOutlineColor,
						);
					});
					component.dimensions = {
						width: Math.ceil(width / pixelRatio),
						height: Math.ceil(height / pixelRatio),
					};
				}

				// had to move this logic here, because the icon scale will affect collisons even when a label was hidden
				// otherwise it causes overlap
				// [TODO]: redo the whole dirty logic as it keeps breaking
				let iconScale = 1;

				if (component.appearance.iconScale) {
					if (typeof component.appearance.iconScale === 'number') {
						iconScale = component.appearance.iconScale;
					} else {
						const { on, input, output, easing } = component.appearance.iconScale;
						if (on === 'zoom-level') {
							iconScale = interpolateMulti(zoomLevel, input, output, easing ?? 'linear');
						} else {
							Logger.warn(`Unsupported interpolation property: ${on}`);
						}
					}
				}

				component.recomputeCurrentStrategy(component.iconVisible ? iconScale : 1);

				if (
					component.canShow &&
					component.visible &&
					(component.enabled || component.visibilityNeedsUpdate !== false)
				) {
					this.draw(component, labelCacheId, this.colliderContext);
				}

				component.textDirty = false;
				component.pinDirty = false;
			}
		}
		this.dirty = false;
	};

	processText = (text: string, size: number, maxWidth: number, maxLines: number, lineHeight: number) => {
		const pixelRatio = this.state.pixelRatio;
		const {
			draw,
			maxWidth: width,
			maxHeight: height,
		} = shaveText({ text, size, pixelRatio, maxWidth, maxLines, lineHeight });

		return {
			textDrawFn: draw,
			width: width + TEXT_STROKE_SIZE * pixelRatio * 2,
			height: height + TEXT_STROKE_SIZE * pixelRatio * 2,
		};
	};

	textToCanvas = (
		textDrawFn: (ctx: CanvasRenderingContext2D, x: number, y: number) => void,
		size: number,
		textAlign: TEXTALIGN,
		width: number,
		height: number,
		foregroundColor: string,
		backgroundColor: string,
	) => {
		const pixelRatio = this.state.pixelRatio;
		let cacheCanvas;
		const bboxWidth = width;
		const bboxHeight = height;

		/**
		 * In order to ensure the integrated intel GPU render text correctly, we need `willReadFrequently` flag. However, only Firefox browser <OffscreenCanvas /> implementated this flag, so we should fallback to using regular canvas for the problematic GPUs.
		 * See: https://mappedin.atlassian.net/browse/SDKS-1488
		 */
		if (window.OffscreenCanvas != null && !shouldDisableOffscreenCanvas()) {
			cacheCanvas = new OffscreenCanvas(bboxWidth, bboxHeight);
		} else {
			cacheCanvas = document.createElement('canvas');
			cacheCanvas.width = bboxWidth;
			cacheCanvas.height = bboxHeight;
			cacheCanvas.style.width = bboxWidth + 'px';
			cacheCanvas.style.height = bboxHeight + 'px';
		}

		/**
		 * Enable `willReadFrequently` to hint to the browser that this canvas will be read from frequently. This achieves two goals:
		 * 1. It improves read performance by sacrificing write performance. This trade off is fine for us since we don't rerender the canvas at all.
		 * 2. It fixes an issue with some Intel GPU rendering canvas2D being distorted. see: https://mappedin.atlassian.net/browse/SDKS-1488
		 */
		const ctx: CanvasRenderingContext2D = cacheCanvas.getContext('2d', { willReadFrequently: true });
		if (!ctx) return;

		const strokeSize = TEXT_STROKE_SIZE * pixelRatio;

		ctx.textAlign = textAlign as CanvasTextAlign;
		ctx.font = `bold ${size * pixelRatio}px 'InterUI-Regular', Helvetica Neue, Arial, sans-serif`;
		// these two lines fix text bleeding issues in W's
		ctx.textBaseline = 'middle';
		ctx.lineJoin = 'miter';
		ctx.miterLimit = 2;
		ctx.strokeStyle = 'white';
		ctx.lineWidth = strokeSize;
		// DEBUG
		// ctx.fillStyle = 'rgba(255, 0, 0, 0.5)';
		// ctx.fillRect(0, 0, bboxWidth, bboxHeight);
		// END DEBUG
		ctx.fillStyle = foregroundColor;
		ctx.strokeStyle = backgroundColor;

		const xOffset =
			textAlign === TEXTALIGN.LEFT
				? // Even when text is left-aligned, make sure to add a small buffer for the stroke
				  strokeSize / 2
				: textAlign === TEXTALIGN.CENTER
				? bboxWidth / 2
				: bboxWidth - strokeSize / 2;

		textDrawFn(ctx, xOffset, (bboxHeight + strokeSize / 2) / 2);

		return cacheCanvas;
	};

	cacheImage = (label: LabelComponent) => {
		if (!label.appearance.icon) {
			return;
		}

		const imageHash = cyrb53(label.appearance.icon);
		// index promises by imageHash, which ensures we only process each image once
		// the promise is resolved when the image has been processed
		if (!this.imagePromiseCache[imageHash]) {
			// Load images asynchronously. We do this so we can fetch and append a new sas token if needed.
			this.imagePromiseCache[imageHash] = new Promise(resolve => {
				this.transformRequest(label.appearance.icon!).then(res => {
					const img = createImage(res.url);
					img.onload = () => {
						this.imageCache[imageHash] = img;
						this.publish('img:loaded');
						resolve(img);
					};
				});
			});
		}

		// Once the image is loaded, set the label as dirty so its size will be recalculated
		if (label.imageHash !== imageHash) {
			label.imageHash = imageHash;
			this.imagePromiseCache[imageHash].then(() => {
				label.dirty = true;
			});
		}
	};

	createPinCanvas = (
		label: LabelComponent,
		pinSize: number,
		backgroundColor: string,
		foregroundColor: string,
		maxIconScale = 1,
	) => {
		let cacheCanvas: OffscreenCanvas | HTMLCanvasElement;
		const pixelRatio = this.state.pixelRatio;

		// we need to create a canvas which will contain the PIN, along with it's shadow
		// so it needs to be bigger than the pin

		// let's scale the canvas size by the maxIconScale so it doesn't get blurry when the icon is scaled up
		const canvasSize = label.totalPinSize * 2 * pixelRatio * maxIconScale;

		if (window.OffscreenCanvas != null) {
			cacheCanvas = new OffscreenCanvas(canvasSize, canvasSize);
		} else {
			cacheCanvas = document.createElement('canvas');
			cacheCanvas.width = canvasSize;
			cacheCanvas.height = canvasSize;
			cacheCanvas.style.width = canvasSize / pixelRatio + 'px';
			cacheCanvas.style.height = canvasSize / pixelRatio + 'px';
		}
		const cacheCanvasContext = cacheCanvas.getContext('2d') as
			| OffscreenCanvasRenderingContext2D
			| CanvasRenderingContext2D;

		cacheCanvasContext.shadowColor = `rgba(0,0,0,0.3)`;
		cacheCanvasContext.shadowOffsetY = PIN_SHADOW_OFFSET * pixelRatio;
		cacheCanvasContext.shadowBlur = PIN_SHADOW_BLUR * pixelRatio;
		cacheCanvasContext.beginPath();

		// draw the pin starting in the center of the canvas
		cacheCanvasContext.arc(
			canvasSize / 2,
			canvasSize / 2,
			// use dotSize + an offset to make the bottom layer bigger
			(pinSize / 2 + (PIN_STROKE_SIZE * pixelRatio) / 2) * maxIconScale,
			0,
			2 * Math.PI,
		);

		cacheCanvasContext.fillStyle = backgroundColor;
		cacheCanvasContext.fill();

		cacheCanvasContext.beginPath();
		cacheCanvasContext.arc(canvasSize / 2, canvasSize / 2, (pinSize / 2) * maxIconScale, 0, 2 * Math.PI);

		cacheCanvasContext.shadowColor = 'transparent';
		cacheCanvasContext.fillStyle = foregroundColor;
		cacheCanvasContext.fill();

		return cacheCanvas;
	};

	prepare = (label: LabelComponent, labelCacheId: string, context: CanvasRenderingContext2D) => {
		const pixelRatio = this.state.pixelRatio;
		context.lineWidth = 4 * pixelRatio;
		const scales =
			typeof label.appearance.iconScale === 'number'
				? [label.appearance.iconScale]
				: label.appearance.iconScale == null
				? []
				: label.appearance.iconScale.output;
		const maxScale = scales.length > 0 ? Math.max(...scales) : undefined;
		// cache the active dot, with background and foreground color
		const activeContext = this.createPinCanvas(
			label,
			label.pinSize,
			label.appearance.pinOutlineColor,
			label.appearance.pinColor,
			maxScale,
		);

		// cache the inactive dot, with background and foreground color
		const inactiveContext = this.createPinCanvas(
			label,
			INACTIVE_PIN_SIZE_NO_ICON * pixelRatio,
			label.appearance.pinOutlineColorInactive,
			label.appearance.pinColorInactive,
			maxScale,
		);

		this.pinCache[labelCacheId] = [activeContext, inactiveContext];
	};

	draw(label: LabelComponent, labelCacheId: string, context: CanvasRenderingContext2D) {
		// if we're not visible, don't draw the label at all
		if (!label.canShow || !label.visible) {
			return;
		}

		context.save();
		context.globalAlpha = label.currentOpacity;

		const pixelRatio = this.state.pixelRatio;

		const dpX = label.projection.x * pixelRatio;
		const dpY = label.projection.y * pixelRatio;

		// generate a cache id using colors and size
		const imageToDraw =
			label.style.pinVisibility === PINVISIBILITY.ACTIVE
				? this.pinCache[labelCacheId][0]
				: this.pinCache[labelCacheId][1];

		// For inactive/hidden pins, use fixed size since they don't scale
		// For active pins, use scaled size for proper zoom/icon scaling
		const drawSize =
			label.style.pinVisibility === PINVISIBILITY.ACTIVE
				? label.scaledPinSize * pixelRatio
				: label.totalPinSize * pixelRatio;

		// If the pin is hidden, skip drawing.
		// Image should only draw if it's active
		if (label.style.pinVisibility !== PINVISIBILITY.HIDDEN) {
			context.drawImage(imageToDraw, dpX - drawSize, dpY - drawSize, drawSize * 2, drawSize * 2);
		}

		if (
			label.appearance.icon &&
			label.iconVisible &&
			label.imageHash &&
			label.appearance.iconSize &&
			this.imageCache[label.imageHash!] &&
			label.style.pinVisibility === PINVISIBILITY.ACTIVE
		) {
			const img = this.imageCache[label.imageHash];
			const circleDiameter = (label.scaledPinSize - label.scaledIconPadding) * pixelRatio;
			const iconFit = label.appearance.iconFit;

			// iconFit === 'fill'. Does not maintain aspect ratio, fills circle
			let scaledWidth = circleDiameter;
			let scaledHeight = circleDiameter;

			const iconFitCache = `${label.imageHash!}_${iconFit}`;
			if (this.imageDimensionsCache[iconFitCache]) {
				scaledWidth = this.imageDimensionsCache[iconFitCache].width;
				scaledHeight = this.imageDimensionsCache[iconFitCache].height;
			} else {
				const imgWidth = img.width * pixelRatio;
				const imgHeight = img.height * pixelRatio;

				// Maintains aspect ratio, scaled down
				if (iconFit === 'contain') {
					const imgDiagonal = Math.sqrt(imgWidth ** 2 + imgHeight ** 2);
					const scale = circleDiameter / imgDiagonal;
					scaledWidth = imgWidth * scale;
					scaledHeight = imgHeight * scale;
				}

				// Maintains aspect ratio, fills circle
				if (iconFit === 'cover') {
					const scaleByWidth = circleDiameter / imgWidth;
					const scaleByHeight = circleDiameter / imgHeight;
					const scale = Math.max(scaleByWidth, scaleByHeight);
					scaledWidth = imgWidth * scale;
					scaledHeight = imgHeight * scale;
				}
			}

			context.save();
			context.beginPath();
			context.arc(dpX, dpY, circleDiameter / 2, 0, 2 * Math.PI);
			context.closePath();
			if (label?.appearance?.iconOverflow !== 'visible') context.clip();
			context.drawImage(img, dpX - scaledWidth / 2, dpY - scaledHeight / 2, scaledWidth, scaledHeight);
			context.restore();
		}

		// draw text
		if (
			label.style.textLabelVisible &&
			label.style.textAlign != null &&
			label.style.left != null &&
			label.style.top != null
		) {
			context.drawImage(
				this.textCache[label.id][label.style.textAlign] as CanvasImageSource,
				dpX + label.style.left * pixelRatio,
				dpY + label.style.top * pixelRatio,
			);
		}

		context.restore();
	}
}
