import { Euler, InstancedMesh, MathUtils, Matrix4, Quaternion, Vector3 } from 'three';
import type { BufferGeometry, Mesh } from 'three';
import type { EntityTypes, RendererState } from '../../renderer';
import { Geometry3DComponents, Geometry3D, Geometry3DObject3D } from '../../entities/geometry3d';
import { batchGeometries, darkenColorForOutline, updateUVsLegacy } from './utils';
import { MeshComponent, type MeshComponentProperties } from '../../components/mesh';
import { StyleComponent } from '../../components/styles/style';
import { renderPolygonGeometry } from '../../geometry/polygons';
import { renderMultiPolygonGeometry } from '../../geometry/multi-polygon';
import { renderLineStringGeometry } from '../../geometry/line-string';
import { renderMultiLineStringGeometry } from '../../geometry/multi-line-string';
import type { Feature, LineString, MultiLineString, MultiPolygon, Point, Polygon } from 'geojson';
import type { LineStyle, ModelProperties, PaintStyle } from '../../types';
import { ModelComponent } from '../../components/model';
import { GeometryGroupObject3D } from '../../entities/geometry-group';
import type { ModelGeometry3D } from '../../entities';
import { Logger, PubSub } from '@packages/internal/common';
import type { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import type { ModelStyle } from '../../components/styles';
import { ModelStyleComponnet, DEFAULT_ROTATION, DEFAULT_SCALE } from '../../components/styles';
import { applyModelGeometryStyle } from '../draw/utils';
import { OutlineComponent } from '../../components/outline';
import { FocusableComponent } from '../../components/focusable';
import { TextureComponent } from '../../components/texture';

function generateIndex(style: any) {
	const { color, opacity, topColor, texture, topTexture } = style;

	return `${color}${opacity}${topColor || ''}${texture || ''}${topTexture || ''}`;
}

export class MeshCreationAndOptimizationSystem extends PubSub<{
	'model-loaded': void;
	'geometry-2d-added': void;
	'geometry-group-added': void;
}> {
	state: RendererState;
	convertTo3DMapPosition: any;
	loader?: GLTFLoader;
	constructor(state: RendererState, convertTo3DMapPosition) {
		super();

		this.state = state;
		this.convertTo3DMapPosition = convertTo3DMapPosition;
	}

	async getGLTFLoader() {
		if (this.loader) {
			return this.loader;
		}
		const { GLTFLoader } = await import('three/examples/jsm/loaders/GLTFLoader.js');
		this.loader = new GLTFLoader();

		return this.loader;
	}

	createEntityFromFeature(
		id: string | number,
		feature: Feature<Polygon | LineString | MultiPolygon, MeshComponentProperties>,
		style?: (LineStyle | PaintStyle) & { focusable?: boolean },
	) {
		const meshComponent = new MeshComponent(feature);

		// Handle bevel merging: style.bevel takes precedence over feature.properties.bevel
		// Only relevant for PaintStyle (polygons), not LineStyle
		const featureBevel = feature?.properties?.bevel;
		const styleBevel = style && 'bevel' in style ? style.bevel : undefined;
		let mergedBevel;

		if (styleBevel) {
			// Style bevel takes precedence - merge/override feature bevel
			mergedBevel = { ...featureBevel, ...styleBevel };
		} else if (featureBevel) {
			// Use feature bevel as-is
			mergedBevel = featureBevel;
		}

		const entity = new Geometry3D(
			meshComponent,
			new StyleComponent({ ...style, url: feature.properties?.image?.path, bevel: mergedBevel }),
		);
		if (style?.outline) {
			entity.components[Geometry3DComponents.Outline] = new OutlineComponent(darkenColorForOutline(style.color));
		}
		if (style?.focusable) {
			entity.components[Geometry3DComponents.Focusable] = new FocusableComponent();
		}
		if (
			feature.properties?.textures != null &&
			Array.isArray(feature.properties.textures) &&
			feature.properties.textures.length > 0
		) {
			entity.components[Geometry3DComponents.TextureComponent] = new TextureComponent(
				feature.properties.textures.find(t => t.face === 'side'),
				feature.properties.textures.find(t => t.face === 'top'),
			);
		} else if (
			(style != null && 'texture' in style && style.texture != null) ||
			(style != null && 'topTexture' in style && style.topTexture != null)
		) {
			entity.components[Geometry3DComponents.TextureComponent] = new TextureComponent(style.texture, style.topTexture);
		}
		// @ts-ignore
		entity.id = id;

		return entity;
	}

	createModelFromFeature(
		id: string | number,
		feature: Feature<Point, ModelProperties>,
		style?: Partial<ModelStyle>,
	): ModelGeometry3D {
		const meshComponent = new ModelComponent(feature);
		let initStyle = { ...style };
		if (feature.properties.verticalOffset != null) {
			initStyle.verticalOffset = feature.properties.verticalOffset;
		}
		if (feature.properties.rotation != null) {
			initStyle.rotation = feature.properties.rotation;
		}
		if (feature.properties.scale != null) {
			initStyle.scale = feature.properties.scale;
		}
		const modelStyle = new ModelStyleComponnet(initStyle);
		const entity = new Geometry3D(meshComponent, modelStyle);
		entity.id = id;

		return entity;
	}

	populateEntityMesh(entity: Geometry3D, geometry: BufferGeometry) {
		const meshComponent = entity.components[Geometry3DComponents.Mesh] as MeshComponent;
		meshComponent.geometry = geometry;
		meshComponent.batchedProps = {
			range: {
				start: 0,
				count: Infinity,
				indexCount: Infinity,
				indexStart: 0,
				vertexCount: Infinity,
				vertexStart: 0,
				reservedVertexCount: Infinity,
				reservedIndexCount: Infinity,
			},
			geometryId: -1,
			positionCount: geometry.attributes.position.count,
		};
		meshComponent.currentHeight = entity.components[Geometry3DComponents.Style].height;
		meshComponent.dirty = false;
	}

	async populateModelGroup(entities: Set<string | number>, url: string, tree: GeometryGroupObject3D) {
		const matrix = new Matrix4();
		const rotation = new Euler();
		const quaternion = new Quaternion();
		const scale = new Vector3();
		const object = new Geometry3DObject3D();

		const loader = await this.getGLTFLoader();
		/**
		 * NOTE: we are moving towards no instancing for now when dealing with single model or loading inline url. This is to supports backwards compat for handling an array of models
		 * See: https://mappedin.atlassian.net/wiki/spaces/SDK/pages/597983234/3D+Asset+Library#Deprecating-FeatureCollection-Support-when-Adding-Model
		 * TODO: caching models
		 */
		const promise = new Promise<void>(resolve => {
			const processGLTF = (gltf: any) => {
				if ('entities3D' in tree.userData) {
					if (tree.userData.entities3D.size > 1) {
						gltf.scene.traverse(child => {
							const gltfChild = child as Mesh;
							if ('isMesh' in gltfChild && gltfChild.isMesh === true) {
								const mesh = new InstancedMesh(gltfChild.geometry, gltfChild.material, entities.size);
								mesh.userData = {
									entities: {},
								};

								object.add(mesh);
								tree.add(object);
							}
						});
					} else {
						gltf.scene.traverse(child => {
							if ('isMesh' in child && child.isMesh === true) {
								// reset any transformations applied to the mesh
								child.position.set(0, 0, 0);
								child.rotation.set(0, 0, 0);
								child.scale.set(1, 1, 1);
								child.updateMatrix();
							}
						});
						object.add(gltf.scene);
						tree.add(object);
						object.userData.isSingleModel = true;
						object.userData.entityId = entities.values().next().value as any;
						object.userData.type = 'model';
					}
				}
				resolve();
			};

			// Check if url is raw GLTF JSON text or a data URL containing JSON
			const isRawGLTF =
				url.trimStart().startsWith('{') || (url.startsWith('data:') && url.includes('application/json'));

			if (isRawGLTF) {
				const gltfText = url.startsWith('data:')
					? atob(url.split(',')[1]) // Extract base64 content from data URL
					: url;
				loader.parse(gltfText, '', processGLTF);
			} else {
				// load the model from the url
				loader.load(url, processGLTF);
			}
		});
		this.optimizePromise.push(
			promise.then(() => {
				let idx = 0;

				let entity: EntityTypes | undefined;
				for (const entityId of entities.values()) {
					entity = this.state.geometry3DMap.get(entityId);
					if (!entity || entity?.type !== 'model') {
						continue;
					}
					const modelComponent = entity.components[Geometry3DComponents.Mesh];
					const styleComponent = entity.components[Geometry3DComponents.Style];

					const feature = entity.components[Geometry3DComponents.Mesh].feature;
					modelComponent.mesh = object;
					modelComponent.instanceIndex = idx;
					applyModelGeometryStyle(entity);
					entity.components[Geometry3DComponents.Style].dirty = false;
					const { x, y } = this.convertTo3DMapPosition(
						feature.geometry.coordinates[1],
						feature.geometry.coordinates[0],
						0,
					);
					modelComponent.position.set(x, y, modelComponent.altitude);
					rotation.set(
						MathUtils.degToRad(styleComponent.rotation?.[0] ?? DEFAULT_ROTATION[0]),
						MathUtils.degToRad(styleComponent.rotation?.[1] ?? DEFAULT_ROTATION[1]),
						MathUtils.degToRad(styleComponent.rotation?.[2] ?? DEFAULT_ROTATION[2]),
					);
					scale.set(
						styleComponent.scale?.[0] ?? DEFAULT_SCALE[0],
						styleComponent.scale?.[1] ?? DEFAULT_SCALE[1],
						styleComponent.scale?.[2] ?? DEFAULT_SCALE[2],
					);
					quaternion.setFromEuler(rotation);
					matrix.compose(modelComponent.position, quaternion, scale);
					object.children.forEach(child => {
						if (!entity || entity?.type !== 'model' || !(child instanceof InstancedMesh)) return;
						child.setMatrixAt(idx, matrix);
						child.userData.entities[idx] = entity.id;
					});
					// TODO: consolidate models to use frustum culled instancing
					if (modelComponent.mesh.userData.isSingleModel) {
						modelComponent.mesh.matrix.copy(matrix);
						modelComponent.mesh.matrix.decompose(
							modelComponent.mesh.position,
							modelComponent.mesh.quaternion,
							modelComponent.mesh.scale,
						);
					}
					idx++;
				}
				this.publish('model-loaded');
			}),
		);

		return object;
	}

	populateEntityGroup(entities: Set<string | number>) {
		let mesh: BufferGeometry;
		let feature: Feature<Polygon | LineString | MultiPolygon | MultiLineString>;
		let style: any;
		let entity: Geometry3D;
		for (const entityId of entities.values()) {
			entity = this.state.geometry3DMap.get(entityId) as Geometry3D;
			if (entity.components[Geometry3DComponents.Mesh] instanceof MeshComponent) {
				feature = entity.components[Geometry3DComponents.Mesh].feature;
				style = entity.components[Geometry3DComponents.Style];
				if (feature.geometry.type === 'Polygon') {
					mesh = renderPolygonGeometry(
						feature as Feature<Polygon, any>,
						this.convertTo3DMapPosition,
						style as PaintStyle,
					);
					if (feature.properties?.textures && feature.properties.textures.length > 0) {
						updateUVsLegacy(mesh, feature.properties.textures, this.state.naturalBearing, this.convertTo3DMapPosition);
					}
					this.populateEntityMesh(entity, mesh);
				} else if (feature.geometry.type === 'MultiPolygon') {
					mesh = renderMultiPolygonGeometry(
						feature as Feature<MultiPolygon, any>,
						this.convertTo3DMapPosition,
						style as PaintStyle,
					);
					this.populateEntityMesh(entity, mesh);
				} else if (feature.geometry.type === 'LineString') {
					mesh = renderLineStringGeometry(
						feature as Feature<LineString, any>,
						this.convertTo3DMapPosition,
						style as unknown as LineStyle,
					);
					this.populateEntityMesh(entity, mesh);
				} else if (feature.geometry.type === 'MultiLineString') {
					mesh = renderMultiLineStringGeometry(
						feature as Feature<MultiLineString, any>,
						this.convertTo3DMapPosition,
						style as unknown as LineStyle,
					);
					this.populateEntityMesh(entity, mesh);
				}
			}
		}

		return entities;
	}

	private optimizePromise: Promise<void>[] = [];

	update() {
		for (const entityId of this.state.geometryGroupIdsToLoad.values()) {
			const entity = this.state.geometry3DMap.get(entityId);
			if (entity instanceof GeometryGroupObject3D && entity.userData.dirty) {
				if (!entity.userData.modelURL) {
					try {
						this.populateEntityGroup(entity.userData.entities3D);
					} catch (e) {
						Logger.warn(e);
					}
				} else if (entity.userData.modelURL) {
					this.populateModelGroup(entity.userData.entities3D, entity.userData.modelURL, entity);
					entity.userData.dirty = false;
				}

				let childEntity: EntityTypes | undefined;
				const index: {
					[id: string]: {
						style: {
							color: string;
							opacity?: number;
						};
						entities: Geometry3D[];
					};
				} = {};

				for (const entityId of entity.userData.entities3D.values()) {
					childEntity = this.state.geometry3DMap.get(entityId);
					const component = childEntity?.components[Geometry3DComponents.Mesh];
					if (childEntity?.type === 'geometry') {
						const style = childEntity.components[Geometry3DComponents.Style];
						const indexString = generateIndex(style);
						if (index[indexString] === undefined) {
							index[indexString] = {
								style: style,
								entities: [],
							};
						}
						index[indexString].entities.push(childEntity);
					} else if (component && 'mesh' in component && component.mesh && component.mesh?.parent !== entity) {
						entity.add(component.mesh);
					}
				}

				for (const indexString in index) {
					const mesh = batchGeometries(index[indexString].entities, index[indexString].style);
					entity.add(mesh);
				}

				// process any attached 2D entities that need to be added to the 3D entity
				for (const entityId of entity.userData.entities3D.values()) {
					childEntity = this.state.geometry3DMap.get(entityId) as Geometry3D;

					if (childEntity != null && childEntity.entities2D.size > 0) {
						for (const entity2D of childEntity.entities2D.values()) {
							if (entity2D) {
								(childEntity as Geometry3D).attach(entity2D);
								this.publish('geometry-2d-added');
							}
						}
					}
				}

				this.publish('geometry-group-added');

				entity.userData.dirty = false;
			}
		}

		return Promise.resolve();
	}
}
