export type IconName = keyof typeof ICONS;

const OPENING_TAG_REGEX = /<svg([^>]*)>/g;

export function getBuiltInIcon(iconName: IconName, color: string, size: number): string;
export function getBuiltInIcon(iconName: string, color: string, size: number): string | null {
	const icon = ICONS[iconName];
	if (!icon) {
		return null;
	}

	return icon.replace(
		OPENING_TAG_REGEX,
		`<svg$1 xmlns="http://www.w3.org/2000/svg" style="fill:${color}; width:${size}px; height:${size}px">`,
	);
}

/**
 * Sanitize an SVG string to be rendered in the DOM.
 */
export function sanitizeSVGString(svg: string) {
	return DOMPurify.sanitize(svg, { USE_PROFILES: { svg: true } });
}

/**
 * Style a given SVG string by adding `fill=currentColor` and `stroke=currentColor` where appropriate
 * and setting `color` on the root element.
 */
export function styleSVGString(svg: string, color: string): string {
	// Extract the SVG content to apply styling easier
	const parser = new DOMParser();
	const doc = parser.parseFromString(svg, 'image/svg+xml');
	const svgElement = doc.documentElement;
	if (svgElement == null || svgElement.tagName === 'parsererror' || svgElement.tagName.toLowerCase() !== 'svg') {
		Logger.error('Failed to parse SVG', { svg, tagName: svgElement?.tagName });
		return svg;
	}

	// Apply color to all path and shape elements
	if (color != null) {
		svgElement.setAttribute('color', color);
		const innerElements = svgElement.querySelectorAll('*');
		innerElements.forEach(element => {
			if (
				(element.getAttribute('fill') != null || element.tagName === 'path') &&
				element.getAttribute('fill') !== 'none'
			) {
				element.setAttribute('fill', 'currentColor');
			}
			if (element.getAttribute('stroke') != null && element.getAttribute('stroke') !== 'none') {
				element.setAttribute('stroke', 'currentColor');
			}
		});
	}

	// Convert back to string
	const serializer = new XMLSerializer();
	return serializer.serializeToString(svgElement);
}

/**
 * Fetch an SVG from a URL and return it as a string.
 */
export async function fetchIcon(
	url: string,
	options?: { color?: string; style?: 'default' | 'fill' },
): Promise<{ icon: string; type: 'svg' | 'image' } | null> {
	let icon = '';
	try {
		if (url.trim() === '') {
			throw new Error('URL is empty!');
		}

		if (options?.style === 'fill') {
			url = url.replace('.svg', '-fill.svg');
		}

		const res = await isomorphicFetch()(url);
		// Check if the response is an SVG
		const contentType = res.headers.get('content-type');
		if (contentType?.includes('image/svg+xml')) {
			const raw = await res.text();
			icon = sanitizeSVGString(raw);
			if (options?.color != null) {
				icon = styleSVGString(icon, options.color);
			}
			return { icon, type: 'svg' };
		} else if (contentType?.includes('image')) {
			// Label creation will fetch the image from cached now that we've fetched it here
			return { icon: url, type: 'image' };
		} else {
			throw new Error(`Expected SVG or Image content, but received: ${contentType}`);
		}
	} catch (e) {
		Logger.error(`Failed to fetch icon from ${url}.`, e);
		return null;
	}
}
