// Stolen from https://github.com/MappedIn/self-serve/blob/master/packages/lib/src/transformers/mvf/utils/label-center.ts

import { type Result, err, ok } from '@mappedin/safe-types/result';
import polylabel from 'polylabel';
import type { Feature, Polygon } from '../types/geojson.js';

/**
 * Calculate the area of a polygon using the shoelace formula
 */
const calculateArea = (polygon: Feature<Polygon, any>): number => {
	// Handle first ring (outer ring)
	const coordinates = polygon.geometry.coordinates[0];
	let area = 0;

	// Shoelace formula
	for (let i = 0; i < coordinates.length - 1; i++) {
		area += coordinates[i][0] * coordinates[i + 1][1] - coordinates[i + 1][0] * coordinates[i][1];
	}

	// Return absolute value of half the sum
	return Math.abs(area / 2);
};

/**
 * Check if a point is inside a polygon using ray casting algorithm
 */
const pointInPolygon = (point: [number, number], polygon: Feature<Polygon, any>): boolean => {
	const [x, y] = point;
	const coordinates = polygon.geometry.coordinates[0];

	let inside = false;
	for (let i = 0, j = coordinates.length - 1; i < coordinates.length; j = i++) {
		const [xi, yi] = coordinates[i];
		const [xj, yj] = coordinates[j];

		const intersect = yi > y !== yj > y && x < ((xj - xi) * (y - yi)) / (yj - yi) + xi;

		if (intersect) inside = !inside;
	}

	return inside;
};

/**
 * Calculate the center of mass of a polygon
 */
const calculateCenterOfMass = (polygon: Feature<Polygon, any>): { geometry: { coordinates: [number, number] } } => {
	const coordinates = polygon.geometry.coordinates[0];
	let sumX = 0;
	let sumY = 0;

	for (let i = 0; i < coordinates.length - 1; i++) {
		sumX += coordinates[i][0];
		sumY += coordinates[i][1];
	}

	return {
		geometry: {
			coordinates: [sumX / (coordinates.length - 1), sumY / (coordinates.length - 1)] as [number, number],
		},
	};
};

/**
 * Picks a precision to use for polylabel based on the size of the polygon. Using
 * a fixed precision means for large polygons, it can take a huge amount of time
 * and memory to compute a center that is "good enough".
 */
const estimatePrecision = (f: Feature<Polygon, any>): number => {
	const polygonArea = calculateArea(f);
	const safePolygonArea = Number.isFinite(polygonArea) ? Math.max(polygonArea, 1) : 1;
	// sqrt makes this behave more consistently from what I've found in terms of
	// finding a near-optimal value without taking too long.
	return Math.sqrt(safePolygonArea) * 0.000001;
};

export const labelCenter = (f: Feature<Polygon, any>): Result<[number, number], Error> => {
	try {
		const center = calculateCenterOfMass(f);
		const centerCoords = center.geometry.coordinates;
		const isInside = pointInPolygon(centerCoords, f);

		if (!isInside) {
			// @ts-expect-error the polylabel types are wrong
			const centerPt = polylabel(f.geometry.coordinates, estimatePrecision(f));
			return ok([centerPt[0], centerPt[1]]);
		}

		return ok(centerCoords);
	} catch (error) {
		return err(new Error('Unable to calculate center of polygon'));
	}
};
