import booleanPointOnLine from '@turf/boolean-point-on-line';
import center from '@turf/center';
import length from '@turf/length';
import lineSlice from '@turf/line-slice';
import nearestPointOnLine from '@turf/nearest-point-on-line';
import type { EntranceFeature, ObstructionWallFeature } from './mvf-types';

const TURF_EPSILON = 0.0000001;

function point(coords: number[]): GeoJSON.Point {
	return {
		type: 'Point',
		coordinates: coords,
	};
}

function splitObstructionByEntrance(wall: ObstructionWallFeature, entrance: EntranceFeature): ObstructionWallFeature[] {
	const { coordinates: entranceCoords } = entrance.geometry;
	const { coordinates: wallCoords } = wall.geometry;
	const entrancePoints = entranceCoords.map(c => nearestPointOnLine(wall, point(c)));

	return wallCoords.reduce((acc, wallCoord) => {
		const line1 = lineSlice(point(wallCoord), entrancePoints[0], wall);
		const line2 = lineSlice(point(wallCoord), entrancePoints[1], wall);
		const newWall = length(line1) < length(line2) ? line1 : line2;

		newWall.properties = {
			...wall.properties,
			entrances: [],
		};

		return acc.concat([newWall as ObstructionWallFeature]);
	}, [] as ObstructionWallFeature[]);
}

export function findAndSplitObstructionByAllEntrances(
	wall: ObstructionWallFeature,
	entrances: EntranceFeature[],
): { entrances: EntranceFeature[]; obstructions: ObstructionWallFeature[] } {
	return entrances.reduce<{ entrances: EntranceFeature[]; obstructions: ObstructionWallFeature[] }>(
		(result, entrance) => {
			const options = { epsilon: TURF_EPSILON };

			const wallWithEntranceIndex = result.obstructions.findIndex(wall =>
				booleanPointOnLine(center(entrance), wall, options),
			);
			const wallWithEntrance = result.obstructions[wallWithEntranceIndex];

			if (wallWithEntrance) {
				const [firstWall, secondWall] = splitObstructionByEntrance(wallWithEntrance, entrance);
				result.entrances.push(entrance);
				result.obstructions.splice(wallWithEntranceIndex, 1, firstWall, secondWall);
			}

			return result;
		},
		{ obstructions: [wall], entrances: [] },
	);
}
