export type TAllGetVenueOptions =
	| TGetVenueOptions
	| TGetVenueMVFOptions
	| (TProcessedMVFOptions & { perspective?: string }); // adding perspective prop here for backwards compatibility

export type TGetVenueOptionsInternal<T extends TAllGetVenueOptions> = Omit<
	T & {
		baseUrl?: string;
		supplementaryUrl?: string;
		noAuth?: boolean;
		includeHidden?: boolean;
		apiGateway?: string;
		authorization?: string;
		headers?: any;
	},
	'things'
> & { things?: any };

export const defaultOptions: TGetVenueOptionsInternal<TGetVenueOptions> = {
	supplementaryUrl: 'https://api-gateway.mappedin.com/analytics/',
	headers: {},
	accessToken: '',
	clientId: '',
	noAuth: false,
	includeHidden: true,
	clientSecret: '',
	apiGateway: '',
	authorization: '',
	perspective: 'Website',
	language: '',
	venue: '',
};

/**
 * @internal
 */
export function parseOptions<T extends TAllGetVenueOptions>(options: T): TGetVenueOptionsInternal<T> {
	const _options = {
		...defaultOptions,
		...options,
	};

	_options.headers = {
		..._options.headers,
		'mappedin-sdk-version': version,
	};

	_options.things = [] as any;

	if ('platformString' in options) {
		_options.headers = {
			..._options.headers,
			'mappedin-platform-version': options.platformString,
		};
	}

	if (_options.noAuth) {
		// Skip auth
	} else if (_options.accessToken) {
		_options.authorization = `Bearer ${_options.accessToken}`;
	} else if (_options.clientId && _options.clientSecret) {
		_options.authorization = `Basic ${(globalThis as any).btoa(`${_options.clientId}:${_options.clientSecret}`)}`;
	}

	// Language code should be case-insensitive
	_options.language = _options.language?.toLowerCase();

	return _options;
}

/**
 * Prepares the request and URL fields to pass into generateAPIRequest
 * @param options options from getVenue- baseUrl and supplementaryUrl are used
 * @param url string describing the url corresponding to the client's request
 * @param supplementary whether to use the supplementaryUrl when sending the request
 */
export function constructParamsForRequest(
	options: TGetVenueOptionsInternal<TGetVenueOptions>,
	url: string,
	supplementary = false,
) {
	const requestOptions = {
		language: options.language,
		clientId: options.clientId,
		clientSecret: options.clientSecret,
	};

	if (supplementary) {
		delete requestOptions.language;
	}

	const { headers } = options;

	headers['Content-Type'] = headers['Content-Type'] || 'text/json';

	if (options.accessToken) {
		headers.Authorization = `Bearer ${options.accessToken}`;
	} else if (!options.noAuth) {
		if (options.clientId && options.clientSecret) {
			headers['x-mappedin-key'] = btoa(options.clientId);
			headers['x-mappedin-secret'] = btoa(options.clientSecret);
		}
	}

	const req = {
		method: 'GET',
		headers,
	};

	if (options.secure) {
		url += '&withcdntoken';
	}

	return { req, url };
}

export async function generateAPIRequest(
	options: TGetVenueOptionsInternal<TGetVenueOptions>,
	url: string,
	supplementary = false,
) {
	/*
  If not supplementary: fail on a non-OK response
  If supplementary: always succeed
  */
	const ret = constructParamsForRequest(options, url, supplementary);

	const response = await isomorphicFetch()(ret.url, ret.req);

	if (!response.ok) {
		throw new Error(`${response.status} ${response.statusText}`);
	}

	return response.json();
}

export const getPerspectivesFromResponse = (
	res: unknown,
): Record<string, { url: string; locale_packs: LocalePackUrls }> | null => {
	const availablePerspectives =
		res != null &&
		typeof res === 'object' &&
		'perspectives' in res &&
		res.perspectives != null &&
		typeof res.perspectives === 'object'
			? res.perspectives
			: null;

	if (availablePerspectives == null || Object.keys(availablePerspectives).length === 0) {
		Logger.warn('No perspectives available in response!');

		return null;
	}

	const perspectives: Record<string, { url: string; locale_packs: LocalePackUrls }> = {};

	for (const [name, perspective] of Object.entries(availablePerspectives)) {
		const url = 'url' in perspective && typeof perspective.url === 'string' ? perspective.url : null;
		const locale_packs =
			'locale_packs' in perspective && typeof perspective.locale_packs === 'object' ? perspective.locale_packs : null;
		if (url == null || locale_packs == null) {
			Logger.warn(`Perspective ${name} is missing url or locale_packs!`);
			continue;
		}
		perspectives[name] = { url, locale_packs };
	}

	return perspectives;
};

/**
 * Get the bundle URL and updated_at time.
 * @internal
 */
export async function getVenueMVFURL(userOptions: TGetVenueBundleOptions, path = `exports/mvf/1/bundle`) {
	const parsedOptions: TGetVenueOptionsInternal<TGetVenueBundleOptions> = parseOptions({
		baseUri: 'https://api-gateway.mappedin.com/',
		...defaultOptions,
		...userOptions,
	});
	const _url = `${parsedOptions.baseUri + path}?venue=${parsedOptions.venue}&version=${
		parsedOptions.version ?? '2.0.0'
	}`;
	parsedOptions.headers['Content-Type'] = 'application/zip';

	const res = await generateAPIRequest(parsedOptions, _url);

	let url = res.url;
	let localePacks = res.locale_packs;

	// Try and load a requested perspective
	const requestedPerspective = parsedOptions.perspective ?? defaultOptions.perspective;
	const availablePerspectives = getPerspectivesFromResponse(res);
	if (requestedPerspective != null && requestedPerspective !== '' && availablePerspectives != null) {
		const perspective = availablePerspectives[requestedPerspective];
		if (perspective == null) {
			Logger.warn(`Perspective ${requestedPerspective} not found in response! Loading default perspective instead.`);
		} else {
			Logger.log(`Loading perspective "${requestedPerspective}"`);
			url = perspective.url;
			localePacks = perspective.locale_packs;
		}
	}

	return {
		url,
		localePacks,
		updated_at: res.updated_at,
	};
}

const getDecoder = (function () {
	let decoder: TextDecoder | undefined;

	return (): TextDecoder => {
		if (!decoder) {
			decoder = new TextDecoder();
		}

		return decoder;
	};
})();

export async function getLanguagePackUnitArray(url: string, { signal }: { signal: AbortSignal }) {
	const data = await (await isomorphicFetch()(url, { signal })).arrayBuffer();

	return new Uint8Array(data);
}

export async function parseLanguagePack(data: Uint8Array) {
	const raw = fflate.unzipSync(data);
	const mvfData: ParsedMVFLocalePack = {} as unknown as any;
	if (raw['enterprise/'] != null) {
		mvfData.enterprise = {};
		const locationsRaw = raw['enterprise/locations.json'];
		if (locationsRaw) {
			mvfData.enterprise.locations = JSON.parse(getDecoder().decode(locationsRaw));
		}
		const locationInstancesRaw = raw['enterprise/locationInstances.json'];
		if (locationInstancesRaw) {
			mvfData.enterprise.locationInstances = JSON.parse(getDecoder().decode(locationInstancesRaw));
		}
		const categoryRaw = raw['enterprise/categories.json'];
		if (categoryRaw) {
			mvfData.enterprise.categories = JSON.parse(getDecoder().decode(categoryRaw));
		}
	}
	if (raw['floorstack.json']) {
		mvfData['floorstack.json'] = JSON.parse(getDecoder().decode(raw['floorstack.json']));
	}
	if (raw['floor.geojson']) {
		mvfData['floor.geojson'] = JSON.parse(getDecoder().decode(raw['floor.geojson']));
	}
	if (raw['manifest.json']) {
		mvfData['manifest.geojson'] = JSON.parse(getDecoder().decode(raw['manifest.json']));
	}

	return mvfData;
}

export async function downloadLanguagePack(
	url: string,
	{ signal }: { signal: AbortSignal },
): Promise<ParsedMVFLocalePack | undefined> {
	try {
		const data = await (await isomorphicFetch()(url, { signal })).arrayBuffer();
		if (signal.aborted) {
			return;
		}
		const compressed = new Uint8Array(data);
		const mvfData = await parseLanguagePack(compressed);

		return mvfData;
	} catch (e) {
		const newError = new Error('downloadLanguagePack failed');
		newError.cause = e;
		throw newError;
	}
}

export function getMVFBundlePathAPIGateway(options: { mvfVersion?: string; useDraftData?: boolean }): string {
	const { mvfVersion, useDraftData } = options;
	if (mvfVersion === '3.0.0') {
		return useDraftData ? `exports/mvf3-draft/1/bundle` : `exports/mvf3/1/bundle`;
	} else if (mvfVersion == null || mvfVersion === '2.0.0') {
		return useDraftData ? `exports/mvf2-draft/1/bundle` : `exports/mvf2/1/bundle`;
	} else if (mvfVersion === '2.0.0-c') {
		/**
		 * Ben Liu, 2025-09-08
		 *
		 * This is a temporary, special MVF version that indicates an MVF v2 created using the MVF v3 exporter + converter. Eventually.
		 * this should be the default MVF v2, but as there are still some bugs to be resolved with the MVF v3 exporter, the regular MVF
		 * v2 is still made using the legacy exporter.
		 *
		 * TODO: This option should be deprecated once the MVF v3 exporter is stable.
		 */
		return useDraftData ? `exports/mvf2c-draft/1/bundle` : `exports/mvf2c/1/bundle`;
	} else {
		throw new Error(`Unsupported MVF version: ${mvfVersion}`);
	}
}

export async function downloadVenueBundleMVFAPIGateway(
	options: TGetVenueBundleOptions,
): Promise<{ compressed: Uint8Array; localePacks: LocalePackUrls }> {
	return new Promise(async (resolve, reject) => {
		try {
			const path = getMVFBundlePathAPIGateway(options);
			const { url, localePacks } = await getVenueMVFURL(options, path);
			const data = await isomorphicFetch()(url).then(res => res.arrayBuffer());
			const compressed = new Uint8Array(data);
			resolve({ compressed, localePacks });
		} catch (e: any) {
			reject(e);
		}
	});
}

export async function downloadVenueBundleMVF(options: TGetVenueBundleOptions): Promise<Uint8Array> {
	return new Promise(async (resolve, reject) => {
		try {
			const { url } = await getVenueMVFURL(options);
			const data = await isomorphicFetch()(url).then(res => res.arrayBuffer());
			const compressed = new Uint8Array(data);
			resolve(compressed);
		} catch (e: any) {
			reject(e);
		}
	});
}

export function parseMVFv1(raw: RawMVFv1): ParsedMVFv1 {
	// https://stackoverflow.com/questions/68468203/why-am-i-getting-textencoder-is-not-defined-in-jest
	const decoder = new TextDecoder();
	const decode = decoder.decode.bind(decoder);

	try {
		const mvfData: ParsedMVFv1 = {
			'manifest.geojson': JSON.parse(decode(raw['manifest.geojson'])),
			'building.geojson': JSON.parse(decode(raw['building.geojson'])),
			'category.geojson': JSON.parse(decode(raw['category.geojson'])),
			'location.geojson': JSON.parse(decode(raw['location.geojson'])),
			obstruction: {},
			space: {},
			level: {},
			node: {},
			connection: {},
		};

		const spaceFiles = Object.keys(raw.space);
		for (const file of spaceFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.space[trimmedName] = JSON.parse(decode(raw.space[file]));
		}

		const obstructionFiles = Object.keys(raw.obstruction);
		for (const file of obstructionFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.obstruction[trimmedName] = JSON.parse(decode(raw.obstruction[file]));
		}

		const nodeFiles = Object.keys(raw.node);
		for (const file of nodeFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.node[trimmedName] = JSON.parse(decode(raw.node[file]));
		}

		const levelFiles = Object.keys(raw.level);
		for (const file of levelFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.level[trimmedName] = JSON.parse(decode(raw.level[file]));
		}

		const connectionFiles = Object.keys(raw.connection);
		for (const file of connectionFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.connection[trimmedName] = JSON.parse(decode(raw.connection[file]));
		}

		return mvfData;
	} catch (e) {
		Logger.error(e);
		throw new Error('MVF parsing failed! Probably encountered a bad file.');
	}
}

export function validateStaticMVFv1Files(data: unknown) {
	if (data == null || typeof data !== 'object') {
		return false;
	}

	const STATIC_MVF_V1_FILES = ['building.geojson', 'category.geojson', 'location.geojson', 'manifest.geojson'] as const;

	for (const fileName of STATIC_MVF_V1_FILES) {
		/**
		 * Checking that the unzipped data is a Uint8Array is our best guess that the data
		 * is correct. At least it matches the typescript definition from @mappedin/mvf.
		 */
		if (data?.[fileName]?.[Symbol.toStringTag] !== 'Uint8Array') {
			return false;
		}
	}

	return true;
}

export async function unzipMVFv1(data: any): Promise<RawMVFv1> {
	return new Promise(async (resolve, reject) => {
		try {
			if (data == null || typeof data !== 'object') {
				throw new Error('Error parsing data! Unzipping failed.');
			}

			if (data![Symbol.toStringTag] !== 'Uint8Array') {
				throw new Error('Unexpected file type! Unzipping failed.');
			}

			const decompressed = fflate.unzipSync(data as Uint8Array);

			if (!validateStaticMVFv1Files(decompressed)) {
				throw new Error('MVF is missing some expect static files. Unzipping failed.');
			}

			const raw: RawMVFv1 = {
				'building.geojson': decompressed['building.geojson'],
				'category.geojson': decompressed['category.geojson'],
				'location.geojson': decompressed['location.geojson'],
				'manifest.geojson': decompressed['manifest.geojson'],
				space: {},
				obstruction: {},
				node: {},
				level: {},
				connection: {},
			};

			const keys = Object.keys(decompressed);
			for (const fileName of keys) {
				// Ignore the file if it has no data
				if (decompressed[fileName].length === 0) {
					continue;
				}

				const path = fileName.split('/');

				if (path.length < 0) {
					throw new Error('Encounted a file with no name! Unzipping failed.');
				}

				if (path.length > 2) {
					throw new Error('Encountered unexpected nesting in MVF! Unzipping failed.');
				}

				// We can ignore any un-nested files as they should have already been attached above
				if (path.length === 1) {
					continue;
				}

				/**
				 * If the file is nested under a folder, construct the nested JSON appropriately
				 */
				if (path[0] === 'image') {
					// TODO: Handle images
					Logger.warn('MVF unzipping encountered an image. Ignoring as this case has not been implemented.');
					continue;
				} else {
					if (raw[path[0]] == null) {
						raw[path[0]] = {};
					}
					raw[path[0]][path[1]] = decompressed[fileName];
				}
			}

			resolve(raw);
		} catch (e) {
			reject(e);
		}
	});
}

export async function downloadMVF(userOptions: TGetMapDataOptions, accessToken: string): Promise<Uint8Array> {
	return new Promise(async (resolve, reject) => {
		try {
			const { url } = await getMakerBundleURL(userOptions, accessToken);

			const data = await isomorphicFetch()(url).then(res => res.arrayBuffer());
			const compressed = new Uint8Array(data);
			resolve(compressed);
		} catch (e: any) {
			reject(e);
		}
	});
}

export async function getMakerBundleURL(userOptions: TGetMapDataOptions, token: string) {
	const url = new URL(
		`${userOptions.baseUri ? normalizeUrl(userOptions.baseUri) : DEFAULT_V6_URI}api/v1/venue/${userOptions.mapId}/mvf`,
	);

	if (userOptions.viewId) {
		url.searchParams.set('configId', userOptions.viewId);
	}

	if (userOptions.mvfVersion) {
		url.searchParams.set('version', userOptions.mvfVersion);
	}

	const response = await isomorphicFetch()(url.toString(), {
		headers: {
			Authorization: `Bearer ${token}`,
		},
	});

	if (!response.ok) throw new Error(`${response.status} ${response.statusText}`);

	return response.json() as unknown as { url: string; updated_at: string };
}

/**
 * @internal
 */
export function parseMVF(raw: RawMVF): ParsedMVF {
	// TODO: Replace this entire function with a call to @mappedin/mvf/rawToParsedMVF
	// https://stackoverflow.com/questions/68468203/why-am-i-getting-textencoder-is-not-defined-in-jest
	const decoder = new TextDecoder();
	const decode = decoder.decode.bind(decoder);

	try {
		const mvfData: ParsedMVF = {
			'connection.json': JSON.parse(decode(raw['connection.json'])),
			'manifest.geojson': JSON.parse(decode(raw['manifest.geojson'])),
			// TODO: remove this once map.geojson is deprecated
			'map.geojson': raw['map.geojson'] ? JSON.parse(decode(raw['map.geojson'])) : undefined,
			// TODO: remove this once mapstack.geojson is deprecated
			'mapstack.geojson': raw['mapstack.geojson'] ? JSON.parse(decode(raw['mapstack.geojson'])) : undefined,
			'node.geojson': JSON.parse(decode(raw['node.geojson'])),
			obstruction: {},
			space: {},
			entrance: {},
			facade: {},
			'tileset.json': raw['tileset.json'] ? JSON.parse(decode(raw['tileset.json'])) : undefined,
			'navigationFlags.json': raw['navigationFlags.json'] ? JSON.parse(decode(raw['navigationFlags.json'])) : undefined,
		};

		if (raw.enterprise != null) {
			mvfData.enterprise = {} as EnterpriseCollection;
			if (raw.enterprise?.['locations.json']) {
				mvfData.enterprise.locations = JSON.parse(decode(raw.enterprise?.['locations.json']));
			}
			if (raw.enterprise?.['categories.json']) {
				mvfData.enterprise.categories = JSON.parse(decode(raw.enterprise?.['categories.json']));
			}
			if (raw.enterprise?.['venue.json']) {
				mvfData.enterprise.venue = JSON.parse(decode(raw.enterprise?.['venue.json']));
			}
			if (raw.enterprise?.['layers.json']) {
				mvfData.enterprise.layers = JSON.parse(decode(raw.enterprise?.['layers.json']));
			}
			if (raw.enterprise?.['locationInstances.json']) {
				mvfData.enterprise.locationInstances = JSON.parse(decode(raw.enterprise?.['locationInstances.json']));
			}
			if (raw.enterprise?.['enterpriseStyles.json']) {
				mvfData.enterprise.enterpriseStyles = JSON.parse(decode(raw.enterprise?.['enterpriseStyles.json']));
			}
			if (raw.enterprise?.['textures.json']) {
				mvfData.enterprise.textures = JSON.parse(decode(raw.enterprise?.['textures.json']));
			}

			if (raw.enterprise?.['floorText.json']) {
				mvfData.enterprise.floorText = JSON.parse(decode(raw.enterprise?.['floorText.json']));
			}
			if (raw.enterprise?.['categoryPriorities.json']) {
				mvfData.enterprise.categoryPriorities = JSON.parse(decode(raw.enterprise?.['categoryPriorities.json']));
			}
		}

		if (raw['floorstack.json'] != null) {
			mvfData['floorstack.json'] = JSON.parse(decode(raw['floorstack.json']));
		} else if (raw['mapstack.json'] != null) {
			mvfData['mapstack.json'] = JSON.parse(decode(raw['mapstack.json']));
		}

		if (mvfData['floorstack.json'] == null && mvfData['mapstack.json'] == null && mvfData['mapstack.geojson'] == null) {
			throw new Error('MVF is missing floorstack.json or mapstack.json/mapstack.geojson');
		}

		if (raw['floor.geojson']) {
			mvfData['floor.geojson'] = JSON.parse(decode(raw['floor.geojson']));
		}

		if (mvfData['floor.geojson'] == null && mvfData['map.geojson'] == null) {
			throw new Error('MVF is missing floor.geojson or map.geojson');
		}

		if ('styles.json' in raw) {
			mvfData['styles.json'] = JSON.parse(decode(raw['styles.json']));
		}

		if ('shapes.json' in raw) {
			mvfData['shapes.json'] = JSON.parse(decode(raw['shapes.json']));
		}

		if ('location.json' in raw) {
			mvfData['location.json'] = JSON.parse(decode(raw['location.json']));
		}

		if ('category.json' in raw) {
			mvfData['category.json'] = JSON.parse(decode(raw['category.json']));
		}

		if (raw.annotation != null) {
			mvfData.annotation = {};
			const annotationFiles = Object.keys(raw.annotation);
			for (const file of annotationFiles) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.annotation![trimmedName] = JSON.parse(decode(raw.annotation[file]));
			}
		}

		const spaceFiles = Object.keys(raw.space);
		for (const file of spaceFiles) {
			const trimmedName = file.replace(/\.json|\.geojson/g, '');
			mvfData.space[trimmedName] = JSON.parse(decode(raw.space[file]));
		}

		if (raw.obstruction != null) {
			const obstructionFiles = Object.keys(raw.obstruction);
			for (const file of obstructionFiles) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.obstruction[trimmedName] = JSON.parse(decode(raw.obstruction[file]));
			}
		} else {
			mvfData.obstruction = {};
		}

		if (raw.entrance != null) {
			const entranceFiles = Object.keys(raw.entrance);
			for (const file of entranceFiles) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.entrance[trimmedName] = JSON.parse(decode(raw.entrance[file]));
			}
		} else {
			mvfData.entrance = {};
		}

		if ('floorImages' in raw && raw['floorImages'] != null) {
			mvfData.floorImages = {};
			for (const file of Object.keys(raw.floorImages)) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.floorImages[trimmedName] = JSON.parse(decode(raw.floorImages[file]));
			}
		}

		if ('textAreas' in raw && raw['textAreas'] != null) {
			mvfData.textAreas = {};
			for (const file of Object.keys(raw.textAreas)) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.textAreas[trimmedName] = JSON.parse(decode(raw.textAreas[file]));
			}
		}

		if ('facade' in raw && raw['facade'] != null) {
			mvfData.facade = {};
			for (const file of Object.keys(raw.facade)) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.facade[trimmedName] = JSON.parse(decode(raw.facade[file]));
			}
		}

		if ('area' in raw && raw['area'] != null) {
			mvfData.area = {};
			for (const file of Object.keys(raw.area)) {
				const trimmedName = file.replace(/\.json|\.geojson/g, '');
				mvfData.area[trimmedName] = JSON.parse(decode(raw.area[file]));
			}
		}

		return mvfData;
	} catch (e) {
		Logger.error(e);
		throw new Error('MVF parsing failed! Probably encountered a bad file.');
	}
}

/**
 * Figure out if the MVF data at least has the static files we expect.
 */
export function validateStaticMVFFiles(data: unknown) {
	if (data == null || typeof data !== 'object') {
		return false;
	}

	const STATIC_MVF_FILES = [
		'connection.json',
		'manifest.geojson',
		'floor.geojson' in data ? 'floor.geojson' : 'map.geojson',
		'node.geojson',
		'floorstack.json' in data ? 'floorstack.json' : 'mapstack.json' in data ? 'mapstack.json' : 'mapstack.geojson',
	];

	// optional files
	if ('styles.json' in data) {
		STATIC_MVF_FILES.push('styles.json');
	}
	if ('shapes.json' in data) {
		STATIC_MVF_FILES.push('shapes.json');
	}

	for (const fileName of STATIC_MVF_FILES) {
		/**
		 * Checking that the unzipped data is a Uint8Array is our best guess that the data
		 * is correct. At least it matches the typescript definition from @mappedin/mvf.
		 */
		if (data[fileName] == null || data[fileName][Symbol.toStringTag] !== 'Uint8Array') {
			return false;
		}
	}

	return true;
}

/**
 * @internal
 */
export async function unzipMVF(data: Uint8Array): Promise<RawMVF> {
	return new Promise(async (resolve, reject) => {
		try {
			if (data == null || typeof data !== 'object') {
				throw new Error('Error parsing data! Unzipping failed.');
			}

			if (data![Symbol.toStringTag] !== 'Uint8Array') {
				throw new Error('Unexpected file type! Unzipping failed.');
			}

			const decompressed = fflate.unzipSync(data as Uint8Array);

			if (!validateStaticMVFFiles(decompressed)) {
				throw new Error('MVF is missing some expect static files. Unzipping failed.');
			}

			const raw = {};
			const keys = Object.keys(decompressed);
			for (const fileName of keys) {
				// Ignore the file if it has no data
				if (decompressed[fileName].length === 0) {
					continue;
				}

				const path = fileName.split('/');

				if (path.length < 0) {
					throw new Error('Encounted a file with no name! Unzipping failed.');
				}

				if (path.length > 2) {
					throw new Error('Encountered unexpected nesting in MVF! Unzipping failed.');
				}

				// We can ignore any un-nested files as they should have already been attached above
				if (path.length === 1) {
					raw[path[0]] = decompressed[fileName];
					continue;
				}

				/**
				 * If the file is nested under a folder, construct the nested JSON appropriately
				 */
				if (path[0] === 'image') {
					// TODO: Handle images
					Logger.warn('MVF unzipping encountered an image. Ignoring as this case has not been implemented.');
					continue;
				} else {
					if (raw[path[0]] == null) {
						raw[path[0]] = {};
					}
					raw[path[0]][path[1]] = decompressed[fileName];
				}
			}

			resolve(raw as RawMVF);
		} catch (e) {
			reject(e);
		}
	});
}

export type LocalePackUrls = {
	[key: string]: string;
};

export function createEnvControl() {
	let environment: InternalServiceEnvironment = 'us';
	let authOption: Partial<Environment> = {};

	function getState() {
		return getAuthStateByEnvironment(getEnvironmentAuthState(environment), authOption);
	}

	return {
		/**
		 * @internal
		 */
		updateByUserOption(userOption: TGetMapDataOptions) {
			if (userOption.environment) {
				environment = userOption.environment;
			}
			if (userOption.baseUri) {
				authOption['baseUri'] = userOption.baseUri;
			}
			if (userOption.baseAuthUri) {
				authOption['baseAuthUri'] = userOption.baseAuthUri;
			}
			if (userOption.analytics?.baseUri) {
				authOption['analyticsBaseUri'] = userOption.analytics?.baseUri;
			}
		},
		/**
		 * @internal
		 */
		updateTileServerBaseUrl(url: string) {
			authOption['tileServerUri'] = url;
		},
		/**
		 * @internal
		 */
		updateEnvironment(env: InternalServiceEnvironment) {
			environment = env;
		},
		/**
		 * @internal
		 */
		setEnterprise(isEnterprise: boolean) {
			if (environment === 'us' || environment === 'us-enterprise') {
				// validate and adjust the environment based on the isEnterprise flag
				environment = isEnterprise ? 'us-enterprise' : 'us';
			}
			// EU is always enterprise, staging environments are not affected
		},
		getBaseUri() {
			return getState().baseUri;
		},
		getBaseAuthUri() {
			return getState().baseAuthUri;
		},
		getAnalyticsBaseUri() {
			return getState().analyticsBaseUri;
		},
		getTileServerUri() {
			return getState().tileServerUri;
		},
		reset() {
			environment = 'us';
			authOption = {};
		},
		/**
		 * @internal
		 */
		__getState: getState,
	};
}
export type EnvControl = ReturnType<typeof createEnvControl>;

function getAuthStateByEnvironment(env: Readonly<Environment>, state: Partial<Environment>): Environment {
	return {
		...env,
		...state,
	};
}

/**
 * Return IDs for all individual maps that are part of a multi-building MVF, not including the
 * outdoor map. If a map is not a multi-building map, an empty array is returned.
 */
export const getBuildingIds = (mvf: ParsedMVF) => {
	const floorStacks = Object.values(mvf['floorstack.json'] ?? mvf['mapstack.json'] ?? {});
	if (floorStacks.length === 1) {
		// Not multi-building
		return [];
	}

	return floorStacks
		.filter(floorStack => floorStack.type !== 'Outdoor')
		.map(floorStack => floorStack.id.replace('fs_', '').replace('ms_', ''));
};

const unzipAndParseMVFv3ToMVFv2 = async (data: Uint8Array) => {
	const start = performance.now();
	const maybeMvf = extractAndConvertMVFv3UnsafeSync(data);
	Logger.log(`Parsed MVFv3 in ${performance.now() - start}ms`);
	maybeMvf.inspectErr(e => {
		Logger.error(e);
		throw new Error('MVFv3 parsing failed!');
	});

	const mvf: ParsedMVF = maybeMvf.unwrap();

	return mvf;
};

export async function unzipAndParseMVFv2(data: Uint8Array, inputVersion = '2.0.0'): Promise<ParsedMVF> {
	switch (inputVersion) {
		case '2.0.0':
		case '2.0.0-c': {
			try {
				const raw = await unzipMVF(data);

				return parseMVF(raw);
			} catch {
				// Try to parse as MVFv3
				return unzipAndParseMVFv3ToMVFv2(data);
			}
		}
		case '3.0.0':
			return unzipAndParseMVFv3ToMVFv2(data);
		default:
			throw new Error(`Unsupported MVF version: ${inputVersion}`);
	}
}
