import { Vector3 } from 'three';
import type { BufferGeometry, LineSegments } from 'three';
import type { EntityId, Interpolation, Position } from '../types';
import type { PathMaterial, PatMeshContainer } from '../entities/geometry3d';
import { Geometry3DObject3D } from '../entities/geometry3d';
import type { FeatureCollection, Point } from 'geojson';
import type { GroupContainerState } from '../entities/group-container';
import { z } from 'zod';
import { geoJSONPoint } from '../utils/schema-validation';

export type PathProperties = {
	/**
	 * The parentId of the point. The point will be anchored to the altitude of this parent group container.
	 */
	parentId?: EntityId<GroupContainerState> | string | null;
};

/**
 * Updatable path state
 * @interface
 */
export type PathUpdateState = Omit<Partial<PathState>, 'id' | 'type'>;

/**
 * State representing a Path
 */
export type PathState = {
	readonly id: string | number;
	readonly type: 'path';
	/**
	 * The position of the path in [lon, lat]
	 */
	readonly position: Position;
	/**
	 * How much the path hovers above the floor plane in meters.
	 */
	verticalOffset?: number;
	/**
	 * Whether the path is visible.
	 */
	visible: boolean;
	/**
	 * Whether the path is interactive, which means it can be hovered over and clicked on.
	 *
	 * @example
	 * ```javascript
	 * 	renderer.on('click', ({ geometry }) => {});
	 * ```
	 */
	interactive?: boolean;
	/**
	 * The fraction of the path that has been drawn. This can be used to animate the drawing of the path.
	 * Note: use values between 0 and 1.
	 */
	completeFraction: number;

	/**
	 * The color of the path.
	 */
	color: string;
	/**
	 * The accent color of the path. When arrows are visible, it is applied to them
	 */
	accentColor: string;
	/**
	 * Display Arrows on Path to indicate direction
	 */
	displayArrowsOnPath: boolean;
	/**
	 * Arrows on path should animate to indicate direction
	 */
	animateArrowsOnPath: boolean;
	/**
	 * The width of the path in meters. Can be a number or an {@link Interpolation}.
	 */
	width: number | Interpolation<'zoom-level', number[]>;
	/**
	 * The fraction of the path that should be highlighted. This can be used to animate the highlight.
	 * @default 0
	 */
	highlightStartFraction: number;
	highlightEndFraction: number;
	highlightColor: string;
	highlightWidthMultiplier: number;
	highlightCompleteFraction: number;
};

export type AddPathOptions = {
	/**
	 * Whether the Path should be clickable.
	 * @default false
	 */
	interactive?: boolean;
	/**
	 * @internal
	 */
	id?: string | number;
	/**
	 * The width of the path.
	 *
	 * @default Interpolation { on: 'zoom-level', input: [17, 22], output: [0.8, 0.4] }
	 */
	width?: number | Interpolation<'zoom-level', number[]>;
	/**
	 * The color of the path.
	 */
	color?: string;
	/**
	 * The accent color of the path. When arrows are visible, it is applied to them
	 */
	accentColor?: string;
	/**
	 * Display Arrows on Path to indicate direction
	 */
	displayArrowsOnPath?: boolean;
	/**
	 * Arrows on path should animate to indicate direction
	 */
	animateArrowsOnPath?: boolean;
	/**
	 * Whether the path should be visible through geometry.
	 */
	visibleThroughGeometry?: boolean;
	/**
	 * Whether the path should be dashed.
	 */
	dashed?: boolean;
	/**
	 * How much the path hovers above the floor plane in meters.
	 */
	verticalOffset?: number;
	/**
	 * The fraction of the path that should be highlighted. This can be used to animate the highlight.
	 * @default 0
	 */
	highlightStartFraction?: number;
	highlightEndFraction?: number;
	highlightColor?: string;
	highlightWidthMultiplier?: number;
	highlightCompleteFraction?: number;
};

export const addPathOptionsSchema = z.object({
	interactive: z.boolean().optional(),
	id: z.string().optional(),
	width: z
		.union([
			z.number(),
			z.object({
				on: z.literal('zoom-level').optional(),
				input: z.array(z.number()).nonempty(),
				output: z.array(z.number()).nonempty(),
				easing: z.string().optional(),
			}),
		])
		.optional(),
	accentColor: z.string().optional(),
	displayArrowsOnPath: z.boolean().optional(),
	animateArrowsOnPath: z.boolean().optional(),
	visibleThroughGeometry: z.boolean().optional(),
	dashed: z.boolean().optional(),
});

export const featureSchema = z.object({
	type: z.enum(['Feature']),
	geometry: geoJSONPoint,
	properties: z.object({
		parentId: z.any().optional(),
	}),
});

export function validatePath(geometry: FeatureCollection<Point, PathProperties>, options: AddPathOptions) {
	const geometrySchema = z.object({
		features: z.array(featureSchema),
	});

	geometrySchema.parse(geometry);
	addPathOptionsSchema.parse(options);
}

export class PathComponent {
	readonly type = 'path';
	mesh = new Geometry3DObject3D() as PatMeshContainer;
	material?: PathMaterial;
	geometry?: BufferGeometry;
	outline?: LineSegments;
	feature: FeatureCollection<Point, PathProperties>;
	options: AddPathOptions;
	width: number | Interpolation<'zoom-level', number[]> = {
		on: 'zoom-level',
		input: [17, 22],
		output: [0.8, 0.4],
	};
	breakpoint = 0;
	// Cache conversion from mercator zoom to altitude, only relevant if width is an interpolation
	altitudeBreakpoints?: number[];
	accentColor = 'blue';
	completeFraction = 1.0;
	altitudeAdjustment = 1;
	displayArrowsOnPath = false;
	animateArrowsOnPath = false;
	visibleThroughGeometry = false;

	highlightStartFraction = 0.0;
	highlightEndFraction = 0.0;
	highlightCompleteFraction = 1.0;
	highlightWidthMultiplier = 1.1;
	highlightColor = 'red';

	dashed = false;
	/**
	 * If the path is vertical it will be rebuilt whenever altitudeDirty = true. This will be set during the first render of the path.
	 */
	isVertical = false;
	dirty = true;
	materialDirty = true;
	constructor(feature: FeatureCollection<Point, PathProperties>, options?: AddPathOptions) {
		this.feature = feature;
		this.options = options || {};
		this.width = options?.width ?? this.width;
		this.accentColor = options?.accentColor ?? this.accentColor;
		this.displayArrowsOnPath = options?.displayArrowsOnPath ?? this.displayArrowsOnPath;
		this.animateArrowsOnPath = options?.animateArrowsOnPath ?? this.animateArrowsOnPath;
		this.visibleThroughGeometry = options?.visibleThroughGeometry ?? this.visibleThroughGeometry;
		this.dashed = options?.dashed ?? this.dashed;
	}

	setColor() {}
	setOpacity() {}

	set visible(visible: boolean) {
		if (this.mesh) {
			this.mesh.visible = visible;
		}
	}

	get visible() {
		return this.mesh ? this.mesh.visible : false;
	}

	position = new Vector3();

	// This seems to be unused in favor of style.altitude
	// TODO: Why is this here?
	#altitude = 0;
	get altitude() {
		return this.#altitude;
	}

	set altitude(value) {
		if (this.mesh) {
			this.mesh.position.z = value;
			this.#altitude = value;
		}
	}

	set complete(value: number) {
		if (this.material) {
			this.material.uniforms.complete.value = value;
			this.material.needsUpdate = true;
		}
	}
}
