import { ExtrudeGeometry, Path, Shape } from 'three';
import ClipperLib from '@packages/internal/clipper-lib/ClipperLib';
import * as BufferGeometryUtils from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import type { Feature, MultiLineString } from 'geojson';

const QUALITY = 100;
const DEFAULT_WALL_WIDTH = 0.22;
const DEFAULT_WALL_HEIGHT = 0.01;

export function renderMultiLineStringGeometry(
	feature: Feature<MultiLineString, any>,
	convertTo3DMapPosition,
	style: any = {},
) {
	const { width = DEFAULT_WALL_WIDTH, height = DEFAULT_WALL_HEIGHT, color = 'gray', opacity = 1, altitude = 0 } = style;

	const boundingBox = {
		min: { x: Infinity, y: Infinity, z: altitude },
		max: { x: -Infinity, y: -Infinity, z: altitude + height },
	};

	const extrudeSettings = {
		bevelEnabled: false,
		depth: height,
	};

	let offsetter = new ClipperLib.ClipperOffset();

	const cartesianCoordinates = feature.geometry.coordinates.map(coordinate => {
		return coordinate.map(coordinate => {
			return convertTo3DMapPosition(coordinate[1], coordinate[0]);
		});
	});

	offsetter.AddPaths(
		cartesianCoordinates.map(v => v.map(c => ({ X: c.x * QUALITY, Y: c.y * QUALITY }))),
		ClipperLib.JoinType.jsMiter,
		ClipperLib.EndType.etOpenRound,
	);
	let polygons = new ClipperLib.PolyTree();

	offsetter.Execute(polygons, (width / 2) * QUALITY);
	const geometries: ExtrudeGeometry[] = [];

	for (const polygon of polygons.m_AllPolys) {
		const shape = new Shape();
		if (polygon.IsHole()) {
			continue;
		}
		boundingBox.min.x = Math.min(boundingBox.min.x, polygon.m_polygon[0].X);
		boundingBox.min.y = Math.min(boundingBox.min.y, polygon.m_polygon[0].Y);
		boundingBox.max.x = Math.max(boundingBox.max.x, polygon.m_polygon[0].X);
		boundingBox.max.y = Math.max(boundingBox.max.y, polygon.m_polygon[0].Y);
		const coords = polygon.m_polygon;
		shape.moveTo(coords[0].X / QUALITY, coords[0].Y / QUALITY);
		coords.slice(1).forEach(coord => shape.lineTo(coord.X / QUALITY, coord.Y / QUALITY));
		shape.lineTo(coords[0].X / QUALITY, coords[0].Y / QUALITY);

		shape.holes = polygon.m_Childs.map(h => {
			const path = new Path();
			const coords = h.m_polygon;
			path.moveTo(coords[0].X / QUALITY, coords[0].Y / QUALITY);
			coords.slice(1).forEach(coord => path.lineTo(coord.X / QUALITY, coord.Y / QUALITY));
			path.lineTo(coords[0].X / QUALITY, coords[0].Y / QUALITY);

			return path;
		});

		geometries.push(new ExtrudeGeometry(shape, extrudeSettings));
	}

	const geometry = BufferGeometryUtils.mergeGeometries(geometries);
	geometry.translate(0, 0, altitude || 0);

	geometry.userData = {
		style: {
			color,
			opacity,
		},
	};

	offsetter.Clear();

	return geometry;
}
