import type { Scene, Camera } from 'three';
import { Color, WebGLRenderer } from 'three';
import Logger from '../../../packages/common/Mappedin.Logger';
import { isTestMode } from './test-mode';
import { RENDER_LAYERS } from '../utils/constants';

export type TRendererOptions = Partial<{
	canvas: HTMLCanvasElement | OffscreenCanvas;
	antialias: boolean;
	backgroundColor: string;
	backgroundAlpha: number;
	alpha: boolean;
	onWebGLRendererError: (e: Error) => void;
	xRayPath: boolean;
	onWebGLContextCreationError: (e: Event) => void;
	onWebGLContextLost: (e: Event) => void;
	onWebGLContextRestored: (e: Event) => void;
}>;

export class Renderer {
	backgroundAlpha = 1.0;
	backgroundColor = new Color();

	width = 0;
	height = 0;

	options: TRendererOptions = {};

	renderer?: WebGLRenderer;

	constructor(renderOptions: TRendererOptions) {
		try {
			this.options = renderOptions;

			if (isTestMode()) {
				this.renderer = {
					setSize: () => {},
					state: {
						reset: () => {},
					},
					properties: {
						get: () => {},
					},
					getSize: () => ({ width: window.innerWidth, height: innerHeight }),
					getDrawingBufferSize: () => ({ width: window.innerWidth, height: innerHeight }),
					getClearAlpha: () => 1.0,
					getClearColor: () => new Color(),
					autoUpdate: true,
					shadowMap: {
						autoUpdate: true,
					},
					getContext: () => {
						return {
							getContextAttributes() {
								return { alpha: true, antialias: false, depth: true, stencil: false };
							},
						};
					},
					readRenderTargetPixelsAsync: () => Promise.resolve([]),
					setRenderTarget: () => {},
					setClearColor: () => {},
					clear: () => {},
					render: () => {},
					clearStencil: () => {},
					clearDepth: () => {},
					domElement: document.createElement('canvas'),
					capabilities: {
						isWebGL2: true,
					},
				} as unknown as WebGLRenderer;

				this.renderer.domElement.style.width = '1920px';
				Object.defineProperty(this.renderer.domElement, 'clientWidth', {
					value: 1920,
				});
				this.renderer.domElement.style.height = '1080px';
				Object.defineProperty(this.renderer.domElement, 'clientHeight', {
					value: 1080,
				});
			} else {
				this.renderer = new WebGLRenderer({
					canvas: renderOptions.canvas,
					alpha: renderOptions.alpha || false,
					stencil: true,
					antialias: typeof renderOptions.antialias === 'boolean' ? renderOptions.antialias : true,
					powerPreference: 'high-performance',
					preserveDrawingBuffer: true,
				});
				this.renderer.localClippingEnabled = true;
			}
			this.backgroundColor = new Color(renderOptions.backgroundColor ?? '#ffffff');
			this.backgroundAlpha = renderOptions.backgroundAlpha ?? 1.0;
		} catch (e) {
			Logger.error(e);
			if (renderOptions.onWebGLRendererError) {
				renderOptions.onWebGLRendererError(e as Error);
			}
		}
		this.domElement()?.addEventListener('webglcontextlost', this.handleWebGLContextLost);
		this.domElement()?.addEventListener('webglcontextrestored', this.handleWebGLContextRestored);
		this.domElement()?.addEventListener('webglcontextcreationerror', this.handleWebGLContextCreationError);
	}

	/**
	 * Dispose of the renderer and its buffers.
	 */
	destroy() {
		if (this.renderer) {
			this.renderer.dispose?.();
		}
		this.domElement()?.removeEventListener('webglcontextlost', this.handleWebGLContextLost);
		this.domElement()?.removeEventListener('webglcontextrestored', this.handleWebGLContextRestored);
		this.domElement()?.removeEventListener('webglcontextcreationerror', this.handleWebGLContextCreationError);
	}

	enabledLayers = new Set([0, 1]);

	/**
	 * Re-render the scene, depending on which parts of the scene have been
	 * invalidated.
	 *
	 * @method render
	 * @param renderTarget {null or WebGLRenderTarget}
	 * @param scene {Scene}
	 * @param sceneCamera {Camera}
	 */
	render(scene: Scene, sceneCamera: Camera) {
		if (this.renderer) {
			this.renderer.state.reset();

			sceneCamera.layers.disableAll();
			for (const l of this.enabledLayers.values()) {
				sceneCamera.layers.enable(l);
			}
			this.renderer.clearStencil();
			this.renderer.autoClear = false;
			this.renderer.setRenderTarget(null);
			this.renderer.render(scene, sceneCamera);

			// clear the depth buffer
			this.renderer.clearDepth();

			// render the scene again with the always on top layer enabled
			sceneCamera.layers.disableAll();
			sceneCamera.layers.enable(RENDER_LAYERS.ALWAYS_ON_TOP_PATHS);
			this.renderer.render(scene, sceneCamera);

			this.renderer.state.reset();
		}
	}

	clear() {
		if (this.renderer) {
			this.renderer.setClearColor(this.backgroundColor, this.backgroundAlpha);
			this.renderer.clear();
		}
	}

	/**
	 * Set the size of the renderer, composer, and all its internal buffers.
	 *
	 * @method setBufferSize
	 * @param width {number}
	 * @param height {number}
	 */
	setBufferSize(width: number, height: number) {
		if (process.env.NODE_ENV === 'test' || !this.renderer) {
			return;
		}
		this.width = width;
		this.height = height;
		this.renderer.setSize(width, height, false);
	}

	/**
	 * Set the color and opacity that will be drawn behind the scene.
	 *
	 * @method setBackgroundColor
	 * @param color {Color}
	 * @param alpha {number}
	 */
	setBackgroundColor(color, alpha) {
		this.backgroundColor = color;
		this.backgroundAlpha = alpha;
	}

	domElement() {
		return this.renderer?.domElement;
	}

	handleWebGLContextCreationError = (event: Event) => {
		this.options.onWebGLContextCreationError?.(event);
	};

	handleWebGLContextLost = (event: Event) => {
		this.options.onWebGLContextLost?.(event);
	};

	handleWebGLContextRestored = (event: Event) => {
		this.options.onWebGLContextRestored?.(event);
	};
}
