import type { Connection } from '@mappedin/mvf-connections';
import type { FloorId, GeometryId } from '@mappedin/mvf-core';
import { typedEntries } from '@mappedin/mvf-core/utils';
import type { NavigationFlagDeclaration, NavigationFlagDeclarations } from '@mappedin/mvf-navigation-flags';
import { hasFlag } from '@mappedin/mvf-navigation-flags';
import type { NodeId } from '@mappedin/mvf-nodes';
import type { ParsedMVF } from '../../types/bundle.js';
import type { BuilderOptions } from '../builderOptions.js';
import type { MVFv2_STANDARD_MVFv3 } from '../index.js';

export type V3Nodes = Pick<MVFv2_STANDARD_MVFv3, 'nodes' | 'connections' | 'floors'>;
export type V2Nodes = Pick<ParsedMVF, 'node.geojson'>;
export type V2Node = V2Nodes['node.geojson']['features'][number];

export function buildNodes(
	v3: V3Nodes,
	{
		geometryMaps,
		navigationFlags,
		incomingEdgesByNodeId,
	}: Pick<BuilderOptions, 'geometryMaps' | 'navigationFlags' | 'incomingEdgesByNodeId'>,
): V2Nodes {
	const v2: V2Nodes = {
		'node.geojson': {
			type: 'FeatureCollection',
			features: [],
		},
	};
	const v2Nodes = v2['node.geojson'].features;

	/**
	 * Connections
	 *
	 * Connections in MVFv3 don't depend on nodes directly. Their entrances and exits should
	 * link to geometry, and then nodes should also link to those geometries. These can all be
	 * many to many relationships.
	 *
	 * We also need to know the change in elevation between floors to correctly calculate the cost
	 * of traversing a connection.
	 *
	 * In MVFv2, there is a single giant node graph, connecting all the floors. Connections are decoration
	 * that adds information to a path, like icons and whether it's accessible or not.
	 *
	 * Therefore, we have to do a fair bit of transformation to generate V2 node edges from V3 connections
	 */

	// Map of floor id to elevation for determining elevation deltas between floors
	const floorElevationById = createFloorElevationById(v3);

	// A map of the nodes that reference a particular geometry id, so we can connect Connection entrances and exits
	// to the correct nodes
	const nodesByGeometryId = createNodesByGeometryId(v3);

	// A map of the neighbors a node should get from Connections
	const connectionNeighborsByNodeId = createConnectionNeighborsByNodeId(v3, nodesByGeometryId, floorElevationById);

	for (const [floorId, v3Nodes] of typedEntries(v3.nodes ?? {})) {
		for (const v3node of v3Nodes?.features ?? []) {
			const firstSpace = geometryMaps.space.get(v3node.properties.geometryIds[0])?.features[0];
			const externalId =
				firstSpace?.geometry.type === 'Point' && firstSpace?.properties.details?.externalId
					? firstSpace.properties.details.externalId
					: '';

			// A node has a flag if:
			// - It has incoming edges
			// - Some incoming edge has that flag
			const incomingEdges = incomingEdgesByNodeId.get(v3node.properties.id) ?? [];
			const knownFlags = Object.keys(navigationFlags ?? {});
			const declarations: NavigationFlagDeclaration[] = [];
			for (const edge of incomingEdges) {
				for (const knownFlag of knownFlags) {
					if (hasFlag(edge, navigationFlags[knownFlag])) {
						declarations.push(navigationFlags[knownFlag]);
					}
				}
			}

			const v2node: V2Node = {
				type: 'Feature',
				geometry: v3node.geometry,
				properties: {
					id: v3node.properties.id,
					floor: floorId,
					// If connections are not walkable, they won't make it into V2. V2 expects connections to attach to spaces.
					space: v3node.properties.geometryIds
						.map((id) => geometryMaps.space.get(id)?.ids)
						.flat()
						.filter((x) => x !== undefined),
					neighbors: [
						...v3node.properties.neighbors.map((n) => ({ id: n.id, weight: n.extraCost })),
						...(connectionNeighborsByNodeId.get(v3node.properties.id) ?? []),
					],
					map: floorId,
					externalId,
					flags: declarations.length > 0 ? buildFlagsArray(declarations) : undefined,
				},
			};
			v2Nodes.push(v2node);
		}
	}
	return v2;
}

export function createFloorElevationById(v3: Pick<V3Nodes, 'floors'>): Map<FloorId, number> {
	const floorElevationById = new Map<FloorId, number>();
	for (const floor of v3.floors?.features ?? []) {
		floorElevationById.set(floor.properties.id, floor.properties.elevation);
	}
	return floorElevationById;
}

export function createNodesByGeometryId(v3: Pick<V3Nodes, 'nodes'>): Map<GeometryId, NodeId[]> {
	const nodesByGeometryId = new Map<GeometryId, NodeId[]>();
	for (const floorNodes of Object.values(v3.nodes ?? {})) {
		for (const node of floorNodes?.features ?? []) {
			if (node.properties.geometryIds.length === 0) {
				continue;
			}
			for (const geometryId of node.properties.geometryIds) {
				const existingNodes = nodesByGeometryId.get(geometryId) ?? [];
				if (existingNodes.includes(node.properties.id)) {
					continue;
				}
				existingNodes.push(node.properties.id);
				nodesByGeometryId.set(geometryId, existingNodes);
			}
		}
	}
	return nodesByGeometryId;
}

export function createConnectionNeighborsByNodeId(
	v3: V3Nodes,
	nodesByGeometryId: Map<GeometryId, NodeId[]>,
	floorElevationById: Map<FloorId, number>,
): Map<NodeId, { id: NodeId; weight: number }[]> {
	// A map of the neighbors a node should get from Connections
	const connectionNeighborsByNodeId = new Map<NodeId, { id: NodeId; weight: number }[]>();

	// For each connection, connect all nodes for all entrances to all nodes for all exits, using the difference
	// in elevation between floors plus the entry cost to calculate the cost
	for (const connection of v3.connections ?? []) {
		const exitNodes: (Connection['exits'][number] & { nodes: NodeId[] })[] = connection.exits.map((exit) => ({
			...exit,
			nodes: nodesByGeometryId.get(exit.geometryId) ?? [],
		}));
		for (const entrance of connection.entrances) {
			const entranceNodeIds = nodesByGeometryId.get(entrance.geometryId) ?? [];
			for (const entranceNode of entranceNodeIds) {
				for (const exitNode of exitNodes) {
					const floorCost =
						Math.abs(
							(floorElevationById.get(entrance.floorId) ?? 0) - (floorElevationById.get(exitNode.floorId) ?? 0),
						) * connection.floorCostMultiplier;
					const existingNeighbors = connectionNeighborsByNodeId.get(entranceNode) ?? [];
					// Technically there could be multiple nodes linked to the same geometry id, so we need to connect all of them
					// It will PROBABLY just be one though.
					existingNeighbors.push(
						...exitNode.nodes
							.filter((id) => id !== entranceNode)
							.map((id) => ({ id, weight: floorCost + connection.entryCost })),
					);
					connectionNeighborsByNodeId.set(entranceNode, existingNeighbors);
				}
			}
		}
	}

	return connectionNeighborsByNodeId;
}

export function buildFlagsArray(
	flagDeclarations: NonNullable<NavigationFlagDeclarations[string]>[],
): number[] | undefined {
	if (flagDeclarations == null || flagDeclarations.length === 0) {
		return;
	}

	const flags = new Array(Math.max(...flagDeclarations.map((f) => f.index)) + 1).fill(0);

	for (const declaration of flagDeclarations) {
		flags[declaration.index] |= 1 << declaration.bit;
	}

	return flags;
}
