import type { Vector2 } from 'three';
import { distance, average, angle } from './utils';

class InputSet {
	input1: Vector2;
	input2: Vector2;
	lastAngle: number;
	lastDistance: number;
	totalDistance: number;
	first: Vector2;
	second: Vector2;

	constructor(input1: Vector2, input2: Vector2) {
		this.input1 = input1;
		this.input2 = input2;

		this.lastAngle = angle(input1, input2);
		this.lastDistance = distance(input1, input2);
		this.totalDistance = 0;

		this.first = input1;
		this.second = input2;
	}

	update(input1: Vector2, input2: Vector2): void {
		const newDistance = distance(input1, input2);

		this.input1 = input1;
		this.input2 = input2;

		const newAngle = angle(input1, input2);
		this.lastAngle = newAngle;
		this.lastDistance = newDistance;
		this.totalDistance += (distance(this.first, input1) + distance(this.second, input2)) / 2;

		this.first = average(this.first, input1);
		this.second = average(this.second, input2);
	}
}

export { InputSet };
