import type { Camera, PerspectiveCamera, Scene } from 'three';
import type { RendererState, Systems } from '../../renderer';
import type RendererCore from '../../renderer';
import type { Renderer } from '../../services/renderer';
import { populateGeometry3DIdsInScene, populateGeometry2DIdsInScene } from '../../utils';
import { PubSub } from '@packages/internal/common';

export class RenderSystem extends PubSub<{
	'measure-canvas': undefined;
	'pre-render': undefined;
	'post-render': undefined;
	'camera-state-change': undefined;
}> {
	private state: RendererState;
	private renderer: Renderer;
	private mode: RendererCore['mode'];
	private scene: Scene;
	private cameraObject: PerspectiveCamera;
	private viewCamera: Camera;
	private systems: Systems;
	constructor(
		renderer: Renderer,
		state: RendererState,
		mode: RendererCore['mode'],
		scene: Scene,
		cameraObject: PerspectiveCamera,
		viewCamera: Camera,
		systems: Systems,
	) {
		super();
		this.renderer = renderer;
		this.state = state;
		this.mode = mode;
		this.scene = scene;
		this.cameraObject = cameraObject;
		this.viewCamera = viewCamera;
		this.systems = systems;
		this.systems.collisionSystem.on('visibility-changed', this.updateEntities2D);
		this.systems.twoDVisiblitySystem.on('draw:2d', this.drawEntities2D);
		this.systems.twoDVisiblitySystem.on('animate:2d', this.animate2D);
		this.systems.cameraSystem.on('pan-start', () => (this.needs2DRecompute = false));
		this.systems.cameraSystem.on('zoom-start', () => (this.needs2DRecompute = false));
		this.systems.cameraSystem.on('zoom-end', () => (this.needs2DRecompute = true));
		this.systems.cameraSystem.on('rotate-start', () => (this.needs2DRecompute = false));
		this.systems.cameraSystem.on('rotate-end', () => (this.needs2DRecompute = true));
	}

	// we want colliders to find a new strategy if this is true
	// otherwise, they will use the same strategy as the previous frame
	// adding to stability when panning, and in the middle of a zoom/rotate
	needs2DRecompute = false;

	twoDdirty = true;
	threeDdirty = true;

	private frameId: number | null = null;
	private twoDFrameId: number | null = null;
	private animationFrameId: number | null = null;
	private nextFrame: number | null = null;

	update = (sync = false) => {
		if (this.renderer?.renderer == null) {
			return Promise.resolve() as Promise<any>;
		}

		if (sync) {
			/**
			 * Perform a synchronous render
			 * First, update the camera and projections
			 * Next, paint
			 */
			const promise = this.commitRenderState();
			this.paint();

			return promise;
		} else {
			if (this.frameId) {
				return Promise.resolve();
			}

			return new Promise<void>((res, rej) => {
				/**
				 * Perform an asynchronous render
				 * First, update the camera and projections
				 * Next, paint
				 */
				this.frameId = requestAnimationFrame(() => {
					if (this.renderer?.renderer == null) {
						return res();
					}
					if (this.mode === 'standalone') {
						return this.commitRenderState()
							?.then(() => {
								this.paint();
								res();
							})
							.catch(error => {
								rej(error);
							});
					} else {
						return res();
					}
				});
			});
		}
	};

	/**
	 * Paint to screen
	 */
	private paint() {
		if (!this.renderer?.renderer) {
			return;
		}

		if (this.mode === 'standalone') {
			this.renderer.clear();
		}

		this.renderer.render(
			this.scene,
			this.state.useStandaloneCamera || this.mode === 'standalone' ? this.cameraObject : this.viewCamera,
		);
	}

	/**
	 * Play 2D animations in a frame
	 */
	private animate2D = () => {
		this.animationFrameId = requestAnimationFrame(() => {
			this.animationFrameId = null;
			this.systems.twoDVisiblitySystem.playAnimations();
		});
	};

	/**
	 * Draw 2D entities in a frame
	 */
	private drawEntities2D = () => {
		if (this.twoDFrameId) {
			return;
		}
		this.twoDFrameId = requestAnimationFrame(() => {
			this.twoDFrameId = null;
			this.systems.twoDDrawSystem.update(this.systems.cameraSystem.getZoomLevel());
		});
	};

	/**
	 * Update 2D entities to determine which ones are visible, and these are to be processed in the next frame
	 */
	private updateEntities2D = () => {
		if (this.twoDFrameId) {
			cancelAnimationFrame(this.twoDFrameId);
			this.twoDFrameId = null;
		}
		this.twoDFrameId = requestAnimationFrame(() => {
			this.twoDFrameId = null;
			// The watermark should insert itself into the quad tree before the collision system updates
			this.systems.watermarkSystem.insertIntoQuadTree(this.systems.collisionSystem.interactiveCollidersQTree);
			this.systems.interactionSystem.updateQuadtree(this.systems.collisionSystem.interactiveCollidersQTree);
			this.systems.twoDVisiblitySystem.update();
			this.systems.twoDDrawSystem.update(this.systems.cameraSystem.getZoomLevel());
			this.systems.domDrawSystem.update(this.systems.cameraSystem.isUserInteracting());
		});
	};

	private commitRenderState = () => {
		try {
			const promises: Promise<any>[] = [];
			this.frameId = null;
			this.publish('pre-render');

			this.state.internalTweenGroup.update();
			this.state.externalTweenGroup.update();

			if (this.mode === 'outdoors-interleaved' || this.mode === 'standalone') {
				this.systems.cameraSystem.update();
			}

			// this.scene.updateMatrixWorld();

			if (this.state.shouldMeasureCanvas) {
				this.state.shouldMeasureCanvas = false;
				this.publish('measure-canvas');
			}

			if (this.twoDdirty) {
				this.state.geometry2DIdsInScene.clear();
				populateGeometry2DIdsInScene(this.state.entityScene, this.state.geometry2DIdsInScene);
				this.systems.domVisiblitySystem.dirty = true;
				this.systems.collisionSystem.collidersDirty = true;
				this.systems.domMutationSystem.dirty = true;
				this.systems.panBoundsSystem.dirty = true;
				this.twoDdirty = false;
			}

			if (this.threeDdirty) {
				this.state.geometry3DIdsInScene.clear();
				this.state.geometryGroupIdsToLoad.clear();
				populateGeometry3DIdsInScene(
					this.state.entityScene,
					this.state.geometry3DIdsInScene,
					this.state.geometryGroupIdsToLoad,
				);
				this.threeDdirty = false;
				this.systems.interactionSystem.dirty3D = true;
				this.systems.panBoundsSystem.dirty = true;
				this.systems.outlineInterpolationSystem.geometries3DDirty = true;
				this.systems.geometryInFocusSystem.focusablesDirty = true;
			}

			// Leaving this as it needs more work, so it doesn't slip through the cracks
			// /**
			//  * [PERF] Since MapLibre does a bunch of renders as the tiles pop-in, we need to make sure that we're not re-rendering
			//  * anything if the camera hasn't moved, and the 2D/3D entities haven't changed
			//  */
			// if (false && !this.twoDdirty && !this.threeDdirty && this.core.cameraSystem.dirty === false) {
			// 	return;
			// }

			const meshOptimizePromise = this.systems.meshOptimizationSystem.update();
			if (meshOptimizePromise) {
				promises.push(meshOptimizePromise);
			}
			this.systems.borderSystem.update();
			this.systems.meshModificationSystem.update();
			this.systems.meshDetachmentSystem.update();
			this.systems.renderOrderSystem.update();
			this.systems.clippingPlaneSystem.update();
			this.systems.imageSystem.update(this.systems.cameraSystem.getRotation());
			promises.push(this.systems.text3DSystem.update(this.systems.cameraSystem.getRotation()));
			this.systems.customGeometrySystem.update();

			this.systems.panBoundsSystem.update();
			this.systems.geometryInFocusSystem.update(
				this.systems.cameraSystem.isCameraMoving(),
				this.systems.cameraSystem.lastCameraStoppedMovingTime(),
			);

			this.systems.outlineInterpolationSystem.update(
				this.systems.cameraSystem.getZoomLevel(),
				this.systems.cameraSystem.zoomDirty,
			);

			this.systems.pathSystem.update(
				this.systems.cameraSystem.minZoomAltitude,
				this.systems.cameraSystem.maxZoomAltitude,
				this.systems.cameraSystem.getZoomAltitude(),
			);
			this.systems.cameraSystem.postRender();

			this.systems.domVisiblitySystem.update();
			this.systems.domMutationSystem.update();
			this.systems.twoDProjectionSystem.update();
			this.systems.occlusion2DSystem.update();
			this.systems.twoDDrawSystem.update(this.systems.cameraSystem.getZoomLevel());
			this.systems.domDrawSystem.update(this.systems.cameraSystem.isUserInteracting());
			this.systems.watermarkSystem.update();

			const needs2DRecompute = this.needs2DRecompute;
			// a double rAf guarantees that this will be called in the next frame
			// looks bad, but actually legit
			this.nextFrame = requestAnimationFrame(() => {
				this.systems.collisionSystem.update(
					this.systems.watermarkSystem.width,
					this.systems.watermarkSystem.height,
					this.systems.watermarkSystem.options.position,
					!needs2DRecompute,
					this.systems.imageSystem.currentImageScreenBBoxes,
				);
			});

			this.systems.textureSystem.update();
			this.systems.drawSystem.update();
			this.systems.geometryDisposalSystem.update();

			this.systems.cameraSystem.dirty = false;
			this.systems.cameraSystem.zoomDirty = false;
			this.systems.cameraSystem.rotationDirty = false;

			// Fire camera-state-change event if camera state is dirty
			if (this.systems.cameraSystem.stateDirty) {
				this.publish('camera-state-change');
				this.systems.cameraSystem.stateDirty = false;
			}

			this.publish('post-render');

			// reset so that we don't recompute colliders the next frame until a camera change sets this true again.
			this.needs2DRecompute = false;

			return Promise.all(promises);
		} catch (e) {
			return Promise.reject(e);
		}
	};

	destroy() {
		if (this.frameId) {
			cancelAnimationFrame(this.frameId);
		}
		if (this.twoDFrameId) {
			cancelAnimationFrame(this.twoDFrameId);
		}
		if (this.animationFrameId) {
			cancelAnimationFrame(this.animationFrameId);
		}
		if (this.nextFrame) {
			cancelAnimationFrame(this.nextFrame);
		}
		this.state.geometry2DIdsInScene.clear();
		this.state.geometry3DIdsInScene.clear();
		this.systems.domVisiblitySystem.dirty = true;
		this.systems.collisionSystem.collidersDirty = true;
		this.update();
		this.systems.collisionSystem.off('visibility-changed', this.updateEntities2D);
		this.systems.twoDVisiblitySystem.off('draw:2d', this.drawEntities2D);
		this.systems.twoDVisiblitySystem.off('animate:2d', this.animate2D);
	}
}
