import type { PathState } from '@mappedin/core-sdk';
import z from 'zod';
import type { Interpolation } from '../../../geojson/src/types';
import { parseCompleteState } from './state-utils';

export const PATH_STATE_TYPE = 'path';

export const PATH_DEFAULTS = {
	color: '#4b90e2',
	width: 1,
	completeFraction: 0,
	visible: true,
	verticalOffset: 0.1,
} as const;

export const pathStateSchema = z.object({
	type: z.literal(PATH_STATE_TYPE).default(PATH_STATE_TYPE),
	/**
	 * The color of the path.
	 * @default #4b90e2
	 */
	color: z.string().default(PATH_DEFAULTS.color),
	/**
	 * The width of the path in meters. Can be a number or an Interpolation.
	 */
	width: z.union([z.number(), z.any()]) as z.ZodType<number | Interpolation<'zoom-level', number[]>>,
	/**
	 * The fraction of the path that should be complete. This can be used to animate the path.
	 * @default 0
	 */
	completeFraction: z.number().default(PATH_DEFAULTS.completeFraction),
	/** Whether the path is visible.
	 * @default true
	 */
	visible: z.boolean().default(PATH_DEFAULTS.visible),
	/**
	 * The altitude of the geometry, in meters
	 * @default 0.1
	 */
	verticalOffset: z.number().default(PATH_DEFAULTS.verticalOffset),
	/**
	 * The start fraction of the path that should be highlighted. Indicates the start of the highlighted section.
	 * @default 0
	 */
	highlightStartFraction: z.number().default(0),
	/**
	 * The end fraction of the path that should be highlighted. Indicates the end of the highlighted section.
	 * @default 0
	 */
	highlightEndFraction: z.number().default(0),
	/**
	 * The color of the highlight.
	 * @default red
	 */
	highlightColor: z.string().default('red'),
	/**
	 * The width multiplier of the highlight. Setting this to 1 will result in no changes to path width.
	 * @default 1.1
	 */
	highlightWidthMultiplier: z.number().default(1.1),
	/**
	 * The fraction of the highlighted path that should be complete. This can be used to animate the highlight.
	 * @default 1
	 */
	highlightCompleteFraction: z.number().default(1),
});

export const pathStateSchemaPartial = pathStateSchema.partial();
const pathStateSchemaStrict = z.strictObject(pathStateSchema.shape);

export type TPathState = z.infer<typeof pathStateSchemaStrict>;

export type TPathUpdateState = z.infer<typeof pathStateSchemaPartial>;

export function getPathState(state: PathState): TPathState {
	const {
		color,
		visible,
		width,
		verticalOffset,
		completeFraction,
		highlightStartFraction,
		highlightEndFraction,
		highlightColor,
		highlightWidthMultiplier,
		highlightCompleteFraction,
	} = state;

	return parseCompleteState(pathStateSchema, {
		color,
		visible,
		width,
		verticalOffset,
		completeFraction,
		highlightStartFraction,
		highlightEndFraction,
		highlightColor,
		highlightWidthMultiplier,
		highlightCompleteFraction,
	});
}
