import destination from '@turf/destination';
import type { Position } from 'geojson';

/**
 * Offsets a line by a specified distance perpendicular to the original line.
 * The function handles both vertical and horizontal lines specifically, and calculates the
 * offset for diagonal lines by determining the line's perpendicular slope.
 *
 * @param {Position} point1 - The starting point of the line ([longitude, latitude]).
 * @param {Position} point2 - The ending point of the line ([longitude, latitude]).
 * @param {number} offset - The distance by which to offset the line, perpendicular to its initial direction.
 * @param {number} angle - The angle of the line in radians, from -180 to 180.
 * @hidden
 * @returns {[Position, Position]} The new line endpoints after the offset.
 */
export function getOffsetLine(point1: Position, point2: Position, offset: number, angle: number): [Position, Position] {
	const destinationPoint1 = destination(point1, offset / 1000, angle);
	const destinationPoint2 = destination(point2, offset / 1000, angle);

	return [destinationPoint1.geometry.coordinates, destinationPoint2.geometry.coordinates];
}
