import { type Coordinate } from '../map-data-objects';
import type { IFocusable } from '../types';
import { computeTotalDistanceBetweenCoordinates } from '../utils/misc';

/**
 * @experimental
 *
 * A segment of a complete path. This can be a continuous portion of the path on a single floor, or
 * the transition of a path between two floors, or a transition between indoors and outdoors.
 */
export class PathSegment implements IFocusable {
	readonly id: string;

	readonly coordinates: Coordinate[];

	#cachedDistance: number | undefined;

	/**
	 * @internal
	 */
	static readonly __type = 'path-segment';

	/**
	 * @internal
	 */
	readonly __type = PathSegment.__type;

	/**
	 * Checks if the provided instance is a PathSegment.
	 */
	static is(instance: object): instance is PathSegment {
		return (instance as { __type: string }).__type === PathSegment.__type;
	}

	constructor(id: string, coordinates: Coordinate[]) {
		this.id = id;
		this.coordinates = coordinates;
	}

	/**
	 * Whether the path segment is a connection segment between two floors.
	 */
	get isConnectionSegment() {
		return this.coordinates.length === 2 && this.coordinates[0].floorId !== this.coordinates[1].floorId;
	}

	/**
	 * The distance of the path segment in meters.
	 */
	get distance() {
		if (this.#cachedDistance) {
			return this.#cachedDistance;
		}

		this.#cachedDistance = computeTotalDistanceBetweenCoordinates(this.coordinates);

		return this.#cachedDistance;
	}

	/** @internal */
	get focusTarget() {
		return this.coordinates;
	}
}

/**
 * Class representing a path on the {@link MapView}.
 *
 * Paths are used to indicate a route on the map and can be added and removed using {@link Paths.add} and {@link Paths.remove}.
 */
export class Path implements IFocusable {
	/**
	 * The path's id
	 */
	id: string;

	/**
	 * The promise that resolves when the current path animation is complete.
	 */
	animation: Promise<void>;

	/**
	 * The coordinates of the path.
	 */
	coordinates: Coordinate[];

	/**
	 * @experimental
	 *
	 * The segments of the path.
	 */
	segments: PathSegment[];

	#cachedDistance: number | undefined;

	/**
	 * @internal
	 */
	static readonly __type = 'path';

	/**
	 * @internal
	 */
	readonly __type = Path.__type;

	/**
	 * Checks if the provided instance is a Path.
	 *
	 * @param instance The instance to check.
	 * @returns {boolean} True if the instance is a Path, false otherwise.
	 */
	static is(instance: object): instance is Path {
		return (instance as { __type: string }).__type === Path.__type;
	}

	/** @internal */
	get focusTarget() {
		return this.coordinates;
	}

	/**
	 * @internal
	 */
	constructor(
		id: string,
		drawAnimation: Promise<void>,
		coordinates: Coordinate[],
		segments: {
			id: string | number;
			coordinates: Coordinate[];
		}[],
	) {
		this.id = id;
		this.animation = drawAnimation;
		this.coordinates = coordinates;
		this.segments = segments.map(({ id, coordinates }) => new PathSegment(id.toString(), coordinates));
	}

	/**
	 * The distance of the path in meters.
	 */
	get distance() {
		if (this.#cachedDistance) {
			return this.#cachedDistance;
		}

		this.#cachedDistance = computeTotalDistanceBetweenCoordinates(this.coordinates);

		return this.#cachedDistance;
	}

	toJSON() {
		return {
			__type: Path.__type,
			id: this.id,
			coordinates: this.coordinates,
			segments: this.segments,
		};
	}
}
