import { type MapDataInternal } from '../../map-data-objects';
import { type GeoJsonApi } from '../api';
import { autoGeometries } from './geometries';
import { autoLabels } from './labels';

export async function auto(mapData: MapDataInternal, api: GeoJsonApi) {
	const promises = [autoLabels(mapData, api), autoGeometries(mapData, api)] as const;
	const [labels] = await Promise.all(promises);
	return { labels };
}
