import type { Texture } from 'three';
import { TextureLoader as TextureLoaderThree } from 'three';

class TextureLoaderSingleton {
	private static instance: TextureLoaderSingleton;
	public loader: TextureLoaderThree;

	private constructor() {
		this.loader = new TextureLoaderThree();
	}

	public static getInstance(): TextureLoaderSingleton {
		if (!TextureLoaderSingleton.instance) {
			TextureLoaderSingleton.instance = new TextureLoaderSingleton();
		}

		return TextureLoaderSingleton.instance;
	}

	public getLoader(): TextureLoaderThree {
		return this.loader;
	}

	public load(url: string) {
		return new Promise<Texture>((resolve, reject) => {
			this.loader.load(url, resolve, undefined, reject);
		});
	}
}

const TextureLoader = TextureLoaderSingleton.getInstance();

export { TextureLoader };
